import React, { useState, useEffect } from 'react';
import { clsx } from 'clsx';
import { CheckoutSkeleton } from '../CheckoutSkeleton';

export const CheckoutCard = ({ data = {}, className, loading = false }) => {
  const [email, setEmail] = useState('');
  const [timedOut, setTimedOut] = useState(false);

  // Auto-show content after 2 seconds if loading is true
  useEffect(() => {
    if (loading) {
      setTimedOut(false);
      const timer = setTimeout(() => {
        setTimedOut(true);
      }, 2000);
      return () => clearTimeout(timer);
    }
  }, [loading]);

  const shouldShowSkeleton = loading && !timedOut;
  
  const cartItems = data.cartItems || [];
  const subtotal = cartItems.reduce((acc, item) => acc + item.price, 0);

  // Show skeleton loading state
  if (shouldShowSkeleton) {
    return <CheckoutSkeleton data={{ variant: 'card' }} className={className} />;
  }

  return (
    <div className={clsx("checkout-card bg-neutral-100 py-16 px-6 lg:px-12", className)}>
      <div className="max-w-7xl mx-auto flex flex-col lg:flex-row gap-8 items-start">
        
        <div className="flex-grow space-y-6">
          <div className="bg-white rounded-3xl p-8 shadow-sm">
            <h2 className="text-2xl font-bold mb-8">{data.email?.label || 'Personal Information'}</h2>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div className="space-y-2">
                <label className="text-xs font-bold text-neutral-400 uppercase ml-1">Email</label>
                <input type="email" placeholder={data.email?.placeholder || "john@example.com"} className="w-full border-neutral-200 rounded-2xl py-4 px-6 focus:border-blue-500 transition-colors" />
              </div>
              <div className="space-y-2">
                <label className="text-xs font-bold text-neutral-400 uppercase ml-1">Phone</label>
                <input type="tel" placeholder={data.shipping?.fields?.phone || "+1 (555) 000-0000"} className="w-full border-neutral-200 rounded-2xl py-4 px-6 focus:border-blue-500 transition-colors" />
              </div>
            </div>
          </div>

          <div className="bg-white rounded-3xl p-8 shadow-sm">
            <h2 className="text-2xl font-bold mb-8">{data.shipping?.title || 'Shipping Address'}</h2>
            <div className="space-y-6">
              <input type="text" placeholder={data.shipping?.fields?.firstName || "Full Name"} className="w-full border-neutral-200 rounded-2xl py-4 px-6 focus:border-blue-500 transition-colors" />
              <input type="text" placeholder={data.shipping?.fields?.address || "Street Address"} className="w-full border-neutral-200 rounded-2xl py-4 px-6 focus:border-blue-500 transition-colors" />
              <div className="grid grid-cols-3 gap-6">
                <input type="text" placeholder={data.shipping?.fields?.city || "City"} className="border-neutral-200 rounded-2xl py-4 px-6 focus:border-blue-500 transition-colors" />
                <input type="text" placeholder={data.shipping?.fields?.state || "State"} className="border-neutral-200 rounded-2xl py-4 px-6 focus:border-blue-500 transition-colors" />
                <input type="text" placeholder={data.shipping?.fields?.zip || "ZIP"} className="border-neutral-200 rounded-2xl py-4 px-6 focus:border-blue-500 transition-colors" />
              </div>
            </div>
          </div>
        </div>

        <div className="w-full lg:w-[400px] space-y-6">
          <div className="bg-white rounded-3xl p-8 shadow-sm">
            <h2 className="text-xl font-bold mb-6">{data.payment?.title || 'Payment'}</h2>
            <div className="grid grid-cols-2 gap-4 mb-8">
              {(data.payment?.methods || [
                { id: 'card', title: 'Card', active: true },
                { id: 'paypal', title: 'PayPal' }
              ]).map((method) => (
                <button key={method.id} className={clsx(
                  "flex flex-col items-center justify-center p-4 border-2 rounded-2xl transition-all",
                  method.active ? "border-blue-600 bg-blue-50 text-blue-600" : "border-transparent bg-neutral-50 text-neutral-400 hover:bg-neutral-100"
                )}>
                   <span className="font-bold">{method.title}</span>
                </button>
              ))}
            </div>
            <div className="space-y-4">
              <input type="text" placeholder="Card Number" className="w-full border-neutral-200 rounded-2xl py-4 px-6" />
              <div className="grid grid-cols-2 gap-4">
                <input type="text" placeholder="MM/YY" className="border-neutral-200 rounded-2xl py-4 px-6" />
                <input type="text" placeholder="CVC" className="border-neutral-200 rounded-2xl py-4 px-6" />
              </div>
            </div>
          </div>

          <div className="bg-blue-600 text-white rounded-3xl p-8 shadow-xl">
            <h2 className="text-xl font-bold mb-6">{data.summary?.title || 'Order Total'}</h2>
            <div className="space-y-3 mb-8 opacity-90">
               {cartItems.map((item, idx) => (
                 <div key={idx} className="flex justify-between text-sm">
                   <span>{item.name}</span>
                   <span className="font-bold">${item.price.toFixed(2)}</span>
                 </div>
               ))}
            </div>
            <div className="flex justify-between items-end border-t border-white/20 pt-6">
               <span className="text-sm font-medium opacity-70">{data.summary?.totalLabel || 'Payable Amount'}</span>
               <span className="text-4xl font-black tracking-tighter">${subtotal.toFixed(2)}</span>
            </div>
            <button className="w-full bg-white text-blue-600 py-5 rounded-2xl mt-10 font-bold text-lg hover:shadow-lg transition-all active:scale-95">
              {data.summary?.buttonText || 'Complete Payment'}
            </button>
          </div>
        </div>

      </div>
    </div>
  );
};
