import { useState, useEffect } from 'react';
import clsx from 'clsx';
import { BlogCard } from '../BlogCard';
import '../../styles/blog.css';

/**
 * BlogGrid Component - Responsive grid layout for blog cards
 * @param {Object} props - Component props
 * @param {Object} props.data - Blog grid configuration data
 * @param {Array} props.data.posts - Array of blog post data objects
 * @param {string} [props.data.title] - Section title
 * @param {string} [props.data.subtitle] - Section subtitle
 * @param {string} [props.data.description] - Section description
 * @param {string} [props.data.variant='default'] - Card variant (passed to BlogCard)
 * @param {string} [props.data.layout='grid'] - Layout type: 'grid' | 'featured-asymmetric'
 * @param {string} [props.data.theme='light'] - Theme: 'light' | 'dark'
 * @param {number} [props.data.columns=3] - Number of columns (1-4)
 * @param {string} [props.data.alignment='left'] - Text alignment: 'left' | 'center' | 'right'
 * @param {string} [props.data.gap='medium'] - Gap between cards: 'small' | 'medium' | 'large'
 * @param {boolean} [props.data.loader=false] - Show loading state
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute
 */
export const BlogGrid = ({ data, className, id, loader = false }) => {
  // Safety check for data
  if (!data || typeof data !== 'object') {
    console.error('BlogGrid: data prop is required and must be an object');
    return null;
  }

  const [showLoader, setShowLoader] = useState(loader);

  // Auto-hide loader after 2 seconds when loader prop is true
  useEffect(() => {
    if (loader) {
      const timer = setTimeout(() => {
        setShowLoader(false);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowLoader(false);
    }
  }, [loader]);

  const {
    posts = [],
    title,
    subtitle,
    description,
    variant = 'default',
    layout = 'grid',
    theme = 'light',
    columns = 3,
    alignment = 'left',
    gap = 'medium',
    className: dataClassName,
  } = data;

  const alignmentClasses = {
    left: 'text-left',
    center: 'text-center',
    right: 'text-right',
  };

  const gapClasses = {
    small: 'gap-4',
    medium: 'gap-6',
    large: 'gap-8',
  };

  const columnClasses = {
    1: 'grid-cols-1',
    2: 'grid-cols-1 md:grid-cols-2',
    3: 'grid-cols-1 md:grid-cols-2 lg:grid-cols-3',
    4: 'grid-cols-1 md:grid-cols-2 lg:grid-cols-4',
  };

  const sectionClasses = clsx(
    'blog-grid-section',
    `blog-grid-${theme}`,
    alignmentClasses[alignment],
    className,
    dataClassName
  );

  const gridClasses = clsx(
    'blog-grid',
    layout === 'featured-asymmetric' ? 'blog-grid-featured-asymmetric' : '',
    layout === 'featured-asymmetric' ? '' : (columnClasses[columns] || columnClasses[3]),
    gapClasses[gap] || gapClasses.medium
  );

  // Featured asymmetric layout: 1 large card left, 2x2 small cards right
  if (layout === 'featured-asymmetric') {
    if (showLoader) {
      // Show skeleton loaders for featured-asymmetric layout
      return (
        <section className={sectionClasses} id={id}>
          {(title || subtitle || description) && (
            <div className="blog-grid-header">
              {subtitle && (
                <div className="blog-grid-subtitle-skeleton"></div>
              )}
              {title && (
                <div className="blog-grid-title-skeleton"></div>
              )}
              {description && (
                <div className="blog-grid-description-skeleton"></div>
              )}
            </div>
          )}
          <div className={gridClasses}>
            {/* Large featured card skeleton */}
            <div className="blog-grid-featured-large">
              <BlogCard
                data={{
                  title: '',
                  variant: 'featured-gradient',
                  theme: theme,
                }}
                loader={true}
              />
            </div>
            
            {/* 2x2 grid of small card skeletons */}
            <div className="blog-grid-featured-small">
              {Array.from({ length: 4 }).map((_, index) => (
                <BlogCard
                  key={`skeleton-small-${index}`}
                  data={{
                    title: '',
                    variant: 'featured-gradient',
                    theme: theme,
                  }}
                  loader={true}
                />
              ))}
            </div>
          </div>
        </section>
      );
    }
    
    // Show actual posts for featured-asymmetric layout
    const featuredPost = posts[0];
    const smallPosts = posts.slice(1, 5); // Get next 4 posts for 2x2 grid
    
    return (
      <section className={sectionClasses} id={id}>
        {(title || subtitle || description) && (
          <div className="blog-grid-header">
            {subtitle && (
              <p className="blog-grid-subtitle">{subtitle}</p>
            )}
            {title && (
              <h2 className="blog-grid-title">{title}</h2>
            )}
            {description && (
              <p className="blog-grid-description">{description}</p>
            )}
          </div>
        )}
        <div className={gridClasses}>
          {/* Large featured card */}
          {featuredPost && (
            <div className="blog-grid-featured-large">
              <BlogCard
                data={{
                  ...featuredPost,
                  variant: 'featured-gradient',
                  theme: featuredPost.theme || theme,
                }}
              />
            </div>
          )}
          
          {/* 2x2 grid of small cards */}
          <div className="blog-grid-featured-small">
            {smallPosts.map((post, index) => (
              <BlogCard
                key={post.id || index}
                data={{
                  ...post,
                  variant: 'featured-gradient',
                  theme: post.theme || theme,
                }}
              />
            ))}
          </div>
        </div>
      </section>
    );
  }

  return (
    <section className={sectionClasses} id={id}>
      {(title || subtitle || description) && (
        <div className="blog-grid-header">
                  {showLoader ? (
            // Show skeleton loaders for header
            <>
              {subtitle && (
                <div className="blog-grid-subtitle-skeleton"></div>
              )}
              {title && (
                <div className="blog-grid-title-skeleton"></div>
              )}
              {description && (
                <div className="blog-grid-description-skeleton"></div>
              )}
            </>
          ) : (
            // Show actual header content
            <>
              {subtitle && (
                <p className="blog-grid-subtitle">{subtitle}</p>
              )}
              {title && (
                <h2 className="blog-grid-title">{title}</h2>
              )}
              {description && (
                <p className="blog-grid-description">{description}</p>
              )}
            </>
          )}
        </div>
      )}
      <div className={gridClasses}>
                  {showLoader ? (
          // Show skeleton loaders
          Array.from({ length: columns || 3 }).map((_, index) => (
            <BlogCard
              key={`skeleton-${index}`}
              data={{ title: '', variant, theme }}
              loader={true}
            />
          ))
        ) : (
          // Show actual posts
          posts.map((post, index) => (
            <BlogCard
              key={post.id || index}
              data={{
                ...post,
                variant: post.variant || variant,
                theme: post.theme || theme,
              }}
            />
          ))
        )}
      </div>
    </section>
  );
};