import React, { useState, useEffect } from 'react';
import { clsx } from 'clsx';
import { CheckoutSkeleton } from '../CheckoutSkeleton';

export const CheckoutMinimal = ({ data = {}, className, loading = false }) => {
  const [email, setEmail] = useState('');
  const [timedOut, setTimedOut] = useState(false);

  // Auto-show content after 2 seconds if loading is true
  useEffect(() => {
    if (loading) {
      setTimedOut(false);
      const timer = setTimeout(() => {
        setTimedOut(true);
      }, 2000);
      return () => clearTimeout(timer);
    }
  }, [loading]);

  const shouldShowSkeleton = loading && !timedOut;

  const cartItems = data.cartItems || [];
  const subtotal = cartItems.reduce((acc, item) => acc + item.price, 0);

  // Show skeleton loading state
  if (shouldShowSkeleton) {
    return <CheckoutSkeleton data={{ variant: 'minimal' }} className={className} />;
  }

  return (
    <div className={clsx("checkout-minimal bg-white py-12 px-4 sm:px-6 lg:px-8", className)}>
      <div className="max-w-4xl mx-auto">
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-x-12 gap-y-10">
          
          <div className="space-y-8">
            <section>
              <h2 className="text-xl font-semibold mb-4 border-b pb-2">{data.email?.label || 'Contact'}</h2>
              <input
                type="email"
                placeholder={data.email?.placeholder || 'Email Address'}
                className="w-full border-gray-300 rounded-md shadow-sm focus:ring-black focus:border-black py-3 px-4"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
              />
            </section>

            <section>
              <h2 className="text-xl font-semibold mb-4 border-b pb-2">{data.shipping?.title || 'Shipping'}</h2>
              <div className="grid grid-cols-2 gap-4">
                <input type="text" placeholder={data.shipping?.fields?.firstName || "First Name"} className="border-gray-300 rounded-md py-3 px-4" />
                <input type="text" placeholder={data.shipping?.fields?.lastName || "Last Name"} className="border-gray-300 rounded-md py-3 px-4" />
              </div>
              <input type="text" placeholder={data.shipping?.fields?.address || "Address"} className="w-full mt-4 border-gray-300 rounded-md py-3 px-4" />
              <div className="grid grid-cols-3 gap-4 mt-4">
                <input type="text" placeholder={data.shipping?.fields?.city || "City"} className="border-gray-300 rounded-md py-3 px-4" />
                <input type="text" placeholder={data.shipping?.fields?.state || "State"} className="border-gray-300 rounded-md py-3 px-4" />
                <input type="text" placeholder={data.shipping?.fields?.zip || "ZIP"} className="border-gray-300 rounded-md py-3 px-4" />
              </div>
            </section>

            <section>
              <h2 className="text-xl font-semibold mb-4 border-b pb-2">{data.payment?.title || 'Payment'}</h2>
              <div className="space-y-4">
                {(data.payment?.methods || [
                  { id: 'card', title: 'Credit Card' },
                  { id: 'paypal', title: 'PayPal' }
                ]).map((method, idx) => (
                  <label key={method.id} className="flex items-center p-4 border rounded-md cursor-pointer hover:bg-gray-50 transition-colors">
                    <input type="radio" name="payment" className="text-black focus:ring-black" defaultChecked={idx === 0} />
                    <span className="ml-3 font-medium">{method.title}</span>
                  </label>
                ))}
              </div>
            </section>
          </div>

          <div className="bg-gray-50 p-6 rounded-lg self-start">
            <h2 className="text-xl font-semibold mb-6">{data.summary?.title || 'Order Summary'}</h2>
            <div className="space-y-4">
              {cartItems.map((item, idx) => (
                <div key={idx} className="flex justify-between text-sm">
                  <span className="text-gray-600">{item.name} x1</span>
                  <span className="font-medium">${item.price.toFixed(2)}</span>
                </div>
              ))}
              <div className="border-t pt-4 mt-4 flex justify-between font-bold text-lg">
                <span>{data.summary?.totalLabel || 'Total'}</span>
                <span>${subtotal.toFixed(2)}</span>
              </div>
            </div>
            <button className="w-full bg-black text-white py-4 rounded-md mt-8 font-semibold hover:bg-gray-800 transition-colors">
              {data.summary?.buttonText || 'Place Order'}
            </button>
          </div>

        </div>
      </div>
    </div>
  );
};
