import { useState, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/product.css';

/**
 * ProductQuickView Component - Quick view modal with multiple variants
 * @param {Object} props - Component props
 * @param {Object} props.data - Quick view configuration data
 * @param {string} [props.data.variant='default'] - Style variant: 'default' | 'modern' | 'minimal' | 'compact' | 'icon' | 'dark' | 'dark-gray'
 * @param {Object} [props.data.product] - Product data object
 * @param {Function} [props.onClose] - Callback when modal closes
 * @param {boolean} [props.loading=false] - Show skeleton loader
 * @param {string} [props.className] - Additional CSS classes
 */
export const ProductQuickView = ({ 
  data = {}, 
  onClose,
  loading = false,
  className 
}) => {
  const {
    variant = 'default',
    product = {
      title: 'Product Name',
      price: 99.99,
      image: 'https://images.unsplash.com/photo-1505740420928-5e560c06d30e?w=400&q=80',
      description: 'Product description here',
    },
  } = data;

  const [isOpen, setIsOpen] = useState(false);
  const [showContent, setShowContent] = useState(!loading);

  useEffect(() => {
    if (loading) {
      setShowContent(false);
      const timer = setTimeout(() => {
        setShowContent(true);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowContent(true);
    }
  }, [loading]);

  useEffect(() => {
    const handleEscape = (e) => {
      if (e.key === 'Escape' && isOpen) {
        handleClose();
      }
    };
    if (isOpen) {
      document.addEventListener('keydown', handleEscape);
      document.body.style.overflow = 'hidden';
    }
    return () => {
      document.removeEventListener('keydown', handleEscape);
      document.body.style.overflow = 'unset';
    };
  }, [isOpen]);

  const handleClose = () => {
    setIsOpen(false);
    if (onClose) {
      onClose();
    }
  };

  const buttonClasses = {
    default: clsx(
      'px-3 sm:px-4 py-2 sm:py-2.5 text-sm sm:text-base rounded-lg',
      'bg-primary-600 hover:bg-primary-700 text-white',
      'flex items-center justify-center gap-2',
      'transition-all duration-200 focus:outline-none focus:ring-2 focus:ring-primary-500'
    ),
    modern: clsx(
      'px-4 sm:px-5 py-2.5 sm:py-3 text-sm sm:text-base rounded-xl font-medium',
      'bg-primary-600 hover:bg-primary-700 text-white shadow-md hover:shadow-lg',
      'flex items-center justify-center gap-2',
      'transition-all duration-300 focus:outline-none focus:ring-2 focus:ring-primary-500'
    ),
    minimal: clsx(
      'px-3 sm:px-4 py-2 text-sm border-b-2 border-primary-600',
      'bg-transparent text-primary-600 hover:text-primary-700',
      'flex items-center justify-center gap-2',
      'transition-all duration-200 focus:outline-none'
    ),
    compact: clsx(
      'px-2 sm:px-3 py-1.5 text-xs sm:text-sm rounded-md',
      'bg-primary-600 hover:bg-primary-700 text-white',
      'flex items-center justify-center gap-1.5',
      'transition-all duration-200 focus:outline-none focus:ring-1 focus:ring-primary-500'
    ),
    icon: clsx(
      'p-2 sm:p-2.5 rounded-lg',
      'bg-primary-500 hover:bg-primary-600 text-white shadow-md hover:shadow-lg',
      'flex items-center justify-center',
      'transition-all duration-300 focus:outline-none focus:ring-2 focus:ring-primary-500 focus:ring-offset-2'
    ),
    dark: clsx(
      'px-3 sm:px-4 py-2 sm:py-2.5 text-sm sm:text-base rounded-lg',
      'bg-primary-500 hover:bg-primary-600 text-white',
      'flex items-center justify-center gap-2',
      'transition-all duration-200 focus:outline-none focus:ring-2 focus:ring-primary-400'
    ),
    'dark-gray': clsx(
      'px-3 sm:px-4 py-2 sm:py-2.5 text-sm sm:text-base rounded-lg',
      'bg-gray-700 hover:bg-gray-600 text-white',
      'flex items-center justify-center gap-2',
      'transition-all duration-200 focus:outline-none focus:ring-2 focus:ring-gray-500'
    ),
  };

  const modalClasses = clsx(
    'fixed inset-0 z-50 overflow-y-auto transition-all duration-300',
    isOpen ? 'opacity-100 visible' : 'opacity-0 invisible pointer-events-none'
  );

  const SkeletonLoader = () => {
    const isDark = variant === 'dark' || variant === 'dark-gray';
    const skeletonBg = isDark ? 'bg-gray-700' : 'bg-gray-200';

    return (
      <button className={clsx(buttonClasses[variant] || buttonClasses.default, className)}>
        {variant === 'icon' ? (
          <div className={clsx('w-5 h-5 rounded product-skeleton', skeletonBg)}></div>
        ) : (
          <div className={clsx('h-4 w-20 rounded product-skeleton', skeletonBg)}></div>
        )}
      </button>
    );
  };

  if (!showContent) {
    return <SkeletonLoader />;
  }

  const EyeIcon = () => (
    <svg className="w-4 h-4 sm:w-5 sm:h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 12a3 3 0 11-6 0 3 3 0 016 0z" />
      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M2.458 12C3.732 7.943 7.523 5 12 5c4.478 0 8.268 2.943 9.542 7-1.274 4.057-5.064 7-9.542 7-4.477 0-8.268-2.943-9.542-7z" />
    </svg>
  );

  return (
    <>
      <button
        type="button"
        onClick={() => setIsOpen(true)}
        className={clsx(buttonClasses[variant] || buttonClasses.default, className)}
      >
        {variant === 'icon' ? (
          <EyeIcon />
        ) : (
          <>
            {variant !== 'minimal' && (
              <span className="flex-shrink-0">
                <EyeIcon />
              </span>
            )}
            <span className="whitespace-nowrap">Quick View</span>
          </>
        )}
      </button>

      {isOpen && (
        <div className={modalClasses}>
          <div className="fixed inset-0 bg-black bg-opacity-50 transition-opacity product-quickview-overlay" onClick={handleClose}></div>
          <div className="flex min-h-full items-center justify-center p-4">
            <div className={clsx(
              'relative bg-white rounded-lg shadow-xl max-w-4xl w-full max-h-[90vh] overflow-auto product-quickview-modal',
              variant === 'modern' && 'rounded-2xl',
              variant === 'dark' && 'bg-gray-800',
              variant === 'dark-gray' && 'bg-gray-900'
            )}>
              <button
                onClick={handleClose}
                className="absolute top-4 right-4 text-gray-400 hover:text-gray-600 z-10"
              >
                <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                </svg>
              </button>
              <div className="p-6 sm:p-8">
                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                  <div>
                    <img src={product.image} alt={product.title} className="w-full h-auto rounded-lg" />
                  </div>
                  <div>
                    <h2 className={clsx(
                      'text-2xl sm:text-3xl font-bold mb-4',
                      variant === 'dark' || variant === 'dark-gray' ? 'text-white' : 'text-gray-900'
                    )}>{product.title}</h2>
                    <p className={clsx(
                      'text-xl sm:text-2xl font-bold mb-4',
                      variant === 'dark' || variant === 'dark-gray' ? 'text-white' : 'text-gray-900'
                    )}>${product.price}</p>
                    <p className={clsx(
                      'text-sm sm:text-base mb-6',
                      variant === 'dark' || variant === 'dark-gray' ? 'text-gray-300' : 'text-gray-600'
                    )}>{product.description}</p>
                    <div className="flex gap-3">
                      <button className="px-6 py-3 bg-primary-600 text-white rounded-lg hover:bg-primary-700">
                        Add to Cart
                      </button>
                      <button className="px-6 py-3 border border-gray-300 rounded-lg hover:bg-gray-50">
                        View Details
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </>
  );
};

