import { useState, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/product.css';

/**
 * ProductSearch Component - Search input for products with multiple variants
 * @param {Object} props - Component props
 * @param {Object} props.data - Search configuration data
 * @param {string} [props.data.variant='default'] - Style variant: 'default' | 'modern' | 'minimal' | 'compact' | 'icon' | 'dark' | 'dark-gray'
 * @param {string} [props.data.placeholder='Search products...'] - Placeholder text
 * @param {string} [props.data.value] - Initial search value
 * @param {boolean} [props.data.showIcon=true] - Show search icon
 * @param {boolean} [props.data.showClearButton=true] - Show clear button
 * @param {Function} [props.onSearch] - Callback when search changes
 * @param {Function} [props.onSubmit] - Callback when form is submitted
 * @param {boolean} [props.loading=false] - Show skeleton loader
 * @param {string} [props.className] - Additional CSS classes
 */
export const ProductSearch = ({ 
  data = {}, 
  onSearch, 
  onSubmit,
  loading = false,
  className 
}) => {
  const {
    variant = 'default',
    placeholder = 'Search products...',
    value: initialValue = '',
    showIcon = true,
    showClearButton = true,
  } = data;

  const [searchValue, setSearchValue] = useState(initialValue);
  const [isExpanded, setIsExpanded] = useState(variant !== 'icon' || !!initialValue);
  const [showContent, setShowContent] = useState(!loading);

  // Show skeleton for 2 seconds when loading is true
  useEffect(() => {
    if (loading) {
      setShowContent(false);
      const timer = setTimeout(() => {
        setShowContent(true);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowContent(true);
    }
  }, [loading]);

  const handleChange = (e) => {
    const newValue = e.target.value;
    setSearchValue(newValue);
    if (onSearch) {
      onSearch(newValue);
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (onSubmit) {
      onSubmit(searchValue);
    }
    if (variant === 'icon' && searchValue) {
      setIsExpanded(false);
    }
  };

  const handleClear = () => {
    setSearchValue('');
    if (onSearch) {
      onSearch('');
    }
    if (variant === 'icon') {
      setIsExpanded(false);
    }
  };

  const handleIconClick = () => {
    if (variant === 'icon' && !isExpanded) {
      setIsExpanded(true);
    }
  };

  // Container classes
  const containerClasses = {
    default: 'relative w-full',
    modern: 'relative w-full',
    minimal: 'relative w-full',
    compact: 'relative w-full',
    icon: clsx('relative transition-all duration-300 ease-in-out', isExpanded ? 'w-full' : 'w-auto'),
    dark: 'relative w-full',
    'dark-gray': 'relative w-full',
  };

  // Input classes - responsive with mobile-first approach
  const inputClasses = {
    default: clsx(
      'w-full py-2 sm:py-2.5 text-sm sm:text-base',
      'pl-8 sm:pl-10 pr-8 sm:pr-10',
      'border border-gray-300 rounded-lg bg-white',
      'focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-primary-500',
      'transition-all duration-200',
      'placeholder:text-gray-400 text-gray-900'
    ),
    modern: clsx(
      'w-full py-2.5 sm:py-3.5 text-sm sm:text-base',
      'pl-10 sm:pl-12 pr-10 sm:pr-12',
      'border-2 border-gray-200 rounded-xl bg-white',
      'focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-primary-500',
      'shadow-sm hover:shadow-md',
      'transition-all duration-300',
      'placeholder:text-gray-400 text-gray-900'
    ),
    minimal: clsx(
      'w-full py-2 sm:py-2.5 text-sm sm:text-base',
      showIcon ? 'pl-6 sm:pl-8' : 'pl-0',
      'pr-4',
      'border-b-2 border-gray-300 bg-transparent',
      'focus:outline-none focus:border-primary-500',
      'transition-all duration-200',
      'placeholder:text-gray-400 text-gray-900 rounded-none'
    ),
    compact: clsx(
      'w-full py-1.5 sm:py-2 text-xs sm:text-sm',
      'pl-7 sm:pl-8 pr-7 sm:pr-8',
      'border border-gray-300 rounded-md bg-white',
      'focus:outline-none focus:ring-1 focus:ring-primary-500 focus:border-primary-500',
      'transition-all duration-200',
      'placeholder:text-gray-400 text-gray-900'
    ),
    icon: clsx(
      'w-full py-2 sm:py-2.5 text-sm sm:text-base',
      'pl-8 sm:pl-10 pr-8 sm:pr-10',
      'border border-gray-300 rounded-lg bg-white',
      'focus:outline-none focus:ring-2 focus:ring-primary-500 focus:border-primary-500',
      'transition-all duration-300',
      'placeholder:text-gray-400 text-gray-900',
      isExpanded 
        ? 'opacity-100 translate-x-0 relative' 
        : 'opacity-0 translate-x-4 pointer-events-none absolute left-0 top-0'
    ),
    dark: clsx(
      'w-full py-2 sm:py-2.5 text-sm sm:text-base',
      'pl-8 sm:pl-10 pr-8 sm:pr-10',
      'border border-gray-600 rounded-lg',
      'bg-gray-800 text-white',
      'focus:outline-none focus:ring-2 focus:ring-primary-400 focus:border-primary-400',
      'transition-all duration-200',
      'placeholder:text-gray-400'
    ),
    'dark-gray': clsx(
      'w-full py-2 sm:py-2.5 text-sm sm:text-base',
      'pl-8 sm:pl-10 pr-8 sm:pr-10',
      'border border-gray-700 rounded-lg',
      'bg-gray-900 text-gray-100',
      'focus:outline-none focus:ring-2 focus:ring-gray-500 focus:border-gray-500',
      'transition-all duration-200',
      'placeholder:text-gray-500'
    ),
  };

  // Icon wrapper classes
  const iconWrapperClasses = {
    default: 'absolute left-3 sm:left-3.5 top-1/2 transform -translate-y-1/2 pointer-events-none z-10',
    modern: 'absolute left-4 sm:left-5 top-1/2 transform -translate-y-1/2 pointer-events-none z-10',
    minimal: 'absolute left-0 sm:left-1 top-1/2 transform -translate-y-1/2 pointer-events-none z-10',
    compact: 'absolute left-2.5 sm:left-3 top-1/2 transform -translate-y-1/2 pointer-events-none z-10',
    icon: clsx(
      'absolute left-3 sm:left-3.5 top-1/2 transform -translate-y-1/2 pointer-events-none z-10',
      'transition-all duration-300',
      isExpanded ? 'opacity-100' : 'opacity-0'
    ),
    dark: 'absolute left-3 sm:left-3.5 top-1/2 transform -translate-y-1/2 pointer-events-none z-10',
    'dark-gray': 'absolute left-3 sm:left-3.5 top-1/2 transform -translate-y-1/2 pointer-events-none z-10',
  };

  // Icon size classes
  const iconSizeClasses = {
    default: 'w-4 h-4 sm:w-5 sm:h-5',
    modern: 'w-4 h-4 sm:w-5 sm:h-5',
    minimal: 'w-4 h-4 sm:w-5 sm:h-5',
    compact: 'w-3.5 h-3.5 sm:w-4 sm:h-4',
    icon: 'w-5 h-5 sm:w-6 sm:h-6',
    dark: 'w-4 h-4 sm:w-5 sm:h-5',
    'dark-gray': 'w-4 h-4 sm:w-5 sm:h-5',
  };

  // Icon color classes
  const iconColorClasses = {
    default: 'text-gray-400',
    modern: 'text-gray-500',
    minimal: 'text-gray-400',
    compact: 'text-gray-400',
    icon: clsx(
      'text-gray-400 transition-colors',
      isExpanded ? 'text-gray-400' : 'text-primary-500'
    ),
    dark: 'text-gray-400',
    'dark-gray': 'text-gray-500',
  };

  // Clear button classes
  const clearButtonClasses = {
    default: 'absolute right-3 sm:right-3.5 top-1/2 transform -translate-y-1/2 z-10',
    modern: 'absolute right-4 sm:right-5 top-1/2 transform -translate-y-1/2 z-10',
    minimal: 'absolute right-0 top-1/2 transform -translate-y-1/2 z-10',
    compact: 'absolute right-2.5 sm:right-3 top-1/2 transform -translate-y-1/2 z-10',
    icon: 'absolute right-3 sm:right-3.5 top-1/2 transform -translate-y-1/2 z-10',
    dark: 'absolute right-3 sm:right-3.5 top-1/2 transform -translate-y-1/2 z-10',
    'dark-gray': 'absolute right-3 sm:right-3.5 top-1/2 transform -translate-y-1/2 z-10',
  };

  // Clear button color classes
  const clearButtonColorClasses = {
    default: 'text-gray-400 hover:text-gray-600',
    modern: 'text-gray-400 hover:text-gray-600',
    minimal: 'text-gray-400 hover:text-gray-600',
    compact: 'text-gray-400 hover:text-gray-600',
    icon: 'text-gray-400 hover:text-gray-600',
    dark: 'text-gray-400 hover:text-gray-300',
    'dark-gray': 'text-gray-500 hover:text-gray-300',
  };

  // Icon button (for icon variant)
  const iconButtonClasses = clsx(
    'flex items-center justify-center',
    'w-10 h-10 sm:w-12 sm:h-12',
    'rounded-lg',
    'bg-primary-500 hover:bg-primary-600',
    'text-white shadow-md hover:shadow-lg',
    'transition-all duration-300',
    'focus:outline-none focus:ring-2 focus:ring-primary-500 focus:ring-offset-2'
  );

  // Skeleton Loader Component
  const SkeletonLoader = () => {
    const isDark = variant === 'dark' || variant === 'dark-gray';
    const skeletonBg = isDark ? 'bg-gray-700' : 'bg-gray-200';
    const skeletonBorder = isDark ? 'border-gray-600' : 'border-gray-300';

    if (variant === 'icon' && !isExpanded) {
      return (
        <div className={clsx(containerClasses[variant] || containerClasses.default, className)}>
          <div className={clsx(
            'w-10 h-10 sm:w-12 sm:h-12 rounded-lg product-skeleton',
            skeletonBg
          )}></div>
        </div>
      );
    }

    return (
      <div className={clsx(containerClasses[variant] || containerClasses.default, className)}>
        <div className="relative w-full">
          {showIcon && (
            <div className={clsx(
              iconWrapperClasses[variant] || iconWrapperClasses.default,
              'w-4 h-4 sm:w-5 sm:h-5 rounded product-skeleton',
              skeletonBg
            )}></div>
          )}
          <div className={clsx(
            inputClasses[variant] || inputClasses.default,
            'product-skeleton border-0',
            skeletonBg
          )}></div>
        </div>
      </div>
    );
  };

  // Show skeleton loader if content is not ready
  if (!showContent) {
    return <SkeletonLoader />;
  }

  return (
    <form 
      onSubmit={handleSubmit} 
      className={clsx(containerClasses[variant] || containerClasses.default, className)}
    >
      <div className="relative">
    
        {/* Icon Button for Icon Variant */}
        {variant === 'icon' && !isExpanded && (
          <button
            type="button"
            onClick={handleIconClick}
            className={iconButtonClasses}
            aria-label="Open search"
          >
            <svg
              className="w-5 h-5 sm:w-6 sm:h-6"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
              />
            </svg>
          </button>
        )}
   

        {/* Search Icon */}
        {showIcon && (variant !== 'icon' || isExpanded) && (
          <div className={iconWrapperClasses[variant] || iconWrapperClasses.default}>
            <svg
              className={clsx(
                iconSizeClasses[variant] || iconSizeClasses.default,
                iconColorClasses[variant] || iconColorClasses.default
              )}
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
              />
            </svg>
          </div>
        )}
        
        {/* Input Field */}
        <input
          type="text"
          value={searchValue}
          onChange={handleChange}
          placeholder={placeholder}
          className={clsx(
            inputClasses[variant] || inputClasses.default,
            // Adjust padding when icon is hidden
            !showIcon && variant === 'default' && 'pl-3 sm:pl-4',
            !showIcon && variant === 'modern' && 'pl-3 sm:pl-5',
            !showIcon && variant === 'compact' && 'pl-2.5 sm:pl-3',
            !showIcon && variant === 'icon' && 'pl-3 sm:pl-4',
            !showIcon && variant === 'dark' && 'pl-3 sm:pl-4',
            !showIcon && variant === 'dark-gray' && 'pl-3 sm:pl-4'
          )}
          onBlur={() => {
            if (variant === 'icon' && !searchValue) {
              setTimeout(() => setIsExpanded(false), 200);
            }
          }}
          autoFocus={variant === 'icon' && isExpanded}
        />

        {/* Clear Button */}
        {showClearButton && searchValue && (variant !== 'icon' || isExpanded) && (
          <button
            type="button"
            onClick={handleClear}
            className={clsx(
              clearButtonClasses[variant] || clearButtonClasses.default,
              'transition-colors duration-200',
              'focus:outline-none focus:ring-2 focus:ring-offset-1 rounded',
              clearButtonColorClasses[variant] || clearButtonColorClasses.default
            )}
            aria-label="Clear search"
          >
            <svg
              className={clsx(
                iconSizeClasses[variant] || iconSizeClasses.default
              )}
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M6 18L18 6M6 6l12 12"
              />
            </svg>
          </button>
        )}
      </div>
    </form>
  );
};

