import { useState } from 'react';
import clsx from 'clsx';
import '../../styles/product.css';

/**
 * ProductActions Component
 * @param {Object} props - Component props
 * @param {Object} props.data - Product actions configuration data
 * @param {Array} props.data.buttons - Array of action buttons
 * @param {boolean} [props.data.showQuantity=true] - Show quantity selector
 * @param {number} [props.data.minQuantity=1] - Minimum quantity
 * @param {number} [props.data.maxQuantity=99] - Maximum quantity
 * @param {boolean} [props.data.inStock=true] - Stock availability
 * @param {string} [props.data.variant='default'] - Style variant: 'default' | 'minimal' | 'stacked' | 'inline'
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute for the section element
 */
export const ProductActions = ({ data, className, id }) => {
  // Safety check for data
  if (!data || typeof data !== 'object') {
    console.error('ProductActions: data prop is required and must be an object');
    return null;
  }

  const [quantity, setQuantity] = useState(1);
  const [isWishlisted, setIsWishlisted] = useState(false);

  const {
    buttons = [
      { text: "Add to Cart", href: "#", variant: "primary", type: "cart" },
      { text: "Buy Now", href: "#", variant: "secondary", type: "purchase" }
    ],
    showQuantity = true,
    minQuantity = 1,
    maxQuantity = 99,
    inStock = true,
    variant = 'default'
  } = data;

  const handleQuantityChange = (newQuantity) => {
    const clampedQuantity = Math.max(minQuantity, Math.min(maxQuantity, newQuantity));
    setQuantity(clampedQuantity);
  };

  const handleButtonClick = (button, event) => {
    if (button.onClick) {
      button.onClick(event, { quantity, inStock });
    }
  };

  // Default variant - Standard layout with quantity and buttons
  if (variant === 'default') {
    return (
      <section
        id={id}
        className={clsx(
          "product-actions-default",
          className
        )}
      >
        <div className="space-y-6">
          {/* Quantity Selector and Wishlist */}
          <div className="flex items-center justify-between">
            {showQuantity && (
              <div className="flex items-center space-x-4">
                <label htmlFor="quantity" className="text-sm font-medium text-gray-700">
                  Quantity:
                </label>
                <div className="flex items-center border border-gray-300 rounded-md">
                  <button
                    type="button"
                    onClick={() => handleQuantityChange(quantity - 1)}
                    disabled={quantity <= minQuantity}
                    className="p-2 text-gray-600 hover:text-gray-800 hover:bg-gray-50 disabled:opacity-50 disabled:cursor-not-allowed"
                    aria-label="Decrease quantity"
                  >
                    <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M20 12H4" />
                    </svg>
                  </button>

                  <input
                    id="quantity"
                    type="number"
                    min={minQuantity}
                    max={maxQuantity}
                    value={quantity}
                    onChange={(e) => handleQuantityChange(parseInt(e.target.value) || minQuantity)}
                    className="w-16 text-center border-0 focus:ring-0 text-sm font-medium"
                    aria-label="Quantity"
                  />

                  <button
                    type="button"
                    onClick={() => handleQuantityChange(quantity + 1)}
                    disabled={quantity >= maxQuantity}
                    className="p-2 text-gray-600 hover:text-gray-800 hover:bg-gray-50 disabled:opacity-50 disabled:cursor-not-allowed"
                    aria-label="Increase quantity"
                  >
                    <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v16m8-8H4" />
                    </svg>
                  </button>
                </div>
              </div>
            )}

            {/* Wishlist Button */}
            <button
              type="button"
              onClick={() => setIsWishlisted(!isWishlisted)}
              className={clsx(
                "p-3 rounded-full border-2 transition-all duration-200",
                isWishlisted
                  ? "border-red-200 bg-red-50 text-red-600"
                  : "border-gray-200 text-gray-400 hover:border-gray-300 hover:text-gray-600"
              )}
              aria-label={isWishlisted ? "Remove from wishlist" : "Add to wishlist"}
            >
              <svg className="w-5 h-5" fill={isWishlisted ? "currentColor" : "none"} stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4.318 6.318a4.5 4.5 0 000 6.364L12 20.364l7.682-7.682a4.5 4.5 0 00-6.364-6.364L12 7.636l-1.318-1.318a4.5 4.5 0 00-6.364 0z" />
              </svg>
            </button>
          </div>

          {/* Action Buttons */}
          <div className="space-y-3">
            {buttons.map((button, index) => (
              <button
                key={index}
                type="button"
                onClick={(e) => handleButtonClick(button, e)}
                disabled={!inStock}
                className={clsx(
                  "w-full py-4 px-6 rounded-lg font-semibold text-sm transition-all duration-200 transform hover:scale-105 focus:outline-none focus:ring-2 focus:ring-offset-2 disabled:opacity-50 disabled:cursor-not-allowed disabled:transform-none",
                  button.variant === 'primary' && "bg-blue-600 hover:bg-blue-700 text-white focus:ring-blue-500 shadow-lg hover:shadow-xl",
                  button.variant === 'secondary' && "bg-gray-900 hover:bg-gray-800 text-white focus:ring-gray-500 shadow-lg hover:shadow-xl",
                  button.variant === 'outline' && "bg-transparent border-2 border-gray-300 text-gray-700 hover:border-gray-400 hover:bg-gray-50 focus:ring-gray-500",
                  button.variant === 'success' && "bg-green-600 hover:bg-green-700 text-white focus:ring-green-500 shadow-lg hover:shadow-xl",
                  button.variant === 'danger' && "bg-red-600 hover:bg-red-700 text-white focus:ring-red-500 shadow-lg hover:shadow-xl"
                )}
              >
                {button.icon && (
                  <span className="mr-2">
                    {typeof button.icon === 'string' ? button.icon : button.icon}
                  </span>
                )}
                {button.text}
              </button>
            ))}
          </div>

          {/* Additional Actions */}
          <div className="flex items-center justify-center space-x-6 pt-4 border-t border-gray-200">
            <button
              type="button"
              className="flex items-center space-x-2 text-sm text-gray-600 hover:text-gray-800 transition-colors"
              aria-label="Share product"
            >
              <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8.684 13.342C8.886 12.938 9 12.482 9 12c0-.482-.114-.938-.316-1.342m0 2.684a3 3 0 110-2.684m0 2.684l6.632 3.316m-6.632-6l6.632-3.316m0 0a3 3 0 105.367-2.684 3 3 0 00-5.367 2.684zm0 9.316a3 3 0 105.367 2.684 3 3 0 00-5.367-2.684z" />
              </svg>
              <span>Share</span>
            </button>

            <button
              type="button"
              className="flex items-center space-x-2 text-sm text-gray-600 hover:text-gray-800 transition-colors"
              aria-label="Compare product"
            >
              <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 19v-6a2 2 0 00-2-2H5a2 2 0 00-2 2v6a2 2 0 002 2h2a2 2 0 002-2zm0 0V9a2 2 0 012-2h2a2 2 0 012 2v10m-6 0a2 2 0 002 2h2a2 2 0 002-2m0 0V5a2 2 0 012-2h2a2 2 0 012 2v14a2 2 0 01-2 2h-2a2 2 0 01-2-2z" />
              </svg>
              <span>Compare</span>
            </button>

            <button
              type="button"
              className="flex items-center space-x-2 text-sm text-gray-600 hover:text-gray-800 transition-colors"
              aria-label="Ask a question"
            >
              <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8.228 9c.549-1.165 2.03-2 3.772-2 2.21 0 4 1.343 4 3 0 1.4-1.278 2.575-3.006 2.907-.542.104-.994.54-.994 1.093m0 3h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
              </svg>
              <span>Ask</span>
            </button>
          </div>
        </div>
      </section>
    );
  }

  // Minimal variant - Clean minimal design
  if (variant === 'minimal') {
    return (
      <section
        id={id}
        className={clsx(
          "product-actions-minimal",
          className
        )}
      >
        <div className="flex items-center space-x-4">
          {showQuantity && (
            <div className="flex items-center border border-gray-200 rounded">
              <button
                type="button"
                onClick={() => handleQuantityChange(quantity - 1)}
                disabled={quantity <= minQuantity}
                className="px-3 py-2 text-gray-600 hover:text-gray-800 disabled:opacity-50 disabled:cursor-not-allowed"
              >
                
              </button>
              <span className="px-3 py-2 text-sm font-medium min-w-[2rem] text-center">
                {quantity}
              </span>
              <button
                type="button"
                onClick={() => handleQuantityChange(quantity + 1)}
                disabled={quantity >= maxQuantity}
                className="px-3 py-2 text-gray-600 hover:text-gray-800 disabled:opacity-50 disabled:cursor-not-allowed"
              >
                +
              </button>
            </div>
          )}

          <button
            type="button"
            onClick={() => setIsWishlisted(!isWishlisted)}
            className={clsx(
              "p-2 rounded transition-colors",
              isWishlisted ? "text-red-600" : "text-gray-400 hover:text-gray-600"
            )}
          >
            <svg className="w-5 h-5" fill={isWishlisted ? "currentColor" : "none"} stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4.318 6.318a4.5 4.5 0 000 6.364L12 20.364l7.682-7.682a4.5 4.5 0 00-6.364-6.364L12 7.636l-1.318-1.318a4.5 4.5 0 00-6.364 0z" />
            </svg>
          </button>

          <button
            type="button"
            onClick={(e) => handleButtonClick(buttons[0] || {}, e)}
            disabled={!inStock}
            className="flex-1 py-3 px-6 bg-gray-900 text-white rounded font-medium hover:bg-gray-800 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
          >
            {buttons[0]?.text || "Add to Cart"}
          </button>
        </div>
      </section>
    );
  }

  // Stacked variant - Vertical button layout
  if (variant === 'stacked') {
    return (
      <section
        id={id}
        className={clsx(
          "product-actions-stacked",
          className
        )}
      >
        <div className="space-y-4">
          {/* Quantity and Wishlist Row */}
          <div className="flex items-center justify-between">
            {showQuantity && (
              <div className="flex items-center space-x-3">
                <span className="text-sm text-gray-600">Qty:</span>
                <select
                  value={quantity}
                  onChange={(e) => handleQuantityChange(parseInt(e.target.value))}
                  className="border border-gray-300 rounded px-3 py-1 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
                >
                  {Array.from({ length: Math.min(10, maxQuantity - minQuantity + 1) }, (_, i) => (
                    <option key={i + minQuantity} value={i + minQuantity}>
                      {i + minQuantity}
                    </option>
                  ))}
                </select>
              </div>
            )}

            <button
              type="button"
              onClick={() => setIsWishlisted(!isWishlisted)}
              className={clsx(
                "flex items-center space-x-2 px-4 py-2 rounded-lg border transition-colors",
                isWishlisted
                  ? "border-red-200 bg-red-50 text-red-600"
                  : "border-gray-200 text-gray-600 hover:border-gray-300 hover:bg-gray-50"
              )}
            >
              <svg className="w-4 h-4" fill={isWishlisted ? "currentColor" : "none"} stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4.318 6.318a4.5 4.5 0 000 6.364L12 20.364l7.682-7.682a4.5 4.5 0 00-6.364-6.364L12 7.636l-1.318-1.318a4.5 4.5 0 00-6.364 0z" />
              </svg>
              <span className="text-sm font-medium">
                {isWishlisted ? "Wishlisted" : "Add to Wishlist"}
              </span>
            </button>
          </div>

          {/* Primary Button */}
          <button
            type="button"
            onClick={(e) => handleButtonClick(buttons[0] || {}, e)}
            disabled={!inStock}
            className="w-full py-4 px-6 bg-blue-600 text-white rounded-lg font-semibold hover:bg-blue-700 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
          >
            {buttons[0]?.text || "Add to Cart"}
          </button>

          {/* Secondary Actions */}
          <div className="grid grid-cols-2 gap-3">
            {buttons.slice(1).map((button, index) => (
              <button
                key={index}
                type="button"
                onClick={(e) => handleButtonClick(button, e)}
                className={clsx(
                  "py-3 px-4 rounded-lg font-medium transition-colors",
                  button.variant === 'secondary' && "bg-gray-100 text-gray-800 hover:bg-gray-200",
                  button.variant === 'outline' && "border border-gray-300 text-gray-700 hover:bg-gray-50"
                )}
              >
                {button.text}
              </button>
            ))}

            {/* Additional Actions */}
            <button
              type="button"
              className="py-3 px-4 bg-gray-100 text-gray-800 rounded-lg font-medium hover:bg-gray-200 transition-colors"
            >
              Share
            </button>
          </div>
        </div>
      </section>
    );
  }

  // Inline variant - Horizontal button layout
  if (variant === 'inline') {
    return (
      <section
        id={id}
        className={clsx(
          "product-actions-inline",
          className
        )}
      >
        <div className="flex items-center space-x-4">
          {showQuantity && (
            <div className="flex items-center space-x-2">
              <span className="text-sm text-gray-600">Qty:</span>
              <div className="flex items-center border border-gray-300 rounded">
                <button
                  type="button"
                  onClick={() => handleQuantityChange(quantity - 1)}
                  disabled={quantity <= minQuantity}
                  className="px-2 py-1 text-gray-600 hover:text-gray-800 disabled:opacity-50"
                >
                  
                </button>
                <span className="px-3 py-1 text-sm font-medium min-w-[2rem] text-center">
                  {quantity}
                </span>
                <button
                  type="button"
                  onClick={() => handleQuantityChange(quantity + 1)}
                  disabled={quantity >= maxQuantity}
                  className="px-2 py-1 text-gray-600 hover:text-gray-800 disabled:opacity-50"
                >
                  +
                </button>
              </div>
            </div>
          )}

          {buttons.map((button, index) => (
            <button
              key={index}
              type="button"
              onClick={(e) => handleButtonClick(button, e)}
              disabled={!inStock}
              className={clsx(
                "px-6 py-3 rounded-lg font-semibold transition-colors disabled:opacity-50 disabled:cursor-not-allowed",
                button.variant === 'primary' && "bg-blue-600 text-white hover:bg-blue-700",
                button.variant === 'secondary' && "bg-gray-800 text-white hover:bg-gray-900",
                button.variant === 'outline' && "border-2 border-gray-300 text-gray-700 hover:bg-gray-50"
              )}
            >
              {button.text}
            </button>
          ))}

          <button
            type="button"
            onClick={() => setIsWishlisted(!isWishlisted)}
            className={clsx(
              "p-3 rounded-lg transition-colors",
              isWishlisted ? "text-red-600 bg-red-50" : "text-gray-400 hover:text-gray-600 hover:bg-gray-50"
            )}
          >
            <svg className="w-5 h-5" fill={isWishlisted ? "currentColor" : "none"} stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4.318 6.318a4.5 4.5 0 000 6.364L12 20.364l7.682-7.682a4.5 4.5 0 00-6.364-6.364L12 7.636l-1.318-1.318a4.5 4.5 0 00-6.364 0z" />
            </svg>
          </button>
        </div>
      </section>
    );
  }

  return null;
};
