import React, { useState, useEffect } from 'react';
import clsx from 'clsx';

/**
 * CartModern Component - Modern cart with card-based design
 * @param {Object} props - Component props
 * @param {Object} props.data - Cart configuration data
 * @param {Array} props.data.items - Cart items array
 * @param {string} [props.variant='default'] - Visual variant: 'default' | 'compact'
 * @param {boolean} [props.loader=false] - Show skeleton loader
 * @param {string} [props.data.title='Shopping Cart'] - Cart title
 * @param {boolean} [props.data.showSubtotal=true] - Show subtotal
 * @param {boolean} [props.data.showTax=true] - Show tax
 * @param {boolean} [props.data.showShipping=true] - Show shipping
 * @param {boolean} [props.data.showTotal=true] - Show total
 * @param {boolean} [props.data.showCheckoutButton=true] - Show checkout button
 * @param {boolean} [props.data.showContinueShopping=true] - Show continue shopping link
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute for the section element
 */
export const CartModern = ({ data, variant = 'default', loader: initialLoader = false, className, id }) => {
  const [cartItems, setCartItems] = useState(data?.items || []);
  const [isLoading, setIsLoading] = useState(initialLoader);

  // Auto-switch from skeleton to content after 2 seconds
  useEffect(() => {
    if (initialLoader) {
      const timer = setTimeout(() => {
        setIsLoading(false);
      }, 2000);

      return () => clearTimeout(timer);
    }
  }, [initialLoader]);

  const loader = initialLoader ? isLoading : false;

  if (!data || typeof data !== 'object') {
    console.error('CartModern: data prop is required and must be an object');
    return null;
  }

  const {
    title = 'Shopping Cart',
    showSubtotal = true,
    showTax = true,
    showShipping = true,
    showTotal = true,
    showCheckoutButton = true,
    showContinueShopping = true,
  } = data;

  // Calculate totals
  const subtotal = cartItems.reduce((total, item) => total + (item.price * item.quantity), 0);
  const tax = subtotal * 0.08; // 8% tax
  const shipping = subtotal > 100 ? 0 : 9.99; // Free shipping over $100
  const total = subtotal + tax + shipping;

  const isCompact = variant === 'compact';

  // Skeleton components
  const SkeletonHeader = () => (
    <div className={clsx("mb-12", isCompact ? "text-left border-b border-gray-100 pb-8" : "text-center")}>
      <div className={clsx("h-16 bg-gray-200 rounded animate-pulse mb-4", isCompact ? "w-64" : "w-80 mx-auto")}></div>
      <div className={clsx("h-6 bg-gray-200 rounded animate-pulse", isCompact ? "w-32" : "w-40 mx-auto")}></div>
    </div>
  );

  const SkeletonCartItem = () => (
    <div className={clsx(
      "transition-all duration-300 animate-pulse",
      isCompact ? "bg-white p-4 border-b border-gray-100 last:border-b-0" : "bg-white rounded-2xl p-6 shadow-sm border border-gray-100"
    )}>
      <div className="flex flex-col sm:flex-row gap-6">
        {/* Product Image Skeleton */}
        <div className="flex-shrink-0">
          <div className="relative">
            <div className={clsx(
              "bg-gray-200 rounded",
              isCompact ? "w-24 h-24" : "w-32 h-32 rounded-xl"
            )}></div>
            {!isCompact && <div className="absolute -top-2 -right-2 w-6 h-6 bg-gray-200 rounded-full"></div>}
          </div>
        </div>

        {/* Product Details Skeleton */}
        <div className="flex-1 min-w-0">
          <div className="flex justify-between items-start mb-4">
            <div className="flex-1">
              <div className={clsx("h-6 bg-gray-200 rounded mb-2", isCompact ? "w-48" : "w-56")}></div>
              {!isCompact && <div className="h-4 bg-gray-200 rounded w-32 mb-2"></div>}
              <div className="h-4 bg-gray-200 rounded w-24"></div>
            </div>
            <div className="w-5 h-5 bg-gray-200 rounded"></div>
          </div>

          {/* Price and Quantity Skeleton */}
          <div className="flex flex-col sm:flex-row sm:justify-between sm:items-center gap-4">
            <div className="flex items-center gap-6">
              <div className={clsx("h-8 bg-gray-200 rounded", isCompact ? "w-16" : "w-20")}></div>
              <div className="flex items-center gap-2 bg-gray-50 rounded-full p-1 border border-gray-100">
                <div className="w-8 h-8 bg-gray-200 rounded-full"></div>
                <div className="w-8 h-6 bg-gray-200 rounded"></div>
                <div className="w-8 h-8 bg-gray-200 rounded-full"></div>
              </div>
            </div>
            <div className="text-right">
              <div className={clsx("h-8 bg-gray-200 rounded", isCompact ? "w-16" : "w-20")}></div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );

  const SkeletonOrderSummary = () => (
    <div className={clsx(isCompact ? "mt-12" : "xl:col-span-2")}>
      <div className={clsx(
        "sticky top-4 transition-all duration-300 animate-pulse",
        isCompact ? "bg-gray-50 rounded-3xl p-8 border border-gray-200" : "bg-white rounded-2xl p-8 shadow-sm border border-gray-100"
      )}>
        <div className="h-8 bg-gray-200 rounded mb-8 w-32"></div>

        <div className="space-y-6">
          {showSubtotal && (
            <div className="flex justify-between items-center">
              <div className="h-5 bg-gray-200 rounded w-16"></div>
              <div className="h-5 bg-gray-200 rounded w-12"></div>
            </div>
          )}

          {showTax && (
            <div className="flex justify-between items-center">
              <div className="h-5 bg-gray-200 rounded w-12"></div>
              <div className="h-5 bg-gray-200 rounded w-10"></div>
            </div>
          )}

          {showShipping && (
            <div className="flex justify-between items-center">
              <div className="h-5 bg-gray-200 rounded w-16"></div>
              <div className="h-5 bg-gray-200 rounded w-14"></div>
            </div>
          )}

          {!isCompact && showShipping && (
            <div className="bg-primary-50 border border-primary-200 rounded-lg p-4">
              <div className="h-4 bg-gray-200 rounded w-48"></div>
              <div className="w-full bg-primary-200 rounded-full h-2 mt-2"></div>
            </div>
          )}

          <hr className="border-gray-200" />

          {showTotal && (
            <div className="flex justify-between items-center">
              <div className="h-6 bg-gray-200 rounded w-12"></div>
              <div className="h-10 bg-gray-200 rounded w-20"></div>
            </div>
          )}
        </div>

        {showCheckoutButton && (
          <div className="h-12 bg-gray-200 rounded-full mt-8 animate-pulse"></div>
        )}

        {showContinueShopping && (
          <div className="h-12 bg-gray-200 rounded-full mt-4 animate-pulse"></div>
        )}

        {/* Trust indicators */}
        {!isCompact && (
          <div className="mt-8 pt-6 border-t border-gray-200">
            <div className="flex items-center justify-center gap-6 text-sm">
              <div className="flex items-center gap-2">
                <div className="w-4 h-4 bg-gray-200 rounded"></div>
                <div className="h-4 bg-gray-200 rounded w-20"></div>
              </div>
              <div className="flex items-center gap-2">
                <div className="w-4 h-4 bg-gray-200 rounded"></div>
                <div className="h-4 bg-gray-200 rounded w-16"></div>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );

  // Show skeleton loader
  if (loader) {
    return (
      <section
        id={id}
        className={clsx(
          'w-full py-8 sm:py-12 lg:py-16 px-4 sm:px-6 lg:px-8',
          isCompact ? 'bg-white' : 'bg-gray-50',
          className
        )}
      >
        <div className={clsx("mx-auto", isCompact ? "max-w-4xl" : "max-w-6xl")}>
          <SkeletonHeader />
          <div className={clsx("grid grid-cols-1 gap-8", isCompact ? "xl:grid-cols-1" : "xl:grid-cols-5")}>
            {/* Cart Items Skeleton */}
            <div className={clsx(isCompact ? "" : "xl:col-span-3", "space-y-6")}>
              <SkeletonCartItem />
              <SkeletonCartItem />
              <SkeletonCartItem />
            </div>
            <SkeletonOrderSummary />
          </div>
        </div>
      </section>
    );
  }

  const updateQuantity = (id, newQuantity) => {
    if (newQuantity <= 0) {
      removeItem(id);
      return;
    }
    setCartItems(items =>
      items.map(item =>
        item.id === id ? { ...item, quantity: newQuantity } : item
      )
    );
  };

  const removeItem = (id) => {
    setCartItems(items => items.filter(item => item.id !== id));
  };

  if (cartItems.length === 0) {
    return (
      <section
        id={id}
        className={clsx(
          'w-full py-8 sm:py-12 lg:py-16 px-4 sm:px-6 lg:px-8 bg-gray-50',
          className
        )}
      >
        <div className="max-w-2xl mx-auto text-center">
          <div className="mb-8">
            <div className="inline-flex items-center justify-center w-20 h-20 bg-white rounded-full shadow-sm">
              <svg
                className="w-10 h-10 text-gray-400"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={1.5}
                  d="M16 11V7a4 4 0 00-8 0v4M5 9h14l1 12H4L5 9z"
                />
              </svg>
            </div>
          </div>
          <h2 className="text-3xl font-bold text-gray-900 mb-4">Your cart is empty</h2>
          <p className="text-gray-600 mb-8">Start shopping to fill your cart with amazing products.</p>
          <button className="inline-flex items-center px-8 py-4 bg-primary-600 text-white font-semibold rounded-xl hover:bg-primary-700 transition-all duration-200 shadow-sm hover:shadow-md">
            <svg className="w-5 h-5 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M7 16l-4-4m0 0l4-4m-4 4h18" />
            </svg>
            Continue Shopping
          </button>
        </div>
      </section>
    );
  }

  return (
    <section
      id={id}
      className={clsx(
        'w-full py-8 sm:py-12 lg:py-16 px-4 sm:px-6 lg:px-8',
        isCompact ? 'bg-white' : 'bg-gray-50',
        className
      )}
    >
      <div className={clsx("mx-auto", isCompact ? "max-w-4xl" : "max-w-6xl")}>
        {/* Header */}
        <div className={clsx("mb-12", isCompact ? "text-left border-b border-gray-100 pb-8" : "text-center")}>
          <h1 className={clsx("font-black text-gray-900 mb-4", isCompact ? "text-3xl" : "text-4xl md:text-5xl")}>{title}</h1>
          <p className={clsx("text-gray-500 font-medium", isCompact ? "text-base" : "text-xl")}>{cartItems.length} item{cartItems.length !== 1 ? 's' : ''} in your cart</p>
        </div>

        <div className={clsx("grid grid-cols-1 gap-8", isCompact ? "xl:grid-cols-1" : "xl:grid-cols-5")}>
          {/* Cart Items */}
          <div className={clsx(isCompact ? "" : "xl:col-span-3", "space-y-6")}>
            {cartItems.map((item) => (
              <div
                key={item.id}
                className={clsx(
                  "transition-all duration-300",
                  isCompact ? "bg-white p-4 border-b border-gray-100 last:border-b-0" : "bg-white rounded-2xl p-6 shadow-sm hover:shadow-lg border border-gray-100"
                )}
              >
                <div className="flex flex-col sm:flex-row gap-6">
                  {/* Product Image */}
                  <div className="flex-shrink-0">
                    <div className="relative">
                      <img
                        src={item.image || '/api/placeholder/140/140'}
                        alt={item.name}
                        className={clsx(
                          "object-cover",
                          isCompact ? "w-24 h-24 rounded-lg" : "w-32 h-32 rounded-xl"
                        )}
                      />
                      {item.badge && (
                        <span className="absolute -top-2 -right-2 bg-primary-600 text-white text-xs font-semibold px-2 py-1 rounded-full">
                          {item.badge}
                        </span>
                      )}
                    </div>
                  </div>

                  {/* Product Details */}
                  <div className="flex-1 min-w-0">
                    <div className="flex justify-between items-start mb-4">
                      <div className="flex-1">
                        <h3 className={clsx("font-bold text-gray-900 mb-2", isCompact ? "text-lg" : "text-xl")}>{item.name}</h3>
                        {!isCompact && item.description && (
                          <p className="text-gray-600 mb-2 text-sm">{item.description}</p>
                        )}
                        {item.variant && (
                          <p className="text-sm text-gray-500 font-medium">Variant: {item.variant}</p>
                        )}
                      </div>
                      <button
                        onClick={() => removeItem(item.id)}
                        className="p-2 text-gray-400 hover:text-red-500 hover:bg-red-50 rounded-lg transition-colors"
                        aria-label="Remove item"
                      >
                        <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                        </svg>
                      </button>
                    </div>

                    {/* Price and Quantity */}
                    <div className="flex flex-col sm:flex-row sm:justify-between sm:items-center gap-4">
                      <div className="flex items-center gap-6">
                        <span className={clsx("font-black text-gray-900", isCompact ? "text-xl" : "text-2xl")}>${item.price.toFixed(2)}</span>
                        <div className="flex items-center gap-2 bg-gray-50 rounded-full p-1 border border-gray-100">
                          <button
                            onClick={() => updateQuantity(item.id, item.quantity - 1)}
                            className="w-8 h-8 flex items-center justify-center text-gray-500 hover:text-black hover:bg-white rounded-full transition-all"
                            aria-label="Decrease quantity"
                          >
                            <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2.5} d="M20 12H4" />
                            </svg>
                          </button>
                          <span className="w-8 text-center font-bold">{item.quantity}</span>
                          <button
                            onClick={() => updateQuantity(item.id, item.quantity + 1)}
                            className="w-8 h-8 flex items-center justify-center text-gray-500 hover:text-black hover:bg-white rounded-full transition-all"
                            aria-label="Increase quantity"
                          >
                            <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2.5} d="M12 6v6m0 0v6m0-6h6m-6 0H6" />
                            </svg>
                          </button>
                        </div>
                      </div>
                      <div className="text-right">
                        <span className={clsx("font-black text-primary-600", isCompact ? "text-xl" : "text-2xl")}>
                          ${(item.price * item.quantity).toFixed(2)}
                        </span>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>

          {/* Order Summary */}
          <div className={clsx(isCompact ? "mt-12" : "xl:col-span-2")}>
            <div className={clsx(
              "sticky top-4 transition-all duration-300",
              isCompact ? "bg-gray-50 rounded-3xl p-8 border border-gray-200" : "bg-white rounded-2xl p-8 shadow-sm border border-gray-100"
            )}>
              <h2 className="text-2xl font-black text-gray-900 mb-8 uppercase tracking-widest">Summary</h2>

              <div className="space-y-6">
                {showSubtotal && (
                  <div className="flex justify-between items-center">
                    <span className="text-gray-600 font-medium">Subtotal</span>
                    <span className="font-semibold text-lg">${subtotal.toFixed(2)}</span>
                  </div>
                )}

                {showTax && (
                  <div className="flex justify-between items-center">
                    <span className="text-gray-600 font-medium">Tax</span>
                    <span className="font-semibold text-lg">${tax.toFixed(2)}</span>
                  </div>
                )}

                {showShipping && (
                  <div className="flex justify-between items-center">
                    <span className="text-gray-600 font-medium">Shipping</span>
                    <span className="font-semibold text-lg">
                      {shipping === 0 ? (
                        <span className="text-green-600">Free</span>
                      ) : (
                        `$${shipping.toFixed(2)}`
                      )}
                    </span>
                  </div>
                )}

                {!isCompact && subtotal < 100 && showShipping && (
                  <div className="bg-primary-50 border border-primary-200 rounded-lg p-4">
                    <p className="text-sm text-primary-700 font-medium">
                      Add ${(100 - subtotal).toFixed(2)} more for free shipping
                    </p>
                    <div className="w-full bg-primary-200 rounded-full h-2 mt-2">
                      <div
                        className="bg-primary-600 h-2 rounded-full transition-all duration-300"
                        style={{ width: `${Math.min((subtotal / 100) * 100, 100)}%` }}
                      ></div>
                    </div>
                  </div>
                )}

                <hr className="border-gray-200" />

                {showTotal && (
                  <div className="flex justify-between items-center">
                    <span className="text-xl font-bold text-gray-900 uppercase tracking-widest">Total</span>
                    <span className="text-3xl font-black text-primary-600">${total.toFixed(2)}</span>
                  </div>
                )}
              </div>

              {showCheckoutButton && (
                <button className="w-full mt-8 px-8 py-4 bg-black text-white font-bold text-lg rounded-full hover:bg-gray-800 transition-all duration-300 shadow-xl hover:shadow-2xl uppercase tracking-widest">
                  Checkout
                </button>
              )}

              {showContinueShopping && (
                <button className="w-full mt-4 px-8 py-4 border-2 border-gray-300 text-gray-700 font-bold text-lg rounded-full hover:bg-gray-50 transition-colors uppercase tracking-widest">
                  Continue Shopping
                </button>
              )}

              {/* Trust indicators */}
              {!isCompact && (
                <div className="mt-8 pt-6 border-t border-gray-200">
                  <div className="flex items-center justify-center gap-6 text-sm text-gray-500">
                    <div className="flex items-center gap-2">
                      <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12l2 2 4-4m6 2a9 9 0 11-18 0 9 9 0 0118 0z" />
                      </svg>
                      <span>Secure Checkout</span>
                    </div>
                    <div className="flex items-center gap-2">
                      <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M20.618 5.984A11.955 11.955 0 0112 2.944a11.955 11.955 0 01-8.618 3.04A12.02 12.02 0 003 9c0 5.591 3.824 10.29 9 11.622 5.176-1.332 9-6.03 9-11.622 0-1.042-.133-2.052-.382-3.016z" />
                      </svg>
                      <span>SSL Protected</span>
                    </div>
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};