import { useState, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/feature-modern.css';

/**
 * FeatureModern Component - Modern feature section with two latest layout variants
 * @param {Object} props - Component props
 * @param {Object} props.data - Feature section configuration data
 * @param {string} [props.data.title] - Main heading text
 * @param {string} [props.data.subtitle] - Subtitle text displayed above title
 * @param {string} [props.data.description] - Description text displayed below title
 * @param {Array} props.data.items - Array of feature items
 * @param {string} [props.data.layout='minimal'] - Layout variant: 'minimal' | 'staggered'
 * @param {string} [props.data.alignment='center'] - Header text alignment: 'left' | 'center' | 'right'
 * @param {number} [props.data.columns=3] - Number of columns for grid layout (1-4)
 * @param {string} [props.data.theme='light'] - Theme variant: 'light' | 'dark'
 * @param {boolean} [props.data.showIcons=true] - Show icons for features
 * @param {string} [props.data.className] - Additional CSS classes for the section
 * @param {string} [props.contextKey='features'] - Key to get data from LayoutDataProvider context (default: 'features')
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute for the section element
 * @param {boolean} [props.loader=false] - Show loading state
 */
export const FeatureModern = ({ data, className, id, loader = false }) => {
  // Safety check for data
  if (!data || typeof data !== 'object') {
    console.error('FeatureModern: data prop is required and must be an object');
    return null;
  }

  const [showLoader, setShowLoader] = useState(loader);

  // Auto-hide loader after 2 seconds when loader prop is true
  useEffect(() => {
    if (loader) {
      const timer = setTimeout(() => {
        setShowLoader(false);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowLoader(false);
    }
  }, [loader]);

  const {
    title,
    subtitle,
    description,
    items = [],
    layout = 'minimal',
    alignment = 'center',
    columns = 3,
    theme = 'light',
    showIcons = true,
    className: dataClassName, // Additional CSS classes for the section
  } = data;

  const isDark = theme === 'dark';
  const [hoveredIndex, setHoveredIndex] = useState(null);

  const alignmentClasses = {
    left: 'text-left items-start',
    center: 'text-center items-center',
    right: 'text-right items-end',
  };

  const gridColumnsClasses = {
    1: 'grid-cols-1',
    2: 'grid-cols-1 md:grid-cols-2',
    3: 'grid-cols-1 md:grid-cols-2 lg:grid-cols-3',
    4: 'grid-cols-1 md:grid-cols-2 lg:grid-cols-4',
  };

  if (items.length === 0) {
    console.warn('FeatureModern: items array is empty');
    return null;
  }

  // Show skeleton loader if loader prop is true
  if (showLoader) {
    return (
      <section
        id={id}
        className={clsx(
          'w-full py-24 sm:py-32 px-4 sm:px-6 lg:px-8',
          isDark ? 'bg-gray-900' : 'bg-white',
          className,
          dataClassName
        )}
      >
        <div className="max-w-7xl mx-auto w-full">
          {/* Header skeleton */}
          <div className={clsx('mb-12 sm:mb-16 flex flex-col', alignmentClasses[alignment])}>
            <div className={clsx('h-5 rounded w-32 mb-4 mx-auto animate-pulse', isDark ? 'bg-gray-700' : 'bg-gray-400')}></div>
            <div className={clsx('h-12 rounded w-full max-w-2xl mb-6 mx-auto animate-pulse', isDark ? 'bg-gray-700' : 'bg-gray-400')}></div>
            <div className={clsx('h-6 rounded w-full max-w-xl mb-8 mx-auto animate-pulse', isDark ? 'bg-gray-800' : 'bg-gray-300')}></div>
          </div>

          {/* Feature cards skeleton */}
          <div className={clsx('grid gap-6 sm:gap-8', gridColumnsClasses[columns])}>
            {[0, 1, 2, 3, 4, 5].slice(0, columns * 2).map((index) => (
              <div key={index} className={clsx(
                'rounded-lg p-6 border',
                isDark ? 'bg-gray-800 border-gray-700' : 'bg-white border-gray-200'
              )}>
                {/* Icon skeleton */}
                {showIcons && (
                  <div className={clsx(
                    'w-14 h-14 rounded-xl mb-5',
                    isDark ? 'bg-gray-700' : 'bg-gray-200'
                  )}></div>
                )}
                {/* Title skeleton */}
                <div className={clsx('h-6 rounded w-3/4 mb-3 animate-pulse', isDark ? 'bg-gray-700' : 'bg-gray-400')}></div>
                {/* Description skeleton */}
                <div className="space-y-2">
                  <div className={clsx('h-4 rounded w-full animate-pulse', isDark ? 'bg-gray-800' : 'bg-gray-300')}></div>
                  <div className={clsx('h-4 rounded w-5/6 animate-pulse', isDark ? 'bg-gray-800' : 'bg-gray-300')}></div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>
    );
  }

  // Modern color palette
  const getColorScheme = (index) => {
    const schemes = [
      {
        primary: 'text-violet-600',
        bg: 'bg-violet-50',
        border: 'border-violet-200',
        accent: 'bg-violet-500',
        gradient: 'from-violet-500 to-purple-600',
      },
      {
        primary: 'text-blue-600',
        bg: 'bg-blue-50',
        border: 'border-blue-200',
        accent: 'bg-blue-500',
        gradient: 'from-blue-500 to-cyan-600',
      },
      {
        primary: 'text-emerald-600',
        bg: 'bg-emerald-50',
        border: 'border-emerald-200',
        accent: 'bg-emerald-500',
        gradient: 'from-emerald-500 to-teal-600',
      },
      {
        primary: 'text-rose-600',
        bg: 'bg-rose-50',
        border: 'border-rose-200',
        accent: 'bg-rose-500',
        gradient: 'from-rose-500 to-pink-600',
      },
      {
        primary: 'text-amber-600',
        bg: 'bg-amber-50',
        border: 'border-amber-200',
        accent: 'bg-amber-500',
        gradient: 'from-amber-500 to-orange-600',
      },
      {
        primary: 'text-indigo-600',
        bg: 'bg-indigo-50',
        border: 'border-indigo-200',
        accent: 'bg-indigo-500',
        gradient: 'from-indigo-500 to-blue-600',
      },
    ];
    return schemes[index % schemes.length];
  };

  const renderIcon = (item, index, colors, variant = 'default') => {
    if (!showIcons || !item.icon) return null;

    // If icon is a React component
    if (typeof item.icon === 'object' && item.icon.type) {
      if (variant === 'minimal') {
        return (
          <div className={clsx(
            'flex items-center justify-center w-14 h-14 rounded-xl mb-5',
            'transition-all duration-300',
            isDark
              ? 'bg-gray-800 border border-gray-700'
              : clsx(colors.bg, colors.border, 'border'),
            hoveredIndex === index && 'scale-110 -rotate-3'
          )}>
            <div className={clsx(
              'w-7 h-7',
              isDark ? 'text-gray-300' : colors.primary
            )}>
              {item.icon}
            </div>
          </div>
        );
      } else {
        // Staggered variant
        return (
          <div className={clsx(
            'relative flex items-center justify-center mb-6',
            'transition-all duration-500',
            hoveredIndex === index && 'scale-110'
          )}>
            <div className={clsx(
              'absolute inset-0 rounded-2xl blur-xl opacity-30',
              `bg-gradient-to-br ${colors.gradient}`,
              'transition-all duration-500',
              hoveredIndex === index && 'opacity-50 scale-150'
            )} />
            <div className={clsx(
              'relative flex items-center justify-center w-20 h-20 rounded-2xl',
              isDark
                ? 'bg-gray-800 border border-gray-700'
                : clsx(colors.bg, colors.border, 'border-2'),
              'transition-all duration-500',
              hoveredIndex === index && 'shadow-2xl'
            )}>
              <div className={clsx(
                'w-10 h-10',
                isDark ? 'text-gray-300' : colors.primary
              )}>
                {item.icon}
              </div>
            </div>
          </div>
        );
      }
    }

    // If icon is a string (emoji or image URL)
    if (typeof item.icon === 'string') {
      // Check if it's an image URL
      if (item.icon.startsWith('http') || item.icon.startsWith('/') || item.icon.includes('.')) {
        return (
          <div className={clsx(
            'flex items-center justify-center w-14 h-14 rounded-xl mb-5 overflow-hidden',
            'transition-all duration-300',
            isDark
              ? 'bg-gray-800 border border-gray-700'
              : clsx(colors.bg, colors.border, 'border'),
            hoveredIndex === index && 'scale-110'
          )}>
            <img
              src={item.icon}
              alt={item.title || 'Feature icon'}
              className="w-full h-full object-cover"
            />
          </div>
        );
      }
      
      // Emoji
      if (variant === 'minimal') {
        return (
          <div className={clsx(
            'flex items-center justify-center w-14 h-14 rounded-xl mb-5 text-2xl',
            'transition-all duration-300',
            isDark
              ? 'bg-gray-800 border border-gray-700'
              : clsx(colors.bg, colors.border, 'border'),
            hoveredIndex === index && 'scale-110 -rotate-3'
          )}>
            {item.icon}
          </div>
        );
      } else {
        return (
          <div className={clsx(
            'relative flex items-center justify-center mb-6',
            'transition-all duration-500',
            hoveredIndex === index && 'scale-110'
          )}>
            <div className={clsx(
              'absolute inset-0 rounded-2xl blur-xl opacity-30',
              `bg-gradient-to-br ${colors.gradient}`,
              'transition-all duration-500',
              hoveredIndex === index && 'opacity-50 scale-150'
            )} />
            <div className={clsx(
              'relative flex items-center justify-center w-20 h-20 rounded-2xl text-3xl',
              isDark
                ? 'bg-gray-800 border border-gray-700'
                : clsx(colors.bg, colors.border, 'border-2')
            )}>
              {item.icon}
            </div>
          </div>
        );
      }
    }

    return null;
  };

  // Minimal Layout - Clean, spacious cards with elegant design
  const renderMinimalLayout = () => {
    return (
      <section
        id={id}
        className={clsx(
          'w-full py-24 sm:py-32 px-4 sm:px-6 lg:px-8',
          'relative',
          isDark ? 'bg-gray-900' : 'bg-white',
          className,
          dataClassName
        )}
      >
        {/* Subtle background pattern */}
        <div className={clsx(
          'absolute inset-0',
          isDark
            ? 'bg-[radial-gradient(circle_at_1px_1px,rgba(255,255,255,0.05)_1px,transparent_0)] bg-[size:40px_40px]'
            : 'bg-[radial-gradient(circle_at_1px_1px,rgba(0,0,0,0.03)_1px,transparent_0)] bg-[size:40px_40px]'
        )} />

        <div className="max-w-7xl mx-auto w-full relative z-10">
          {/* Header */}
          {(title || subtitle || description) && (
            <div
              className={clsx(
                'mb-16 sm:mb-20 flex flex-col',
                alignmentClasses[alignment]
              )}
            >
              {subtitle && (
                <span className={clsx(
                  'inline-block text-sm font-semibold uppercase tracking-wider mb-4 px-3 py-1.5 rounded-full',
                  isDark
                    ? 'bg-gray-800 text-gray-300 border border-gray-700'
                    : 'bg-gray-100 text-gray-600'
                )}>
                  {subtitle}
                </span>
              )}

              {title && (
                <h2 className={clsx(
                  'text-4xl sm:text-5xl md:text-6xl font-bold mb-6 leading-tight',
                  isDark ? 'text-white' : 'text-gray-900'
                )}>
                  {title}
                </h2>
              )}

              {description && (
                <p
                  className={clsx(
                    'text-lg sm:text-xl max-w-3xl leading-relaxed',
                    isDark ? 'text-gray-400' : 'text-gray-600',
                    alignment === 'center' && 'mx-auto',
                    alignment === 'right' && 'ml-auto',
                    alignment === 'left' && 'mr-auto'
                  )}
                >
                  {description}
                </p>
              )}
            </div>
          )}

          {/* Features Grid */}
          <div className={clsx('grid gap-6 sm:gap-8', gridColumnsClasses[columns] || gridColumnsClasses[3])}>
            {items.map((item, index) => {
              const colors = getColorScheme(index);
              
              return (
                <div
                  key={index}
                  className="group relative"
                  onMouseEnter={() => setHoveredIndex(index)}
                  onMouseLeave={() => setHoveredIndex(null)}
                >
                  {/* Card */}
                  <div
                    className={clsx(
                      'relative h-full p-8 rounded-2xl',
                      'transition-all duration-300',
                      'border',
                      isDark
                        ? 'bg-gray-800/50 border-gray-700 hover:border-gray-600 hover:bg-gray-800'
                        : 'bg-white border-gray-200 hover:border-gray-300 hover:shadow-xl',
                      'hover:-translate-y-1'
                    )}
                  >
                    {/* Accent line */}
                    <div className={clsx(
                      'absolute top-0 left-8 right-8 h-0.5 rounded-full',
                      'transition-all duration-300',
                      isDark ? 'bg-gray-700' : 'bg-gray-100',
                      hoveredIndex === index && clsx(
                        `bg-gradient-to-r ${colors.gradient}`,
                        'h-1'
                      )
                    )} />

                    {/* Content */}
                    <div className="relative">
                      {/* Icon */}
                      {renderIcon(item, index, colors, 'minimal')}

                      {/* Title */}
                      {item.title && (
                        <h3 className={clsx(
                          'text-xl sm:text-2xl font-bold mb-3',
                          'transition-colors duration-300',
                          isDark ? 'text-white' : 'text-gray-900',
                          hoveredIndex === index && colors.primary
                        )}>
                          {item.title}
                        </h3>
                      )}

                      {/* Description */}
                      {item.description && (
                        <p className={clsx(
                          'text-base leading-relaxed mb-6',
                          isDark ? 'text-gray-400' : 'text-gray-600'
                        )}>
                          {item.description}
                        </p>
                      )}

                      {/* Link */}
                      {(item.link || item.button) && (
                        <a
                          href={item.link || item.button?.href || '#'}
                          onClick={item.button?.onClick}
                          className={clsx(
                            'inline-flex items-center gap-2 text-sm font-semibold',
                            'transition-all duration-300 group/link',
                            isDark
                              ? 'text-gray-400 hover:text-white'
                              : clsx(colors.primary, 'hover:gap-3')
                          )}
                        >
                          {item.linkText || item.button?.text || 'Learn more'}
                          <svg 
                            className={clsx(
                              'w-4 h-4 transition-transform duration-300 group-hover/link:translate-x-1',
                              isDark ? 'text-gray-400' : colors.primary
                            )}
                            fill="none"
                            stroke="currentColor"
                            viewBox="0 0 24 24"
                          >
                            <path
                              strokeLinecap="round"
                              strokeLinejoin="round"
                              strokeWidth={2}
                              d="M17 8l4 4m0 0l-4 4m4-4H3"
                            />
                          </svg>
                        </a>
                      )}
                    </div>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      </section>
    );
  };

  // Staggered Layout - Dynamic cards with varied sizes and positions
  const renderStaggeredLayout = () => {
    const getStaggerStyle = (index) => {
      const patterns = [
        { marginTop: '0', height: 'auto' },
        { marginTop: '2rem', height: 'auto' },
        { marginTop: '0', height: 'auto' },
        { marginTop: '4rem', height: 'auto' },
        { marginTop: '0', height: 'auto' },
        { marginTop: '2rem', height: 'auto' },
      ];
      return patterns[index % patterns.length];
    };

    return (
      <section
        id={id}
        className={clsx(
          'w-full py-24 sm:py-32 px-4 sm:px-6 lg:px-8',
          'relative overflow-hidden',
          isDark ? 'bg-gray-950' : 'bg-gradient-to-b from-gray-50 to-white',
          className,
          dataClassName
        )}
      >
        {/* Animated gradient background */}
        <div className="absolute inset-0 overflow-hidden pointer-events-none">
          <div className={clsx(
            'absolute -top-40 -right-40 w-80 h-80 rounded-full blur-3xl opacity-20',
            'bg-gradient-to-br from-violet-500 to-purple-600',
            'animate-pulse'
          )} />
          <div className={clsx(
            'absolute -bottom-40 -left-40 w-80 h-80 rounded-full blur-3xl opacity-20',
            'bg-gradient-to-br from-blue-500 to-cyan-600',
            'animate-pulse animation-delay-2000'
          )} />
        </div>

        <div className="max-w-7xl mx-auto w-full relative z-10">
          {/* Header */}
          {(title || subtitle || description) && (
            <div
              className={clsx(
                'mb-20 sm:mb-24 flex flex-col',
                alignmentClasses[alignment]
              )}
            >
              {subtitle && (
                <p className={clsx(
                  'text-sm font-semibold uppercase tracking-widest mb-4',
                  isDark ? 'text-gray-400' : 'text-gray-500'
                )}>
                  {subtitle}
                </p>
              )}

              {title && (
                <h2 className={clsx(
                  'text-4xl sm:text-5xl md:text-6xl lg:text-7xl font-extrabold mb-6 leading-tight',
                  'bg-clip-text text-transparent bg-gradient-to-r',
                  isDark
                    ? 'from-white to-gray-400'
                    : 'from-gray-900 to-gray-600'
                )}>
                  {title}
                </h2>
              )}

              {description && (
                <p
                  className={clsx(
                    'text-lg sm:text-xl max-w-3xl leading-relaxed',
                    isDark ? 'text-gray-400' : 'text-gray-600',
                    alignment === 'center' && 'mx-auto',
                    alignment === 'right' && 'ml-auto',
                    alignment === 'left' && 'mr-auto'
                  )}
                >
                  {description}
                </p>
              )}
            </div>
          )}

          {/* Features Grid - Staggered */}
          <div className={clsx('grid gap-6 sm:gap-8', gridColumnsClasses[columns] || gridColumnsClasses[3])}>
            {items.map((item, index) => {
              const colors = getColorScheme(index);
              const stagger = getStaggerStyle(index);
              
              return (
                <div
                  key={index}
                  className="group relative"
                  style={{ marginTop: stagger.marginTop }}
                  onMouseEnter={() => setHoveredIndex(index)}
                  onMouseLeave={() => setHoveredIndex(null)}
                >
                  {/* Card */}
                  <div
                    className={clsx(
                      'relative h-full p-8 sm:p-10 rounded-3xl',
                      'transition-all duration-500',
                      'border-2',
                      isDark
                        ? 'bg-gray-900/80 border-gray-800 backdrop-blur-sm hover:border-gray-700 hover:bg-gray-900'
                        : 'bg-white/80 border-gray-200 backdrop-blur-sm hover:border-gray-300 hover:shadow-2xl',
                      'hover:-translate-y-2 hover:scale-[1.02]'
                    )}
                  >
                    {/* Gradient border effect on hover */}
                    <div className={clsx(
                      'absolute inset-0 rounded-3xl opacity-0 transition-opacity duration-500 pointer-events-none',
                      `bg-gradient-to-br ${colors.gradient}`,
                      'p-[2px] -z-10',
                      hoveredIndex === index && 'opacity-100'
                    )}>
                      <div className={clsx(
                        'w-full h-full rounded-3xl',
                        isDark ? 'bg-gray-900' : 'bg-white'
                      )} />
                    </div>

                    {/* Content */}
                    <div className="relative">
                      {/* Icon */}
                      {renderIcon(item, index, colors, 'staggered')}

                      {/* Badge/Number */}
                      {item.badge && (
                        <span className={clsx(
                          'inline-block text-xs font-bold uppercase tracking-widest px-3 py-1.5 rounded-full mb-4',
                          `bg-gradient-to-r ${colors.gradient} text-white`
                        )}>
                          {item.badge}
                        </span>
                      )}

                      {/* Title */}
                      {item.title && (
                        <h3 className={clsx(
                          'text-2xl sm:text-3xl font-bold mb-4',
                          'transition-colors duration-300',
                          isDark ? 'text-white' : 'text-gray-900'
                        )}>
                          {item.title}
                        </h3>
                      )}

                      {/* Description */}
                      {item.description && (
                        <p className={clsx(
                          'text-base sm:text-lg leading-relaxed mb-6',
                          isDark ? 'text-gray-400' : 'text-gray-600'
                        )}>
                          {item.description}
                        </p>
                      )}

                      {/* Feature List */}
                      {item.features && item.features.length > 0 && (
                        <ul className="space-y-3 mb-6">
                          {item.features.map((feature, fIndex) => (
                            <li 
                              key={fIndex} 
                              className="flex items-center gap-3"
                            >
                              <div className={clsx(
                                'flex-shrink-0 w-5 h-5 rounded-full flex items-center justify-center',
                                `bg-gradient-to-br ${colors.gradient}`
                              )}>
                                <svg
                                  className="w-3 h-3 text-white"
                                  fill="none"
                                  stroke="currentColor"
                                  viewBox="0 0 24 24"
                                >
                                  <path
                                    strokeLinecap="round"
                                    strokeLinejoin="round"
                                    strokeWidth={3}
                                    d="M5 13l4 4L19 7"
                                  />
                                </svg>
                              </div>
                              <span className={clsx(
                                'text-sm',
                                isDark ? 'text-gray-400' : 'text-gray-600'
                              )}>
                                {feature}
                              </span>
                            </li>
                          ))}
                        </ul>
                      )}

                      {/* Link/Button */}
                      {(item.link || item.button) && (
                        <a
                          href={item.link || item.button?.href || '#'}
                          onClick={item.button?.onClick}
                          className={clsx(
                            'inline-flex items-center gap-2 px-6 py-3 rounded-xl',
                            'text-sm font-semibold transition-all duration-300',
                            `bg-gradient-to-r ${colors.gradient} text-white`,
                            'hover:shadow-lg hover:scale-105',
                            'group/btn'
                          )}
                        >
                          {item.linkText || item.button?.text || 'Learn more'}
                          <svg 
                            className="w-4 h-4 transition-transform duration-300 group-hover/btn:translate-x-1"
                            fill="none"
                            stroke="currentColor"
                            viewBox="0 0 24 24"
                          >
                            <path
                              strokeLinecap="round"
                              strokeLinejoin="round"
                              strokeWidth={2}
                              d="M17 8l4 4m0 0l-4 4m4-4H3"
                            />
                          </svg>
                        </a>
                      )}
                    </div>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      </section>
    );
  };

  // Render based on layout
  if (layout === 'staggered') {
    return renderStaggeredLayout();
  }

  return renderMinimalLayout();
};
