import { useState } from 'react';
import clsx from 'clsx';
import '../../styles/product.css';

// Default color options for products - light/pastel variants
const defaultColors = {
  orange: '#FB923C', // Orange-400 (lighter orange)
  teal: '#5EEAD4', // Teal-300 (lighter teal)
  pink: '#F9A8D4', // Pink-300 (lighter pink)
  purple: '#C4B5FD', // Purple-300 (light purple)
  magenta: '#F9A8D4', // Pink-300 (light magenta/pink)
  yellow: '#FDE047', // Yellow-300 (light yellow)
};

// Product Card Component for profile/lesson variant (overlapping cards with product image, colored top section, features)
const ProfileLessonCard = ({ product, index, currency }) => {
  // Get product data
  const title = product.title || '';
  const brand = product.brand || product.creator || '';
  const images = Array.isArray(product.images) 
    ? product.images 
    : product.image 
      ? [product.image]
      : [];
  const productImage = images.length > 0 ? images[0] : '';
  const features = product.features || product.categories || product.tags || [];
  const price = product.price;
  const topColor = product.topColor || product.color || '#FBCFE8'; // Default pink
  const imageBgColor = product.imageBgColor || product.avatarBgColor || '#DBEAFE'; // Default light blue

  // Get button configuration
  const buttonConfig = product.button || {};
  const buttonText = buttonConfig.text || 'BUY NOW';
  const buttonHref = buttonConfig.href || '#';
  const buttonOnClick = buttonConfig.onClick;

  const formatPrice = (price) => {
    if (typeof price === 'number' && price % 1 === 0) {
      return price;
    } else if (typeof price === 'number') {
      return price.toFixed(2);
    }
    return price;
  };

  // Calculate rotation and z-index for overlapping effect
  const rotations = index % 3 === 0 ? '-3deg' : index % 3 === 1 ? '0deg' : '3deg';
  const zIndex = index % 3 === 1 ? 10 : 5;

  return (
    <div
      className="w-full sm:w-[300px] md:w-[320px] lg:w-[340px] xl:w-[360px] relative bg-white rounded-3xl shadow-xl"
      style={{
        transform: `rotate(${rotations})`,
        zIndex: zIndex,
        transition: 'transform 0.3s ease',
      }}
      onMouseEnter={(e) => {
        e.currentTarget.style.transform = `rotate(0deg) scale(1.05)`;
        e.currentTarget.style.zIndex = '20';
      }}
      onMouseLeave={(e) => {
        e.currentTarget.style.transform = `rotate(${rotations}) scale(1)`;
        e.currentTarget.style.zIndex = zIndex.toString();
      }}
    >
      {/* Colored Top Section */}
      <div
        className="relative pt-16 sm:pt-20 md:pt-20 pb-4 sm:pb-5 md:pb-6 px-4 sm:px-5 md:px-6"
        style={{ backgroundColor: topColor }}
      >
        {/* Product Image - Protruding Above */}
        <div className="absolute -top-16 sm:-top-18 md:-top-20 left-1/2 transform -translate-x-1/2">
          <div
            className="w-32 h-32 sm:w-36 sm:h-36 md:w-40 md:h-40 rounded-full flex items-center justify-center border-4 border-white shadow-xl overflow-hidden"
            style={{ backgroundColor: imageBgColor }}
          >
            {productImage ? (
              <img
                src={productImage}
                alt={title}
                className="w-full h-full object-cover"
              />
            ) : (
              <div className="w-full h-full bg-gray-300 flex items-center justify-center">
                <svg className="w-20 h-20 text-gray-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z" />
                </svg>
              </div>
            )}
          </div>
        </div>

        {/* Product Title */}
        {title && (
            <h3 className="text-center font-bold text-lg sm:text-xl md:text-2xl text-gray-900 mt-2 sm:mt-3">
            {title}
          </h3>
        )}

        {/* Brand/Creator */}
        {brand && (
          <p className="text-center text-base text-gray-700 mt-2">
            {brand}
          </p>
        )}
      </div>

      {/* White Bottom Section with Features */}
      <div className="bg-white px-4 sm:px-5 md:px-6 pb-4 sm:pb-5 md:pb-6 pt-4 sm:pt-5 md:pt-6">
        {features.length > 0 ? (
          <div className="space-y-3 sm:space-y-4 mb-3 sm:mb-4">
            {features.slice(0, 3).map((feature, idx) => {
              const flagColors = ['#3B82F6', '#EF4444', '#A855F7']; // Blue, Red, Purple
              const flagColor = typeof feature === 'object' && feature.flagColor 
                ? feature.flagColor 
                : flagColors[idx % flagColors.length];
              const featureText = typeof feature === 'object' && feature.label 
                ? feature.label 
                : typeof feature === 'object' && feature.name 
                  ? feature.name 
                  : feature;
              
              return (
                <div key={idx} className="flex items-center gap-2 sm:gap-3">
                  {/* Flag Icon */}
                  <svg
                    className="w-4 h-4 sm:w-5 sm:h-5 flex-shrink-0"
                    fill={flagColor}
                    viewBox="0 0 24 24"
                  >
                    <path d="M3 2v20h2v-8h14l-2-5 2-5H5V2H3z"/>
                  </svg>
                  {/* Feature Text */}
                  <span className="text-sm sm:text-base text-gray-900">
                    {featureText}
                  </span>
                </div>
              );
            })}
          </div>
        ) : null}

        {/* Price and Button */}
        <div className="mt-auto pt-3 sm:pt-4 border-t border-gray-200">
          {price !== undefined && (
            <div className="mb-3 sm:mb-4">
              <span className="text-gray-900 font-bold text-xl sm:text-2xl">
                {formatPrice(price)}{currency}
              </span>
            </div>
          )}

          {/* Buy Now Button */}
          {buttonOnClick ? (
            <button
              onClick={buttonOnClick}
              className="w-full bg-gray-900 text-white px-4 sm:px-5 md:px-6 py-2.5 sm:py-3 rounded-lg font-semibold text-xs sm:text-sm md:text-base uppercase tracking-wide hover:bg-gray-800 transition-colors duration-200"
            >
              {buttonText}
            </button>
          ) : (
            <a
              href={buttonHref}
              className="block w-full bg-gray-900 text-white px-4 sm:px-5 md:px-6 py-2.5 sm:py-3 rounded-lg font-semibold text-xs sm:text-sm md:text-base uppercase tracking-wide hover:bg-gray-800 transition-colors duration-200 text-center"
            >
              {buttonText}
            </a>
          )}
        </div>
      </div>
    </div>
  );
};

// Product Card Component for horizontal variant (simple two-column layout: image left, details right)
const HorizontalProductCard = ({ product, currency }) => {
  // Get images array (support single image or multiple)
  const images = Array.isArray(product.images) 
    ? product.images 
    : product.image 
      ? [product.image]
      : [];

  // Get button configuration
  const buttonConfig = product.button || {};
  const buttonText = buttonConfig.text || 'BUY NOW';
  const buttonHref = buttonConfig.href || '#';
  const buttonOnClick = buttonConfig.onClick;

  // Get optional properties - black & white only
  const grayShade = product.grayShade || '#F3F4F6'; // Light gray for image section

  const formatPrice = (price) => {
    if (typeof price === 'number' && price % 1 === 0) {
      return price;
    } else if (typeof price === 'number') {
      return price.toFixed(2);
    }
    return price;
  };

  return (
    <div className="relative bg-white border border-gray-200 rounded-lg overflow-hidden flex flex-col sm:flex-row h-full">
      {/* Left Section - Product Image */}
      <div 
        className="w-full sm:w-2/5 lg:w-2/5 flex items-center justify-center p-3 sm:p-4 md:p-5"
        style={{ backgroundColor: grayShade }}
      >
        {images.length > 0 && images[0] ? (
          <img
            src={images[0]}
            alt={product.title || 'Product'}
            className="w-full h-auto object-contain"
            loading="lazy"
            onError={(e) => {
              e.target.style.display = 'none';
            }}
          />
        ) : (
          <div className="w-full h-32 flex items-center justify-center">
            <svg className="w-10 h-10 text-gray-300" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z" />
            </svg>
          </div>
        )}
      </div>

      {/* Right Section - Product Details */}
      <div className="w-full sm:w-3/5 bg-white p-4 sm:p-5 md:p-6 flex flex-col justify-between">
        <div>
          {/* Product Title */}
          {product.title && (
            <h3 className="text-gray-900 font-semibold text-base sm:text-lg md:text-xl mb-2 sm:mb-3">
              {product.title}
            </h3>
          )}

          {/* Product Description */}
          {product.description && (
            <p className="text-gray-600 text-xs sm:text-sm md:text-base mb-3 sm:mb-4 line-clamp-2 sm:line-clamp-3">
              {product.description}
            </p>
          )}
        </div>

        <div className="mt-auto flex items-center justify-between">
          {/* Price */}
          {product.price !== undefined && (
            <span className="text-gray-900 font-bold text-lg sm:text-xl md:text-2xl">
              {formatPrice(product.price)}{currency}
            </span>
          )}

          {/* Buy Now Button */}
          {buttonOnClick ? (
            <button
              onClick={buttonOnClick}
              className="bg-gray-900 text-white px-4 py-2 rounded text-sm font-medium hover:bg-gray-800 transition-colors"
            >
              {buttonText}
            </button>
          ) : (
            <a
              href={buttonHref}
              className="bg-gray-900 text-white px-4 py-2 rounded text-sm font-medium hover:bg-gray-800 transition-colors"
            >
              {buttonText}
            </a>
          )}
        </div>
      </div>
    </div>
  );
};

// Product Card Component for arch-top variant (arch-like rounded top, black & white only)
const ArchTopProductCard = ({ product, currency }) => {
  // Get gray shade (light gray for top section)
  const grayShade = product.grayShade || '#E5E7EB'; // Light gray by default
  
  // Get images array (support single image or multiple)
  const images = Array.isArray(product.images) 
    ? product.images 
    : product.image 
      ? [product.image]
      : [];

  // Get button configuration
  const buttonConfig = product.button || {};
  const buttonText = buttonConfig.text || 'BUY NOW';
  const buttonHref = buttonConfig.href || '#';
  const buttonOnClick = buttonConfig.onClick;

  // Favorite icon state
  const [isFavorite, setIsFavorite] = useState(product.favorite || false);

  const formatPrice = (price) => {
    if (typeof price === 'number' && price % 1 === 0) {
      return price;
    } else if (typeof price === 'number') {
      return price.toFixed(2);
    }
    return price;
  };

  return (
    <div className="relative bg-white rounded-3xl overflow-hidden shadow-lg hover:shadow-xl transition-shadow duration-300 flex flex-col h-full">
      {/* Arch-like Rounded Top Section with Image */}
      <div
        className="px-4 sm:px-5 md:px-6 pt-5 sm:pt-6 md:pt-7 pb-6 sm:pb-8 md:pb-10 relative min-h-[200px] sm:min-h-[220px] md:min-h-[240px] lg:min-h-[260px]"
        style={{ 
          backgroundColor: grayShade,
          clipPath: 'ellipse(130% 110% at 50% -10%)'
        }}
      >

        {/* Favorite Icon */}
        <div className="absolute top-4 right-4 sm:top-5 sm:right-5 z-10">
          <button
            onClick={() => setIsFavorite(!isFavorite)}
            className="w-9 h-9 rounded-full bg-transparent border-2 border-gray-600 flex items-center justify-center hover:border-gray-800 transition-colors"
            aria-label="Add to favorites"
          >
            <svg
              className={`w-3.5 h-3.5 sm:w-4 sm:h-4 ${isFavorite ? 'fill-red-500 text-red-500' : 'text-gray-600'}`}
              fill={isFavorite ? 'currentColor' : 'none'}
              stroke="currentColor"
              viewBox="0 0 24 24"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M4.318 6.318a4.5 4.5 0 000 6.364L12 20.364l7.682-7.682a4.5 4.5 0 00-6.364-6.364L12 7.636l-1.318-1.318a4.5 4.5 0 00-6.364 0z"
              />
            </svg>
          </button>
        </div>

        {/* New Badge */}
        {product.badge && (
          <div className="absolute top-4 left-1/2 transform -translate-x-1/2 sm:top-5 z-10">
            <span className="bg-gray-100 text-gray-900 text-xs font-semibold px-3 py-1 rounded-full border border-gray-300">
              {product.badge}
            </span>
          </div>
        )}

        {/* Product Image with Dashed Frame */}
        <div className="relative h-full flex items-center justify-center mt-6 sm:mt-8">
          <div className="relative" style={{ transform: 'rotate(-8deg)' }}>
            {/* Dashed Frame */}
            <div 
              className="absolute border-2 border-white border-dashed pointer-events-none w-32 h-32 sm:w-36 sm:h-36 md:w-40 md:h-40"
              style={{ 
                transform: 'translate(-50%, -50%)',
                left: '50%',
                top: '50%',
                zIndex: 1
              }}
            ></div>
            {images.length > 0 && images[0] ? (
              <img
                src={images[0]}
                alt={product.title || 'Product'}
                className="w-32 h-32 sm:w-36 sm:h-36 object-contain relative z-10"
                loading="lazy"
                onError={(e) => {
                  e.target.style.display = 'none';
                }}
              />
            ) : (
              <div className="w-28 h-28 sm:w-32 sm:h-32 md:w-36 md:h-36 flex items-center justify-center bg-white/30 rounded-lg relative z-10">
                <svg className="w-10 h-10 sm:w-12 sm:h-12 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z" />
                </svg>
              </div>
            )}
          </div>
        </div>
      </div>

      {/* White Bottom Section */}
      <div className="bg-white px-5 pb-5 pt-6 sm:px-6 sm:pb-6 sm:pt-7 flex flex-col flex-1">
        {/* Product Name */}
        {product.title && (
          <h3 className="text-gray-900 font-bold text-xl sm:text-2xl mb-3 text-center">
            {product.title}
          </h3>
        )}

        {/* Price Badge */}
        {product.price !== undefined && (
          <div className="mb-3 sm:mb-4 flex justify-center">
            <span className="inline-block px-4 sm:px-5 md:px-6 py-1.5 sm:py-2 rounded-full bg-gray-900 text-white text-xs sm:text-sm md:text-base font-semibold">
              {currency}{formatPrice(product.price)} USD
            </span>
          </div>
        )}

        {/* Separator Line */}
        <div className="flex justify-center mb-3 sm:mb-4">
          <div className="w-16 h-0.5 bg-gray-400"></div>
        </div>

        {/* Description */}
        {product.description && (
          <p className="text-gray-600 text-xs sm:text-sm md:text-base leading-relaxed text-center line-clamp-3 mb-3 sm:mb-4 md:mb-5">
            {product.description}
          </p>
        )}

        {/* Buy Now Button */}
        <div className="mt-auto">
          {buttonOnClick ? (
            <button
              onClick={buttonOnClick}
              className="w-full bg-gray-900 text-white px-4 py-2.5 rounded-lg font-semibold text-sm uppercase tracking-wide hover:bg-gray-800 transition-colors duration-200"
            >
              {buttonText}
            </button>
          ) : (
            <a
              href={buttonHref}
              className="block w-full bg-gray-900 text-white px-4 py-2.5 rounded-lg font-semibold text-sm uppercase tracking-wide hover:bg-gray-800 transition-colors duration-200 text-center"
            >
              {buttonText}
            </a>
          )}
        </div>
      </div>
    </div>
  );
};

// Product Card Component for beauty variant (image in top section, dual price, new badge)
const BeautyProductCard = ({ product, currency }) => {
  // Get gray shade (light gray for top section) - black & white only
  const grayShade = product.grayShade || '#E5E7EB'; // Light gray by default

  // Get images array (support single image or multiple)
  const images = Array.isArray(product.images) 
    ? product.images 
    : product.image 
      ? [product.image]
      : [];

  // Get button configuration
  const buttonConfig = product.button || {};
  const buttonText = buttonConfig.text || 'BUY NOW';
  const buttonHref = buttonConfig.href || '#';
  const buttonOnClick = buttonConfig.onClick;

  const formatPrice = (price) => {
    if (typeof price === 'number' && price % 1 === 0) {
      return price;
    } else if (typeof price === 'number') {
      return price.toFixed(1);
    }
    return price;
  };

  return (
    <div className="relative bg-white rounded-3xl overflow-hidden shadow-lg hover:shadow-xl transition-shadow duration-300 flex flex-col h-full">
      {/* Gray Top Section with Image */}
      <div
        className="px-4 sm:px-5 md:px-6 pt-4 sm:pt-5 md:pt-6 pb-5 sm:pb-6 md:pb-8 relative min-h-[180px] sm:min-h-[200px] md:min-h-[220px] lg:min-h-[240px]"
        style={{ backgroundColor: grayShade }}
      >
        {/* New Badge */}
        {product.badge && (
          <div className="absolute top-3 left-3 sm:top-4 sm:left-4 z-10">
            <span className="bg-gray-900 text-white text-xs sm:text-sm font-semibold px-2 sm:px-2.5 md:px-3 py-0.5 sm:py-1 rounded-md">
              {product.badge}
            </span>
          </div>
        )}

        {/* Circular Price Badge */}
        {product.price !== undefined && (
          <div className="absolute top-3 right-3 sm:top-4 sm:right-4 z-10">
            <div
              className="w-14 h-14 sm:w-16 sm:h-16 rounded-full bg-white flex items-center justify-center border-4 border-gray-400 font-bold text-sm sm:text-base"
            >
              <span className="text-gray-900">
                {currency}{formatPrice(product.price)}
              </span>
            </div>
          </div>
        )}

        {/* Product Image with Tilt */}
        <div className="relative h-full flex items-center justify-center mt-8 sm:mt-10">
          <div className="relative" style={{ transform: 'rotate(-5deg)' }}>
            {images.length > 0 && images[0] ? (
              <img
                src={images[0]}
                alt={product.title || 'Product'}
                className="w-28 h-28 sm:w-32 sm:h-32 md:w-36 md:h-40 object-contain"
                loading="lazy"
                onError={(e) => {
                  e.target.style.display = 'none';
                }}
              />
            ) : (
              <div className="w-28 h-28 sm:w-32 sm:h-32 md:w-36 md:h-40 flex items-center justify-center bg-white/30 rounded-full">
                <svg className="w-10 h-10 sm:w-12 sm:h-12 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z" />
                </svg>
              </div>
            )}
          </div>
        </div>

        {/* Category and Brand in Top Section */}
        <div className="absolute bottom-4 left-5 right-5 sm:left-6 sm:right-6 sm:bottom-6">
          {product.category && (
            <h3 className="text-gray-900 font-bold text-base sm:text-lg md:text-xl mb-1">
              {product.category}
            </h3>
          )}
          {product.brand && (
            <p className="text-gray-700 text-xs sm:text-sm line-clamp-2">
              {product.brand}
            </p>
          )}
        </div>
      </div>

      {/* White Bottom Section */}
      <div className="bg-white px-4 sm:px-5 md:px-6 pb-4 sm:pb-5 md:pb-6 pt-3 sm:pt-4 md:pt-5 flex flex-col flex-1">
        {/* Pill-shaped Price Tag */}
        {product.price !== undefined && (
          <div className="mb-2 sm:mb-3 md:mb-4 flex justify-center">
            <span className="inline-block px-3 sm:px-4 md:px-5 py-1 sm:py-1.5 rounded-full bg-gray-900 text-white text-xs sm:text-sm md:text-base font-semibold">
              {currency}{formatPrice(product.price)}
            </span>
          </div>
        )}

        {/* Features/Specifications in Bottom Section */}
        <div className="mb-3 sm:mb-4 md:mb-5">
          {product.title && (
            <h3 className="text-gray-900 font-bold text-base sm:text-lg md:text-xl mb-1 sm:mb-2">
              {product.title}
            </h3>
          )}
          {product.features && Array.isArray(product.features) && product.features.length > 0 ? (
            <ul className="text-gray-600 text-sm leading-relaxed space-y-1">
              {product.features.slice(0, 3).map((feature, idx) => (
                <li key={idx} className="flex items-center gap-2">
                  <span className="text-gray-900"></span>
                  <span>{typeof feature === 'string' ? feature : feature.label || feature.name}</span>
                </li>
              ))}
            </ul>
          ) : product.specifications && Array.isArray(product.specifications) && product.specifications.length > 0 ? (
            <ul className="text-gray-600 text-sm leading-relaxed space-y-1">
              {product.specifications.slice(0, 3).map((spec, idx) => (
                <li key={idx} className="flex items-center gap-2">
                  <span className="text-gray-900"></span>
                  <span>{typeof spec === 'string' ? spec : spec.label || spec.name}</span>
                </li>
              ))}
            </ul>
          ) : null}
        </div>

        {/* Buy Now Button */}
        <div className="mt-auto">
          {buttonOnClick ? (
            <button
              onClick={buttonOnClick}
              className="w-full bg-gray-900 text-white px-4 py-2.5 rounded-lg font-semibold text-sm uppercase tracking-wide hover:bg-gray-800 transition-colors duration-200"
            >
              {buttonText}
            </button>
          ) : (
            <a
              href={buttonHref}
              className="block w-full bg-gray-900 text-white px-4 py-2.5 rounded-lg font-semibold text-sm uppercase tracking-wide hover:bg-gray-800 transition-colors duration-200 text-center"
            >
              {buttonText}
            </a>
          )}
        </div>
      </div>
    </div>
  );
};

// Product Card Component for split-color variant
const SplitColorProductCard = ({ product, currency }) => {
  const [currentImageIndex, setCurrentImageIndex] = useState(0);
  
  // Get gray shade (default gray for top section) - black & white only
  const grayShade = product.grayShade || '#E5E7EB'; // Light gray by default

  // Get images array (support single image or multiple)
  const images = Array.isArray(product.images) 
    ? product.images 
    : product.image 
      ? [product.image]
      : [];

  // Pagination dots count (use images length or default to 4)
  const dotsCount = product.paginationDots || (images.length > 1 ? images.length : 4);

  // Get button configuration
  const buttonConfig = product.button || {};
  const buttonText = buttonConfig.text || 'ADD TO CART';
  const buttonHref = buttonConfig.href || '#';
  const buttonOnClick = buttonConfig.onClick;


  return (
    <div className="relative bg-white rounded-3xl overflow-hidden shadow-lg hover:shadow-xl transition-shadow duration-300 flex flex-col h-full">
      {/* Gray Top Section */}
      <div
        className="px-4 sm:px-6 md:px-8 pt-5 sm:pt-6 md:pt-8 pb-6 sm:pb-8 md:pb-10 relative"
        style={{ backgroundColor: grayShade }}
      >
        {/* Circular Price Badge */}
        {product.price !== undefined && (
          <div className="absolute top-4 right-4 sm:top-6 sm:right-6">
            <div
              className="w-14 h-14 sm:w-16 sm:h-16 md:w-[72px] md:h-[72px] lg:w-20 lg:h-20 rounded-full bg-white flex items-center justify-center border-2 md:border-4 border-gray-400 font-bold text-sm sm:text-base md:text-lg lg:text-xl"
            >
              <span className="text-gray-900">
                {currency}{typeof product.price === 'number' && product.price % 1 === 0 
                  ? product.price 
                  : typeof product.price === 'number' 
                    ? product.price.toFixed(2) 
                    : product.price}
              </span>
            </div>
          </div>
        )}

        {/* Title and Description */}
        <div className="pr-20 sm:pr-24">
          {product.title && (
            <h3 className="text-gray-900 font-bold text-xl sm:text-2xl mb-2">
              {product.title}
            </h3>
          )}
          {product.description && (
            <p className="text-gray-700 text-xs sm:text-sm md:text-base">
              {product.description}
            </p>
          )}
        </div>
      </div>

      {/* White Bottom Section */}
      <div className="bg-white px-6 pb-6 pt-8 sm:px-8 sm:pb-8 sm:pt-10 flex flex-col items-center flex-1">
        {/* Circular Image Container */}
        <div className="relative mb-6 sm:mb-8">
          <div
            className="w-48 h-48 sm:w-56 sm:h-56 rounded-full flex items-center justify-center overflow-hidden mx-auto"
            style={{ backgroundColor: product.imageBgColor || '#F3F4F6' }}
          >
            {images.length > 0 && images[currentImageIndex] ? (
              <img
                src={images[currentImageIndex]}
                alt={product.title || 'Product'}
                className="w-full h-full object-contain p-3 sm:p-4 md:p-5"
                loading="lazy"
                onError={(e) => {
                  e.target.style.display = 'none';
                }}
              />
            ) : (
              <div className="w-full h-full flex items-center justify-center">
                <svg
                  className="w-12 h-12 sm:w-14 sm:h-14 md:w-16 md:h-16 text-gray-400"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z"
                  />
                </svg>
              </div>
            )}
          </div>
        </div>

        {/* Pagination Dots */}
        <div className="flex gap-2 mb-6 sm:mb-8 justify-center">
          {Array.from({ length: dotsCount }).map((_, dotIndex) => (
            <button
              key={dotIndex}
              onClick={() => {
                if (images.length > 1) {
                  setCurrentImageIndex(dotIndex % images.length);
                }
              }}
              className={clsx(
                'w-1.5 h-1.5 sm:w-2 sm:h-2 rounded-full transition-colors duration-200',
                dotIndex === (currentImageIndex % dotsCount) || (images.length <= 1 && dotIndex === 0)
                  ? 'bg-gray-600 opacity-100'
                  : 'bg-gray-300 opacity-40'
              )}
              aria-label={`View image ${dotIndex + 1}`}
            />
          ))}
        </div>

        {/* Add to Cart Button */}
        <div className="w-full mt-auto">
          {buttonOnClick ? (
            <button
              onClick={buttonOnClick}
              className="w-full bg-gray-900 text-white px-6 py-3 rounded-lg font-semibold text-sm sm:text-base uppercase tracking-wide hover:bg-gray-800 transition-colors duration-200"
            >
              {buttonText}
            </button>
          ) : (
            <a
              href={buttonHref}
              className="block w-full bg-gray-900 text-white px-6 py-3 rounded-lg font-semibold text-sm sm:text-base uppercase tracking-wide hover:bg-gray-800 transition-colors duration-200 text-center"
            >
              {buttonText}
            </a>
          )}
        </div>
      </div>
    </div>
  );
};

/**
 * ProductStandard Component - Product cards with split-color circular image variant
 * @param {Object} props - Component props
 * @param {Object} props.data - Product section configuration data
 * @param {string} [props.data.title] - Main heading text
 * @param {string} [props.data.subtitle] - Subtitle text displayed above title
 * @param {string} [props.data.description] - Description text displayed below title
 * @param {Array} props.data.products - Array of product items
 * @param {string} [props.data.variant='split-color'] - Style variant: 'split-color' | 'beauty' | 'arch-top' | 'horizontal'
 * @param {string} [props.data.alignment='center'] - Header text alignment: 'left' | 'center' | 'right'
 * @param {number} [props.data.columns=3] - Number of columns for grid layout (1-4)
 * @param {string} [props.data.currency='$'] - Currency symbol
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute for the section element
 * @param {boolean} [props.loader=false] - Show loading state
 */
export const ProductStandard = ({ data, className, id, loader = false }) => {
  // Safety check for data
  if (!data || typeof data !== 'object') {
    console.error('ProductStandard: data prop is required and must be an object');
    return null;
  }

  const {
    title,
    subtitle,
    description,
    products = [],
    variant = 'split-color',
    alignment = 'center',
    columns = 3,
    currency = '$',
    className: dataClassName,
  } = data;

  const alignmentClasses = {
    left: 'text-left items-start',
    center: 'text-center items-center',
    right: 'text-right items-end',
  };

  const gridColumnsClasses = {
    1: 'grid-cols-1',
    2: 'grid-cols-1 sm:grid-cols-2',
    3: 'grid-cols-1 sm:grid-cols-1 lg:grid-cols-3',
    4: 'grid-cols-1 sm:grid-cols-1 lg:grid-cols-4',
  };


  return (
    <section
      id={id}
      className={clsx(
        'w-full py-8 sm:py-12 md:py-16 lg:py-20 xl:py-24 px-4 sm:px-6 md:px-8 lg:px-10 xl:px-12',
        className,
        dataClassName
      )}
    >
      <div className="max-w-7xl mx-auto w-full">
        {/* Header Section */}
        {(title || subtitle || description) && (
          <div
            className={clsx(
              'mb-8 sm:mb-10 md:mb-12 lg:mb-16 flex flex-col',
              alignmentClasses[alignment] || alignmentClasses.center
            )}
          >
            {subtitle && (
              <p className="text-sm sm:text-base font-semibold text-primary-600 uppercase tracking-wide mb-2">
                {subtitle}
              </p>
            )}
            {title && (
              <h2 className="text-2xl sm:text-3xl md:text-4xl lg:text-5xl xl:text-6xl font-bold text-gray-900 mb-3 sm:mb-4">
                {title}
              </h2>
            )}
            {description && (
              <p className="text-sm sm:text-base md:text-lg text-gray-600 max-w-2xl mx-auto px-4 sm:px-0">
                {description}
              </p>
            )}
          </div>
        )}

        {/* Products Grid */}
        {products.length > 0 && (
          <div
            className={clsx(
              variant === 'overlapping' 
                ? 'flex flex-wrap justify-center gap-4 sm:gap-5 md:gap-6 items-start mt-12 sm:mt-16 md:mt-24 lg:mt-32 xl:mt-[8rem]'
                : 'grid gap-4 sm:gap-5 md:gap-6 lg:gap-8',
              variant !== 'overlapping' && (gridColumnsClasses[columns] || gridColumnsClasses[3])
            )}
          >
            {products.map((product, index) => {
              if (variant === 'overlapping' || variant === 'profile-lesson') {
                return (
                  <ProfileLessonCard
                    key={index}
                    product={product}
                    index={index}
                    currency={currency}
                  />
                );
              }
              if (variant === 'horizontal') {
                return (
                  <HorizontalProductCard
                    key={index}
                    product={product}
                    currency={currency}
                  />
                );
              }
              if (variant === 'arch-top') {
                return (
                  <ArchTopProductCard
                    key={index}
                    product={product}
                    currency={currency}
                  />
                );
              }
              if (variant === 'beauty') {
                return (
                  <BeautyProductCard
                    key={index}
                    product={product}
                    currency={currency}
                  />
                );
              }
              // Default to split-color variant
              return (
                <SplitColorProductCard
                  key={index}
                  product={product}
                  currency={currency}
                />
              );
            })}
          </div>
        )}

        {/* Empty State */}
        {products.length === 0 && (
          <div className="text-center py-12">
            <p className="text-gray-500 text-sm sm:text-base md:text-lg">No products available</p>
          </div>
        )}
      </div>
    </section>
  );
};
