import { useState, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/product.css';

/**
 * ProductSingleMinimal Component
 * @param {Object} props - Component props
 * @param {Object} props.data - Product configuration data
 * @param {string} props.data.title - Product title
 * @param {string} props.data.subtitle - Product subtitle/category
 * @param {string} props.data.description - Product description
 * @param {number} props.data.price - Product price
 * @param {number} [props.data.originalPrice] - Original price for showing discount
 * @param {string|string[]} props.data.image - Product image URL(s)
 * @param {Array} [props.data.images] - Array of product images for gallery
 * @param {Array} [props.data.features] - Array of product features
 * @param {Array} [props.data.specifications] - Array of product specifications
 * @param {number} [props.data.rating] - Product rating (0-5)
 * @param {string} [props.data.badge] - Badge text ("New", "Sale", "Hot", etc.)
 * @param {boolean} [props.data.inStock=true] - Stock availability
 * @param {Object} [props.data.button] - CTA button configuration
 * @param {string} [props.data.variant='clean'] - Style variant: 'clean' | 'focused' | 'overlay' | 'sidebar' | 'gallery'
 * @param {string} [props.data.currency='$'] - Currency symbol
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute for the section element
 */
export const ProductSingleMinimal = ({ data, className, id }) => {
  // Safety check for data
  if (!data || typeof data !== 'object') {
    console.error('ProductSingleMinimal: data prop is required and must be an object');
    return null;
  }

  const [selectedImage, setSelectedImage] = useState(0);
  const [activeTab, setActiveTab] = useState('description');
  const [sidebarOpen, setSidebarOpen] = useState(false);

  const {
    title,
    subtitle,
    description,
    price,
    originalPrice,
    image,
    images = [],
    features = [],
    specifications = [],
    rating,
    badge,
    inStock = true,
    button = { text: "Add to Cart", href: "#", variant: "primary" },
    variant = 'clean',
    currency = '$'
  } = data;

  // Combine single image and images array
  const allImages = image ? [image, ...images] : images;

  // Rating display
  const renderRating = (rating) => {
    if (!rating) return null;
    const stars = [];
    const fullStars = Math.floor(rating);
    const hasHalfStar = rating % 1 !== 0;

    for (let i = 0; i < fullStars; i++) {
      stars.push(
        <svg key={i} className="w-4 h-4 text-gray-600 fill-current" viewBox="0 0 20 20">
          <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
        </svg>
      );
    }

    if (hasHalfStar) {
      stars.push(
        <svg key="half" className="w-4 h-4 text-gray-600 fill-current" viewBox="0 0 20 20">
          <defs>
            <linearGradient id="halfStar">
              <stop offset="50%" stopColor="currentColor" />
              <stop offset="50%" stopColor="transparent" />
            </linearGradient>
          </defs>
          <path fill="url(#halfStar)" d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
        </svg>
      );
    }

    const emptyStars = 5 - Math.ceil(rating);
    for (let i = 0; i < emptyStars; i++) {
      stars.push(
        <svg key={`empty-${i}`} className="w-4 h-4 text-gray-300 fill-current" viewBox="0 0 20 20">
          <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
        </svg>
      );
    }

    return (
      <div className="flex items-center space-x-1">
        {stars}
        <span className="text-sm text-gray-500 ml-2">({rating})</span>
      </div>
    );
  };

  // Clean variant - Minimalist clean design
  if (variant === 'clean') {
    return (
      <section
        id={id}
        className={clsx(
          "product-single-minimal-clean bg-white",
          className
        )}
      >
        <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 py-16 lg:py-24">
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-16">
            {/* Product Image */}
            <div className="space-y-6">
              <div className="aspect-square bg-gray-50 rounded-lg overflow-hidden">
                {allImages.length > 0 && (
                  <img
                    src={allImages[selectedImage]}
                    alt={title}
                    className="w-full h-full object-cover"
                  />
                )}
              </div>
              {allImages.length > 1 && (
                <div className="grid grid-cols-4 gap-4">
                  {allImages.slice(0, 4).map((img, index) => (
                    <button
                      key={index}
                      onClick={() => setSelectedImage(index)}
                      className={clsx(
                        "aspect-square bg-gray-50 rounded-lg overflow-hidden border-2 transition-colors",
                        selectedImage === index ? "border-gray-900" : "border-transparent"
                      )}
                    >
                      <img
                        src={img}
                        alt={`${title} ${index + 1}`}
                        className="w-full h-full object-cover"
                      />
                    </button>
                  ))}
                </div>
              )}
            </div>

            {/* Product Info */}
            <div className="space-y-8">
              {subtitle && (
                <p className="text-sm text-gray-500 uppercase tracking-wider font-medium">
                  {subtitle}
                </p>
              )}
              <h1 className="text-4xl font-light text-gray-900 leading-tight">
                {title}
              </h1>

              {rating && renderRating(rating)}

              <div className="flex items-baseline space-x-4">
                <span className="text-3xl font-light text-gray-900">
                  {currency}{price}
                </span>
                {originalPrice && (
                  <span className="text-xl text-gray-400 line-through">
                    {currency}{originalPrice}
                  </span>
                )}
                {badge && (
                  <span className="text-sm font-medium text-gray-600 uppercase tracking-wider">
                    {badge}
                  </span>
                )}
              </div>

              <div className="flex items-center space-x-2">
                <span className={clsx(
                  "text-sm font-medium uppercase tracking-wider",
                  inStock ? "text-gray-600" : "text-red-600"
                )}>
                  {inStock ? "In Stock" : "Out of Stock"}
                </span>
              </div>

              <p className="text-gray-600 leading-relaxed text-lg">
                {description}
              </p>

              <div className="pt-8">
                <button
                  className="w-full py-4 px-8 border border-gray-900 text-gray-900 hover:bg-gray-900 hover:text-white transition-colors font-medium text-lg"
                >
                  {button.text}
                </button>
              </div>

              {features.length > 0 && (
                <div className="pt-8 space-y-4">
                  <h3 className="text-lg font-medium text-gray-900">Features</h3>
                  <ul className="space-y-2">
                    {features.map((feature, index) => (
                      <li key={index} className="flex items-center space-x-3 text-gray-600">
                        <span className="text-gray-400"></span>
                        <span>{feature}</span>
                      </li>
                    ))}
                  </ul>
                </div>
              )}
            </div>
          </div>
        </div>
      </section>
    );
  }

  // Focused variant - Focused on product with centered layout
  if (variant === 'focused') {
    return (
      <section
        id={id}
        className={clsx(
          "product-single-minimal-focused bg-gray-50",
          className
        )}
      >
        <div className="max-w-2xl mx-auto px-4 sm:px-6 lg:px-8 py-20 lg:py-32">
          <div className="text-center space-y-12">
            {/* Product Image */}
            <div className="relative">
              <div className="aspect-square bg-white rounded-2xl shadow-sm overflow-hidden mx-auto max-w-md">
                {allImages.length > 0 && (
                  <img
                    src={allImages[selectedImage]}
                    alt={title}
                    className="w-full h-full object-cover"
                  />
                )}
              </div>
              {badge && (
                <div className="absolute top-4 right-4">
                  <span className="inline-flex items-center px-3 py-1 rounded-full text-xs font-medium bg-gray-900 text-white">
                    {badge}
                  </span>
                </div>
              )}
            </div>

            {/* Product Info */}
            <div className="space-y-6">
              {subtitle && (
                <p className="text-sm text-gray-500 uppercase tracking-wider font-medium">
                  {subtitle}
                </p>
              )}
              <h1 className="text-5xl font-light text-gray-900 leading-tight">
                {title}
              </h1>

              {rating && (
                <div className="flex justify-center">
                  {renderRating(rating)}
                </div>
              )}

              <div className="flex items-baseline justify-center space-x-4">
                <span className="text-4xl font-light text-gray-900">
                  {currency}{price}
                </span>
                {originalPrice && (
                  <span className="text-2xl text-gray-400 line-through">
                    {currency}{originalPrice}
                  </span>
                )}
              </div>

              <div className="flex justify-center">
                <span className={clsx(
                  "text-sm font-medium uppercase tracking-wider",
                  inStock ? "text-gray-600" : "text-red-600"
                )}>
                  {inStock ? "Available" : "Unavailable"}
                </span>
              </div>

              <div className="max-w-lg mx-auto">
                <p className="text-gray-600 leading-relaxed text-lg">
                  {description}
                </p>
              </div>

              <div className="pt-4">
                <button
                  className="px-16 py-4 bg-gray-900 text-white hover:bg-gray-800 transition-colors font-medium text-lg"
                >
                  {button.text}
                </button>
              </div>

              {features.length > 0 && (
                <div className="pt-12 space-y-6">
                  <h3 className="text-xl font-medium text-gray-900">Product Features</h3>
                  <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                    {features.map((feature, index) => (
                      <div key={index} className="flex items-center space-x-3 text-gray-600">
                        <span className="text-gray-400 text-lg"></span>
                        <span className="text-sm">{feature}</span>
                      </div>
                    ))}
                  </div>
                </div>
              )}
            </div>

            {/* Image Gallery */}
            {allImages.length > 1 && (
              <div className="pt-12">
                <div className="flex justify-center space-x-4">
                  {allImages.slice(0, 5).map((img, index) => (
                    <button
                      key={index}
                      onClick={() => setSelectedImage(index)}
                      className={clsx(
                        "w-16 h-16 rounded-lg overflow-hidden border-2 transition-colors",
                        selectedImage === index ? "border-gray-900" : "border-gray-200"
                      )}
                    >
                      <img
                        src={img}
                        alt={`${title} ${index + 1}`}
                        className="w-full h-full object-cover"
                      />
                    </button>
                  ))}
                </div>
              </div>
            )}
          </div>
        </div>
      </section>
    );
  }

  // Overlay variant - Image with overlay information
  if (variant === 'overlay') {
    return (
      <section
        id={id}
        className={clsx(
          "product-single-minimal-overlay relative min-h-screen",
          className
        )}
      >
        {/* Background Image */}
        <div className="absolute inset-0">
          {allImages.length > 0 && (
            <img
              src={allImages[selectedImage]}
              alt={title}
              className="w-full h-full object-cover"
            />
          )}
          <div className="absolute inset-0 bg-black bg-opacity-40"></div>
        </div>

        {/* Content Overlay */}
        <div className="relative z-10 flex items-center justify-center min-h-screen">
          <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 text-center text-white">
            <div className="space-y-8">
              {subtitle && (
                <p className="text-sm uppercase tracking-wider font-medium text-white/80">
                  {subtitle}
                </p>
              )}
              <h1 className="text-6xl lg:text-7xl font-light leading-tight">
                {title}
              </h1>

              <div className="flex justify-center">
                <div className="bg-white/10 backdrop-blur-sm rounded-lg px-8 py-4">
                  <div className="flex items-center justify-center space-x-6">
                    <span className="text-4xl font-light">
                      {currency}{price}
                    </span>
                    {originalPrice && (
                      <span className="text-2xl text-white/60 line-through">
                        {currency}{originalPrice}
                      </span>
                    )}
                    {badge && (
                      <span className="text-sm font-medium bg-white/20 px-3 py-1 rounded-full">
                        {badge}
                      </span>
                    )}
                  </div>
                </div>
              </div>

              <div className="max-w-2xl mx-auto">
                <p className="text-xl leading-relaxed text-white/90">
                  {description}
                </p>
              </div>

              <div className="flex justify-center space-x-4">
                <div className="bg-white/10 backdrop-blur-sm rounded-lg px-6 py-3">
                  <span className={clsx(
                    "text-sm font-medium uppercase tracking-wider",
                    inStock ? "text-white" : "text-red-300"
                  )}>
                    {inStock ? "In Stock" : "Out of Stock"}
                  </span>
                </div>
                <button
                  className="px-12 py-4 bg-white text-gray-900 hover:bg-gray-100 transition-colors font-medium text-lg"
                >
                  {button.text}
                </button>
              </div>

              {features.length > 0 && (
                <div className="pt-12">
                  <div className="grid grid-cols-1 sm:grid-cols-3 gap-6 max-w-4xl mx-auto">
                    {features.slice(0, 3).map((feature, index) => (
                      <div key={index} className="bg-white/10 backdrop-blur-sm rounded-lg p-6">
                        <div className="flex items-center space-x-3">
                          <span className="text-white/60"></span>
                          <span className="text-white text-sm">{feature}</span>
                        </div>
                      </div>
                    ))}
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>

        {/* Thumbnail Navigation */}
        {allImages.length > 1 && (
          <div className="absolute bottom-8 left-1/2 transform -translate-x-1/2">
            <div className="flex space-x-4 bg-black/20 backdrop-blur-sm rounded-lg p-4">
              {allImages.slice(0, 5).map((img, index) => (
                <button
                  key={index}
                  onClick={() => setSelectedImage(index)}
                  className={clsx(
                    "w-12 h-12 rounded-lg overflow-hidden border-2 transition-colors",
                    selectedImage === index ? "border-white" : "border-white/30"
                  )}
                >
                  <img
                    src={img}
                    alt={`${title} ${index + 1}`}
                    className="w-full h-full object-cover"
                  />
                </button>
              ))}
            </div>
          </div>
        )}
      </section>
    );
  }

  // Sidebar variant - Content in sidebar layout
  if (variant === 'sidebar') {
    return (
      <section
        id={id}
        className={clsx(
          "product-single-minimal-sidebar bg-white",
          className
        )}
      >
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="grid grid-cols-1 lg:grid-cols-3 gap-12">
            {/* Main Content - Product Image */}
            <div className="lg:col-span-2">
              <div className="aspect-[4/3] bg-gray-50 rounded-lg overflow-hidden">
                {allImages.length > 0 && (
                  <img
                    src={allImages[selectedImage]}
                    alt={title}
                    className="w-full h-full object-cover"
                  />
                )}
              </div>

              {allImages.length > 1 && (
                <div className="flex space-x-4 mt-6 overflow-x-auto pb-2">
                  {allImages.map((img, index) => (
                    <button
                      key={index}
                      onClick={() => setSelectedImage(index)}
                      className={clsx(
                        "flex-shrink-0 w-20 h-20 rounded-lg overflow-hidden border-2 transition-colors",
                        selectedImage === index ? "border-gray-900" : "border-gray-200"
                      )}
                    >
                      <img
                        src={img}
                        alt={`${title} ${index + 1}`}
                        className="w-full h-full object-cover"
                      />
                    </button>
                  ))}
                </div>
              )}
            </div>

            {/* Sidebar - Product Info */}
            <div className="space-y-8">
              {subtitle && (
                <p className="text-sm text-gray-500 uppercase tracking-wider font-medium">
                  {subtitle}
                </p>
              )}
              <h1 className="text-3xl font-light text-gray-900 leading-tight">
                {title}
              </h1>

              {rating && renderRating(rating)}

              <div className="space-y-4">
                <div className="flex items-baseline space-x-3">
                  <span className="text-3xl font-light text-gray-900">
                    {currency}{price}
                  </span>
                  {originalPrice && (
                    <span className="text-xl text-gray-400 line-through">
                      {currency}{originalPrice}
                    </span>
                  )}
                </div>

                {badge && (
                  <span className="text-sm font-medium text-gray-600 uppercase tracking-wider">
                    {badge}
                  </span>
                )}

                <div>
                  <span className={clsx(
                    "text-sm font-medium uppercase tracking-wider",
                    inStock ? "text-gray-600" : "text-red-600"
                  )}>
                    {inStock ? "In Stock" : "Out of Stock"}
                  </span>
                </div>
              </div>

              <p className="text-gray-600 leading-relaxed">
                {description}
              </p>

              <button
                className="w-full py-4 px-6 border border-gray-900 text-gray-900 hover:bg-gray-900 hover:text-white transition-colors font-medium"
              >
                {button.text}
              </button>

              {features.length > 0 && (
                <div className="space-y-4">
                  <h3 className="text-lg font-medium text-gray-900">Features</h3>
                  <ul className="space-y-3">
                    {features.map((feature, index) => (
                      <li key={index} className="flex items-start space-x-2 text-gray-600">
                        <span className="text-gray-400 mt-1"></span>
                        <span className="text-sm leading-relaxed">{feature}</span>
                      </li>
                    ))}
                  </ul>
                </div>
              )}

              {specifications.length > 0 && (
                <div className="space-y-4 pt-6 border-t border-gray-200">
                  <h3 className="text-lg font-medium text-gray-900">Specifications</h3>
                  <div className="space-y-2">
                    {specifications.slice(0, 4).map((spec, index) => (
                      <div key={index} className="flex justify-between text-sm">
                        <span className="text-gray-600">{spec.label}</span>
                        <span className="text-gray-900 font-medium">{spec.value}</span>
                      </div>
                    ))}
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>
      </section>
    );
  }

  // Gallery variant - Full gallery experience
  if (variant === 'gallery') {
    return (
      <section
        id={id}
        className={clsx(
          "product-single-minimal-gallery bg-white",
          className
        )}
      >
        {/* Hero Section with Large Image */}
        <div className="relative">
          <div className="aspect-[16/9] bg-gray-50 overflow-hidden">
            {allImages.length > 0 && (
              <img
                src={allImages[selectedImage]}
                alt={title}
                className="w-full h-full object-cover"
              />
            )}
          </div>

          {/* Overlay Content */}
          <div className="absolute inset-0 flex items-end">
            <div className="w-full bg-gradient-to-t from-black/60 via-transparent to-transparent p-8 lg:p-16">
              <div className="max-w-4xl mx-auto">
                <div className="text-white space-y-4">
                  {subtitle && (
                    <p className="text-sm uppercase tracking-wider font-medium text-white/80">
                      {subtitle}
                    </p>
                  )}
                  <h1 className="text-5xl lg:text-6xl font-light leading-tight">
                    {title}
                  </h1>

                  <div className="flex items-center space-x-6 pt-4">
                    <span className="text-4xl font-light">
                      {currency}{price}
                    </span>
                    {originalPrice && (
                      <span className="text-2xl text-white/60 line-through">
                        {currency}{originalPrice}
                      </span>
                    )}
                    {badge && (
                      <span className="text-sm font-medium bg-white/20 px-3 py-1 rounded-full">
                        {badge}
                      </span>
                    )}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Content Section */}
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-16 lg:py-24">
          <div className="grid grid-cols-1 lg:grid-cols-3 gap-16">
            {/* Product Info */}
            <div className="lg:col-span-2 space-y-8">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
                <div className="space-y-6">
                  <h2 className="text-2xl font-light text-gray-900">Product Details</h2>
                  <p className="text-gray-600 leading-relaxed text-lg">
                    {description}
                  </p>

                  <div className="flex items-center space-x-4">
                    {rating && renderRating(rating)}
                    <span className={clsx(
                      "text-sm font-medium uppercase tracking-wider",
                      inStock ? "text-gray-600" : "text-red-600"
                    )}>
                      {inStock ? "Available" : "Sold Out"}
                    </span>
                  </div>
                </div>

                {features.length > 0 && (
                  <div className="space-y-6">
                    <h3 className="text-xl font-medium text-gray-900">Key Features</h3>
                    <ul className="space-y-3">
                      {features.map((feature, index) => (
                        <li key={index} className="flex items-center space-x-3 text-gray-600">
                          <span className="text-gray-400"></span>
                          <span>{feature}</span>
                        </li>
                      ))}
                    </ul>
                  </div>
                )}
              </div>

              {/* Gallery Grid */}
              {allImages.length > 1 && (
                <div className="pt-12">
                  <h3 className="text-xl font-medium text-gray-900 mb-8">Product Gallery</h3>
                  <div className="grid grid-cols-2 md:grid-cols-3 gap-6">
                    {allImages.map((img, index) => (
                      <button
                        key={index}
                        onClick={() => setSelectedImage(index)}
                        className={clsx(
                          "aspect-square bg-gray-50 rounded-lg overflow-hidden transition-all duration-300 transform hover:scale-105",
                          selectedImage === index ? "ring-4 ring-gray-900" : ""
                        )}
                      >
                        <img
                          src={img}
                          alt={`${title} ${index + 1}`}
                          className="w-full h-full object-cover"
                        />
                      </button>
                    ))}
                  </div>
                </div>
              )}

              {/* Specifications */}
              {specifications.length > 0 && (
                <div className="pt-12 space-y-6">
                  <h3 className="text-xl font-medium text-gray-900">Specifications</h3>
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    {specifications.map((spec, index) => (
                      <div key={index} className="flex justify-between py-3 px-4 bg-gray-50 rounded-lg">
                        <span className="text-gray-600 font-medium">{spec.label}</span>
                        <span className="text-gray-900">{spec.value}</span>
                      </div>
                    ))}
                  </div>
                </div>
              )}
            </div>

            {/* Sidebar CTA */}
            <div className="space-y-8">
              <div className="bg-gray-50 rounded-lg p-8 space-y-6">
                <div className="text-center">
                  <div className="text-3xl font-light text-gray-900 mb-2">
                    {currency}{price}
                  </div>
                  {originalPrice && (
                    <div className="text-xl text-gray-400 line-through">
                      {currency}{originalPrice}
                    </div>
                  )}
                </div>

                <button
                  className="w-full py-4 px-6 bg-gray-900 text-white hover:bg-gray-800 transition-colors font-medium text-lg"
                >
                  {button.text}
                </button>

                <div className="text-center">
                  <span className="text-sm text-gray-500">
                    Free shipping on orders over $100
                  </span>
                </div>
              </div>

              {/* Additional Info */}
              <div className="space-y-4 text-sm text-gray-600">
                <div className="flex items-center space-x-2">
                  <svg className="w-4 h-4 text-green-500" fill="currentColor" viewBox="0 0 20 20">
                    <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                  </svg>
                  <span>30-day return policy</span>
                </div>
                <div className="flex items-center space-x-2">
                  <svg className="w-4 h-4 text-green-500" fill="currentColor" viewBox="0 0 20 20">
                    <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                  </svg>
                  <span>2-year warranty</span>
                </div>
                <div className="flex items-center space-x-2">
                  <svg className="w-4 h-4 text-green-500" fill="currentColor" viewBox="0 0 20 20">
                    <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                  </svg>
                  <span>Secure checkout</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    );
  }

  return null;
};
