import { useState, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/product.css';

/**
 * ProductSingleClassic Component
 * @param {Object} props - Component props
 * @param {Object} props.data - Product configuration data
 * @param {string} props.data.title - Product title
 * @param {string} props.data.subtitle - Product subtitle/category
 * @param {string} props.data.description - Product description
 * @param {number} props.data.price - Product price
 * @param {number} [props.data.originalPrice] - Original price for showing discount
 * @param {string|string[]} props.data.image - Product image URL(s)
 * @param {Array} [props.data.images] - Array of product images for gallery
 * @param {Array} [props.data.features] - Array of product features
 * @param {Array} [props.data.specifications] - Array of product specifications
 * @param {number} [props.data.rating] - Product rating (0-5)
 * @param {string} [props.data.badge] - Badge text ("New", "Sale", "Hot", etc.)
 * @param {boolean} [props.data.inStock=true] - Stock availability
 * @param {Object} [props.data.button] - CTA button configuration
 * @param {string} [props.data.variant='default'] - Style variant: 'default' | 'centered' | 'split' | 'compact' | 'detailed'
 * @param {string} [props.data.currency='$'] - Currency symbol
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute for the section element
 */
export const ProductSingleClassic = ({ data, className, id }) => {
  // Safety check for data
  if (!data || typeof data !== 'object') {
    console.error('ProductSingleClassic: data prop is required and must be an object');
    return null;
  }

  const [selectedImage, setSelectedImage] = useState(0);
  const [activeTab, setActiveTab] = useState('description');

  const {
    title,
    subtitle,
    description,
    price,
    originalPrice,
    image,
    images = [],
    features = [],
    specifications = [],
    rating,
    badge,
    inStock = true,
    button = { text: "Add to Cart", href: "#", variant: "primary" },
    variant = 'default',
    currency = '$'
  } = data;

  // Combine single image and images array
  const allImages = image ? [image, ...images] : images;

  // Rating display
  const renderRating = (rating) => {
    if (!rating) return null;
    const stars = [];
    const fullStars = Math.floor(rating);
    const hasHalfStar = rating % 1 !== 0;

    for (let i = 0; i < fullStars; i++) {
      stars.push(
        <svg key={i} className="w-4 h-4 text-yellow-400 fill-current" viewBox="0 0 20 20">
          <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
        </svg>
      );
    }

    if (hasHalfStar) {
      stars.push(
        <svg key="half" className="w-4 h-4 text-yellow-400 fill-current" viewBox="0 0 20 20">
          <defs>
            <linearGradient id="halfStar">
              <stop offset="50%" stopColor="currentColor" />
              <stop offset="50%" stopColor="transparent" />
            </linearGradient>
          </defs>
          <path fill="url(#halfStar)" d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
        </svg>
      );
    }

    const emptyStars = 5 - Math.ceil(rating);
    for (let i = 0; i < emptyStars; i++) {
      stars.push(
        <svg key={`empty-${i}`} className="w-4 h-4 text-gray-300 fill-current" viewBox="0 0 20 20">
          <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
        </svg>
      );
    }

    return (
      <div className="flex items-center space-x-1">
        {stars}
        <span className="text-sm text-gray-600 ml-2">({rating})</span>
      </div>
    );
  };

  // Default variant - Standard product layout
  if (variant === 'default') {
    return (
      <section
        id={id}
        className={clsx(
          "product-single-classic-default bg-white",
          className
        )}
      >
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8 lg:py-16">
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-8 lg:gap-16">
            {/* Product Images */}
            <div className="space-y-4">
              <div className="aspect-square bg-gray-100 rounded-lg overflow-hidden">
                {allImages.length > 0 && (
                  <img
                    src={allImages[selectedImage]}
                    alt={title}
                    className="w-full h-full object-cover"
                  />
                )}
              </div>
              {allImages.length > 1 && (
                <div className="grid grid-cols-4 gap-4">
                  {allImages.slice(0, 4).map((img, index) => (
                    <button
                      key={index}
                      onClick={() => setSelectedImage(index)}
                      className={clsx(
                        "aspect-square bg-gray-100 rounded-lg overflow-hidden border-2 transition-colors",
                        selectedImage === index ? "border-blue-500" : "border-transparent"
                      )}
                    >
                      <img
                        src={img}
                        alt={`${title} ${index + 1}`}
                        className="w-full h-full object-cover"
                      />
                    </button>
                  ))}
                </div>
              )}
            </div>

            {/* Product Info */}
            <div className="space-y-6">
              {subtitle && (
                <p className="text-sm font-medium text-blue-600 uppercase tracking-wide">
                  {subtitle}
                </p>
              )}
              <h1 className="text-3xl lg:text-4xl font-bold text-gray-900">
                {title}
              </h1>

              {rating && renderRating(rating)}

              <div className="flex items-center space-x-4">
                <span className="text-3xl font-bold text-gray-900">
                  {currency}{price}
                </span>
                {originalPrice && (
                  <span className="text-xl text-gray-500 line-through">
                    {currency}{originalPrice}
                  </span>
                )}
                {badge && (
                  <span className="inline-flex items-center px-3 py-1 rounded-full text-xs font-medium bg-red-100 text-red-800">
                    {badge}
                  </span>
                )}
              </div>

              <div className="flex items-center space-x-2">
                <span className={clsx(
                  "inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium",
                  inStock ? "bg-green-100 text-green-800" : "bg-red-100 text-red-800"
                )}>
                  {inStock ? "In Stock" : "Out of Stock"}
                </span>
              </div>

              <div className="prose prose-gray max-w-none">
                <p className="text-gray-600">{description}</p>
              </div>

              <button
                className={clsx(
                  "w-full px-8 py-4 rounded-lg font-semibold text-white transition-colors",
                  button.variant === 'secondary' ? "bg-gray-900 hover:bg-gray-800" :
                  button.variant === 'outline' ? "bg-transparent border-2 border-gray-900 text-gray-900 hover:bg-gray-900 hover:text-white" :
                  "bg-blue-600 hover:bg-blue-700"
                )}
              >
                {button.text}
              </button>

              {features.length > 0 && (
                <div className="space-y-3">
                  <h3 className="text-lg font-semibold text-gray-900">Features</h3>
                  <ul className="space-y-2">
                    {features.map((feature, index) => (
                      <li key={index} className="flex items-center space-x-2">
                        <svg className="w-5 h-5 text-green-500" fill="currentColor" viewBox="0 0 20 20">
                          <path fillRule="evenodd" d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z" clipRule="evenodd" />
                        </svg>
                        <span className="text-gray-700">{feature}</span>
                      </li>
                    ))}
                  </ul>
                </div>
              )}
            </div>
          </div>
        </div>
      </section>
    );
  }

  // Centered variant - Centered layout with hero-style presentation
  if (variant === 'centered') {
    return (
      <section
        id={id}
        className={clsx(
          "product-single-classic-centered bg-gray-50",
          className
        )}
      >
        <div className="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8 py-12 lg:py-20">
          <div className="text-center mb-12">
            {subtitle && (
              <p className="text-sm font-medium text-blue-600 uppercase tracking-wide mb-4">
                {subtitle}
              </p>
            )}
            <h1 className="text-4xl lg:text-5xl font-bold text-gray-900 mb-6">
              {title}
            </h1>
            {rating && (
              <div className="flex justify-center mb-6">
                {renderRating(rating)}
              </div>
            )}
          </div>

          <div className="bg-white rounded-2xl shadow-xl overflow-hidden">
            <div className="aspect-video bg-gray-100 relative">
              {allImages.length > 0 && (
                <img
                  src={allImages[selectedImage]}
                  alt={title}
                  className="w-full h-full object-cover"
                />
              )}
              {badge && (
                <div className="absolute top-4 left-4">
                  <span className="inline-flex items-center px-3 py-1 rounded-full text-sm font-medium bg-red-500 text-white">
                    {badge}
                  </span>
                </div>
              )}
            </div>

            <div className="p-8 lg:p-12">
              <div className="text-center mb-8">
                <div className="flex items-center justify-center space-x-4 mb-4">
                  <span className="text-4xl font-bold text-gray-900">
                    {currency}{price}
                  </span>
                  {originalPrice && (
                    <span className="text-2xl text-gray-500 line-through">
                      {currency}{originalPrice}
                    </span>
                  )}
                </div>
                <div className="flex justify-center mb-6">
                  <span className={clsx(
                    "inline-flex items-center px-3 py-1 rounded-full text-sm font-medium",
                    inStock ? "bg-green-100 text-green-800" : "bg-red-100 text-red-800"
                  )}>
                    {inStock ? "In Stock" : "Out of Stock"}
                  </span>
                </div>
              </div>

              <div className="prose prose-gray max-w-none text-center mb-8">
                <p className="text-lg text-gray-600">{description}</p>
              </div>

              <div className="text-center">
                <button
                  className={clsx(
                    "px-12 py-4 rounded-lg font-semibold text-white text-lg transition-colors",
                    button.variant === 'secondary' ? "bg-gray-900 hover:bg-gray-800" :
                    button.variant === 'outline' ? "bg-transparent border-2 border-gray-900 text-gray-900 hover:bg-gray-900 hover:text-white" :
                    "bg-blue-600 hover:bg-blue-700"
                  )}
                >
                  {button.text}
                </button>
              </div>

              {features.length > 0 && (
                <div className="mt-12 pt-8 border-t border-gray-200">
                  <h3 className="text-xl font-semibold text-gray-900 text-center mb-6">Features</h3>
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    {features.map((feature, index) => (
                      <div key={index} className="flex items-center space-x-3">
                        <svg className="w-5 h-5 text-green-500 flex-shrink-0" fill="currentColor" viewBox="0 0 20 20">
                          <path fillRule="evenodd" d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z" clipRule="evenodd" />
                        </svg>
                        <span className="text-gray-700">{feature}</span>
                      </div>
                    ))}
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>
      </section>
    );
  }

  // Split variant - Split screen layout
  if (variant === 'split') {
    return (
      <section
        id={id}
        className={clsx(
          "product-single-classic-split min-h-screen",
          className
        )}
      >
        <div className="grid grid-cols-1 lg:grid-cols-2 min-h-screen">
          {/* Left side - Image */}
          <div className="relative bg-gray-900">
            {allImages.length > 0 && (
              <img
                src={allImages[selectedImage]}
                alt={title}
                className="w-full h-full object-cover"
              />
            )}
            {badge && (
              <div className="absolute top-8 left-8">
                <span className="inline-flex items-center px-4 py-2 rounded-full text-sm font-medium bg-white text-gray-900">
                  {badge}
                </span>
              </div>
            )}

            {allImages.length > 1 && (
              <div className="absolute bottom-8 left-8 right-8">
                <div className="flex space-x-2">
                  {allImages.slice(0, 4).map((img, index) => (
                    <button
                      key={index}
                      onClick={() => setSelectedImage(index)}
                      className={clsx(
                        "w-16 h-16 rounded-lg overflow-hidden border-2 transition-colors flex-shrink-0",
                        selectedImage === index ? "border-white" : "border-white/30"
                      )}
                    >
                      <img
                        src={img}
                        alt={`${title} ${index + 1}`}
                        className="w-full h-full object-cover"
                      />
                    </button>
                  ))}
                </div>
              </div>
            )}
          </div>

          {/* Right side - Content */}
          <div className="flex items-center justify-center p-8 lg:p-16 bg-white">
            <div className="max-w-lg w-full space-y-8">
              {subtitle && (
                <p className="text-sm font-medium text-blue-600 uppercase tracking-wide">
                  {subtitle}
                </p>
              )}
              <h1 className="text-4xl lg:text-5xl font-bold text-gray-900">
                {title}
              </h1>

              {rating && renderRating(rating)}

              <div className="flex items-center space-x-4">
                <span className="text-4xl font-bold text-gray-900">
                  {currency}{price}
                </span>
                {originalPrice && (
                  <span className="text-2xl text-gray-500 line-through">
                    {currency}{originalPrice}
                  </span>
                )}
              </div>

              <div className="flex items-center space-x-2 mb-6">
                <span className={clsx(
                  "inline-flex items-center px-3 py-1 rounded-full text-sm font-medium",
                  inStock ? "bg-green-100 text-green-800" : "bg-red-100 text-red-800"
                )}>
                  {inStock ? "In Stock" : "Out of Stock"}
                </span>
              </div>

              <div className="prose prose-gray max-w-none">
                <p className="text-lg text-gray-600">{description}</p>
              </div>

              <button
                className={clsx(
                  "w-full px-8 py-4 rounded-lg font-semibold text-white text-lg transition-colors",
                  button.variant === 'secondary' ? "bg-gray-900 hover:bg-gray-800" :
                  button.variant === 'outline' ? "bg-transparent border-2 border-gray-900 text-gray-900 hover:bg-gray-900 hover:text-white" :
                  "bg-blue-600 hover:bg-blue-700"
                )}
              >
                {button.text}
              </button>

              {features.length > 0 && (
                <div className="space-y-4">
                  <h3 className="text-lg font-semibold text-gray-900">Key Features</h3>
                  <ul className="space-y-2">
                    {features.map((feature, index) => (
                      <li key={index} className="flex items-center space-x-2">
                        <svg className="w-5 h-5 text-green-500 flex-shrink-0" fill="currentColor" viewBox="0 0 20 20">
                          <path fillRule="evenodd" d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z" clipRule="evenodd" />
                        </svg>
                        <span className="text-gray-700">{feature}</span>
                      </li>
                    ))}
                  </ul>
                </div>
              )}
            </div>
          </div>
        </div>
      </section>
    );
  }

  // Compact variant - Compact layout for smaller spaces
  if (variant === 'compact') {
    return (
      <section
        id={id}
        className={clsx(
          "product-single-classic-compact bg-white border border-gray-200 rounded-lg overflow-hidden",
          className
        )}
      >
        <div className="p-6">
          <div className="flex flex-col sm:flex-row gap-6">
            {/* Product Image */}
            <div className="sm:w-1/3">
              <div className="aspect-square bg-gray-100 rounded-lg overflow-hidden">
                {allImages.length > 0 && (
                  <img
                    src={allImages[selectedImage]}
                    alt={title}
                    className="w-full h-full object-cover"
                  />
                )}
              </div>
              {allImages.length > 1 && (
                <div className="flex space-x-2 mt-3">
                  {allImages.slice(0, 3).map((img, index) => (
                    <button
                      key={index}
                      onClick={() => setSelectedImage(index)}
                      className={clsx(
                        "w-12 h-12 rounded border-2 transition-colors flex-shrink-0",
                        selectedImage === index ? "border-blue-500" : "border-gray-300"
                      )}
                    >
                      <img
                        src={img}
                        alt={`${title} ${index + 1}`}
                        className="w-full h-full object-cover rounded"
                      />
                    </button>
                  ))}
                </div>
              )}
            </div>

            {/* Product Info */}
            <div className="sm:w-2/3 space-y-4">
              {subtitle && (
                <p className="text-xs font-medium text-blue-600 uppercase tracking-wide">
                  {subtitle}
                </p>
              )}
              <h2 className="text-xl font-bold text-gray-900">
                {title}
              </h2>

              {rating && renderRating(rating)}

              <div className="flex items-center space-x-3">
                <span className="text-2xl font-bold text-gray-900">
                  {currency}{price}
                </span>
                {originalPrice && (
                  <span className="text-lg text-gray-500 line-through">
                    {currency}{originalPrice}
                  </span>
                )}
                {badge && (
                  <span className="inline-flex items-center px-2 py-1 rounded-full text-xs font-medium bg-red-100 text-red-800">
                    {badge}
                  </span>
                )}
              </div>

              <div className="flex items-center space-x-2">
                <span className={clsx(
                  "inline-flex items-center px-2 py-1 rounded-full text-xs font-medium",
                  inStock ? "bg-green-100 text-green-800" : "bg-red-100 text-red-800"
                )}>
                  {inStock ? "In Stock" : "Out of Stock"}
                </span>
              </div>

              <p className="text-sm text-gray-600 line-clamp-3">
                {description}
              </p>

              <button
                className={clsx(
                  "w-full px-6 py-3 rounded-lg font-semibold text-white text-sm transition-colors",
                  button.variant === 'secondary' ? "bg-gray-900 hover:bg-gray-800" :
                  button.variant === 'outline' ? "bg-transparent border border-gray-900 text-gray-900 hover:bg-gray-900 hover:text-white" :
                  "bg-blue-600 hover:bg-blue-700"
                )}
              >
                {button.text}
              </button>

              {features.length > 0 && (
                <div className="pt-4 border-t border-gray-200">
                  <ul className="space-y-1">
                    {features.slice(0, 3).map((feature, index) => (
                      <li key={index} className="flex items-center space-x-2 text-sm">
                        <svg className="w-4 h-4 text-green-500 flex-shrink-0" fill="currentColor" viewBox="0 0 20 20">
                          <path fillRule="evenodd" d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z" clipRule="evenodd" />
                        </svg>
                        <span className="text-gray-700">{feature}</span>
                      </li>
                    ))}
                  </ul>
                </div>
              )}
            </div>
          </div>
        </div>
      </section>
    );
  }

  // Detailed variant - Full detailed layout with tabs
  if (variant === 'detailed') {
    return (
      <section
        id={id}
        className={clsx(
          "product-single-classic-detailed bg-white",
          className
        )}
      >
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8 lg:py-16">
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-12 mb-16">
            {/* Product Images */}
            <div className="space-y-6">
              <div className="aspect-square bg-gray-100 rounded-xl overflow-hidden">
                {allImages.length > 0 && (
                  <img
                    src={allImages[selectedImage]}
                    alt={title}
                    className="w-full h-full object-cover"
                  />
                )}
              </div>
              {allImages.length > 1 && (
                <div className="grid grid-cols-5 gap-4">
                  {allImages.slice(0, 5).map((img, index) => (
                    <button
                      key={index}
                      onClick={() => setSelectedImage(index)}
                      className={clsx(
                        "aspect-square bg-gray-100 rounded-lg overflow-hidden border-2 transition-colors",
                        selectedImage === index ? "border-blue-500" : "border-transparent"
                      )}
                    >
                      <img
                        src={img}
                        alt={`${title} ${index + 1}`}
                        className="w-full h-full object-cover"
                      />
                    </button>
                  ))}
                </div>
              )}
            </div>

            {/* Product Info */}
            <div className="space-y-8">
              {subtitle && (
                <p className="text-sm font-medium text-blue-600 uppercase tracking-wide">
                  {subtitle}
                </p>
              )}
              <h1 className="text-4xl lg:text-5xl font-bold text-gray-900">
                {title}
              </h1>

              {rating && renderRating(rating)}

              <div className="flex items-center space-x-6">
                <span className="text-5xl font-bold text-gray-900">
                  {currency}{price}
                </span>
                {originalPrice && (
                  <span className="text-3xl text-gray-500 line-through">
                    {currency}{originalPrice}
                  </span>
                )}
                {badge && (
                  <span className="inline-flex items-center px-4 py-2 rounded-full text-sm font-medium bg-red-500 text-white">
                    {badge}
                  </span>
                )}
              </div>

              <div className="flex items-center space-x-3">
                <span className={clsx(
                  "inline-flex items-center px-4 py-2 rounded-full text-sm font-medium",
                  inStock ? "bg-green-100 text-green-800" : "bg-red-100 text-red-800"
                )}>
                  {inStock ? " In Stock" : " Out of Stock"}
                </span>
              </div>

              <div className="prose prose-gray max-w-none">
                <p className="text-xl text-gray-600 leading-relaxed">{description}</p>
              </div>

              <button
                className={clsx(
                  "w-full px-12 py-5 rounded-xl font-bold text-white text-xl transition-all transform hover:scale-105",
                  button.variant === 'secondary' ? "bg-gray-900 hover:bg-gray-800 shadow-lg hover:shadow-xl" :
                  button.variant === 'outline' ? "bg-transparent border-2 border-gray-900 text-gray-900 hover:bg-gray-900 hover:text-white shadow-lg hover:shadow-xl" :
                  "bg-gradient-to-r from-blue-600 to-blue-700 hover:from-blue-700 hover:to-blue-800 shadow-lg hover:shadow-xl"
                )}
              >
                {button.text}
              </button>

              {features.length > 0 && (
                <div className="bg-gray-50 rounded-xl p-6">
                  <h3 className="text-xl font-semibold text-gray-900 mb-4">Key Features</h3>
                  <ul className="space-y-3">
                    {features.map((feature, index) => (
                      <li key={index} className="flex items-start space-x-3">
                        <svg className="w-6 h-6 text-green-500 flex-shrink-0 mt-0.5" fill="currentColor" viewBox="0 0 20 20">
                          <path fillRule="evenodd" d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z" clipRule="evenodd" />
                        </svg>
                        <span className="text-gray-700 text-lg">{feature}</span>
                      </li>
                    ))}
                  </ul>
                </div>
              )}
            </div>
          </div>

          {/* Detailed Tabs Section */}
          <div className="border-t border-gray-200 pt-16">
            <div className="flex space-x-8 mb-8">
              <button
                onClick={() => setActiveTab('description')}
                className={clsx(
                  "px-6 py-3 rounded-lg font-semibold transition-colors",
                  activeTab === 'description' ? "bg-blue-600 text-white" : "text-gray-600 hover:text-gray-900"
                )}
              >
                Description
              </button>
              <button
                onClick={() => setActiveTab('specifications')}
                className={clsx(
                  "px-6 py-3 rounded-lg font-semibold transition-colors",
                  activeTab === 'specifications' ? "bg-blue-600 text-white" : "text-gray-600 hover:text-gray-900"
                )}
              >
                Specifications
              </button>
              <button
                onClick={() => setActiveTab('reviews')}
                className={clsx(
                  "px-6 py-3 rounded-lg font-semibold transition-colors",
                  activeTab === 'reviews' ? "bg-blue-600 text-white" : "text-gray-600 hover:text-gray-900"
                )}
              >
                Reviews
              </button>
            </div>

            <div className="bg-gray-50 rounded-xl p-8">
              {activeTab === 'description' && (
                <div className="prose prose-gray max-w-none">
                  <p className="text-lg text-gray-700 leading-relaxed">{description}</p>
                  {features.length > 0 && (
                    <div className="mt-8">
                      <h3 className="text-2xl font-semibold text-gray-900 mb-4">Product Features</h3>
                      <ul className="space-y-2">
                        {features.map((feature, index) => (
                          <li key={index} className="flex items-center space-x-3">
                            <svg className="w-5 h-5 text-blue-600" fill="currentColor" viewBox="0 0 20 20">
                              <path fillRule="evenodd" d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z" clipRule="evenodd" />
                            </svg>
                            <span className="text-gray-700">{feature}</span>
                          </li>
                        ))}
                      </ul>
                    </div>
                  )}
                </div>
              )}

              {activeTab === 'specifications' && (
                <div>
                  <h3 className="text-2xl font-semibold text-gray-900 mb-6">Technical Specifications</h3>
                  {specifications.length > 0 ? (
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                      {specifications.map((spec, index) => (
                        <div key={index} className="flex justify-between py-3 border-b border-gray-200">
                          <span className="font-medium text-gray-900">{spec.label}</span>
                          <span className="text-gray-700">{spec.value}</span>
                        </div>
                      ))}
                    </div>
                  ) : (
                    <p className="text-gray-600">Specifications coming soon.</p>
                  )}
                </div>
              )}

              {activeTab === 'reviews' && (
                <div>
                  <h3 className="text-2xl font-semibold text-gray-900 mb-6">Customer Reviews</h3>
                  {rating ? (
                    <div className="space-y-6">
                      <div className="flex items-center space-x-4">
                        {renderRating(rating)}
                        <span className="text-lg text-gray-600">Based on customer reviews</span>
                      </div>
                      <p className="text-gray-600">Detailed reviews coming soon.</p>
                    </div>
                  ) : (
                    <p className="text-gray-600">No reviews yet. Be the first to review this product!</p>
                  )}
                </div>
              )}
            </div>
          </div>
        </div>
      </section>
    );
  }

  return null;
};
