import { useState , useEffect} from 'react';
import clsx from 'clsx';
import '../../styles/price.css';

/**
 * PricePremium Component - Premium pricing section with toggle and comparison features
 * @param {Object} props - Component props
 * @param {Object} props.data - Price section configuration data
 * @param {string} [props.data.title] - Main heading text
 * @param {string} [props.data.subtitle] - Subtitle text displayed above title
 * @param {string} [props.data.description] - Description text displayed below title
 * @param {Array} props.data.plans - Array of pricing plans
 * @param {string} [props.data.alignment='center'] - Header text alignment: 'left' | 'center' | 'right'
 * @param {number} [props.data.columns=3] - Number of columns for grid layout (1-4)
 * @param {string} [props.data.currency='$'] - Currency symbol
 * @param {boolean} [props.data.showToggle=true] - Show monthly/yearly toggle
 * @param {boolean} [props.data.showComparison=false] - Show comparison table view
 * @param {number} [props.data.featuredPlanIndex] - Index of the featured/popular plan
 * @param {Object} [props.data.plans[].button] - Button configuration object
 * @param {string} [props.data.plans[].button.text] - Button text (defaults to 'Get Started')
 * @param {string} [props.data.plans[].button.href] - Button link URL
 * @param {Function} [props.data.plans[].button.onClick] - Button click handler
 * @param {string} [props.data.className] - Additional CSS classes for the section
 * @param {string} [props.contextKey='pricing'] - Key to get data from LayoutDataProvider context (default: 'pricing')
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute for the section element
 * @param {boolean} [props.loader=false] - Show loading state
 */
export const PricePremium = ({ data, className, id, loader = false }) => {
  // Safety check for data
  if (!data || typeof data !== 'object') {
    console.error('PricePremium: data prop is required and must be an object');
    return null;
  }

  const {
    title,
    subtitle,
    description,
    plans = [],
    alignment = 'center',
    columns = 3,
    currency = '$',
    showToggle = true,
    showComparison = false,
    featuredPlanIndex,
    className: dataClassName,
  } = data;

  const [billingPeriod, setBillingPeriod] = useState('month');
  const [showLoader, setShowLoader] = useState(loader);

  // Auto-hide loader after 2 seconds when loader prop is true
  useEffect(() => {
    if (loader) {
      const timer = setTimeout(() => {
        setShowLoader(false);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowLoader(false);
    }
  }, [loader]);

  // Show skeleton loader if loader prop is true
  if (showLoader) {
    const alignmentClasses = {
      left: 'text-left items-start',
      center: 'text-center items-center',
      right: 'text-right items-end',
    };

    const gridColumnsClasses = {
      1: 'grid-cols-1',
      2: 'grid-cols-1 md:grid-cols-2',
      3: 'grid-cols-1 md:grid-cols-2 lg:grid-cols-3',
      4: 'grid-cols-1 md:grid-cols-2 lg:grid-cols-4',
    };

    const effectiveAlignment = alignment || 'center';

    return (
      <section
        id={id}
        className={clsx(
          'w-full py-12 sm:py-16 lg:py-20 px-4 sm:px-6 lg:px-8',
          'bg-white',
          className,
          dataClassName
        )}
      >
        <div className="max-w-7xl mx-auto w-full">
          {/* Header skeleton */}
          <div className={clsx('mb-10 sm:mb-12 flex flex-col', alignmentClasses[effectiveAlignment])}>
            <div className="h-4 bg-gray-400 rounded w-24 mb-3 mx-auto animate-pulse"></div>
            <div className="h-10 bg-gray-400 rounded w-full max-w-2xl mb-4 mx-auto animate-pulse"></div>
            <div className="h-5 bg-gray-300 rounded w-full max-w-xl mb-6 mx-auto animate-pulse"></div>
          </div>

          {/* Toggle skeleton */}
          {showToggle && (
            <div className="flex justify-center mb-8">
              <div className="h-12 bg-gray-200 rounded-full w-64 animate-pulse"></div>
            </div>
          )}

          {/* Pricing cards skeleton */}
          <div className={clsx('grid gap-6', gridColumnsClasses[columns])}>
            {[0, 1, 2].map((index) => (
              <div key={index} className="border border-gray-200 rounded-lg p-6 bg-white">
                {/* Plan name skeleton */}
                <div className="h-6 bg-gray-400 rounded w-32 mb-4 animate-pulse"></div>
                
                {/* Price skeleton */}
                <div className="mb-4">
                  <div className="h-12 bg-gray-400 rounded w-24 mb-2 animate-pulse"></div>
                  <div className="h-4 bg-gray-300 rounded w-20 animate-pulse"></div>
                </div>

                {/* Features skeleton */}
                <div className="space-y-3 mb-6">
                  {[0, 1, 2, 3].map((featureIndex) => (
                    <div key={featureIndex} className="flex items-center gap-2">
                      <div className="h-4 w-4 bg-gray-300 rounded animate-pulse"></div>
                      <div className="h-4 bg-gray-300 rounded flex-1 animate-pulse"></div>
                    </div>
                  ))}
                </div>

                {/* Button skeleton */}
                <div className="h-12 bg-gray-400 rounded-lg w-full animate-pulse"></div>
              </div>
            ))}
          </div>
        </div>
      </section>
    );
  }

  const alignmentClasses = {
    left: 'text-left items-start',
    center: 'text-center items-center',
    right: 'text-right items-end',
  };

  const gridColumnsClasses = {
    1: 'grid-cols-1',
    2: 'grid-cols-1 sm:grid-cols-2',
    3: 'grid-cols-1 sm:grid-cols-2 lg:grid-cols-3',
    4: 'grid-cols-1 sm:grid-cols-2 lg:grid-cols-4',
  };

  // Get price based on billing period
  const getPrice = (plan) => {
    if (billingPeriod === 'year' && plan.yearlyPrice !== undefined) {
      return plan.yearlyPrice;
    }
    return plan.price;
  };

  // Get savings percentage
  const getSavings = (plan) => {
    if (billingPeriod === 'year' && plan.yearlyPrice !== undefined && plan.price) {
      const monthlyTotal = plan.price * 12;
      const savings = ((monthlyTotal - plan.yearlyPrice) / monthlyTotal) * 100;
      return Math.round(savings);
    }
    return null;
  };

  if (plans.length === 0) {
    console.warn('PricePremium: plans array is empty');
    return null;
  }

  return (
    <section
      id={id}
      className={clsx(
        'w-full py-16 sm:py-20 lg:py-24 px-4 sm:px-6 lg:px-8',
        'bg-white',
        className,
        dataClassName
      )}
    >
      <div className="max-w-7xl mx-auto w-full">
        {/* Header */}
        {(title || subtitle || description) && (
          <div
            className={clsx(
              'mb-12 sm:mb-16 flex flex-col',
              alignmentClasses[alignment]
            )}
          >
            {subtitle && (
              <p className="text-sm sm:text-base font-semibold text-primary-600 uppercase tracking-wider mb-3">
                {subtitle}
              </p>
            )}

            {title && (
              <h2 className="text-4xl sm:text-5xl md:text-6xl font-bold text-gray-900 mb-6 leading-tight">
                {title}
              </h2>
            )}

            {description && (
              <p
                className={clsx(
                  'text-lg sm:text-xl text-gray-600 max-w-3xl',
                  alignment === 'center' && 'mx-auto',
                  alignment === 'right' && 'ml-auto',
                  alignment === 'left' && 'mr-auto'
                )}
              >
                {description}
              </p>
            )}
          </div>
        )}

        {/* Billing Toggle */}
        {showToggle && (
          <div className="flex justify-center mb-12">
            <div className="inline-flex items-center bg-gray-100 rounded-full p-1">
              <button
                onClick={() => setBillingPeriod('month')}
                className={clsx(
                  'px-6 py-3 rounded-full font-semibold text-sm transition-all duration-300',
                  billingPeriod === 'month'
                    ? 'bg-white text-gray-900 shadow-md'
                    : 'text-gray-600 hover:text-gray-900'
                )}
              >
                Monthly
              </button>
              <button
                onClick={() => setBillingPeriod('year')}
                className={clsx(
                  'px-6 py-3 rounded-full font-semibold text-sm transition-all duration-300',
                  billingPeriod === 'year'
                    ? 'bg-white text-gray-900 shadow-md'
                    : 'text-gray-600 hover:text-gray-900'
                )}
              >
                Yearly
                {plans.some(p => p.yearlyPrice !== undefined) && (
                  <span className="ml-2 px-2 py-0.5 bg-primary-100 text-primary-700 text-xs rounded-full">
                    Save up to {Math.max(...plans.filter(p => p.yearlyPrice).map(p => getSavings(p) || 0))}%
                  </span>
                )}
              </button>
            </div>
          </div>
        )}

        {/* Comparison Table View */}
        {showComparison ? (
          <div className="overflow-x-auto">
            <table className="w-full border-collapse">
              <thead>
                <tr className="border-b-2 border-gray-200">
                  <th className="text-left py-4 px-6 font-semibold text-gray-900">Features</th>
                  {plans.map((plan, index) => (
                    <th key={index} className="text-center py-4 px-6 font-semibold text-gray-900">
                      {plan.name}
                      {featuredPlanIndex === index && (
                        <span className="ml-2 px-2 py-1 text-xs font-semibold bg-primary-600 text-white rounded-full">
                          Popular
                        </span>
                      )}
                    </th>
                  ))}
                </tr>
              </thead>
              <tbody>
                {/* Price Row */}
                <tr className="border-b border-gray-100">
                  <td className="py-6 px-6 font-semibold text-gray-900">Price</td>
                  {plans.map((plan, index) => (
                    <td key={index} className="py-6 px-6 text-center">
                      <div className="text-3xl font-bold text-gray-900">
                        {currency}{getPrice(plan)}
                      </div>
                      <div className="text-sm text-gray-500 mt-1">/{billingPeriod}</div>
                      {getSavings(plan) && (
                        <div className="text-xs text-green-600 font-semibold mt-1">
                          Save {getSavings(plan)}%
                        </div>
                      )}
                    </td>
                  ))}
                </tr>
                {/* Features Rows */}
                {plans[0]?.features?.map((_, featureIndex) => (
                  <tr key={featureIndex} className="border-b border-gray-100">
                    <td className="py-4 px-6 text-gray-700">
                      {typeof plans[0].features[featureIndex] === 'string'
                        ? plans[0].features[featureIndex]
                        : plans[0].features[featureIndex].text}
                    </td>
                    {plans.map((plan, planIndex) => {
                      const feature = plan.features?.[featureIndex];
                      const featureText = typeof feature === 'string' ? feature : feature?.text;
                      const featureIncluded = typeof feature === 'string' ? true : (feature?.included !== false);
                      
                      return (
                        <td key={planIndex} className="py-4 px-6 text-center">
                          {featureIncluded ? (
                            <svg
                              className="w-6 h-6 mx-auto text-green-500"
                              fill="none"
                              stroke="currentColor"
                              viewBox="0 0 24 24"
                            >
                              <path
                                strokeLinecap="round"
                                strokeLinejoin="round"
                                strokeWidth={2}
                                d="M5 13l4 4L19 7"
                              />
                            </svg>
                          ) : (
                            <svg
                              className="w-6 h-6 mx-auto text-gray-300"
                              fill="none"
                              stroke="currentColor"
                              viewBox="0 0 24 24"
                            >
                              <path
                                strokeLinecap="round"
                                strokeLinejoin="round"
                                strokeWidth={2}
                                d="M6 18L18 6M6 6l12 12"
                              />
                            </svg>
                          )}
                        </td>
                      );
                    })}
                  </tr>
                ))}
                {/* CTA Row */}
                <tr>
                  <td className="py-6 px-6"></td>
                  {plans.map((plan, index) => (
                    <td key={index} className="py-6 px-6 text-center">
                      {plan.button && (
                        <a
                          href={plan.button.href || '#'}
                          onClick={plan.button.onClick}
                          className={clsx(
                            'inline-block px-8 py-3 rounded-lg font-semibold text-base transition-all duration-300',
                            'transform hover:scale-105',
                            featuredPlanIndex === index
                              ? 'bg-primary-600 text-white hover:bg-primary-700 shadow-lg'
                              : 'bg-gray-900 text-white hover:bg-gray-800 shadow-md',
                            plan.button.className
                          )}
                        >
                          {plan.button.text || 'Get Started'}
                        </a>
                      )}
                    </td>
                  ))}
                </tr>
              </tbody>
            </table>
          </div>
        ) : (
          /* Grid/Card View */
          <div className={clsx('grid gap-6 sm:gap-8', gridColumnsClasses[columns] || gridColumnsClasses[3])}>
            {plans.map((plan, index) => {
              const isFeatured = featuredPlanIndex === index || plan.featured || plan.popular;
              const savings = getSavings(plan);
              
              return (
                <div
                  key={index}
                  className={clsx(
                    'relative transition-all duration-300 flex flex-col h-full',
                    'rounded-2xl p-8 sm:p-10',
                    isFeatured
                      ? 'bg-gradient-to-br from-primary-600 via-primary-700 to-primary-800 text-white shadow-2xl border-2 border-primary-500 scale-105 z-10'
                      : 'bg-white border-2 border-gray-200 shadow-lg hover:shadow-xl hover:border-primary-200',
                    'transform hover:scale-[1.02]'
                  )}
                >
                  {/* Featured Badge */}
                  {isFeatured && (
                    <div className="absolute -top-4 left-1/2 -translate-x-1/2">
                      <span className="inline-flex items-center px-4 py-2 text-xs font-bold uppercase tracking-wider bg-primary-500 text-white rounded-full shadow-lg">
                        {plan.badgeText || 'Most Popular'}
                      </span>
                    </div>
                  )}

                  {/* Plan Name */}
                  {plan.name && (
                    <h3 className={clsx(
                      'text-3xl sm:text-4xl font-bold mb-3',
                      isFeatured ? 'text-white' : 'text-gray-900'
                    )}>
                      {plan.name}
                    </h3>
                  )}

                  {/* Plan Description */}
                  {plan.description && (
                    <p className={clsx(
                      'text-base mb-6',
                      isFeatured ? 'text-primary-100' : 'text-gray-600'
                    )}>
                      {plan.description}
                    </p>
                  )}

                  {/* Price */}
                  <div className="mb-8">
                    <div className={clsx(
                      'flex items-baseline justify-center',
                      isFeatured ? 'text-white' : 'text-gray-900'
                    )}>
                      <span className="text-5xl sm:text-6xl font-extrabold">
                        {currency}{getPrice(plan)}
                      </span>
                      <span className={clsx(
                        'text-lg sm:text-xl ml-2 font-medium',
                        isFeatured ? 'text-primary-100' : 'text-gray-500'
                      )}>
                        /{billingPeriod}
                      </span>
                    </div>
                    {savings && (
                      <p className="text-center mt-2 text-sm font-semibold text-green-600">
                        Save {savings}% annually
                      </p>
                    )}
                    {plan.priceNote && (
                      <p className={clsx(
                        'text-sm mt-3 text-center',
                        isFeatured ? 'text-primary-100' : 'text-gray-500'
                      )}>
                        {plan.priceNote}
                      </p>
                    )}
                  </div>

                  {/* Features List */}
                  {plan.features && plan.features.length > 0 && (
                    <ul className="space-y-4 mb-8 flex-1">
                      {plan.features.map((feature, fIndex) => {
                        const featureText = typeof feature === 'string' ? feature : feature.text;
                        const featureIncluded = typeof feature === 'string' ? true : (feature.included !== false);
                        
                        return (
                          <li 
                            key={fIndex} 
                            className="flex items-start"
                          >
                            {featureIncluded ? (
                              <svg
                                className={clsx(
                                  'w-6 h-6 flex-shrink-0 mt-0.5 mr-3',
                                  isFeatured ? 'text-white' : 'text-primary-600'
                                )}
                                fill="none"
                                stroke="currentColor"
                                viewBox="0 0 24 24"
                              >
                                <path
                                  strokeLinecap="round"
                                  strokeLinejoin="round"
                                  strokeWidth={2.5}
                                  d="M5 13l4 4L19 7"
                                />
                              </svg>
                            ) : (
                              <svg
                                className="w-6 h-6 flex-shrink-0 mt-0.5 mr-3 text-gray-400"
                                fill="none"
                                stroke="currentColor"
                                viewBox="0 0 24 24"
                              >
                                <path
                                  strokeLinecap="round"
                                  strokeLinejoin="round"
                                  strokeWidth={2}
                                  d="M6 18L18 6M6 6l12 12"
                                />
                              </svg>
                            )}
                            <span className={clsx(
                              'text-base',
                              isFeatured ? 'text-white' : 'text-gray-700'
                            )}>
                              {featureText}
                            </span>
                          </li>
                        );
                      })}
                    </ul>
                  )}

                  {/* CTA Button */}
                  {plan.button && (
                    <a
                      href={plan.button.href || '#'}
                      onClick={plan.button.onClick}
                      className={clsx(
                        'block w-full px-6 py-4 rounded-xl font-bold text-base transition-all duration-300',
                        'text-center transform hover:scale-105 active:scale-95',
                        isFeatured
                          ? 'bg-white text-primary-600 hover:bg-gray-50 shadow-lg'
                          : 'bg-gradient-to-r from-primary-600 to-primary-700 text-white hover:from-primary-700 hover:to-primary-800 shadow-lg hover:shadow-xl',
                        plan.button.className
                      )}
                    >
                      {plan.button.text || 'Get Started'}
                    </a>
                  )}
                </div>
              );
            })}
          </div>
        )}
      </div>
    </section>
  );
};

