import { useState, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/product.css';

/**
 * ProductPagination Component - Pagination controls for products with multiple variants
 * @param {Object} props - Component props
 * @param {Object} props.data - Pagination configuration data
 * @param {string} [props.data.variant='default'] - Style variant: 'default' | 'modern' | 'minimal' | 'compact' | 'icon' | 'dark' | 'dark-gray'
 * @param {number} props.data.currentPage - Current page number (1-based)
 * @param {number} props.data.totalPages - Total number of pages
 * @param {number} [props.data.itemsPerPage=12] - Items per page
 * @param {number} [props.data.totalItems] - Total number of items
 * @param {number} [props.data.maxVisiblePages=5] - Maximum visible page numbers
 * @param {boolean} [props.data.showFirstLast=true] - Show first/last buttons
 * @param {boolean} [props.data.showPrevNext=true] - Show prev/next buttons
 * @param {Function} [props.onPageChange] - Callback when page changes
 * @param {boolean} [props.loading=false] - Show skeleton loader
 * @param {string} [props.className] - Additional CSS classes
 */
export const ProductPagination = ({ 
  data = {}, 
  onPageChange,
  loading = false,
  className 
}) => {
  const {
    variant = 'default',
    currentPage: initialPage = 1,
    totalPages = 1,
    itemsPerPage = 12,
    totalItems,
    maxVisiblePages = 5,
    showFirstLast = true,
    showPrevNext = true,
  } = data;

  const [currentPage, setCurrentPage] = useState(Math.min(initialPage, totalPages));
  const [showContent, setShowContent] = useState(!loading);

  // Show skeleton for 2 seconds when loading is true
  useEffect(() => {
    if (loading) {
      setShowContent(false);
      const timer = setTimeout(() => {
        setShowContent(true);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowContent(true);
    }
  }, [loading]);

  const handlePageChange = (page) => {
    if (page >= 1 && page <= totalPages && page !== currentPage) {
      setCurrentPage(page);
      if (onPageChange) {
        onPageChange(page);
      }
    }
  };

  // Calculate visible page numbers
  const getVisiblePages = () => {
    const pages = [];
    const half = Math.floor(maxVisiblePages / 2);
    let start = Math.max(1, currentPage - half);
    let end = Math.min(totalPages, start + maxVisiblePages - 1);

    if (end - start < maxVisiblePages - 1) {
      start = Math.max(1, end - maxVisiblePages + 1);
    }

    for (let i = start; i <= end; i++) {
      pages.push(i);
    }

    return pages;
  };

  const visiblePages = getVisiblePages();
  const startItem = (currentPage - 1) * itemsPerPage + 1;
  const endItem = Math.min(currentPage * itemsPerPage, totalItems || totalPages * itemsPerPage);

  // Icon size classes
  const iconSizeClasses = {
    default: 'w-4 h-4 sm:w-5 sm:h-5',
    modern: 'w-4 h-4 sm:w-5 sm:h-5',
    minimal: 'w-4 h-4 sm:w-5 sm:h-5',
    compact: 'w-3.5 h-3.5 sm:w-4 sm:h-4',
    icon: 'w-5 h-5 sm:w-6 sm:h-6',
    dark: 'w-4 h-4 sm:w-5 sm:h-5',
    'dark-gray': 'w-4 h-4 sm:w-5 sm:h-5',
  };

  // Container classes - responsive with mobile-first approach
  const containerClasses = {
    default: 'flex flex-col sm:flex-row items-center justify-between gap-3 sm:gap-4',
    modern: 'flex flex-col sm:flex-row items-center justify-between gap-3 sm:gap-4',
    minimal: 'flex items-center justify-center gap-1 sm:gap-2',
    compact: 'flex items-center justify-center gap-0.5 sm:gap-1',
    icon: 'flex items-center justify-center gap-1 sm:gap-2',
    dark: 'flex flex-col sm:flex-row items-center justify-between gap-3 sm:gap-4',
    'dark-gray': 'flex flex-col sm:flex-row items-center justify-between gap-3 sm:gap-4',
  };

  // Info text classes
  const infoTextClasses = {
    default: 'text-xs sm:text-sm text-gray-600',
    modern: 'text-xs sm:text-sm text-gray-600',
    minimal: 'hidden',
    compact: 'hidden',
    icon: 'hidden',
    dark: 'text-xs sm:text-sm text-gray-300',
    'dark-gray': 'text-xs sm:text-sm text-gray-400',
  };

  // Button classes
  const getButtonClasses = (isActive = false, isDisabled = false) => {
    const baseClasses = {
      default: clsx(
        'px-2.5 sm:px-3 py-1.5 sm:py-2 text-xs sm:text-sm font-medium rounded-md',
        'transition-all duration-200',
        isDisabled 
          ? 'bg-gray-100 text-gray-400 cursor-not-allowed' 
          : isActive
            ? 'bg-primary-600 text-white shadow-sm'
            : 'bg-white text-gray-700 border border-gray-300 hover:bg-gray-50'
      ),
      modern: clsx(
        'px-3 sm:px-4 py-1.5 sm:py-2 text-xs sm:text-sm font-medium rounded-lg',
        'transition-all duration-300',
        isDisabled 
          ? 'bg-gray-100 text-gray-400 cursor-not-allowed' 
          : isActive
            ? 'bg-primary-600 text-white shadow-md'
            : 'bg-white text-gray-700 border border-gray-200 hover:bg-gray-50 hover:border-gray-300 hover:shadow-sm'
      ),
      minimal: clsx(
        'px-2 sm:px-3 py-1 sm:py-1.5 text-xs sm:text-sm transition-colors',
        isDisabled 
          ? 'text-gray-300 cursor-not-allowed' 
          : isActive
            ? 'text-primary-600 font-semibold border-b-2 border-primary-600'
            : 'text-gray-600 hover:text-gray-900'
      ),
      compact: clsx(
        'px-1.5 sm:px-2 py-1 text-xs font-medium rounded transition-all duration-200',
        isDisabled 
          ? 'bg-gray-100 text-gray-400 cursor-not-allowed' 
          : isActive
            ? 'bg-primary-600 text-white'
            : 'bg-white text-gray-700 border border-gray-300 hover:bg-gray-50'
      ),
      icon: clsx(
        'p-2 sm:p-2.5 rounded-lg transition-all duration-300',
        isDisabled 
          ? 'bg-gray-200 text-gray-400 cursor-not-allowed' 
          : isActive
            ? 'bg-primary-600 text-white shadow-lg'
            : 'bg-primary-500 text-white hover:bg-primary-600 shadow-md hover:shadow-lg'
      ),
      dark: clsx(
        'px-2.5 sm:px-3 py-1.5 sm:py-2 text-xs sm:text-sm font-medium rounded-md',
        'transition-all duration-200',
        isDisabled 
          ? 'bg-gray-700 text-gray-500 cursor-not-allowed' 
          : isActive
            ? 'bg-primary-500 text-white shadow-sm'
            : 'bg-gray-800 text-gray-300 border border-gray-600 hover:bg-gray-700'
      ),
      'dark-gray': clsx(
        'px-2.5 sm:px-3 py-1.5 sm:py-2 text-xs sm:text-sm font-medium rounded-md',
        'transition-all duration-200',
        isDisabled 
          ? 'bg-gray-800 text-gray-600 cursor-not-allowed' 
          : isActive
            ? 'bg-gray-700 text-white shadow-sm'
            : 'bg-gray-900 text-gray-400 border border-gray-700 hover:bg-gray-800'
      ),
    };
    return baseClasses[variant] || baseClasses.default;
  };

  // Controls wrapper classes
  const controlsWrapperClasses = {
    default: 'flex items-center gap-1 sm:gap-2',
    modern: 'flex items-center gap-1.5 sm:gap-2',
    minimal: 'flex items-center gap-1 sm:gap-2',
    compact: 'flex items-center gap-0.5 sm:gap-1',
    icon: 'flex items-center gap-1 sm:gap-2',
    dark: 'flex items-center gap-1 sm:gap-2',
    'dark-gray': 'flex items-center gap-1 sm:gap-2',
  };

  // Skeleton Loader Component
  const SkeletonLoader = () => {
    const isDark = variant === 'dark' || variant === 'dark-gray';
    const skeletonBg = isDark ? 'bg-gray-700' : 'bg-gray-200';
    const skeletonText = isDark ? 'bg-gray-600' : 'bg-gray-300';

    return (
      <div className={clsx(containerClasses[variant] || containerClasses.default, className)}>
        {/* Items info skeleton */}
        {totalItems && variant !== 'minimal' && variant !== 'compact' && variant !== 'icon' && (
          <div className={clsx(
            'h-4 w-32 sm:w-40 rounded product-skeleton',
            skeletonText
          )}></div>
        )}

        {/* Pagination controls skeleton */}
        <div className={controlsWrapperClasses[variant] || controlsWrapperClasses.default}>
          {showFirstLast && (
            <div className={clsx(
              'w-8 h-8 sm:w-9 sm:h-9 rounded-md product-skeleton',
              skeletonBg
            )}></div>
          )}
          {showPrevNext && (
            <div className={clsx(
              'w-8 h-8 sm:w-9 sm:h-9 rounded-md product-skeleton',
              skeletonBg
            )}></div>
          )}
          {Array.from({ length: Math.min(maxVisiblePages, totalPages) }).map((_, i) => (
            <div
              key={i}
              className={clsx(
                variant === 'minimal' ? 'h-6 w-6 sm:h-7 sm:w-7' : 'w-8 h-8 sm:w-9 sm:h-9',
                variant === 'compact' ? 'w-7 h-7 sm:w-8 sm:h-8' : '',
                variant === 'icon' ? 'w-10 h-10 sm:w-12 sm:h-12 rounded-lg' : 'rounded-md',
                'product-skeleton',
                i === Math.floor(maxVisiblePages / 2) ? skeletonText : skeletonBg
              )}
            ></div>
          ))}
          {showPrevNext && (
            <div className={clsx(
              'w-8 h-8 sm:w-9 sm:h-9 rounded-md product-skeleton',
              skeletonBg
            )}></div>
          )}
          {showFirstLast && (
            <div className={clsx(
              'w-8 h-8 sm:w-9 sm:h-9 rounded-md product-skeleton',
              skeletonBg
            )}></div>
          )}
        </div>
      </div>
    );
  };

  // Show skeleton loader if content is not ready
  if (!showContent) {
    return <SkeletonLoader />;
  }

  if (totalPages <= 1) {
    return null;
  }

  // Icon components
  const FirstIcon = () => (
    <svg className={iconSizeClasses[variant] || iconSizeClasses.default} fill="none" stroke="currentColor" viewBox="0 0 24 24">
      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M11 19l-7-7 7-7m8 14l-7-7 7-7" />
    </svg>
  );

  const PrevIcon = () => (
    <svg className={iconSizeClasses[variant] || iconSizeClasses.default} fill="none" stroke="currentColor" viewBox="0 0 24 24">
      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
    </svg>
  );

  const NextIcon = () => (
    <svg className={iconSizeClasses[variant] || iconSizeClasses.default} fill="none" stroke="currentColor" viewBox="0 0 24 24">
      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
    </svg>
  );

  const LastIcon = () => (
    <svg className={iconSizeClasses[variant] || iconSizeClasses.default} fill="none" stroke="currentColor" viewBox="0 0 24 24">
      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 5l7 7-7 7M5 5l7 7-7 7" />
    </svg>
  );

  return (
    <div className={clsx(containerClasses[variant] || containerClasses.default, className)}>
      {/* Items info */}
      {totalItems && variant !== 'minimal' && variant !== 'compact' && variant !== 'icon' && (
        <div className={infoTextClasses[variant] || infoTextClasses.default}>
          Showing {startItem} to {endItem} of {totalItems} products
        </div>
      )}

      {/* Pagination controls */}
      <div className={controlsWrapperClasses[variant] || controlsWrapperClasses.default}>
        {/* First button */}
        {showFirstLast && (
          <button
            onClick={() => handlePageChange(1)}
            disabled={currentPage === 1}
            className={getButtonClasses(false, currentPage === 1)}
            aria-label="First page"
            type="button"
          >
            {variant === 'icon' ? <FirstIcon /> : <FirstIcon />}
          </button>
        )}

        {/* Previous button */}
        {showPrevNext && (
          <button
            onClick={() => handlePageChange(currentPage - 1)}
            disabled={currentPage === 1}
            className={getButtonClasses(false, currentPage === 1)}
            aria-label="Previous page"
            type="button"
          >
            {variant === 'icon' ? <PrevIcon /> : <PrevIcon />}
          </button>
        )}

        {/* Page numbers */}
        {visiblePages.map((page) => (
          <button
            key={page}
            onClick={() => handlePageChange(page)}
            className={getButtonClasses(page === currentPage)}
            aria-label={`Page ${page}`}
            aria-current={page === currentPage ? 'page' : undefined}
            type="button"
          >
            {page}
          </button>
        ))}

        {/* Next button */}
        {showPrevNext && (
          <button
            onClick={() => handlePageChange(currentPage + 1)}
            disabled={currentPage === totalPages}
            className={getButtonClasses(false, currentPage === totalPages)}
            aria-label="Next page"
            type="button"
          >
            {variant === 'icon' ? <NextIcon /> : <NextIcon />}
          </button>
        )}

        {/* Last button */}
        {showFirstLast && (
          <button
            onClick={() => handlePageChange(totalPages)}
            disabled={currentPage === totalPages}
            className={getButtonClasses(false, currentPage === totalPages)}
            aria-label="Last page"
            type="button"
          >
            {variant === 'icon' ? <LastIcon /> : <LastIcon />}
          </button>
        )}
      </div>
    </div>
  );
};

