import { useState, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/feature-prime.css';

/**
 * FeaturePrime Component - Premium feature sections with split layouts
 * @param {Object} props - Component props
 * @param {Object} props.data - Feature section configuration data
 * @param {string} [props.data.title] - Main heading text
 * @param {string} [props.data.subtitle] - Subtitle text displayed above title
 * @param {string} [props.data.description] - Description text displayed below title
 * @param {Array} props.data.items - Array of feature items
 * @param {string} [props.data.layout='split-stacked'] - Layout variant: 'split-stacked' | 'split-grid'
 * @param {string} [props.data.theme='light'] - Theme variant: 'light' | 'dark'
 * @param {string} [props.data.buttonText] - CTA button text
 * @param {string} [props.data.buttonHref] - CTA button link
 * @param {Function} [props.data.buttonOnClick] - CTA button click handler
 * @param {ReactNode} [props.data.illustration] - Custom illustration component
 * @param {string} [props.data.className] - Additional CSS classes for the section
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute for the section element
 * @param {boolean} [props.loader=false] - Show loading state
 */
export const FeaturePrime = ({ data, className, id, loader = false }) => {
    // Safety check for data
    if (!data || typeof data !== 'object') {
        console.error('FeaturePrime: data prop is required and must be an object');
        return null;
    }

    const [showLoader, setShowLoader] = useState(loader);

    // Auto-hide loader after 2 seconds when loader prop is true
    useEffect(() => {
        if (loader) {
            const timer = setTimeout(() => {
                setShowLoader(false);
            }, 2000);
            return () => clearTimeout(timer);
        } else {
            setShowLoader(false);
        }
    }, [loader]);

    const {
        title,
        subtitle,
        description,
        items = [],
        layout = 'split-stacked',
        theme = 'light',
        buttonText,
        buttonHref,
        buttonOnClick,
        illustration,
        className: dataClassName,
    } = data;

    const isDark = theme === 'dark';
    const [hoveredIndex, setHoveredIndex] = useState(null);

    // Show skeleton loader if loader prop is true (check this FIRST before items check)
    if (showLoader) {
        // Get layout from data, default to split-stacked
        const skeletonLayout = layout || 'split-stacked';
        
        return (
            <section
                id={id}
                className={clsx(
                    'w-full py-16 sm:py-20 lg:py-24 px-4 sm:px-6 lg:px-8',
                    isDark ? 'bg-gray-900' : 'bg-white',
                    className,
                    dataClassName
                )}
            >
                <div className="max-w-7xl mx-auto w-full">
                    {/* Header skeleton */}
                    <div className="text-center mb-12 sm:mb-16">
                        {/* Subtitle skeleton */}
                        <div className={clsx('h-5 rounded w-32 mx-auto mb-4 animate-pulse', isDark ? 'bg-gray-700' : 'bg-gray-400')}></div>
                        {/* Title skeleton */}
                        <div className={clsx('h-12 rounded w-full max-w-2xl mx-auto mb-6 animate-pulse', isDark ? 'bg-gray-700' : 'bg-gray-400')}></div>
                        {/* Description skeleton */}
                        <div className={clsx('h-6 rounded w-full max-w-xl mx-auto mb-8 animate-pulse', isDark ? 'bg-gray-800' : 'bg-gray-300')}></div>
                    </div>

                    {/* Split Stacked Layout skeleton */}
                    {skeletonLayout === 'split-stacked' && (
                        <div className="grid lg:grid-cols-2 gap-12 items-center">
                            {/* Left side - Feature items skeleton */}
                            <div className="space-y-8">
                                {[0, 1, 2, 3].map((index) => (
                                    <div key={index} className="flex gap-4">
                                        {/* Icon skeleton */}
                                        <div className="flex-shrink-0">
                                            <div className={clsx(
                                                'w-14 h-14 rounded-lg animate-pulse',
                                                isDark ? 'bg-gray-700' : 'bg-gray-200'
                                            )}></div>
                                        </div>
                                        {/* Content skeleton */}
                                        <div className="flex-1 space-y-3">
                                            <div className={clsx(
                                                'h-6 rounded w-3/4 animate-pulse',
                                                isDark ? 'bg-gray-700' : 'bg-gray-300'
                                            )}></div>
                                            <div className={clsx(
                                                'h-4 rounded w-full animate-pulse',
                                                isDark ? 'bg-gray-800' : 'bg-gray-200'
                                            )}></div>
                                            <div className={clsx(
                                                'h-4 rounded w-5/6 animate-pulse',
                                                isDark ? 'bg-gray-800' : 'bg-gray-200'
                                            )}></div>
                                        </div>
                                    </div>
                                ))}
                                {/* Button skeleton */}
                                <div className={clsx('h-12 rounded-lg w-40 mt-6 animate-pulse', isDark ? 'bg-gray-700' : 'bg-gray-300')}></div>
                            </div>

                            {/* Right side - Illustration skeleton */}
                            <div className="hidden lg:block">
                                <div className={clsx(
                                    'h-96 rounded-2xl animate-pulse',
                                    isDark ? 'bg-gray-800' : 'bg-gray-200'
                                )}></div>
                            </div>
                        </div>
                    )}

                    {/* Split Grid Layout skeleton */}
                    {skeletonLayout === 'split-grid' && (
                        <div className="grid lg:grid-cols-2 gap-12 items-start">
                            {/* Left side - Text content skeleton */}
                            <div className="space-y-6">
                                {/* Subtitle skeleton */}
                                <div className={clsx('h-5 rounded w-32 animate-pulse', isDark ? 'bg-gray-700' : 'bg-gray-400')}></div>
                                {/* Title skeleton */}
                                <div className={clsx('h-12 rounded w-full mb-4 animate-pulse', isDark ? 'bg-gray-700' : 'bg-gray-400')}></div>
                                {/* Description skeleton */}
                                <div className={clsx('h-6 rounded w-full mb-6 animate-pulse', isDark ? 'bg-gray-800' : 'bg-gray-300')}></div>
                                {/* Button skeleton */}
                                <div className={clsx('h-12 rounded-lg w-40 animate-pulse', isDark ? 'bg-gray-700' : 'bg-gray-300')}></div>
                            </div>

                            {/* Right side - Grid cards skeleton */}
                            <div className="grid grid-cols-2 gap-4">
                                {[0, 1, 2, 3].map((index) => (
                                    <div key={index} className={clsx(
                                        'rounded-2xl p-6 border animate-pulse',
                                        isDark ? 'bg-gray-800 border-gray-700' : 'bg-gray-50 border-gray-200'
                                    )}>
                                        {/* Icon skeleton */}
                                        <div className={clsx(
                                            'w-12 h-12 rounded-lg mb-4 mx-auto animate-pulse',
                                            isDark ? 'bg-gray-700' : 'bg-gray-200'
                                        )}></div>
                                        {/* Title skeleton */}
                                        <div className={clsx(
                                            'h-5 rounded w-3/4 mx-auto mb-3 animate-pulse',
                                            isDark ? 'bg-gray-700' : 'bg-gray-300'
                                        )}></div>
                                        {/* Description skeleton */}
                                        <div className={clsx(
                                            'h-4 rounded w-full mb-2 animate-pulse',
                                            isDark ? 'bg-gray-800' : 'bg-gray-200'
                                        )}></div>
                                        <div className={clsx(
                                            'h-4 rounded w-5/6 mx-auto animate-pulse',
                                            isDark ? 'bg-gray-800' : 'bg-gray-200'
                                        )}></div>
                                    </div>
                                ))}
                            </div>
                        </div>
                    )}
                </div>
            </section>
        );
    }

    // Check if items array is empty (only after loader check)
    if (items.length === 0) {
        console.warn('FeaturePrime: items array is empty');
        return null;
    }

    // Color schemes for icons based on image designs
    const getIconColors = (index) => {
        const schemes = [
            {
                bg: 'bg-green-100',
                border: 'border-green-500',
                icon: 'text-green-600',
            },
            {
                bg: 'bg-orange-100',
                border: 'border-orange-500',
                icon: 'text-orange-600',
            },
            {
                bg: 'bg-blue-100',
                border: 'border-blue-500',
                icon: 'text-blue-600',
            },
            {
                bg: 'bg-red-100',
                border: 'border-red-500',
                icon: 'text-red-600',
            },
            {
                bg: 'bg-purple-100',
                border: 'border-purple-500',
                icon: 'text-purple-600',
            },
            {
                bg: 'bg-cyan-100',
                border: 'border-cyan-500',
                icon: 'text-cyan-600',
            },
        ];
        return schemes[index % schemes.length];
    };

    const renderIcon = (item, index, variant = 'stacked') => {
        if (!item.icon) return null;

        // Stacked layout icons - colored squares with outlined icons
        if (variant === 'stacked') {
            const colors = getIconColors(index);

            // If icon is a React component
            if (typeof item.icon === 'object' && item.icon.type) {
                return (
                    <div className={clsx(
                        'w-14 h-14 rounded-lg border-2 flex items-center justify-center mb-4',
                        isDark
                            ? 'bg-gray-800 border-gray-700'
                            : clsx(colors.bg, colors.border)
                    )}>
                        <div className={clsx(
                            'w-7 h-7',
                            isDark ? 'text-gray-300' : colors.icon
                        )}>
                            {item.icon}
                        </div>
                    </div>
                );
            }

            // If icon is a string (emoji or image)
            if (typeof item.icon === 'string') {
                if (item.icon.startsWith('http') || item.icon.startsWith('/') || item.icon.includes('.')) {
                    return (
                        <div className={clsx(
                            'w-14 h-14 rounded-lg border-2 flex items-center justify-center mb-4 overflow-hidden',
                            isDark
                                ? 'bg-gray-800 border-gray-700'
                                : clsx(colors.bg, colors.border)
                        )}>
                            <img
                                src={item.icon}
                                alt={item.title || 'Feature icon'}
                                className="w-full h-full object-cover"
                            />
                        </div>
                    );
                }

                return (
                    <div className={clsx(
                        'w-14 h-14 rounded-lg border-2 flex items-center justify-center mb-4 text-2xl',
                        isDark
                            ? 'bg-gray-800 border-gray-700'
                            : clsx(colors.bg, colors.border)
                    )}>
                        {item.icon}
                    </div>
                );
            }
        }

        // Grid layout icons - outlined icons for dark theme
        if (variant === 'grid') {
            // If icon is a React component
            if (typeof item.icon === 'object' && item.icon.type) {
                return (
                    <div className="w-12 h-12 flex items-center justify-center mb-4 mx-auto">
                        <div className={clsx(
                            'w-12 h-12',
                            isDark ? 'text-blue-400' : 'text-blue-600'
                        )}>
                            {item.icon}
                        </div>
                    </div>
                );
            }

            // If icon is a string
            if (typeof item.icon === 'string') {
                if (item.icon.startsWith('http') || item.icon.startsWith('/') || item.icon.includes('.')) {
                    return (
                        <div className="w-12 h-12 flex items-center justify-center mb-4 mx-auto">
                            <img
                                src={item.icon}
                                alt={item.title || 'Feature icon'}
                                className="w-12 h-12 object-contain"
                            />
                        </div>
                    );
                }

                return (
                    <div className={clsx(
                        'w-12 h-12 flex items-center justify-center mb-4 mx-auto text-3xl',
                        isDark ? 'text-blue-400' : 'text-blue-600'
                    )}>
                        {item.icon}
                    </div>
                );
            }
        }

        return null;
    };

    // Default illustration component for split-stacked layout
    const DefaultIllustration = () => (
        <div className="relative w-full h-full flex items-center justify-center">
            {/* Phone/Device */}
            <div className="relative z-10 transform rotate-[15deg]">
                <div className={clsx(
                    'w-64 h-96 rounded-3xl p-6 shadow-2xl',
                    isDark ? 'bg-gray-800 border-2 border-gray-700' : 'bg-white border-2 border-gray-200'
                )}>
                    {/* Dashboard mockup */}
                    <div className="w-full h-full rounded-2xl bg-gray-100 p-4 space-y-3">
                        {/* Pie chart */}
                        <div className="flex items-center justify-center h-24 bg-gradient-to-br from-blue-400 to-green-400 rounded-lg" />
                        {/* Bar charts */}
                        <div className="flex gap-2 h-16">
                            <div className="flex-1 bg-purple-400 rounded" />
                            <div className="flex-1 bg-green-400 rounded" />
                            <div className="flex-1 bg-orange-400 rounded" />
                            <div className="flex-1 bg-blue-400 rounded" />
                        </div>
                        {/* Line graph */}
                        <div className="flex gap-2 h-16">
                            <div className="flex-1 bg-orange-400 rounded-t" />
                            <div className="flex-1 bg-blue-400 rounded-t" />
                        </div>
                    </div>
                </div>
            </div>

            {/* Floating blocks */}
            <div className="absolute left-0 top-1/4 w-24 h-24 bg-orange-400/20 backdrop-blur-sm rounded-lg transform rotate-12" />
            <div className="absolute right-0 top-1/3 w-20 h-20 bg-green-400/20 backdrop-blur-sm rounded-lg transform -rotate-12" />
            <div className="absolute left-1/4 bottom-1/4 w-16 h-16 bg-blue-400/20 backdrop-blur-sm rounded-lg transform rotate-45" />

            {/* Icons */}
            <div className="absolute top-0 right-1/4 w-12 h-12 bg-orange-500/30 rounded-lg flex items-center justify-center text-white text-xl backdrop-blur-sm">
                
            </div>
            <div className="absolute bottom-1/4 right-0 w-12 h-12 bg-blue-500/30 rounded-lg flex items-center justify-center text-white text-lg backdrop-blur-sm font-mono">
                &lt;/&gt;
            </div>
        </div>
    );

    // Split Stacked Layout - Text/Illustration left, stacked cards right
    const renderSplitStackedLayout = () => {
        return (
            <section
                id={id}
                className={clsx(
                    'w-full py-16 sm:py-20 lg:py-24 px-4 sm:px-6 lg:px-8',
                    isDark ? 'bg-gray-900' : 'bg-white',
                    className,
                    dataClassName
                )}
            >
                <div className="max-w-7xl mx-auto">
                    <div className="grid grid-cols-2 lg:grid-cols-2 gap-12 lg:gap-16 items-center">
                        {/* Left Section - Text & Illustration */}
                        <div className="space-y-8">
                            {/* Title */}
                            {title && (
                                <h2 className={clsx(
                                    'text-4xl sm:text-5xl lg:text-6xl font-bold leading-tight',
                                    isDark ? 'text-white' : 'text-gray-900'
                                )}>
                                    {title}
                                </h2>
                            )}

                            {/* Description */}
                            {description && (
                                <p className={clsx(
                                    'text-lg sm:text-xl leading-relaxed',
                                    isDark ? 'text-gray-400' : 'text-gray-600'
                                )}>
                                    {description}
                                </p>
                            )}

                            {/* Illustration */}
                            <div className="w-full h-96 lg:h-[500px] mt-8">
                                {illustration || <DefaultIllustration />}
                            </div>
                        </div>

                        {/* Right Section - Stacked Feature Cards */}
                        <div className="space-y-6">
                            {items.map((item, index) => {
                                const colors = getIconColors(index);

                                return (
                                    <div
                                        key={index}
                                        className={clsx(
                                            'p-6 rounded-2xl transition-all duration-300',
                                            'border',
                                            isDark
                                                ? 'bg-gray-800/50 border-gray-700 hover:bg-gray-800 hover:border-gray-600'
                                                : 'bg-gray-50 border-gray-200 hover:bg-white hover:shadow-lg hover:border-gray-300',
                                            hoveredIndex === index && 'scale-[1.02]'
                                        )}
                                        onMouseEnter={() => setHoveredIndex(index)}
                                        onMouseLeave={() => setHoveredIndex(null)}
                                    >
                                        {/* Icon */}
                                        {renderIcon(item, index, 'stacked')}

                                        {/* Title */}
                                        {item.title && (
                                            <h3 className={clsx(
                                                'text-xl sm:text-2xl font-bold mb-3',
                                                isDark ? 'text-white' : 'text-gray-900'
                                            )}>
                                                {item.title}
                                            </h3>
                                        )}

                                        {/* Description */}
                                        {item.description && (
                                            <p className={clsx(
                                                'text-base leading-relaxed',
                                                isDark ? 'text-gray-400' : 'text-gray-600'
                                            )}>
                                                {item.description}
                                            </p>
                                        )}
                                    </div>
                                );
                            })}

                            {/* CTA Button */}
                            {(buttonText || buttonHref) && (
                                <button
                                    onClick={buttonOnClick}
                                    className={clsx(
                                        'w-full py-4 px-6 rounded-xl font-semibold text-base',
                                        'transition-all duration-300',
                                        isDark
                                            ? 'bg-blue-600 hover:bg-blue-700 text-white'
                                            : 'bg-blue-600 hover:bg-blue-700 text-white',
                                        'hover:shadow-lg hover:scale-[1.02]'
                                    )}
                                >
                                    {buttonText || 'Explore More'}
                                </button>
                            )}
                        </div>
                    </div>
                </div>
            </section>
        );
    };

    // Split Grid Layout - Text/CTA left, 2x2 grid cards right with glassmorphism
    const renderSplitGridLayout = () => {
        return (
            <section
                id={id}
                className={clsx(
                    'w-full py-16 sm:py-20 lg:py-24 px-4 sm:px-6 lg:px-8',
                    isDark ? 'bg-gray-900' : 'bg-white',
                    className,
                    dataClassName
                )}
            >
                <div className="max-w-7xl mx-auto">
                   

                    <div className="grid grid-cols-2 lg:grid-cols-2 gap-12 lg:gap-16 items-center">
                        {/* Left Section - Text & CTA */}
                        <div className="space-y-6">
                            {isDark && subtitle && (
                                <div className="mb-8">
                                    <p className="text-sm font-semibold uppercase tracking-widest text-blue-400">
                                        {subtitle}
                                    </p>
                                </div>
                            )}
                            {/* Subtitle (for light theme) */}
                            {!isDark && subtitle && (
                                <p className={clsx(
                                    'text-sm font-semibold uppercase tracking-widest',
                                    'text-blue-600'
                                )}>
                                    {subtitle}
                                </p>
                            )}

                            {/* Title */}
                            {title && (
                                <h2 className={clsx(
                                    'text-4xl sm:text-5xl lg:text-6xl font-bold leading-tight',
                                    isDark ? 'text-white' : 'text-gray-900'
                                )}>
                                    {title}
                                </h2>
                            )}

                            {/* Description */}
                            {description && (
                                <p className={clsx(
                                    'text-lg leading-relaxed',
                                    isDark ? 'text-gray-400' : 'text-gray-600'
                                )}>
                                    {description}
                                </p>
                            )}

                            {/* CTA Button */}
                            {(buttonText || buttonHref) && (
                                <button
                                    onClick={buttonOnClick}
                                    className={clsx(
                                        'px-8 py-4 rounded-xl font-semibold text-base',
                                        'transition-all duration-300',
                                        isDark
                                            ? 'bg-blue-600 hover:bg-blue-700 text-white'
                                            : 'bg-blue-600 hover:bg-blue-700 text-white',
                                        'hover:shadow-xl hover:scale-105'
                                    )}
                                >
                                    {buttonText || 'Start for free'}
                                </button>
                            )}
                        </div>

                         {/* Right Section - 2x2 Grid Cards */}
                         <div className="grid grid-cols-2 gap-6">
                             {items.map((item, index) => {
                                 const isHighlighted = index === 1; // Second card (top-right) is highlighted

                                 return (
                                     <div
                                         key={index}
                                         className={clsx(
                                             'relative p-6 rounded-2xl transition-all duration-500',
                                             'backdrop-blur-sm border',
                                             isDark
                                                 ? 'bg-gray-800/30 border-gray-700/50 backdrop-blur-md'
                                                 : 'bg-gray-50/80 border-gray-200/50 backdrop-blur-sm',
                                             // Default rotation for highlighted card
                                             isHighlighted && 'transform rotate-2',
                                             // Hover states
                                             isHighlighted && hoveredIndex === index && [
                                                 'rotate-2 scale-105',
                                                 isDark
                                                     ? 'shadow-2xl bg-gray-800/50 border-gray-600'
                                                     : 'shadow-2xl bg-white border-gray-300'
                                             ],
                                             !isHighlighted && hoveredIndex === index && [
                                                 'transform scale-105',
                                                 isDark
                                                     ? 'shadow-xl bg-gray-800/40 border-gray-600'
                                                     : 'shadow-xl bg-white border-gray-300'
                                             ]
                                         )}
                                         onMouseEnter={() => setHoveredIndex(index)}
                                         onMouseLeave={() => setHoveredIndex(null)}
                                     >
                                        {/* Icon */}
                                        {renderIcon(item, index, 'grid')}

                                        {/* Title */}
                                        {item.title && (
                                            <h3 className={clsx(
                                                'text-lg sm:text-xl font-bold mb-3 text-center',
                                                isDark ? 'text-white' : 'text-gray-900'
                                            )}>
                                                {item.title}
                                            </h3>
                                        )}

                                        {/* Description */}
                                        {item.description && (
                                            <p className={clsx(
                                                'text-sm leading-relaxed text-center',
                                                isDark ? 'text-gray-400' : 'text-gray-600'
                                            )}>
                                                {item.description}
                                            </p>
                                        )}
                                    </div>
                                );
                            })}
                        </div>
                    </div>
                </div>
            </section>
        );
    };

    // Render based on layout
    if (layout === 'split-grid') {
        return renderSplitGridLayout();
    }

    return renderSplitStackedLayout();
};

