import { useState, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/testimonial-carousel.css';

/**
 * TestimonialCarousel Component
 * Horizontal scrolling carousel with large testimonials
 * @param {Object} props - Component props
 * @param {Object} props.data - Testimonial section configuration data
 * @param {string} [props.data.title] - Main heading text
 * @param {string} [props.data.subtitle] - Subtitle text displayed above title
 * @param {string} [props.data.description] - Description text displayed below title
 * @param {Array} props.data.testimonials - Array of testimonial items
 * @param {string} [props.data.alignment='center'] - Header text alignment: 'left' | 'center' | 'right'
 * @param {boolean} [props.data.autoPlay=true] - Auto-play carousel
 * @param {number} [props.data.autoPlayInterval=5000] - Auto-play interval in milliseconds
 * @param {boolean} [props.data.showRating=true] - Show star ratings
 * @param {boolean} [props.data.showAvatar=true] - Show user avatars
 * @param {boolean} [props.data.showNavigation=true] - Show navigation arrows
 * @param {boolean} [props.data.showDots=true] - Show dot indicators
 * @param {string} [props.data.className] - Additional CSS classes
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute for the section element
 * @param {boolean} [props.loader=false] - Show loading state
 */
export const TestimonialCarousel = ({ data, className, id, loader = false }) => {
  if (!data || typeof data !== 'object') {
    console.error('TestimonialCarousel: data prop is required and must be an object');
    return null;
  }

  const [showLoader, setShowLoader] = useState(loader);

  // Auto-hide loader after 2 seconds when loader prop is true
  useEffect(() => {
    if (loader) {
      const timer = setTimeout(() => {
        setShowLoader(false);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowLoader(false);
    }
  }, [loader]);

  const {
    title,
    subtitle,
    description,
    testimonials = [],
    alignment = 'center',
    autoPlay = true,
    autoPlayInterval = 5000,
    showRating = true,
    showAvatar = true,
    showNavigation = true,
    showDots = true,
    className: dataClassName,
  } = data;

  // Show skeleton loader if loader prop is true (check this FIRST before any other checks)
  if (showLoader) {
    const alignmentClasses = {
      left: 'text-left items-start',
      center: 'text-center items-center',
      right: 'text-right items-end',
    };

    const effectiveAlignment = alignment || 'center';

    return (
      <section
        id={id}
        className={clsx(
          'w-full py-12 sm:py-16 lg:py-20 px-4 sm:px-6 lg:px-8',
          'bg-white',
          className,
          dataClassName
        )}
      >
        <div className="max-w-7xl mx-auto w-full">
          {/* Header skeleton */}
          <div className={clsx('mb-10 sm:mb-12 flex flex-col', alignmentClasses[effectiveAlignment])}>
            {/* Subtitle skeleton */}
            <div className="h-4 bg-gray-400 rounded w-24 mb-3 mx-auto animate-pulse"></div>
            {/* Title skeleton */}
            <div className="h-10 bg-gray-400 rounded w-full max-w-2xl mb-4 mx-auto animate-pulse"></div>
            {/* Description skeleton */}
            <div className="h-5 bg-gray-300 rounded w-full max-w-xl mb-6 mx-auto animate-pulse"></div>
          </div>

          {/* Carousel card skeleton */}
          <div className="relative">
            {/* Testimonial card skeleton */}
            <div className="bg-white border border-gray-200 rounded-xl p-8 shadow-lg max-w-3xl mx-auto">
              {/* Rating skeleton */}
              <div className="flex gap-1 justify-center mb-6">
                {[0, 1, 2, 3, 4].map((star) => (
                  <div key={star} className="w-5 h-5 bg-gray-300 rounded animate-pulse"></div>
                ))}
              </div>

              {/* Quote text skeleton */}
              <div className="space-y-3 mb-8">
                <div className="h-5 bg-gray-400 rounded w-full animate-pulse"></div>
                <div className="h-5 bg-gray-400 rounded w-full animate-pulse"></div>
                <div className="h-5 bg-gray-400 rounded w-3/4 mx-auto animate-pulse"></div>
              </div>

              {/* Author skeleton */}
              <div className="flex items-center justify-center gap-4">
                <div className="w-16 h-16 bg-gray-300 rounded-full animate-pulse"></div>
                <div className="space-y-2">
                  <div className="h-5 bg-gray-400 rounded w-32 animate-pulse"></div>
                  <div className="h-4 bg-gray-300 rounded w-24 animate-pulse"></div>
                </div>
              </div>
            </div>

            {/* Navigation dots skeleton */}
            <div className="flex justify-center gap-2 mt-8">
              {[0, 1, 2].map((dot) => (
                <div key={dot} className="w-2 h-2 bg-gray-300 rounded-full animate-pulse"></div>
              ))}
            </div>
          </div>
        </div>
      </section>
    );
  }

  const [currentIndex, setCurrentIndex] = useState(0);
  const [isPaused, setIsPaused] = useState(false);

  const alignmentClasses = {
    left: 'text-left items-start',
    center: 'text-center items-center',
    right: 'text-right items-end',
  };

  // Auto-play functionality
  useEffect(() => {
    if (!autoPlay || isPaused || testimonials.length <= 1) return;

    const interval = setInterval(() => {
      setCurrentIndex((prev) => (prev + 1) % testimonials.length);
    }, autoPlayInterval);

    return () => clearInterval(interval);
  }, [autoPlay, autoPlayInterval, isPaused, testimonials.length]);

  const goToSlide = (index) => {
    setCurrentIndex(index);
  };

  const goToPrevious = () => {
    setCurrentIndex((prev) => (prev - 1 + testimonials.length) % testimonials.length);
  };

  const goToNext = () => {
    setCurrentIndex((prev) => (prev + 1) % testimonials.length);
  };

  const renderRating = (rating) => {
    if (!showRating || !rating) return null;

    const fullStars = Math.floor(rating);
    const hasHalfStar = rating % 1 !== 0;

    return (
      <div className="flex items-center justify-center gap-1 mb-6">
        {[...Array(5)].map((_, i) => {
          if (i < fullStars) {
            return (
              <svg
                key={i}
                className="w-6 h-6 text-yellow-400 fill-current"
                viewBox="0 0 20 20"
              >
                <path d="M10 15l-5.878 3.09 1.123-6.545L.489 6.91l6.572-.955L10 0l2.939 5.955 6.572.955-4.756 4.635 1.123 6.545z" />
              </svg>
            );
          } else if (i === fullStars && hasHalfStar) {
            return (
              <svg
                key={i}
                className="w-6 h-6 text-yellow-400 fill-current"
                viewBox="0 0 20 20"
              >
                <defs>
                  <linearGradient id={`half-carousel-${i}`}>
                    <stop offset="50%" stopColor="currentColor" />
                    <stop offset="50%" stopColor="transparent" stopOpacity="1" />
                  </linearGradient>
                </defs>
                <path
                  fill={`url(#half-carousel-${i})`}
                  d="M10 15l-5.878 3.09 1.123-6.545L.489 6.91l6.572-.955L10 0l2.939 5.955 6.572.955-4.756 4.635 1.123 6.545z"
                />
              </svg>
            );
          } else {
            return (
              <svg
                key={i}
                className="w-6 h-6 text-gray-300 fill-current"
                viewBox="0 0 20 20"
              >
                <path d="M10 15l-5.878 3.09 1.123-6.545L.489 6.91l6.572-.955L10 0l2.939 5.955 6.572.955-4.756 4.635 1.123 6.545z" />
              </svg>
            );
          }
        })}
      </div>
    );
  };

  const renderAvatar = (testimonial) => {
    if (!showAvatar) return null;

    if (testimonial.avatar) {
      return (
        <img
          src={testimonial.avatar}
          alt={testimonial.name || 'User'}
          className="w-20 h-20 sm:w-24 sm:h-24 rounded-full object-cover mx-auto mb-6 border-4 border-white shadow-xl"
        />
      );
    }

    const initials = testimonial.name
      ? testimonial.name
          .split(' ')
          .map((n) => n[0])
          .join('')
          .toUpperCase()
          .slice(0, 2)
      : 'U';

    return (
      <div className="w-20 h-20 sm:w-24 sm:h-24 rounded-full bg-gradient-to-br from-primary-500 to-primary-700 text-white flex items-center justify-center font-bold text-xl sm:text-2xl mx-auto mb-6 border-4 border-white shadow-xl">
        {initials}
      </div>
    );
  };

  if (testimonials.length === 0) {
    console.warn('TestimonialCarousel: testimonials array is empty');
    return null;
  }

  const currentTestimonial = testimonials[currentIndex];

  return (
    <section
      id={id}
      className={clsx(
        'w-full py-16 sm:py-20 lg:py-24 px-4 sm:px-6 lg:px-8 bg-gradient-to-br from-gray-50 via-white to-gray-50 relative overflow-hidden',
        className,
        dataClassName
      )}
      onMouseEnter={() => setIsPaused(true)}
      onMouseLeave={() => setIsPaused(false)}
    >
      {/* Decorative background elements */}
      <div className="absolute inset-0 overflow-hidden pointer-events-none">
        <div className="absolute top-0 left-1/4 w-96 h-96 bg-primary-100 rounded-full mix-blend-multiply filter blur-3xl opacity-20 animate-blob"></div>
        <div className="absolute top-0 right-1/4 w-96 h-96 bg-purple-100 rounded-full mix-blend-multiply filter blur-3xl opacity-20 animate-blob animation-delay-2000"></div>
        <div className="absolute bottom-0 left-1/2 w-96 h-96 bg-pink-100 rounded-full mix-blend-multiply filter blur-3xl opacity-20 animate-blob animation-delay-4000"></div>
      </div>

      <div className="max-w-6xl mx-auto w-full relative z-10">
        {/* Header */}
        {(title || subtitle || description) && (
          <div
            className={clsx(
              'mb-12 sm:mb-16 flex flex-col',
              alignmentClasses[alignment]
            )}
          >
            {subtitle && (
              <p className="text-sm sm:text-base font-semibold text-primary-600 uppercase tracking-wider mb-3">
                {subtitle}
              </p>
            )}

            {title && (
              <h2 className="text-4xl sm:text-5xl md:text-6xl font-bold text-gray-900 mb-6 leading-tight">
                {title}
              </h2>
            )}

            {description && (
              <p
                className={clsx(
                  'text-lg sm:text-xl text-gray-600 max-w-3xl',
                  alignment === 'center' && 'mx-auto',
                  alignment === 'right' && 'ml-auto',
                  alignment === 'left' && 'mr-auto'
                )}
              >
                {description}
              </p>
            )}
          </div>
        )}

        {/* Carousel Container */}
        <div className="relative">
          {/* Navigation Arrows */}
          {showNavigation && testimonials.length > 1 && (
            <>
              <button
                onClick={goToPrevious}
                className="absolute left-0 top-1/2 -translate-y-1/2 -translate-x-4 sm:-translate-x-12 z-20 w-12 h-12 sm:w-14 sm:h-14 rounded-full bg-white shadow-lg hover:shadow-xl flex items-center justify-center text-gray-700 hover:text-primary-600 transition-all duration-300 hover:scale-110 border border-gray-200"
                aria-label="Previous testimonial"
              >
                <svg
                  className="w-6 h-6"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M15 19l-7-7 7-7"
                  />
                </svg>
              </button>
              <button
                onClick={goToNext}
                className="absolute right-0 top-1/2 -translate-y-1/2 translate-x-4 sm:translate-x-12 z-20 w-12 h-12 sm:w-14 sm:h-14 rounded-full bg-white shadow-lg hover:shadow-xl flex items-center justify-center text-gray-700 hover:text-primary-600 transition-all duration-300 hover:scale-110 border border-gray-200"
                aria-label="Next testimonial"
              >
                <svg
                  className="w-6 h-6"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M9 5l7 7-7 7"
                  />
                </svg>
              </button>
            </>
          )}

          {/* Testimonial Card */}
          <div className="bg-white rounded-3xl shadow-2xl p-8 sm:p-12 lg:p-16 mx-auto max-w-4xl transform transition-all duration-500 ease-in-out hover:shadow-3xl">
            {/* Quote Icon */}
            <div className="flex justify-center mb-6">
              <svg
                className="w-12 h-12 sm:w-16 sm:h-16 text-primary-300"
                fill="currentColor"
                viewBox="0 0 24 24"
              >
                <path d="M14.017 21v-7.391c0-5.704 3.731-9.57 8.983-10.609l.996 2.151c-2.432.917-3.995 3.638-3.995 5.849h4v10h-9.984zm-14.017 0v-7.391c0-5.704 3.748-9.57 9-10.609l.996 2.151c-2.433.917-3.996 3.638-3.996 5.849h3.983v10h-9.983z" />
              </svg>
            </div>

            {/* Avatar */}
            {renderAvatar(currentTestimonial)}

            {/* Rating */}
            {renderRating(currentTestimonial.rating)}

            {/* Testimonial Text */}
            {(currentTestimonial.text || currentTestimonial.content) && (
              <p className="text-xl sm:text-2xl lg:text-3xl text-gray-800 mb-8 leading-relaxed font-light italic text-center px-4">
                "{currentTestimonial.text || currentTestimonial.content}"
              </p>
            )}

            {/* Author Info */}
            <div className="text-center">
              {currentTestimonial.name && (
                <h4 className="text-xl sm:text-2xl font-bold text-gray-900 mb-2">
                  {currentTestimonial.name}
                </h4>
              )}
              <div className="flex flex-col sm:flex-row items-center justify-center gap-2 sm:gap-4">
                {currentTestimonial.role && (
                  <p className="text-base sm:text-lg text-gray-600 font-medium">
                    {currentTestimonial.role}
                  </p>
                )}
                {currentTestimonial.company && (
                  <>
                    {currentTestimonial.role && (
                      <span className="hidden sm:inline text-gray-400"></span>
                    )}
                    <p className="text-base sm:text-lg text-primary-600 font-semibold">
                      {currentTestimonial.company}
                    </p>
                  </>
                )}
              </div>
            </div>
          </div>

          {/* Dot Indicators */}
          {showDots && testimonials.length > 1 && (
            <div className="flex justify-center items-center gap-2 mt-8">
              {testimonials.map((_, index) => (
                <button
                  key={index}
                  onClick={() => goToSlide(index)}
                  className={clsx(
                    'w-3 h-3 rounded-full transition-all duration-300',
                    index === currentIndex
                      ? 'bg-primary-600 w-8 scale-110'
                      : 'bg-gray-300 hover:bg-gray-400'
                  )}
                  aria-label={`Go to testimonial ${index + 1}`}
                />
              ))}
            </div>
          )}
        </div>
      </div>
    </section>
  );
};

