import { useState, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/testimonial-timeline.css';

/**
 * TestimonialTimeline Component
 * Vertical timeline layout with alternating sides
 * @param {Object} props - Component props
 * @param {Object} props.data - Testimonial section configuration data
 * @param {string} [props.data.title] - Main heading text
 * @param {string} [props.data.subtitle] - Subtitle text displayed above title
 * @param {string} [props.data.description] - Description text displayed below title
 * @param {Array} props.data.testimonials - Array of testimonial items
 * @param {string} [props.data.alignment='center'] - Header text alignment: 'left' | 'center' | 'right'
 * @param {string} [props.data.variant='default'] - Layout variant: 'default' | 'avatar-opposite'
 * @param {boolean} [props.data.showRating=true] - Show star ratings
 * @param {boolean} [props.data.showAvatar=true] - Show user avatars
 * @param {string} [props.data.className] - Additional CSS classes
 * @param {string} [props.contextKey='testimonials'] - Key to get data from LayoutDataProvider context (default: 'testimonials')
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute for the section element
 * @param {boolean} [props.loader=false] - Show loading state
 */
export const TestimonialTimeline = ({ data, className, id, loader = false }) => {
  if (!data || typeof data !== 'object') {
    console.error('TestimonialTimeline: data prop is required and must be an object');
    return null;
  }

  const [showLoader, setShowLoader] = useState(loader);

  // Auto-hide loader after 2 seconds when loader prop is true
  useEffect(() => {
    if (loader) {
      const timer = setTimeout(() => {
        setShowLoader(false);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowLoader(false);
    }
  }, [loader]);

  const {
    title,
    subtitle,
    description,
    testimonials = [],
    alignment = 'center',
    variant = 'default',
    showRating = true,
    showAvatar = true,
    className: dataClassName,
  } = data;

  // Show skeleton loader if loader prop is true
  if (showLoader) {
    const alignmentClasses = {
      left: 'text-left items-start',
      center: 'text-center items-center',
      right: 'text-right items-end',
    };

    const effectiveAlignment = alignment || 'center';

    return (
      <section
        id={id}
        className={clsx(
          'w-full py-12 sm:py-16 lg:py-20 px-4 sm:px-6 lg:px-8',
          'bg-white',
          className,
          dataClassName
        )}
      >
        <div className="max-w-7xl mx-auto w-full">
          {/* Header skeleton */}
          <div className={clsx('mb-10 sm:mb-12 flex flex-col', alignmentClasses[effectiveAlignment])}>
            <div className="h-4 bg-gray-400 rounded w-24 mb-3 mx-auto animate-pulse"></div>
            <div className="h-10 bg-gray-400 rounded w-full max-w-2xl mb-4 mx-auto animate-pulse"></div>
            <div className="h-5 bg-gray-300 rounded w-full max-w-xl mb-6 mx-auto animate-pulse"></div>
          </div>

          {/* Timeline skeleton */}
          <div className="relative">
            {/* Vertical line skeleton */}
            <div className="absolute left-8 top-0 bottom-0 w-0.5 bg-gray-200"></div>

            {/* Timeline items skeleton */}
            <div className="space-y-8">
              {[0, 1, 2, 3].map((index) => (
                <div key={index} className="relative flex gap-6">
                  {/* Avatar skeleton */}
                  <div className="flex-shrink-0">
                    <div className="w-16 h-16 bg-gray-300 rounded-full animate-pulse"></div>
                  </div>

                  {/* Content skeleton */}
                  <div className="flex-1 pt-2">
                    {/* Rating skeleton */}
                    <div className="flex gap-1 mb-3">
                      {[0, 1, 2, 3, 4].map((star) => (
                        <div key={star} className="w-4 h-4 bg-gray-300 rounded animate-pulse"></div>
                      ))}
                    </div>

                    {/* Text skeleton */}
                    <div className="space-y-2 mb-4">
                      <div className="h-4 bg-gray-400 rounded w-full animate-pulse"></div>
                      <div className="h-4 bg-gray-400 rounded w-5/6 animate-pulse"></div>
                      <div className="h-4 bg-gray-300 rounded w-4/6 animate-pulse"></div>
                    </div>

                    {/* Author skeleton */}
                    <div className="flex items-center gap-3">
                      <div className="h-5 bg-gray-400 rounded w-32 animate-pulse"></div>
                      <div className="h-4 bg-gray-300 rounded w-24 animate-pulse"></div>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </section>
    );
  }

  const alignmentClasses = {
    left: 'text-left items-start',
    center: 'text-center items-center',
    right: 'text-right items-end',
  };

  const renderRating = (rating) => {
    if (!showRating || !rating) return null;

    const fullStars = Math.floor(rating);
    const hasHalfStar = rating % 1 !== 0;

    return (
      <div className="flex items-center gap-1 mb-4">
        {[...Array(5)].map((_, i) => {
          if (i < fullStars) {
            return (
              <svg
                key={i}
                className="w-4 h-4 text-yellow-400 fill-current"
                viewBox="0 0 20 20"
              >
                <path d="M10 15l-5.878 3.09 1.123-6.545L.489 6.91l6.572-.955L10 0l2.939 5.955 6.572.955-4.756 4.635 1.123 6.545z" />
              </svg>
            );
          } else if (i === fullStars && hasHalfStar) {
            return (
              <svg
                key={i}
                className="w-4 h-4 text-yellow-400 fill-current"
                viewBox="0 0 20 20"
              >
                <defs>
                  <linearGradient id={`half-timeline-${i}`}>
                    <stop offset="50%" stopColor="currentColor" />
                    <stop offset="50%" stopColor="transparent" stopOpacity="1" />
                  </linearGradient>
                </defs>
                <path
                  fill={`url(#half-timeline-${i})`}
                  d="M10 15l-5.878 3.09 1.123-6.545L.489 6.91l6.572-.955L10 0l2.939 5.955 6.572.955-4.756 4.635 1.123 6.545z"
                />
              </svg>
            );
          } else {
            return (
              <svg
                key={i}
                className="w-4 h-4 text-gray-300 fill-current"
                viewBox="0 0 20 20"
              >
                <path d="M10 15l-5.878 3.09 1.123-6.545L.489 6.91l6.572-.955L10 0l2.939 5.955 6.572.955-4.756 4.635 1.123 6.545z" />
              </svg>
            );
          }
        })}
      </div>
    );
  };

  const renderAvatar = (testimonial, isOpposite = false) => {
    if (!showAvatar) return null;

    const sizeClasses = isOpposite 
      ? 'w-20 h-20 sm:w-24 sm:h-24 lg:w-28 lg:h-28' 
      : 'w-14 h-14 sm:w-16 sm:h-16';
    const textSizeClasses = isOpposite 
      ? 'text-lg sm:text-xl lg:text-2xl' 
      : 'text-sm sm:text-base';

    const avatarWrapperClasses = isOpposite
      ? 'timeline-avatar-opposite relative group'
      : '';

    if (testimonial.avatar) {
      return (
        <div className={avatarWrapperClasses}>
          {/* Glow effect ring */}
          {isOpposite && (
            <div className="absolute inset-0 rounded-full bg-primary-400 opacity-0 group-hover:opacity-30 blur-xl transition-opacity duration-300 animate-pulse-slow"></div>
          )}
          {/* Outer ring */}
          {isOpposite && (
            <div className="absolute inset-0 rounded-full border-4 border-primary-200 opacity-0 group-hover:opacity-100 transition-opacity duration-300 scale-110"></div>
          )}
          <img
            src={testimonial.avatar}
            alt={testimonial.name || 'User'}
            className={clsx(
              'rounded-full object-cover border-2 border-white shadow-lg flex-shrink-0 relative z-10 transition-all duration-300',
              isOpposite && 'group-hover:scale-110 group-hover:shadow-2xl group-hover:border-primary-300',
              sizeClasses
            )}
          />
        </div>
      );
    }

    const initials = testimonial.name
      ? testimonial.name
          .split(' ')
          .map((n) => n[0])
          .join('')
          .toUpperCase()
          .slice(0, 2)
      : 'U';

    return (
      <div className={avatarWrapperClasses}>
        {/* Glow effect ring */}
        {isOpposite && (
          <div className="absolute inset-0 rounded-full bg-primary-400 opacity-0 group-hover:opacity-30 blur-xl transition-opacity duration-300 animate-pulse-slow"></div>
        )}
        {/* Outer ring */}
        {isOpposite && (
          <div className="absolute inset-0 rounded-full border-4 border-primary-200 opacity-0 group-hover:opacity-100 transition-opacity duration-300 scale-110"></div>
        )}
        <div className={clsx(
          'rounded-full bg-gradient-to-br from-primary-500 to-primary-700 text-white flex items-center justify-center font-bold border-2 border-white shadow-lg flex-shrink-0 relative z-10 transition-all duration-300',
          isOpposite && 'group-hover:scale-110 group-hover:shadow-2xl group-hover:border-primary-300 group-hover:from-primary-600 group-hover:to-primary-800',
          sizeClasses,
          textSizeClasses
        )}>
          {initials}
        </div>
      </div>
    );
  };

  if (testimonials.length === 0) {
    console.warn('TestimonialTimeline: testimonials array is empty');
    return null;
  }

  return (
    <section
      id={id}
      className={clsx(
        'w-full py-16 sm:py-20 lg:py-24 px-4 sm:px-6 lg:px-8 bg-white relative',
        className,
        dataClassName
      )}
    >
      <div className="max-w-7xl mx-auto w-full">
        {/* Header */}
        {(title || subtitle || description) && (
          <div
            className={clsx(
              'mb-12 sm:mb-16 flex flex-col',
              alignmentClasses[alignment]
            )}
          >
            {subtitle && (
              <p className="text-sm sm:text-base font-semibold text-primary-600 uppercase tracking-wider mb-3">
                {subtitle}
              </p>
            )}

            {title && (
              <h2 className="text-3xl sm:text-4xl md:text-5xl font-bold text-gray-900 mb-4 leading-tight">
                {title}
              </h2>
            )}

            {description && (
              <p
                className={clsx(
                  'text-base sm:text-lg text-gray-600 max-w-2xl',
                  alignment === 'center' && 'mx-auto',
                  alignment === 'right' && 'ml-auto',
                  alignment === 'left' && 'mr-auto'
                )}
              >
                {description}
              </p>
            )}
          </div>
        )}

        {/* Timeline Container */}
        <div className="relative w-full timeline-container">
          {/* Vertical Timeline Line - positioned absolutely to span full height */}
          <div className="absolute left-1/2 transform -translate-x-1/2 w-1 bg-gradient-to-b from-primary-200 via-primary-400 to-primary-200 hidden lg:block timeline-line-fix"></div>

          {/* Timeline Items Wrapper - ensures proper height calculation */}
          <div className="relative z-0 pb-8">
            <div className="space-y-12 sm:space-y-16 lg:space-y-20">
            {testimonials.map((testimonial, index) => {
              const isEven = index % 2 === 0;
              const isLeft = isEven;
              const isAvatarOpposite = variant === 'avatar-opposite';

              return (
                <div
                  key={index}
                  className={clsx(
                    'relative flex items-center timeline-item',
                    'lg:flex-row',
                    isLeft ? 'lg:flex-row-reverse' : 'lg:flex-row',
                    'flex-col lg:justify-between'
                  )}
                >
                  {/* Timeline Node */}
                  <div className="absolute left-1/2 transform -translate-x-1/2 -translate-y-1/2 top-1/2 z-10 hidden lg:flex">
                    <div className="w-6 h-6 rounded-full bg-primary-600 border-4 border-white shadow-lg timeline-node"></div>
                  </div>

                  {/* Avatar on opposite side (for avatar-opposite variant) */}
                  {isAvatarOpposite && (
                    <div className={clsx(
                      'hidden lg:flex items-center relative group',
                      'w-[20%]',
                      isLeft ? 'justify-end pl-8' : 'justify-start pr-8'
                    )}>
                      {/* Connecting line from avatar to timeline */}
                      <div className={clsx(
                        'absolute top-1/2 transform -translate-y-1/2 h-0.5 bg-gradient-to-r bg-primary-300 opacity-50 transition-all duration-300 group-hover:opacity-100 group-hover:w-20',
                        isLeft 
                          ? 'right-0 w-16 origin-right' 
                          : 'left-0 w-16 origin-left'
                      )}></div>
                      <div className="relative z-20">
                        {renderAvatar(testimonial, true)}
                      </div>
                    </div>
                  )}

                  {/* Testimonial Card */}
                  <div
                    className={clsx(
                      'w-full lg:w-[60%] relative',
                      isLeft ? 'lg:pr-8' : 'lg:pl-8'
                    )}
                  >
                    <div
                      className={clsx(
                        'bg-gradient-to-br from-gray-50 to-white rounded-2xl p-6 sm:p-8 shadow-lg hover:shadow-xl transition-all duration-300 border-l-4 timeline-card',
                        isLeft
                          ? 'border-l-primary-500 lg:border-l-0 lg:border-r-4 lg:border-r-primary-500'
                          : 'border-l-primary-500'
                      )}
                    >
                      {/* Quote Icon */}
                      <div className="mb-4">
                        <svg
                          className="w-8 h-8 text-primary-300"
                          fill="currentColor"
                          viewBox="0 0 24 24"
                        >
                          <path d="M14.017 21v-7.391c0-5.704 3.731-9.57 8.983-10.609l.996 2.151c-2.432.917-3.995 3.638-3.995 5.849h4v10h-9.984zm-14.017 0v-7.391c0-5.704 3.748-9.57 9-10.609l.996 2.151c-2.433.917-3.996 3.638-3.996 5.849h3.983v10h-9.983z" />
                        </svg>
                      </div>

                      {/* Rating */}
                      {renderRating(testimonial.rating)}

                      {/* Testimonial Text */}
                      {(testimonial.text || testimonial.content) && (
                        <p className="text-base sm:text-lg text-gray-700 mb-6 leading-relaxed">
                          {testimonial.text || testimonial.content}
                        </p>
                      )}

                      {/* Author Info */}
                      <div className={clsx(
                        'pt-4 border-t border-gray-200',
                        isAvatarOpposite ? 'flex flex-col' : 'flex items-center gap-4'
                      )}>
                        {!isAvatarOpposite && renderAvatar(testimonial)}
                        <div>
                          {testimonial.name && (
                            <h4 className="text-lg font-bold text-gray-900 mb-1">
                              {testimonial.name}
                            </h4>
                          )}
                          <div className="flex flex-col">
                            {testimonial.role && (
                              <p className="text-sm text-gray-600">
                                {testimonial.role}
                              </p>
                            )}
                            {testimonial.company && (
                              <p className="text-sm text-primary-600 font-medium">
                                {testimonial.company}
                              </p>
                            )}
                          </div>
                        </div>
                      </div>
                    </div>

                    {/* Arrow pointing to timeline */}
                    <div
                      className={clsx(
                        'hidden lg:block absolute top-1/2 transform -translate-y-1/2 w-0 h-0',
                        isLeft
                          ? 'right-0 border-t-8 border-b-8 border-l-8 border-t-transparent border-b-transparent border-l-primary-500'
                          : 'left-0 border-t-8 border-b-8 border-r-8 border-t-transparent border-b-transparent border-r-primary-500'
                      )}
                    ></div>
                  </div>

                  {/* Empty space or Avatar for alternating layout */}
                  {!isAvatarOpposite && (
                    <div className="hidden lg:block w-[20%]"></div>
                  )}
                </div>
              );
            })}
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

