import { useState, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/product.css';

/**
 * ProductToggle Component - Toggle between grid and list view with multiple variants
 * @param {Object} props - Component props
 * @param {Object} props.data - Toggle configuration data
 * @param {string} [props.data.variant='default'] - Style variant: 'default' | 'modern' | 'minimal' | 'compact' | 'icon' | 'dark' | 'dark-gray'
 * @param {string} [props.data.defaultView='grid'] - Default view: 'grid' | 'list'
 * @param {boolean} [props.data.showLabel=true] - Show view label
 * @param {Function} [props.onViewChange] - Callback when view changes
 * @param {boolean} [props.loading=false] - Show skeleton loader
 * @param {string} [props.className] - Additional CSS classes
 */
export const ProductToggle = ({ 
  data = {}, 
  onViewChange,
  loading = false,
  className 
}) => {
  const {
    variant = 'default',
    defaultView = 'grid',
    showLabel = true,
  } = data;

  const [currentView, setCurrentView] = useState(defaultView);
  const [showContent, setShowContent] = useState(!loading);

  // Show skeleton for 2 seconds when loading is true
  useEffect(() => {
    if (loading) {
      setShowContent(false);
      const timer = setTimeout(() => {
        setShowContent(true);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowContent(true);
    }
  }, [loading]);

  const handleViewChange = (view) => {
    setCurrentView(view);
    if (onViewChange) {
      onViewChange(view);
    }
  };

  // Container classes - responsive with mobile-first approach
  const containerClasses = {
    default: 'flex items-center gap-2 sm:gap-2.5',
    modern: 'flex items-center gap-2 sm:gap-3 bg-gray-100 p-1 sm:p-1.5 rounded-lg sm:rounded-xl',
    minimal: 'flex items-center gap-1 sm:gap-2',
    compact: 'flex items-center gap-1 sm:gap-1.5',
    icon: 'flex items-center gap-1 sm:gap-2',
    dark: 'flex items-center gap-2 sm:gap-2.5 bg-gray-800 p-1 sm:p-1.5 rounded-lg',
    'dark-gray': 'flex items-center gap-2 sm:gap-2.5 bg-gray-900 p-1 sm:p-1.5 rounded-lg',
  };

  // Icon size classes
  const iconSizeClasses = {
    default: 'w-4 h-4 sm:w-5 sm:h-5',
    modern: 'w-4 h-4 sm:w-5 sm:h-5',
    minimal: 'w-4 h-4 sm:w-5 sm:h-5',
    compact: 'w-3.5 h-3.5 sm:w-4 sm:h-4',
    icon: 'w-5 h-5 sm:w-6 sm:h-6',
    dark: 'w-4 h-4 sm:w-5 sm:h-5',
    'dark-gray': 'w-4 h-4 sm:w-5 sm:h-5',
  };

  // Button classes
  const getButtonClasses = (view) => {
    const isActive = currentView === view;
    const baseClasses = {
      default: clsx(
        'p-1.5 sm:p-2 rounded-md sm:rounded-lg',
        'transition-all duration-200',
        '',
        isActive 
          ? 'bg-primary-600 text-white shadow-sm' 
          : 'bg-gray-200 text-gray-600 hover:bg-gray-300'
      ),
      modern: clsx(
        'px-3 sm:px-4 py-1.5 sm:py-2 rounded-lg font-medium text-xs sm:text-sm',
        'transition-all duration-300',
        '',
        isActive 
          ? 'bg-white text-primary-700 shadow-md' 
          : 'text-gray-600 hover:text-gray-900'
      ),
      minimal: clsx(
        'p-1.5 sm:p-2 transition-all duration-200',
        '',
        isActive 
          ? 'text-primary-600 border-b-2 border-primary-600' 
          : 'text-gray-500 hover:text-gray-700'
      ),
      compact: clsx(
        'p-1 sm:p-1.5 rounded transition-all duration-200',
        '',
        isActive 
          ? 'bg-primary-600 text-white' 
          : 'bg-gray-200 text-gray-600 hover:bg-gray-300'
      ),
      icon: clsx(
        'p-2 sm:p-2.5 rounded-lg',
        'transition-all duration-300',
        '',
        isActive 
          ? 'bg-primary-600 text-white shadow-lg' 
          : 'bg-primary-500 text-white hover:bg-primary-600 shadow-md hover:shadow-lg'
      ),
      dark: clsx(
        'p-1.5 sm:p-2 rounded-md sm:rounded-lg',
        'transition-all duration-200',
        '',
        isActive 
          ? 'bg-primary-500 text-white shadow-sm' 
          : 'bg-gray-700 text-gray-300 hover:bg-gray-600'
      ),
      'dark-gray': clsx(
        'p-1.5 sm:p-2 rounded-md sm:rounded-lg',
        'transition-all duration-200',
        '',
        isActive 
          ? 'bg-gray-700 text-white shadow-sm' 
          : 'bg-gray-800 text-gray-400 hover:bg-gray-700'
      ),
    };
    return baseClasses[variant] || baseClasses.default;
  };

  // Label classes
  const labelClasses = {
    default: 'text-xs sm:text-sm font-medium text-gray-700 mr-1 sm:mr-2',
    modern: 'text-xs sm:text-sm font-medium text-gray-700 mr-2 sm:mr-3',
    minimal: 'text-xs sm:text-sm font-medium text-gray-700 mr-1 sm:mr-2',
    compact: 'text-xs font-medium text-gray-700 mr-1',
    icon: 'hidden',
    dark: 'text-xs sm:text-sm font-medium text-gray-300 mr-1 sm:mr-2',
    'dark-gray': 'text-xs sm:text-sm font-medium text-gray-400 mr-1 sm:mr-2',
  };

  // Grid icon SVG
  const GridIcon = ({ className }) => (
    <svg
      className={className}
      fill="none"
      stroke="currentColor"
      viewBox="0 0 24 24"
    >
      <path
        strokeLinecap="round"
        strokeLinejoin="round"
        strokeWidth={2}
        d="M4 6a2 2 0 012-2h2a2 2 0 012 2v2a2 2 0 01-2 2H6a2 2 0 01-2-2V6zM14 6a2 2 0 012-2h2a2 2 0 012 2v2a2 2 0 01-2 2h-2a2 2 0 01-2-2V6zM4 16a2 2 0 012-2h2a2 2 0 012 2v2a2 2 0 01-2 2H6a2 2 0 01-2-2v-2zM14 16a2 2 0 012-2h2a2 2 0 012 2v2a2 2 0 01-2 2h-2a2 2 0 01-2-2v-2z"
      />
    </svg>
  );

  // List icon SVG
  const ListIcon = ({ className }) => (
    <svg
      className={className}
      fill="none"
      stroke="currentColor"
      viewBox="0 0 24 24"
    >
      <path
        strokeLinecap="round"
        strokeLinejoin="round"
        strokeWidth={2}
        d="M4 6h16M4 12h16M4 18h16"
      />
    </svg>
  );

  // Skeleton Loader Component
  const SkeletonLoader = () => {
    const isDark = variant === 'dark' || variant === 'dark-gray';
    const skeletonBg = isDark ? 'bg-gray-700' : 'bg-gray-200';
    const skeletonText = isDark ? 'bg-gray-600' : 'bg-gray-300';

    return (
      <div className={clsx(containerClasses[variant] || containerClasses.default, className)}>
        {showLabel && variant !== 'icon' && (
          <div className={clsx(
            labelClasses[variant] || labelClasses.default,
            'h-4 w-12 sm:w-16 rounded product-skeleton',
            skeletonText
          )}></div>
        )}
        <div className={clsx(
          variant === 'icon' ? 'w-10 h-10 sm:w-12 sm:h-12' : 'w-8 h-8 sm:w-9 sm:h-9',
          variant === 'compact' ? 'w-7 h-7 sm:w-8 sm:h-8' : '',
          'rounded-lg product-skeleton',
          skeletonBg
        )}></div>
        <div className={clsx(
          variant === 'icon' ? 'w-10 h-10 sm:w-12 sm:h-12' : 'w-8 h-8 sm:w-9 sm:h-9',
          variant === 'compact' ? 'w-7 h-7 sm:w-8 sm:h-8' : '',
          'rounded-lg product-skeleton',
          skeletonBg
        )}></div>
      </div>
    );
  };

  // Show skeleton loader if content is not ready
  if (!showContent) {
    return <SkeletonLoader />;
  }

  return (
    <div className={clsx(containerClasses[variant] || containerClasses.default, className)}>
      {showLabel && variant !== 'icon' && (
        <span className={labelClasses[variant] || labelClasses.default}>
          View:
        </span>
      )}
      
      <button
        onClick={() => handleViewChange('grid')}
        className={getButtonClasses('grid')}
        aria-label="Grid view"
        title="Grid view"
        type="button"
      >
        <GridIcon className={iconSizeClasses[variant] || iconSizeClasses.default} />
      </button>

      <button
        onClick={() => handleViewChange('list')}
        className={getButtonClasses('list')}
        aria-label="List view"
        title="List view"
        type="button"
      >
        <ListIcon className={iconSizeClasses[variant] || iconSizeClasses.default} />
      </button>
    </div>
  );
};

