import React, { useState, useEffect } from 'react';
import clsx from 'clsx';

/**
 * CartPremium Component - Premium cart with luxury design
 * @param {Object} props - Component props
 * @param {Object} props.data - Cart configuration data
 * @param {Array} props.data.items - Cart items array
 * @param {string} [props.variant='default'] - Visual variant: 'default' | 'luxury'
 * @param {boolean} [props.loader=false] - Show skeleton loader
 * @param {string} [props.data.title='Luxury Collection'] - Cart title
 * @param {boolean} [props.data.showSubtotal=true] - Show subtotal
 * @param {boolean} [props.data.showTax=true] - Show tax
 * @param {boolean} [props.data.showShipping=true] - Show shipping
 * @param {boolean} [props.data.showTotal=true] - Show total
 * @param {boolean} [props.data.showCheckoutButton=true] - Show checkout button
 * @param {boolean} [props.data.showContinueShopping=true] - Show continue shopping link
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute for the section element
 */
export const CartPremium = ({ data, variant = 'default', loader: initialLoader = false, className, id }) => {
  const [cartItems, setCartItems] = useState(data?.items || []);
  const [isLoading, setIsLoading] = useState(initialLoader);

  // Auto-switch from skeleton to content after 2 seconds
  useEffect(() => {
    if (initialLoader) {
      const timer = setTimeout(() => {
        setIsLoading(false);
      }, 2000);

      return () => clearTimeout(timer);
    }
  }, [initialLoader]);

  const loader = initialLoader ? isLoading : false;

  if (!data || typeof data !== 'object') {
    console.error('CartPremium: data prop is required and must be an object');
    return null;
  }

  const {
    title = 'Luxury Collection',
    showSubtotal = true,
    showTax = true,
    showShipping = true,
    showTotal = true,
    showCheckoutButton = true,
    showContinueShopping = true,
  } = data;

  // Calculate totals
  const subtotal = cartItems.reduce((total, item) => total + (item.price * item.quantity), 0);
  const tax = subtotal * 0.08; // 8% tax
  const shipping = subtotal > 500 ? 0 : 0; // Free shipping for premium
  const total = subtotal + tax + shipping;

  const isLuxury = variant === 'luxury';

  // Skeleton components
  const SkeletonHeader = () => (
    <div className="text-center mb-12 sm:mb-16">
      <div className={clsx("h-16 bg-gray-200 rounded animate-pulse mb-4", isLuxury ? "w-80" : "w-96 mx-auto")}></div>
      <div className={clsx("h-8 bg-gray-200 rounded animate-pulse", isLuxury ? "w-48 mx-auto" : "w-64 mx-auto")}></div>
    </div>
  );

  const SkeletonCartItem = () => (
    <div className={clsx(
      "p-6 sm:p-8 transition-all duration-500 animate-pulse",
      isLuxury ? "bg-white border-b-2 border-gray-900" : "bg-gray-900 rounded-3xl border border-gray-800 hover:border-gray-700"
    )}>
      <div className="flex flex-col lg:flex-row gap-6 sm:gap-8">
        {/* Product Image Skeleton */}
        <div className="flex-shrink-0 flex justify-center lg:justify-start">
          <div className="relative w-full max-w-[200px] lg:w-40">
            <div className={clsx(
              "w-full aspect-square bg-gray-200",
              isLuxury ? "border-2 border-gray-900" : "rounded-2xl"
            )}></div>
            <div className="absolute -top-3 -right-3 w-8 h-8 bg-gray-200 rounded-full"></div>
          </div>
        </div>

        {/* Product Details Skeleton */}
        <div className="flex-1 min-w-0">
          <div className="flex justify-between items-start mb-4 sm:mb-6">
            <div className="flex-1 pr-4">
              <div className={clsx("h-8 bg-gray-200 rounded mb-2 sm:mb-3", isLuxury ? "w-56" : "w-48")}></div>
              <div className={clsx("h-6 bg-gray-200 rounded mb-2 sm:mb-3", isLuxury ? "w-64" : "w-60")}></div>
              <div className="h-4 bg-gray-200 rounded w-24"></div>
            </div>
            <div className="w-6 h-6 bg-gray-200 rounded-xl flex-shrink-0"></div>
          </div>

          {/* Price and Quantity Skeleton */}
          <div className="flex flex-col sm:flex-row justify-between items-start sm:items-end gap-4 sm:gap-6">
            <div className="flex flex-wrap items-center gap-4 sm:gap-8 w-full sm:w-auto">
              <div className={clsx("h-8 bg-gray-200 rounded", isLuxury ? "w-20" : "w-24")}></div>
              <div className={clsx("flex items-center gap-3 sm:gap-4 p-1.5 sm:p-2 bg-gray-200", isLuxury ? "border border-gray-900" : "rounded-2xl")}>
                <div className="w-10 h-10 sm:w-12 sm:h-12 bg-gray-300 rounded-xl"></div>
                <div className="w-10 sm:w-16 h-6 bg-gray-300 rounded"></div>
                <div className="w-10 h-10 sm:w-12 sm:h-12 bg-gray-300 rounded-xl"></div>
              </div>
            </div>
            <div className="text-right w-full sm:w-auto">
              <div className={clsx("h-8 bg-gray-200 rounded", isLuxury ? "w-24" : "w-28")}></div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );

  const SkeletonOrderSummary = () => (
    <div className="xl:col-span-2 mt-8 xl:mt-0">
      <div className={clsx(
        "p-6 sm:p-8 sticky top-4 transition-all duration-500 animate-pulse",
        isLuxury ? "bg-white border-4 border-gray-900 shadow-[20px_20px_0px_0px_rgba(0,0,0,1)]" : "bg-gray-900 rounded-3xl border border-gray-800"
      )}>
        <div className={clsx("h-8 bg-gray-200 rounded mb-6 sm:mb-8", isLuxury ? "w-24" : "w-32")}></div>

        <div className="space-y-4 sm:space-y-6">
          {showSubtotal && (
            <div className="flex justify-between items-center">
              <div className="h-5 bg-gray-200 rounded w-16"></div>
              <div className="h-5 bg-gray-200 rounded w-12"></div>
            </div>
          )}

          {showTax && (
            <div className="flex justify-between items-center">
              <div className="h-5 bg-gray-200 rounded w-12"></div>
              <div className="h-5 bg-gray-200 rounded w-10"></div>
            </div>
          )}

          {showShipping && (
            <div className="flex justify-between items-center">
              <div className="h-5 bg-gray-200 rounded w-16"></div>
              <div className="h-5 bg-gray-200 rounded w-14"></div>
            </div>
          )}

          <hr className={isLuxury ? "border-gray-900 border-t-2" : "border-gray-800"} />

          {showTotal && (
            <div className="flex justify-between items-center">
              <div className="h-8 bg-gray-200 rounded w-12"></div>
              <div className="h-12 bg-gray-200 rounded w-24"></div>
            </div>
          )}
        </div>

        {showCheckoutButton && (
          <div className={clsx("h-14 bg-gray-200 rounded mt-8 sm:mt-10 animate-pulse", isLuxury ? "" : "rounded-2xl")}></div>
        )}

        {showContinueShopping && (
          <div className={clsx("h-14 bg-gray-200 rounded mt-4 sm:mt-6 animate-pulse", isLuxury ? "" : "rounded-2xl")}></div>
        )}

        {/* Premium features */}
        <div className={clsx("mt-8 sm:mt-10 pt-6 sm:pt-8 border-t", isLuxury ? "border-gray-900" : "border-gray-800")}>
          <div className="space-y-3 sm:space-y-4">
            <div className="flex items-center gap-3 sm:gap-4">
              <div className="w-4 h-4 sm:w-5 sm:h-5 bg-gray-200 rounded"></div>
              <div className="h-4 bg-gray-200 rounded w-40"></div>
            </div>
            <div className="flex items-center gap-3 sm:gap-4">
              <div className="w-4 h-4 sm:w-5 sm:h-5 bg-gray-200 rounded"></div>
              <div className="h-4 bg-gray-200 rounded w-36"></div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );

  // Show skeleton loader
  if (loader) {
    return (
      <section
        id={id}
        className={clsx(
          'w-full py-12 sm:py-16 md:py-20 lg:py-24 px-4 sm:px-6 lg:px-8',
          isLuxury ? 'bg-white' : 'bg-black',
          className
        )}
      >
        <div className="max-w-7xl mx-auto">
          <SkeletonHeader />
          <div className="grid grid-cols-1 xl:grid-cols-5 gap-8 sm:gap-12">
            {/* Cart Items Skeleton */}
            <div className="xl:col-span-3 space-y-6 sm:space-y-8">
              <SkeletonCartItem />
              <SkeletonCartItem />
              <SkeletonCartItem />
            </div>
            <SkeletonOrderSummary />
          </div>
        </div>
      </section>
    );
  }

  const updateQuantity = (id, newQuantity) => {
    if (newQuantity <= 0) {
      removeItem(id);
      return;
    }
    setCartItems(items =>
      items.map(item =>
        item.id === id ? { ...item, quantity: newQuantity } : item
      )
    );
  };

  const removeItem = (id) => {
    setCartItems(items => items.filter(item => item.id !== id));
  };

  if (cartItems.length === 0) {
    return (
      <section
        id={id}
        className={clsx(
          'w-full py-12 sm:py-16 md:py-20 lg:py-24 px-4 sm:px-6 lg:px-8 bg-black',
          className
        )}
      >
        <div className="max-w-2xl mx-auto text-center">
          <div className="mb-8 sm:mb-12">
            <div className="inline-flex items-center justify-center w-20 h-20 sm:w-24 sm:h-24 bg-gray-900 rounded-full">
              <svg
                className="w-10 h-10 sm:w-12 sm:h-12 text-white"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth={1.5}
                  d="M16 11V7a4 4 0 00-8 0v4M5 9h14l1 12H4L5 9z"
                />
              </svg>
            </div>
          </div>
          <h2 className="text-2xl sm:text-3xl md:text-4xl font-light text-white mb-4 sm:mb-6">Your collection awaits</h2>
          <p className="text-gray-400 text-base sm:text-lg mb-8 sm:mb-12">Discover our exclusive luxury items.</p>
          <button className="inline-flex items-center px-8 sm:px-12 py-4 sm:py-5 bg-white text-black font-semibold text-base sm:text-lg hover:bg-gray-100 transition-all duration-300 rounded-lg">
            Explore Collection
          </button>
        </div>
      </section>
    );
  }

  return (
    <section
      id={id}
      className={clsx(
        'w-full py-12 sm:py-16 md:py-20 lg:py-24 px-4 sm:px-6 lg:px-8',
        isLuxury ? 'bg-white' : 'bg-black',
        className
      )}
    >
      <div className="max-w-7xl mx-auto">
        {/* Header */}
        <div className="text-center mb-12 sm:mb-16">
          <h1 className={clsx("text-3xl sm:text-4xl md:text-5xl font-light mb-4 italic font-serif", isLuxury ? "text-black" : "text-white")}>{title}</h1>
          <p className={clsx("text-sm sm:text-base md:text-xl uppercase tracking-[0.3em] sm:tracking-[0.4em] font-bold opacity-60", isLuxury ? "text-gray-900" : "text-gray-400")}>{cartItems.length} exclusive item{cartItems.length !== 1 ? 's' : ''}</p>
        </div>

        <div className="grid grid-cols-1 xl:grid-cols-5 gap-8 sm:gap-12">
          {/* Cart Items */}
          <div className="xl:col-span-3 space-y-6 sm:space-y-8">
            {cartItems.map((item) => (
              <div
                key={item.id}
                className={clsx(
                  "p-6 sm:p-8 transition-all duration-500",
                  isLuxury ? "bg-white border-b-2 border-gray-900" : "bg-gray-900 rounded-3xl border border-gray-800 hover:border-gray-700"
                )}
              >
                <div className="flex flex-col lg:flex-row gap-6 sm:gap-8">
                  {/* Product Image */}
                  <div className="flex-shrink-0 flex justify-center lg:justify-start">
                    <div className="relative w-full max-w-[200px] lg:w-40">
                      <img
                        src={item.image || '/api/placeholder/160/160'}
                        alt={item.name}
                        className={clsx(
                          "w-full aspect-square object-cover",
                          isLuxury ? "rounded-none border-2 border-gray-900" : "rounded-2xl"
                        )}
                      />
                      {item.badge && (
                        <div className="absolute -top-3 -right-3 bg-primary-600 text-white text-xs font-bold px-3 py-1 rounded-full">
                          {item.badge}
                        </div>
                      )}
                    </div>
                  </div>

                  {/* Product Details */}
                  <div className="flex-1 min-w-0">
                    <div className="flex justify-between items-start mb-4 sm:mb-6">
                      <div className="flex-1 pr-4">
                        <h3 className={clsx("text-xl sm:text-2xl font-light mb-2 sm:mb-3 italic font-serif", isLuxury ? "text-black" : "text-white")}>{item.name}</h3>
                        {item.description && (
                          <p className={clsx("text-base sm:text-lg mb-2 sm:mb-3 font-medium line-clamp-2 lg:line-clamp-none", isLuxury ? "text-gray-600" : "text-gray-400")}>{item.description}</p>
                        )}
                        {item.variant && (
                          <p className="text-xs sm:text-sm text-gray-500 font-medium">Variant: {item.variant}</p>
                        )}
                      </div>
                      <button
                        onClick={() => removeItem(item.id)}
                        className="p-2 sm:p-3 text-gray-500 hover:text-red-400 hover:bg-red-900/20 rounded-xl transition-colors flex-shrink-0"
                        aria-label="Remove item"
                      >
                        <svg className="w-5 h-5 sm:w-6 sm:h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                        </svg>
                      </button>
                    </div>

                    {/* Price and Quantity */}
                    <div className="flex flex-col sm:flex-row justify-between items-start sm:items-end gap-4 sm:gap-6">
                      <div className="flex flex-wrap items-center gap-4 sm:gap-8 w-full sm:w-auto">
                        <div className={clsx("text-2xl sm:text-3xl font-light italic font-serif", isLuxury ? "text-black" : "text-white")}>${item.price.toFixed(2)}</div>
                        <div className={clsx("flex items-center gap-3 sm:gap-4 p-1.5 sm:p-2", isLuxury ? "bg-gray-100 border border-gray-900" : "bg-gray-800 rounded-2xl")}>
                          <button
                            onClick={() => updateQuantity(item.id, item.quantity - 1)}
                            className={clsx(
                              "w-10 h-10 sm:w-12 sm:h-12 flex items-center justify-center transition-colors",
                              isLuxury ? "text-black hover:bg-gray-200" : "text-gray-400 hover:text-white hover:bg-gray-700 rounded-xl"
                            )}
                            aria-label="Decrease quantity"
                          >
                            <svg className="w-5 h-5 sm:w-6 sm:h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M20 12H4" />
                            </svg>
                          </button>
                          <span className={clsx("w-10 sm:w-16 text-center font-bold text-lg sm:text-xl", isLuxury ? "text-black" : "text-white")}>{item.quantity}</span>
                          <button
                            onClick={() => updateQuantity(item.id, item.quantity + 1)}
                            className={clsx(
                              "w-10 h-10 sm:w-12 sm:h-12 flex items-center justify-center transition-colors",
                              isLuxury ? "text-black hover:bg-gray-200" : "text-gray-400 hover:text-white hover:bg-gray-700 rounded-xl"
                            )}
                            aria-label="Increase quantity"
                          >
                            <svg className="w-5 h-5 sm:w-6 sm:h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 6v6m0 0v6m0-6h6m-6 0H6" />
                            </svg>
                          </button>
                        </div>
                      </div>
                      <div className="text-right w-full sm:w-auto">
                        <div className={clsx("text-2xl sm:text-3xl font-black", isLuxury ? "text-primary-600" : "text-primary-400")}>
                          ${(item.price * item.quantity).toFixed(2)}
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>

          {/* Order Summary */}
          <div className="xl:col-span-2 mt-8 xl:mt-0">
            <div className={clsx(
              "p-6 sm:p-8 sticky top-4 transition-all duration-500",
              isLuxury ? "bg-white border-4 border-gray-900 shadow-[20px_20px_0px_0px_rgba(0,0,0,1)]" : "bg-gray-900 rounded-3xl border border-gray-800"
            )}>
              <h2 className={clsx("text-2xl sm:text-3xl font-light mb-6 sm:mb-8 italic font-serif", isLuxury ? "text-black" : "text-white")}>Summary</h2>

              <div className="space-y-4 sm:space-y-6">
                {showSubtotal && (
                  <div className="flex justify-between items-center">
                    <span className={clsx("text-base sm:text-lg font-medium", isLuxury ? "text-gray-600" : "text-gray-400")}>Subtotal</span>
                    <span className={clsx("text-lg sm:text-xl font-light", isLuxury ? "text-black" : "text-white")}>${subtotal.toFixed(2)}</span>
                  </div>
                )}

                {showTax && (
                  <div className="flex justify-between items-center">
                    <span className={clsx("text-base sm:text-lg font-medium", isLuxury ? "text-gray-600" : "text-gray-400")}>Tax</span>
                    <span className={clsx("text-lg sm:text-xl font-light", isLuxury ? "text-black" : "text-white")}>${tax.toFixed(2)}</span>
                  </div>
                )}

                {showShipping && (
                  <div className="flex justify-between items-center">
                    <span className={clsx("text-base sm:text-lg font-medium", isLuxury ? "text-gray-600" : "text-gray-400")}>Shipping</span>
                    <span className={clsx("text-lg sm:text-xl font-light", isLuxury ? "text-black" : "text-white")}>
                      {shipping === 0 ? (
                        <span className={isLuxury ? "text-primary-600" : "text-green-400"}>Complimentary</span>
                      ) : (
                        `$${shipping.toFixed(2)}`
                      )}
                    </span>
                  </div>
                )}

                <hr className={isLuxury ? "border-gray-900 border-t-2" : "border-gray-800"} />

                {showTotal && (
                  <div className="flex justify-between items-center">
                    <span className={clsx("text-xl sm:text-2xl font-light italic font-serif", isLuxury ? "text-black" : "text-white")}>Total</span>
                    <span className={clsx("text-3xl sm:text-4xl font-black", isLuxury ? "text-primary-600" : "text-primary-400")}>${total.toFixed(2)}</span>
                  </div>
                )}
              </div>

              {showCheckoutButton && (
                <button className={clsx(
                  "w-full mt-8 sm:mt-10 px-8 sm:px-12 py-5 sm:py-6 font-bold text-lg sm:text-xl transition-all duration-300 uppercase tracking-[0.2em] sm:tracking-[0.3em]",
                  isLuxury ? "bg-black text-white hover:bg-primary-600" : "bg-white text-black hover:bg-gray-100 rounded-2xl shadow-lg"
                )}>
                  Checkout Now
                </button>
              )}

              {showContinueShopping && (
                <button className={clsx(
                  "w-full mt-4 sm:mt-6 px-8 sm:px-12 py-5 sm:py-6 font-bold text-lg sm:text-xl transition-all duration-300 uppercase tracking-[0.2em] sm:tracking-[0.3em] border-2",
                  isLuxury ? "bg-white text-black border-gray-900 hover:bg-gray-50" : "bg-transparent text-gray-400 border-gray-800 hover:text-white hover:border-gray-600 rounded-2xl"
                )}>
                  Continue Shopping
                </button>
              )}

              {/* Premium features */}
              <div className={clsx("mt-8 sm:mt-10 pt-6 sm:pt-8 border-t", isLuxury ? "border-gray-900" : "border-gray-800")}>
                <div className="space-y-3 sm:space-y-4">
                  <div className={clsx("flex items-center gap-3 sm:gap-4", isLuxury ? "text-gray-900" : "text-gray-400")}>
                    <svg className="w-4 h-4 sm:w-5 sm:h-5 flex-shrink-0" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
                    </svg>
                    <span className="font-bold uppercase tracking-wider text-xs sm:text-sm">Free premium packaging</span>
                  </div>
                  <div className={clsx("flex items-center gap-3 sm:gap-4", isLuxury ? "text-gray-900" : "text-gray-400")}>
                    <svg className="w-4 h-4 sm:w-5 sm:h-5 flex-shrink-0" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M20.618 5.984A11.955 11.955 0 0112 2.944a11.955 11.955 0 01-8.618 3.04A12.02 12.02 0 003 9c0 5.591 3.824 10.29 9 11.622 5.176-1.332 9-6.03 9-11.622 0-1.042-.133-2.052-.382-3.016z" />
                    </svg>
                    <span className="font-bold uppercase tracking-wider text-xs sm:text-sm">Authenticity guarantee</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};