import clsx from 'clsx';
import '../../styles/checkout.css';

/**
 * CheckoutSkeleton Component - Skeleton loader for checkout components
 * @param {Object} props - Component props
 * @param {Object} props.data - Skeleton configuration data
 * @param {string} [props.data.variant='modern'] - Style variant: 'modern' | 'minimal' | 'card' | 'flow' | 'premium' | 'peloton'
 * @param {boolean} [props.data.showHeader=true] - Show header skeleton
 * @param {boolean} [props.data.showForm=true] - Show form skeleton
 * @param {boolean} [props.data.showSummary=true] - Show order summary skeleton
 * @param {boolean} [props.data.showPayment=true] - Show payment section skeleton
 * @param {boolean} [props.data.showShipping=true] - Show shipping section skeleton
 * @param {string} [props.className] - Additional CSS classes
 *
 * Usage:
 * // Default behavior: Shows skeleton for 2 seconds, then content automatically
 * <CheckoutModern data={checkoutData} />
 *
 * // Manual control: Override automatic behavior with loading prop
 * const [loading, setLoading] = useState(true);
 * useEffect(() => {
 *   fetchCheckoutData().then(() => setLoading(false));
 * }, []);
 *
 * return <CheckoutModern data={checkoutData} loading={loading} />;
 */
export const CheckoutSkeleton = ({
  data = {},
  className
}) => {
  const {
    variant = 'modern',
    showHeader = true,
    showForm = true,
    showSummary = true,
    showPayment = true,
    showShipping = true,
  } = data;

  // Container classes
  const containerClasses = {
    modern: 'checkout-modern min-h-screen bg-neutral-50 py-12 px-6',
    minimal: 'checkout-minimal bg-white py-12 px-4 sm:px-6 lg:px-8',
    card: 'checkout-card bg-gray-50 py-12 px-4 sm:px-6',
    flow: 'checkout-flow bg-white py-12 px-4 sm:px-6 lg:px-8',
    premium: 'checkout-premium bg-gradient-to-br from-gray-50 to-gray-100 py-16 px-6',
    peloton: 'checkout-peloton bg-black text-white py-16 px-6',
  };

  // Grid classes
  const gridClasses = {
    modern: 'max-w-6xl mx-auto grid grid-cols-1 lg:grid-cols-3 gap-12',
    minimal: 'max-w-4xl mx-auto grid grid-cols-1 lg:grid-cols-2 gap-x-12 gap-y-10',
    card: 'max-w-5xl mx-auto grid grid-cols-1 lg:grid-cols-2 gap-8',
    flow: 'max-w-4xl mx-auto',
    premium: 'max-w-7xl mx-auto grid grid-cols-1 lg:grid-cols-3 gap-16',
    peloton: 'max-w-6xl mx-auto grid grid-cols-1 lg:grid-cols-2 gap-16',
  };

  // Header skeleton
  const HeaderSkeleton = () => {
    if (!showHeader) return null;

    switch (variant) {
      case 'modern':
        return (
          <div className="flex items-center space-x-4 mb-8">
            <div className="checkout-skeleton w-10 h-10 rounded-full"></div>
            <div className="checkout-skeleton h-8 w-48 rounded-lg"></div>
          </div>
        );
      case 'minimal':
        return (
          <div className="mb-8">
            <div className="checkout-skeleton h-7 w-32 rounded mb-4"></div>
            <div className="checkout-skeleton h-12 w-full rounded-md"></div>
          </div>
        );
      case 'card':
        return (
          <div className="text-center mb-12">
            <div className="checkout-skeleton h-10 w-64 rounded-lg mx-auto mb-4"></div>
            <div className="checkout-skeleton h-5 w-96 rounded mx-auto"></div>
          </div>
        );
      case 'flow':
        return (
          <div className="mb-12">
            <div className="checkout-skeleton h-9 w-40 rounded mb-6"></div>
            <div className="flex justify-between mb-8">
              {[1, 2, 3].map((step) => (
                <div key={step} className="flex flex-col items-center">
                  <div className="checkout-skeleton w-12 h-12 rounded-full mb-2"></div>
                  <div className="checkout-skeleton h-4 w-16 rounded"></div>
                </div>
              ))}
            </div>
          </div>
        );
      case 'premium':
        return (
          <div className="text-center mb-16">
            <div className="checkout-skeleton h-12 w-80 rounded-xl mx-auto mb-6"></div>
            <div className="checkout-skeleton h-6 w-96 rounded-lg mx-auto"></div>
          </div>
        );
      case 'peloton':
        return (
          <div className="text-center mb-16">
            <div className="checkout-skeleton h-16 w-96 rounded-2xl mx-auto mb-8"></div>
            <div className="checkout-skeleton h-6 w-80 rounded-lg mx-auto"></div>
          </div>
        );
      default:
        return null;
    }
  };

  // Form skeleton
  const FormSkeleton = () => {
    if (!showForm) return null;

    switch (variant) {
      case 'modern':
        return (
          <div className="space-y-12">
            {/* Email section */}
            <div className="bg-white p-8 rounded-2xl shadow-sm border border-neutral-100 space-y-6">
              <div className="checkout-skeleton h-12 w-full rounded-xl"></div>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div className="checkout-skeleton h-12 w-full rounded-xl"></div>
                <div className="checkout-skeleton h-12 w-full rounded-xl"></div>
              </div>
              <div className="checkout-skeleton h-12 w-full rounded-xl"></div>
            </div>

            {/* Payment section */}
            {showPayment && (
              <div className="bg-white p-8 rounded-2xl shadow-sm border border-neutral-100 grid grid-cols-1 md:grid-cols-2 gap-4">
                <div className="checkout-skeleton h-16 w-full rounded-2xl"></div>
                <div className="checkout-skeleton h-16 w-full rounded-2xl"></div>
              </div>
            )}
          </div>
        );
      case 'minimal':
        return (
          <div className="space-y-8">
            {/* Contact section */}
            <section>
              <div className="checkout-skeleton h-6 w-24 rounded mb-4"></div>
              <div className="checkout-skeleton h-12 w-full rounded-md"></div>
            </section>

            {/* Shipping section */}
            {showShipping && (
              <section>
                <div className="checkout-skeleton h-6 w-32 rounded mb-4"></div>
                <div className="grid grid-cols-2 gap-4">
                  <div className="checkout-skeleton h-12 w-full rounded-md"></div>
                  <div className="checkout-skeleton h-12 w-full rounded-md"></div>
                </div>
                <div className="checkout-skeleton h-12 w-full rounded-md mt-4"></div>
                <div className="grid grid-cols-3 gap-4 mt-4">
                  <div className="checkout-skeleton h-12 w-full rounded-md"></div>
                  <div className="checkout-skeleton h-12 w-full rounded-md"></div>
                  <div className="checkout-skeleton h-12 w-full rounded-md"></div>
                </div>
              </section>
            )}

            {/* Payment section */}
            {showPayment && (
              <section>
                <div className="checkout-skeleton h-6 w-28 rounded mb-4"></div>
                <div className="space-y-4">
                  <div className="checkout-skeleton h-16 w-full rounded-md"></div>
                  <div className="checkout-skeleton h-16 w-full rounded-md"></div>
                </div>
              </section>
            )}
          </div>
        );
      case 'card':
        return (
          <div className="bg-white p-8 rounded-2xl shadow-lg space-y-6">
            <div className="checkout-skeleton h-12 w-full rounded-xl"></div>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div className="checkout-skeleton h-12 w-full rounded-xl"></div>
              <div className="checkout-skeleton h-12 w-full rounded-xl"></div>
            </div>
            <div className="checkout-skeleton h-12 w-full rounded-xl"></div>
            <div className="checkout-skeleton h-16 w-full rounded-xl"></div>
          </div>
        );
      case 'flow':
        return (
          <div className="space-y-8">
            <div className="checkout-skeleton h-12 w-full rounded-lg"></div>
            <div className="checkout-skeleton h-12 w-full rounded-lg"></div>
            <div className="checkout-skeleton h-32 w-full rounded-lg"></div>
          </div>
        );
      case 'premium':
        return (
          <div className="bg-white p-10 rounded-3xl shadow-2xl space-y-8">
            <div className="checkout-skeleton h-14 w-full rounded-2xl"></div>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
              <div className="checkout-skeleton h-14 w-full rounded-2xl"></div>
              <div className="checkout-skeleton h-14 w-full rounded-2xl"></div>
            </div>
            <div className="checkout-skeleton h-14 w-full rounded-2xl"></div>
            <div className="checkout-skeleton h-20 w-full rounded-2xl"></div>
          </div>
        );
      case 'peloton':
        return (
          <div className="bg-gray-900 p-12 rounded-3xl space-y-8">
            <div className="checkout-skeleton h-16 w-full rounded-2xl"></div>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
              <div className="checkout-skeleton h-16 w-full rounded-2xl"></div>
              <div className="checkout-skeleton h-16 w-full rounded-2xl"></div>
            </div>
            <div className="checkout-skeleton h-16 w-full rounded-2xl"></div>
            <div className="checkout-skeleton h-20 w-full rounded-2xl"></div>
          </div>
        );
      default:
        return null;
    }
  };

  // Summary skeleton
  const SummarySkeleton = () => {
    if (!showSummary) return null;

    switch (variant) {
      case 'modern':
        return (
          <div className="lg:col-span-1">
            <div className="bg-white p-8 rounded-3xl shadow-xl border border-neutral-100 sticky top-8">
              <div className="checkout-skeleton h-7 w-24 rounded mb-8"></div>
              <div className="space-y-6 mb-8 max-h-60 overflow-y-auto pr-2">
                {[1, 2, 3].map((item) => (
                  <div key={item} className="flex items-center justify-between group">
                    <div className="flex items-center space-x-4">
                      <div className="checkout-skeleton w-12 h-12 rounded-lg flex-shrink-0"></div>
                      <div>
                        <div className="checkout-skeleton h-4 w-24 rounded mb-1"></div>
                        <div className="checkout-skeleton h-3 w-12 rounded"></div>
                      </div>
                    </div>
                    <div className="checkout-skeleton h-4 w-12 rounded"></div>
                  </div>
                ))}
              </div>
              <div className="space-y-4 border-t pt-8">
                <div className="flex justify-between">
                  <div className="checkout-skeleton h-4 w-16 rounded"></div>
                  <div className="checkout-skeleton h-4 w-12 rounded"></div>
                </div>
                <div className="flex justify-between text-sm">
                  <div className="checkout-skeleton h-3 w-20 rounded"></div>
                  <div className="checkout-skeleton h-3 w-8 rounded"></div>
                </div>
                <div className="flex justify-between pt-4 border-t">
                  <div className="checkout-skeleton h-6 w-12 rounded"></div>
                  <div className="checkout-skeleton h-6 w-16 rounded"></div>
                </div>
              </div>
              <div className="checkout-skeleton h-14 w-full rounded-2xl mt-10"></div>
            </div>
          </div>
        );
      case 'minimal':
        return (
          <div className="bg-gray-50 p-6 rounded-lg self-start">
            <div className="checkout-skeleton h-6 w-32 rounded mb-6"></div>
            <div className="space-y-4">
              {[1, 2, 3].map((item) => (
                <div key={item} className="flex justify-between text-sm">
                  <div className="checkout-skeleton h-4 w-32 rounded"></div>
                  <div className="checkout-skeleton h-4 w-12 rounded"></div>
                </div>
              ))}
              <div className="border-t pt-4 mt-4 flex justify-between font-bold">
                <div className="checkout-skeleton h-5 w-12 rounded"></div>
                <div className="checkout-skeleton h-5 w-16 rounded"></div>
              </div>
            </div>
            <div className="checkout-skeleton h-12 w-full rounded-md mt-8"></div>
          </div>
        );
      case 'card':
        return (
          <div className="bg-white p-8 rounded-2xl shadow-lg">
            <div className="checkout-skeleton h-8 w-40 rounded mb-6"></div>
            <div className="space-y-4 mb-8">
              {[1, 2].map((item) => (
                <div key={item} className="flex justify-between">
                  <div className="checkout-skeleton h-5 w-28 rounded"></div>
                  <div className="checkout-skeleton h-5 w-14 rounded"></div>
                </div>
              ))}
            </div>
            <div className="checkout-skeleton h-12 w-full rounded-xl"></div>
          </div>
        );
      case 'premium':
        return (
          <div className="bg-white p-10 rounded-3xl shadow-2xl">
            <div className="checkout-skeleton h-8 w-48 rounded-xl mb-8"></div>
            <div className="space-y-6 mb-10">
              {[1, 2, 3].map((item) => (
                <div key={item} className="flex items-center justify-between">
                  <div className="flex items-center space-x-4">
                    <div className="checkout-skeleton w-16 h-16 rounded-2xl"></div>
                    <div>
                      <div className="checkout-skeleton h-5 w-32 rounded-lg mb-2"></div>
                      <div className="checkout-skeleton h-4 w-16 rounded"></div>
                    </div>
                  </div>
                  <div className="checkout-skeleton h-6 w-14 rounded-lg"></div>
                </div>
              ))}
            </div>
            <div className="checkout-skeleton h-16 w-full rounded-2xl"></div>
          </div>
        );
      case 'peloton':
        return (
          <div className="bg-gray-900 p-12 rounded-3xl">
            <div className="checkout-skeleton h-10 w-56 rounded-2xl mb-8"></div>
            <div className="space-y-8 mb-12">
              {[1, 2, 3].map((item) => (
                <div key={item} className="flex items-center justify-between">
                  <div className="flex items-center space-x-6">
                    <div className="checkout-skeleton w-20 h-20 rounded-2xl"></div>
                    <div>
                      <div className="checkout-skeleton h-6 w-40 rounded-xl mb-3"></div>
                      <div className="checkout-skeleton h-4 w-20 rounded"></div>
                    </div>
                  </div>
                  <div className="checkout-skeleton h-7 w-16 rounded-xl"></div>
                </div>
              ))}
            </div>
            <div className="checkout-skeleton h-16 w-full rounded-2xl"></div>
          </div>
        );
      default:
        return null;
    }
  };

  return (
    <div className={clsx(
      containerClasses[variant] || containerClasses.modern,
      className
    )}>
      <div className={gridClasses[variant] || gridClasses.modern}>
        <div className={variant === 'modern' || variant === 'premium' ? 'lg:col-span-2 space-y-12' : ''}>
          <HeaderSkeleton />
          <FormSkeleton />
        </div>
        {showSummary && (variant === 'modern' || variant === 'minimal' || variant === 'card' || variant === 'premium' || variant === 'peloton') && (
          <SummarySkeleton />
        )}
        {variant === 'flow' && showSummary && (
          <div className="mt-12">
            <SummarySkeleton />
          </div>
        )}
      </div>
    </div>
  );
};
