import clsx from 'clsx';
import '../../styles/product.css';

/**
 * ProductFeatures Component
 * @param {Object} props - Component props
 * @param {Object} props.data - Product features data
 * @param {string} [props.data.title='Features'] - Section title
 * @param {Array} props.data.features - Array of feature objects
 * @param {string} [props.data.variant='default'] - Style variant: 'default' | 'grid' | 'minimal' | 'icons'
 * @param {number} [props.data.columns=2] - Number of columns for grid layout
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute for the section element
 */
export const ProductFeatures = ({ data, className, id }) => {
  // Safety check for data
  if (!data || typeof data !== 'object') {
    console.error('ProductFeatures: data prop is required and must be an object');
    return null;
  }

  const {
    title = 'Features',
    features = [],
    variant = 'default',
    columns = 2
  } = data;

  if (features.length === 0) {
    return null;
  }

  // Default variant - Standard feature list
  if (variant === 'default') {
    return (
      <section
        id={id}
        className={clsx(
          "product-features-default",
          className
        )}
      >
        <div className="space-y-6">
          <h3 className="text-2xl font-bold text-gray-900">{title}</h3>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            {features.map((feature, index) => (
              <div key={index} className="flex items-start space-x-4">
                <div className="flex-shrink-0">
                  {feature.icon ? (
                    <div className="w-8 h-8 bg-blue-100 rounded-lg flex items-center justify-center">
                      {typeof feature.icon === 'string' ? (
                        <span className="text-blue-600 text-lg">{feature.icon}</span>
                      ) : (
                        feature.icon
                      )}
                    </div>
                  ) : (
                    <div className="w-8 h-8 bg-green-100 rounded-lg flex items-center justify-center">
                      <svg className="w-4 h-4 text-green-600" fill="currentColor" viewBox="0 0 20 20">
                        <path fillRule="evenodd" d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z" clipRule="evenodd" />
                      </svg>
                    </div>
                  )}
                </div>

                <div className="flex-1">
                  <h4 className="text-lg font-semibold text-gray-900 mb-2">
                    {feature.title}
                  </h4>
                  <p className="text-gray-600 leading-relaxed">
                    {feature.description}
                  </p>
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>
    );
  }

  // Grid variant - Grid layout for features
  if (variant === 'grid') {
    return (
      <section
        id={id}
        className={clsx(
          "product-features-grid",
          className
        )}
      >
        <div className="space-y-8">
          <h3 className="text-2xl font-bold text-gray-900 text-center">{title}</h3>

          <div className={clsx(
            "grid gap-6",
            columns === 1 && "grid-cols-1",
            columns === 2 && "grid-cols-1 md:grid-cols-2",
            columns === 3 && "grid-cols-1 md:grid-cols-2 lg:grid-cols-3",
            columns === 4 && "grid-cols-1 md:grid-cols-2 lg:grid-cols-4"
          )}>
            {features.map((feature, index) => (
              <div key={index} className="bg-white rounded-xl shadow-sm border border-gray-200 p-6 hover:shadow-md transition-shadow">
                <div className="text-center space-y-4">
                  {feature.icon && (
                    <div className="w-12 h-12 bg-blue-100 rounded-xl flex items-center justify-center mx-auto">
                      {typeof feature.icon === 'string' ? (
                        <span className="text-blue-600 text-2xl">{feature.icon}</span>
                      ) : (
                        feature.icon
                      )}
                    </div>
                  )}

                  <h4 className="text-lg font-semibold text-gray-900">
                    {feature.title}
                  </h4>

                  <p className="text-gray-600 text-sm leading-relaxed">
                    {feature.description}
                  </p>
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>
    );
  }

  // Minimal variant - Clean minimal design
  if (variant === 'minimal') {
    return (
      <section
        id={id}
        className={clsx(
          "product-features-minimal",
          className
        )}
      >
        <div className="space-y-4">
          <h3 className="text-xl font-semibold text-gray-900">{title}</h3>

          <div className="space-y-3">
            {features.map((feature, index) => (
              <div key={index} className="flex items-center space-x-3">
                <span className="w-1 h-1 bg-gray-400 rounded-full flex-shrink-0"></span>
                <span className="text-gray-700">{feature.title}</span>
                {feature.description && (
                  <span className="text-gray-500 text-sm"> {feature.description}</span>
                )}
              </div>
            ))}
          </div>
        </div>
      </section>
    );
  }

  // Icons variant - Icon-focused layout
  if (variant === 'icons') {
    return (
      <section
        id={id}
        className={clsx(
          "product-features-icons",
          className
        )}
      >
        <div className="space-y-8">
          <h3 className="text-2xl font-bold text-gray-900 text-center">{title}</h3>

          <div className="flex flex-wrap justify-center gap-8">
            {features.map((feature, index) => (
              <div key={index} className="text-center space-y-3 max-w-xs">
                {feature.icon && (
                  <div className="w-16 h-16 bg-gradient-to-br from-blue-500 to-purple-600 rounded-2xl flex items-center justify-center mx-auto shadow-lg">
                    {typeof feature.icon === 'string' ? (
                      <span className="text-white text-2xl">{feature.icon}</span>
                    ) : (
                      <div className="text-white">
                        {feature.icon}
                      </div>
                    )}
                  </div>
                )}

                <h4 className="text-lg font-semibold text-gray-900">
                  {feature.title}
                </h4>

                {feature.description && (
                  <p className="text-gray-600 text-sm leading-relaxed">
                    {feature.description}
                  </p>
                )}
              </div>
            ))}
          </div>
        </div>
      </section>
    );
  }

  return null;
};
