import { useState, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/product.css';

/**
 * ProductLayout Component
 * @param {Object} props - Component props
 * @param {Object} props.data - Product section configuration data
 * @param {string} [props.data.title] - Main heading text
 * @param {string} [props.data.subtitle] - Subtitle text displayed above title
 * @param {string} [props.data.description] - Description text displayed below title
 * @param {Array} props.data.products - Array of product items
 * @param {string} [props.data.variant='default'] - Style variant: 'default' | 'grid' | 'card' | 'minimal' | 'featured' | 'modern' | 'bordered' | 'gradient' | 'shadow' | 'ecommerce'
 * @param {string} [props.data.alignment='center'] - Header text alignment: 'left' | 'center' | 'right'
 * @param {number} [props.data.columns=3] - Number of columns for grid layout (1-4)
 * @param {string} [props.data.currency='$'] - Currency symbol
 * @param {boolean} [props.data.showRating=false] - Show rating stars
 * @param {boolean} [props.data.showBadge=false] - Show badge (new, sale, etc.)
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute for the section element
 * @param {boolean} [props.loader=false] - Show loading state (auto-hides after 2 seconds)
 */
export const ProductLayout = ({ data, className, id, loader = false }) => {
  // Safety check for data
  if (!data || typeof data !== 'object') {
    console.error('ProductLayout: data prop is required and must be an object');
    return null;
  }

  const [showLoader, setShowLoader] = useState(loader);

  // Auto-hide loader after 2 seconds when loader prop is true
  useEffect(() => {
    if (loader) {
      const timer = setTimeout(() => {
        setShowLoader(false);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowLoader(false);
    }
  }, [loader]);

  const {
    title,
    subtitle,
    description,
    products = [],
    variant = 'default',
    alignment = 'center',
    columns = 3,
    currency = '$',
    showRating = false,
    showBadge = false,
    className: dataClassName,
  } = data;

  // Show skeleton loader if loader prop is true
  if (showLoader) {
    const alignmentClasses = {
      left: 'text-left items-start',
      center: 'text-center items-center',
      right: 'text-right items-end',
    };

    return (
      <section
        id={id}
        className={clsx(
          'w-full py-12 sm:py-16 lg:py-20 px-4 sm:px-6 lg:px-8',
          'bg-white',
          className,
          dataClassName
        )}
      >
        <div className="max-w-7xl mx-auto w-full">
          {/* Header skeleton */}
          <div className={clsx('mb-10 sm:mb-12 flex flex-col', alignmentClasses[alignment])}>
            {subtitle && (
              <div className="h-4 bg-gray-400 rounded w-24 mb-3 mx-auto animate-pulse"></div>
            )}
            {title && (
              <div className="h-10 bg-gray-400 rounded w-full max-w-2xl mb-4 mx-auto animate-pulse"></div>
            )}
            {description && (
              <div className="h-5 bg-gray-300 rounded w-full max-w-xl mb-6 mx-auto animate-pulse"></div>
            )}
          </div>

          {/* Product cards skeleton */}
          <div className={clsx(
            'grid gap-6',
            columns === 1 ? 'grid-cols-1' :
            columns === 2 ? 'grid-cols-1 sm:grid-cols-2' :
            columns === 3 ? 'grid-cols-1 sm:grid-cols-2 lg:grid-cols-3' :
            'grid-cols-1 sm:grid-cols-2 lg:grid-cols-4'
          )}>
            {[0, 1, 2, 3].slice(0, columns).map((index) => (
              <div key={index} className="border border-gray-200 rounded-lg p-4 bg-white">
                {/* Image skeleton */}
                <div className="h-48 bg-gray-300 rounded-lg mb-4 animate-pulse"></div>
                
                {/* Badge skeleton */}
                {showBadge && (
                  <div className="h-5 bg-gray-300 rounded w-16 mb-2 animate-pulse"></div>
                )}

                {/* Title skeleton */}
                <div className="h-6 bg-gray-400 rounded w-3/4 mb-2 animate-pulse"></div>
                
                {/* Rating skeleton */}
                {showRating && (
                  <div className="h-4 bg-gray-300 rounded w-24 mb-2 animate-pulse"></div>
                )}

                {/* Price skeleton */}
                <div className="h-8 bg-gray-400 rounded w-20 mb-4 animate-pulse"></div>

                {/* Button skeleton */}
                <div className="h-10 bg-gray-400 rounded-lg w-full animate-pulse"></div>
              </div>
            ))}
          </div>
        </div>
      </section>
    );
  }

  const alignmentClasses = {
    left: 'text-left items-start',
    center: 'text-center items-center',
    right: 'text-right items-end',
  };

  const gridColumnsClasses = {
    1: 'grid-cols-1',
    2: 'grid-cols-1 sm:grid-cols-1',
    3: 'grid-cols-1 sm:grid-cols-1 lg:grid-cols-3',
    4: 'grid-cols-1 sm:grid-cols-1 lg:grid-cols-4',
  };

  // Render rating stars
  const renderRating = (rating = 0) => {
    const fullStars = Math.floor(rating);
    const hasHalfStar = rating % 1 >= 0.5;
    const emptyStars = 5 - fullStars - (hasHalfStar ? 1 : 0);

    return (
      <div className="flex items-center gap-0.5">
        {[...Array(fullStars)].map((_, i) => (
          <svg key={i} className="w-4 h-4 text-yellow-400 fill-current" viewBox="0 0 20 20">
            <path d="M10 15l-5.878 3.09 1.123-6.545L.489 6.91l6.572-.955L10 0l2.939 5.955 6.572.955-4.756 4.635 1.123 6.545z" />
          </svg>
        ))}
        {hasHalfStar && (
          <svg className="w-4 h-4 text-yellow-400 fill-current" viewBox="0 0 20 20">
            <defs>
              <linearGradient id="half">
                <stop offset="50%" stopColor="currentColor" />
                <stop offset="50%" stopColor="transparent" stopOpacity="1" />
              </linearGradient>
            </defs>
            <path fill="url(#half)" d="M10 15l-5.878 3.09 1.123-6.545L.489 6.91l6.572-.955L10 0l2.939 5.955 6.572.955-4.756 4.635 1.123 6.545z" />
          </svg>
        )}
        {[...Array(emptyStars)].map((_, i) => (
          <svg key={i} className="w-4 h-4 text-gray-300 fill-current" viewBox="0 0 20 20">
            <path d="M10 15l-5.878 3.09 1.123-6.545L.489 6.91l6.572-.955L10 0l2.939 5.955 6.572.955-4.756 4.635 1.123 6.545z" />
          </svg>
        ))}
        {rating > 0 && (
          <span className="ml-1 text-sm text-gray-600">({rating.toFixed(1)})</span>
        )}
      </div>
    );
  };

  // Get card classes based on variant
  const getCardClasses = (product, index) => {
    const baseClasses = 'relative overflow-hidden transition-all duration-300';
    const isFeatured = product.featured || product.popular;

    // Default variant - clean white cards with subtle shadows
    if (variant === 'default') {
      return clsx(
        baseClasses,
        'bg-white rounded-lg p-4 sm:p-6 border border-gray-200 shadow-sm',
        'hover:shadow-lg hover:-translate-y-1'
      );
    }

    // Grid variant - compact grid layout
    if (variant === 'grid') {
      return clsx(
        baseClasses,
        'bg-white rounded-lg p-4 border border-gray-200',
        'hover:shadow-md hover:border-primary-300'
      );
    }

    // Card variant - modern card with prominent shadow
    if (variant === 'card') {
      return clsx(
        baseClasses,
        'bg-white rounded-xl p-5 sm:p-6 border border-gray-200 shadow-lg',
        isFeatured && 'ring-2 ring-primary-500 shadow-xl',
        'hover:shadow-2xl hover:scale-105'
      );
    }

    // Minimal variant - clean minimal design
    if (variant === 'minimal') {
      return clsx(
        baseClasses,
        'bg-gray-50 rounded-lg p-4 sm:p-6 border-b-2 border-gray-200',
        'hover:border-primary-500 hover:bg-gray-100'
      );
    }

    // Featured variant - scale effect on featured products
    if (variant === 'featured') {
      return clsx(
        baseClasses,
        'bg-white rounded-lg p-5 sm:p-6 border-2',
        isFeatured 
          ? 'border-primary-500 shadow-xl scale-105 z-10 bg-gradient-to-br from-primary-50 to-white' 
          : 'border-gray-200 shadow-sm',
        'hover:shadow-lg'
      );
    }

    // Modern variant - rounded corners with modern shadows
    if (variant === 'modern') {
      return clsx(
        baseClasses,
        'bg-white rounded-2xl p-5 sm:p-6 border border-gray-100 shadow-lg',
        isFeatured && 'border-primary-400 shadow-2xl bg-gradient-to-br from-primary-50 to-white',
        'hover:shadow-xl hover:-translate-y-2'
      );
    }

    // Bordered variant - prominent borders
    if (variant === 'bordered') {
      return clsx(
        baseClasses,
        'bg-white rounded-lg p-5 sm:p-6 border-2',
        isFeatured ? 'border-primary-600 shadow-md' : 'border-gray-300',
        'hover:border-primary-400'
      );
    }

    // Gradient variant - gradient backgrounds
    if (variant === 'gradient') {
      return clsx(
        baseClasses,
        'rounded-lg p-5 sm:p-6 border shadow-sm',
        isFeatured 
          ? 'bg-gradient-to-br from-primary-500 via-primary-600 to-primary-700 text-white border-transparent shadow-lg' 
          : 'bg-gradient-to-br from-gray-50 via-white to-gray-50 border-gray-200',
        'hover:shadow-lg'
      );
    }

    // Shadow variant - prominent shadows
    if (variant === 'shadow') {
      return clsx(
        baseClasses,
        'bg-white rounded-lg p-5 sm:p-6 border border-gray-200',
        isFeatured ? 'shadow-xl border-gray-300' : 'shadow-md',
        'hover:shadow-2xl'
      );
    }

    // Ecommerce variant - ecommerce-style layout
    if (variant === 'ecommerce') {
      return clsx(
        baseClasses,
        'bg-white rounded-lg p-4 sm:p-5 border border-gray-200 shadow-sm',
        'hover:shadow-lg hover:border-primary-400 group'
      );
    }

    // Pill variant - tall pill card with split layout
    if (variant === 'pill') {
      return clsx(
        baseClasses,
        'bg-transparent p-0',
        'hover:-translate-y-1 transition-transform duration-300'
      );
    }

    // Horizontal banner variant - wide colored strip card
    if (variant === 'banner') {
      return clsx(
        baseClasses,
        'bg-transparent rounded-2xl p-0 shadow-lg overflow-hidden',
        'hover:shadow-2xl hover:-translate-y-1'
      );
    }

    // Default fallback
    return clsx(
      baseClasses,
      'bg-white rounded-lg p-5 sm:p-6 border border-gray-200 shadow-sm',
      'hover:shadow-lg'
    );
  };

  // Render product image
  const renderImage = (product, index) => {
    const imageClasses = {
      default: 'w-full h-48 sm:h-56 object-cover rounded-lg mb-4',
      grid: 'w-full h-40 sm:h-48 object-cover rounded-md mb-3',
      card: 'w-full h-52 sm:h-64 object-cover rounded-xl mb-4',
      minimal: 'w-full h-40 sm:h-48 object-cover rounded-lg mb-4',
      featured: 'w-full h-48 sm:h-56 object-cover rounded-lg mb-4',
      modern: 'w-full h-48 sm:h-64 object-cover rounded-2xl mb-4',
      bordered: 'w-full h-48 sm:h-56 object-cover rounded-lg mb-4',
      gradient: 'w-full h-48 sm:h-56 object-cover rounded-lg mb-4',
      shadow: 'w-full h-48 sm:h-56 object-cover rounded-lg mb-4',
      ecommerce: 'w-full h-48 sm:h-56 object-cover rounded-lg mb-4 group-hover:scale-105 transition-transform duration-300',
    };

    const placeholder = (
      <div className={clsx(
        'bg-gradient-to-br from-gray-100 to-gray-200 flex items-center justify-center mb-4',
        imageClasses[variant] || imageClasses.default
      )}>
        <svg className="w-16 h-16 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z" />
        </svg>
      </div>
    );

    if (product.image) {
      return (
        <div className="relative overflow-hidden rounded-lg">
          <img
            src={product.image}
            alt={product.title || 'Product'}
            className={imageClasses[variant] || imageClasses.default}
            loading="lazy"
            onError={(e) => {
              e.target.style.display = 'none';
              const placeholder = e.target.parentElement.querySelector('.image-placeholder');
              if (placeholder) placeholder.style.display = 'flex';
            }}
          />
          <div className="image-placeholder hidden bg-gradient-to-br from-gray-100 to-gray-200 items-center justify-center mb-4" style={{ display: 'none' }}>
            <svg className="w-16 h-16 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z" />
            </svg>
          </div>
          {product.badge && showBadge && (
            <span className={clsx(
              'absolute top-2 right-2 px-2 py-1 text-xs font-semibold rounded z-10',
              product.badge === 'New' && 'bg-green-500 text-white',
              product.badge === 'Sale' && 'bg-red-500 text-white',
              product.badge === 'Hot' && 'bg-orange-500 text-white',
              !['New', 'Sale', 'Hot'].includes(product.badge) && 'bg-primary-500 text-white'
            )}>
              {product.badge}
            </span>
          )}
        </div>
      );
    }

    // Placeholder if no image or image error
    return (
      <div className="relative">
        {placeholder}
        {product.badge && showBadge && (
          <span className={clsx(
            'absolute top-2 right-2 px-2 py-1 text-xs font-semibold rounded z-10',
            product.badge === 'New' && 'bg-green-500 text-white',
            product.badge === 'Sale' && 'bg-red-500 text-white',
            product.badge === 'Hot' && 'bg-orange-500 text-white',
            !['New', 'Sale', 'Hot'].includes(product.badge) && 'bg-primary-500 text-white'
          )}>
            {product.badge}
          </span>
        )}
      </div>
    );
  };

  // Render product button
  const renderButton = (product) => {
    const buttonConfig = product.button || {};
    const buttonText = buttonConfig.text || 'View Product';
    const buttonHref = buttonConfig.href || '#';
    const buttonVariant = buttonConfig.variant || 'primary';
    const buttonOnClick = buttonConfig.onClick;

    const buttonClasses = {
      primary: 'bg-primary-600 text-white hover:bg-primary-700',
      secondary: 'bg-gray-600 text-white hover:bg-gray-700',
      outline: 'border-2 border-primary-600 text-primary-600 hover:bg-primary-50',
    };

    const baseButtonClasses = clsx(
      'w-full px-4 py-2.5 rounded-lg font-semibold text-sm transition-colors duration-200',
      buttonClasses[buttonVariant] || buttonClasses.primary
    );

    if (buttonOnClick) {
      return (
        <button onClick={buttonOnClick} className={baseButtonClasses}>
          {buttonText}
        </button>
      );
    }

    return (
      <a href={buttonHref} className={baseButtonClasses}>
        {buttonText}
      </a>
    );
  };

  return (
    <section
      id={id}
      className={clsx(
        'w-full py-12 sm:py-16 lg:py-20 px-4 sm:px-6 lg:px-8',
        variant === 'gradient' && 'bg-gradient-to-br from-gray-50 to-white',
        variant !== 'gradient' && 'bg-white',
        className,
        dataClassName
      )}
    >
      <div className="max-w-7xl mx-auto w-full">
        {/* Header Section */}
        {(title || subtitle || description) && (
          <div className={clsx('mb-10 sm:mb-12 lg:mb-16 flex flex-col', alignmentClasses[alignment])}>
            {subtitle && (
              <p className="text-sm sm:text-base font-semibold text-primary-600 uppercase tracking-wide mb-2">
                {subtitle}
              </p>
            )}
            {title && (
              <h2 className="text-3xl sm:text-4xl lg:text-5xl font-bold text-gray-900 mb-4">
                {title}
              </h2>
            )}
            {description && (
              <p className="text-base sm:text-lg text-gray-600 max-w-2xl mx-auto">
                {description}
              </p>
            )}
          </div>
        )}

        {/* Products Grid */}
        {products.length > 0 && (
          <div className={clsx('grid gap-6 sm:gap-8', gridColumnsClasses[columns] || gridColumnsClasses[3])}>
            {products.map((product, index) => {
              // Special pill layout (tall rounded card with dark bottom)
              if (variant === 'pill') {
                const buttonConfig = product.button || {};
                const buttonText = buttonConfig.text || 'Buy Now';
                const buttonHref = buttonConfig.href || '#';
                const buttonOnClick = buttonConfig.onClick;

                return (
                  <div key={index} className={getCardClasses(product, index)}>
                    <div className="bg-white rounded-full overflow-hidden shadow-2xl flex flex-col h-full max-w-sm mx-auto">
                      {/* White top section with image */}
                      <div className="bg-white pt-8 px-6 pb-4 flex justify-center items-center flex-1 min-h-[280px] sm:min-h-[320px]">
                        {product.image ? (
                          <img
                            src={product.image}
                            alt={product.title || 'Product'}
                            className="w-full max-w-[200px] h-auto object-contain"
                            loading="lazy"
                            onError={(e) => {
                              e.target.style.display = 'none';
                            }}
                          />
                        ) : (
                          <div className="w-full max-w-[200px] h-64 bg-gray-100 rounded-2xl flex items-center justify-center">
                            <svg className="w-16 h-16 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z" />
                            </svg>
                          </div>
                        )}
                      </div>

                      {/* Dark bottom section */}
                      <div className="bg-gray-900 px-6 py-6 sm:py-8 text-center rounded-b-[3rem] sm:rounded-b-[4rem]">
                        {product.title && (
                          <h3 className="text-lg sm:text-xl font-semibold text-white mb-2">
                            {product.title}
                          </h3>
                        )}
                        {product.description && (() => {
                          const maxWords = 8;
                          const words = product.description.split(' ');
                          const truncated = words.length > maxWords 
                            ? words.slice(0, maxWords).join(' ') + '...'
                            : product.description;
                          return (
                            <p className="text-sm sm:text-base text-gray-300 mb-5 sm:mb-6">
                              {truncated}
                            </p>
                          );
                        })()}
                        
                        {/* Buy Now Button - White with black text, pill-shaped */}
                        <div className="mb-4 sm:mb-5">
                          {buttonOnClick ? (
                            <button
                              onClick={buttonOnClick}
                              className="bg-white text-gray-900 px-8 py-2.5 sm:py-3 rounded-full font-semibold text-sm sm:text-base border border-gray-200 hover:bg-gray-50 transition-colors duration-200 w-full max-w-[200px]"
                            >
                              {buttonText}
                            </button>
                          ) : (
                            <a
                              href={buttonHref}
                              className="inline-block bg-white text-gray-900 px-8 py-2.5 sm:py-3 rounded-full font-semibold text-sm sm:text-base border border-gray-200 hover:bg-gray-50 transition-colors duration-200 w-full max-w-[200px]"
                            >
                              {buttonText}
                            </a>
                          )}
                        </div>

                        {/* Price */}
                        {product.price !== undefined && (
                          <div className="text-white">
                            <span className="text-base sm:text-lg font-medium">
                              {currency}{product.price.toFixed ? product.price.toFixed(2) : product.price}
                            </span>
                          </div>
                        )}
                      </div>
                    </div>
                  </div>
                );
              }

              // Special horizontal banner layout
              if (variant === 'banner') {
                return (
                  <div key={index} className={getCardClasses(product, index)}>
                    <div className="bg-gradient-to-r from-primary-500/90 via-primary-400/90 to-primary-500/90 text-white rounded-2xl p-4 sm:p-5 flex flex-col sm:flex-row items-center gap-4 sm:gap-6">
                      {/* Image */}
                      <div className="w-20 h-20 sm:w-24 sm:h-24 rounded-2xl overflow-hidden bg-white/10 flex-shrink-0">
                        {product.image ? (
                          <img
                            src={product.image}
                            alt={product.title || 'Product'}
                            className="w-full h-full object-cover"
                            loading="lazy"
                          />
                        ) : null}
                      </div>

                      {/* Text */}
                      <div className="flex-1 text-left">
                        {product.title && (
                          <h3 className="text-base sm:text-lg font-semibold mb-1">
                            {product.title}
                          </h3>
                        )}
                        {product.description && (
                          <p className="text-xs sm:text-sm text-white/90 mb-2 line-clamp-2">
                            {product.description}
                          </p>
                        )}
                        {showRating && product.rating !== undefined && (
                          <div className="mt-1">
                            {renderRating(product.rating)}
                          </div>
                        )}
                      </div>

                      {/* Price + CTA */}
                      <div className="flex flex-col items-end gap-2">
                        {product.price !== undefined && (
                          <div className="text-right">
                            <span className="text-sm sm:text-base font-semibold">
                              {currency}{product.price}
                            </span>
                            {product.originalPrice && product.originalPrice > product.price && (
                              <div className="text-xs line-through text-white/70">
                                {currency}{product.originalPrice}
                              </div>
                            )}
                          </div>
                        )}
                        <div className="w-full sm:w-auto">
                          {renderButton(product)}
                        </div>
                      </div>
                    </div>
                  </div>
                );
              }

              // Default layouts for all other variants
              return (
                <div key={index} className={getCardClasses(product, index)}>
                  {/* Product Image */}
                  {renderImage(product, index)}

                  {/* Product Badge */}
                  {product.badge && showBadge && variant !== 'ecommerce' && (
                    <div className="mb-2">
                      <span className={clsx(
                        'inline-block px-2 py-1 text-xs font-semibold rounded',
                        product.badge === 'New' && 'bg-green-500 text-white',
                        product.badge === 'Sale' && 'bg-red-500 text-white',
                        product.badge === 'Hot' && 'bg-orange-500 text-white',
                        !['New', 'Sale', 'Hot'].includes(product.badge) && 'bg-primary-500 text-white'
                      )}>
                        {product.badge}
                      </span>
                    </div>
                  )}

                  {/* Product Title */}
                  {product.title && (
                    <h3 className={clsx(
                      'text-lg sm:text-xl font-bold text-gray-900 mb-2',
                      variant === 'minimal' && 'text-xl sm:text-2xl',
                      variant === 'gradient' && product.featured && 'text-white'
                    )}>
                      {product.title}
                    </h3>
                  )}

                  {/* Product Description */}
                  {product.description && (
                    <p className={clsx(
                      'text-sm sm:text-base text-gray-600 mb-3 line-clamp-2',
                      variant === 'gradient' && product.featured && 'text-white/90'
                    )}>
                      {product.description}
                    </p>
                  )}

                  {/* Rating */}
                  {showRating && product.rating !== undefined && (
                    <div className="mb-3">
                      {renderRating(product.rating)}
                    </div>
                  )}

                  {/* Price */}
                  {product.price !== undefined && (
                    <div className="mb-4">
                      <div className="flex items-baseline gap-2">
                        <span className={clsx(
                          'text-2xl sm:text-3xl font-bold',
                          variant === 'gradient' && product.featured ? 'text-white' : 'text-gray-900'
                        )}>
                          {currency}{product.price}
                        </span>
                        {product.originalPrice && product.originalPrice > product.price && (
                          <span className={clsx(
                            'text-base sm:text-lg line-through',
                            variant === 'gradient' && product.featured ? 'text-white/70' : 'text-gray-400'
                          )}>
                            {currency}{product.originalPrice}
                          </span>
                        )}
                      </div>
                      {product.priceNote && (
                        <p className={clsx(
                          'text-xs sm:text-sm mt-1',
                          variant === 'gradient' && product.featured ? 'text-white/80' : 'text-gray-500'
                        )}>
                          {product.priceNote}
                        </p>
                      )}
                    </div>
                  )}

                  {/* Product Button */}
                  {renderButton(product)}
                </div>
              );
            })}
          </div>
        )}

        {/* Empty State */}
        {products.length === 0 && (
          <div className="text-center py-12">
            <p className="text-gray-500 text-lg">No products available</p>
          </div>
        )}
      </div>
    </section>
  );
};
