import { useState, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/price.css';

/**
 * PriceBoard Component
 * @param {Object} props - Component props
 * @param {Object} props.data - Price section configuration data
 * @param {string} [props.data.title] - Main heading text
 * @param {string} [props.data.subtitle] - Subtitle text displayed above title
 * @param {string} [props.data.description] - Description text displayed below title
 * @param {Array} props.data.plans - Array of pricing plans
 * @param {string} [props.data.variant='default'] - Style variant: 'default' | 'bordered' | 'minimal' | 'gradient' | 'shadow' | 'colored' | 'outlined' | 'filled' | 'card' | 'table' | 'featured' | 'modern' | 'classic' | 'minimal-card' | 'gradient-card'
 * @param {string} [props.data.alignment='center'] - Header text alignment: 'left' | 'center' | 'right'
 * @param {string} [props.data.cardAlignment] - Card text alignment: 'left' | 'center' | 'right' (defaults to alignment if not specified)
 * @param {number} [props.data.columns=3] - Number of columns for grid layout (1-4)
 * @param {string} [props.data.currency='$'] - Currency symbol
 * @param {string} [props.data.billingPeriod='month'] - Billing period: 'month' | 'year' | 'week' | 'day'
 * @param {boolean} [props.data.showPopular=false] - Show popular badge on featured plan
 * @param {number} [props.data.featuredPlanIndex] - Index of the featured/popular plan
 * @param {Object} [props.data.plans[].button] - Button configuration object
 * @param {string} [props.data.plans[].button.text] - Button text (defaults to 'Get Started')
 * @param {string} [props.data.plans[].button.href] - Button link URL
 * @param {Function} [props.data.plans[].button.onClick] - Button click handler
 * @param {string} [props.data.plans[].button.variant] - Button style variant: 'primary' | 'secondary' | 'outline' (if not specified, uses automatic styling based on plan variant and featured status)
 * @param {string} [props.data.plans[].button.className] - Additional CSS classes for button
 * @param {string} [props.data.className] - Additional CSS classes for the section
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute for the section element
 * @param {boolean} [props.loader=false] - Show loading state (auto-hides after 2 seconds)
 */
export const PriceBoard = ({ data, className, id, loader = false }) => {
  // Safety check for data
  if (!data || typeof data !== 'object') {
    console.error('PriceBoard: data prop is required and must be an object');
    return null;
  }

  const [showLoader, setShowLoader] = useState(loader);

  // Auto-hide loader after 2 seconds when loader prop is true
  useEffect(() => {
    if (loader) {
      const timer = setTimeout(() => {
        setShowLoader(false);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowLoader(false);
    }
  }, [loader]);

  const {
    title,
    subtitle,
    description,
    plans = [],
    variant = 'default',
    alignment = 'center',
    cardAlignment,
    columns = 3,
    currency = '$',
    billingPeriod = 'month',
    showPopular = false,
    featuredPlanIndex,
    className: dataClassName,
  } = data;

  // Show skeleton loader if loader prop is true
  if (showLoader) {
    const alignmentClasses = {
      left: 'text-left items-start',
      center: 'text-center items-center',
      right: 'text-right items-end',
    };

    return (
      <section
        id={id}
        className={clsx(
          'w-full py-12 sm:py-16 lg:py-20 px-4 sm:px-6 lg:px-8',
          'bg-white',
          className,
          dataClassName
        )}
      >
        <div className="max-w-7xl mx-auto w-full">
          {/* Header skeleton */}
          <div className={clsx('mb-10 sm:mb-12 flex flex-col', alignmentClasses[alignment])}>
            <div className="h-4 bg-gray-400 rounded w-24 mb-3 mx-auto animate-pulse"></div>
            <div className="h-10 bg-gray-400 rounded w-full max-w-2xl mb-4 mx-auto animate-pulse"></div>
            <div className="h-5 bg-gray-300 rounded w-full max-w-xl mb-6 mx-auto animate-pulse"></div>
          </div>

          {/* Pricing cards skeleton */}
          <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-6">
            {[0, 1, 2].map((index) => (
              <div key={index} className="border border-gray-200 rounded-lg p-6 bg-white">
                {/* Plan name skeleton */}
                <div className="h-6 bg-gray-400 rounded w-32 mb-4 animate-pulse"></div>
                
                {/* Price skeleton */}
                <div className="mb-4">
                  <div className="h-12 bg-gray-400 rounded w-24 mb-2 animate-pulse"></div>
                  <div className="h-4 bg-gray-300 rounded w-20 animate-pulse"></div>
                </div>

                {/* Features skeleton */}
                <div className="space-y-3 mb-6">
                  {[0, 1, 2, 3].map((featureIndex) => (
                    <div key={featureIndex} className="flex items-center gap-2">
                      <div className="h-4 w-4 bg-gray-300 rounded animate-pulse"></div>
                      <div className="h-4 bg-gray-300 rounded flex-1 animate-pulse"></div>
                    </div>
                  ))}
                </div>

                {/* Button skeleton */}
                <div className="h-12 bg-gray-400 rounded-lg w-full animate-pulse"></div>
              </div>
            ))}
          </div>
        </div>
      </section>
    );
  }

  const [selectedPlan, setSelectedPlan] = useState(featuredPlanIndex || null);

  // Use cardAlignment if provided, otherwise fall back to alignment
  const effectiveCardAlignment = cardAlignment || alignment;

  const alignmentClasses = {
    left: 'text-left items-start',
    center: 'text-center items-center',
    right: 'text-right items-end',
  };

  const cardAlignmentClasses = {
    left: 'text-left',
    center: 'text-center',
    right: 'text-right',
  };

  const variantClasses = {
    default: 'bg-white',
    bordered: 'bg-white',
    minimal: 'bg-transparent',
    gradient: 'bg-white',
    shadow: 'bg-white',
    colored: 'bg-gray-50',
    outlined: 'bg-white',
    filled: 'bg-white',
    card: 'bg-white',
    table: 'bg-white',
    featured: 'bg-white',
    modern: 'bg-white',
    classic: 'bg-white',
    'minimal-card': 'bg-transparent',
    'gradient-card': 'bg-gradient-to-br from-primary-50 via-primary-100 to-primary-200',
  };

  const gridColumnsClasses = {
    1: 'grid-cols-1',
    2: 'grid-cols-1 sm:grid-cols-2',
    3: 'grid-cols-1 sm:grid-cols-2 lg:grid-cols-3',
    4: 'grid-cols-1 sm:grid-cols-2 lg:grid-cols-4',
  };

  const getPlanClasses = (plan, index) => {
    const isFeatured = featuredPlanIndex === index || plan.featured || plan.popular;
    const baseClasses = 'transition-all duration-300 flex flex-col h-full';

    // Default variant - clean white cards
    if (variant === 'default') {
      return clsx(
        baseClasses,
        'bg-white rounded-lg p-6 sm:p-8 border border-gray-200',
        isFeatured && 'border-primary-500 shadow-md',
        'hover:shadow-lg hover:border-gray-300'
      );
    }

    // Bordered variant - thick borders always visible
    if (variant === 'bordered') {
      return clsx(
        baseClasses,
        'bg-white rounded-lg p-6 sm:p-8 border-2',
        isFeatured ? 'border-primary-500 shadow-md' : 'border-gray-300',
        'hover:border-gray-400'
      );
    }

    // Minimal variant - transparent with subtle borders
    if (variant === 'minimal') {
      return clsx(
        baseClasses,
        'bg-transparent rounded-lg p-6 sm:p-8 border border-gray-100',
        isFeatured && 'bg-gray-50 border-gray-200',
        'hover:bg-gray-50'
      );
    }

    // Gradient variant - light primary gradient backgrounds on all cards
    if (variant === 'gradient') {
      return clsx(
        baseClasses,
        'rounded-lg p-6 sm:p-8 border shadow-sm',
        isFeatured 
          ? 'bg-gradient-to-br from-primary-400 via-primary-500 to-primary-600 text-white border-transparent shadow-lg' 
          : 'bg-gradient-to-br from-primary-50 via-primary-100 to-primary-200 border-gray-200',
        'hover:shadow-lg'
      );
    }

    // Shadow variant - prominent shadows always visible
    if (variant === 'shadow') {
      return clsx(
        baseClasses,
        'bg-white rounded-lg p-6 sm:p-8 border border-gray-200',
        isFeatured ? 'shadow-xl border-gray-300' : 'shadow-md',
        'hover:shadow-lg'
      );
    }

    // Colored variant - colored backgrounds on all cards
    if (variant === 'colored') {
      return clsx(
        baseClasses,
        'rounded-lg p-6 sm:p-8 border border-gray-200',
        isFeatured 
          ? 'bg-primary-500 border-primary-600 text-white shadow-md' 
          : 'bg-primary-50 border-primary-100',
        'hover:bg-primary-100'
      );
    }

    // Outlined variant - thick outlined borders always visible
    if (variant === 'outlined') {
      return clsx(
        baseClasses,
        'bg-white rounded-lg p-6 sm:p-8 border-2',
        isFeatured ? 'border-primary-600 shadow-md' : 'border-gray-400',
        'hover:border-gray-500'
      );
    }

    // Filled variant - filled backgrounds on all cards
    if (variant === 'filled') {
      return clsx(
        baseClasses,
        'rounded-lg p-6 sm:p-8 border',
        isFeatured 
          ? 'bg-primary-500 border-primary-600 text-white shadow-lg' 
          : 'bg-gray-200 border-gray-300',
        'hover:shadow-md'
      );
    }

    // Card variant - modern card with shadow always visible
    if (variant === 'card') {
      return clsx(
        baseClasses,
        'bg-white rounded-xl p-6 sm:p-8 border border-gray-200',
        isFeatured ? 'ring-2 ring-primary-500 shadow-xl' : 'shadow-lg',
        'hover:shadow-xl hover:scale-105'
      );
    }

    // Featured variant - scale effect on featured plan
    if (variant === 'featured') {
      return clsx(
        baseClasses,
        'bg-white rounded-lg p-6 sm:p-8 border-2',
        isFeatured 
          ? 'border-primary-500 shadow-xl scale-105 z-10' 
          : 'border-gray-200 shadow-sm',
        'hover:shadow-lg'
      );
    }

    // Modern variant - rounded corners with shadows
    if (variant === 'modern') {
      return clsx(
        baseClasses,
        'bg-white rounded-2xl p-6 sm:p-8 border border-gray-100',
        isFeatured 
          ? 'border-primary-400 shadow-2xl bg-gradient-to-br from-primary-50 to-white' 
          : 'shadow-lg',
        'hover:shadow-xl'
      );
    }

    // Classic variant - traditional style with thick borders
    if (variant === 'classic') {
      return clsx(
        baseClasses,
        'bg-white rounded p-6 sm:p-8 border-2',
        isFeatured ? 'border-primary-600 bg-primary-50' : 'border-gray-400',
        'hover:border-gray-500'
      );
    }

    // Minimal-card variant - minimal with subtle cards
    if (variant === 'minimal-card') {
      return clsx(
        baseClasses,
        'bg-white rounded-lg p-6 sm:p-8 border border-gray-200 shadow-sm',
        isFeatured && 'shadow-md border-gray-300',
        'hover:shadow-md'
      );
    }

    // Gradient-card variant - light primary gradient on featured, subtle on others
    if (variant === 'gradient-card') {
      return clsx(
        baseClasses,
        'rounded-lg p-6 sm:p-8 border',
        isFeatured 
          ? 'bg-gradient-to-br from-primary-100 via-primary-200 to-primary-300 text-gray-900 border-primary-200 shadow-xl' 
          : 'bg-white border-gray-200 shadow-sm',
        'hover:shadow-lg'
      );
    }

    // Table variant - handled separately
    if (variant === 'table') {
      return clsx(
        'transition-all duration-300',
        isFeatured && 'bg-primary-50'
      );
    }

    return clsx(baseClasses, 'bg-white rounded-lg p-6 sm:p-8');
  };

  const getPriceClasses = (plan, index) => {
    const isFeatured = featuredPlanIndex === index || plan.featured || plan.popular;
    
    // Gradient variant - white text on featured, dark on regular
    if (variant === 'gradient') {
      return isFeatured ? 'text-white' : 'text-gray-900';
    }
    
    // Filled variant - white text on featured, dark on others
    if (variant === 'filled') {
      return isFeatured ? 'text-white' : 'text-gray-900';
    }
    
    // Colored variant - white text on featured, dark on others
    if (variant === 'colored') {
      return isFeatured ? 'text-white' : 'text-gray-900';
    }
    
    // Gradient-card variant - dark text on featured (light background)
    if (variant === 'gradient-card' && isFeatured) {
      return 'text-gray-900';
    }
    
    return 'text-gray-900';
  };

  const getTitleClasses = (plan, index) => {
    const isFeatured = featuredPlanIndex === index || plan.featured || plan.popular;
    
    // Gradient variant - white text on featured, dark on regular
    if (variant === 'gradient') {
      return isFeatured ? 'text-white' : 'text-gray-900';
    }
    
    // Filled variant - white text on featured, dark on others
    if (variant === 'filled') {
      return isFeatured ? 'text-white' : 'text-gray-900';
    }
    
    // Colored variant - white text on featured, dark on others
    if (variant === 'colored') {
      return isFeatured ? 'text-white' : 'text-gray-900';
    }
    
    // Gradient-card variant - dark text on featured (light background)
    if (variant === 'gradient-card' && isFeatured) {
      return 'text-gray-900';
    }
    
    return 'text-gray-900';
  };

  const getDescriptionClasses = (plan, index) => {
    const isFeatured = featuredPlanIndex === index || plan.featured || plan.popular;
    
    // Gradient variant - light primary text on featured, gray on regular
    if (variant === 'gradient') {
      return isFeatured ? 'text-primary-50' : 'text-gray-600';
    }
    
    // Filled variant - light text on featured, gray on others
    if (variant === 'filled') {
      return isFeatured ? 'text-primary-100' : 'text-gray-600';
    }
    
    // Colored variant - light text on featured, gray on others
    if (variant === 'colored') {
      return isFeatured ? 'text-primary-100' : 'text-gray-600';
    }
    
    // Gradient-card variant - gray text on featured (light background)
    if (variant === 'gradient-card' && isFeatured) {
      return 'text-gray-600';
    }
    
    return 'text-gray-600';
  };

  const getFeatureClasses = (plan, index) => {
    const isFeatured = featuredPlanIndex === index || plan.featured || plan.popular;
    
    // Gradient variant - white text on featured, dark on regular
    if (variant === 'gradient') {
      return isFeatured ? 'text-white' : 'text-gray-700';
    }
    
    // Filled variant - white text on featured, dark on others
    if (variant === 'filled') {
      return isFeatured ? 'text-white' : 'text-gray-700';
    }
    
    // Colored variant - white text on featured, dark on others
    if (variant === 'colored') {
      return isFeatured ? 'text-white' : 'text-gray-700';
    }
    
    // Gradient-card variant - dark text on featured (light background)
    if (variant === 'gradient-card' && isFeatured) {
      return 'text-gray-900';
    }
    
    return 'text-gray-700';
  };

  const getButtonClasses = (plan, index) => {
    const isFeatured = featuredPlanIndex === index || plan.featured || plan.popular;
    
    // Gradient variant - white button on featured, dark on regular
    if (variant === 'gradient') {
      return isFeatured 
        ? 'bg-white text-primary-600 hover:bg-gray-100' 
        : 'bg-primary-600 text-white hover:bg-primary-700';
    }
    
    // Filled variant - white button on featured, dark on others
    if (variant === 'filled') {
      return isFeatured 
        ? 'bg-white text-primary-600 hover:bg-gray-100' 
        : 'bg-gray-900 text-white hover:bg-gray-800';
    }
    
    // Colored variant - white button on featured, dark on others
    if (variant === 'colored') {
      return isFeatured 
        ? 'bg-white text-primary-600 hover:bg-gray-100' 
        : 'bg-primary-600 text-white hover:bg-primary-700';
    }
    
    // Gradient-card variant - primary button on featured (light background)
    if (variant === 'gradient-card' && isFeatured) {
      return 'bg-primary-600 text-white hover:bg-primary-700';
    }
    
    // Default button styles
    if (isFeatured) {
      return 'bg-primary-600 text-white hover:bg-primary-700';
    }
    
    return 'bg-gray-900 text-white hover:bg-gray-800';
  };

  // Button variant classes - same as SlideFlow
  const buttonVariantClasses = {
    primary: 'bg-primary-600 hover:bg-primary-700 text-white',
    secondary: 'bg-gray-600 hover:bg-gray-700 text-white',
    outline: 'border-2 border-white text-white hover:bg-white hover:text-gray-900',
  };

  // Get button variant classes with context awareness for outline variant
  const getButtonVariantClasses = (buttonVariant, plan, index) => {
    if (!buttonVariant || !buttonVariantClasses[buttonVariant]) {
      return null;
    }

    const isFeatured = featuredPlanIndex === index || plan.featured || plan.popular;
    const isDarkBackground = variant === 'gradient' || variant === 'filled' || variant === 'colored';
    
    // For outline variant, adapt based on background
    if (buttonVariant === 'outline') {
      // Dark backgrounds (gradient, filled, colored, gradient-card) with featured plan - use white border
      if (isDarkBackground && isFeatured) {
        return 'border-2 border-white text-white hover:bg-white hover:text-gray-900';
      }
      // Light backgrounds (default, minimal-card, bordered, etc.) - use gray border for visibility
      return 'border-2 border-gray-300 text-gray-700 hover:bg-gray-50';
    }

    // For primary and secondary variants, always use standard classes (same as SlideFlow)
    // These work on both light and dark backgrounds
    if (buttonVariant === 'primary') {
      return 'bg-primary-600 hover:bg-primary-700 text-white';
    }
    if (buttonVariant === 'secondary') {
      return 'bg-gray-600 hover:bg-gray-700 text-white';
    }

    return buttonVariantClasses[buttonVariant];
  };

  if (plans.length === 0) {
    console.warn('PriceBoard: plans array is empty');
    return null;
  }

  // Table variant - special layout
  if (variant === 'table') {
    return (
      <section
        id={id}
        className={clsx(
          'w-full py-12 sm:py-16 lg:py-20 px-4 sm:px-6 lg:px-8',
          variantClasses[variant],
          className,
          dataClassName
        )}
      >
        <div className="max-w-7xl mx-auto w-full">
          {/* Header */}
          {(title || subtitle || description) && (
            <div
              className={clsx(
                'mb-10 sm:mb-12 flex flex-col',
                alignmentClasses[alignment]
              )}
            >
              {subtitle && (
                <p className="text-sm sm:text-base font-semibold text-primary-600 uppercase tracking-wider mb-3">
                  {subtitle}
                </p>
              )}

              {title && (
                <h2 className="text-3xl sm:text-4xl md:text-5xl font-bold text-gray-900 mb-4 leading-tight">
                  {title}
                </h2>
              )}

              {description && (
                <p
                  className={clsx(
                    'text-base sm:text-lg text-gray-500 max-w-2xl',
                    alignment === 'center' && 'mx-auto',
                    alignment === 'right' && 'ml-auto',
                    alignment === 'left' && 'mr-auto'
                  )}
                >
                  {description}
                </p>
              )}
            </div>
          )}

          {/* Table */}
          <div className="overflow-x-auto">
            <table className="w-full border-collapse">
              <thead>
                <tr className="border-b-2 border-gray-200">
                  <th className="text-left py-4 px-4 font-semibold text-gray-900">Plan</th>
                  <th className="text-center py-4 px-4 font-semibold text-gray-900">Price</th>
                  <th className="text-center py-4 px-4 font-semibold text-gray-900">Features</th>
                  <th className="text-center py-4 px-4 font-semibold text-gray-900">Action</th>
                </tr>
              </thead>
              <tbody>
                {plans.map((plan, index) => {
                  const isFeatured = featuredPlanIndex === index || plan.featured || plan.popular;
                  
                  return (
                    <tr
                      key={index}
                      className={clsx(
                        getPlanClasses(plan, index),
                        'border-b border-gray-200'
                      )}
                    >
                      <td className="py-4 px-4">
                        <div className="flex items-center gap-2">
                          <h3 className={clsx('text-lg font-bold', getTitleClasses(plan, index))}>
                            {plan.name}
                          </h3>
                          {isFeatured && showPopular && (
                            <span className="px-2 py-1 text-xs font-semibold bg-primary-600 text-white rounded-full">
                              {plan.badgeText || 'Popular'}
                            </span>
                          )}
                        </div>
                        {plan.description && (
                          <p className={clsx('text-sm mt-1', getDescriptionClasses(plan, index))}>
                            {plan.description}
                          </p>
                        )}
                      </td>
                      <td className="py-4 px-4 text-center">
                        <div className={getPriceClasses(plan, index)}>
                          <span className="text-2xl font-bold">
                            {currency}{plan.price}
                          </span>
                          <span className="text-sm text-gray-500 ml-1">
                            /{billingPeriod}
                          </span>
                        </div>
                      </td>
                      <td className="py-4 px-4">
                        <ul className="space-y-2">
                          {plan.features?.slice(0, 3).map((feature, fIndex) => (
                            <li key={fIndex} className={clsx('text-sm', getFeatureClasses(plan, index))}>
                              {typeof feature === 'string' ? feature : feature.text}
                            </li>
                          ))}
                          {plan.features?.length > 3 && (
                            <li className={clsx('text-sm font-semibold', getFeatureClasses(plan, index))}>
                              +{plan.features.length - 3} more
                            </li>
                          )}
                        </ul>
                      </td>
                      <td className="py-4 px-4 text-center">
                        {plan.button && (
                          <a
                            href={plan.button.href || '#'}
                            onClick={plan.button.onClick}
                            className={clsx(
                              'inline-block px-6 py-2 rounded-lg font-semibold text-sm transition-all duration-300',
                              plan.button.variant && getButtonVariantClasses(plan.button.variant, plan, index)
                                ? getButtonVariantClasses(plan.button.variant, plan, index)
                                : getButtonClasses(plan, index),
                              plan.button.className
                            )}
                          >
                            {plan.button.text || 'Get Started'}
                          </a>
                        )}
                      </td>
                    </tr>
                  );
                })}
              </tbody>
            </table>
          </div>
        </div>
      </section>
    );
  }

  // Grid/Card variants
  return (
    <section
      id={id}
      className={clsx(
        'w-full py-12 sm:py-16 lg:py-20 px-4 sm:px-6 lg:px-8',
        variantClasses[variant],
        className,
        dataClassName
      )}
    >
      <div className="max-w-7xl mx-auto w-full">
        {/* Header */}
        {(title || subtitle || description) && (
          <div
            className={clsx(
              'mb-10 sm:mb-12 flex flex-col',
              alignmentClasses[alignment]
            )}
          >
            {subtitle && (
              <p className="text-sm sm:text-base font-semibold text-primary-600 uppercase tracking-wider mb-3">
                {subtitle}
              </p>
            )}

            {title && (
              <h2 className="text-3xl sm:text-4xl md:text-5xl font-bold text-gray-900 mb-4 leading-tight">
                {title}
              </h2>
            )}

            {description && (
              <p
                className={clsx(
                  'text-base sm:text-lg text-gray-500 max-w-2xl',
                  alignment === 'center' && 'mx-auto',
                  alignment === 'right' && 'ml-auto',
                  alignment === 'left' && 'mr-auto'
                )}
              >
                {description}
              </p>
            )}
          </div>
        )}

        {/* Pricing Plans Grid */}
        <div className={clsx('grid gap-6 sm:gap-8', gridColumnsClasses[columns] || gridColumnsClasses[3])}>
          {plans.map((plan, index) => {
            const isFeatured = featuredPlanIndex === index || plan.featured || plan.popular;
            
            return (
              <div
                key={index}
                className={clsx(
                  getPlanClasses(plan, index),
                  cardAlignmentClasses[effectiveCardAlignment]
                )}
              >
                {/* Popular/Featured Badge */}
                {isFeatured && showPopular && (
                  <div className={clsx(
                    'mb-4',
                    effectiveCardAlignment === 'center' && 'flex justify-center',
                    effectiveCardAlignment === 'right' && 'flex justify-end'
                  )}>
                    <span className={clsx(
                      'inline-block px-3 py-1 text-xs font-semibold rounded-full',
                      variant === 'gradient' ? 'bg-white text-primary-600' :
                      variant === 'filled' && isFeatured ? 'bg-white text-primary-600' :
                      variant === 'colored' && isFeatured ? 'bg-white text-primary-600' :
                      variant === 'gradient-card' && isFeatured ? 'bg-primary-600 text-white' :
                      'bg-primary-600 text-white'
                    )}>
                      {plan.badgeText || 'Popular'}
                    </span>
                  </div>
                )}

                {/* Plan Name */}
                {plan.name && (
                  <h3 className={clsx('text-2xl sm:text-3xl font-bold mb-2', getTitleClasses(plan, index))}>
                    {plan.name}
                  </h3>
                )}

                {/* Plan Description */}
                {plan.description && (
                  <p className={clsx('text-sm sm:text-base mb-6', getDescriptionClasses(plan, index))}>
                    {plan.description}
                  </p>
                )}

                {/* Price */}
                <div className="mb-6">
                  <div className={clsx(
                    'flex items-baseline',
                    effectiveCardAlignment === 'center' && 'justify-center',
                    effectiveCardAlignment === 'right' && 'justify-end',
                    getPriceClasses(plan, index)
                  )}>
                    <span className="text-4xl sm:text-5xl font-bold">
                      {currency}{plan.price}
                    </span>
                    <span className={clsx(
                      'text-base sm:text-lg ml-2',
                      variant === 'gradient' && isFeatured ? 'text-primary-50' :
                      variant === 'filled' && isFeatured ? 'text-primary-100' : 
                      variant === 'colored' && isFeatured ? 'text-primary-100' :
                      variant === 'gradient-card' && isFeatured ? 'text-gray-500' : 
                      'text-gray-500'
                    )}>
                      /{billingPeriod}
                    </span>
                  </div>
                  {plan.priceNote && (
                    <p className={clsx('text-sm mt-2', getDescriptionClasses(plan, index))}>
                      {plan.priceNote}
                    </p>
                  )}
                </div>

                {/* Features List */}
                {plan.features && plan.features.length > 0 && (
                  <ul className={clsx(
                    'space-y-3 mb-8 flex-1',
                    effectiveCardAlignment === 'center' && 'items-center',
                    effectiveCardAlignment === 'right' && 'items-end'
                  )}>
                    {plan.features.map((feature, fIndex) => {
                      const featureText = typeof feature === 'string' ? feature : feature.text;
                      const featureIncluded = typeof feature === 'string' ? true : (feature.included !== false);
                      
                      return (
                        <li 
                          key={fIndex} 
                          className={clsx(
                            'flex items-start',
                            effectiveCardAlignment === 'center' && 'justify-center',
                            effectiveCardAlignment === 'right' && 'justify-end'
                          )}
                        >
                          {featureIncluded ? (
                            <svg
                              className={clsx(
                                'w-5 h-5 flex-shrink-0 mt-0.5',
                                effectiveCardAlignment === 'left' ? 'mr-3' :
                                effectiveCardAlignment === 'center' ? 'mr-2' :
                                'mr-3',
                                variant === 'gradient' && isFeatured ? 'text-white' :
                                variant === 'filled' && isFeatured ? 'text-white' :
                                variant === 'colored' && isFeatured ? 'text-white' :
                                variant === 'gradient-card' && isFeatured ? 'text-green-500' :
                                'text-green-500'
                              )}
                              fill="none"
                              stroke="currentColor"
                              viewBox="0 0 24 24"
                            >
                              <path
                                strokeLinecap="round"
                                strokeLinejoin="round"
                                strokeWidth={2}
                                d="M5 13l4 4L19 7"
                              />
                            </svg>
                          ) : (
                            <svg
                              className={clsx(
                                'w-5 h-5 flex-shrink-0 mt-0.5',
                                effectiveCardAlignment === 'left' ? 'mr-3' :
                                effectiveCardAlignment === 'center' ? 'mr-2' :
                                'mr-3',
                                'text-gray-400'
                              )}
                              fill="none"
                              stroke="currentColor"
                              viewBox="0 0 24 24"
                            >
                              <path
                                strokeLinecap="round"
                                strokeLinejoin="round"
                                strokeWidth={2}
                                d="M6 18L18 6M6 6l12 12"
                              />
                            </svg>
                          )}
                          <span className={clsx('text-sm sm:text-base', getFeatureClasses(plan, index))}>
                            {featureText}
                          </span>
                        </li>
                      );
                    })}
                  </ul>
                )}

                {/* CTA Button */}
                {plan.button && (
                  <a
                    href={plan.button.href || '#'}
                    onClick={plan.button.onClick}
                    className={clsx(
                      'block w-full px-6 py-3 rounded-lg font-semibold text-base transition-all duration-300',
                      effectiveCardAlignment === 'center' && 'text-center',
                      effectiveCardAlignment === 'right' && 'text-right',
                      plan.button.variant && getButtonVariantClasses(plan.button.variant, plan, index)
                        ? getButtonVariantClasses(plan.button.variant, plan, index)
                        : getButtonClasses(plan, index),
                      plan.button.className
                    )}
                  >
                    {plan.button.text || 'Get Started'}
                  </a>
                )}
              </div>
            );
          })}
        </div>
      </div>
    </section>
  );
};

