import React, { useState, useEffect } from 'react';
import clsx from 'clsx';

/**
 * CartStandard Component - Standard cart layout with product list
 * @param {Object} props - Component props
 * @param {Object} props.data - Cart configuration data
 * @param {Array} props.data.items - Cart items array
 * @param {string} [props.variant='default'] - Visual variant: 'default' | 'bordered'
 * @param {boolean} [props.loader=false] - Show skeleton loader
 * @param {string} [props.data.title='Your Cart'] - Cart title
 * @param {boolean} [props.data.showSubtotal=true] - Show subtotal
 * @param {boolean} [props.data.showTax=true] - Show tax
 * @param {boolean} [props.data.showShipping=true] - Show shipping
 * @param {boolean} [props.data.showTotal=true] - Show total
 * @param {boolean} [props.data.showCheckoutButton=true] - Show checkout button
 * @param {boolean} [props.data.showContinueShopping=true] - Show continue shopping link
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute for the section element
 */
export const CartStandard = ({ data, variant = 'default', loader: initialLoader = false, className, id }) => {
  const [cartItems, setCartItems] = useState(data?.items || []);
  const [isLoading, setIsLoading] = useState(initialLoader);

  // Auto-switch from skeleton to content after 2 seconds
  useEffect(() => {
    if (initialLoader) {
      const timer = setTimeout(() => {
        setIsLoading(false);
      }, 2000);

      return () => clearTimeout(timer);
    }
  }, [initialLoader]);

  const loader = initialLoader ? isLoading : false;

  if (!data || typeof data !== 'object') {
    console.error('CartStandard: data prop is required and must be an object');
    return null;
  }

  const {
    title = 'Your Cart',
    showSubtotal = true,
    showTax = true,
    showShipping = true,
    showTotal = true,
    showCheckoutButton = true,
    showContinueShopping = true,
  } = data;

  // Calculate totals
  const subtotal = cartItems.reduce((total, item) => total + (item.price * item.quantity), 0);
  const tax = subtotal * 0.08; // 8% tax
  const shipping = subtotal > 100 ? 0 : 9.99; // Free shipping over $100
  const total = subtotal + tax + shipping;

  const isBordered = variant === 'bordered';

  // Skeleton components
  const SkeletonHeader = () => (
    <div className="mb-8">
      <div className="h-9 bg-gray-200 rounded animate-pulse w-48 mb-2"></div>
      <div className="h-5 bg-gray-200 rounded animate-pulse w-32"></div>
    </div>
  );

  const SkeletonCartItem = () => (
    <div className={clsx(
      "flex flex-col sm:flex-row gap-4 p-6 bg-white transition-all duration-300 animate-pulse",
      isBordered ? "border-2 border-gray-900 rounded-none shadow-[4px_4px_0px_0px_rgba(0,0,0,1)]" : "border border-gray-200 rounded-lg shadow-sm"
    )}>
      {/* Product Image Skeleton */}
      <div className="flex-shrink-0">
        <div className={clsx(
          "w-24 h-24 bg-gray-200",
          isBordered ? "border border-gray-900" : "rounded-lg"
        )}></div>
      </div>

      {/* Product Details Skeleton */}
      <div className="flex-1 min-w-0">
        <div className="flex justify-between items-start">
          <div className="flex-1">
            <div className="h-5 bg-gray-200 rounded w-40 mb-1"></div>
            <div className="h-4 bg-gray-200 rounded w-48 mb-2"></div>
            <div className="h-4 bg-gray-200 rounded w-24"></div>
          </div>
          <div className="w-5 h-5 bg-gray-200 rounded"></div>
        </div>

        {/* Price and Quantity Skeleton */}
        <div className="flex justify-between items-center mt-4">
          <div className="flex items-center gap-3">
            <div className="h-5 bg-gray-200 rounded w-12"></div>
            <div className={clsx(
              "flex items-center gap-2 bg-gray-200",
              isBordered ? "border-2 border-gray-900" : "border border-gray-300 rounded"
            )}>
              <div className="w-8 h-8 bg-gray-300 rounded"></div>
              <div className="w-8 h-6 bg-gray-300 rounded"></div>
              <div className="w-8 h-8 bg-gray-300 rounded"></div>
            </div>
          </div>
          <div className="h-5 bg-gray-200 rounded w-16"></div>
        </div>
      </div>
    </div>
  );

  const SkeletonOrderSummary = () => (
    <div className="lg:col-span-1">
      <div className={clsx(
        "bg-white p-6 sticky top-4 transition-all duration-300 animate-pulse",
        isBordered ? "border-2 border-gray-900 rounded-none shadow-[8px_8px_0px_0px_rgba(0,0,0,1)]" : "border border-gray-200 rounded-lg shadow-sm"
      )}>
        <div className="h-6 bg-gray-200 rounded mb-6 w-32"></div>

        <div className="space-y-4">
          {showSubtotal && (
            <div className="flex justify-between text-sm">
              <div className="h-4 bg-gray-200 rounded w-16"></div>
              <div className="h-4 bg-gray-200 rounded w-12"></div>
            </div>
          )}

          {showTax && (
            <div className="flex justify-between text-sm">
              <div className="h-4 bg-gray-200 rounded w-12"></div>
              <div className="h-4 bg-gray-200 rounded w-10"></div>
            </div>
          )}

          {showShipping && (
            <div className="flex justify-between text-sm">
              <div className="h-4 bg-gray-200 rounded w-16"></div>
              <div className="h-4 bg-gray-200 rounded w-14"></div>
            </div>
          )}

          {showShipping && (
            <div className="h-4 bg-gray-200 rounded w-48"></div>
          )}

          <hr className="border-gray-200" />

          {showTotal && (
            <div className="flex justify-between text-lg font-bold">
              <div className="h-5 bg-gray-200 rounded w-12"></div>
              <div className="h-5 bg-gray-200 rounded w-16"></div>
            </div>
          )}
        </div>

        {showCheckoutButton && (
          <div className={clsx("h-10 bg-gray-200 rounded mt-6 animate-pulse", isBordered ? "" : "rounded-lg")}></div>
        )}

        {showContinueShopping && (
          <div className={clsx("h-10 bg-gray-200 rounded mt-3 animate-pulse", isBordered ? "" : "rounded-lg")}></div>
        )}
      </div>
    </div>
  );

  // Show skeleton loader
  if (loader) {
    return (
      <section
        id={id}
        className={clsx(
          'w-full py-8 sm:py-12 lg:py-16 px-4 sm:px-6 lg:px-8',
          className
        )}
      >
        <div className="max-w-4xl mx-auto">
          <SkeletonHeader />
          <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
            {/* Cart Items Skeleton */}
            <div className="lg:col-span-2 space-y-4">
              <SkeletonCartItem />
              <SkeletonCartItem />
              <SkeletonCartItem />
            </div>
            <SkeletonOrderSummary />
          </div>
        </div>
      </section>
    );
  }

  const updateQuantity = (id, newQuantity) => {
    if (newQuantity <= 0) {
      removeItem(id);
      return;
    }
    setCartItems(items =>
      items.map(item =>
        item.id === id ? { ...item, quantity: newQuantity } : item
      )
    );
  };

  const removeItem = (id) => {
    setCartItems(items => items.filter(item => item.id !== id));
  };

  if (cartItems.length === 0) {
    return (
      <section
        id={id}
        className={clsx(
          'w-full py-8 sm:py-12 lg:py-16 px-4 sm:px-6 lg:px-8',
          className
        )}
      >
        <div className="max-w-4xl mx-auto text-center">
          <div className="mb-8">
            <svg
              className="mx-auto h-24 w-24 text-gray-400"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={1.5}
                d="M16 11V7a4 4 0 00-8 0v4M5 9h14l1 12H4L5 9z"
              />
            </svg>
          </div>
          <h2 className="text-2xl font-bold text-gray-900 mb-4">Your cart is empty</h2>
          <p className="text-gray-600 mb-8">Looks like you haven't added anything to your cart yet.</p>
          <button className="inline-flex items-center px-6 py-3 bg-primary-600 text-white font-medium rounded-lg hover:bg-primary-700 transition-colors">
            Continue Shopping
          </button>
        </div>
      </section>
    );
  }

  return (
    <section
      id={id}
      className={clsx(
        'w-full py-8 sm:py-12 lg:py-16 px-4 sm:px-6 lg:px-8',
        className
      )}
    >
      <div className="max-w-4xl mx-auto">
        {/* Header */}
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-gray-900">{title}</h1>
          <p className="text-gray-600 mt-2">{cartItems.length} item{cartItems.length !== 1 ? 's' : ''} in your cart</p>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          {/* Cart Items */}
          <div className="lg:col-span-2 space-y-4">
            {cartItems.map((item) => (
              <div
                key={item.id}
                className={clsx(
                  "flex flex-col sm:flex-row gap-4 p-6 bg-white transition-all duration-300",
                  isBordered ? "border-2 border-gray-900 rounded-none shadow-[4px_4px_0px_0px_rgba(0,0,0,1)]" : "border border-gray-200 rounded-lg shadow-sm hover:shadow-md"
                )}
              >
                {/* Product Image */}
                <div className="flex-shrink-0">
                  <img
                    src={item.image || '/api/placeholder/120/120'}
                    alt={item.name}
                    className={clsx(
                      "w-24 h-24 object-cover",
                      isBordered ? "rounded-none border border-gray-900" : "rounded-lg"
                    )}
                  />
                </div>

                {/* Product Details */}
                <div className="flex-1 min-w-0">
                  <div className="flex justify-between items-start">
                    <div className="flex-1">
                      <h3 className="text-lg font-semibold text-gray-900 mb-1">{item.name}</h3>
                      {item.description && (
                        <p className="text-sm text-gray-600 mb-2">{item.description}</p>
                      )}
                      {item.variant && (
                        <p className="text-sm text-gray-500">Variant: {item.variant}</p>
                      )}
                    </div>
                    <button
                      onClick={() => removeItem(item.id)}
                      className="p-2 text-gray-400 hover:text-red-500 transition-colors"
                      aria-label="Remove item"
                    >
                      <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                      </svg>
                    </button>
                  </div>

                  {/* Price and Quantity */}
                  <div className="flex justify-between items-center mt-4">
                    <div className="flex items-center gap-3">
                      <span className="text-lg font-semibold text-gray-900">${item.price.toFixed(2)}</span>
                      <div className={clsx(
                        "flex items-center gap-2",
                        isBordered ? "border-2 border-gray-900 bg-white" : "border border-gray-300 rounded"
                      )}>
                        <button
                          onClick={() => updateQuantity(item.id, item.quantity - 1)}
                          className={clsx(
                            "w-8 h-8 flex items-center justify-center transition-colors",
                            isBordered ? "hover:bg-gray-100" : "hover:bg-gray-50 rounded"
                          )}
                          aria-label="Decrease quantity"
                        >
                          <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M20 12H4" />
                          </svg>
                        </button>
                        <span className="w-8 text-center font-bold">{item.quantity}</span>
                        <button
                          onClick={() => updateQuantity(item.id, item.quantity + 1)}
                          className={clsx(
                            "w-8 h-8 flex items-center justify-center transition-colors",
                            isBordered ? "hover:bg-gray-100" : "hover:bg-gray-50 rounded"
                          )}
                          aria-label="Increase quantity"
                        >
                          <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 6v6m0 0v6m0-6h6m-6 0H6" />
                          </svg>
                        </button>
                      </div>
                    </div>
                    <span className="text-lg font-semibold text-gray-900">
                      ${(item.price * item.quantity).toFixed(2)}
                    </span>
                  </div>
                </div>
              </div>
            ))}
          </div>

          {/* Order Summary */}
          <div className="lg:col-span-1">
            <div className={clsx(
              "bg-white p-6 sticky top-4 transition-all duration-300",
              isBordered ? "border-2 border-gray-900 rounded-none shadow-[8px_8px_0px_0px_rgba(0,0,0,1)]" : "border border-gray-200 rounded-lg shadow-sm"
            )}>
              <h2 className="text-xl font-bold text-gray-900 mb-6 uppercase tracking-wider">Order Summary</h2>

              <div className="space-y-4">
                {showSubtotal && (
                  <div className="flex justify-between text-sm">
                    <span className="text-gray-600">Subtotal</span>
                    <span className="font-medium">${subtotal.toFixed(2)}</span>
                  </div>
                )}

                {showTax && (
                  <div className="flex justify-between text-sm">
                    <span className="text-gray-600">Tax</span>
                    <span className="font-medium">${tax.toFixed(2)}</span>
                  </div>
                )}

                {showShipping && (
                  <div className="flex justify-between text-sm">
                    <span className="text-gray-600">Shipping</span>
                    <span className="font-medium">
                      {shipping === 0 ? 'Free' : `$${shipping.toFixed(2)}`}
                    </span>
                  </div>
                )}

                {subtotal < 100 && showShipping && (
                  <p className="text-xs text-gray-500">
                    Add ${(100 - subtotal).toFixed(2)} more for free shipping
                  </p>
                )}

                <hr className="border-gray-200" />

                {showTotal && (
                  <div className="flex justify-between text-lg font-bold">
                    <span>Total</span>
                    <span>${total.toFixed(2)}</span>
                  </div>
                )}
              </div>

              {showCheckoutButton && (
                <button className={clsx(
                  "w-full mt-6 px-6 py-3 font-bold transition-all duration-300",
                  isBordered ? "bg-black text-white hover:translate-x-1 hover:translate-y-1 hover:shadow-none shadow-[4px_4px_0px_0px_rgba(59,130,246,1)]" : "bg-primary-600 text-white rounded-lg hover:bg-primary-700 shadow-sm"
                )}>
                  Proceed to Checkout
                </button>
              )}

              {showContinueShopping && (
                <button className={clsx(
                  "w-full mt-3 px-6 py-3 font-bold transition-all duration-300",
                  isBordered ? "bg-white text-black border-2 border-gray-900 hover:bg-gray-50" : "border border-gray-300 text-gray-700 rounded-lg hover:bg-gray-50 shadow-sm"
                )}>
                  Continue Shopping
                </button>
              )}
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};