  // Render logo
  const renderLogo = () => {
    if (typeof logo === 'string' && logo.startsWith('http')) {
      return <img src={logo} alt="Logo" className="h-8 w-auto" />;
    }
    
    if (typeof logo === 'object') {
      if (logo.image) {
        return <img src={logo.image} alt={logo.text || 'Logo'} className="h-8 w-auto" />;
      }
      if (logo.text) {
        return <span className="text-xl font-bold">{logo.text}</span>;
      }
    }

    return <span className="text-xl font-bold">{logo}</span>;
  };

