import React, { useState, useEffect } from 'react';
import clsx from 'clsx';

/**
 * CartSidebar Component - Compact sidebar cart layout
 * @param {Object} props - Component props
 * @param {Object} props.data - Cart configuration data
 * @param {Array} props.data.items - Cart items array
 * @param {string} [props.variant='default'] - Visual variant: 'default' | 'minimal'
 * @param {boolean} [props.loader=false] - Show skeleton loader
 * @param {string} [props.data.title='Your Cart'] - Cart title
 * @param {boolean} [props.data.showSubtotal=true] - Show subtotal
 * @param {boolean} [props.data.showTax=true] - Show tax
 * @param {boolean} [props.data.showShipping=true] - Show shipping
 * @param {boolean} [props.data.showTotal=true] - Show total
 * @param {boolean} [props.data.showCheckoutButton=true] - Show checkout button
 * @param {boolean} [props.data.showContinueShopping=true] - Show continue shopping link
 * @param {boolean} [props.isOpen=false] - Whether sidebar is open
 * @param {boolean} [props.inline=false] - Whether to render inline (for demos)
 * @param {function} [props.onClose] - Close callback function
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute for the section element
 */
export const CartSidebar = ({ data, variant = 'default', loader: initialLoader = false, isOpen = false, inline = false, onClose, className, id }) => {
  const [cartItems, setCartItems] = useState(data?.items || []);
  const [isLoading, setIsLoading] = useState(initialLoader);

  // Auto-switch from skeleton to content after 2 seconds
  useEffect(() => {
    if (initialLoader) {
      const timer = setTimeout(() => {
        setIsLoading(false);
      }, 2000);

      return () => clearTimeout(timer);
    }
  }, [initialLoader]);

  const loader = initialLoader ? isLoading : false;

  if (!data || typeof data !== 'object') {
    console.error('CartSidebar: data prop is required and must be an object');
    return null;
  }

  const {
    title = 'Your Cart',
    showSubtotal = true,
    showTax = true,
    showShipping = true,
    showTotal = true,
    showCheckoutButton = true,
    showContinueShopping = true,
  } = data;

  // Calculate totals
  const subtotal = cartItems.reduce((total, item) => total + (item.price * item.quantity), 0);
  const tax = subtotal * 0.08; // 8% tax
  const shipping = subtotal > 100 ? 0 : 9.99; // Free shipping over $100
  const total = subtotal + tax + shipping;

  const isMinimal = variant === 'minimal';

  // Skeleton components
  const SkeletonHeader = () => (
    <div className={clsx("flex items-center justify-between p-6", isMinimal ? "bg-black text-white" : "border-b border-gray-200")}>
      <div>
        <div className={clsx("h-6 bg-gray-200 rounded animate-pulse mb-1", isMinimal ? "w-24" : "w-20")}></div>
        <div className={clsx("h-3 bg-gray-200 rounded animate-pulse", isMinimal ? "w-16" : "w-12")}></div>
      </div>
      {!inline && <div className="w-6 h-6 bg-gray-200 rounded-full animate-pulse"></div>}
    </div>
  );

  const SkeletonCartItem = () => (
    <div className={clsx("flex gap-4 group animate-pulse", isMinimal && "pb-6 border-b border-gray-200 last:border-b-0")}>
      {/* Product Image Skeleton */}
      <div className={clsx(
        "flex-shrink-0 bg-gray-200",
        isMinimal ? "border-2 border-black" : "border border-gray-100 rounded-lg"
      )}>
        <div className="w-24 h-24"></div>
      </div>

      {/* Product Details Skeleton */}
      <div className="flex-1 min-w-0 flex flex-col">
        <div className="flex justify-between items-start mb-1">
          <div className={clsx(
            "h-4 bg-gray-200 rounded mb-auto",
            isMinimal ? "w-32" : "w-28"
          )}></div>
          <div className="w-4 h-4 bg-gray-200 rounded flex-shrink-0 ml-2"></div>
        </div>

        <div className="h-3 bg-gray-200 rounded w-20 mb-auto"></div>

        {/* Price and Quantity Skeleton */}
        <div className="flex justify-between items-end mt-4">
          <div className={clsx(
            "flex items-center p-1 bg-gray-200",
            isMinimal ? "border-black" : "border-gray-100 rounded-full"
          )}>
            <div className="w-7 h-7 bg-gray-300 rounded-full"></div>
            <div className="w-8 h-6 bg-gray-300 rounded mx-1"></div>
            <div className="w-7 h-7 bg-gray-300 rounded-full"></div>
          </div>
          <div className={clsx("h-4 bg-gray-200 rounded", isMinimal ? "w-12" : "w-14")}></div>
        </div>
      </div>
    </div>
  );

  const SkeletonOrderSummary = () => (
    <div className={clsx("p-8 space-y-6", isMinimal ? "bg-white border-t-4 border-black" : "bg-gray-50/50 border-t border-gray-100")}>
      {/* Order Summary */}
      <div className="space-y-3">
        {showSubtotal && (
          <div className="flex justify-between text-sm">
            <div className="h-3 bg-gray-200 rounded w-12"></div>
            <div className="h-3 bg-gray-200 rounded w-8"></div>
          </div>
        )}

        {showTax && (
          <div className="flex justify-between text-sm">
            <div className="h-3 bg-gray-200 rounded w-8"></div>
            <div className="h-3 bg-gray-200 rounded w-6"></div>
          </div>
        )}

        {showShipping && (
          <div className="flex justify-between text-sm">
            <div className="h-3 bg-gray-200 rounded w-12"></div>
            <div className="h-3 bg-gray-200 rounded w-10"></div>
          </div>
        )}

        <div className={clsx("pt-4 mt-4 border-t", isMinimal ? "border-black border-dashed" : "border-gray-200")}>
          <div className="flex justify-between items-center">
            <div className="h-4 bg-gray-200 rounded w-10"></div>
            <div className={clsx("h-8 bg-gray-200 rounded", isMinimal ? "w-16" : "w-20")}></div>
          </div>
        </div>
      </div>

      {/* Action Buttons */}
      <div className="space-y-3">
        {showCheckoutButton && (
          <div className="h-12 bg-gray-200 rounded animate-pulse"></div>
        )}

        {showContinueShopping && (
          <div className="h-12 bg-gray-200 rounded animate-pulse"></div>
        )}
      </div>
    </div>
  );

  const skeletonContent = (
    <div
      className={clsx(
        'h-full w-full max-w-md shadow-xl flex flex-col transition-all duration-300 animate-pulse',
        isMinimal ? 'bg-gray-50' : 'bg-white',
        !inline && 'fixed right-0 top-0 z-50 transform ease-in-out duration-500',
        !inline && (isOpen ? 'translate-x-0' : 'translate-x-full'),
        inline && 'relative translate-x-0 border-l border-gray-100',
        className
      )}
      id={id}
    >
      <SkeletonHeader />
      {/* Cart Items Skeleton */}
      <div className="flex-1 overflow-y-auto p-6 space-y-6">
        <SkeletonCartItem />
        <SkeletonCartItem />
        <SkeletonCartItem />
      </div>
      <SkeletonOrderSummary />
    </div>
  );

  // Show skeleton loader
  if (loader) {
    if (inline) {
      return skeletonContent;
    }

    return (
      <>
        {/* Overlay */}
        {isOpen && (
          <div
            className="fixed inset-0 bg-black/40 backdrop-blur-sm z-40 transition-all duration-500 ease-in-out opacity-100"
          />
        )}
        {skeletonContent}
      </>
    );
  }

  const updateQuantity = (id, newQuantity) => {
    if (newQuantity <= 0) {
      removeItem(id);
      return;
    }
    setCartItems(items =>
      items.map(item =>
        item.id === id ? { ...item, quantity: newQuantity } : item
      )
    );
  };

  const removeItem = (id) => {
    setCartItems(items => items.filter(item => item.id !== id));
  };

  const content = (
    <div
      className={clsx(
        'h-full w-full max-w-md shadow-xl flex flex-col transition-all duration-300',
        isMinimal ? 'bg-gray-50' : 'bg-white',
        !inline && 'fixed right-0 top-0 z-50 transform ease-in-out duration-500',
        !inline && (isOpen ? 'translate-x-0' : 'translate-x-full'),
        inline && 'relative translate-x-0 border-l border-gray-100',
        className
      )}
      id={id}
    >
      {/* Header */}
      <div className={clsx("flex items-center justify-between p-6", isMinimal ? "bg-black text-white" : "border-b border-gray-200")}>
        <div>
          <h2 className={clsx("text-xl font-bold", isMinimal ? "text-white" : "text-gray-900")}>{title}</h2>
          <p className={clsx("text-xs mt-1 uppercase tracking-wider font-semibold", isMinimal ? "text-gray-400" : "text-gray-500")}>
            {cartItems.length} {cartItems.length === 1 ? 'Item' : 'Items'}
          </p>
        </div>
        {!inline && (
          <button
            onClick={onClose}
            className={clsx(
              "p-2 rounded-full transition-all duration-200 hover:rotate-90",
              isMinimal ? "hover:bg-gray-800 text-white" : "hover:bg-gray-100 text-gray-500"
            )}
            aria-label="Close cart"
          >
            <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
            </svg>
          </button>
        )}
      </div>

      {cartItems.length === 0 ? (
        <div className="flex-1 flex flex-col items-center justify-center p-8 text-center">
          <div className="w-20 h-20 bg-gray-50 rounded-full flex items-center justify-center mb-6 border border-gray-100">
            <svg
              className="w-10 h-10 text-gray-300"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={1.5}
                d="M16 11V7a4 4 0 00-8 0v4M5 9h14l1 12H4L5 9z"
              />
            </svg>
          </div>
          <h3 className="text-lg font-bold text-gray-900 mb-2">Your cart is empty</h3>
          <p className="text-gray-500 mb-8 max-w-[200px] mx-auto">Discover our collection and fill it with something special.</p>
          <button
            onClick={onClose}
            className="px-8 py-3 bg-black text-white font-bold text-sm rounded-none hover:bg-gray-800 transition-all duration-200 uppercase tracking-widest"
          >
            Continue Shopping
          </button>
        </div>
      ) : (
        <>
          {/* Cart Items */}
          <div className="flex-1 overflow-y-auto p-6 space-y-6 scrollbar-hide">
            {cartItems.map((item) => (
              <div
                key={item.id}
                className={clsx("flex gap-4 group", isMinimal && "pb-6 border-b border-gray-200 last:border-b-0")}
              >
                {/* Product Image */}
                <div className={clsx(
                  "flex-shrink-0 relative overflow-hidden bg-gray-50",
                  isMinimal ? "rounded-none border-2 border-black" : "rounded-lg border border-gray-100"
                )}>
                  <img
                    src={item.image || '/api/placeholder/100/100'}
                    alt={item.name}
                    className="w-24 h-24 object-cover transform transition-transform duration-500 group-hover:scale-110"
                  />
                </div>

                {/* Product Details */}
                <div className="flex-1 min-w-0 flex flex-col">
                  <div className="flex justify-between items-start mb-1">
                    <h3 className={clsx(
                      "text-sm line-clamp-2 leading-snug group-hover:text-primary-600 transition-colors",
                      isMinimal ? "font-black uppercase italic" : "font-bold text-gray-900"
                    )}>{item.name}</h3>
                    <button
                      onClick={() => removeItem(item.id)}
                      className="p-1 text-gray-300 hover:text-red-500 transition-colors flex-shrink-0 ml-2"
                      aria-label="Remove item"
                    >
                      <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                      </svg>
                    </button>
                  </div>

                  {item.variant && (
                    <p className="text-xs text-gray-400 mb-auto uppercase tracking-widest font-bold">{item.variant}</p>
                  )}

                  {/* Price and Quantity */}
                  <div className="flex justify-between items-end mt-4">
                    <div className={clsx(
                      "flex items-center p-1 border",
                      isMinimal ? "bg-white border-black" : "bg-gray-50 rounded-full border-gray-100"
                    )}>
                      <button
                        onClick={() => updateQuantity(item.id, item.quantity - 1)}
                        className={clsx(
                          "w-7 h-7 flex items-center justify-center transition-all duration-200",
                          isMinimal ? "text-black hover:bg-gray-100" : "text-gray-500 hover:text-black hover:bg-white rounded-full"
                        )}
                        aria-label="Decrease quantity"
                      >
                        <svg className="w-3 h-3" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2.5} d="M20 12H4" />
                        </svg>
                      </button>
                      <span className="w-8 text-center text-xs font-bold text-gray-900">{item.quantity}</span>
                      <button
                        onClick={() => updateQuantity(item.id, item.quantity + 1)}
                        className={clsx(
                          "w-7 h-7 flex items-center justify-center transition-all duration-200",
                          isMinimal ? "text-black hover:bg-gray-100" : "text-gray-500 hover:text-black hover:bg-white rounded-full"
                        )}
                        aria-label="Increase quantity"
                      >
                        <svg className="w-3 h-3" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2.5} d="M12 6v6m0 0v6m0-6h6m-6 0H6" />
                        </svg>
                      </button>
                    </div>
                    <span className={clsx("text-sm font-black text-gray-900", isMinimal && "italic")}>
                      ${(item.price * item.quantity).toFixed(2)}
                    </span>
                  </div>
                </div>
              </div>
            ))}
          </div>

          {/* Order Summary & Actions */}
          <div className={clsx("p-8 space-y-6", isMinimal ? "bg-white border-t-4 border-black" : "bg-gray-50/50 border-t border-gray-100")}>
            {/* Order Summary */}
            <div className="space-y-3">
              {showSubtotal && (
                <div className="flex justify-between text-sm">
                  <span className="text-gray-500 font-medium uppercase tracking-widest text-[10px]">Subtotal</span>
                  <span className="text-gray-900 font-bold">${subtotal.toFixed(2)}</span>
                </div>
              )}

              {showTax && (
                <div className="flex justify-between text-sm">
                  <span className="text-gray-500 font-medium uppercase tracking-widest text-[10px]">Tax</span>
                  <span className="text-gray-900 font-bold">${tax.toFixed(2)}</span>
                </div>
              )}

              {showShipping && (
                <div className="flex justify-between text-sm">
                  <span className="text-gray-500 font-medium uppercase tracking-widest text-[10px]">Shipping</span>
                  <span className="text-primary-600 font-bold">
                    {shipping === 0 ? 'FREE' : `$${shipping.toFixed(2)}`}
                  </span>
                </div>
              )}

              <div className={clsx("pt-4 mt-4 border-t", isMinimal ? "border-black border-dashed" : "border-gray-200")}>
                <div className="flex justify-between items-center">
                  <span className="text-base font-bold text-gray-900 uppercase tracking-widest">Total</span>
                  <span className={clsx("font-black text-gray-900", isMinimal ? "text-3xl italic" : "text-2xl")}>${total.toFixed(2)}</span>
                </div>
              </div>
            </div>

            {/* Action Buttons */}
            <div className="space-y-3">
              {showCheckoutButton && (
                <button className={clsx(
                  "w-full px-8 py-4 font-bold text-sm uppercase tracking-widest shadow-lg transition-all duration-300",
                  isMinimal ? "bg-black text-white hover:bg-primary-600 rounded-none shadow-[4px_4px_0px_0px_rgba(59,130,246,1)]" : "bg-black text-white hover:bg-gray-800 rounded-none hover:shadow-xl"
                )}>
                  Checkout Now
                </button>
              )}

              {showContinueShopping && (
                <button
                  onClick={onClose}
                  className={clsx(
                    "w-full px-8 py-4 font-bold text-sm uppercase tracking-widest transition-all duration-300 border-2",
                    isMinimal ? "bg-white text-black border-black hover:bg-gray-100 rounded-none" : "bg-white text-gray-700 border-gray-300 rounded-none hover:bg-gray-50"
                  )}
                >
                  Continue Shopping
                </button>
              )}
            </div>
          </div>
        </>
      )}
    </div>
  );

  if (inline) {
    return content;
  }

  return (
    <>
      {/* Overlay */}
      {isOpen && (
        <div
          className="fixed inset-0 bg-black/40 backdrop-blur-sm z-40 transition-all duration-500 ease-in-out opacity-100"
          onClick={onClose}
        />
      )}
      {content}
    </>
  );
};