import React, { useState, useEffect } from 'react';
import { clsx } from 'clsx';

export const CheckoutPaymentSection = ({ 
  data = {}, 
  selectedId, 
  onSelect, 
  variant = 'standard', 
  className,
  loading = false
}) => {
  const [timedOut, setTimedOut] = useState(false);

  // Auto-show content after 2 seconds if loading is true
  useEffect(() => {
    if (loading) {
      setTimedOut(false);
      const timer = setTimeout(() => {
        setTimedOut(true);
      }, 2000);
      return () => clearTimeout(timer);
    }
  }, [loading]);

  const shouldShowSkeleton = loading && !timedOut;
  const { title, methods = [] } = data;

  const containerClass = clsx(
    "checkout-payment-section",
    variant === 'grid' ? "grid grid-cols-1 md:grid-cols-2 gap-4" : "space-y-3",
    variant === 'premium' && "space-y-6"
  );

  // Show skeleton loading state
  if (shouldShowSkeleton) {
    return (
      <section className={className}>
        <h2 className={clsx(
          "mb-4 uppercase",
          variant === 'standard' && "text-sm font-bold tracking-widest text-gray-900",
          variant === 'modern' && "text-2xl font-black tracking-tight text-gray-900",
          variant === 'compact' && "text-xs font-black text-gray-400 tracking-[0.2em]",
          variant === 'premium' && "text-[10px] uppercase tracking-widest text-neutral-600 font-bold block ml-1"
        )}>
          {data.title || 'Payment Method'}
        </h2>

        <div className={containerClass}>
          {[1, 2].map((item) => (
            <div key={item} className="checkout-skeleton h-16 w-full rounded-lg"></div>
          ))}
        </div>
      </section>
    );
  }

  return (
    <section className={className}>
      <h2 className={clsx(
        "mb-4 uppercase",
        variant === 'standard' && "text-sm font-bold tracking-widest text-gray-900",
        variant === 'modern' && "text-2xl font-black tracking-tight text-gray-900",
        variant === 'compact' && "text-xs font-black text-gray-400 tracking-[0.2em]",
        variant === 'premium' && "text-[10px] uppercase tracking-widest text-neutral-600 font-bold block ml-1"
      )}>
        {title || 'Payment Method'}
      </h2>
      
      <div className={containerClass}>
        {methods.map((method) => {
          const isSelected = selectedId === method.id;

          if (variant === 'compact') {
            return (
              <label 
                key={method.id} 
                className={clsx(
                  "flex items-center p-4 border rounded-md cursor-pointer transition-colors",
                  isSelected ? "bg-gray-50 border-black" : "hover:bg-gray-50 border-gray-200"
                )}
              >
                <input 
                  type="radio" 
                  name="payment" 
                  className="text-black focus:ring-black"
                  checked={isSelected}
                  onChange={() => onSelect && onSelect(method.id)}
                />
                <span className="ml-3 font-medium">{method.title}</span>
              </label>
            );
          }

          return (
            <div 
              key={method.id} 
              onClick={() => onSelect && onSelect(method.id)}
              className={clsx(
                "flex justify-between items-center cursor-pointer transition-all",
                variant === 'standard' && clsx(
                  "border p-6 rounded-sm",
                  isSelected ? "border-black bg-gray-50" : "border-gray-300 hover:border-black"
                ),
                variant === 'modern' && clsx(
                  "p-8 rounded-2xl border-2 shadow-sm transition-all",
                  isSelected ? "border-black shadow-md" : "border-transparent bg-white hover:border-black hover:shadow-md"
                ),
                variant === 'grid' && clsx(
                  "border-2 p-6 rounded-2xl flex-col items-start space-y-4 transition-all",
                  isSelected ? "border-blue-600 bg-blue-50 shadow-inner" : "border-neutral-100 hover:border-blue-600 hover:bg-blue-50"
                ),
                variant === 'premium' && clsx(
                  "border-b p-6 group transition-colors",
                  isSelected ? "border-white bg-neutral-900" : "border-neutral-800 hover:border-white"
                )
              )}
            >
              <div>
                <p className={clsx(
                  "font-bold text-sm transition-colors",
                  variant === 'premium' && clsx(
                    "text-xl font-light",
                    isSelected ? "text-white" : "text-neutral-400 group-hover:text-white"
                  )
                )}>
                  {method.title}
                </p>
                <p className="text-xs text-gray-500 mt-1">{method.subtitle}</p>
              </div>
              <div className="flex space-x-2 items-center">
                {method.logos && method.logos.map((logo, lIdx) => (
                  <img 
                    key={lIdx} 
                    src={logo.src} 
                    alt={logo.alt} 
                    className={clsx(
                      "h-4 transition-opacity", 
                      variant === 'premium' && (isSelected ? "invert opacity-100" : "invert opacity-50 group-hover:opacity-100")
                    )} 
                  />
                ))}
                {method.logoSrc && (
                  <img 
                    src={method.logoSrc} 
                    alt={method.logoAlt} 
                    className={clsx(
                      variant === 'grid' ? "h-8" : "h-6", 
                      "transition-opacity",
                      variant === 'premium' && (isSelected ? "invert opacity-100" : "invert opacity-50 group-hover:opacity-100")
                    )} 
                  />
                )}
              </div>
            </div>
          );
        })}
      </div>
    </section>
  );
};
