import clsx from 'clsx';
import '../../styles/product.css';
import { ProductLayout } from '../ProductLayout/ProductLayout';
import { ProductStandard } from '../ProductStandard/ProductStandard';
import { ProductModern } from '../ProductModern/ProductModern';

/**
 * ProductRelated Component
 * Container component for related products that supports both data-driven and children-based usage
 * @param {Object} props - Component props
 * @param {Object} props.data - Related products data (when not using children)
 * @param {string} [props.data.title='Related Products'] - Section title
 * @param {Array} props.data.products - Array of related product objects
 * @param {string} [props.data.variant='default'] - Style variant from available component variants:
 *   ProductLayout: 'default', 'grid', 'card', 'minimal', 'featured', 'modern', 'bordered', 'gradient', 'shadow', 'ecommerce', 'pill', 'banner'
 *   ProductStandard: 'split-color', 'beauty', 'arch-top', 'horizontal', 'profile-lesson'
 *   ProductModern: 'glassmorphism', 'minimal', 'gradient'
 * @param {string} [props.data.component='auto'] - Component to use: 'auto' | 'layout' | 'standard' | 'modern'. When 'auto', component is selected based on variant
 * @param {number} [props.data.columns=4] - Number of columns for grid layout (1-4)
 * @param {boolean} [props.data.showPrice=true] - Show product prices (inherited from component behavior)
 * @param {boolean} [props.data.showRating=true] - Show product ratings
 * @param {React.ReactNode} props.children - Child product components to render (alternative to data prop)
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute for the section element
 */
export const ProductRelated = ({ data, className, id, children }) => {
  // If children are provided, render them directly with optional wrapper
  if (children) {
    return (
      <section
        id={id}
        className={clsx(
          'w-full py-12 sm:py-16 lg:py-20 px-4 sm:px-6 lg:px-8',
          'bg-white',
          className
        )}
      >
        <div className="max-w-7xl mx-auto w-full">
          {children}
        </div>
      </section>
    );
  }

  // Safety check for data when not using children
  if (!data || typeof data !== 'object') {
    console.error('ProductRelated: data prop is required when not using children');
    return null;
  }

  const {
    title = 'Related Products',
    products = [],
    variant = 'default',
    component = 'auto',
    columns = 4,
    showPrice = true,
    showRating = true
  } = data;

  if (products.length === 0) {
    return null;
  }

  // Define available variants for each component
  const layoutVariants = ['default', 'grid', 'card', 'minimal', 'featured', 'modern', 'bordered', 'gradient', 'shadow', 'ecommerce', 'pill', 'banner'];
  const standardVariants = ['split-color', 'beauty', 'arch-top', 'horizontal', 'profile-lesson'];
  const modernVariants = ['glassmorphism', 'minimal', 'gradient'];

  // Determine which component to use
  let selectedComponent = component;
  if (selectedComponent === 'auto') {
    if (layoutVariants.includes(variant)) {
      selectedComponent = 'layout';
    } else if (standardVariants.includes(variant)) {
      selectedComponent = 'standard';
    } else if (modernVariants.includes(variant)) {
      selectedComponent = 'modern';
    } else {
      // Fallback to layout for unknown variants
      selectedComponent = 'layout';
    }
  }

  // Create common data object
  const commonData = {
    title,
    products,
    variant,
    columns,
    currency: '$'
  };

  // Add component-specific props
  if (selectedComponent === 'layout') {
    commonData.showRating = showRating;
    commonData.showBadge = true;
  }

  // Render the appropriate component
  if (selectedComponent === 'layout') {
    return (
      <ProductLayout
        id={id}
        data={commonData}
        className={clsx(`product-related-${variant}`, className)}
      />
    );
  }

  if (selectedComponent === 'standard') {
    return (
      <ProductStandard
        id={id}
        data={commonData}
        className={clsx(`product-related-${variant}`, className)}
      />
    );
  }

  if (selectedComponent === 'modern') {
    return (
      <ProductModern
        id={id}
        data={commonData}
        className={clsx(`product-related-${variant}`, className)}
      />
    );
  }

  return null;
};
