import { useState, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/blog.css';

/**
 * BlogCard Component - Modern blog article card layouts
 * @param {Object} props - Component props
 * @param {Object} props.data - Blog card configuration data
 * @param {string} props.data.title - Article title
 * @param {string} [props.data.description] - Article description/excerpt
 * @param {string} [props.data.image] - Image URL for the article
 * @param {string} [props.data.author] - Author name
 * @param {string} [props.data.authorImage] - Author avatar image URL
 * @param {string} [props.data.date] - Publication date
 * @param {string} [props.data.category] - Article category/tag
 * @param {string} [props.data.readTime] - Estimated read time
 * @param {number} [props.data.likes] - Number of likes
 * @param {number} [props.data.views] - Number of views
 * @param {number} [props.data.comments] - Number of comments
 * @param {string} [props.data.href] - Link URL for the article
 * @param {Object|string} [props.data.button] - Button configuration (object with text and href) or button text string
 * @param {string} [props.data.button.text] - Button text
 * @param {string} [props.data.button.href] - Button link URL
 * @param {string} [props.data.variant='default'] - Style variant: 'default' | 'with-image' | 'with-footer' | 'vertical' | 'gradient-border' | 'background-image' | 'minimal' | 'modern' | 'elegant' | 'featured-gradient' | 'button' | 'background-metrics'
 * @param {string} [props.data.theme='light'] - Theme: 'light' | 'dark'
 * @param {boolean} [props.data.hoverEffect=true] - Enable hover effects
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute
 * @param {boolean} [props.loader=false] - Show loading state
 */
export const BlogCard = ({ data, className, id, loader = false }) => {
  // Safety check for data
  if (!data || typeof data !== 'object') {
    console.error('BlogCard: data prop is required and must be an object');
    return null;
  }

  const {
    title,
    description,
    image,
    author,
    authorImage,
    date,
    category,
    readTime,
    likes,
    views,
    comments,
    href = '#',
    button,
    variant = 'default',
    theme = 'light',
    hoverEffect = true,
    className: dataClassName,
  } = data;

  // Normalize button prop - can be string or object
  const buttonConfig = typeof button === 'string' 
    ? { text: button, href: href } 
    : button;

  const [isHovered, setIsHovered] = useState(false);
  const [showLoader, setShowLoader] = useState(loader);

  // Auto-hide loader after 2 seconds when loader prop is true
  useEffect(() => {
    if (loader) {
      const timer = setTimeout(() => {
        setShowLoader(false);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowLoader(false);
    }
  }, [loader]);

  // Loading skeleton
  if (showLoader) {
    return (
      <div className={clsx('blog-card-skeleton', className)} id={id}>
        <div className="skeleton-image"></div>
        <div className="skeleton-content">
          <div className="skeleton-line skeleton-line-title"></div>
          <div className="skeleton-line"></div>
          <div className="skeleton-line skeleton-line-short"></div>
        </div>
      </div>
    );
  }

  const handleClick = () => {
    if (href && href !== '#') {
      window.location.href = href;
    }
  };

  const cardClasses = clsx(
    'blog-card',
    `blog-card-${variant}`,
    `blog-card-${theme}`,
    hoverEffect && 'blog-card-hover',
    isHovered && 'blog-card-hovered',
    className,
    dataClassName
  );

  // Default variant - Clean article card
  if (variant === 'default') {
    return (
      <article
        className={cardClasses}
        id={id}
        onMouseEnter={() => setIsHovered(true)}
        onMouseLeave={() => setIsHovered(false)}
        onClick={handleClick}
      >
        {image && (
          <div className="blog-card-image">
            <img src={image} alt={title} />
          </div>
        )}
        <div className="blog-card-content">
          {category && (
            <span className="blog-card-category">{category}</span>
          )}
          <h3 className="blog-card-title">{title}</h3>
          {description && (
            <p className="blog-card-description">{description}</p>
          )}
          <div className="blog-card-meta">
            {author && (
              <span className="blog-card-author">{author}</span>
            )}
            {date && (
              <span className="blog-card-date">{date}</span>
            )}
          </div>
        </div>
      </article>
    );
  }

  // With Image variant - Article card with prominent image
  if (variant === 'with-image') {
    return (
      <article
        className={cardClasses}
        id={id}
        onMouseEnter={() => setIsHovered(true)}
        onMouseLeave={() => setIsHovered(false)}
        onClick={handleClick}
      >
        {image && (
          <div className="blog-card-image-wrapper">
            <img src={image} alt={title} />
          </div>
        )}
        <div className="blog-card-content">
          {category && (
            <span className="blog-card-category-badge">{category}</span>
          )}
          <h3 className="blog-card-title">{title}</h3>
          {description && (
            <p className="blog-card-description">{description}</p>
          )}
          {author && (
            <div className="blog-card-footer">
              {authorImage && (
                <img src={authorImage} alt={author} className="blog-card-author-avatar" />
              )}
              <div>
                <p className="blog-card-author-name">{author}</p>
                {date && <p className="blog-card-date-small">{date}</p>}
              </div>
            </div>
          )}
        </div>
      </article>
    );
  }

  // With Footer variant - Card with detailed footer
  if (variant === 'with-footer') {
    return (
      <article
        className={cardClasses}
        id={id}
        onMouseEnter={() => setIsHovered(true)}
        onMouseLeave={() => setIsHovered(false)}
        onClick={handleClick}
      >
        {image && (
          <div className="blog-card-image-wrapper">
            <img src={image} alt={title} />
          </div>
        )}
        <div className="blog-card-content">
          {category && (
            <span className="blog-card-category-tag">{category}</span>
          )}
          <h3 className="blog-card-title">{title}</h3>
          {author && (
            <div className="blog-card-author-section">
              {authorImage && (
                <img src={authorImage} alt={author} className="blog-card-author-avatar-small" />
              )}
              <span className="blog-card-author-name">{author}</span>
            </div>
          )}
        </div>
        <div className="blog-card-footer-detailed">
          {date && <span className="blog-card-footer-text">{date}</span>}
          {likes !== undefined && (
            <span className="blog-card-footer-text">{likes} people liked this</span>
          )}
        </div>
      </article>
    );
  }

  // Vertical variant - Horizontal article card (image left, content right)
  if (variant === 'vertical') {
    return (
      <article
        className={cardClasses}
        id={id}
        onMouseEnter={() => setIsHovered(true)}
        onMouseLeave={() => setIsHovered(false)}
        onClick={handleClick}
      >
        <div className="blog-card-vertical-wrapper">
          {image && (
            <div className="blog-card-image-vertical">
              <img src={image} alt={title} />
            </div>
          )}
          <div className="blog-card-content-vertical">
            {category && (
              <span className="blog-card-category-vertical">{category}</span>
            )}
            <h3 className="blog-card-title-vertical">{title}</h3>
            {author && date && (
              <div className="blog-card-meta-vertical">
                {authorImage && (
                  <img src={authorImage} alt={author} className="blog-card-author-avatar-vertical" />
                )}
                <span className="blog-card-author-vertical">{author}</span>
                <span className="blog-card-divider"></span>
                <span className="blog-card-date-vertical">{date}</span>
              </div>
            )}
          </div>
        </div>
      </article>
    );
  }

  // Gradient Border variant - Card with gradient border
  if (variant === 'gradient-border') {
    return (
      <article
        className={cardClasses}
        id={id}
        onMouseEnter={() => setIsHovered(true)}
        onMouseLeave={() => setIsHovered(false)}
        onClick={handleClick}
      >
        <div className="blog-card-gradient-border">
          <div className="blog-card-inner">
            {category && (
              <span className="blog-card-category">{category}</span>
            )}
            <h3 className="blog-card-title">{title}</h3>
            {description && (
              <p className="blog-card-description">{description}</p>
            )}
          </div>
        </div>
      </article>
    );
  }

  // Background Image variant - Card with image as background
  if (variant === 'background-image') {
    const handleButtonClick = (e) => {
      e.stopPropagation();
      if (buttonConfig?.href && buttonConfig.href !== '#') {
        window.location.href = buttonConfig.href;
      }
    };

    return (
      <article
        className={cardClasses}
        id={id}
        onMouseEnter={() => setIsHovered(true)}
        onMouseLeave={() => setIsHovered(false)}
        onClick={handleClick}
        style={image ? { backgroundImage: `url(${image})` } : {}}
      >
        <div className="blog-card-background-overlay">
          <div className="blog-card-content-overlay">
            {category && (
              <span className="blog-card-category-light">{category}</span>
            )}
            <h3 className="blog-card-title-light">{title}</h3>
            {buttonConfig && buttonConfig.text && (
              <button
                onClick={handleButtonClick}
                className="blog-card-background-button"
              >
                {buttonConfig.text}
              </button>
            )}
            <div className="blog-card-author-info">
              {authorImage && (
                <img src={authorImage} alt={author} className="blog-card-author-avatar-circle" />
              )}
              <div>
                {author && <p className="blog-card-author-name-light">{author}</p>}
                {likes !== undefined && (
                  <p className="blog-card-likes">{likes}</p>
                )}
              </div>
            </div>
          </div>
        </div>
      </article>
    );
  }

  // Minimal variant - Clean minimal design
  if (variant === 'minimal') {
    return (
      <article
        className={cardClasses}
        id={id}
        onMouseEnter={() => setIsHovered(true)}
        onMouseLeave={() => setIsHovered(false)}
        onClick={handleClick}
      >
        {image && (
          <div className="blog-card-image-minimal">
            <img src={image} alt={title} />
          </div>
        )}
        <div className="blog-card-content-minimal">
          {date && <span className="blog-card-date-minimal">{date}</span>}
          <h3 className="blog-card-title-minimal">{title}</h3>
          {description && (
            <p className="blog-card-description-minimal">{description}</p>
          )}
        </div>
      </article>
    );
  }

  // Modern variant - Contemporary design with shadows
  if (variant === 'modern') {
    return (
      <article
        className={cardClasses}
        id={id}
        onMouseEnter={() => setIsHovered(true)}
        onMouseLeave={() => setIsHovered(false)}
        onClick={handleClick}
      >
        {image && (
          <div className="blog-card-image-modern">
            <img src={image} alt={title} />
            {category && (
              <span className="blog-card-category-modern">{category}</span>
            )}
          </div>
        )}
        <div className="blog-card-content-modern">
          <h3 className="blog-card-title-modern">{title}</h3>
          {description && (
            <p className="blog-card-description-modern">{description}</p>
          )}
          <div className="blog-card-footer-modern">
            <div className="blog-card-author-modern">
              {authorImage && (
                <img src={authorImage} alt={author} className="blog-card-author-avatar-modern" />
              )}
              <div>
                {author && <span className="blog-card-author-name-modern">{author}</span>}
                {date && <span className="blog-card-date-modern">{date}</span>}
              </div>
            </div>
            {readTime && (
              <span className="blog-card-read-time">{readTime}</span>
            )}
          </div>
        </div>
      </article>
    );
  }

  // Elegant variant - Sophisticated design
  if (variant === 'elegant') {
    return (
      <article
        className={cardClasses}
        id={id}
        onMouseEnter={() => setIsHovered(true)}
        onMouseLeave={() => setIsHovered(false)}
        onClick={handleClick}
      >
        {image && (
          <div className="blog-card-image-elegant">
            <img src={image} alt={title} />
          </div>
        )}
        <div className="blog-card-content-elegant">
          <div className="blog-card-header-elegant">
            {category && (
              <span className="blog-card-category-elegant">{category}</span>
            )}
            {date && <span className="blog-card-date-elegant">{date}</span>}
          </div>
          <h3 className="blog-card-title-elegant">{title}</h3>
          {description && (
            <p className="blog-card-description-elegant">{description}</p>
          )}
          <div className="blog-card-footer-elegant">
            {author && (
              <div className="blog-card-author-elegant">
                {authorImage && (
                  <img src={authorImage} alt={author} className="blog-card-author-avatar-elegant" />
                )}
                <span className="blog-card-author-name-elegant">{author}</span>
              </div>
            )}
            {readTime && (
              <span className="blog-card-read-time-elegant">{readTime}</span>
            )}
          </div>
        </div>
      </article>
    );
  }

  // Featured Gradient variant - Card with gradient overlay bar on image
  if (variant === 'featured-gradient') {
    return (
      <article
        className={cardClasses}
        id={id}
        onMouseEnter={() => setIsHovered(true)}
        onMouseLeave={() => setIsHovered(false)}
        onClick={handleClick}
      >
        {image && (
          <div className="blog-card-image-featured-gradient">
            <img src={image} alt={title} />
            <div className="blog-card-gradient-overlay">
              {author && <span className="blog-card-gradient-author">{author}</span>}
              {date && <span className="blog-card-gradient-date">{date}</span>}
            </div>
          </div>
        )}
        <div className="blog-card-content-featured-gradient">
          <h3 className="blog-card-title-featured-gradient">{title}</h3>
          {description && (
            <p className="blog-card-description-featured-gradient">{description}</p>
          )}
        </div>
      </article>
    );
  }

  // Background Metrics variant - Background image with title, author, views, and comments
  if (variant === 'background-metrics') {
    return (
      <article
        className={cardClasses}
        id={id}
        onMouseEnter={() => setIsHovered(true)}
        onMouseLeave={() => setIsHovered(false)}
        onClick={handleClick}
        style={image ? { backgroundImage: `url(${image})` } : {}}
      >
        <div className="blog-card-background-metrics-overlay">
          <div className="blog-card-content-metrics">
            <h3 className="blog-card-title-metrics">{title}</h3>
            <div className="blog-card-meta-metrics">
              {author && (
                <span className="blog-card-author-metrics">{author}</span>
              )}
              <div className="blog-card-engagement-metrics">
                {views !== undefined && (
                  <span className="blog-card-views-metrics">
                    <svg className="blog-card-icon-metrics" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 12a3 3 0 11-6 0 3 3 0 016 0z" />
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M2.458 12C3.732 7.943 7.523 5 12 5c4.478 0 8.268 2.943 9.542 7-1.274 4.057-5.064 7-9.542 7-4.477 0-8.268-2.943-9.542-7z" />
                    </svg>
                    {views}
                  </span>
                )}
                {comments !== undefined && (
                  <span className="blog-card-comments-metrics">
                    <svg className="blog-card-icon-metrics" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 12h.01M12 12h.01M16 12h.01M21 12c0 4.418-4.03 8-9 8a9.863 9.863 0 01-4.255-.949L3 20l1.395-3.72C3.512 15.042 3 13.574 3 12c0-4.418 4.03-8 9-8s9 3.582 9 8z" />
                    </svg>
                    {comments}
                  </span>
                )}
              </div>
            </div>
          </div>
        </div>
      </article>
    );
  }

  // Fallback to default
  return (
    <article className={cardClasses} id={id}>
      <h3>{title}</h3>
      {description && <p>{description}</p>}
    </article>
  );
};