import { useState, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/product.css';

/**
 * ProductDescription Component - Product description with title, subtitle, description, and button
 * @param {Object} props - Component props
 * @param {Object} props.data - Description configuration data
 * @param {string} [props.data.variant='default'] - Style variant: 'default' | 'modern' | 'minimal' | 'compact' | 'icon' | 'dark' | 'dark-gray'
 * @param {string} [props.data.title] - Product title
 * @param {string} [props.data.subtitle] - Product subtitle
 * @param {string} [props.data.description] - Product description
 * @param {string} [props.data.buttonText='Learn More'] - Button text
 * @param {string} [props.data.buttonVariant='primary'] - Button variant: 'primary' | 'secondary' | 'outline'
 * @param {Function} [props.onButtonClick] - Callback when button is clicked
 * @param {boolean} [props.loading=false] - Show skeleton loader
 * @param {string} [props.className] - Additional CSS classes
 */
export const ProductDescription = ({ 
  data = {}, 
  onButtonClick,
  loading = false,
  className 
}) => {
  const {
    variant = 'default',
    title = 'Product Title',
    subtitle,
    description = 'Product description goes here. This is a detailed description of the product features and benefits.',
    buttonText = 'Learn More',
    buttonVariant = 'primary',
  } = data;

  const [showContent, setShowContent] = useState(!loading);

  useEffect(() => {
    if (loading) {
      setShowContent(false);
      const timer = setTimeout(() => {
        setShowContent(true);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowContent(true);
    }
  }, [loading]);

  const handleButtonClick = () => {
    if (onButtonClick) {
      onButtonClick();
    }
  };

  // Container classes
  const containerClasses = {
    default: 'w-full',
    modern: 'w-full',
    minimal: 'w-full',
    compact: 'w-full',
    icon: 'w-full',
    dark: 'w-full',
    'dark-gray': 'w-full',
  };

  // Title classes
  const titleClasses = {
    default: clsx(
      'text-2xl sm:text-3xl lg:text-4xl font-bold mb-3 sm:mb-4',
      'text-gray-900'
    ),
    modern: clsx(
      'text-3xl sm:text-4xl lg:text-5xl font-extrabold mb-4 sm:mb-5',
      'text-gray-900 bg-gradient-to-r from-gray-900 to-gray-700 bg-clip-text text-transparent'
    ),
    minimal: clsx(
      'text-xl sm:text-2xl font-semibold mb-2 sm:mb-3',
      'text-gray-900'
    ),
    compact: clsx(
      'text-lg sm:text-xl font-bold mb-2',
      'text-gray-900'
    ),
    icon: clsx(
      'text-2xl sm:text-3xl font-bold mb-3 sm:mb-4',
      'text-gray-900'
    ),
    dark: clsx(
      'text-2xl sm:text-3xl lg:text-4xl font-bold mb-3 sm:mb-4',
      'text-white'
    ),
    'dark-gray': clsx(
      'text-2xl sm:text-3xl lg:text-4xl font-bold mb-3 sm:mb-4',
      'text-gray-100'
    ),
  };

  // Subtitle classes
  const subtitleClasses = {
    default: clsx(
      'text-sm sm:text-base font-medium mb-2 sm:mb-3',
      'text-primary-600'
    ),
    modern: clsx(
      'text-base sm:text-lg font-semibold mb-3 sm:mb-4',
      'text-primary-600 uppercase tracking-wide'
    ),
    minimal: clsx(
      'text-xs sm:text-sm font-medium mb-1 sm:mb-2',
      'text-primary-600'
    ),
    compact: clsx(
      'text-xs sm:text-sm font-medium mb-1.5',
      'text-primary-600'
    ),
    icon: clsx(
      'text-sm sm:text-base font-medium mb-2 sm:mb-3',
      'text-primary-600'
    ),
    dark: clsx(
      'text-sm sm:text-base font-medium mb-2 sm:mb-3',
      'text-primary-400'
    ),
    'dark-gray': clsx(
      'text-sm sm:text-base font-medium mb-2 sm:mb-3',
      'text-gray-400'
    ),
  };

  // Description classes
  const descriptionClasses = {
    default: clsx(
      'text-sm sm:text-base text-gray-600 mb-4 sm:mb-6',
      'leading-relaxed'
    ),
    modern: clsx(
      'text-base sm:text-lg text-gray-700 mb-6 sm:mb-8',
      'leading-relaxed'
    ),
    minimal: clsx(
      'text-sm text-gray-600 mb-3 sm:mb-4',
      'leading-relaxed'
    ),
    compact: clsx(
      'text-xs sm:text-sm text-gray-600 mb-3',
      'leading-relaxed'
    ),
    icon: clsx(
      'text-sm sm:text-base text-gray-600 mb-4 sm:mb-6',
      'leading-relaxed'
    ),
    dark: clsx(
      'text-sm sm:text-base text-gray-300 mb-4 sm:mb-6',
      'leading-relaxed'
    ),
    'dark-gray': clsx(
      'text-sm sm:text-base text-gray-400 mb-4 sm:mb-6',
      'leading-relaxed'
    ),
  };

  // Button classes
  const getButtonClasses = (btnVariant) => {
    const baseClasses = {
      default: clsx(
        'px-4 sm:px-6 py-2 sm:py-2.5 text-sm sm:text-base rounded-lg',
        'font-medium transition-all duration-200',
        'flex items-center justify-center gap-2'
      ),
      modern: clsx(
        'px-6 sm:px-8 py-3 sm:py-3.5 text-base sm:text-lg rounded-xl',
        'font-semibold transition-all duration-300 shadow-md',
        'flex items-center justify-center gap-2'
      ),
      minimal: clsx(
        'px-3 sm:px-4 py-1.5 sm:py-2 text-sm border-b-2',
        'font-medium transition-all duration-200',
        'flex items-center justify-center gap-2 bg-transparent'
      ),
      compact: clsx(
        'px-3 sm:px-4 py-1.5 text-xs sm:text-sm rounded-md',
        'font-medium transition-all duration-200',
        'flex items-center justify-center gap-1.5'
      ),
      icon: clsx(
        'p-2 sm:p-2.5 rounded-lg',
        'transition-all duration-300 shadow-md',
        'flex items-center justify-center'
      ),
      dark: clsx(
        'px-4 sm:px-6 py-2 sm:py-2.5 text-sm sm:text-base rounded-lg',
        'font-medium transition-all duration-200',
        'flex items-center justify-center gap-2'
      ),
      'dark-gray': clsx(
        'px-4 sm:px-6 py-2 sm:py-2.5 text-sm sm:text-base rounded-lg',
        'font-medium transition-all duration-200',
        'flex items-center justify-center gap-2'
      ),
    };

    const variantClasses = {
      primary: {
        default: 'bg-primary-600 hover:bg-primary-700 text-white',
        modern: 'bg-primary-600 hover:bg-primary-700 text-white hover:shadow-lg',
        minimal: 'border-primary-600 text-primary-600 hover:border-primary-700 hover:text-primary-700',
        compact: 'bg-primary-600 hover:bg-primary-700 text-white',
        icon: 'bg-primary-500 hover:bg-primary-600 text-white hover:shadow-lg',
        dark: 'bg-primary-500 hover:bg-primary-600 text-white',
        'dark-gray': 'bg-gray-500 hover:bg-gray-600 text-white',
      },
      secondary: {
        default: 'bg-gray-200 hover:bg-gray-300 text-gray-900',
        modern: 'bg-gray-200 hover:bg-gray-300 text-gray-900 hover:shadow-lg',
        minimal: 'border-gray-600 text-gray-600 hover:border-gray-700 hover:text-gray-700',
        compact: 'bg-gray-200 hover:bg-gray-300 text-gray-900',
        icon: 'bg-gray-500 hover:bg-gray-600 text-white hover:shadow-lg',
        dark: 'bg-gray-700 hover:bg-gray-600 text-white',
        'dark-gray': 'bg-gray-800 hover:bg-gray-700 text-white',
      },
      outline: {
        default: 'border-2 border-primary-600 text-primary-600 hover:bg-primary-50',
        modern: 'border-2 border-primary-600 text-primary-600 hover:bg-primary-50 hover:shadow-lg',
        minimal: 'border-b-2 border-primary-600 text-primary-600 hover:border-primary-700 hover:text-primary-700',
        compact: 'border border-primary-600 text-primary-600 hover:bg-primary-50',
        icon: 'border-2 border-primary-500 text-primary-500 hover:bg-primary-50 hover:shadow-lg',
        dark: 'border-2 border-primary-400 text-primary-400 hover:bg-primary-900',
        'dark-gray': 'border-2 border-gray-400 text-gray-400 hover:bg-gray-900',
      },
    };

    return clsx(
      baseClasses[variant] || baseClasses.default,
      variantClasses[btnVariant]?.[variant] || variantClasses.primary[variant] || variantClasses.primary.default
    );
  };

  const SkeletonLoader = () => {
    const isDark = variant === 'dark' || variant === 'dark-gray';
    const skeletonBg = isDark ? 'bg-gray-700' : 'bg-gray-200';

    return (
      <div className={clsx(containerClasses[variant] || containerClasses.default, className)}>
        {subtitle && (
          <div className={clsx('h-4 w-24 mb-3 rounded product-skeleton', skeletonBg)}></div>
        )}
        <div className={clsx('h-8 sm:h-10 w-3/4 mb-4 rounded product-skeleton', skeletonBg)}></div>
        <div className={clsx('h-4 w-full mb-2 rounded product-skeleton', skeletonBg)}></div>
        <div className={clsx('h-4 w-5/6 mb-4 rounded product-skeleton', skeletonBg)}></div>
        <div className={clsx('h-10 sm:h-12 w-32 rounded product-skeleton', skeletonBg)}></div>
      </div>
    );
  };

  if (!showContent) {
    return <SkeletonLoader />;
  }

  const ArrowIcon = () => (
    <svg className="w-4 h-4 sm:w-5 sm:h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 7l5 5m0 0l-5 5m5-5H6" />
    </svg>
  );

  return (
    <div className={clsx(containerClasses[variant] || containerClasses.default, className)}>
      {subtitle && (
        <p className={subtitleClasses[variant] || subtitleClasses.default}>
          {subtitle}
        </p>
      )}
      
      <h2 className={titleClasses[variant] || titleClasses.default}>
        {title}
      </h2>
      
      {description && (
        <p className={descriptionClasses[variant] || descriptionClasses.default}>
          {description}
        </p>
      )}
      
      {buttonText && (
        <button
          type="button"
          onClick={handleButtonClick}
          className={getButtonClasses(buttonVariant)}
        >
          {variant === 'icon' ? (
            <ArrowIcon />
          ) : (
            <>
              <span>{buttonText}</span>
              {variant !== 'minimal' && variant !== 'compact' && (
                <span className="flex-shrink-0">
                  <ArrowIcon />
                </span>
              )}
            </>
          )}
        </button>
      )}
    </div>
  );
};

