import { useState, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/blog.css';

/**
 * BlogComments Component - Comments section for blog posts
 * @param {Object} props - Component props
 * @param {Object} props.data - Comments configuration data
 * @param {Array} [props.data.comments=[]] - Array of comment objects
 * @param {number} [props.data.totalComments] - Total number of comments
 * @param {boolean} [props.data.allowNewComments=true] - Allow users to add new comments
 * @param {string} [props.data.variant='default'] - Style variant: 'default' | 'nested' | 'threaded' | 'minimal'
 * @param {string} [props.data.theme='light'] - Theme: 'light' | 'dark'
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute
 * @param {boolean} [props.loader=false] - Show loading state
 */
export const BlogComments = ({ data, className, id, loader = false }) => {
  if (!data || typeof data !== 'object') {
    console.error('BlogComments: data prop is required and must be an object');
    return null;
  }

  const [showLoader, setShowLoader] = useState(loader);
  const [comments, setComments] = useState(data.comments || []);
  const [newComment, setNewComment] = useState({ name: '', email: '', content: '' });

  useEffect(() => {
    if (loader) {
      const timer = setTimeout(() => {
        setShowLoader(false);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowLoader(false);
    }
  }, [loader]);

  useEffect(() => {
    setComments(data.comments || []);
  }, [data.comments]);

  const {
    totalComments,
    allowNewComments = true,
    variant = 'default',
    theme = 'light',
    className: dataClassName,
  } = data;

  const handleSubmit = (e) => {
    e.preventDefault();
    // Handle comment submission - this would typically be handled by parent component
    console.log('Comment submitted:', newComment);
    setNewComment({ name: '', email: '', content: '' });
  };

  const containerClasses = clsx(
    'blog-comments',
    `blog-comments-${variant}`,
    `blog-comments-${theme}`,
    className,
    dataClassName
  );

  // Loading skeleton
  if (showLoader) {
    return (
      <div className={containerClasses} id={id}>
        <div className="blog-comments-skeleton">
          <div className="skeleton-line skeleton-line-title"></div>
          <div className="blog-comments-skeleton-item">
            <div className="skeleton-avatar"></div>
            <div className="skeleton-content">
              <div className="skeleton-line"></div>
              <div className="skeleton-line"></div>
              <div className="skeleton-line skeleton-line-short"></div>
            </div>
          </div>
        </div>
      </div>
    );
  }

  // Render comment item
  const renderComment = (comment, index, depth = 0) => {
    return (
      <div key={index} className={clsx('blog-comment', depth > 0 && 'blog-comment-reply')}>
        <div className="blog-comment-header">
          {comment.avatar && (
            <img src={comment.avatar} alt={comment.author} className="blog-comment-avatar" />
          )}
          <div className="blog-comment-meta">
            <h4 className="blog-comment-author">{comment.author}</h4>
            {comment.date && (
              <span className="blog-comment-date">{comment.date}</span>
            )}
          </div>
        </div>
        <div className="blog-comment-content">
          {typeof comment.content === 'string' ? (
            <p>{comment.content}</p>
          ) : (
            comment.content
          )}
        </div>
        {comment.replies && comment.replies.length > 0 && variant !== 'minimal' && (
          <div className="blog-comment-replies">
            {comment.replies.map((reply, replyIndex) =>
              renderComment(reply, `${index}-${replyIndex}`, depth + 1)
            )}
          </div>
        )}
      </div>
    );
  };

  // Default variant
  if (variant === 'default' || variant === 'nested' || variant === 'threaded') {
    return (
      <div className={containerClasses} id={id}>
        <div className="blog-comments-header">
          <h2 className="blog-comments-title">
            {totalComments !== undefined ? `${totalComments} ` : ''}
            {totalComments === 1 ? 'Comment' : 'Comments'}
          </h2>
        </div>

        {comments.length > 0 ? (
          <div className="blog-comments-list">
            {comments.map((comment, index) => renderComment(comment, index))}
          </div>
        ) : (
          <div className="blog-comments-empty">
            <p>No comments yet. Be the first to comment!</p>
          </div>
        )}

        {allowNewComments && (
          <div className="blog-comments-form">
            <h3 className="blog-comments-form-title">Leave a Comment</h3>
            <form onSubmit={handleSubmit}>
              <div className="blog-comments-form-row">
                <input
                  type="text"
                  placeholder="Your Name"
                  className="blog-comments-form-input"
                  value={newComment.name}
                  onChange={(e) => setNewComment({ ...newComment, name: e.target.value })}
                  required
                />
                <input
                  type="email"
                  placeholder="Your Email"
                  className="blog-comments-form-input"
                  value={newComment.email}
                  onChange={(e) => setNewComment({ ...newComment, email: e.target.value })}
                  required
                />
              </div>
              <textarea
                placeholder="Your Comment"
                className="blog-comments-form-textarea"
                rows="5"
                value={newComment.content}
                onChange={(e) => setNewComment({ ...newComment, content: e.target.value })}
                required
              />
              <button type="submit" className="blog-comments-form-submit">
                Post Comment
              </button>
            </form>
          </div>
        )}
      </div>
    );
  }

  // Minimal variant
  if (variant === 'minimal') {
    return (
      <div className={containerClasses} id={id}>
        <div className="blog-comments-header-minimal">
          <h2 className="blog-comments-title-minimal">
            {totalComments !== undefined ? `${totalComments} ` : ''}Comments
          </h2>
        </div>

        {comments.length > 0 ? (
          <div className="blog-comments-list-minimal">
            {comments.map((comment, index) => (
              <div key={index} className="blog-comment-minimal">
                <div className="blog-comment-header-minimal">
                  <span className="blog-comment-author-minimal">{comment.author}</span>
                  {comment.date && (
                    <span className="blog-comment-date-minimal">  {comment.date}</span>
                  )}
                </div>
                <p className="blog-comment-content-minimal">{comment.content}</p>
              </div>
            ))}
          </div>
        ) : (
          <p className="blog-comments-empty-minimal">No comments yet.</p>
        )}
      </div>
    );
  }

  return null;
};