import { useState, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/faq.css';

/**
 * FaqHub Component
 * @param {Object} props - Component props
 * @param {Object} props.data - FAQ section configuration data
 * @param {string} [props.data.title] - Main heading text
 * @param {string} [props.data.subtitle] - Subtitle text displayed above title
 * @param {string} [props.data.description] - Description text displayed below title
 * @param {Array} props.data.items - Array of FAQ items with question and answer
 * @param {string} [props.data.alignment='center'] - Text alignment: 'left' | 'center' | 'right'
 * @param {string} [props.data.variant='default'] - FAQ style variant: 'default' | 'bordered' | 'minimal' | 'blue' | 'shadow' | 'colored' | 'outlined' | 'filled' | 'gradient'
 * @param {boolean} [props.data.allowMultipleOpen=false] - Allow multiple items to be open at once
 * @param {string} [props.data.className] - Additional CSS classes for the section
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute for the section element

 */
export const FaqHub = ({ data, className, id, loader = false }) => {
  // Safety check for data
  if (!data || typeof data !== 'object') {
    console.error('FaqHub: data prop is required and must be an object');
    return null;
  }

  const [showLoader, setShowLoader] = useState(loader);

  // Auto-hide loader after 2 seconds when loader prop is true
  useEffect(() => {
    if (loader) {
      const timer = setTimeout(() => {
        setShowLoader(false);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowLoader(false);
    }
  }, [loader]);

  // Show skeleton loader if loader prop is true
  if (showLoader) {
    const { alignment = 'center', variant = 'default', className: dataClassName } = data || {};
    const alignmentClasses = {
      left: 'text-left items-start',
      center: 'text-center items-center',
      right: 'text-right items-end',
    };
    const variantClasses = {
      default: 'bg-white',
      bordered: 'bg-white border border-gray-200',
      minimal: 'bg-transparent',
      blue: 'bg-white',
      shadow: 'bg-white',
      colored: 'bg-white',
      outlined: 'bg-white',
      filled: 'bg-white',
      gradient: 'bg-white',
    };

    return (
      <section
        id={id}
        className={clsx(
          'w-full py-12 sm:py-16 lg:py-20 px-4 sm:px-6 lg:px-8',
          variantClasses[variant],
          className,
          dataClassName
        )}
      >
        <div className="max-w-4xl mx-auto w-full">
          {/* Header skeleton */}
          <div className={clsx('mb-10 sm:mb-12 flex flex-col', alignmentClasses[alignment])}>
            <div className="h-4 bg-gray-300 rounded w-24 mb-3 mx-auto animate-pulse"></div>
            <div className="h-10 bg-gray-300 rounded w-full max-w-md mb-4 mx-auto animate-pulse"></div>
            <div className="h-5 bg-gray-300 rounded w-full max-w-xl mb-6 mx-auto animate-pulse"></div>
          </div>

          {/* FAQ items skeleton */}
          <div className="space-y-3">
            {[0, 1, 2, 3, 4].map((index) => (
              <div key={index} className="border border-gray-200 rounded-lg p-4">
                <div className="flex items-center justify-between">
                  <div className="h-6 bg-gray-300 rounded w-3/4 animate-pulse"></div>
                  <div className="h-5 w-5 bg-gray-300 rounded animate-pulse"></div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>
    );
  }

  const {
    title,
    subtitle,
    description,
    items = [],
    alignment = 'center',
    variant = 'default',
    allowMultipleOpen = false,
    className: dataClassName,
  } = data;

  // Initialize with first item open by default
  const [openItems, setOpenItems] = useState(new Set(items.length > 0 ? [0] : []));

  const toggleItem = (index) => {
    setOpenItems((prev) => {
      const newSet = new Set(prev);
      if (newSet.has(index)) {
        newSet.delete(index);
      } else {
        if (!allowMultipleOpen) {
          newSet.clear();
        }
        newSet.add(index);
      }
      return newSet;
    });
  };

  const alignmentClasses = {
    left: 'text-left items-start',
    center: 'text-center items-center',
    right: 'text-right items-end',
  };

  const variantClasses = {
    default: 'bg-white',
    bordered: 'bg-white border border-gray-200',
    minimal: 'bg-transparent',
    blue: 'bg-white',
    shadow: 'bg-white',
    colored: 'bg-white',
    outlined: 'bg-white',
    filled: 'bg-white',
    gradient: 'bg-white',
  };

  if (items.length === 0) {
    console.warn('FaqHub: items array is empty');
    return null;
  }

 

  return (
    <section
      id={id}
      className={clsx(
        'w-full py-12 sm:py-16 lg:py-20 px-4 sm:px-6 lg:px-8',
        variantClasses[variant],
        className,
        dataClassName
      )}
    >
      <div className="max-w-4xl mx-auto w-full">
        {/* Header */}
        {(title || subtitle || description) && (
          <div
            className={clsx(
              'mb-10 sm:mb-12 flex flex-col',
              alignmentClasses[alignment]
            )}
          >
            {subtitle && (
              <p className="text-sm sm:text-base font-semibold text-primary-600 uppercase tracking-wider mb-3">
                {subtitle}
              </p>
            )}

            {title && (
              <h2 className="text-3xl sm:text-4xl md:text-5xl font-bold text-gray-900 mb-4 leading-tight">
                {title}
              </h2>
            )}

            {description && (
              <p
                className={clsx(
                  'text-base sm:text-lg text-gray-500 max-w-2xl',
                  alignment === 'center' && 'mx-auto',
                  alignment === 'right' && 'ml-auto',
                  alignment === 'left' && 'mr-auto'
                )}
              >
                {description}
              </p>
            )}
          </div>
        )}

        {/* FAQ Items */}
        <div className="space-y-4">
          {items.map((item, index) => {
            const isOpen = openItems.has(index);
            const { question, answer } = item;

            if (!question || !answer) {
              console.warn(`FaqHub: Item at index ${index} is missing question or answer`);
              return null;
            }

            // Get item styling based on variant and state
            const getItemClasses = () => {
              // Blue variant - blue border for open items
              if (variant === 'blue') {
                return clsx(
                  'bg-white rounded-lg transition-all duration-300',
                  isOpen 
                    ? 'border-2 border-primary-500 shadow-sm' 
                    : 'border border-gray-200 hover:border-primary-300'
                );
              }
              
              // Shadow variant - enhanced shadows
              if (variant === 'shadow') {
                return clsx(
                  'bg-white rounded-lg transition-all duration-300',
                  isOpen 
                    ? 'border border-gray-200 shadow-lg' 
                    : 'border border-gray-200 shadow-sm hover:shadow-md'
                );
              }
              
              // Colored variant - colored backgrounds
              if (variant === 'colored') {
                return clsx(
                  'rounded-lg transition-all duration-300',
                  isOpen 
                    ? 'bg-primary-50 border-2 border-primary-500' 
                    : 'bg-white border border-gray-200 hover:bg-gray-50'
                );
              }
              
              // Outlined variant - thicker borders
              if (variant === 'outlined') {
                return clsx(
                  'bg-white rounded-lg transition-all duration-300',
                  isOpen 
                    ? 'border-2 border-primary-600' 
                    : 'border-2 border-gray-300 hover:border-gray-400'
                );
              }
              
              // Filled variant - filled backgrounds
              if (variant === 'filled') {
                return clsx(
                  'rounded-lg transition-all duration-300',
                  isOpen 
                    ? 'bg-primary-500 border-2 border-primary-600' 
                    : 'bg-gray-100 border border-gray-300 hover:bg-gray-200'
                );
              }
              
              // Gradient variant - gradient accent
              if (variant === 'gradient') {
                return clsx(
                  'bg-white rounded-lg transition-all duration-300 relative',
                  isOpen 
                    ? 'border border-gray-200 shadow-md' 
                    : 'border border-gray-200 hover:shadow-sm'
                );
              }
              
              // Minimal variant
              if (variant === 'minimal') {
                return clsx(
                  'bg-white rounded-lg transition-all duration-300',
                  isOpen 
                    ? 'border border-gray-200' 
                    : 'border border-gray-200'
                );
              }
              
              // Bordered variant
              if (variant === 'bordered') {
                return clsx(
                  'bg-white rounded-lg transition-all duration-300',
                  isOpen 
                    ? 'border-2 border-gray-300' 
                    : 'border-2 border-gray-200 hover:border-gray-300'
                );
              }
              
              // Default variant - clean and simple
              return clsx(
                'bg-white rounded-lg transition-all duration-300',
                isOpen 
                  ? 'border border-gray-200' 
                  : 'border border-gray-200'
              );
            };

            return (
              <div
                key={index}
                className={getItemClasses()}
              >
                {/* Gradient accent for gradient variant */}
                {variant === 'gradient' && isOpen && (
                  <div className="absolute top-0 left-0 right-0 h-1 bg-gradient-to-r from-primary-500 via-primary-500 to-pink-500 rounded-t-lg" />
                )}
                <button
                  onClick={() => toggleItem(index)}
                  className={clsx(
                    'w-full px-6 py-4 sm:px-8 sm:py-5 flex items-center justify-between text-left rounded-lg transition-colors',
                    isOpen ? 'rounded-b-none' : 'rounded-lg',
                    variant === 'filled' && isOpen && 'text-white',
                    variant === 'filled' && !isOpen && 'text-gray-900'
                  )}
                  aria-expanded={isOpen}
                  aria-controls={`faq-answer-${index}`}
                >
                  <span className={clsx(
                    'text-base sm:text-lg font-semibold pr-4',
                    variant === 'filled' && isOpen ? 'text-white' : 'text-gray-900'
                  )}>
                    {question}
                  </span>
                  <svg
                    className={clsx(
                      'w-5 h-5 sm:w-6 sm:h-6 flex-shrink-0 transition-transform duration-300',
                      variant === 'filled' && isOpen ? 'text-white' : 'text-gray-500',
                      isOpen && 'transform rotate-180'
                    )}
                    fill="none"
                    stroke="currentColor"
                    viewBox="0 0 24 24"
                    aria-hidden="true"
                  >
                    <path
                      strokeLinecap="round"
                      strokeLinejoin="round"
                      strokeWidth={2}
                      d="M19 9l-7 7-7-7"
                    />
                  </svg>
                </button>

                <div
                  id={`faq-answer-${index}`}
                  className={clsx(
                    'overflow-hidden transition-all duration-300 ease-in-out',
                    isOpen ? 'max-h-[1000px] opacity-100' : 'max-h-0 opacity-0'
                  )}
                >
                  <div className={clsx(
                    'px-6 pb-5 sm:px-8 sm:pb-6 pt-4',
                    variant !== 'filled' && 'border-t border-gray-200',
                    variant === 'filled' && isOpen && 'border-t border-primary-400',
                    variant === 'colored' && isOpen && 'border-t border-primary-200'
                  )}>
                    <p className={clsx(
                      'text-sm sm:text-base leading-relaxed',
                      variant === 'filled' && isOpen ? 'text-white' : 'text-gray-600'
                    )}>
                      {answer}
                    </p>
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </section>
  );
};

