import React, { useState, useEffect } from 'react';
import { clsx } from 'clsx';
import { CheckoutSkeleton } from '../CheckoutSkeleton';

export const CheckoutPeloton = ({ data = {}, className, loading = false }) => {
  const [email, setEmail] = useState('');
  const [timedOut, setTimedOut] = useState(false);

  // Auto-show content after 2 seconds if loading is true
  useEffect(() => {
    if (loading) {
      setTimedOut(false);
      const timer = setTimeout(() => {
        setTimedOut(true);
      }, 2000);
      return () => clearTimeout(timer);
    }
  }, [loading]);

  const shouldShowSkeleton = loading && !timedOut;
  
  const cartItems = data.cartItems || [];
  const subtotal = cartItems.reduce((acc, item) => acc + item.price, 0);

  // Show skeleton loading state
  if (shouldShowSkeleton) {
    return <CheckoutSkeleton data={{ variant: 'peloton' }} className={className} />;
  }

  return (
    <div className={clsx("checkout-peloton bg-white min-h-screen font-sans text-gray-900", className)}>
      <div className="max-w-6xl mx-auto px-4 py-8 lg:py-12">
        <div className="grid grid-cols-1 lg:grid-cols-12 gap-12">
          
          {/* Left Column - Forms */}
          <div className="lg:col-span-7 space-y-10">
            
            {/* Email Section */}
            <section>
              <h2 className="text-sm font-bold tracking-widest mb-4 uppercase">{data.email?.label || 'Email'}</h2>
              <div className="relative">
                <input
                  type="email"
                  placeholder={data.email?.placeholder || 'Email Address*'}
                  className="w-full border border-gray-300 px-4 py-3 focus:border-black transition-colors outline-none"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                />
              </div>
            </section>

            {/* Protection Plan Section */}
            {data.protection && (
              <section>
                <h2 className="text-xs font-bold tracking-[0.15em] mb-4 uppercase">{data.protection.title}</h2>
                <div className="border border-gray-100 p-8 space-y-6">
                  <p className="text-sm text-gray-700 leading-relaxed">
                    {data.protection.description} <a href={data.protection.learnMoreLink || '#'} className="text-gray-900 underline font-medium">{data.protection.learnMoreText || 'Learn More'}</a>
                  </p>
                  
                  <div className="space-y-5">
                    {data.protection.options?.map((option) => (
                      <label key={option.id} className="flex items-start cursor-pointer group">
                        <div className="relative flex items-center justify-center mt-0.5">
                          <input type="checkbox" className="peer appearance-none w-5 h-5 border border-gray-300 rounded-sm checked:bg-black checked:border-black transition-all" />
                          <svg className="absolute w-3.5 h-3.5 text-white opacity-0 peer-checked:opacity-100 transition-opacity" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={3.5} d="M5 13l4 4L19 7" />
                          </svg>
                        </div>
                        <span className="ml-4 text-sm font-bold text-gray-800 leading-tight">
                          {option.label}
                        </span>
                      </label>
                    ))}
                  </div>

                  <p className="text-[10px] text-gray-400 pt-2 leading-relaxed uppercase tracking-wide">
                    {data.protection.termsText} <a href={data.protection.termsLink || '#'} className="underline">{data.protection.termsLinkText}</a>.
                  </p>
                </div>
              </section>
            )}

            {/* Shipping Section */}
            <section>
              <h2 className="text-sm font-bold tracking-widest mb-4 uppercase">{data.shipping?.title || 'Shipping'}</h2>
              <div className="space-y-4">
                <div className="grid grid-cols-2 gap-4">
                  <input
                    type="text"
                    placeholder={data.shipping?.fields?.firstName || 'First Name'}
                    className="w-full border border-gray-300 px-4 py-3 focus:border-black outline-none transition-colors"
                  />
                  <input
                    type="text"
                    placeholder={data.shipping?.fields?.lastName || 'Last Name'}
                    className="w-full border border-gray-300 px-4 py-3 focus:border-black outline-none transition-colors"
                  />
                </div>
                
                <div className="relative">
                  <span className="absolute left-4 top-1/2 -translate-y-1/2 text-gray-500 text-sm">{data.shipping?.fields?.phone || 'Phone'} <br/> <span className="text-black font-medium">+1</span></span>
                  <input
                    type="tel"
                    className="w-full border border-gray-300 pl-20 pr-4 py-3 focus:border-black outline-none transition-colors"
                  />
                </div>

                <input
                  type="text"
                  placeholder={data.shipping?.fields?.address || 'Address'}
                  className="w-full border border-gray-300 px-4 py-3 focus:border-black outline-none transition-colors"
                />

                <input
                  type="text"
                  placeholder={data.shipping?.fields?.apt || 'Apt, Suite'}
                  className="w-full border border-gray-300 px-4 py-3 focus:border-black outline-none transition-colors"
                />

                <div className="grid grid-cols-12 gap-4">
                  <div className="col-span-4">
                    <input
                      type="text"
                      placeholder={data.shipping?.fields?.city || 'City*'}
                      className="w-full border border-gray-300 px-4 py-3 focus:border-black outline-none transition-colors"
                    />
                  </div>
                  <div className="col-span-4 relative">
                    <select className="w-full appearance-none border border-gray-300 px-4 py-3 focus:border-black outline-none transition-colors bg-white" defaultValue="">
                      <option value="" disabled>{data.shipping?.fields?.state || 'State*'}</option>
                      <option value="NY">New York</option>
                      <option value="CA">California</option>
                    </select>
                    <div className="absolute right-4 top-1/2 -translate-y-1/2 pointer-events-none">
                      <svg className="w-4 h-4 text-gray-400" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
                      </svg>
                    </div>
                  </div>
                  <div className="col-span-4">
                    <input
                      type="text"
                      placeholder={data.shipping?.fields?.zip || 'ZIP Code*'}
                      className="w-full border border-gray-300 px-4 py-3 focus:border-black outline-none transition-colors"
                    />
                  </div>
                </div>

                <div className="relative">
                   <div className="absolute left-4 top-2 text-[10px] text-gray-500 uppercase tracking-widest font-bold">{data.shipping?.fields?.country || 'Country'}</div>
                   <select className="w-full appearance-none border border-gray-300 px-4 pt-6 pb-2 focus:border-black outline-none transition-colors bg-white font-medium" defaultValue="US">
                      <option value="US">United States</option>
                    </select>
                    <div className="absolute right-4 top-1/2 -translate-y-1/2 pointer-events-none mt-2">
                      <svg className="w-4 h-4 text-gray-400" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
                      </svg>
                    </div>
                </div>

                <div className="pt-2">
                   <p className="text-sm font-bold flex items-center">
                    {data.shipping?.delivery?.label || 'Order now, est. delivery by:'} 
                    <button className="ml-1 text-gray-400 hover:text-black">
                      <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20">
                        <path fillRule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7-4a1 1 0 11-2 0 1 1 0 012 0zM9 9a1 1 0 000 2v3a1 1 0 001 1h1a1 1 0 100-2v-3a1 1 0 00-1-1H9z" clipRule="evenodd" />
                      </svg>
                    </button>
                  </p>
                  <p className="text-xs text-gray-500 mt-1">{data.shipping?.delivery?.estimateText || 'Enter your ZIP code for an estimate.'}</p>
                </div>

                <label className="flex items-center cursor-pointer group pt-4">
                  <div className="relative flex items-center justify-center">
                    <input type="checkbox" className="peer appearance-none w-5 h-5 border border-gray-300 checked:bg-black checked:border-black transition-all" />
                    <svg className="absolute w-3 h-3 text-white opacity-0 peer-checked:opacity-100 transition-opacity" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={3} d="M5 13l4 4L19 7" />
                    </svg>
                  </div>
                  <span className="ml-3 text-sm font-medium">{data.shipping?.giftLabel || 'This order is a gift'}</span>
                </label>
              </div>
            </section>

            {/* Payment Method Section */}
            <section>
              <h2 className="text-sm font-bold tracking-widest mb-4 uppercase">{data.payment?.title || 'Payment Method'}</h2>
              <div className="space-y-3">
                {data.payment?.methods?.map((method) => (
                  <div key={method.id} className="border border-gray-300 p-6 flex justify-between items-center cursor-pointer hover:border-black transition-colors">
                    <div>
                      <p className="font-bold text-sm">{method.title}</p>
                      <p className="text-xs text-gray-500 mt-1">{method.subtitle}</p>
                    </div>
                    <div className="flex space-x-2 items-center">
                      {method.logos && method.logos.map((logo, lIdx) => (
                        <img key={lIdx} src={logo.src} alt={logo.alt} className="h-4" />
                      ))}
                      {method.logoSrc && (
                        <img src={method.logoSrc} alt={method.logoAlt} className="h-6" />
                      )}
                    </div>
                  </div>
                ))}
              </div>
            </section>

          </div>

          {/* Right Column - Summary */}
          <div className="lg:col-span-5 space-y-8">
            
            {/* Cart Summary */}
            <div className="bg-[#f5f7f9] p-8">
              <h2 className="text-sm font-bold tracking-widest mb-6 uppercase">{data.summary?.title || 'Cart Summary'}</h2>
              
              <div className="space-y-6">
                {cartItems.map((item, idx) => (
                  <div key={idx} className="flex justify-between items-start text-sm">
                    <div className="space-y-1">
                      <p className="font-bold flex items-center">
                        <span className="mr-2">{idx + 1}</span> {item.name}
                        {item.hasInfoIcon && (
                          <button className="ml-1 text-gray-400">
                            <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20">
                              <path fillRule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7-4a1 1 0 11-2 0 1 1 0 012 0zM9 9a1 1 0 000 2v3a1 1 0 001 1h1a1 1 0 100-2v-3a1 1 0 00-1-1H9z" clipRule="evenodd" />
                            </svg>
                          </button>
                        )}
                      </p>
                      {item.details && item.details.map((detail, dIdx) => (
                        <p key={dIdx} className="text-gray-500 text-xs ml-4">{detail}</p>
                      ))}
                      {item.isSubscription && (
                         <p className="text-gray-500 text-[11px] ml-4 leading-relaxed max-w-[240px]">
                            {item.subscriptionDetail}
                         </p>
                      )}
                    </div>
                    <p className="font-bold">${item.price.toLocaleString(undefined, { minimumFractionDigits: 2 })}</p>
                  </div>
                ))}
              </div>

              <div className="mt-8 pt-8 border-t border-gray-200 space-y-4">
                <div className="flex justify-between text-sm">
                  <p className="text-gray-600">{(data.summary?.subtotalLabel || 'Subtotal')} ({cartItems.length}):</p>
                  <p className="font-bold">${subtotal.toLocaleString(undefined, { minimumFractionDigits: 2 })}</p>
                </div>
                <div className="flex justify-between text-sm">
                  <p className="text-gray-600">{data.summary?.deliveryLabel || 'Est. delivery and setup'}</p>
                  <p className="font-bold">{data.summary?.deliveryValue || 'Included'}</p>
                </div>
                <div className="flex justify-between text-sm">
                  <p className="text-gray-600 flex items-center">
                    {data.summary?.taxLabel || 'Estimated Tax:'} 
                    <button className="ml-1 text-gray-400">
                      <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20">
                        <path fillRule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7-4a1 1 0 11-2 0 1 1 0 012 0zM9 9a1 1 0 000 2v3a1 1 0 001 1h1a1 1 0 100-2v-3a1 1 0 00-1-1H9z" clipRule="evenodd" />
                      </svg>
                    </button>
                  </p>
                  <p className="font-bold">--</p>
                </div>
                <button className="text-sm underline font-medium">{data.summary?.promoLabel || 'Enter promo code'}</button>
              </div>

              <div className="mt-8 pt-8 border-t border-gray-200">
                <div className="flex justify-between items-center">
                  <p className="text-sm font-bold uppercase tracking-widest">{data.summary?.totalLabel || 'Estimated Total'}</p>
                  <p className="text-xl font-bold">${subtotal.toLocaleString(undefined, { minimumFractionDigits: 2 })}</p>
                </div>
                {data.summary?.financingText && (
                  <p className="text-xs text-gray-500 mt-4 leading-relaxed">
                    {data.summary.financingText}
                  </p>
                )}
              </div>
            </div>

            {/* Home Trial info */}
            {data.trial && (
              <div className="border border-gray-200 p-8 space-y-6">
                <h2 className="text-[11px] font-bold tracking-[0.2em] mb-4 uppercase">{data.trial.title}</h2>
                <ul className="space-y-4">
                  {data.trial.items?.map((text, i) => (
                    <li key={i} className="flex items-start text-sm">
                      <span className="flex items-center justify-center w-6 h-6 border border-black rounded-full text-[10px] font-bold mr-4 shrink-0">
                        {i + 1}
                      </span>
                      <span className="text-gray-700 leading-tight pt-1">{text}</span>
                    </li>
                  ))}
                </ul>
                <p className="text-[10px] text-gray-500 pt-2">
                  {data.trial.termsText} <a href={data.trial.termsLink || '#'} className="underline">{data.trial.termsLinkText}</a>.
                </p>
              </div>
            )}

          </div>
        </div>
      </div>
    </div>
  );
};
