import Link from 'next/link';
import clsx from 'clsx';
import '../../styles/product.css';

/**
 * ProductBreadcrumbs Component
 * @param {Object} props - Component props
 * @param {Object} props.data - Breadcrumb data
 * @param {Array} props.data.items - Array of breadcrumb items
 * @param {string} [props.data.separator='/'] - Separator between items
 * @param {boolean} [props.data.showHome=true] - Show home link
 * @param {string} [props.data.variant='default'] - Style variant: 'default' | 'minimal' | 'pills'
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute for the section element
 */
export const ProductBreadcrumbs = ({ data, className, id }) => {
  // Safety check for data
  if (!data || typeof data !== 'object') {
    console.error('ProductBreadcrumbs: data prop is required and must be an object');
    return null;
  }

  const {
    items = [],
    separator = '/',
    showHome = true,
    variant = 'default'
  } = data;

  // Add home if not present and showHome is true
  const allItems = showHome && items.length > 0 && items[0].label !== 'Home'
    ? [{ label: 'Home', href: '/' }, ...items]
    : items;

  if (allItems.length === 0) {
    return null;
  }

  // Default variant - Standard breadcrumb navigation
  if (variant === 'default') {
    return (
      <nav
        id={id}
        className={clsx(
          "product-breadcrumbs-default",
          className
        )}
        aria-label="Breadcrumb"
      >
        <ol className="flex items-center space-x-2 text-sm text-gray-500">
          {allItems.map((item, index) => {
            const isLast = index === allItems.length - 1;
            const isFirst = index === 0;

            return (
              <li key={index} className="flex items-center">
                {!isFirst && (
                  <span className="mx-2 text-gray-400" aria-hidden="true">
                    {separator}
                  </span>
                )}

                {isLast ? (
                  <span
                    className="text-gray-900 font-medium"
                    aria-current="page"
                  >
                    {item.label}
                  </span>
                ) : (
                  <Link
                    href={item.href}
                    className="hover:text-gray-700 transition-colors"
                  >
                    {item.label}
                  </Link>
                )}
              </li>
            );
          })}
        </ol>
      </nav>
    );
  }

  // Minimal variant - Clean minimal design
  if (variant === 'minimal') {
    return (
      <nav
        id={id}
        className={clsx(
          "product-breadcrumbs-minimal",
          className
        )}
        aria-label="Breadcrumb"
      >
        <ol className="flex items-center space-x-1 text-xs text-gray-500">
          {allItems.map((item, index) => {
            const isLast = index === allItems.length - 1;

            return (
              <li key={index} className="flex items-center">
                {index > 0 && (
                  <span className="mx-1 text-gray-300" aria-hidden="true">
                    {separator}
                  </span>
                )}

                {isLast ? (
                  <span className="text-gray-700 font-medium">
                    {item.label}
                  </span>
                ) : (
                  <Link
                    href={item.href}
                    className="hover:text-gray-600 transition-colors"
                  >
                    {item.label}
                  </Link>
                )}
              </li>
            );
          })}
        </ol>
      </nav>
    );
  }

  // Pills variant - Pill-style navigation
  if (variant === 'pills') {
    return (
      <nav
        id={id}
        className={clsx(
          "product-breadcrumbs-pills",
          className
        )}
        aria-label="Breadcrumb"
      >
        <ol className="flex items-center space-x-2">
          {allItems.map((item, index) => {
            const isLast = index === allItems.length - 1;

            return (
              <li key={index} className="flex items-center">
                {index > 0 && (
                  <svg
                    className="w-4 h-4 text-gray-400 mx-1"
                    fill="currentColor"
                    viewBox="0 0 20 20"
                    aria-hidden="true"
                  >
                    <path
                      fillRule="evenodd"
                      d="M7.293 14.707a1 1 0 010-1.414L10.586 10 7.293 6.707a1 1 0 011.414-1.414l4 4a1 1 0 010 1.414l-4 4a1 1 0 01-1.414 0z"
                      clipRule="evenodd"
                    />
                  </svg>
                )}

                {isLast ? (
                  <span
                    className="inline-flex items-center px-3 py-1 rounded-full text-sm font-medium bg-gray-900 text-white"
                    aria-current="page"
                  >
                    {item.label}
                  </span>
                ) : (
                  <Link
                    href={item.href}
                    className="inline-flex items-center px-3 py-1 rounded-full text-sm font-medium bg-gray-100 text-gray-700 hover:bg-gray-200 transition-colors"
                  >
                    {item.label}
                  </Link>
                )}
              </li>
            );
          })}
        </ol>
      </nav>
    );
  }

  return null;
};
