import React, { useState, useEffect } from 'react';
import { clsx } from 'clsx';
import { CheckoutSkeleton } from '../CheckoutSkeleton';

export const CheckoutFlow = ({ data = {}, className, loading = false }) => {
  const [step, setStep] = useState(1);
  const [timedOut, setTimedOut] = useState(false);

  // Auto-show content after 2 seconds if loading is true
  useEffect(() => {
    if (loading) {
      setTimedOut(false);
      const timer = setTimeout(() => {
        setTimedOut(true);
      }, 2000);
      return () => clearTimeout(timer);
    }
  }, [loading]);

  const shouldShowSkeleton = loading && !timedOut;
  const cartItems = data.cartItems || [];
  const subtotal = cartItems.reduce((acc, item) => acc + item.price, 0);

  const steps = data.steps || ['Details', 'Shipping', 'Payment'];

  // Show skeleton loading state
  if (shouldShowSkeleton) {
    return <CheckoutSkeleton data={{ variant: 'flow' }} className={className} />;
  }

  return (
    <div className={clsx("checkout-flow min-h-screen bg-white py-12 px-6", className)}>
      <div className="max-w-2xl mx-auto">
        
        {/* Progress Bar */}
        <div className="flex justify-between mb-16 relative">
          <div className="absolute top-1/2 left-0 w-full h-0.5 bg-neutral-100 -translate-y-1/2 -z-10"></div>
          {steps.map((s, idx) => (
            <div key={idx} className="flex flex-col items-center">
              <div className={clsx(
                "w-10 h-10 rounded-full flex items-center justify-center font-bold text-sm transition-all duration-500",
                step > idx + 1 ? "bg-green-500 text-white" : step === idx + 1 ? "bg-black text-white scale-125" : "bg-white border-2 border-neutral-200 text-neutral-400"
              )}>
                {step > idx + 1 ? '' : idx + 1}
              </div>
              <span className={clsx("text-xs mt-3 font-bold uppercase tracking-widest", step === idx + 1 ? "text-black" : "text-neutral-400")}>
                {s}
              </span>
            </div>
          ))}
        </div>

        <div className="min-h-[400px]">
          {step === 1 && (
            <div className="space-y-8 animate-in fade-in slide-in-from-bottom-4 duration-500">
              <h2 className="text-3xl font-black">{data.email?.label || 'Your Information'}</h2>
              <div className="space-y-4">
                <input type="email" placeholder={data.email?.placeholder || "Email"} className="w-full border-b-2 border-neutral-100 py-4 focus:border-black transition-colors outline-none text-xl font-medium" />
                <input type="tel" placeholder={data.shipping?.fields?.phone || "Phone"} className="w-full border-b-2 border-neutral-100 py-4 focus:border-black transition-colors outline-none text-xl font-medium" />
              </div>
            </div>
          )}

          {step === 2 && (
            <div className="space-y-8 animate-in fade-in slide-in-from-bottom-4 duration-500">
              <h2 className="text-3xl font-black">{data.shipping?.title || 'Shipping Address'}</h2>
              <div className="grid grid-cols-2 gap-8">
                <input type="text" placeholder={data.shipping?.fields?.firstName || "First Name"} className="w-full border-b-2 border-neutral-100 py-4 focus:border-black outline-none text-xl" />
                <input type="text" placeholder={data.shipping?.fields?.lastName || "Last Name"} className="w-full border-b-2 border-neutral-100 py-4 focus:border-black outline-none text-xl" />
              </div>
              <input type="text" placeholder={data.shipping?.fields?.address || "Address"} className="w-full border-b-2 border-neutral-100 py-4 focus:border-black transition-colors outline-none text-xl" />
            </div>
          )}

          {step === 3 && (
            <div className="space-y-8 animate-in fade-in slide-in-from-bottom-4 duration-500">
              <h2 className="text-3xl font-black">{data.payment?.title || 'Payment Details'}</h2>
              <div className="bg-neutral-50 p-8 rounded-3xl space-y-4">
                 <div className="flex justify-between items-center text-neutral-500 mb-4">
                    <span>{data.summary?.title || 'Order Summary'}</span>
                    <span className="font-bold text-black">${subtotal.toFixed(2)}</span>
                 </div>
                 <input type="text" placeholder="Card Number" className="w-full bg-white rounded-xl py-4 px-6 border border-neutral-100" />
                 <div className="grid grid-cols-2 gap-4">
                   <input type="text" placeholder="MM/YY" className="w-full bg-white rounded-xl py-4 px-6 border border-neutral-100" />
                   <input type="text" placeholder="CVC" className="w-full bg-white rounded-xl py-4 px-6 border border-neutral-100" />
                 </div>
              </div>
            </div>
          )}
        </div>

        <div className="flex justify-between mt-16 pt-8 border-t border-neutral-100">
          <button 
            disabled={step === 1}
            onClick={() => setStep(s => s - 1)}
            className="text-neutral-400 font-bold hover:text-black transition-colors disabled:opacity-0"
          >
             {data.backText || 'Back'}
          </button>
          <button 
            onClick={() => step < 3 ? setStep(s => s + 1) : null}
            className="bg-black text-white px-12 py-4 rounded-full font-black text-lg hover:px-16 transition-all"
          >
            {step === 3 ? (data.summary?.buttonText || 'Place Order') : (data.nextText || 'Next Step ')}
          </button>
        </div>

      </div>
    </div>
  );
};
