import { useState, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/blog.css';

/**
 * BlogNavigation Component - Previous/Next post navigation
 * @param {Object} props - Component props
 * @param {Object} props.data - Navigation configuration data
 * @param {Object} [props.data.previous] - Previous post object {title, href, image}
 * @param {Object} [props.data.next] - Next post object {title, href, image}
 * @param {string} [props.data.variant='default'] - Style variant: 'default' | 'cards' | 'minimal' | 'compact'
 * @param {string} [props.data.theme='light'] - Theme: 'light' | 'dark'
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute
 * @param {boolean} [props.loader=false] - Show loading state
 */
export const BlogNavigation = ({ data, className, id, loader = false }) => {
  if (!data || typeof data !== 'object') {
    console.error('BlogNavigation: data prop is required and must be an object');
    return null;
  }

  const [showLoader, setShowLoader] = useState(loader);

  useEffect(() => {
    if (loader) {
      const timer = setTimeout(() => {
        setShowLoader(false);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowLoader(false);
    }
  }, [loader]);

  const {
    previous,
    next,
    variant = 'default',
    theme = 'light',
    className: dataClassName,
  } = data;

  const containerClasses = clsx(
    'blog-navigation',
    `blog-navigation-${variant}`,
    `blog-navigation-${theme}`,
    className,
    dataClassName
  );

  // Loading skeleton
  if (showLoader) {
    return (
      <div className={containerClasses} id={id}>
        <div className="blog-navigation-skeleton">
          <div className="blog-navigation-skeleton-item"></div>
          <div className="blog-navigation-skeleton-item"></div>
        </div>
      </div>
    );
  }

  // Default variant - Arrow navigation
  if (variant === 'default') {
    return (
      <div className={containerClasses} id={id}>
        <div className="blog-navigation-content">
          {previous && (
            <a href={previous.href || '#'} className="blog-navigation-link blog-navigation-prev">
              <div className="blog-navigation-arrow">
                <svg fill="none" stroke="currentColor" viewBox="0 0 24 24" className="w-5 h-5">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
                </svg>
              </div>
              <div className="blog-navigation-text">
                <span className="blog-navigation-label">Previous</span>
                <span className="blog-navigation-title">{previous.title}</span>
              </div>
            </a>
          )}
          {next && (
            <a href={next.href || '#'} className="blog-navigation-link blog-navigation-next">
              <div className="blog-navigation-text">
                <span className="blog-navigation-label">Next</span>
                <span className="blog-navigation-title">{next.title}</span>
              </div>
              <div className="blog-navigation-arrow">
                <svg fill="none" stroke="currentColor" viewBox="0 0 24 24" className="w-5 h-5">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
                </svg>
              </div>
            </a>
          )}
        </div>
      </div>
    );
  }

  // Cards variant - Card-style navigation with images
  if (variant === 'cards') {
    return (
      <div className={containerClasses} id={id}>
        <div className="blog-navigation-cards">
          {previous && (
            <a href={previous.href || '#'} className="blog-navigation-card blog-navigation-card-prev">
              {previous.image && (
                <img src={previous.image} alt={previous.title} className="blog-navigation-card-image" />
              )}
              <div className="blog-navigation-card-content">
                <span className="blog-navigation-card-label"> Previous</span>
                <h3 className="blog-navigation-card-title">{previous.title}</h3>
              </div>
            </a>
          )}
          {next && (
            <a href={next.href || '#'} className="blog-navigation-card blog-navigation-card-next">
              {next.image && (
                <img src={next.image} alt={next.title} className="blog-navigation-card-image" />
              )}
              <div className="blog-navigation-card-content">
                <span className="blog-navigation-card-label">Next </span>
                <h3 className="blog-navigation-card-title">{next.title}</h3>
              </div>
            </a>
          )}
        </div>
      </div>
    );
  }

  // Minimal variant - Simple text links
  if (variant === 'minimal') {
    return (
      <div className={containerClasses} id={id}>
        <div className="blog-navigation-minimal">
          {previous && (
            <a href={previous.href || '#'} className="blog-navigation-link-minimal">
               {previous.title}
            </a>
          )}
          {next && (
            <a href={next.href || '#'} className="blog-navigation-link-minimal">
              {next.title} 
            </a>
          )}
        </div>
      </div>
    );
  }

  // Compact variant - Smaller arrows
  if (variant === 'compact') {
    return (
      <div className={containerClasses} id={id}>
        <div className="blog-navigation-compact">
          {previous && (
            <a href={previous.href || '#'} className="blog-navigation-link-compact">
              <svg fill="none" stroke="currentColor" viewBox="0 0 24 24" className="w-4 h-4">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
              </svg>
              <span>{previous.title}</span>
            </a>
          )}
          {next && (
            <a href={next.href || '#'} className="blog-navigation-link-compact">
              <span>{next.title}</span>
              <svg fill="none" stroke="currentColor" viewBox="0 0 24 24" className="w-4 h-4">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
              </svg>
            </a>
          )}
        </div>
      </div>
    );
  }

  return null;
};