import { useState, useEffect, useRef } from 'react';
import clsx from 'clsx';
import '../../styles/carousel.css';

/**
 * SlideFlow Component - Comprehensive Carousel Component
 * @param {Object} props - Component props
 * @param {Object} props.data - Carousel configuration data
 * @param {string} [props.data.title] - Main heading text
 * @param {string} [props.data.subtitle] - Subtitle text displayed above title
 * @param {string} [props.data.description] - Description text displayed below title
 * @param {Array} props.data.slides - Array of slide items
 * @param {string} [props.data.variant='default'] - Style variant: 'default' | 'autoplay' | 'dots' | 'arrows' | 'thumbnails' | 'fade' | 'slide' | 'vertical' | 'multiple' | 'infinite' | 'center' | 'minimal' | 'modern' | 'classic' | 'card' | 'fullscreen'
 * @param {string} [props.data.transition='slide'] - Transition type: 'slide' | 'fade' | 'zoom' | 'flip'
 * @param {string} [props.data.alignment='center'] - Text alignment: 'left' | 'center' | 'right'
 * @param {number} [props.data.slidesToShow=1] - Number of slides visible at once (for multiple variant)
 * @param {number} [props.data.autoplayInterval=3000] - Autoplay interval in milliseconds
 * @param {boolean} [props.data.showArrows=true] - Show navigation arrows
 * @param {boolean} [props.data.showDots=true] - Show dot indicators
 * @param {boolean} [props.data.showThumbnails=false] - Show thumbnail navigation
 * @param {boolean} [props.data.infinite=true] - Enable infinite loop
 * @param {boolean} [props.data.pauseOnHover=true] - Pause autoplay on hover
 * @param {Object|Array} [props.data.slides[].button] - Single button object (backward compatibility)
 * @param {Array} [props.data.slides[].buttons] - Array of button objects for slide
 * @param {string} [props.data.slides[].button.variant] - Button variant: 'primary' | 'secondary' | 'outline'
 * @param {string} [props.data.slides[].button.text] - Button text
 * @param {string} [props.data.slides[].button.href] - Button link URL
 * @param {Function} [props.data.slides[].button.onClick] - Button click handler
 * @param {string} [props.data.slides[].button.className] - Additional CSS classes for button
 * @param {string} [props.data.className] - Additional CSS classes for the section
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute for the section element
 * @param {boolean} [props.loader=false] - Show loading state
 */
export const SlideFlow = ({ data, className, id, loader = false }) => {
  // Safety check for data
  if (!data || typeof data !== 'object') {
    console.error('SlideFlow: data prop is required and must be an object');
    return null;
  }

  const [showLoader, setShowLoader] = useState(loader);

  // Auto-hide loader after 2 seconds when loader prop is true
  useEffect(() => {
    if (loader) {
      const timer = setTimeout(() => {
        setShowLoader(false);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowLoader(false);
    }
  }, [loader]);

  const {
    title,
    subtitle,
    description,
    slides = [],
    variant = 'default',
    transition = 'slide',
    alignment = 'center',
    slidesToShow = 1,
    autoplayInterval = 3000,
    showArrows = true,
    showDots = true,
    showThumbnails = false,
    infinite = true,
    pauseOnHover = true,
    className: dataClassName,
  } = data;

  // Show loader if loader prop is true
  if (showLoader) {
    return (
      <section
        id={id}
        className={clsx(
          'w-full py-12 sm:py-16 lg:py-20 px-4 sm:px-6 lg:px-8',
          'bg-white',
          className,
          dataClassName
        )}
      >
        <div className="max-w-7xl mx-auto w-full flex items-center justify-center min-h-[400px]">
          <div className="flex flex-col items-center gap-4">
            <div className="w-12 h-12 border-4 border-primary-200 border-t-primary-600 rounded-full animate-spin"></div>
            <p className="text-gray-600 text-sm">Loading...</p>
          </div>
        </div>
      </section>
    );
  }

  const [currentIndex, setCurrentIndex] = useState(0);
  const [isPaused, setIsPaused] = useState(false);
  const [isTransitioning, setIsTransitioning] = useState(false);
  const carouselRef = useRef(null);
  const autoplayTimerRef = useRef(null);

  // Determine if autoplay should be enabled
  const isAutoplay = variant === 'autoplay' || variant.includes('autoplay');

  // Handle next slide
  const nextSlide = () => {
    if (isTransitioning) return;
    setIsTransitioning(true);
    
    if (infinite || currentIndex < slides.length - slidesToShow) {
      setCurrentIndex((prev) => (prev + 1) % slides.length);
    } else {
      setCurrentIndex(slides.length - slidesToShow);
    }
    
    setTimeout(() => setIsTransitioning(false), 300);
  };

  // Handle previous slide
  const prevSlide = () => {
    if (isTransitioning) return;
    setIsTransitioning(true);
    
    if (infinite || currentIndex > 0) {
      setCurrentIndex((prev) => (prev - 1 + slides.length) % slides.length);
    } else {
      setCurrentIndex(0);
    }
    
    setTimeout(() => setIsTransitioning(false), 300);
  };

  // Go to specific slide
  const goToSlide = (index) => {
    if (isTransitioning) return;
    setIsTransitioning(true);
    setCurrentIndex(index);
    setTimeout(() => setIsTransitioning(false), 300);
  };

  // Autoplay effect
  useEffect(() => {
    if (isAutoplay && !isPaused && slides.length > 1) {
      autoplayTimerRef.current = setInterval(() => {
        setCurrentIndex((prev) => {
          if (infinite || prev < slides.length - slidesToShow) {
            return (prev + 1) % slides.length;
          }
          return slides.length - slidesToShow;
        });
      }, autoplayInterval);

      return () => {
        if (autoplayTimerRef.current) {
          clearInterval(autoplayTimerRef.current);
        }
      };
    }
  }, [isAutoplay, isPaused, autoplayInterval, slides.length, infinite, slidesToShow]);

  // Handle mouse enter/leave for pause on hover
  const handleMouseEnter = () => {
    if (pauseOnHover && isAutoplay) {
      setIsPaused(true);
    }
  };

  const handleMouseLeave = () => {
    if (pauseOnHover && isAutoplay) {
      setIsPaused(false);
    }
  };

  if (slides.length === 0) {
    console.warn('SlideFlow: slides array is empty');
    return null;
  }

  const alignmentClasses = {
    left: 'text-left items-start',
    center: 'text-center items-center',
    right: 'text-right items-end',
  };

  // Button variant classes - same as PriceBoard
  const buttonVariantClasses = {
    primary: 'bg-primary-600 hover:bg-primary-700 text-white',
    secondary: 'bg-gray-600 hover:bg-gray-700 text-white',
    outline: 'border-2 border-white text-white hover:bg-white hover:text-gray-900',
  };

  const variantClasses = {
    default: 'bg-white',
    autoplay: 'bg-white',
    dots: 'bg-white',
    arrows: 'bg-white',
    thumbnails: 'bg-white',
    fade: 'bg-white',
    slide: 'bg-white',
    vertical: 'bg-white',
    multiple: 'bg-white',
    infinite: 'bg-white',
    center: 'bg-white',
    minimal: 'bg-transparent',
    modern: 'bg-gray-50',
    classic: 'bg-white',
    card: 'bg-white',
    fullscreen: 'bg-black',
  };

  const getCarouselClasses = () => {
    const baseClasses = 'relative w-full overflow-hidden';
    
    if (variant === 'fullscreen') {
      return clsx(baseClasses, 'h-screen');
    }
    
    if (variant === 'vertical') {
      return clsx(baseClasses, 'h-[600px]');
    }
    
    return clsx(baseClasses, 'h-[500px] sm:h-[600px]');
  };

  const getSlideClasses = (index) => {
    const baseClasses = 'absolute inset-0 transition-all duration-500 ease-in-out';
    
    // Fade transition
    if (transition === 'fade' || variant === 'fade') {
      const isActive = index >= currentIndex && index < currentIndex + slidesToShow;
      return clsx(
        baseClasses,
        isActive ? 'opacity-100 z-10' : 'opacity-0 z-0'
      );
    }
    
    // Zoom transition
    if (transition === 'zoom') {
      const isActive = index >= currentIndex && index < currentIndex + slidesToShow;
      return clsx(
        baseClasses,
        isActive ? 'opacity-100 scale-100 z-10' : 'opacity-0 scale-110 z-0'
      );
    }
    
    // Flip transition
    if (transition === 'flip') {
      const isActive = index >= currentIndex && index < currentIndex + slidesToShow;
      return clsx(
        baseClasses,
        isActive ? 'opacity-100 z-10' : 'opacity-0 z-0'
      );
    }
    
    // All other transitions use inline styles for transforms
    return baseClasses;
  };

  const getSlideStyle = (index) => {
    // Fade transition - no transform needed
    if (transition === 'fade' || variant === 'fade') {
      return {};
    }
    
    // Zoom transition - handled by classes
    if (transition === 'zoom') {
      return {};
    }
    
    // Flip transition - handled by classes
    if (transition === 'flip') {
      return {};
    }
    
    // Vertical variant
    if (variant === 'vertical') {
      const offset = (index - currentIndex) * 100;
      return {
        transform: `translateY(${offset}%)`,
      };
    }
    
    // Multiple slides visible
    if (variant === 'multiple' || slidesToShow > 1) {
      const offset = (index - currentIndex) * (100 / slidesToShow);
      const isVisible = index >= currentIndex && index < currentIndex + slidesToShow;
      return {
        transform: `translateX(${offset}%)`,
        opacity: isVisible ? 1 : 0,
        zIndex: isVisible ? 10 : 0,
      };
    }
    
    // Center mode
    if (variant === 'center') {
      const offset = (index - currentIndex) * 100;
      const isActive = index === currentIndex;
      return {
        transform: `translateX(${offset}%) scale(${isActive ? 1 : 0.95})`,
        opacity: isActive ? 1 : 0.5,
        zIndex: isActive ? 10 : 0,
      };
    }
    
    // Default slide transition
    const offset = (index - currentIndex) * 100;
    return {
      transform: `translateX(${offset}%)`,
    };
  };

  const renderSlide = (slide, index) => {
    const slideContent = slide.image ? (
      <div className="relative w-full h-full">
        <img
          src={slide.image}
          alt={slide.title || `Slide ${index + 1}`}
          className="w-full h-full object-cover"
        />
        {(slide.title || slide.description || slide.content) && (
          <div
            className={clsx(
              'absolute inset-0 flex flex-col justify-center px-4 sm:px-6 lg:px-8',
              variant === 'fullscreen' && 'bg-black bg-opacity-40',
              alignmentClasses[alignment]
            )}
          >
            {slide.title && (
              <h3 className="text-2xl sm:text-3xl md:text-4xl font-bold text-white mb-4">
                {slide.title}
              </h3>
            )}
            {slide.description && (
              <p className="text-base sm:text-lg md:text-xl text-white mb-6 max-w-2xl">
                {slide.description}
              </p>
            )}
            {slide.content && (
              <div className="text-white">{slide.content}</div>
            )}
            {/* Buttons */}
            {slide.buttons && slide.buttons.length > 0 && (
              <div
                className={clsx(
                  'flex flex-wrap gap-4 mt-6',
                  alignment === 'center' && 'justify-center',
                  alignment === 'right' && 'justify-end',
                  alignment === 'left' && 'justify-start'
                )}
              >
                {slide.buttons.map((button, btnIndex) => {
                  const ButtonComponent = button.href ? 'a' : 'button';
                  return (
                    <ButtonComponent
                      key={btnIndex}
                      href={button.href}
                      onClick={button.onClick}
                      className={clsx(
                        'px-6 py-3 sm:px-8 sm:py-4 rounded-lg font-semibold text-base sm:text-lg transition-all duration-300 transform hover:scale-105 focus:outline-none focus:ring-2 focus:ring-white focus:ring-offset-2 focus:ring-offset-transparent',
                        buttonVariantClasses[button.variant || 'primary'],
                        button.className
                      )}
                    >
                      {button.text}
                    </ButtonComponent>
                  );
                })}
              </div>
            )}
            {/* Single button (backward compatibility) */}
            {slide.button && !slide.buttons && (
              <div
                className={clsx(
                  'flex flex-wrap gap-4 mt-6',
                  alignment === 'center' && 'justify-center',
                  alignment === 'right' && 'justify-end',
                  alignment === 'left' && 'justify-start'
                )}
              >
                {(() => {
                  const ButtonComponent = slide.button.href ? 'a' : 'button';
                  return (
                    <ButtonComponent
                      href={slide.button.href}
                      onClick={slide.button.onClick}
                      className={clsx(
                        'px-6 py-3 sm:px-8 sm:py-4 rounded-lg font-semibold text-base sm:text-lg transition-all duration-300 transform hover:scale-105 focus:outline-none focus:ring-2 focus:ring-white focus:ring-offset-2 focus:ring-offset-transparent',
                        buttonVariantClasses[slide.button.variant || 'primary'],
                        slide.button.className
                      )}
                    >
                      {slide.button.text || 'Learn More'}
                    </ButtonComponent>
                  );
                })()}
              </div>
            )}
          </div>
        )}
      </div>
    ) : (
      <div
        className={clsx(
          'w-full h-full flex flex-col justify-center px-4 sm:px-6 lg:px-8',
          variant === 'card' && 'bg-white rounded-lg shadow-lg m-4',
          variant === 'minimal' && 'bg-transparent',
          variant === 'modern' && 'bg-gradient-to-br from-primary-50 to-purple-50 rounded-lg m-4',
          variant === 'classic' && 'bg-white border-2 border-gray-300 rounded m-4',
          alignmentClasses[alignment]
        )}
      >
        {slide.title && (
          <h3 className="text-2xl sm:text-3xl md:text-4xl font-bold text-gray-900 mb-4">
            {slide.title}
          </h3>
        )}
        {slide.description && (
          <p className="text-base sm:text-lg md:text-xl text-gray-600 mb-6 max-w-2xl">
            {slide.description}
          </p>
        )}
        {slide.content && (
          <div className="text-gray-700">{slide.content}</div>
        )}
        {/* Buttons */}
        {slide.buttons && slide.buttons.length > 0 && (
          <div
            className={clsx(
              'flex flex-wrap gap-4 mt-6',
              alignment === 'center' && 'justify-center',
              alignment === 'right' && 'justify-end',
              alignment === 'left' && 'justify-start'
            )}
          >
            {slide.buttons.map((button, btnIndex) => {
              const ButtonComponent = button.href ? 'a' : 'button';
              return (
                <ButtonComponent
                  key={btnIndex}
                  href={button.href}
                  onClick={button.onClick}
                    className={clsx(
                      'px-6 py-3 sm:px-8 sm:py-4 rounded-lg font-semibold text-base sm:text-lg transition-all duration-300 transform hover:scale-105 focus:outline-none focus:ring-2 focus:ring-primary-500 focus:ring-offset-2',
                      buttonVariantClasses[button.variant || 'primary'],
                      button.className
                    )}
                >
                  {button.text}
                </ButtonComponent>
              );
            })}
          </div>
        )}
        {/* Single button (backward compatibility) */}
        {slide.button && !slide.buttons && (
          <div
            className={clsx(
              'flex flex-wrap gap-4 mt-6',
              alignment === 'center' && 'justify-center',
              alignment === 'right' && 'justify-end',
              alignment === 'left' && 'justify-start'
            )}
          >
            {(() => {
              const ButtonComponent = slide.button.href ? 'a' : 'button';
              return (
                <ButtonComponent
                  href={slide.button.href}
                  onClick={slide.button.onClick}
                  className={clsx(
                    'px-6 py-3 sm:px-8 sm:py-4 rounded-lg font-semibold text-base sm:text-lg transition-all duration-300 transform hover:scale-105',
                    buttonVariantClasses[slide.button.variant || 'primary'],
                    slide.button.className
                  )}
                >
                  {slide.button.text || 'Learn More'}
                </ButtonComponent>
              );
            })()}
          </div>
        )}
      </div>
    );

    return (
      <div
        key={index}
        className={getSlideClasses(index)}
        style={getSlideStyle(index)}
      >
        {slideContent}
      </div>
    );
  };

  const renderArrows = () => {
    if (!showArrows || variant === 'minimal') return null;

    const arrowClasses = clsx(
      'absolute top-1/2 -translate-y-1/2 z-30',
      'w-10 h-10 sm:w-12 sm:h-12',
      'flex items-center justify-center',
      'bg-white bg-opacity-90 hover:bg-opacity-100',
      'rounded-full shadow-lg',
      'transition-all duration-300',
      'cursor-pointer',
      'text-gray-800 hover:text-primary-600',
      variant === 'fullscreen' && 'bg-black bg-opacity-50 text-white hover:bg-opacity-70',
      variant === 'modern' && 'bg-primary-600 text-white hover:bg-primary-700',
      variant === 'classic' && 'border-2 border-gray-300 hover:border-gray-400'
    );

    return (
      <>
        <button
          onClick={prevSlide}
          className={clsx(arrowClasses, 'left-2 sm:left-4')}
          aria-label="Previous slide"
        >
          <svg
            className="w-5 h-5 sm:w-6 sm:h-6"
            fill="none"
            stroke="currentColor"
            viewBox="0 0 24 24"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M15 19l-7-7 7-7"
            />
          </svg>
        </button>
        <button
          onClick={nextSlide}
          className={clsx(arrowClasses, 'right-2 sm:right-4')}
          aria-label="Next slide"
        >
          <svg
            className="w-5 h-5 sm:w-6 sm:h-6"
            fill="none"
            stroke="currentColor"
            viewBox="0 0 24 24"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M9 5l7 7-7 7"
            />
          </svg>
        </button>
      </>
    );
  };

  const renderDots = () => {
    if (!showDots || variant === 'minimal' || variant === 'thumbnails') return null;

    return (
      <div className="absolute bottom-4 left-1/2 -translate-x-1/2 z-30 flex gap-2">
        {slides.map((_, index) => {
          const isActive = index === currentIndex;
          return (
            <button
              key={index}
              onClick={() => goToSlide(index)}
              className={clsx(
                'w-2 h-2 sm:w-3 sm:h-3 rounded-full transition-all duration-300',
                isActive
                  ? variant === 'fullscreen'
                    ? 'bg-white w-8'
                    : variant === 'modern'
                    ? 'bg-primary-600 w-8'
                    : 'bg-primary-600 w-8'
                  : variant === 'fullscreen'
                  ? 'bg-white bg-opacity-50'
                  : 'bg-gray-400 hover:bg-gray-500'
              )}
              aria-label={`Go to slide ${index + 1}`}
            />
          );
        })}
      </div>
    );
  };

  const renderThumbnails = () => {
    if (!showThumbnails || variant !== 'thumbnails') return null;

    return (
      <div className="absolute bottom-0 left-0 right-0 z-30 bg-black bg-opacity-50 p-4 overflow-x-auto">
        <div className="flex gap-2 justify-center">
          {slides.map((slide, index) => {
            const isActive = index === currentIndex;
            return (
              <button
                key={index}
                onClick={() => goToSlide(index)}
                className={clsx(
                  'flex-shrink-0 w-20 h-20 sm:w-24 sm:h-24 rounded overflow-hidden border-2 transition-all duration-300',
                  isActive
                    ? 'border-primary-500 scale-110'
                    : 'border-transparent opacity-60 hover:opacity-100'
                )}
                aria-label={`Go to slide ${index + 1}`}
              >
                {slide.image ? (
                  <img
                    src={slide.image}
                    alt={slide.title || `Thumbnail ${index + 1}`}
                    className="w-full h-full object-cover"
                  />
                ) : (
                  <div className="w-full h-full bg-gray-300 flex items-center justify-center text-gray-600 text-xs">
                    {index + 1}
                  </div>
                )}
              </button>
            );
          })}
        </div>
      </div>
    );
  };

  return (
    <section
      id={id}
      className={clsx(
        'w-full py-12 sm:py-16 lg:py-20 px-4 sm:px-6 lg:px-8',
        variantClasses[variant] || variantClasses.default,
        className,
        dataClassName
      )}
    >
      <div className="max-w-7xl mx-auto w-full">
        {/* Header */}
        {(title || subtitle || description) && (
          <div
            className={clsx(
              'mb-10 sm:mb-12 flex flex-col',
              alignmentClasses[alignment]
            )}
          >
            {subtitle && (
              <p className="text-sm sm:text-base font-semibold text-primary-600 uppercase tracking-wider mb-3">
                {subtitle}
              </p>
            )}

            {title && (
              <h2 className="text-3xl sm:text-4xl md:text-5xl font-bold text-gray-900 mb-4 leading-tight">
                {title}
              </h2>
            )}

            {description && (
              <p
                className={clsx(
                  'text-base sm:text-lg text-gray-500 max-w-2xl',
                  alignment === 'center' && 'mx-auto',
                  alignment === 'right' && 'ml-auto',
                  alignment === 'left' && 'mr-auto'
                )}
              >
                {description}
              </p>
            )}
          </div>
        )}

        {/* Carousel */}
        <div
          ref={carouselRef}
          className={getCarouselClasses()}
          onMouseEnter={handleMouseEnter}
          onMouseLeave={handleMouseLeave}
        >
          {/* Slides */}
          <div className="relative w-full h-full">
            {slides.map((slide, index) => renderSlide(slide, index))}
          </div>

          {/* Navigation Arrows */}
          {renderArrows()}

          {/* Dot Indicators */}
          {renderDots()}

          {/* Thumbnail Navigation */}
          {renderThumbnails()}
        </div>
      </div>
    </section>
  );
};

