import { useEffect, useState } from 'react';
import clsx from 'clsx';
import '../../styles/product.css';

/**
 * ProductBadge Component - Sale/New/Hot badges with multiple variants
 * @param {Object} props - Component props
 * @param {Object} props.data - Badge configuration data
 * @param {string} [props.data.variant='default'] - Style variant: 'default' | 'modern' | 'minimal' | 'compact' | 'icon' | 'dark' | 'dark-gray'
 * @param {string} [props.data.type='new'] - Badge type: 'new' | 'sale' | 'hot' | 'featured' | 'custom'
 * @param {string} [props.data.text] - Custom badge text
 * @param {boolean} [props.loading=false] - Show skeleton loader
 * @param {string} [props.className] - Additional CSS classes
 */
export const ProductBadge = ({ 
  data = {}, 
  loading = false,
  className 
}) => {
  const {
    variant = 'default',
    type = 'new',
    text,
  } = data;

  const [showContent, setShowContent] = useState(!loading);

  useEffect(() => {
    if (loading) {
      setShowContent(false);
      const timer = setTimeout(() => {
        setShowContent(true);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowContent(true);
    }
  }, [loading]);

  const badgeText = text || {
    new: 'New',
    sale: 'Sale',
    hot: 'Hot',
    featured: 'Featured',
    custom: 'Custom'
  }[type] || 'New';

  const badgeClasses = {
    default: clsx(
      'inline-flex items-center px-2.5 sm:px-3 py-1 sm:py-1.5 text-xs sm:text-sm font-semibold rounded-md',
      type === 'new' && 'bg-blue-100 text-blue-800',
      type === 'sale' && 'bg-red-100 text-red-800',
      type === 'hot' && 'bg-orange-100 text-orange-800',
      type === 'featured' && 'bg-purple-100 text-purple-800',
      type === 'custom' && 'bg-gray-100 text-gray-800'
    ),
    modern: clsx(
      'inline-flex items-center px-3 sm:px-4 py-1.5 sm:py-2 text-xs sm:text-sm font-bold rounded-lg shadow-sm',
      type === 'new' && 'bg-gradient-to-r from-blue-500 to-blue-600 text-white',
      type === 'sale' && 'bg-gradient-to-r from-red-500 to-red-600 text-white',
      type === 'hot' && 'bg-gradient-to-r from-orange-500 to-orange-600 text-white',
      type === 'featured' && 'bg-gradient-to-r from-purple-500 to-purple-600 text-white',
      type === 'custom' && 'bg-gradient-to-r from-gray-500 to-gray-600 text-white'
    ),
    minimal: clsx(
      'inline-flex items-center px-2 py-0.5 text-xs font-medium border-b-2',
      type === 'new' && 'border-blue-500 text-blue-700',
      type === 'sale' && 'border-red-500 text-red-700',
      type === 'hot' && 'border-orange-500 text-orange-700',
      type === 'featured' && 'border-purple-500 text-purple-700',
      type === 'custom' && 'border-gray-500 text-gray-700'
    ),
    compact: clsx(
      'inline-flex items-center px-1.5 py-0.5 text-xs font-semibold rounded',
      type === 'new' && 'bg-blue-100 text-blue-800',
      type === 'sale' && 'bg-red-100 text-red-800',
      type === 'hot' && 'bg-orange-100 text-orange-800',
      type === 'featured' && 'bg-purple-100 text-purple-800',
      type === 'custom' && 'bg-gray-100 text-gray-800'
    ),
    icon: clsx(
      'inline-flex items-center justify-center w-8 h-8 rounded-full text-xs font-bold',
      type === 'new' && 'bg-blue-500 text-white',
      type === 'sale' && 'bg-red-500 text-white',
      type === 'hot' && 'bg-orange-500 text-white',
      type === 'featured' && 'bg-purple-500 text-white',
      type === 'custom' && 'bg-gray-500 text-white'
    ),
    dark: clsx(
      'inline-flex items-center px-2.5 sm:px-3 py-1 sm:py-1.5 text-xs sm:text-sm font-semibold rounded-md',
      type === 'new' && 'bg-blue-900 text-blue-200',
      type === 'sale' && 'bg-red-900 text-red-200',
      type === 'hot' && 'bg-orange-900 text-orange-200',
      type === 'featured' && 'bg-purple-900 text-purple-200',
      type === 'custom' && 'bg-gray-700 text-gray-200'
    ),
    'dark-gray': clsx(
      'inline-flex items-center px-2.5 sm:px-3 py-1 sm:py-1.5 text-xs sm:text-sm font-semibold rounded-md',
      type === 'new' && 'bg-blue-800 text-blue-100',
      type === 'sale' && 'bg-red-800 text-red-100',
      type === 'hot' && 'bg-orange-800 text-orange-100',
      type === 'featured' && 'bg-purple-800 text-purple-100',
      type === 'custom' && 'bg-gray-800 text-gray-100'
    ),
  };

  const SkeletonLoader = () => {
    const isDark = variant === 'dark' || variant === 'dark-gray';
    const skeletonBg = isDark ? 'bg-gray-700' : 'bg-gray-200';

    return (
      <div className={clsx(
        badgeClasses[variant] || badgeClasses.default,
        'product-skeleton w-12 h-6 sm:w-16 sm:h-7',
        skeletonBg
      )}></div>
    );
  };

  if (!showContent) {
    return <SkeletonLoader />;
  }

  return (
    <span className={clsx(badgeClasses[variant] || badgeClasses.default, className)}>
      {variant === 'icon' ? badgeText.charAt(0) : badgeText}
    </span>
  );
};

