import { useState, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/product.css';

/**
 * ProductFilter Component - Advanced filter dropdowns/chips with multiple variants
 * @param {Object} props - Component props
 * @param {Object} props.data - Filter configuration data
 * @param {string} [props.data.variant='default'] - Style variant: 'default' | 'modern' | 'minimal' | 'compact' | 'icon' | 'dark' | 'dark-gray'
 * @param {Array} [props.data.filters=[]] - Array of filter options
 * @param {string} [props.data.placeholder='Filter products...'] - Placeholder text
 * @param {boolean} [props.data.multiple=true] - Allow multiple selections
 * @param {Function} [props.onFilterChange] - Callback when filters change
 * @param {boolean} [props.loading=false] - Show skeleton loader
 * @param {string} [props.className] - Additional CSS classes
 */
export const ProductFilter = ({ 
  data = {}, 
  onFilterChange,
  loading = false,
  className 
}) => {
  const {
    variant = 'default',
    filters = [],
    placeholder = 'Filter products...',
    multiple = true,
  } = data;

  const [selectedFilters, setSelectedFilters] = useState([]);
  const [isOpen, setIsOpen] = useState(false);
  const [showContent, setShowContent] = useState(!loading);

  useEffect(() => {
    if (loading) {
      setShowContent(false);
      const timer = setTimeout(() => {
        setShowContent(true);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowContent(true);
    }
  }, [loading]);

  const handleFilterToggle = (filter) => {
    let newSelected;
    if (multiple) {
      newSelected = selectedFilters.includes(filter)
        ? selectedFilters.filter(f => f !== filter)
        : [...selectedFilters, filter];
    } else {
      newSelected = selectedFilters.includes(filter) ? [] : [filter];
      setIsOpen(false);
    }
    setSelectedFilters(newSelected);
    if (onFilterChange) {
      onFilterChange(newSelected);
    }
  };

  const handleClear = () => {
    setSelectedFilters([]);
    if (onFilterChange) {
      onFilterChange([]);
    }
  };

  // Container classes
  const containerClasses = {
    default: 'relative inline-block w-full sm:w-auto',
    modern: 'relative inline-block w-full sm:w-auto',
    minimal: 'relative inline-block w-full sm:w-auto',
    compact: 'relative inline-block w-full sm:w-auto',
    icon: 'relative inline-block',
    dark: 'relative inline-block w-full sm:w-auto',
    'dark-gray': 'relative inline-block w-full sm:w-auto',
  };

  // Button classes
  const buttonClasses = {
    default: clsx(
      'w-full sm:w-auto px-3 sm:px-4 py-2 sm:py-2.5 text-sm sm:text-base',
      'bg-white border border-gray-300 rounded-lg',
      'flex items-center justify-between gap-2',
      'hover:bg-gray-50 transition-all duration-200',
      'focus:outline-none focus:ring-2 focus:ring-primary-500'
    ),
    modern: clsx(
      'w-full sm:w-auto px-4 sm:px-5 py-2.5 sm:py-3 text-sm sm:text-base',
      'bg-white border-2 border-gray-200 rounded-xl shadow-sm',
      'flex items-center justify-between gap-2',
      'hover:shadow-md transition-all duration-300',
      'focus:outline-none focus:ring-2 focus:ring-primary-500'
    ),
    minimal: clsx(
      'w-full sm:w-auto px-3 sm:px-4 py-2 text-sm',
      'bg-transparent border-b-2 border-gray-300 rounded-none',
      'flex items-center justify-between gap-2',
      'hover:border-primary-500 transition-all duration-200',
      'focus:outline-none focus:border-primary-500'
    ),
    compact: clsx(
      'w-full sm:w-auto px-2 sm:px-3 py-1.5 text-xs sm:text-sm',
      'bg-white border border-gray-300 rounded-md',
      'flex items-center justify-between gap-1.5',
      'hover:bg-gray-50 transition-all duration-200',
      'focus:outline-none focus:ring-1 focus:ring-primary-500'
    ),
    icon: clsx(
      'p-2 sm:p-2.5 rounded-lg',
      'bg-primary-500 hover:bg-primary-600 text-white',
      'shadow-md hover:shadow-lg transition-all duration-300',
      'focus:outline-none focus:ring-2 focus:ring-primary-500 focus:ring-offset-2'
    ),
    dark: clsx(
      'w-full sm:w-auto px-3 sm:px-4 py-2 sm:py-2.5 text-sm sm:text-base',
      'bg-gray-800 border border-gray-600 rounded-lg text-white',
      'flex items-center justify-between gap-2',
      'hover:bg-primary-700 transition-all duration-200',
      'focus:outline-none focus:ring-2 focus:ring-primary-400'
    ),
    'dark-gray': clsx(
      'w-full sm:w-auto px-3 sm:px-4 py-2 sm:py-2.5 text-sm sm:text-base',
      'bg-gray-900 border border-gray-700 rounded-lg text-white',
      'flex items-center justify-between gap-2',
      'hover:bg-gray-800 transition-all duration-200',
      'focus:outline-none focus:ring-2 focus:ring-gray-500'
    ),
  };

  // Dropdown classes
  const dropdownClasses = {
    default: clsx(
      'absolute z-50 mt-1 w-full sm:w-64 bg-white border border-gray-300 rounded-lg shadow-lg',
      'max-h-60 overflow-auto',
      isOpen ? 'opacity-100 visible translate-y-0' : 'opacity-0 invisible -translate-y-2'
    ),
    modern: clsx(
      'absolute z-50 mt-2 w-full sm:w-72 bg-white border-2 border-gray-200 rounded-xl shadow-xl',
      'max-h-64 overflow-auto',
      isOpen ? 'opacity-100 visible translate-y-0' : 'opacity-0 invisible -translate-y-2'
    ),
    minimal: clsx(
      'absolute z-50 mt-1 w-full sm:w-64 bg-white border border-gray-200 rounded-lg shadow-md',
      'max-h-60 overflow-auto',
      isOpen ? 'opacity-100 visible translate-y-0' : 'opacity-0 invisible -translate-y-2'
    ),
    compact: clsx(
      'absolute z-50 mt-1 w-full sm:w-56 bg-white border border-gray-300 rounded-md shadow-lg',
      'max-h-48 overflow-auto',
      isOpen ? 'opacity-100 visible translate-y-0' : 'opacity-0 invisible -translate-y-2'
    ),
    icon: clsx(
      'absolute z-50 mt-2 right-0 w-64 sm:w-72 bg-white border border-gray-300 rounded-lg shadow-xl',
      'max-h-64 overflow-auto',
      isOpen ? 'opacity-100 visible translate-y-0' : 'opacity-0 invisible -translate-y-2'
    ),
    dark: clsx(
      'absolute z-50 mt-1 w-full sm:w-64 bg-gray-800 border border-gray-600 rounded-lg shadow-xl',
      'max-h-60 overflow-auto',
      isOpen ? 'opacity-100 visible translate-y-0' : 'opacity-0 invisible -translate-y-2'
    ),
    'dark-gray': clsx(
      'absolute z-50 mt-1 w-full sm:w-64 bg-gray-900 border border-gray-700 rounded-lg shadow-xl',
      'max-h-60 overflow-auto',
      isOpen ? 'opacity-100 visible translate-y-0' : 'opacity-0 invisible -translate-y-2'
    ),
  };

  // Skeleton Loader
  const SkeletonLoader = () => {
    const isDark = variant === 'dark' || variant === 'dark-gray';
    const skeletonBg = isDark ? 'bg-gray-700' : 'bg-gray-200';

    return (
      <div className={clsx(containerClasses[variant] || containerClasses.default, className)}>
        <div className={clsx(
          buttonClasses[variant] || buttonClasses.default,
          'product-skeleton h-10 sm:h-11'
        )}></div>
      </div>
    );
  };

  if (!showContent) {
    return <SkeletonLoader />;
  }

  const FilterIcon = () => (
    <svg className="w-4 h-4 sm:w-5 sm:h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 4a1 1 0 011-1h16a1 1 0 011 1v2.586a1 1 0 01-.293.707l-6.414 6.414a1 1 0 00-.293.707V17l-4 4v-6.586a1 1 0 00-.293-.707L3.293 7.293A1 1 0 013 6.586V4z" />
    </svg>
  );

  const ChevronIcon = () => (
    <svg className={clsx(
      'w-4 h-4 transition-transform duration-200',
      isOpen && 'transform rotate-180'
    )} fill="none" stroke="currentColor" viewBox="0 0 24 24">
      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
    </svg>
  );

  return (
    <div className={clsx(containerClasses[variant] || containerClasses.default, className)}>
      <button
        type="button"
        onClick={() => setIsOpen(!isOpen)}
        className={buttonClasses[variant] || buttonClasses.default}
      >
        {variant === 'icon' ? (
          <FilterIcon />
        ) : (
          <>
            <span className="flex items-center gap-2">
              {variant !== 'minimal' && <FilterIcon />}
              <span>
                {selectedFilters.length > 0 
                  ? `${selectedFilters.length} selected` 
                  : placeholder}
              </span>
            </span>
            <ChevronIcon />
          </>
        )}
      </button>

      {isOpen && (
        <>
          <div 
            className="fixed inset-0 z-40" 
            onClick={() => setIsOpen(false)}
          ></div>
          <div className={dropdownClasses[variant] || dropdownClasses.default}>
            {selectedFilters.length > 0 && (
              <div className={clsx(
                'p-2 border-b',
                variant === 'dark' || variant === 'dark-gray'
                  ? 'border-gray-600'
                  : 'border-gray-200'
              )}>
                <button
                  onClick={handleClear}
                  className={clsx(
                    'text-xs',
                    variant === 'dark' || variant === 'dark-gray'
                      ? 'text-white hover:text-gray-200'
                      : 'text-primary-600 hover:text-primary-700'
                  )}
                >
                  Clear all
                </button>
              </div>
            )}
            <div className="p-2">
              {filters.map((filter, index) => {
                const isSelected = selectedFilters.includes(filter);
                const isDark = variant === 'dark' || variant === 'dark-gray';
                return (
                  <label
                    key={index}
                    className={clsx(
                      'flex items-center gap-2 p-2 rounded cursor-pointer',
                      isDark
                        ? 'hover:bg-primary-700'
                        : 'hover:bg-gray-50',
                      isSelected && (isDark
                        ? 'bg-gray-700'
                        : 'bg-primary-50'
                      )
                    )}
                  >
                    <input
                      type={multiple ? 'checkbox' : 'radio'}
                      checked={isSelected}
                      onChange={() => handleFilterToggle(filter)}
                      className="w-4 h-4 text-primary-600"
                    />
                    <span className={clsx(
                      'text-sm',
                      isDark && 'text-white'
                    )}>{filter}</span>
                  </label>
                );
              })}
            </div>
          </div>
        </>
      )}
    </div>
  );
};

