import React, { useState, useEffect } from 'react';
import { clsx } from 'clsx';
import { CheckoutSkeleton } from '../CheckoutSkeleton';

export const CheckoutPremium = ({ data = {}, className, loading = false }) => {
  const [email, setEmail] = useState('');
  const [timedOut, setTimedOut] = useState(false);

  // Auto-show content after 2 seconds if loading is true
  useEffect(() => {
    if (loading) {
      setTimedOut(false);
      const timer = setTimeout(() => {
        setTimedOut(true);
      }, 2000);
      return () => clearTimeout(timer);
    }
  }, [loading]);

  const shouldShowSkeleton = loading && !timedOut;
  const cartItems = data.cartItems || [];
  const subtotal = cartItems.reduce((acc, item) => acc + item.price, 0);

  // Show skeleton loading state
  if (shouldShowSkeleton) {
    return <CheckoutSkeleton data={{ variant: 'premium' }} className={className} />;
  }

  return (
    <div className={clsx("checkout-premium min-h-screen bg-[#0a0a0a] text-white py-20 px-10", className)}>
      <div className="max-w-7xl mx-auto flex flex-col lg:flex-row gap-20">
        
        <div className="flex-1 space-y-20">
          <header className="space-y-4">
            <h1 className="text-6xl font-serif italic tracking-tight">{data.header?.title || 'Secure Checkout'}</h1>
            <p className="text-neutral-500 max-w-md uppercase text-[10px] tracking-[0.3em] font-bold">{data.header?.description || 'Refined shopping experience for premium collections.'}</p>
          </header>

          <div className="space-y-12">
            <section className="space-y-6">
              <label className="text-[10px] uppercase tracking-widest text-neutral-600 font-bold block ml-1">{data.email?.label || 'Account'}</label>
              <input type="email" placeholder={data.email?.placeholder || "Your Email"} className="w-full bg-transparent border-b border-neutral-800 py-6 text-2xl font-light focus:border-white transition-colors outline-none placeholder:text-neutral-200" />
            </section>

            <section className="space-y-6">
               <label className="text-[10px] uppercase tracking-widest text-neutral-600 font-bold block ml-1">{data.shipping?.title || 'Destination'}</label>
               <div className="grid grid-cols-2 gap-10">
                 <input type="text" placeholder={data.shipping?.fields?.firstName || "First Name"} className="w-full bg-transparent border-b border-neutral-800 py-6 text-xl font-light focus:border-white transition-colors outline-none placeholder:text-neutral-200" />
                 <input type="text" placeholder={data.shipping?.fields?.lastName || "Last Name"} className="w-full bg-transparent border-b border-neutral-800 py-6 text-xl font-light focus:border-white transition-colors outline-none placeholder:text-neutral-200" />
               </div>
               <input type="text" placeholder={data.shipping?.fields?.address || "Shipping Address"} className="w-full bg-transparent border-b border-neutral-800 py-6 text-xl font-light focus:border-white transition-colors outline-none placeholder:text-neutral-200" />
            </section>
          </div>
        </div>

        <div className="w-full lg:w-[450px]">
          <div className="bg-[#111111] p-12 border border-neutral-900 rounded-[40px] sticky top-20 shadow-2xl">
            <h2 className="text-sm font-bold uppercase tracking-[0.4em] text-neutral-600 mb-12">{data.summary?.title || 'Collection Summary'}</h2>
            <div className="space-y-8 mb-16">
              {cartItems.map((item, idx) => (
                <div key={idx} className="flex justify-between items-end group">
                  <div className="space-y-2">
                    <p className="text-lg font-serif italic">{item.name}</p>
                    <p className="text-[10px] uppercase text-neutral-600 tracking-widest font-bold">Qty 01</p>
                  </div>
                  <span className="text-xl font-light">${item.price.toFixed(2)}</span>
                </div>
              ))}
            </div>

            <div className="space-y-6 border-t border-neutral-900 pt-10">
              <div className="flex justify-between text-neutral-600">
                <span className="text-xs uppercase tracking-widest">{data.summary?.deliveryLabel || 'Complimentary Delivery'}</span>
                <span className="text-xs font-bold tracking-widest text-white">{data.summary?.deliveryValue || 'INCLUDED'}</span>
              </div>
              <div className="flex justify-between items-end pt-4">
                 <span className="text-xs uppercase tracking-widest text-neutral-500 mb-2">{data.summary?.totalLabel || 'Total Amount'}</span>
                 <span className="text-5xl font-serif italic tracking-tighter">${subtotal.toFixed(2)}</span>
              </div>
            </div>

            <button className="w-full bg-white text-black py-6 rounded-full mt-16 font-bold uppercase text-xs tracking-[0.3em] hover:bg-neutral-200 transition-colors">
              {data.summary?.buttonText || 'Authorize Transaction'}
            </button>

            <p className="text-center text-[9px] text-neutral-700 uppercase tracking-widest mt-8 leading-loose">
              {data.footer?.text || <>By placing your order, you agree to our <br/> luxury experience terms and privacy policies.</>}
            </p>
          </div>
        </div>

      </div>
    </div>
  );
};
