import { useState, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/blog.css';

/**
 * BlogSingle Component - Single blog post page layout
 * @param {Object} props - Component props
 * @param {Object} props.data - Blog post configuration data
 * @param {string} props.data.title - Article title
 * @param {string} [props.data.subtitle] - Article subtitle
 * @param {string} [props.data.content] - Article content (HTML string or JSX)
 * @param {string} [props.data.excerpt] - Article excerpt/summary
 * @param {string} [props.data.image] - Featured image URL
 * @param {string} [props.data.author] - Author name
 * @param {string} [props.data.authorImage] - Author avatar image URL
 * @param {string} [props.data.authorBio] - Author biography
 * @param {string} [props.data.date] - Publication date
 * @param {string} [props.data.updatedDate] - Last updated date
 * @param {string} [props.data.category] - Article category
 * @param {Array} [props.data.tags=[]] - Array of tag strings
 * @param {string} [props.data.readTime] - Estimated read time
 * @param {number} [props.data.views] - Number of views
 * @param {number} [props.data.likes] - Number of likes
 * @param {number} [props.data.comments] - Number of comments
 * @param {string} [props.data.variant='default'] - Style variant: 'default' | 'modern' | 'elegant' | 'minimal' | 'centered'
 * @param {string} [props.data.theme='light'] - Theme: 'light' | 'dark'
 * @param {boolean} [props.data.showAuthor=true] - Show author section
 * @param {boolean} [props.data.showTags=true] - Show tags section
 * @param {boolean} [props.data.showSocialShare=true] - Show social sharing buttons
 * @param {boolean} [props.data.showRelatedPosts=false] - Show related posts section
 * @param {Array} [props.data.relatedPosts=[]] - Array of related post objects
 * @param {string} [props.data.socialShareUrl] - URL for social sharing
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute
 * @param {boolean} [props.loader=false] - Show loading state
 */
export const BlogSingle = ({ data, className, id, loader = false }) => {
  // Safety check for data
  if (!data || typeof data !== 'object') {
    console.error('BlogSingle: data prop is required and must be an object');
    return null;
  }

  const [showLoader, setShowLoader] = useState(loader);

  // Auto-hide loader after 2 seconds when loader prop is true
  useEffect(() => {
    if (loader) {
      const timer = setTimeout(() => {
        setShowLoader(false);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowLoader(false);
    }
  }, [loader]);

  const {
    title,
    subtitle,
    content,
    excerpt,
    image,
    author,
    authorImage,
    authorBio,
    date,
    updatedDate,
    category,
    tags = [],
    readTime,
    views,
    likes,
    comments,
    variant = 'default',
    theme = 'light',
    showAuthor = true,
    showTags = true,
    showSocialShare = true,
    showRelatedPosts = false,
    relatedPosts = [],
    socialShareUrl = typeof window !== 'undefined' ? window.location.href : '',
    className: dataClassName,
  } = data;

  // Social sharing handlers
  const handleSocialShare = (platform) => {
    const encodedUrl = encodeURIComponent(socialShareUrl);
    const encodedTitle = encodeURIComponent(title || '');
    const encodedDescription = encodeURIComponent(excerpt || '');

    const shareUrls = {
      twitter: `https://twitter.com/intent/tweet?url=${encodedUrl}&text=${encodedTitle}`,
      facebook: `https://www.facebook.com/sharer/sharer.php?u=${encodedUrl}`,
      linkedin: `https://www.linkedin.com/sharing/share-offsite/?url=${encodedUrl}`,
      whatsapp: `https://wa.me/?text=${encodedTitle} ${encodedUrl}`,
      telegram: `https://t.me/share/url?url=${encodedUrl}&text=${encodedTitle}`,
      copy: () => {
        if (navigator.clipboard) {
          navigator.clipboard.writeText(socialShareUrl);
          alert('Link copied to clipboard!');
        }
      },
    };

    if (platform === 'copy') {
      shareUrls.copy();
    } else if (shareUrls[platform]) {
      window.open(shareUrls[platform], '_blank', 'width=600,height=400');
    }
  };

  const containerClasses = clsx(
    'blog-single',
    `blog-single-${variant}`,
    `blog-single-${theme}`,
    className,
    dataClassName
  );

  // Loading skeleton
  if (showLoader) {
    return (
      <article className={containerClasses} id={id}>
        <div className="blog-single-skeleton">
          <div className="skeleton-image-full"></div>
          <div className="blog-single-container">
            <div className="skeleton-line skeleton-line-title"></div>
            <div className="skeleton-line"></div>
            <div className="skeleton-line skeleton-line-short"></div>
            <div className="skeleton-content">
              <div className="skeleton-line"></div>
              <div className="skeleton-line"></div>
              <div className="skeleton-line"></div>
              <div className="skeleton-line skeleton-line-short"></div>
            </div>
          </div>
        </div>
      </article>
    );
  }

  // Default variant - Standard blog post layout
  if (variant === 'default') {
    return (
      <article className={containerClasses} id={id}>
        {/* Hero Image */}
        {image && (
          <div className="blog-single-hero">
            <img src={image} alt={title} className="blog-single-hero-image" />
          </div>
        )}

        <div className="blog-single-container">
          {/* Header */}
          <header className="blog-single-header">
            {category && (
              <span className="blog-single-category">{category}</span>
            )}
            <h1 className="blog-single-title">{title}</h1>
            {subtitle && (
              <p className="blog-single-subtitle">{subtitle}</p>
            )}
            {excerpt && (
              <p className="blog-single-excerpt">{excerpt}</p>
            )}

            {/* Meta Information */}
            <div className="blog-single-meta">
              {authorImage && (
                <img src={authorImage} alt={author} className="blog-single-author-avatar" />
              )}
              <div className="blog-single-meta-info">
                {author && (
                  <span className="blog-single-author">{author}</span>
                )}
                {date && (
                  <span className="blog-single-date">{date}</span>
                )}
                {readTime && (
                  <span className="blog-single-read-time">{readTime}</span>
                )}
              </div>
            </div>

            {/* Engagement Stats */}
            {(views !== undefined || likes !== undefined || comments !== undefined) && (
              <div className="blog-single-stats">
                {views !== undefined && (
                  <span className="blog-single-stat">
                    <svg className="blog-single-stat-icon" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 12a3 3 0 11-6 0 3 3 0 016 0z" />
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M2.458 12C3.732 7.943 7.523 5 12 5c4.478 0 8.268 2.943 9.542 7-1.274 4.057-5.064 7-9.542 7-4.477 0-8.268-2.943-9.542-7z" />
                    </svg>
                    {views}
                  </span>
                )}
                {likes !== undefined && (
                  <span className="blog-single-stat">
                    <svg className="blog-single-stat-icon" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4.318 6.318a4.5 4.5 0 000 6.364L12 20.364l7.682-7.682a4.5 4.5 0 00-6.364-6.364L12 7.636l-1.318-1.318a4.5 4.5 0 00-6.364 0z" />
                    </svg>
                    {likes}
                  </span>
                )}
                {comments !== undefined && (
                  <span className="blog-single-stat">
                    <svg className="blog-single-stat-icon" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 12h.01M12 12h.01M16 12h.01M21 12c0 4.418-4.03 8-9 8a9.863 9.863 0 01-4.255-.949L3 20l1.395-3.72C3.512 15.042 3 13.574 3 12c0-4.418 4.03-8 9-8s9 3.582 9 8z" />
                    </svg>
                    {comments}
                  </span>
                )}
              </div>
            )}
          </header>

          {/* Content */}
          {content && (
            <div className="blog-single-content">
              {typeof content === 'string' ? (
                <div dangerouslySetInnerHTML={{ __html: content }} />
              ) : (
                content
              )}
            </div>
          )}

          {/* Tags */}
          {showTags && tags.length > 0 && (
            <div className="blog-single-tags">
              <span className="blog-single-tags-label">Tags:</span>
              {tags.map((tag, index) => (
                <span key={index} className="blog-single-tag">{tag}</span>
              ))}
            </div>
          )}

          {/* Social Share */}
          {showSocialShare && (
            <div className="blog-single-social">
              <span className="blog-single-social-label">Share:</span>
              <button onClick={() => handleSocialShare('twitter')} className="blog-single-social-btn" aria-label="Share on Twitter">
                <svg fill="currentColor" viewBox="0 0 24 24" className="blog-single-social-icon">
                  <path d="M8.29 20.251c7.547 0 11.675-6.253 11.675-11.675 0-.178 0-.355-.012-.53A8.348 8.348 0 0022 5.92a8.19 8.19 0 01-2.357.646 4.118 4.118 0 001.804-2.27 8.224 8.224 0 01-2.605.996 4.107 4.107 0 00-6.993 3.743 11.65 11.65 0 01-8.457-4.287 4.106 4.106 0 001.27 5.477A4.072 4.072 0 012.8 9.713v.052a4.105 4.105 0 003.292 4.022 4.095 4.095 0 01-1.853.07 4.108 4.108 0 003.834 2.85A8.233 8.233 0 012 18.407a11.616 11.616 0 006.29 1.84" />
                </svg>
              </button>
              <button onClick={() => handleSocialShare('facebook')} className="blog-single-social-btn" aria-label="Share on Facebook">
                <svg fill="currentColor" viewBox="0 0 24 24" className="blog-single-social-icon">
                  <path fillRule="evenodd" d="M22 12c0-5.523-4.477-10-10-10S2 6.477 2 12c0 4.991 3.657 9.128 8.438 9.878v-6.987h-2.54V12h2.54V9.797c0-2.506 1.492-3.89 3.777-3.89 1.094 0 2.238.195 2.238.195v2.46h-1.26c-1.243 0-1.63.771-1.63 1.562V12h2.773l-.443 2.89h-2.33v6.988C18.343 21.128 22 16.991 22 12z" clipRule="evenodd" />
                </svg>
              </button>
              <button onClick={() => handleSocialShare('linkedin')} className="blog-single-social-btn" aria-label="Share on LinkedIn">
                <svg fill="currentColor" viewBox="0 0 24 24" className="blog-single-social-icon">
                  <path fillRule="evenodd" d="M19 0h-14c-2.761 0-5 2.239-5 5v14c0 2.761 2.239 5 5 5h14c2.762 0 5-2.239 5-5v-14c0-2.761-2.238-5-5-5zm-11 19h-3v-11h3v11zm-1.5-12.268c-.966 0-1.75-.79-1.75-1.764s.784-1.764 1.75-1.764 1.75.79 1.75 1.764-.783 1.764-1.75 1.764zm13.5 12.268h-3v-5.604c0-3.368-4-3.113-4 0v5.604h-3v-11h3v1.765c1.396-2.586 7-2.777 7 2.476v6.759z" clipRule="evenodd" />
                </svg>
              </button>
              <button onClick={() => handleSocialShare('copy')} className="blog-single-social-btn" aria-label="Copy link">
                <svg fill="none" stroke="currentColor" viewBox="0 0 24 24" className="blog-single-social-icon">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 16H6a2 2 0 01-2-2V6a2 2 0 012-2h8a2 2 0 012 2v2m-6 12h8a2 2 0 002-2v-8a2 2 0 00-2-2h-8a2 2 0 00-2 2v8a2 2 0 002 2z" />
                </svg>
              </button>
            </div>
          )}

          {/* Author Section */}
          {showAuthor && author && (
            <div className="blog-single-author-section">
              {authorImage && (
                <img src={authorImage} alt={author} className="blog-single-author-section-avatar" />
              )}
              <div className="blog-single-author-section-info">
                <h3 className="blog-single-author-section-name">{author}</h3>
                {authorBio && (
                  <p className="blog-single-author-section-bio">{authorBio}</p>
                )}
              </div>
            </div>
          )}

          {/* Related Posts */}
          {showRelatedPosts && relatedPosts.length > 0 && (
            <div className="blog-single-related">
              <h2 className="blog-single-related-title">Related Posts</h2>
              <div className="blog-single-related-grid">
                {relatedPosts.map((post, index) => (
                  <a key={index} href={post.href || '#'} className="blog-single-related-card">
                    {post.image && (
                      <img src={post.image} alt={post.title} className="blog-single-related-image" />
                    )}
                    <h3 className="blog-single-related-card-title">{post.title}</h3>
                    {post.date && (
                      <span className="blog-single-related-card-date">{post.date}</span>
                    )}
                  </a>
                ))}
              </div>
            </div>
          )}
        </div>
      </article>
    );
  }

  // Modern variant - Contemporary design with enhanced visuals
  if (variant === 'modern') {
    return (
      <article className={containerClasses} id={id}>
        {/* Hero Image with Overlay */}
        {image && (
          <div className="blog-single-hero-modern">
            <div className="blog-single-hero-modern-overlay"></div>
            <img src={image} alt={title} className="blog-single-hero-modern-image" />
            <div className="blog-single-hero-modern-content">
              {category && (
                <span className="blog-single-category-modern">{category}</span>
              )}
              <h1 className="blog-single-title-modern">{title}</h1>
              {subtitle && (
                <p className="blog-single-subtitle-modern">{subtitle}</p>
              )}
            </div>
          </div>
        )}

        <div className="blog-single-container-modern">
          {/* Meta Bar */}
          <div className="blog-single-meta-modern">
            <div className="blog-single-meta-modern-left">
              {authorImage && (
                <img src={authorImage} alt={author} className="blog-single-meta-avatar-modern" />
              )}
              <div>
                {author && <span className="blog-single-meta-author-modern">{author}</span>}
                <div className="blog-single-meta-details-modern">
                  {date && <span>{date}</span>}
                  {readTime && <span> {readTime}</span>}
                  {views !== undefined && <span> {views} views</span>}
                </div>
              </div>
            </div>
            {showSocialShare && (
              <div className="blog-single-meta-modern-right">
                {['twitter', 'facebook', 'linkedin', 'copy'].map((platform) => (
                  <button
                    key={platform}
                    onClick={() => handleSocialShare(platform)}
                    className="blog-single-social-btn-modern"
                    aria-label={`Share on ${platform}`}
                  >
                    {platform === 'twitter' && (
                      <svg fill="currentColor" viewBox="0 0 24 24" className="w-5 h-5">
                        <path d="M8.29 20.251c7.547 0 11.675-6.253 11.675-11.675 0-.178 0-.355-.012-.53A8.348 8.348 0 0022 5.92a8.19 8.19 0 01-2.357.646 4.118 4.118 0 001.804-2.27 8.224 8.224 0 01-2.605.996 4.107 4.107 0 00-6.993 3.743 11.65 11.65 0 01-8.457-4.287 4.106 4.106 0 001.27 5.477A4.072 4.072 0 012.8 9.713v.052a4.105 4.105 0 003.292 4.022 4.095 4.095 0 01-1.853.07 4.108 4.108 0 003.834 2.85A8.233 8.233 0 012 18.407a11.616 11.616 0 006.29 1.84" />
                      </svg>
                    )}
                    {platform === 'facebook' && (
                      <svg fill="currentColor" viewBox="0 0 24 24" className="w-5 h-5">
                        <path fillRule="evenodd" d="M22 12c0-5.523-4.477-10-10-10S2 6.477 2 12c0 4.991 3.657 9.128 8.438 9.878v-6.987h-2.54V12h2.54V9.797c0-2.506 1.492-3.89 3.777-3.89 1.094 0 2.238.195 2.238.195v2.46h-1.26c-1.243 0-1.63.771-1.63 1.562V12h2.773l-.443 2.89h-2.33v6.988C18.343 21.128 22 16.991 22 12z" clipRule="evenodd" />
                      </svg>
                    )}
                    {platform === 'linkedin' && (
                      <svg fill="currentColor" viewBox="0 0 24 24" className="w-5 h-5">
                        <path fillRule="evenodd" d="M19 0h-14c-2.761 0-5 2.239-5 5v14c0 2.761 2.239 5 5 5h14c2.762 0 5-2.239 5-5v-14c0-2.761-2.238-5-5-5zm-11 19h-3v-11h3v11zm-1.5-12.268c-.966 0-1.75-.79-1.75-1.764s.784-1.764 1.75-1.764 1.75.79 1.75 1.764-.783 1.764-1.75 1.764zm13.5 12.268h-3v-5.604c0-3.368-4-3.113-4 0v5.604h-3v-11h3v1.765c1.396-2.586 7-2.777 7 2.476v6.759z" clipRule="evenodd" />
                      </svg>
                    )}
                    {platform === 'copy' && (
                      <svg fill="none" stroke="currentColor" viewBox="0 0 24 24" className="w-5 h-5">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 16H6a2 2 0 01-2-2V6a2 2 0 012-2h8a2 2 0 012 2v2m-6 12h8a2 2 0 002-2v-8a2 2 0 00-2-2h-8a2 2 0 00-2 2v8a2 2 0 002 2z" />
                      </svg>
                    )}
                  </button>
                ))}
              </div>
            )}
          </div>

          {/* Content */}
          {content && (
            <div className="blog-single-content-modern">
              {typeof content === 'string' ? (
                <div dangerouslySetInnerHTML={{ __html: content }} />
              ) : (
                content
              )}
            </div>
          )}

          {/* Tags */}
          {showTags && tags.length > 0 && (
            <div className="blog-single-tags-modern">
              {tags.map((tag, index) => (
                <span key={index} className="blog-single-tag-modern">{tag}</span>
              ))}
            </div>
          )}

          {/* Author Section */}
          {showAuthor && author && (
            <div className="blog-single-author-section-modern">
              {authorImage && (
                <img src={authorImage} alt={author} className="blog-single-author-section-avatar-modern" />
              )}
              <div className="blog-single-author-section-info-modern">
                <h3 className="blog-single-author-section-name-modern">{author}</h3>
                {authorBio && (
                  <p className="blog-single-author-section-bio-modern">{authorBio}</p>
                )}
              </div>
            </div>
          )}
        </div>
      </article>
    );
  }

  // Elegant variant - Sophisticated and refined design
  if (variant === 'elegant') {
    return (
      <article className={containerClasses} id={id}>
        <div className="blog-single-container-elegant">
          {/* Header */}
          <header className="blog-single-header-elegant">
            {category && (
              <span className="blog-single-category-elegant">{category}</span>
            )}
            <h1 className="blog-single-title-elegant">{title}</h1>
            {subtitle && (
              <p className="blog-single-subtitle-elegant">{subtitle}</p>
            )}
            {excerpt && (
              <p className="blog-single-excerpt-elegant">{excerpt}</p>
            )}

            {/* Divider */}
            <div className="blog-single-divider-elegant"></div>

            {/* Meta */}
            <div className="blog-single-meta-elegant">
              {authorImage && (
                <img src={authorImage} alt={author} className="blog-single-meta-avatar-elegant" />
              )}
              <div className="blog-single-meta-info-elegant">
                <span className="blog-single-meta-author-elegant">{author}</span>
                <span className="blog-single-meta-date-elegant">{date}</span>
                {readTime && (
                  <span className="blog-single-meta-read-time-elegant">{readTime}</span>
                )}
              </div>
            </div>
          </header>

          {/* Featured Image */}
          {image && (
            <div className="blog-single-image-elegant">
              <img src={image} alt={title} />
            </div>
          )}

          {/* Content */}
          {content && (
            <div className="blog-single-content-elegant">
              {typeof content === 'string' ? (
                <div dangerouslySetInnerHTML={{ __html: content }} />
              ) : (
                content
              )}
            </div>
          )}

          {/* Footer Section */}
          <footer className="blog-single-footer-elegant">
            {/* Tags */}
            {showTags && tags.length > 0 && (
              <div className="blog-single-tags-elegant">
                <span className="blog-single-tags-label-elegant">Tags:</span>
                <div className="blog-single-tags-list-elegant">
                  {tags.map((tag, index) => (
                    <span key={index} className="blog-single-tag-elegant">{tag}</span>
                  ))}
                </div>
              </div>
            )}

            {/* Social Share */}
            {showSocialShare && (
              <div className="blog-single-social-elegant">
                <span className="blog-single-social-label-elegant">Share Article:</span>
                <div className="blog-single-social-buttons-elegant">
                  {['twitter', 'facebook', 'linkedin', 'copy'].map((platform) => (
                    <button
                      key={platform}
                      onClick={() => handleSocialShare(platform)}
                      className="blog-single-social-btn-elegant"
                      aria-label={`Share on ${platform}`}
                    >
                      {platform === 'twitter' && (
                        <svg fill="currentColor" viewBox="0 0 24 24" className="w-4 h-4">
                          <path d="M8.29 20.251c7.547 0 11.675-6.253 11.675-11.675 0-.178 0-.355-.012-.53A8.348 8.348 0 0022 5.92a8.19 8.19 0 01-2.357.646 4.118 4.118 0 001.804-2.27 8.224 8.224 0 01-2.605.996 4.107 4.107 0 00-6.993 3.743 11.65 11.65 0 01-8.457-4.287 4.106 4.106 0 001.27 5.477A4.072 4.072 0 012.8 9.713v.052a4.105 4.105 0 003.292 4.022 4.095 4.095 0 01-1.853.07 4.108 4.108 0 003.834 2.85A8.233 8.233 0 012 18.407a11.616 11.616 0 006.29 1.84" />
                        </svg>
                      )}
                      {platform === 'facebook' && (
                        <svg fill="currentColor" viewBox="0 0 24 24" className="w-4 h-4">
                          <path fillRule="evenodd" d="M22 12c0-5.523-4.477-10-10-10S2 6.477 2 12c0 4.991 3.657 9.128 8.438 9.878v-6.987h-2.54V12h2.54V9.797c0-2.506 1.492-3.89 3.777-3.89 1.094 0 2.238.195 2.238.195v2.46h-1.26c-1.243 0-1.63.771-1.63 1.562V12h2.773l-.443 2.89h-2.33v6.988C18.343 21.128 22 16.991 22 12z" clipRule="evenodd" />
                        </svg>
                      )}
                      {platform === 'linkedin' && (
                        <svg fill="currentColor" viewBox="0 0 24 24" className="w-4 h-4">
                          <path fillRule="evenodd" d="M19 0h-14c-2.761 0-5 2.239-5 5v14c0 2.761 2.239 5 5 5h14c2.762 0 5-2.239 5-5v-14c0-2.761-2.238-5-5-5zm-11 19h-3v-11h3v11zm-1.5-12.268c-.966 0-1.75-.79-1.75-1.764s.784-1.764 1.75-1.764 1.75.79 1.75 1.764-.783 1.764-1.75 1.764zm13.5 12.268h-3v-5.604c0-3.368-4-3.113-4 0v5.604h-3v-11h3v1.765c1.396-2.586 7-2.777 7 2.476v6.759z" clipRule="evenodd" />
                        </svg>
                      )}
                      {platform === 'copy' && (
                        <svg fill="none" stroke="currentColor" viewBox="0 0 24 24" className="w-4 h-4">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 16H6a2 2 0 01-2-2V6a2 2 0 012-2h8a2 2 0 012 2v2m-6 12h8a2 2 0 002-2v-8a2 2 0 00-2-2h-8a2 2 0 00-2 2v8a2 2 0 002 2z" />
                        </svg>
                      )}
                    </button>
                  ))}
                </div>
              </div>
            )}

            {/* Author Section */}
            {showAuthor && author && (
              <div className="blog-single-author-section-elegant">
                {authorImage && (
                  <img src={authorImage} alt={author} className="blog-single-author-section-avatar-elegant" />
                )}
                <div className="blog-single-author-section-info-elegant">
                  <h3 className="blog-single-author-section-name-elegant">About {author}</h3>
                  {authorBio && (
                    <p className="blog-single-author-section-bio-elegant">{authorBio}</p>
                  )}
                </div>
              </div>
            )}
          </footer>
        </div>
      </article>
    );
  }

  // Minimal variant - Clean and simple design
  if (variant === 'minimal') {
    return (
      <article className={containerClasses} id={id}>
        <div className="blog-single-container-minimal">
          {/* Header */}
          <header className="blog-single-header-minimal">
            <h1 className="blog-single-title-minimal">{title}</h1>
            <div className="blog-single-meta-minimal">
              {date && <span>{date}</span>}
              {readTime && <span> {readTime}</span>}
              {category && <span> {category}</span>}
            </div>
          </header>

          {/* Featured Image */}
          {image && (
            <div className="blog-single-image-minimal">
              <img src={image} alt={title} />
            </div>
          )}

          {/* Content */}
          {content && (
            <div className="blog-single-content-minimal">
              {typeof content === 'string' ? (
                <div dangerouslySetInnerHTML={{ __html: content }} />
              ) : (
                content
              )}
            </div>
          )}

          {/* Footer */}
          <footer className="blog-single-footer-minimal">
            {showTags && tags.length > 0 && (
              <div className="blog-single-tags-minimal">
                {tags.map((tag, index) => (
                  <span key={index} className="blog-single-tag-minimal">#{tag}</span>
                ))}
              </div>
            )}
            {showAuthor && author && (
              <div className="blog-single-author-minimal">
                {authorImage && (
                  <img src={authorImage} alt={author} className="blog-single-author-avatar-minimal" />
                )}
                <span>{author}</span>
              </div>
            )}
          </footer>
        </div>
      </article>
    );
  }

  // Centered variant - Center-aligned content layout
  if (variant === 'centered') {
    return (
      <article className={containerClasses} id={id}>
        {/* Hero Image */}
        {image && (
          <div className="blog-single-hero-centered">
            <img src={image} alt={title} className="blog-single-hero-image-centered" />
          </div>
        )}

        <div className="blog-single-container-centered">
          {/* Header */}
          <header className="blog-single-header-centered">
            {category && (
              <span className="blog-single-category-centered">{category}</span>
            )}
            <h1 className="blog-single-title-centered">{title}</h1>
            {subtitle && (
              <p className="blog-single-subtitle-centered">{subtitle}</p>
            )}
            {excerpt && (
              <p className="blog-single-excerpt-centered">{excerpt}</p>
            )}

            {/* Meta */}
            <div className="blog-single-meta-centered">
              {authorImage && (
                <img src={authorImage} alt={author} className="blog-single-meta-avatar-centered" />
              )}
              <div>
                {author && <span className="blog-single-meta-author-centered">{author}</span>}
                <div>
                  {date && <span>{date}</span>}
                  {readTime && <span>  {readTime}</span>}
                </div>
              </div>
            </div>
          </header>

          {/* Content */}
          {content && (
            <div className="blog-single-content-centered">
              {typeof content === 'string' ? (
                <div dangerouslySetInnerHTML={{ __html: content }} />
              ) : (
                content
              )}
            </div>
          )}

          {/* Footer */}
          <footer className="blog-single-footer-centered">
            {showTags && tags.length > 0 && (
              <div className="blog-single-tags-centered">
                {tags.map((tag, index) => (
                  <span key={index} className="blog-single-tag-centered">{tag}</span>
                ))}
              </div>
            )}

            {showSocialShare && (
              <div className="blog-single-social-centered">
                {['twitter', 'facebook', 'linkedin', 'copy'].map((platform) => (
                  <button
                    key={platform}
                    onClick={() => handleSocialShare(platform)}
                    className="blog-single-social-btn-centered"
                    aria-label={`Share on ${platform}`}
                  >
                    {platform === 'twitter' && (
                      <svg fill="currentColor" viewBox="0 0 24 24" className="w-5 h-5">
                        <path d="M8.29 20.251c7.547 0 11.675-6.253 11.675-11.675 0-.178 0-.355-.012-.53A8.348 8.348 0 0022 5.92a8.19 8.19 0 01-2.357.646 4.118 4.118 0 001.804-2.27 8.224 8.224 0 01-2.605.996 4.107 4.107 0 00-6.993 3.743 11.65 11.65 0 01-8.457-4.287 4.106 4.106 0 001.27 5.477A4.072 4.072 0 012.8 9.713v.052a4.105 4.105 0 003.292 4.022 4.095 4.095 0 01-1.853.07 4.108 4.108 0 003.834 2.85A8.233 8.233 0 012 18.407a11.616 11.616 0 006.29 1.84" />
                      </svg>
                    )}
                    {platform === 'facebook' && (
                      <svg fill="currentColor" viewBox="0 0 24 24" className="w-5 h-5">
                        <path fillRule="evenodd" d="M22 12c0-5.523-4.477-10-10-10S2 6.477 2 12c0 4.991 3.657 9.128 8.438 9.878v-6.987h-2.54V12h2.54V9.797c0-2.506 1.492-3.89 3.777-3.89 1.094 0 2.238.195 2.238.195v2.46h-1.26c-1.243 0-1.63.771-1.63 1.562V12h2.773l-.443 2.89h-2.33v6.988C18.343 21.128 22 16.991 22 12z" clipRule="evenodd" />
                      </svg>
                    )}
                    {platform === 'linkedin' && (
                      <svg fill="currentColor" viewBox="0 0 24 24" className="w-5 h-5">
                        <path fillRule="evenodd" d="M19 0h-14c-2.761 0-5 2.239-5 5v14c0 2.761 2.239 5 5 5h14c2.762 0 5-2.239 5-5v-14c0-2.761-2.238-5-5-5zm-11 19h-3v-11h3v11zm-1.5-12.268c-.966 0-1.75-.79-1.75-1.764s.784-1.764 1.75-1.764 1.75.79 1.75 1.764-.783 1.764-1.75 1.764zm13.5 12.268h-3v-5.604c0-3.368-4-3.113-4 0v5.604h-3v-11h3v1.765c1.396-2.586 7-2.777 7 2.476v6.759z" clipRule="evenodd" />
                      </svg>
                    )}
                    {platform === 'copy' && (
                      <svg fill="none" stroke="currentColor" viewBox="0 0 24 24" className="w-5 h-5">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 16H6a2 2 0 01-2-2V6a2 2 0 012-2h8a2 2 0 012 2v2m-6 12h8a2 2 0 002-2v-8a2 2 0 00-2-2h-8a2 2 0 00-2 2v8a2 2 0 002 2z" />
                      </svg>
                    )}
                  </button>
                ))}
              </div>
            )}

            {showAuthor && author && (
              <div className="blog-single-author-section-centered">
                {authorImage && (
                  <img src={authorImage} alt={author} className="blog-single-author-section-avatar-centered" />
                )}
                <div>
                  <h3 className="blog-single-author-section-name-centered">{author}</h3>
                  {authorBio && (
                    <p className="blog-single-author-section-bio-centered">{authorBio}</p>
                  )}
                </div>
              </div>
            )}
          </footer>
        </div>
      </article>
    );
  }

  // Fallback to default
  return null;
};