import { useState, useRef, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/faq-modern.css';

/**
 * FaqModern Component - Modern sleek FAQ layout with side-by-side design
 * @param {Object} props - Component props
 * @param {Object} props.data - FAQ section configuration data
 * @param {string} [props.data.title] - Main heading text
 * @param {string} [props.data.subtitle] - Subtitle text displayed above title
 * @param {string} [props.data.description] - Description text displayed below title
 * @param {Array} props.data.items - Array of FAQ items with question and answer
 * @param {string} [props.data.alignment='center'] - Text alignment: 'left' | 'center' | 'right'
 * @param {string} [props.data.variant='default'] - FAQ style variant: 'default' | 'minimal' | 'accent' | 'glass' | 'backgroundImage'
 * @param {string} [props.data.backgroundImage] - Background image URL for the section
 * @param {number} [props.data.backgroundOverlay=0.5] - Background overlay opacity (0-1)
 * @param {string} [props.data.backgroundPosition='center'] - Background image position
 * @param {string} [props.data.primaryColor] - Custom primary color (hex, rgb, or CSS color)
 * @param {string} [props.data.accentColor] - Custom accent color (hex, rgb, or CSS color)
 * @param {string} [props.data.textColor] - Custom text color for title/subtitle (hex, rgb, or CSS color)
 * @param {string} [props.data.hoverColor] - Custom hover color (hex, rgb, or CSS color)
 * @param {boolean} [props.data.allowMultipleOpen=false] - Allow multiple items to be open at once
 * @param {string} [props.data.className] - Additional CSS classes for the section
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute for the section element

 */
export const FaqModern = ({ data, className, id, loader = false }) => {
  // Safety check for data
  if (!data || typeof data !== 'object') {
    console.error('FaqModern: data prop is required and must be an object');
    return null;
  }

  const [showLoader, setShowLoader] = useState(loader);

  // Auto-hide loader after 2 seconds when loader prop is true
  useEffect(() => {
    if (loader) {
      const timer = setTimeout(() => {
        setShowLoader(false);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowLoader(false);
    }
  }, [loader]);

  // Show skeleton loader if loader prop is true
  if (showLoader) {
    const { alignment = 'center', variant = 'default', className: dataClassName } = data || {};
    const alignmentClasses = {
      left: 'text-left items-start',
      center: 'text-center items-center',
      right: 'text-right items-end',
    };
    const variantClasses = {
      default: 'bg-white',
      minimal: 'bg-transparent relative',
      accent: 'bg-gradient-to-br from-primary-50 via-white to-primary-50',
      glass: 'bg-white/80 backdrop-blur-lg',
      backgroundImage: 'relative',
    };

    return (
      <section
        id={id}
        className={clsx(
          'w-full py-12 sm:py-16 lg:py-20 px-4 sm:px-6 lg:px-8',
          variantClasses[variant],
          className,
          dataClassName
        )}
      >
        <div className="max-w-4xl mx-auto w-full">
          {/* Header skeleton */}
          <div className={clsx('mb-10 sm:mb-12 flex flex-col', alignmentClasses[alignment])}>
            <div className="h-4 bg-gray-300 rounded w-24 mb-3 mx-auto animate-pulse"></div>
            <div className="h-10 bg-gray-300 rounded w-full max-w-md mb-4 mx-auto animate-pulse"></div>
            <div className="h-5 bg-gray-300 rounded w-full max-w-xl mb-6 mx-auto animate-pulse"></div>
          </div>

          {/* FAQ items skeleton */}
          <div className="space-y-4">
            {[0, 1, 2, 3, 4].map((index) => (
              <div key={index} className="border border-gray-200 rounded-lg p-4">
                <div className="flex items-center justify-between mb-2">
                  <div className="h-6 bg-gray-300 rounded w-3/4 animate-pulse"></div>
                  <div className="h-6 w-6 bg-gray-300 rounded animate-pulse"></div>
                </div>
                <div className="h-4 bg-gray-200 rounded w-full mt-2 animate-pulse"></div>
              </div>
            ))}
          </div>
        </div>
      </section>
    );
  }

  const {
    title,
    subtitle,
    description,
    items = [],
    alignment = 'center',
    variant = 'default',
    backgroundImage,
    backgroundOverlay = 0.5,
    backgroundPosition = 'center',
    primaryColor,
    accentColor,
    textColor,
    hoverColor,
    allowMultipleOpen = false,
    className: dataClassName,
  } = data;

  // Initialize with first item open by default
  const [openItems, setOpenItems] = useState(new Set(items.length > 0 ? [0] : []));

  const toggleItem = (index) => {
    setOpenItems((prev) => {
      const newSet = new Set(prev);
      if (newSet.has(index)) {
        newSet.delete(index);
      } else {
        if (!allowMultipleOpen) {
          newSet.clear();
        }
        newSet.add(index);
      }
      return newSet;
    });
  };

  const alignmentClasses = {
    left: 'text-left items-start',
    center: 'text-center items-center',
    right: 'text-right items-end',
  };

  const variantClasses = {
    default: 'bg-white',
    minimal: 'bg-transparent relative',
    accent: 'bg-gradient-to-br from-primary-50 via-white to-primary-50',
    glass: 'bg-white/80 backdrop-blur-lg',
    backgroundImage: 'relative',
  };

  if (items.length === 0) {
    console.warn('FaqModern: items array is empty');
    return null;
  }

  const hasBackgroundImage = variant === 'backgroundImage' && backgroundImage;

  // Helper function to convert hex to rgba
  const hexToRgba = (hex, alpha = 1) => {
    if (!hex) return undefined;
    const r = parseInt(hex.slice(1, 3), 16);
    const g = parseInt(hex.slice(3, 5), 16);
    const b = parseInt(hex.slice(5, 7), 16);
    return `rgba(${r}, ${g}, ${b}, ${alpha})`;
  };

  // Build custom styles object for color overrides
  const sectionStyles = hasBackgroundImage ? {
    backgroundImage: `url(${backgroundImage})`,
    backgroundSize: 'cover',
    backgroundPosition: backgroundPosition,
    backgroundRepeat: 'no-repeat',
    backgroundAttachment: 'fixed',
  } : {};

  // Add CSS custom properties for hover colors
  if (primaryColor || hoverColor) {
    sectionStyles['--primary-color'] = primaryColor;
    sectionStyles['--hover-color'] = hoverColor || primaryColor;
    if (primaryColor) {
      sectionStyles['--primary-100'] = hexToRgba(primaryColor, 0.1);
      sectionStyles['--primary-500'] = hexToRgba(primaryColor, 0.3);
    }
    if (hoverColor || primaryColor) {
      const hover = hoverColor || primaryColor;
      sectionStyles['--hover-bg'] = hexToRgba(hover, 0.1);
      sectionStyles['--hover-bg-medium'] = hexToRgba(hover, 0.3);
    }
  }

  return (
    <section
      id={id}
      className={clsx(
        'w-full py-12 sm:py-16 lg:py-20 px-4 sm:px-6 lg:px-8 relative',
        variantClasses[variant],
        className,
        dataClassName
      )}
      style={Object.keys(sectionStyles).length > 0 ? sectionStyles : undefined}
    >
      {/* Background Image Overlay */}
      {hasBackgroundImage && (
        <div 
          className="absolute inset-0 z-0"
          style={{
            backgroundColor: `rgba(0, 0, 0, ${backgroundOverlay})`,
          }}
        />
      )}

      <div className="max-w-5xl mx-auto w-full relative z-10">
        {/* Header */}
        {(title || subtitle || description) && (
          <div
            className={clsx(
              'mb-12 sm:mb-16 flex flex-col',
              alignmentClasses[alignment]
            )}
          >
            {subtitle && (
              <p 
                className={clsx(
                  "text-sm sm:text-base font-semibold uppercase tracking-wider mb-3",
                  hasBackgroundImage ? 'text-white' : variant === 'minimal' ? 'text-primary-400' : 'text-primary-600'
                )}
                style={textColor && !hasBackgroundImage && variant !== 'minimal' ? { color: textColor } : primaryColor && variant !== 'minimal' && !hasBackgroundImage ? { color: primaryColor } : undefined}
              >
                {subtitle}
              </p>
            )}

            {title && (
              <h2 className={clsx(
                "text-3xl sm:text-4xl md:text-5xl font-bold mb-4 leading-tight",
                hasBackgroundImage ? 'text-white' : variant === 'minimal' ? 'text-white' : 'text-gray-900'
              )}>
                {title}
              </h2>
            )}

            {description && (
              <p
                className={clsx(
                  'text-base sm:text-lg max-w-2xl',
                  hasBackgroundImage ? 'text-gray-100' : variant === 'minimal' ? 'text-gray-200' : 'text-gray-500',
                  alignment === 'center' && 'mx-auto',
                  alignment === 'right' && 'ml-auto',
                  alignment === 'left' && 'mr-auto'
                )}
              >
                {description}
              </p>
            )}
          </div>
        )}

        {/* FAQ Items - Modern Layout */}
        <div className="space-y-3">
          {items.map((item, index) => {
            const isOpen = openItems.has(index);
            const { question, answer } = item;

            if (!question || !answer) {
              console.warn(`FaqModern: Item at index ${index} is missing question or answer`);
              return null;
            }

            // Refs for hover effect coordination
            const badgeRef = useRef(null);
            const textRef = useRef(null);
            const iconRef = useRef(null);

            const handleMouseEnter = () => {
              if (isOpen || hasBackgroundImage || variant === 'minimal') return;
              const hover = hoverColor || primaryColor;
              if (!hover) return;

              if (badgeRef.current) {
                badgeRef.current.style.backgroundColor = hexToRgba(hover, 0.15);
                badgeRef.current.style.color = hover;
              }
              if (textRef.current) {
                textRef.current.style.color = hover;
              }
              if (iconRef.current) {
                iconRef.current.style.backgroundColor = hexToRgba(hover, 0.15);
                iconRef.current.style.color = hover;
              }
            };

            const handleMouseLeave = () => {
              if (isOpen || hasBackgroundImage || variant === 'minimal') return;

              if (badgeRef.current) {
                badgeRef.current.style.backgroundColor = '';
                badgeRef.current.style.color = '';
              }
              if (textRef.current) {
                textRef.current.style.color = '';
              }
              if (iconRef.current) {
                iconRef.current.style.backgroundColor = '';
                iconRef.current.style.color = '';
              }
            };

            return (
              <div
                key={index}
                className={clsx(
                  'group relative overflow-hidden rounded-2xl transition-all duration-300',
                  isOpen
                    ? hasBackgroundImage
                      ? 'bg-white/95 backdrop-blur-xl shadow-2xl border-2 border-white/50'
                      : variant === 'minimal'
                      ? 'bg-white/95 backdrop-blur-xl shadow-2xl border border-white/30'
                      : 'bg-white shadow-xl border-2 border-primary-500'
                    : hasBackgroundImage
                    ? 'bg-white/90 backdrop-blur-lg border border-white/40 hover:bg-white/95 hover:border-white/60 hover:shadow-xl'
                    : variant === 'minimal'
                    ? 'bg-white/70 backdrop-blur-lg border border-white/20 hover:bg-white/85 hover:border-white/40 hover:shadow-xl'
                    : 'bg-white/60 hover:bg-white border border-gray-200 hover:border-primary-300 hover:shadow-lg'
                )}
                style={isOpen && primaryColor && !hasBackgroundImage && variant !== 'minimal' ? {
                  borderColor: primaryColor,
                } : !isOpen && !hasBackgroundImage && variant !== 'minimal' && (hoverColor || primaryColor) ? {
                  transition: 'border-color 0.3s ease',
                } : undefined}
                onMouseEnter={(e) => {
                  if (!isOpen && !hasBackgroundImage && variant !== 'minimal' && (hoverColor || primaryColor)) {
                    e.currentTarget.style.borderColor = hoverColor || primaryColor;
                  }
                  handleMouseEnter();
                }}
                onMouseLeave={(e) => {
                  if (!isOpen && !hasBackgroundImage && variant !== 'minimal') {
                    e.currentTarget.style.borderColor = '';
                  }
                  handleMouseLeave();
                }}
              >
                {/* Accent line for open items */}
                {isOpen && (
                  <div className="" />
                )}

                <div className="flex flex-col sm:flex-row">
                  {/* Question Section */}
                  <button
                    onClick={() => toggleItem(index)}
                    className={clsx(
                      'flex-1 px-6 py-5 sm:py-6 flex items-center justify-between text-left',
                      'transition-all duration-300',
                      isOpen && variant === 'minimal'
                        ? 'bg-white/40'
                        : isOpen && 'bg-gradient-to-r from-primary-50/50 to-transparent'
                    )}
                    style={isOpen && primaryColor && variant !== 'minimal' ? {
                      backgroundImage: `linear-gradient(to right, ${primaryColor}0d, transparent)`,
                    } : undefined}
                    aria-expanded={isOpen}
                    aria-controls={`faq-modern-answer-${index}`}
                  >
                    <div className="flex items-start gap-4 flex-1">
                      {/* Number Badge */}
                      <div
                        ref={badgeRef}
                        className={clsx(
                          'flex-shrink-0 w-10 h-10 rounded-full flex items-center justify-center font-bold text-sm transition-all duration-300',
                          isOpen
                            ? variant === 'minimal'
                              ? 'bg-gradient-to-br from-primary-500 to-primary-600 text-white shadow-lg scale-110'
                              : 'bg-primary-500 text-white shadow-lg scale-110'
                            : variant === 'minimal'
                            ? 'bg-white/30 text-gray-700 group-hover:bg-primary-500/30 group-hover:text-primary-600'
                            : 'bg-gray-100 text-gray-600 group-hover:bg-primary-100 group-hover:text-primary-600'
                        )}
                        style={isOpen && primaryColor ? {
                          backgroundColor: primaryColor,
                          backgroundImage: variant === 'minimal' && accentColor ? `linear-gradient(to bottom right, ${primaryColor}, ${accentColor})` : undefined,
                        } : !isOpen && (hoverColor || primaryColor) ? {
                          transition: 'background-color 0.3s ease, color 0.3s ease',
                        } : undefined}
                      >
                        {index + 1}
                      </div>

                      <span
                        ref={textRef}
                        className={clsx(
                          'text-base sm:text-lg font-semibold flex-1 transition-colors',
                          isOpen 
                            ? variant === 'minimal'
                              ? 'text-gray-900'
                              : 'text-primary-700' 
                            : variant === 'minimal'
                            ? 'text-gray-800 group-hover:text-gray-900'
                            : 'text-gray-900 group-hover:text-primary-600'
                        )}
                        style={isOpen && primaryColor && variant !== 'minimal' ? {
                          color: primaryColor,
                        } : !isOpen && variant !== 'minimal' && (hoverColor || primaryColor) ? {
                          transition: 'color 0.3s ease',
                        } : undefined}
                      >
                        {question}
                      </span>
                    </div>

                    {/* Icon */}
                    <div className="flex-shrink-0 ml-4">
                      <div
                        ref={iconRef}
                        className={clsx(
                          'w-10 h-10 rounded-lg flex items-center justify-center transition-all duration-300',
                          isOpen
                            ? variant === 'minimal'
                              ? 'bg-gradient-to-br from-primary-500 to-primary-600 text-white rotate-180 shadow-lg'
                              : 'bg-primary-500 text-white rotate-180 shadow-md'
                            : variant === 'minimal'
                            ? 'bg-white/30 text-gray-600 group-hover:bg-primary-500/30 group-hover:text-primary-600'
                            : 'bg-gray-100 text-gray-600 group-hover:bg-primary-100 group-hover:text-primary-600'
                        )}
                        style={isOpen && primaryColor ? {
                          backgroundColor: primaryColor,
                          backgroundImage: variant === 'minimal' && accentColor ? `linear-gradient(to bottom right, ${primaryColor}, ${accentColor})` : undefined,
                        } : !isOpen && (hoverColor || primaryColor) ? {
                          transition: 'background-color 0.3s ease, color 0.3s ease',
                        } : undefined}
                      >
                        <svg
                          className="w-5 h-5"
                          fill="none"
                          stroke="currentColor"
                          viewBox="0 0 24 24"
                          aria-hidden="true"
                        >
                          <path
                            strokeLinecap="round"
                            strokeLinejoin="round"
                            strokeWidth={2.5}
                            d="M19 9l-7 7-7-7"
                          />
                        </svg>
                      </div>
                    </div>
                  </button>
                </div>

                {/* Answer Section */}
                <div
                  id={`faq-modern-answer-${index}`}
                  className={clsx(
                    'overflow-hidden transition-all duration-300 ease-in-out',
                    isOpen ? 'max-h-[500px] opacity-100' : 'max-h-0 opacity-0'
                  )}
                >
                  <div className={clsx(
                    "px-6 pb-6 pt-2",
                    variant === 'minimal' && isOpen
                      ? 'border-t border-white/20'
                      : 'border-t border-gray-100'
                  )}>
                    <div className="flex gap-4">
                      <div className="flex-shrink-0 w-10" /> {/* Spacer for alignment */}
                      <div className="flex-1">
                        <p className={clsx(
                          "text-sm sm:text-base leading-relaxed pl-0",
                          variant === 'minimal' ? 'text-gray-700' : 'text-gray-600'
                        )}>
                          {answer}
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </section>
  );
};

