import clsx from 'clsx';
import '../../styles/product.css';

/**
 * ProductSpecifications Component
 * @param {Object} props - Component props
 * @param {Object} props.data - Product specifications data
 * @param {string} [props.data.title='Specifications'] - Section title
 * @param {Array} props.data.specifications - Array of specification objects
 * @param {string} [props.data.variant='default'] - Style variant: 'default' | 'table' | 'card' | 'minimal'
 * @param {boolean} [props.data.collapsible=false] - Allow collapsible sections
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute for the section element
 */
export const ProductSpecifications = ({ data, className, id }) => {
  // Safety check for data
  if (!data || typeof data !== 'object') {
    console.error('ProductSpecifications: data prop is required and must be an object');
    return null;
  }

  const {
    title = 'Specifications',
    specifications = [],
    variant = 'default',
    collapsible = false
  } = data;

  if (specifications.length === 0) {
    return null;
  }

  // Default variant - Simple key-value list
  if (variant === 'default') {
    return (
      <section
        id={id}
        className={clsx(
          "product-specifications-default",
          className
        )}
      >
        <div className="space-y-6">
          <h3 className="text-2xl font-bold text-gray-900">{title}</h3>

          <div className="space-y-4">
            {specifications.map((spec, index) => (
              <div key={index} className="flex justify-between py-3 border-b border-gray-200">
                <span className="font-medium text-gray-900">{spec.label}</span>
                <span className="text-gray-700">{spec.value}</span>
              </div>
            ))}
          </div>
        </div>
      </section>
    );
  }

  // Table variant - Table layout
  if (variant === 'table') {
    return (
      <section
        id={id}
        className={clsx(
          "product-specifications-table",
          className
        )}
      >
        <div className="space-y-6">
          <h3 className="text-2xl font-bold text-gray-900">{title}</h3>

          <div className="bg-white border border-gray-200 rounded-lg overflow-hidden">
            <table className="w-full">
              <tbody>
                {specifications.map((spec, index) => (
                  <tr key={index} className={index % 2 === 0 ? "bg-gray-50" : "bg-white"}>
                    <td className="px-6 py-4 font-medium text-gray-900 border-r border-gray-200">
                      {spec.label}
                    </td>
                    <td className="px-6 py-4 text-gray-700">
                      {spec.value}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      </section>
    );
  }

  // Card variant - Card-based layout
  if (variant === 'card') {
    return (
      <section
        id={id}
        className={clsx(
          "product-specifications-card bg-white rounded-lg shadow-sm border border-gray-200",
          className
        )}
      >
        <div className="p-6">
          <div className="flex items-center space-x-2 mb-6">
            <svg className="w-5 h-5 text-gray-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5H7a2 2 0 00-2 2v10a2 2 0 002 2h8a2 2 0 002-2V7a2 2 0 00-2-2h-2M9 5a2 2 0 002 2h2a2 2 0 002-2M9 5a2 2 0 012-2h2a2 2 0 012 2" />
            </svg>
            <h3 className="text-xl font-semibold text-gray-900">{title}</h3>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            {specifications.map((spec, index) => (
              <div key={index} className="bg-gray-50 rounded-lg p-4">
                <div className="text-xs font-semibold text-gray-500 uppercase tracking-wide mb-1">
                  {spec.label}
                </div>
                <div className="text-gray-900 font-medium">
                  {spec.value}
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>
    );
  }

  // Minimal variant - Clean minimal design
  if (variant === 'minimal') {
    return (
      <section
        id={id}
        className={clsx(
          "product-specifications-minimal",
          className
        )}
      >
        <div className="space-y-3">
          {specifications.map((spec, index) => (
            <div key={index} className="flex items-center space-x-3 text-sm">
              <span className="text-gray-500 font-medium min-w-[120px]">{spec.label}:</span>
              <span className="text-gray-900">{spec.value}</span>
            </div>
          ))}
        </div>
      </section>
    );
  }

  return null;
};
