import { useState, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/blog.css';

/**
 * BlogShare Component - Social sharing buttons
 * @param {Object} props - Component props
 * @param {Object} props.data - Share configuration data
 * @param {string} props.data.url - URL to share
 * @param {string} [props.data.title] - Title of the content to share
 * @param {string} [props.data.description] - Description of the content
 * @param {Array} [props.data.platforms=['twitter', 'facebook', 'linkedin', 'copy']] - Platforms to show
 * @param {string} [props.data.variant='default'] - Style variant: 'default' | 'buttons' | 'icons' | 'minimal'
 * @param {string} [props.data.theme='light'] - Theme: 'light' | 'dark'
 * @param {string} [props.data.alignment='left'] - Alignment: 'left' | 'center' | 'right'
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute
 * @param {boolean} [props.loader=false] - Show loading state
 */
export const BlogShare = ({ data, className, id, loader = false }) => {
  if (!data || typeof data !== 'object') {
    console.error('BlogShare: data prop is required and must be an object');
    return null;
  }

  const [showLoader, setShowLoader] = useState(loader);
  const [copied, setCopied] = useState(false);

  useEffect(() => {
    if (loader) {
      const timer = setTimeout(() => {
        setShowLoader(false);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowLoader(false);
    }
  }, [loader]);

  const {
    url = typeof window !== 'undefined' ? window.location.href : '',
    title = '',
    description = '',
    platforms = ['twitter', 'facebook', 'linkedin', 'copy'],
    variant = 'default',
    theme = 'light',
    alignment = 'left',
    className: dataClassName,
  } = data;

  const handleShare = (platform) => {
    const encodedUrl = encodeURIComponent(url);
    const encodedTitle = encodeURIComponent(title);
    const encodedDescription = encodeURIComponent(description);

    const shareUrls = {
      twitter: `https://twitter.com/intent/tweet?url=${encodedUrl}&text=${encodedTitle}`,
      facebook: `https://www.facebook.com/sharer/sharer.php?u=${encodedUrl}`,
      linkedin: `https://www.linkedin.com/sharing/share-offsite/?url=${encodedUrl}`,
      whatsapp: `https://wa.me/?text=${encodedTitle} ${encodedUrl}`,
      telegram: `https://t.me/share/url?url=${encodedUrl}&text=${encodedTitle}`,
      reddit: `https://reddit.com/submit?url=${encodedUrl}&title=${encodedTitle}`,
      pinterest: `https://pinterest.com/pin/create/button/?url=${encodedUrl}&description=${encodedTitle}`,
      copy: () => {
        if (navigator.clipboard) {
          navigator.clipboard.writeText(url);
          setCopied(true);
          setTimeout(() => setCopied(false), 2000);
        }
      },
    };

    if (platform === 'copy') {
      shareUrls.copy();
    } else if (shareUrls[platform]) {
      window.open(shareUrls[platform], '_blank', 'width=600,height=400');
    }
  };

  const getPlatformIcon = (platform) => {
    switch (platform) {
      case 'twitter':
        return (
          <svg fill="currentColor" viewBox="0 0 24 24" className="w-5 h-5">
            <path d="M8.29 20.251c7.547 0 11.675-6.253 11.675-11.675 0-.178 0-.355-.012-.53A8.348 8.348 0 0022 5.92a8.19 8.19 0 01-2.357.646 4.118 4.118 0 001.804-2.27 8.224 8.224 0 01-2.605.996 4.107 4.107 0 00-6.993 3.743 11.65 11.65 0 01-8.457-4.287 4.106 4.106 0 001.27 5.477A4.072 4.072 0 012.8 9.713v.052a4.105 4.105 0 003.292 4.022 4.095 4.095 0 01-1.853.07 4.108 4.108 0 003.834 2.85A8.233 8.233 0 012 18.407a11.616 11.616 0 006.29 1.84" />
          </svg>
        );
      case 'facebook':
        return (
          <svg fill="currentColor" viewBox="0 0 24 24" className="w-5 h-5">
            <path fillRule="evenodd" d="M22 12c0-5.523-4.477-10-10-10S2 6.477 2 12c0 4.991 3.657 9.128 8.438 9.878v-6.987h-2.54V12h2.54V9.797c0-2.506 1.492-3.89 3.777-3.89 1.094 0 2.238.195 2.238.195v2.46h-1.26c-1.243 0-1.63.771-1.63 1.562V12h2.773l-.443 2.89h-2.33v6.988C18.343 21.128 22 16.991 22 12z" clipRule="evenodd" />
          </svg>
        );
      case 'linkedin':
        return (
          <svg fill="currentColor" viewBox="0 0 24 24" className="w-5 h-5">
            <path fillRule="evenodd" d="M19 0h-14c-2.761 0-5 2.239-5 5v14c0 2.761 2.239 5 5 5h14c2.762 0 5-2.239 5-5v-14c0-2.761-2.238-5-5-5zm-11 19h-3v-11h3v11zm-1.5-12.268c-.966 0-1.75-.79-1.75-1.764s.784-1.764 1.75-1.764 1.75.79 1.75 1.764-.783 1.764-1.75 1.764zm13.5 12.268h-3v-5.604c0-3.368-4-3.113-4 0v5.604h-3v-11h3v1.765c1.396-2.586 7-2.777 7 2.476v6.759z" clipRule="evenodd" />
          </svg>
        );
      case 'copy':
        return (
          <svg fill="none" stroke="currentColor" viewBox="0 0 24 24" className="w-5 h-5">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 16H6a2 2 0 01-2-2V6a2 2 0 012-2h8a2 2 0 012 2v2m-6 12h8a2 2 0 002-2v-8a2 2 0 00-2-2h-8a2 2 0 00-2 2v8a2 2 0 002 2z" />
          </svg>
        );
      default:
        return (
          <svg fill="none" stroke="currentColor" viewBox="0 0 24 24" className="w-5 h-5">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13.828 10.172a4 4 0 00-5.656 0l-4 4a4 4 0 105.656 5.656l1.102-1.101m-.758-4.899a4 4 0 005.656 0l4-4a4 4 0 00-5.656-5.656l-1.1 1.1" />
          </svg>
        );
    }
  };

  const getPlatformName = (platform) => {
    return platform.charAt(0).toUpperCase() + platform.slice(1);
  };

  const containerClasses = clsx(
    'blog-share',
    `blog-share-${variant}`,
    `blog-share-${theme}`,
    `blog-share-${alignment}`,
    className,
    dataClassName
  );

  // Loading skeleton
  if (showLoader) {
    return (
      <div className={containerClasses} id={id}>
        <div className="blog-share-skeleton">
          {platforms.map((_, index) => (
            <div key={index} className="skeleton-button"></div>
          ))}
        </div>
      </div>
    );
  }

  // Default variant - Icons with labels
  if (variant === 'default' || variant === 'icons') {
    return (
      <div className={containerClasses} id={id}>
        <span className="blog-share-label">Share:</span>
        <div className="blog-share-buttons">
          {platforms.map((platform, index) => (
            <button
              key={index}
              onClick={() => handleShare(platform)}
              className={clsx('blog-share-button', platform === 'copy' && copied && 'blog-share-button-copied')}
              aria-label={`Share on ${getPlatformName(platform)}`}
            >
              {getPlatformIcon(platform)}
              {variant === 'default' && <span>{getPlatformName(platform)}</span>}
            </button>
          ))}
        </div>
      </div>
    );
  }

  // Buttons variant - Full button style
  if (variant === 'buttons') {
    return (
      <div className={containerClasses} id={id}>
        <div className="blog-share-buttons-full">
          {platforms.map((platform, index) => (
            <button
              key={index}
              onClick={() => handleShare(platform)}
              className={clsx('blog-share-button-full', `blog-share-button-${platform}`)}
            >
              {getPlatformIcon(platform)}
              <span>{getPlatformName(platform)}</span>
            </button>
          ))}
        </div>
      </div>
    );
  }

  // Minimal variant - Just icons
  if (variant === 'minimal') {
    return (
      <div className={containerClasses} id={id}>
        <div className="blog-share-minimal">
          {platforms.map((platform, index) => (
            <button
              key={index}
              onClick={() => handleShare(platform)}
              className="blog-share-button-minimal"
              aria-label={`Share on ${getPlatformName(platform)}`}
            >
              {getPlatformIcon(platform)}
            </button>
          ))}
        </div>
      </div>
    );
  }

  return null;
};