import React, { useState, useEffect } from 'react';
import { clsx } from 'clsx';

export const CheckoutProtectionSection = ({ 
  data = {},
  variant = 'standard',
  className,
  loading = false
}) => {
  const [timedOut, setTimedOut] = useState(false);

  // Auto-show content after 2 seconds if loading is true
  useEffect(() => {
    if (loading) {
      setTimedOut(false);
      const timer = setTimeout(() => {
        setTimedOut(true);
      }, 2000);
      return () => clearTimeout(timer);
    }
  }, [loading]);

  const shouldShowSkeleton = loading && !timedOut;
  const {
    title, 
    description, 
    learnMoreText, 
    learnMoreLink, 
    options = [], 
    termsText, 
    termsLinkText, 
    termsLink
  } = data;

  const isModern = variant === 'modern';
  const isMinimal = variant === 'minimal';
  const isPremium = variant === 'premium';

  // Show skeleton loading state
  if (shouldShowSkeleton) {
    return (
      <section className={clsx("checkout-protection-section", className)}>
        <h2 className={clsx(
          "mb-4 uppercase",
          variant === 'standard' && "text-xs font-bold tracking-[0.15em] text-gray-900",
          variant === 'modern' && "text-2xl font-black tracking-tight text-gray-900",
          variant === 'minimal' && "text-sm font-semibold text-gray-900",
          variant === 'premium' && "text-[10px] uppercase tracking-widest text-neutral-600 font-bold block ml-1"
        )}>
          {data.title || 'Protection Plans'}
        </h2>

        <div className="space-y-4">
          {[1, 2].map((item) => (
            <div key={item} className="checkout-skeleton h-20 w-full rounded-lg"></div>
          ))}
        </div>
      </section>
    );
  }

  return (
    <section className={clsx("checkout-protection-section", className)}>
      <h2 className={clsx(
        "mb-4 uppercase",
        variant === 'standard' && "text-xs font-bold tracking-[0.15em] text-gray-900",
        variant === 'modern' && "text-2xl font-black tracking-tight text-gray-900",
        variant === 'minimal' && "text-xs font-black text-gray-400 tracking-[0.2em]",
        variant === 'premium' && "text-[10px] uppercase tracking-widest text-neutral-600 font-bold block ml-1"
      )}>
        {title}
      </h2>
      <div className={clsx(
        "transition-all rounded-sm",
        variant === 'standard' && "border border-gray-100 p-8 space-y-6",
        variant === 'modern' && "bg-white p-8 rounded-2xl shadow-sm border border-neutral-100 space-y-6",
        variant === 'minimal' && "space-y-4",
        variant === 'premium' && "border border-neutral-900 p-10 space-y-8 bg-[#111111] rounded-[30px]"
      )}>
        <p className={clsx("text-sm leading-relaxed", isPremium ? "text-neutral-400 font-serif italic text-lg" : "text-gray-700")}>
          {description} <a href={learnMoreLink || '#'} className={clsx("underline font-medium", isPremium ? "text-white" : "text-gray-900")}>{learnMoreText || 'Learn More'}</a>
        </p>
        
        <div className={clsx("space-y-5", isMinimal && "space-y-3", isPremium && "space-y-6")}>
          {options.map((option) => (
            <label key={option.id} className="flex items-start cursor-pointer group">
              <div className="relative flex items-center justify-center mt-0.5">
                <input type="checkbox" className={clsx(
                  "peer appearance-none w-5 h-5 border transition-all",
                  isPremium ? "border-neutral-800 rounded-lg checked:bg-white checked:border-white" : "border-gray-300 rounded-sm checked:bg-black checked:border-black"
                )} />
                <svg className={clsx(
                  "absolute w-3.5 h-3.5 opacity-0 peer-checked:opacity-100 transition-opacity",
                  isPremium ? "text-black" : "text-white"
                )} fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={3.5} d="M5 13l4 4L19 7" />
                </svg>
              </div>
              <span className={clsx(
                "ml-4 leading-tight transition-colors group-hover:text-black",
                isModern ? "text-lg font-bold text-gray-800" : "text-sm font-bold text-gray-800",
                isMinimal && "text-sm font-medium text-gray-600",
                isPremium && "text-base font-light text-neutral-400 group-hover:text-white"
              )}>
                {option.label}
              </span>
            </label>
          ))}
        </div>

        {termsText && (
          <p className={clsx(
            "text-[10px] pt-2 leading-relaxed uppercase tracking-wide",
            isPremium ? "text-neutral-700" : "text-gray-400"
          )}>
            {termsText} <a href={termsLink || '#'} className="underline">{termsLinkText}</a>.
          </p>
        )}
      </div>
    </section>
  );
};
