import { useState } from 'react';
import clsx from 'clsx';
import '../../styles/product.css';

/**
 * ProductGallery Component
 * @param {Object} props - Component props
 * @param {Object} props.data - Gallery configuration data
 * @param {string|string[]} props.data.images - Product image URLs
 * @param {string} [props.data.title] - Product title for alt text
 * @param {string} [props.data.variant='default'] - Style variant: 'default' | 'grid' | 'carousel' | 'minimal' | 'fullscreen'
 * @param {boolean} [props.data.showThumbnails=true] - Show thumbnail navigation
 * @param {boolean} [props.data.allowFullscreen=false] - Allow fullscreen view
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute for the section element
 */
export const ProductGallery = ({ data, className, id }) => {
  // Safety check for data
  if (!data || typeof data !== 'object') {
    console.error('ProductGallery: data prop is required and must be an object');
    return null;
  }

  const [selectedImage, setSelectedImage] = useState(0);
  const [isFullscreen, setIsFullscreen] = useState(false);

  const {
    images = [],
    title = 'Product',
    variant = 'default',
    showThumbnails = true,
    allowFullscreen = false
  } = data;

  // Ensure images is an array
  const allImages = Array.isArray(images) ? images : [images];

  if (allImages.length === 0) {
    return null;
  }

  // Default variant - Standard gallery with thumbnails
  if (variant === 'default') {
    return (
      <section
        id={id}
        className={clsx(
          "product-gallery-default",
          className
        )}
      >
        <div className="space-y-4">
          {/* Main Image */}
          <div className="aspect-square bg-gray-100 rounded-lg overflow-hidden">
            <img
              src={allImages[selectedImage]}
              alt={`${title} - Image ${selectedImage + 1}`}
              className="w-full h-full object-cover"
            />
            {allowFullscreen && (
              <button
                onClick={() => setIsFullscreen(true)}
                className="absolute top-4 right-4 bg-black/50 hover:bg-black/70 text-white p-2 rounded-full transition-colors"
                aria-label="View fullscreen"
              >
                <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0zM10 7v3m0 0v3m0-3h3m-3 0H7" />
                </svg>
              </button>
            )}
          </div>

          {/* Thumbnails */}
          {showThumbnails && allImages.length > 1 && (
            <div className="grid grid-cols-4 gap-4">
              {allImages.slice(0, 4).map((img, index) => (
                <button
                  key={index}
                  onClick={() => setSelectedImage(index)}
                  className={clsx(
                    "aspect-square bg-gray-100 rounded-lg overflow-hidden border-2 transition-colors",
                    selectedImage === index ? "border-blue-500" : "border-transparent hover:border-gray-300"
                  )}
                >
                  <img
                    src={img}
                    alt={`${title} thumbnail ${index + 1}`}
                    className="w-full h-full object-cover"
                  />
                </button>
              ))}
            </div>
          )}
        </div>

        {/* Fullscreen Modal */}
        {isFullscreen && (
          <div className="fixed inset-0 bg-black bg-opacity-90 z-50 flex items-center justify-center p-4">
            <button
              onClick={() => setIsFullscreen(false)}
              className="absolute top-4 right-4 text-white hover:text-gray-300 transition-colors"
              aria-label="Close fullscreen"
            >
              <svg className="w-8 h-8" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
              </svg>
            </button>

            <div className="max-w-4xl max-h-full relative">
              <img
                src={allImages[selectedImage]}
                alt={`${title} - Fullscreen`}
                className="max-w-full max-h-full object-contain"
              />

              {/* Navigation arrows */}
              {allImages.length > 1 && (
                <>
                  <button
                    onClick={() => setSelectedImage((selectedImage - 1 + allImages.length) % allImages.length)}
                    className="absolute left-4 top-1/2 transform -translate-y-1/2 bg-black/50 hover:bg-black/70 text-white p-3 rounded-full transition-colors"
                    aria-label="Previous image"
                  >
                    <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
                    </svg>
                  </button>
                  <button
                    onClick={() => setSelectedImage((selectedImage + 1) % allImages.length)}
                    className="absolute right-4 top-1/2 transform -translate-y-1/2 bg-black/50 hover:bg-black/70 text-white p-3 rounded-full transition-colors"
                    aria-label="Next image"
                  >
                    <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
                    </svg>
                  </button>
                </>
              )}

              {/* Thumbnail strip */}
              {allImages.length > 1 && (
                <div className="absolute bottom-4 left-1/2 transform -translate-x-1/2 flex space-x-2 bg-black/50 rounded-lg p-2">
                  {allImages.map((img, index) => (
                    <button
                      key={index}
                      onClick={() => setSelectedImage(index)}
                      className={clsx(
                        "w-12 h-12 rounded overflow-hidden border-2 transition-colors",
                        selectedImage === index ? "border-white" : "border-transparent"
                      )}
                    >
                      <img
                        src={img}
                        alt={`${title} thumbnail ${index + 1}`}
                        className="w-full h-full object-cover"
                      />
                    </button>
                  ))}
                </div>
              )}
            </div>
          </div>
        )}
      </section>
    );
  }

  // Grid variant - Grid layout for multiple images
  if (variant === 'grid') {
    return (
      <section
        id={id}
        className={clsx(
          "product-gallery-grid",
          className
        )}
      >
        <div className="grid grid-cols-2 md:grid-cols-3 gap-4">
          {allImages.map((img, index) => (
            <button
              key={index}
              onClick={() => setSelectedImage(index)}
              className={clsx(
                "aspect-square bg-gray-100 rounded-lg overflow-hidden transition-all duration-300 hover:scale-105",
                selectedImage === index ? "ring-4 ring-blue-500" : ""
              )}
            >
              <img
                src={img}
                alt={`${title} - Image ${index + 1}`}
                className="w-full h-full object-cover"
              />
            </button>
          ))}
        </div>

        {/* Fullscreen Modal */}
        {isFullscreen && (
          <div className="fixed inset-0 bg-black bg-opacity-90 z-50 flex items-center justify-center p-4">
            <button
              onClick={() => setIsFullscreen(false)}
              className="absolute top-4 right-4 text-white hover:text-gray-300 transition-colors"
              aria-label="Close fullscreen"
            >
              <svg className="w-8 h-8" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
              </svg>
            </button>

            <div className="max-w-4xl max-h-full">
              <img
                src={allImages[selectedImage]}
                alt={`${title} - Fullscreen`}
                className="max-w-full max-h-full object-contain"
              />
            </div>
          </div>
        )}
      </section>
    );
  }

  // Carousel variant - Horizontal scrolling carousel
  if (variant === 'carousel') {
    return (
      <section
        id={id}
        className={clsx(
          "product-gallery-carousel",
          className
        )}
      >
        <div className="relative">
          {/* Main Image */}
          <div className="aspect-square bg-gray-100 rounded-lg overflow-hidden mb-4">
            <img
              src={allImages[selectedImage]}
              alt={`${title} - Image ${selectedImage + 1}`}
              className="w-full h-full object-cover"
            />
          </div>

          {/* Carousel Navigation */}
          {allImages.length > 1 && (
            <div className="flex items-center justify-center space-x-4">
              <button
                onClick={() => setSelectedImage((selectedImage - 1 + allImages.length) % allImages.length)}
                className="bg-white border border-gray-200 hover:bg-gray-50 p-2 rounded-full transition-colors"
                aria-label="Previous image"
              >
                <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
                </svg>
              </button>

              {/* Dots indicator */}
              <div className="flex space-x-2">
                {allImages.map((_, index) => (
                  <button
                    key={index}
                    onClick={() => setSelectedImage(index)}
                    className={clsx(
                      "w-2 h-2 rounded-full transition-colors",
                      selectedImage === index ? "bg-blue-500" : "bg-gray-300"
                    )}
                    aria-label={`Go to image ${index + 1}`}
                  />
                ))}
              </div>

              <button
                onClick={() => setSelectedImage((selectedImage + 1) % allImages.length)}
                className="bg-white border border-gray-200 hover:bg-gray-50 p-2 rounded-full transition-colors"
                aria-label="Next image"
              >
                <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
                </svg>
              </button>
            </div>
          )}
        </div>
      </section>
    );
  }

  // Minimal variant - Clean minimal design
  if (variant === 'minimal') {
    return (
      <section
        id={id}
        className={clsx(
          "product-gallery-minimal",
          className
        )}
      >
        <div className="aspect-square bg-gray-50 rounded-lg overflow-hidden">
          <img
            src={allImages[selectedImage]}
            alt={`${title} - Image ${selectedImage + 1}`}
            className="w-full h-full object-cover"
          />
        </div>

        {allImages.length > 1 && (
          <div className="flex justify-center space-x-2 mt-4">
            {allImages.map((_, index) => (
              <button
                key={index}
                onClick={() => setSelectedImage(index)}
                className={clsx(
                  "w-2 h-2 rounded-full transition-colors",
                  selectedImage === index ? "bg-gray-900" : "bg-gray-300"
                )}
                aria-label={`Go to image ${index + 1}`}
              />
            ))}
          </div>
        )}
      </section>
    );
  }

  // Fullscreen variant - Full width/height gallery
  if (variant === 'fullscreen') {
    return (
      <section
        id={id}
        className={clsx(
          "product-gallery-fullscreen relative min-h-screen",
          className
        )}
      >
        <div className="absolute inset-0">
          <img
            src={allImages[selectedImage]}
            alt={`${title} - Fullscreen`}
            className="w-full h-full object-cover"
          />
          <div className="absolute inset-0 bg-black bg-opacity-30"></div>
        </div>

        <div className="relative z-10 flex items-center justify-center min-h-screen p-8">
          <div className="text-white text-center">
            <h2 className="text-4xl font-bold mb-4">{title}</h2>
            <p className="text-lg opacity-90">Image {selectedImage + 1} of {allImages.length}</p>
          </div>
        </div>

        {/* Navigation */}
        {allImages.length > 1 && (
          <>
            <button
              onClick={() => setSelectedImage((selectedImage - 1 + allImages.length) % allImages.length)}
              className="absolute left-8 top-1/2 transform -translate-y-1/2 bg-white/20 backdrop-blur-sm hover:bg-white/30 text-white p-4 rounded-full transition-colors"
              aria-label="Previous image"
            >
              <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
              </svg>
            </button>
            <button
              onClick={() => setSelectedImage((selectedImage + 1) % allImages.length)}
              className="absolute right-8 top-1/2 transform -translate-y-1/2 bg-white/20 backdrop-blur-sm hover:bg-white/30 text-white p-4 rounded-full transition-colors"
              aria-label="Next image"
            >
              <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
              </svg>
            </button>
          </>
        )}

        {/* Thumbnail strip */}
        {allImages.length > 1 && (
          <div className="absolute bottom-8 left-1/2 transform -translate-x-1/2 flex space-x-2 bg-black/20 backdrop-blur-sm rounded-lg p-2">
            {allImages.map((img, index) => (
              <button
                key={index}
                onClick={() => setSelectedImage(index)}
                className={clsx(
                  "w-12 h-12 rounded overflow-hidden border-2 transition-colors",
                  selectedImage === index ? "border-white" : "border-transparent"
                )}
              >
                <img
                  src={img}
                  alt={`${title} thumbnail ${index + 1}`}
                  className="w-full h-full object-cover"
                />
              </button>
            ))}
          </div>
        )}
      </section>
    );
  }

  return null;
};
