import React, { useState, useEffect } from 'react';
import { clsx } from 'clsx';

export const CheckoutOrderSummary = ({
  data = {},
  variant = 'standard',
  className,
  loading = false
}) => {
  const [timedOut, setTimedOut] = useState(false);

  // Auto-show content after 2 seconds if loading is true
  useEffect(() => {
    if (loading) {
      setTimedOut(false);
      const timer = setTimeout(() => {
        setTimedOut(true);
      }, 2000);
      return () => clearTimeout(timer);
    }
  }, [loading]);

  const shouldShowSkeleton = loading && !timedOut;
  const {
    title, 
    items = [], 
    subtotalLabel, 
    deliveryLabel, 
    deliveryValue, 
    taxLabel, 
    promoLabel, 
    totalLabel, 
    financingText
  } = data;

  const subtotal = items.reduce((acc, item) => acc + item.price, 0);

  const variants = {
    standard: "bg-[#f5f7f9] p-8",
    card: "bg-white p-8 rounded-3xl shadow-xl border border-neutral-100",
    premium: "bg-[#111111] text-white p-12 border border-neutral-900 rounded-[40px] shadow-2xl",
    minimal: "bg-transparent p-0 border-t pt-8"
  };

  const isPremium = variant === 'premium';

  // Show skeleton loading state
  if (shouldShowSkeleton) {
    return (
      <div className={clsx(variants[variant] || variants.standard, className)}>
        <h2 className={clsx(
          "mb-6 uppercase tracking-widest",
          variant === 'standard' && "text-sm font-bold text-gray-900",
          variant === 'card' && "text-xl font-black tracking-tight text-gray-900",
          variant === 'premium' && "text-2xl font-serif italic text-white",
          variant === 'minimal' && "text-lg font-semibold text-gray-900 border-b pb-2"
        )}>
          {data.title || 'Order Summary'}
        </h2>

        <div className="space-y-4 mb-6">
          {[1, 2, 3].map((item) => (
            <div key={item} className="flex justify-between items-center">
              <div className="flex items-center space-x-3">
                <div className="checkout-skeleton w-12 h-12 rounded-lg"></div>
                <div>
                  <div className="checkout-skeleton h-4 w-24 rounded mb-1"></div>
                  <div className="checkout-skeleton h-3 w-12 rounded"></div>
                </div>
              </div>
              <div className="checkout-skeleton h-4 w-12 rounded"></div>
            </div>
          ))}
        </div>

        <div className="space-y-3 border-t pt-6">
          <div className="flex justify-between">
            <div className="checkout-skeleton h-4 w-16 rounded"></div>
            <div className="checkout-skeleton h-4 w-12 rounded"></div>
          </div>
          <div className="flex justify-between">
            <div className="checkout-skeleton h-3 w-20 rounded"></div>
            <div className="checkout-skeleton h-3 w-8 bg-green-100 rounded"></div>
          </div>
          <div className="flex justify-between pt-3 border-t">
            <div className="checkout-skeleton h-5 w-12 rounded"></div>
            <div className="checkout-skeleton h-5 w-16 rounded"></div>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className={clsx("checkout-order-summary rounded-sm transition-all", variants[variant] || variants.standard, className)}>
      <h2 className={clsx(
        "uppercase mb-6",
        isPremium ? "text-sm font-bold tracking-[0.4em] text-neutral-600" : "text-sm font-bold tracking-widest"
      )}>
        {title || 'Order Summary'}
      </h2>
      
      <div className="space-y-6">
        {items.map((item, idx) => (
          <div key={idx} className="flex justify-between items-start text-sm">
            <div className="space-y-1">
              <p className={clsx("font-bold flex items-center", isPremium && "font-serif italic text-lg")}>
                <span className={clsx("mr-2", isPremium && "text-[10px] uppercase text-neutral-600 font-sans italic-none not-italic font-bold")}>{idx + 1}</span> {item.name}
                {item.hasInfoIcon && !isPremium && (
                  <button className="ml-1 text-gray-400">
                    <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20">
                      <path fillRule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7-4a1 1 0 11-2 0 1 1 0 012 0zM9 9a1 1 0 000 2v3a1 1 0 001 1h1a1 1 0 100-2v-3a1 1 0 00-1-1H9z" clipRule="evenodd" />
                    </svg>
                  </button>
                )}
              </p>
              {item.details && item.details.map((detail, dIdx) => (
                <p key={dIdx} className={clsx("text-xs ml-4", isPremium ? "text-neutral-600 uppercase tracking-widest font-bold" : "text-gray-500")}>{detail}</p>
              ))}
              {item.isSubscription && (
                 <p className={clsx("text-[11px] ml-4 leading-relaxed max-w-[240px]", isPremium ? "text-neutral-500" : "text-gray-500")}>
                    {item.subscriptionDetail}
                 </p>
              )}
            </div>
            <p className={clsx("font-bold", isPremium && "text-xl font-light")}>${item.price.toLocaleString(undefined, { minimumFractionDigits: 2 })}</p>
          </div>
        ))}
      </div>

      <div className={clsx("mt-8 pt-8 border-t space-y-4", isPremium ? "border-neutral-900" : "border-gray-200")}>
        <div className="flex justify-between text-sm">
          <p className={isPremium ? "text-neutral-600 uppercase tracking-widest text-xs" : "text-gray-600"}>{subtotalLabel || 'Subtotal'} ({items.length}):</p>
          <p className="font-bold">${subtotal.toLocaleString(undefined, { minimumFractionDigits: 2 })}</p>
        </div>
        <div className="flex justify-between text-sm">
          <p className={isPremium ? "text-neutral-600 uppercase tracking-widest text-xs" : "text-gray-600"}>{deliveryLabel || 'Est. delivery'}</p>
          <p className={clsx("font-bold", isPremium && "text-white")}>{deliveryValue || 'Included'}</p>
        </div>
        {!isPremium && (
          <>
            <div className="flex justify-between text-sm">
              <p className="text-gray-600 flex items-center">
                {taxLabel || 'Estimated Tax:'} 
                <button className="ml-1 text-gray-400">
                  <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20">
                    <path fillRule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7-4a1 1 0 11-2 0 1 1 0 012 0zM9 9a1 1 0 000 2v3a1 1 0 001 1h1a1 1 0 100-2v-3a1 1 0 00-1-1H9z" clipRule="evenodd" />
                  </svg>
                </button>
              </p>
              <p className="font-bold">--</p>
            </div>
            <button className="text-sm underline font-medium">{promoLabel || 'Enter promo code'}</button>
          </>
        )}
      </div>

      <div className={clsx("mt-8 pt-8 border-t", isPremium ? "border-neutral-900" : "border-gray-200")}>
        <div className="flex justify-between items-center">
          <p className={clsx("text-sm font-bold uppercase tracking-widest", isPremium && "text-neutral-500 mb-2")}>{totalLabel || 'Estimated Total'}</p>
          <p className={clsx("font-bold", isPremium ? "text-5xl font-serif italic tracking-tighter" : "text-xl")}>${subtotal.toLocaleString(undefined, { minimumFractionDigits: 2 })}</p>
        </div>
        {financingText && (
          <p className={clsx("text-xs mt-4 leading-relaxed", isPremium ? "text-neutral-700" : "text-gray-500")}>
            {financingText}
          </p>
        )}
      </div>
    </div>
  );
};
