import clsx from 'clsx';
import '../../styles/product.css';

/**
 * ProductInfo Component
 * @param {Object} props - Component props
 * @param {Object} props.data - Product information data
 * @param {string} props.data.title - Product title
 * @param {string} [props.data.subtitle] - Product subtitle/category
 * @param {string} [props.data.sku] - Product SKU
 * @param {string} [props.data.brand] - Product brand
 * @param {number} props.data.price - Product price
 * @param {number} [props.data.originalPrice] - Original price for showing discount
 * @param {number} [props.data.rating] - Product rating (0-5)
 * @param {number} [props.data.reviewCount] - Number of reviews
 * @param {string} [props.data.badge] - Badge text ("New", "Sale", "Hot", etc.)
 * @param {boolean} [props.data.inStock=true] - Stock availability
 * @param {string} [props.data.availability] - Detailed availability text
 * @param {Array} [props.data.tags] - Product tags
 * @param {string} [props.data.variant='default'] - Style variant: 'default' | 'minimal' | 'card' | 'compact'
 * @param {string} [props.data.currency='$'] - Currency symbol
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute for the section element
 */
export const ProductInfo = ({ data, className, id }) => {
  // Safety check for data
  if (!data || typeof data !== 'object') {
    console.error('ProductInfo: data prop is required and must be an object');
    return null;
  }

  const {
    title,
    subtitle,
    sku,
    brand,
    price,
    originalPrice,
    rating,
    reviewCount,
    badge,
    inStock = true,
    availability,
    tags = [],
    variant = 'default',
    currency = '$'
  } = data;

  // Rating display
  const renderRating = (rating) => {
    if (!rating) return null;
    const stars = [];
    const fullStars = Math.floor(rating);
    const hasHalfStar = rating % 1 !== 0;

    for (let i = 0; i < fullStars; i++) {
      stars.push(
        <svg key={i} className="w-4 h-4 text-yellow-400 fill-current" viewBox="0 0 20 20">
          <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
        </svg>
      );
    }

    if (hasHalfStar) {
      stars.push(
        <svg key="half" className="w-4 h-4 text-yellow-400 fill-current" viewBox="0 0 20 20">
          <defs>
            <linearGradient id="halfStar">
              <stop offset="50%" stopColor="currentColor" />
              <stop offset="50%" stopColor="transparent" />
            </linearGradient>
          </defs>
          <path fill="url(#halfStar)" d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
        </svg>
      );
    }

    const emptyStars = 5 - Math.ceil(rating);
    for (let i = 0; i < emptyStars; i++) {
      stars.push(
        <svg key={`empty-${i}`} className="w-4 h-4 text-gray-300 fill-current" viewBox="0 0 20 20">
          <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
        </svg>
      );
    }

    return (
      <div className="flex items-center space-x-2">
        <div className="flex items-center space-x-1">
          {stars}
        </div>
        {rating && <span className="text-sm text-gray-600">({rating})</span>}
        {reviewCount && <span className="text-sm text-gray-500"> {reviewCount} reviews</span>}
      </div>
    );
  };

  // Default variant - Standard product information layout
  if (variant === 'default') {
    return (
      <section
        id={id}
        className={clsx(
          "product-info-default",
          className
        )}
      >
        <div className="space-y-4">
          {/* Brand and Category */}
          {(brand || subtitle) && (
            <div className="flex items-center space-x-4">
              {brand && (
                <span className="text-sm font-medium text-blue-600 uppercase tracking-wide">
                  {brand}
                </span>
              )}
              {subtitle && (
                <span className="text-sm text-gray-500 uppercase tracking-wide">
                  {subtitle}
                </span>
              )}
            </div>
          )}

          {/* Title */}
          <h1 className="text-3xl lg:text-4xl font-bold text-gray-900 leading-tight">
            {title}
          </h1>

          {/* SKU */}
          {sku && (
            <p className="text-sm text-gray-500">
              SKU: {sku}
            </p>
          )}

          {/* Rating and Reviews */}
          {(rating || reviewCount) && (
            <div className="flex items-center space-x-4">
              {renderRating(rating)}
            </div>
          )}

          {/* Price */}
          <div className="flex items-center space-x-4">
            <span className="text-3xl font-bold text-gray-900">
              {currency}{price}
            </span>
            {originalPrice && (
              <span className="text-xl text-gray-500 line-through">
                {currency}{originalPrice}
              </span>
            )}
            {originalPrice && price && (
              <span className="text-sm font-medium text-green-600">
                Save {currency}{(originalPrice - price).toFixed(2)}
              </span>
            )}
          </div>

          {/* Availability */}
          <div className="flex items-center space-x-4">
            <div className="flex items-center space-x-2">
              <span className={clsx(
                "inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium",
                inStock ? "bg-green-100 text-green-800" : "bg-red-100 text-red-800"
              )}>
                <svg className={clsx(
                  "w-3 h-3 mr-1",
                  inStock ? "text-green-600" : "text-red-600"
                )} fill="currentColor" viewBox="0 0 20 20">
                  <path fillRule="evenodd" d={inStock ? "M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" : "M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z"} clipRule="evenodd" />
                </svg>
                {inStock ? "In Stock" : "Out of Stock"}
              </span>
            </div>
            {availability && (
              <span className="text-sm text-gray-600">
                {availability}
              </span>
            )}
            {badge && (
              <span className="inline-flex items-center px-3 py-1 rounded-full text-xs font-medium bg-red-100 text-red-800">
                {badge}
              </span>
            )}
          </div>

          {/* Tags */}
          {tags.length > 0 && (
            <div className="flex flex-wrap gap-2">
              {tags.map((tag, index) => (
                <span
                  key={index}
                  className="inline-flex items-center px-3 py-1 rounded-full text-xs font-medium bg-gray-100 text-gray-800"
                >
                  {tag}
                </span>
              ))}
            </div>
          )}
        </div>
      </section>
    );
  }

  // Minimal variant - Clean minimal design
  if (variant === 'minimal') {
    return (
      <section
        id={id}
        className={clsx(
          "product-info-minimal",
          className
        )}
      >
        <div className="space-y-3">
          {/* Brand */}
          {brand && (
            <p className="text-sm text-gray-500 uppercase tracking-wider font-medium">
              {brand}
            </p>
          )}

          {/* Title */}
          <h1 className="text-3xl font-light text-gray-900 leading-tight">
            {title}
          </h1>

          {/* Subtitle */}
          {subtitle && (
            <p className="text-sm text-gray-600">
              {subtitle}
            </p>
          )}

          {/* Price */}
          <div className="flex items-baseline space-x-3">
            <span className="text-2xl font-light text-gray-900">
              {currency}{price}
            </span>
            {originalPrice && (
              <span className="text-lg text-gray-400 line-through">
                {currency}{originalPrice}
              </span>
            )}
          </div>

          {/* Rating */}
          {rating && (
            <div>
              {renderRating(rating)}
            </div>
          )}

          {/* Availability */}
          <div className="flex items-center space-x-2">
            <span className={clsx(
              "text-sm font-medium uppercase tracking-wider",
              inStock ? "text-gray-600" : "text-red-600"
            )}>
              {inStock ? "Available" : "Sold Out"}
            </span>
            {badge && (
              <span className="text-sm font-medium text-gray-600 uppercase tracking-wider">
                 {badge}
              </span>
            )}
          </div>
        </div>
      </section>
    );
  }

  // Card variant - Card-style layout
  if (variant === 'card') {
    return (
      <section
        id={id}
        className={clsx(
          "product-info-card bg-white rounded-lg shadow-sm border border-gray-200 p-6",
          className
        )}
      >
        <div className="space-y-4">
          {/* Header */}
          <div className="flex items-start justify-between">
            <div className="flex-1">
              {brand && (
                <p className="text-xs text-blue-600 uppercase tracking-wide font-medium mb-1">
                  {brand}
                </p>
              )}
              <h2 className="text-xl font-semibold text-gray-900 leading-tight">
                {title}
              </h2>
              {subtitle && (
                <p className="text-sm text-gray-600 mt-1">
                  {subtitle}
                </p>
              )}
            </div>
            {badge && (
              <span className="inline-flex items-center px-2 py-1 rounded text-xs font-medium bg-red-100 text-red-800 ml-4">
                {badge}
              </span>
            )}
          </div>

          {/* Price and Rating */}
          <div className="flex items-center justify-between">
            <div className="flex items-baseline space-x-2">
              <span className="text-2xl font-bold text-gray-900">
                {currency}{price}
              </span>
              {originalPrice && (
                <span className="text-lg text-gray-500 line-through">
                  {currency}{originalPrice}
                </span>
              )}
            </div>
            {rating && (
              <div className="flex items-center space-x-1">
                <svg className="w-4 h-4 text-yellow-400 fill-current" viewBox="0 0 20 20">
                  <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
                </svg>
                <span className="text-sm text-gray-600">{rating}</span>
                {reviewCount && (
                  <span className="text-sm text-gray-500">({reviewCount})</span>
                )}
              </div>
            )}
          </div>

          {/* Availability */}
          <div className="flex items-center space-x-2">
            <span className={clsx(
              "inline-flex items-center px-2 py-1 rounded-full text-xs font-medium",
              inStock ? "bg-green-100 text-green-800" : "bg-red-100 text-red-800"
            )}>
              {inStock ? " In Stock" : " Out of Stock"}
            </span>
            {availability && (
              <span className="text-xs text-gray-600">
                {availability}
              </span>
            )}
          </div>

          {/* SKU and Tags */}
          {(sku || tags.length > 0) && (
            <div className="flex items-center justify-between pt-2 border-t border-gray-100">
              {sku && (
                <span className="text-xs text-gray-500">
                  SKU: {sku}
                </span>
              )}
              {tags.length > 0 && (
                <div className="flex flex-wrap gap-1">
                  {tags.slice(0, 3).map((tag, index) => (
                    <span
                      key={index}
                      className="inline-flex items-center px-2 py-1 rounded text-xs font-medium bg-gray-100 text-gray-700"
                    >
                      {tag}
                    </span>
                  ))}
                </div>
              )}
            </div>
          )}
        </div>
      </section>
    );
  }

  // Compact variant - Space-efficient layout
  if (variant === 'compact') {
    return (
      <section
        id={id}
        className={clsx(
          "product-info-compact",
          className
        )}
      >
        <div className="flex items-center justify-between">
          <div className="flex-1 min-w-0">
            <h3 className="text-lg font-semibold text-gray-900 truncate">
              {title}
            </h3>
            {brand && (
              <p className="text-sm text-gray-600 truncate">
                by {brand}
              </p>
            )}
          </div>

          <div className="flex items-center space-x-4 ml-4">
            <div className="text-right">
              <div className="flex items-baseline space-x-2">
                <span className="text-xl font-bold text-gray-900">
                  {currency}{price}
                </span>
                {originalPrice && (
                  <span className="text-sm text-gray-500 line-through">
                    {currency}{originalPrice}
                  </span>
                )}
              </div>
              {rating && (
                <div className="flex items-center justify-end space-x-1 mt-1">
                  <svg className="w-3 h-3 text-yellow-400 fill-current" viewBox="0 0 20 20">
                    <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
                  </svg>
                  <span className="text-xs text-gray-600">{rating}</span>
                </div>
              )}
            </div>

            <div className="flex items-center space-x-2">
              <span className={clsx(
                "inline-flex items-center px-2 py-1 rounded-full text-xs font-medium",
                inStock ? "bg-green-100 text-green-800" : "bg-red-100 text-red-800"
              )}>
                {inStock ? "In Stock" : "Out"}
              </span>
              {badge && (
                <span className="inline-flex items-center px-2 py-1 rounded-full text-xs font-medium bg-red-100 text-red-800">
                  {badge}
                </span>
              )}
            </div>
          </div>
        </div>
      </section>
    );
  }

  return null;
};
