import { useState, useMemo, useEffect } from 'react';
import clsx from 'clsx';
import { ProductLayout } from '../ProductLayout/ProductLayout';
import { ProductModern } from '../ProductModern/ProductModern';
import { ProductStandard } from '../ProductStandard/ProductStandard';
import { ProductSidebar } from '../ProductSidebar';
import { ProductSearch } from '../ProductSearch';
import { ProductToggle } from '../ProductToggle';
import { ProductPagination } from '../ProductPagination';
import '../../styles/product.css';

/**
 * ProductShop Component - Complete product page with sidebar, search, toggle, and pagination
 * @param {Object} props - Component props
 * @param {Object} props.data - Product shop configuration data
 * @param {string} [props.data.layout='sidebar'] - Layout: 'sidebar' | 'fullwidth'
 * @param {string} [props.data.productComponent='ProductLayout'] - Product component: 'ProductLayout' | 'ProductModern' | 'ProductStandard'
 * @param {Object} props.data.products - Product data for the selected component
 * @param {Object} [props.data.sidebar] - Sidebar configuration
 * @param {Object} [props.data.search] - Search configuration
 * @param {Object} [props.data.toggle] - Toggle configuration
 * @param {Object} [props.data.pagination] - Pagination configuration
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute for the section element
 */
export const ProductShop = ({ data, className, id }) => {
  if (!data || typeof data !== 'object') {
    console.error('ProductShop: data prop is required and must be an object');
    return null;
  }

  const {
    layout = 'sidebar',
    productComponent = 'ProductLayout',
    products: productsConfig = {},
    sidebar = {},
    search = {},
    toggle = {},
    pagination = {},
  } = data;

  // Extract products array from products config
  const allProducts = Array.isArray(productsConfig) 
    ? productsConfig 
    : (productsConfig.products || []);

  const [searchQuery, setSearchQuery] = useState('');
  const [selectedCategories, setSelectedCategories] = useState(
    sidebar.categories?.filter(cat => cat.active).map(cat => cat.label) || []
  );
  const [selectedFilters, setSelectedFilters] = useState({});
  const [priceRange, setPriceRange] = useState(
    sidebar.priceRange || pagination.priceRange || { 
      min: 0, 
      max: 1000, 
      currentMin: 0, 
      currentMax: 1000 
    }
  );
  const [currentView, setCurrentView] = useState(toggle.defaultView || 'grid');
  const [currentPage, setCurrentPage] = useState(pagination.currentPage || 1);
  const [sidebarOpen, setSidebarOpen] = useState(false);

  // Reset page when filters change
  useEffect(() => {
    setCurrentPage(1);
  }, [searchQuery, selectedCategories, selectedFilters, priceRange]);

  // Filter products based on search, categories, filters, and price range
  const filteredProducts = useMemo(() => {
    // Ensure allProducts is an array
    if (!Array.isArray(allProducts)) {
      return [];
    }
    
    let filtered = [...allProducts];

    // Search filter
    if (searchQuery && searchQuery.trim()) {
      const query = searchQuery.toLowerCase().trim();
      filtered = filtered.filter(product => 
        product.title?.toLowerCase().includes(query) ||
        product.description?.toLowerCase().includes(query) ||
        product.category?.toLowerCase().includes(query) ||
        product.brand?.toLowerCase().includes(query)
      );
    }

    // Category filter
    if (selectedCategories.length > 0) {
      // If "All Products" is selected, show all (or if no specific category is selected)
      const hasAllProducts = selectedCategories.includes('All Products');
      if (!hasAllProducts) {
        filtered = filtered.filter(product => 
          selectedCategories.includes(product.category)
        );
      }
    }

    // Price range filter
    if (priceRange && priceRange.currentMin !== undefined && priceRange.currentMax !== undefined) {
      filtered = filtered.filter(product => {
        const price = typeof product.price === 'number' ? product.price : parseFloat(product.price) || 0;
        return price >= priceRange.currentMin && price <= priceRange.currentMax;
      });
    }

    // Additional filters (rating, badge, etc.)
    if (Object.keys(selectedFilters).length > 0) {
      Object.entries(selectedFilters).forEach(([filterType, filterValue]) => {
        if (filterValue !== null && filterValue !== undefined) {
          filtered = filtered.filter(product => {
            // Rating filter - check if rating is greater than or equal to filter value
            if (filterType === 'rating') {
              const productRating = typeof product.rating === 'number' ? product.rating : parseFloat(product.rating) || 0;
              return productRating >= filterValue;
            }
            
            // Badge filter - exact match
            if (filterType === 'badge') {
              return product.badge === filterValue;
            }
            
            // Generic filter - check exact match or array inclusion
            const productValue = product[filterType];
            if (Array.isArray(productValue)) {
              return productValue.includes(filterValue);
            }
            return productValue === filterValue;
          });
        }
      });
    }

    return filtered;
  }, [allProducts, searchQuery, selectedCategories, selectedFilters, priceRange]);

  // Paginate products
  const itemsPerPage = pagination.itemsPerPage || 12;
  const totalPages = Math.ceil(filteredProducts.length / itemsPerPage);
  const startIndex = (currentPage - 1) * itemsPerPage;
  const endIndex = startIndex + itemsPerPage;
  const paginatedProducts = filteredProducts.slice(startIndex, endIndex);

  // Update pagination when total pages change
  useEffect(() => {
    if (currentPage > totalPages && totalPages > 0) {
      setCurrentPage(1);
    }
  }, [totalPages]);

  // Render product component
  const renderProductComponent = () => {
    const productData = {
      ...productsConfig,
      products: paginatedProducts,
    };

    switch (productComponent) {
      case 'ProductModern':
        return <ProductModern data={productData} />;
      case 'ProductStandard':
        return <ProductStandard data={productData} />;
      case 'ProductLayout':
      default:
        return <ProductLayout data={productData} />;
    }
  };

  return (
    <section
      id={id}
      className={clsx(
        'w-full py-8 sm:py-12 lg:py-16 px-4 sm:px-6 lg:px-8',
        className
      )}
    >
      <div className="max-w-7xl mx-auto w-full">
        {/* Top Bar: Search and Toggle */}
        <div className="flex flex-col sm:flex-row items-start sm:items-center justify-between gap-4 mb-6 sm:mb-8">
          <div className="w-full sm:w-auto sm:flex-1 max-w-md">
            <ProductSearch
              data={search}
              onSearch={(query) => {
                setSearchQuery(query);
              }}
            />
          </div>
          <div className="flex items-center gap-4">
            <ProductToggle
              data={toggle}
              onViewChange={setCurrentView}
            />
            {layout === 'sidebar' && (
              <button
                onClick={() => setSidebarOpen(!sidebarOpen)}
                className="lg:hidden p-2 border border-gray-300 rounded-lg hover:bg-gray-50 transition-colors"
                aria-label="Toggle sidebar"
              >
                <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
                </svg>
              </button>
            )}
          </div>
        </div>

        {/* Main Content Area */}
        <div className={clsx(
          'flex gap-6 lg:gap-8',
          layout === 'sidebar' ? 'flex-col lg:flex-row' : 'flex-col'
        )}>
          {/* Sidebar */}
          {layout === 'sidebar' && (
            <>
              {/* Mobile Sidebar Overlay */}
              {sidebarOpen && (
                <div
                  className="fixed inset-0 bg-black bg-opacity-50 z-40 lg:hidden"
                  onClick={() => setSidebarOpen(false)}
                />
              )}

              {/* Sidebar */}
              <aside className={clsx(
                'w-full lg:w-64 flex-shrink-0',
                sidebarOpen 
                  ? 'fixed left-0 top-0 h-full z-50 lg:relative lg:z-auto lg:h-auto overflow-y-auto bg-white p-4 lg:p-0'
                  : 'hidden lg:block'
              )}>
                <div className="flex items-center justify-between mb-4 lg:hidden">
                  <h2 className="text-lg font-bold">Filters</h2>
                  <button
                    onClick={() => setSidebarOpen(false)}
                    className="p-2 hover:bg-gray-100 rounded-lg"
                    aria-label="Close sidebar"
                  >
                    <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                    </svg>
                  </button>
                </div>
                <ProductSidebar
                  data={{
                    ...sidebar,
                    priceRange: {
                      ...(sidebar.priceRange || {}),
                      ...priceRange,
                    },
                  }}
                  onCategoryChange={(categories) => {
                    setSelectedCategories(categories);
                  }}
                  onFilterChange={(filters) => {
                    setSelectedFilters(filters);
                  }}
                  onPriceRangeChange={(range) => {
                    setPriceRange(range);
                  }}
                />
              </aside>
            </>
          )}

          {/* Products Area */}
          <div className="flex-1 min-w-0">
            {/* Results count */}
            <div className="mb-4 text-sm text-gray-600">
              Showing {filteredProducts.length} product{filteredProducts.length !== 1 ? 's' : ''}
            </div>

            {/* Product Grid/List */}
            <div className={clsx(
              currentView === 'list' && 'space-y-4'
            )}>
              {renderProductComponent()}
            </div>

            {/* Pagination */}
            {filteredProducts.length > 0 && (
              <div className="mt-8 sm:mt-12">
                <ProductPagination
                  data={{
                    ...pagination,
                    currentPage,
                    totalPages,
                    totalItems: filteredProducts.length,
                    itemsPerPage,
                  }}
                  onPageChange={setCurrentPage}
                />
              </div>
            )}

            {/* Empty State */}
            {filteredProducts.length === 0 && (
              <div className="text-center py-12 sm:py-16">
                <svg
                  className="mx-auto h-12 w-12 text-gray-400"
                  fill="none"
                  stroke="currentColor"
                  viewBox="0 0 24 24"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M20 13V6a2 2 0 00-2-2H6a2 2 0 00-2 2v7m16 0v5a2 2 0 01-2 2H6a2 2 0 01-2-2v-5m16 0h-2.586a1 1 0 00-.707.293l-2.414 2.414a1 1 0 01-.707.293h-3.172a1 1 0 01-.707-.293l-2.414-2.414A1 1 0 006.586 13H4"
                  />
                </svg>
                <h3 className="mt-2 text-sm font-medium text-gray-900">No products found</h3>
                <p className="mt-1 text-sm text-gray-500">
                  Try adjusting your search or filter criteria.
                </p>
              </div>
            )}
          </div>
        </div>
      </div>
    </section>
  );
};

