import React, { useState, useEffect } from 'react';
import { clsx } from 'clsx';

export const CheckoutTrialSection = ({ data = {}, variant = 'standard', className, loading = false }) => {
  const [timedOut, setTimedOut] = useState(false);

  // Auto-show content after 2 seconds if loading is true
  useEffect(() => {
    if (loading) {
      setTimedOut(false);
      const timer = setTimeout(() => {
        setTimedOut(true);
      }, 2000);
      return () => clearTimeout(timer);
    }
  }, [loading]);

  const shouldShowSkeleton = loading && !timedOut;
  const { title, items = [], termsText, termsLinkText, termsLink } = data;

  const isMinimal = variant === 'minimal';
  const isPremium = variant === 'premium';

  // Show skeleton loading state
  if (shouldShowSkeleton) {
    return (
      <div className={clsx(
        "checkout-trial-section transition-all",
        variant === 'standard' && "border border-gray-200 p-8 space-y-6 rounded-sm",
        variant === 'minimal' && "space-y-4",
        variant === 'premium' && "bg-[#111111] text-white p-12 rounded-[40px] border border-neutral-900",
        className
      )}>
        <h2 className={clsx(
          "uppercase mb-4",
          variant === 'standard' && "text-[11px] font-bold tracking-[0.2em]",
          variant === 'minimal' && "text-sm font-semibold",
          variant === 'premium' && "text-[10px] uppercase tracking-widest font-bold"
        )}>
          {data.title || 'Free Trial'}
        </h2>

        <div className="space-y-4">
          {[1, 2].map((item) => (
            <div key={item} className="checkout-skeleton h-16 w-full rounded-lg"></div>
          ))}
        </div>
      </div>
    );
  }

  return (
    <div className={clsx(
      "checkout-trial-section transition-all",
      variant === 'standard' && "border border-gray-200 p-8 space-y-6 rounded-sm",
      variant === 'minimal' && "space-y-4",
      variant === 'premium' && "bg-[#111111] text-white p-12 rounded-[40px] border border-neutral-900",
      className
    )}>
      <h2 className={clsx(
        "uppercase mb-4",
        variant === 'standard' && "text-[11px] font-bold tracking-[0.2em]",
        variant === 'minimal' && "text-xs font-black text-gray-400 tracking-[0.2em]",
        variant === 'premium' && "text-sm font-bold tracking-[0.4em] text-neutral-600 mb-8"
      )}>
        {title}
      </h2>
      <ul className={clsx("space-y-4", isPremium && "space-y-8")}>
        {items.map((text, i) => (
          <li key={i} className="flex items-start text-sm">
            <span className={clsx(
              "flex items-center justify-center shrink-0 mr-4",
              variant === 'standard' && "w-6 h-6 border border-black rounded-full text-[10px] font-bold",
              variant === 'minimal' && "text-black font-black",
              variant === 'premium' && "w-8 h-8 bg-neutral-900 border border-neutral-800 rounded-xl text-[10px] font-bold text-neutral-400"
            )}>
              {variant === 'minimal' ? '' : i + 1}
            </span>
            <span className={clsx(
              "leading-tight pt-1",
              variant === 'standard' && "text-gray-700",
              variant === 'minimal' && "text-gray-500",
              variant === 'premium' && "text-lg font-serif italic text-neutral-300"
            )}>
              {text}
            </span>
          </li>
        ))}
      </ul>
      {(termsText || termsLinkText) && (
        <p className={clsx(
          "text-[10px] pt-2 leading-loose uppercase tracking-widest",
          isPremium ? "text-neutral-700 text-center mt-8" : "text-gray-500"
        )}>
          {termsText} <a href={termsLink || '#'} className="underline">{termsLinkText}</a>.
        </p>
      )}
    </div>
  );
};
