import { useState, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/hero.css';

/**
 * HeroGradient Component - Modern Gradient Hero with Animated Elements
 * @param {Object} props - Component props
 * @param {Object} props.data - Hero section configuration data
 * @param {string} props.data.title - Main heading text
 * @param {string} [props.data.subtitle] - Subtitle text displayed above title
 * @param {string} [props.data.description] - Description text displayed below title
 * @param {Array} [props.data.buttons=[]] - Array of call-to-action buttons
 * @param {Array} [props.data.features=[]] - Array of feature highlights
 * @param {string} [props.data.gradient='default'] - Gradient variant: 'default' | 'purple' | 'blue' | 'orange' | 'green'
 * @param {boolean} [props.data.showFloatingCards=true] - Show floating feature cards
 * @param {string} [props.data.featureAlignment='center'] - Feature alignment: 'left' | 'center' | 'right'
 * @param {string} [props.data.className] - Additional CSS classes for the section
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute for the section element
 * @param {boolean} [props.loader=false] - Show loading state
 */
export const HeroGradient = ({ data, className, id, loader = false }) => {
  if (!data || typeof data !== 'object') {
    console.error('HeroGradient: data prop is required and must be an object');
    return null;
  }

  const [showLoader, setShowLoader] = useState(loader);

  // Auto-hide loader after 2 seconds when loader prop is true
  useEffect(() => {
    if (loader) {
      const timer = setTimeout(() => {
        setShowLoader(false);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowLoader(false);
    }
  }, [loader]);

  const {
    title,
    subtitle,
    description,
    buttons = [],
    features = [],
    gradient = 'default',
    showFloatingCards = true,
    featureAlignment = 'center',
    className: dataClassName,
  } = data;

  const gradientClasses = {
    default: 'from-blue-600 via-purple-600 to-pink-600',
    purple: 'from-purple-600 via-pink-600 to-red-600',
    blue: 'from-blue-500 via-cyan-500 to-teal-500',
    orange: 'from-orange-500 via-red-500 to-pink-500',
    green: 'from-green-500 via-emerald-500 to-teal-500',
  };

  const buttonVariantClasses = {
    primary: 'bg-white text-gray-900 hover:bg-gray-100 shadow-lg hover:shadow-xl',
    secondary: 'bg-gray-900/20 backdrop-blur-sm text-white border-2 border-white/30 hover:bg-white/10',
    outline: 'bg-transparent text-white border-2 border-white hover:bg-white hover:text-gray-900',
  };

  const featureAlignmentClasses = {
    left: 'text-left items-start',
    center: 'text-center items-center',
    right: 'text-right items-end',
  };

  // Show skeleton loader if loader prop is true
  if (showLoader) {
    return (
      <section
        id={id}
        className={clsx(
          'relative w-full min-h-screen flex items-center justify-center overflow-visible px-4 sm:px-6 lg:px-8 py-20',
          'bg-gradient-to-br from-gray-200 via-gray-300 to-gray-400',
          className,
          dataClassName
        )}
      >
        {/* Animated gradient background skeleton */}
        <div className="absolute inset-0 bg-gradient-to-br from-gray-200 via-gray-300 to-gray-400 animate-pulse"></div>

        {/* Floating cards skeleton */}
        {showFloatingCards && (
          <>
            {[0, 1, 2].map((index) => (
              <div
                key={index}
                className="absolute bg-white/20 backdrop-blur-md rounded-2xl p-4 border border-white/30 shadow-xl animate-pulse"
                style={{
                  top: index === 2 ? '80%' : `${20 + index * 25}%`,
                  right: index % 2 === 0 ? '5%' : '2%',
                  zIndex: 50,
                  width: '180px',
                  height: '80px',
                }}
              >
                <div className="flex items-center gap-3">
                  <div className="w-8 h-8 bg-white/30 rounded animate-pulse"></div>
                  <div className="flex-1 space-y-2">
                    <div className="h-3 bg-white/30 rounded w-20 animate-pulse"></div>
                    <div className="h-2 bg-white/30 rounded w-16 animate-pulse"></div>
                  </div>
                </div>
              </div>
            ))}
          </>
        )}

        {/* Main Content Skeleton */}
        <div className="relative z-10 max-w-5xl mx-auto w-full text-center">
          {/* Subtitle skeleton */}
          <div className="inline-block mb-6">
            <div className="h-8 bg-white/30 rounded-full w-40 mx-auto animate-pulse"></div>
          </div>

          {/* Title skeleton */}
          <div className="space-y-4 mb-6">
            <div className="h-16 bg-white/30 rounded w-full max-w-3xl mx-auto animate-pulse"></div>
            <div className="h-16 bg-white/30 rounded w-full max-w-2xl mx-auto animate-pulse"></div>
          </div>

          {/* Description skeleton */}
          <div className="space-y-2 mb-10 max-w-3xl mx-auto">
            <div className="h-6 bg-white/30 rounded w-full animate-pulse"></div>
            <div className="h-6 bg-white/30 rounded w-5/6 mx-auto animate-pulse"></div>
            <div className="h-6 bg-white/30 rounded w-4/6 mx-auto animate-pulse"></div>
          </div>

          {/* Buttons skeleton */}
          <div className="flex flex-wrap gap-4 justify-center mb-12">
            <div className="h-14 bg-white/30 rounded-full w-36 animate-pulse"></div>
            <div className="h-14 bg-white/30 rounded-full w-32 animate-pulse"></div>
          </div>

          {/* Feature list skeleton */}
          {features.length > 0 && (
            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4 mt-12">
              {[0, 1, 2].map((index) => (
                <div
                  key={index}
                  className="bg-white/10 backdrop-blur-md rounded-xl p-6 border border-white/20 animate-pulse"
                >
                  <div className="w-12 h-12 bg-white/30 rounded-lg mb-3 mx-auto animate-pulse"></div>
                  <div className="h-5 bg-white/30 rounded w-3/4 mx-auto mb-2 animate-pulse"></div>
                  <div className="h-4 bg-white/30 rounded w-full animate-pulse"></div>
                </div>
              ))}
            </div>
          )}
        </div>

        {/* Scroll indicator skeleton */}
        <div className="absolute bottom-8 left-1/2 transform -translate-x-1/2">
          <div className="w-6 h-10 border-2 border-white/30 rounded-full flex justify-center">
            <div className="w-1.5 h-3 bg-white/30 rounded-full mt-2 animate-pulse"></div>
          </div>
        </div>
      </section>
    );
  }

  return (
    <section
      id={id}
      className={clsx(
        'relative w-full min-h-screen flex items-center justify-center overflow-visible px-4 sm:px-6 lg:px-8 py-20',
        className,
        dataClassName
      )}
    >
      {/* Animated Gradient Background */}
      <div
        className={clsx(
          'absolute inset-0 bg-gradient-to-br overflow-hidden',
          gradientClasses[gradient],
          'animate-gradient'
        )}
      >
        <div className="absolute inset-0 bg-[url('data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iNjAiIGhlaWdodD0iNjAiIHZpZXdCb3g9IjAgMCA2MCA2MCIgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIj48ZyBmaWxsPSJub25lIiBmaWxsLXJ1bGU9ImV2ZW5vZGQiPjxnIGZpbGw9IiNmZmZmZmYiIGZpbGwtb3BhY2l0eT0iMC4wNSI+PGNpcmNsZSBjeD0iMzAiIGN5PSIzMCIgcj0iMiIvPjwvZz48L2c+PC9zdmc+')] opacity-30"></div>
      </div>

      {/* Animated Blob Shapes */}
      <div className="absolute top-0 left-0 w-96 h-96 bg-white/10 rounded-full blur-3xl animate-pulse"></div>
      <div className="absolute bottom-0 right-0 w-96 h-96 bg-white/10 rounded-full blur-3xl animate-pulse delay-1000"></div>
      <div className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 w-96 h-96 bg-white/5 rounded-full blur-3xl animate-pulse delay-500"></div>

      {/* Floating Feature Cards */}
      {showFloatingCards && features.length > 0 && (
        <>
          {features.slice(0, 3).map((feature, index) => (
            <div
              key={index}
              className={clsx(
                'absolute block bg-white/10 backdrop-blur-md rounded-2xl p-3 sm:p-4 border border-white/20 shadow-xl',
                'animate-float'
              )}
              style={{
                top: index === 2 ? '80%' : `${20 + index * 25}%`,
                right: index % 2 === 0 ? '5%' : '2%',
                zIndex: 50,
                animationDelay: `${index * 0.5}s`,
                animationDuration: `${3 + index}s`,
                maxWidth: '200px',
                pointerEvents: 'auto',
                willChange: 'transform',
              }}
            >
              <div className="flex items-center gap-2 sm:gap-3">
                {feature.icon && (
                  <div className="text-xl sm:text-2xl flex-shrink-0">
                    {typeof feature.icon === 'string' ? (
                      <span>{feature.icon}</span>
                    ) : (
                      <div className="w-5 h-5 sm:w-6 sm:h-6">{feature.icon}</div>
                    )}
                  </div>
                )}
                <div>
                  <div className="text-white font-semibold text-xs sm:text-sm">{feature.title}</div>
                  {feature.description && (
                    <div className="text-white/80 text-[10px] sm:text-xs mt-1">{feature.description}</div>
                  )}
                </div>
              </div>
            </div>
          ))}
        </>
      )}

      {/* Main Content */}
      <div className="relative z-10 max-w-5xl mx-auto w-full text-center">
        {/* Subtitle */}
        {subtitle && (
          <div className="inline-block mb-6">
            <span className="px-4 py-2 bg-white/20 backdrop-blur-sm text-white text-sm font-semibold uppercase tracking-wider rounded-full border border-white/30">
              {subtitle}
            </span>
          </div>
        )}

        {/* Title */}
        <h1 className="text-5xl sm:text-6xl md:text-7xl lg:text-8xl font-bold text-white mb-6 leading-tight">
          <span className="block">{title}</span>
        </h1>

        {/* Description */}
        {description && (
          <p className="text-xl sm:text-2xl md:text-3xl text-white/90 mb-10 max-w-3xl mx-auto leading-relaxed">
            {description}
          </p>
        )}

        {/* Buttons */}
        {buttons.length > 0 && (
          <div className="flex flex-wrap gap-4 justify-center mb-12">
            {buttons.map((button, index) => {
              const ButtonComponent = button.href ? 'a' : 'button';
              return (
                <ButtonComponent
                  key={index}
                  href={button.href}
                  onClick={button.onClick}
                  className={clsx(
                    'px-8 py-4 rounded-full font-semibold text-lg transition-all duration-300',
                    'transform hover:scale-105 active:scale-95',
                    'focus:outline-none focus:ring-4 focus:ring-white/50',
                    buttonVariantClasses[button.variant || 'primary'],
                    button.className
                  )}
                >
                  {button.text}
                </ButtonComponent>
              );
            })}
          </div>
        )}

        {/* Feature List */}
        {features.length > 0 && (
          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4 mt-12">
            {features.map((feature, index) => (
              <div
                key={index}
                className={clsx(
                  "bg-white/10 backdrop-blur-md rounded-xl p-6 border border-white/20 hover:bg-white/15 transition-all duration-300 flex flex-col",
                  featureAlignmentClasses[featureAlignment]
                )}
              >
                {feature.icon && (
                  <div className={clsx(
                    "mb-3",
                    featureAlignment === 'center' && "mx-auto",
                    featureAlignment === 'right' && "ml-auto"
                  )}>
                    {typeof feature.icon === 'string' ? (
                      <div className="text-3xl">{feature.icon}</div>
                    ) : (
                      <div className="w-10 h-10 text-white">{feature.icon}</div>
                    )}
                  </div>
                )}
                <h3 className="text-white font-semibold text-lg mb-2">{feature.title}</h3>
                {feature.description && (
                  <p className="text-white/80 text-sm">{feature.description}</p>
                )}
              </div>
            ))}
          </div>
        )}
      </div>

      {/* Scroll Indicator */}
      <div className="absolute bottom-8 left-1/2 transform -translate-x-1/2 animate-bounce">
        <div className="w-6 h-10 border-2 border-white/50 rounded-full flex justify-center">
          <div className="w-1.5 h-3 bg-white/50 rounded-full mt-2"></div>
        </div>
      </div>
    </section>
  );
};

