import { useState, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/faq-visual.css';

/**
 * FaqVisual Component - FAQ layout with image, button, and beautiful backgrounds
 * @param {Object} props - Component props
 * @param {Object} props.data - FAQ section configuration data
 * @param {string} [props.data.title] - Main heading text
 * @param {string} [props.data.subtitle] - Subtitle text displayed above title
 * @param {string} [props.data.description] - Description text displayed below title
 * @param {Array} props.data.items - Array of FAQ items with question and answer
 * @param {string} [props.data.image] - Image URL for the visual section
 * @param {string} [props.data.imageAlt] - Alt text for the image
 * @param {string} [props.data.imagePosition='right'] - Image position: 'left' | 'right'
 * @param {string} [props.data.backgroundImage] - Background image URL for the section
 * @param {number} [props.data.backgroundOverlay=0.5] - Background overlay opacity (0-1)
 * @param {string} [props.data.backgroundPosition='center'] - Background image position
 * @param {Object|Array} [props.data.button] - CTA button configuration (single button object or array of button objects)
 * @param {string} [props.data.button.text] - Button text (when single button)
 * @param {string} [props.data.button.href] - Button link URL (when single button)
 * @param {string} [props.data.button.variant='primary'] - Button style: 'primary' | 'secondary' | 'outline' (when single button)
 * @param {Array<Object>} [props.data.buttons] - Array of button objects (alternative to button prop)
 * @param {string} [props.data.alignment='left'] - Text alignment: 'left' | 'center' | 'right'
 * @param {string} [props.data.textPosition='left'] - Text position when no image: 'left' | 'right' (controls which side text appears on)
 * @param {string} [props.data.variant='default'] - FAQ style variant: 'default' | 'gradient' | 'pattern' | 'glass'
 * @param {boolean} [props.data.allowMultipleOpen=false] - Allow multiple items to be open at once
 * @param {string} [props.data.className] - Additional CSS classes for the section
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute for the section element
 * @param {boolean} [props.loader=false] - Show loading state
 */
export const FaqVisual = ({ data, className, id, loader = false }) => {
  // Safety check for data
  if (!data || typeof data !== 'object') {
    console.error('FaqVisual: data prop is required and must be an object');
    return null;
  }

  const [showLoader, setShowLoader] = useState(loader);

  // Auto-hide loader after 2 seconds when loader prop is true
  useEffect(() => {
    if (loader) {
      const timer = setTimeout(() => {
        setShowLoader(false);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowLoader(false);
    }
  }, [loader]);

  // Show skeleton loader if loader prop is true
  if (showLoader) {
    const { alignment = 'left', variant = 'default', imagePosition = 'left', className: dataClassName } = data || {};
    const alignmentClasses = {
      left: 'text-left items-start',
      center: 'text-center items-center',
      right: 'text-right items-end',
    };

    return (
      <section
        id={id}
        className={clsx(
          'w-full py-12 sm:py-16 lg:py-20 px-4 sm:px-6 lg:px-8 relative',
          'bg-white',
          className,
          dataClassName
        )}
      >
        <div className="max-w-7xl mx-auto w-full">
          {/* Header Section Skeleton */}
          <div className={clsx('mb-10 sm:mb-12 lg:mb-16 flex flex-col', alignmentClasses[alignment])}>
            {/* Subtitle skeleton */}
            <div className="h-4 bg-gray-400 rounded w-16 mb-3 animate-pulse"></div>
            
            {/* Title skeleton */}
            <div className="h-12 bg-gray-400 rounded w-full max-w-2xl mb-4 animate-pulse"></div>
            
            {/* Description skeleton */}
            <div className="h-5 bg-gray-400 rounded w-full max-w-xl mb-6 animate-pulse"></div>
            
            {/* Button skeleton */}
            <div className="h-12 bg-gray-400 rounded-lg w-40 animate-pulse"></div>
          </div>

          {/* Two Column Layout: Image + FAQ Items */}
          <div className="grid lg:grid-cols-2 gap-8 lg:gap-12 items-start">
            {/* Image skeleton - Left side */}
            <div className={clsx(imagePosition === 'right' ? 'order-2' : 'order-1')}>
              <div className="h-96 bg-gray-400 rounded-lg animate-pulse"></div>
            </div>

            {/* FAQ items skeleton - Right side */}
            <div className={clsx('space-y-4', imagePosition === 'right' ? 'order-1' : 'order-2')}>
              {[0, 1, 2, 3].map((index) => (
                <div key={index} className={clsx(
                  'border rounded-lg p-5 transition-all',
                  index === 0 
                    ? 'border-gray-200 bg-white' 
                    : 'border-gray-200 bg-white'
                )}>
                  {/* Question skeleton */}
                  <div className="flex items-center justify-between mb-3">
                    <div className="h-6 bg-gray-400 rounded w-3/4 animate-pulse"></div>
                    <div className={clsx(
                      'h-5 w-5 rounded flex items-center justify-center',
                      index === 0 ? 'bg-gray-400' : 'bg-gray-200'
                    )}>
                      <div className="h-3 w-3 bg-gray-400 rounded animate-pulse"></div>
                    </div>
                  </div>
                  
                  {/* Answer skeleton - only show for first item (open state) */}
                  {index === 0 && (
                    <div className="space-y-2 mt-3 pt-3 border-t border-gray-200">
                      <div className="h-4 bg-gray-400 rounded w-full animate-pulse"></div>
                      <div className="h-4 bg-gray-400 rounded w-5/6 animate-pulse"></div>
                      <div className="h-4 bg-gray-400 rounded w-4/6 animate-pulse"></div>
                    </div>
                  )}
                </div>
              ))}
              
              {/* Button skeleton at bottom */}
              <div className="mt-6">
                <div className="h-12 bg-gray-400 rounded-lg w-40 animate-pulse"></div>
              </div>
            </div>
          </div>
        </div>
      </section>
    );
  }

  const {
    title,
    subtitle,
    description,
    items = [],
    image,
    imageAlt = 'FAQ illustration',
    imagePosition = 'right',
    backgroundImage,
    backgroundOverlay = 0.5,
    backgroundPosition = 'center',
    button,
    buttons,
    alignment = 'left',
    textPosition = 'left',
    variant = 'default',
    allowMultipleOpen = false,
    className: dataClassName,
  } = data;


  // Initialize with first item open by default
  const [openItems, setOpenItems] = useState(new Set(items.length > 0 ? [0] : []));

  const toggleItem = (index) => {
    setOpenItems((prev) => {
      const newSet = new Set(prev);
      if (newSet.has(index)) {
        newSet.delete(index);
      } else {
        if (!allowMultipleOpen) {
          newSet.clear();
        }
        newSet.add(index);
      }
      return newSet;
    });
  };

  const alignmentClasses = {
    left: 'text-left items-start',
    center: 'text-center items-center',
    right: 'text-right items-end',
  };

  const variantClasses = {
    default: 'bg-white',
    gradient: 'bg-gradient-to-br from-primary-50 via-white to-primary-100/30 relative overflow-hidden',
    pattern: 'bg-white relative overflow-hidden',
    glass: 'bg-white/90 backdrop-blur-xl border border-white/20',
    dark: 'bg-gradient-to-br from-gray-900 via-gray-800 to-gray-900 relative overflow-hidden',
  };

  const buttonVariants = {
    primary: 'bg-primary-600 hover:bg-primary-700 text-white shadow-lg hover:shadow-xl',
    secondary: 'bg-gray-900 hover:bg-gray-800 text-white shadow-lg hover:shadow-xl',
    outline: 'bg-transparent border-2 border-primary-600 text-primary-600 hover:bg-primary-600 hover:text-white',
  };

  // Normalize buttons: support both single button object and array of buttons
  const normalizedButtons = buttons || (button ? [button] : []);

  // Helper function to render a single button
  const renderButton = (btn, index) => {
    if (!btn || !btn.text) return null;
    
    const buttonClasses = clsx(
      'inline-flex items-center px-6 py-3 sm:px-8 sm:py-4 rounded-lg font-semibold text-base transition-all duration-300',
      buttonVariants[btn.variant || 'primary']
    );

    const buttonContent = (
      <>
        {btn.text}
        <svg
          className="ml-2 w-5 h-5"
          fill="none"
          stroke="currentColor"
          viewBox="0 0 24 24"
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            strokeWidth={2}
            d="M13 7l5 5m0 0l-5 5m5-5H6"
          />
        </svg>
      </>
    );

    if (btn.href) {
      return (
        <a
          key={index}
          href={btn.href}
          className={buttonClasses}
        >
          {buttonContent}
        </a>
      );
    }

    return (
      <button
        key={index}
        className={buttonClasses}
      >
        {buttonContent}
      </button>
    );
  };

  // Helper function to render button group
  const renderButtons = () => {
    if (!normalizedButtons || normalizedButtons.length === 0) return null;

    return (
      <div className={clsx(
        'mt-6 sm:mt-8 flex flex-wrap gap-3 sm:gap-4',
        alignment === 'center' && 'justify-center',
        alignment === 'right' && 'justify-end',
        alignment === 'left' && 'justify-start'
      )}>
        {normalizedButtons.map((btn, index) => renderButton(btn, index))}
      </div>
    );
  };

  if (items.length === 0) {
    console.warn('FaqVisual: items array is empty');
    return null;
  }

  const isImageLeft = imagePosition === 'left';

  return (
    <section
      id={id}
      className={clsx(
        'w-full py-12 sm:py-16 lg:py-20 px-4 sm:px-6 lg:px-8 relative',
        variantClasses[variant],
        className,
        dataClassName
      )}
      style={backgroundImage ? {
        backgroundImage: `url(${backgroundImage})`,
        backgroundSize: 'cover',
        backgroundPosition: backgroundPosition,
        backgroundRepeat: 'no-repeat',
        backgroundAttachment: 'fixed',
      } : {}}
    >
      {/* Background Image Overlay */}
      {backgroundImage && (
        <div 
          className="absolute inset-0 z-0"
          style={{
            backgroundColor: `rgba(0, 0, 0, ${backgroundOverlay})`,
          }}
        />
      )}
      {/* Gradient decorative elements */}
      {variant === 'gradient' && !backgroundImage && (
        <>
          <div className="absolute top-0 right-0 w-96 h-96 bg-primary-200/30 rounded-full blur-3xl -translate-y-1/2 translate-x-1/2 z-0" />
          <div className="absolute bottom-0 left-0 w-96 h-96 bg-primary-300/20 rounded-full blur-3xl translate-y-1/2 -translate-x-1/2 z-0" />
          <div className="absolute top-1/2 left-1/2 w-64 h-64 bg-primary-200/20 rounded-full blur-3xl -translate-x-1/2 -translate-y-1/2 z-0" />
        </>
      )}

      {/* Pattern overlay for pattern variant */}
      {variant === 'pattern' && (
        <div className="absolute inset-0 opacity-5">
          <div className="absolute inset-0" style={{
            backgroundImage: `url("data:image/svg+xml,%3Csvg width='60' height='60' viewBox='0 0 60 60' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%23000000' fill-opacity='1'%3E%3Cpath d='M36 34v-4h-2v4h-4v2h4v4h2v-4h4v-2h-4zm0-30V0h-2v4h-4v2h4v4h2V6h4V4h-4zM6 34v-4H4v4H0v2h4v4h2v-4h4v-2H6zM6 4V0H4v4H0v2h4v4h2V6h4V4H6z'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E")`,
          }} />
        </div>
      )}

      <div className="max-w-7xl mx-auto w-full relative z-10">
        {/* Layout: If no image, show title/description and FAQs side by side */}
        {!image && (title || subtitle || description) ? (
          <div className="flex flex-col lg:flex-row gap-8 lg:gap-12 xl:gap-16 items-start">
            {/* Text Section - Position controlled by textPosition prop */}
            <div className={clsx(
              "w-full lg:w-1/2 flex flex-col",
              textPosition === 'right' ? 'order-1 lg:order-2' : 'order-1',
              alignment === 'right' && 'lg:items-end lg:text-right',
              alignment === 'center' && 'lg:items-center lg:text-center',
              alignment === 'left' && 'lg:items-start lg:text-left'
            )}>
              {subtitle && (
                <p className={clsx(
                  "text-sm sm:text-base font-semibold uppercase tracking-wider mb-3",
                  backgroundImage ? 'text-white' : variant === 'dark' ? 'text-primary-400' : 'text-primary-600'
                )}>
                  {subtitle}
                </p>
              )}

              {title && (
                <h2 className={clsx(
                  "text-3xl sm:text-4xl md:text-5xl font-bold mb-4 sm:mb-6 leading-tight",
                  backgroundImage ? 'text-white' : variant === 'dark' ? 'text-white' : 'text-gray-900'
                )}>
                  {title}
                </h2>
              )}

              {description && (
                <p className={clsx(
                  'text-base sm:text-lg',
                  backgroundImage ? 'text-gray-100' : variant === 'dark' ? 'text-gray-300' : 'text-gray-600'
                )}>
                  {description}
                </p>
              )}

              {/* CTA Buttons */}
              {renderButtons()}
            </div>

            {/* FAQ Items Section - Position controlled by textPosition prop */}
            <div className={clsx(
              "w-full lg:w-1/2 flex flex-col",
              textPosition === 'right' ? 'order-2 lg:order-1' : 'order-2'
            )}>
              {/* FAQ Items */}
              <div className="space-y-3 w-full">
              {items.map((item, index) => {
                const isOpen = openItems.has(index);
                const { question, answer } = item;

                if (!question || !answer) {
                  console.warn(`FaqVisual: Item at index ${index} is missing question or answer`);
                  return null;
                }

                return (
                  <div
                    key={index}
                    className={clsx(
                      'group relative overflow-hidden rounded-lg transition-all duration-200',
                      isOpen
                        ? variant === 'gradient'
                          ? 'bg-gradient-to-br from-white via-primary-50/40 to-primary-100/30 shadow-md border-2 border-primary-200/50'
                          : variant === 'dark'
                          ? 'bg-gray-800/90 backdrop-blur-sm shadow-lg border-2 border-primary-500/30'
                          : backgroundImage
                          ? 'bg-white/95 backdrop-blur-sm shadow-lg border-2 border-white/50'
                          : 'bg-white shadow-sm border border-gray-200'
                        : variant === 'gradient'
                        ? 'bg-gradient-to-br from-white/90 via-primary-50/20 to-primary-100/10 backdrop-blur-sm border border-primary-200/30 hover:from-white hover:via-primary-50/40 hover:to-primary-100/30 hover:shadow-md hover:border-primary-300/50'
                        : variant === 'dark'
                        ? 'bg-gray-800/60 backdrop-blur-sm border border-gray-700/50 hover:bg-gray-800/80 hover:border-primary-500/30 hover:shadow-lg'
                        : backgroundImage
                        ? 'bg-white/90 backdrop-blur-sm border border-white/40 hover:bg-white/95 hover:border-white/60 hover:shadow-lg'
                        : 'bg-white border border-gray-200 hover:border-gray-300'
                    )}
                  >
                    {/* Gradient accent line for gradient variant */}
                    {variant === 'gradient' && (
                      <div className={clsx(
                        'absolute top-0 left-0 w-1 h-full bg-gradient-to-b from-primary-500 via-primary-400 to-primary-600 transition-opacity duration-200',
                        isOpen ? 'opacity-100' : 'opacity-0 group-hover:opacity-50'
                      )} />
                    )}

                    {/* Dark variant accent line */}
                    {variant === 'dark' && (
                      <div className={clsx(
                        'absolute top-0 left-0 w-1 h-full bg-gradient-to-b from-primary-500 via-primary-400 to-primary-600 transition-opacity duration-200',
                        isOpen ? 'opacity-100' : 'opacity-0 group-hover:opacity-70'
                      )} />
                    )}
                    
                    <button
                      onClick={() => toggleItem(index)}
                      className={clsx(
                        "w-full px-5 py-4 flex items-center justify-between text-left transition-all duration-200",
                        variant === 'gradient' && isOpen && 'bg-gradient-to-r from-primary-50/60 via-primary-50/30 to-transparent',
                        variant === 'dark' && isOpen && 'bg-gradient-to-r from-primary-500/20 via-primary-500/10 to-transparent'
                      )}
                      aria-expanded={isOpen}
                      aria-controls={`faq-visual-answer-${index}`}
                    >
                      <span
                        className={clsx(
                          'text-base font-medium flex-1 pr-4 transition-colors',
                          isOpen 
                            ? variant === 'gradient' 
                              ? 'text-primary-700' 
                              : variant === 'dark'
                              ? 'text-white'
                              : 'text-gray-900' 
                            : variant === 'gradient'
                            ? 'text-gray-800 group-hover:text-primary-700'
                            : variant === 'dark'
                            ? 'text-gray-200 group-hover:text-white'
                            : 'text-gray-800'
                        )}
                      >
                        {question}
                      </span>
                      <div
                        className={clsx(
                          'flex-shrink-0 w-7 h-7 rounded-full flex items-center justify-center transition-all duration-200',
                          isOpen
                            ? variant === 'gradient'
                              ? 'bg-gradient-to-br from-primary-500 to-primary-600 text-white shadow-md'
                              : variant === 'dark'
                              ? 'bg-gradient-to-br from-primary-500 to-primary-600 text-white shadow-md'
                              : 'bg-gray-100 text-gray-700'
                            : variant === 'gradient'
                            ? 'bg-gradient-to-br from-primary-100 to-primary-200 text-primary-600 group-hover:from-primary-200 group-hover:to-primary-300 group-hover:text-primary-700'
                            : variant === 'dark'
                            ? 'bg-gray-700/50 text-gray-300 group-hover:bg-primary-500/30 group-hover:text-primary-300'
                            : 'bg-gray-50 text-gray-600 group-hover:bg-gray-100'
                        )}
                      >
                        {isOpen ? (
                          <svg
                            className="w-4 h-4"
                            fill="none"
                            stroke="currentColor"
                            viewBox="0 0 24 24"
                            aria-hidden="true"
                          >
                            <path
                              strokeLinecap="round"
                              strokeLinejoin="round"
                              strokeWidth={2.5}
                              d="M6 18L18 6M6 6l12 12"
                            />
                          </svg>
                        ) : (
                          <svg
                            className="w-4 h-4"
                            fill="none"
                            stroke="currentColor"
                            viewBox="0 0 24 24"
                            aria-hidden="true"
                          >
                            <path
                              strokeLinecap="round"
                              strokeLinejoin="round"
                              strokeWidth={2.5}
                              d="M12 4v16m8-8H4"
                            />
                          </svg>
                        )}
                      </div>
                    </button>

                    {/* Answer Section */}
                    <div
                      id={`faq-visual-answer-${index}`}
                      className={clsx(
                        'overflow-hidden transition-all duration-300 ease-in-out',
                        isOpen ? 'max-h-[500px] opacity-100' : 'max-h-0 opacity-0'
                      )}
                    >
                      <div className={clsx(
                        "px-5 pb-4 pt-0 relative",
                        variant === 'gradient' && isOpen
                          ? 'border-t border-primary-200/30'
                          : variant === 'dark' && isOpen
                          ? 'border-t border-gray-700/50'
                          : 'border-t border-gray-100'
                      )}>
                        {variant === 'gradient' && isOpen && (
                          <div className="absolute top-0 left-0 right-0 h-px bg-gradient-to-r from-transparent via-primary-400/50 to-transparent" />
                        )}
                        {variant === 'dark' && isOpen && (
                          <div className="absolute top-0 left-0 right-0 h-px bg-gradient-to-r from-transparent via-primary-500/50 to-transparent" />
                        )}
                        <p className={clsx(
                          "text-sm leading-relaxed",
                          variant === 'gradient' && isOpen
                            ? 'text-gray-700'
                            : variant === 'dark'
                            ? 'text-gray-300'
                            : 'text-gray-600'
                        )}>
                          {answer}
                        </p>
                      </div>
                    </div>
                  </div>
                );
              })}
              </div>
            </div>
          </div>
        ) : (
          <>
            {/* Header Section (when image is present) */}
            {(title || subtitle || description) && (
              <div
                className={clsx(
                  'mb-10 sm:mb-12 lg:mb-16 flex flex-col',
                  alignmentClasses[alignment]
                )}
              >
                {subtitle && (
                  <p className={clsx(
                    "text-sm sm:text-base font-semibold uppercase tracking-wider mb-3",
                    backgroundImage ? 'text-white' : variant === 'dark' ? 'text-primary-400' : 'text-primary-600'
                  )}>
                    {subtitle}
                  </p>
                )}

                {title && (
                  <h2 className={clsx(
                    "text-3xl sm:text-4xl md:text-5xl font-bold mb-4 leading-tight",
                    backgroundImage ? 'text-white' : variant === 'dark' ? 'text-white' : 'text-gray-900'
                  )}>
                    {title}
                  </h2>
                )}

                {description && (
                  <p
                    className={clsx(
                      'text-base sm:text-lg max-w-2xl',
                      backgroundImage ? 'text-gray-100' : variant === 'dark' ? 'text-gray-300' : 'text-gray-500',
                      alignment === 'center' && 'mx-auto',
                      alignment === 'right' && 'ml-auto',
                      alignment === 'left' && 'mr-auto'
                    )}
                  >
                    {description}
                  </p>
                )}

                {/* CTA Buttons in Header */}
                {normalizedButtons && normalizedButtons.length > 0 && (
                  <div className={clsx(
                    'mt-6 sm:mt-8 flex flex-wrap gap-3 sm:gap-4',
                    alignment === 'center' && 'justify-center',
                    alignment === 'right' && 'justify-end',
                    alignment === 'left' && 'justify-start'
                  )}>
                    {normalizedButtons.map((btn, index) => renderButton(btn, index))}
                  </div>
                )}
              </div>
            )}

            {/* Main Content: Image and FAQ Side by Side */}
            <div className="flex flex-col lg:flex-row gap-8 lg:gap-10 xl:gap-12 items-start">
              {/* Image Section */}
              {image && (
                <div
                  className={clsx(
                    'w-full lg:w-1/2',
                    isImageLeft ? 'order-1' : 'order-2'
                  )}
                >
                  <div className="relative rounded-xl overflow-hidden bg-gray-900 shadow-lg">
                    <div className="relative w-full overflow-hidden">
                      <img
                        src={image}
                        alt={imageAlt}
                        className="w-full h-full object-cover"
                      />
                    </div>
                  </div>
                </div>
              )}

              {/* FAQ Content Section */}
              <div
                className={clsx(
                  'w-full lg:w-1/2 flex flex-col justify-start',
                  isImageLeft ? 'order-2' : 'order-1',
                  alignmentClasses[alignment]
                )}
              >
                {/* FAQ Items */}
                <div className="space-y-3 w-full">
                  {items.map((item, index) => {
                    const isOpen = openItems.has(index);
                    const { question, answer } = item;

                    if (!question || !answer) {
                      console.warn(`FaqVisual: Item at index ${index} is missing question or answer`);
                      return null;
                    }

                    return (
                      <div
                        key={index}
                        className={clsx(
                          'group relative overflow-hidden rounded-lg transition-all duration-200',
                          isOpen
                            ? variant === 'gradient'
                              ? 'bg-gradient-to-br from-white via-primary-50/40 to-primary-100/30 shadow-md border-2 border-primary-200/50'
                              : variant === 'dark'
                              ? 'bg-gray-800/90 backdrop-blur-sm shadow-lg border-2 border-primary-500/30'
                              : backgroundImage
                              ? 'bg-white/95 backdrop-blur-sm shadow-lg border-2 border-white/50'
                              : 'bg-white shadow-sm border border-gray-200'
                            : variant === 'gradient'
                            ? 'bg-gradient-to-br from-white/90 via-primary-50/20 to-primary-100/10 backdrop-blur-sm border border-primary-200/30 hover:from-white hover:via-primary-50/40 hover:to-primary-100/30 hover:shadow-md hover:border-primary-300/50'
                            : variant === 'dark'
                            ? 'bg-gray-800/60 backdrop-blur-sm border border-gray-700/50 hover:bg-gray-800/80 hover:border-primary-500/30 hover:shadow-lg'
                            : backgroundImage
                            ? 'bg-white/90 backdrop-blur-sm border border-white/40 hover:bg-white/95 hover:border-white/60 hover:shadow-lg'
                            : 'bg-white border border-gray-200 hover:border-gray-300'
                        )}
                      >
                        {/* Gradient accent line for gradient variant */}
                        {variant === 'gradient' && (
                          <div className={clsx(
                            'absolute top-0 left-0 w-1 h-full bg-gradient-to-b from-primary-500 via-primary-400 to-primary-600 transition-opacity duration-200',
                            isOpen ? 'opacity-100' : 'opacity-0 group-hover:opacity-50'
                          )} />
                        )}

                        {/* Dark variant accent line */}
                        {variant === 'dark' && (
                          <div className={clsx(
                            'absolute top-0 left-0 w-1 h-full bg-gradient-to-b from-primary-500 via-primary-400 to-primary-600 transition-opacity duration-200',
                            isOpen ? 'opacity-100' : 'opacity-0 group-hover:opacity-70'
                          )} />
                        )}
                        
                        <button
                          onClick={() => toggleItem(index)}
                          className={clsx(
                            "w-full px-5 py-4 flex items-center justify-between text-left transition-all duration-200",
                            variant === 'gradient' && isOpen && 'bg-gradient-to-r from-primary-50/60 via-primary-50/30 to-transparent',
                            variant === 'dark' && isOpen && 'bg-gradient-to-r from-primary-500/20 via-primary-500/10 to-transparent'
                          )}
                          aria-expanded={isOpen}
                          aria-controls={`faq-visual-answer-${index}`}
                        >
                          <span
                            className={clsx(
                              'text-base font-medium flex-1 pr-4 transition-colors',
                              isOpen 
                                ? variant === 'gradient' 
                                  ? 'text-primary-700' 
                                  : variant === 'dark'
                                  ? 'text-white'
                                  : 'text-gray-900' 
                                : variant === 'gradient'
                                ? 'text-gray-800 group-hover:text-primary-700'
                                : variant === 'dark'
                                ? 'text-gray-200 group-hover:text-white'
                                : 'text-gray-800'
                            )}
                          >
                            {question}
                          </span>
                          <div
                            className={clsx(
                              'flex-shrink-0 w-7 h-7 rounded-full flex items-center justify-center transition-all duration-200',
                              isOpen
                                ? variant === 'gradient'
                                  ? 'bg-gradient-to-br from-primary-500 to-primary-600 text-white shadow-md'
                                  : variant === 'dark'
                                  ? 'bg-gradient-to-br from-primary-500 to-primary-600 text-white shadow-md'
                                  : 'bg-gray-100 text-gray-700'
                                : variant === 'gradient'
                                ? 'bg-gradient-to-br from-primary-100 to-primary-200 text-primary-600 group-hover:from-primary-200 group-hover:to-primary-300 group-hover:text-primary-700'
                                : variant === 'dark'
                                ? 'bg-gray-700/50 text-gray-300 group-hover:bg-primary-500/30 group-hover:text-primary-300'
                                : 'bg-gray-50 text-gray-600 group-hover:bg-gray-100'
                            )}
                          >
                            {isOpen ? (
                              <svg
                                className="w-4 h-4"
                                fill="none"
                                stroke="currentColor"
                                viewBox="0 0 24 24"
                                aria-hidden="true"
                              >
                                <path
                                  strokeLinecap="round"
                                  strokeLinejoin="round"
                                  strokeWidth={2.5}
                                  d="M6 18L18 6M6 6l12 12"
                                />
                              </svg>
                            ) : (
                              <svg
                                className="w-4 h-4"
                                fill="none"
                                stroke="currentColor"
                                viewBox="0 0 24 24"
                                aria-hidden="true"
                              >
                                <path
                                  strokeLinecap="round"
                                  strokeLinejoin="round"
                                  strokeWidth={2.5}
                                  d="M12 4v16m8-8H4"
                                />
                              </svg>
                            )}
                          </div>
                        </button>

                        {/* Answer Section */}
                        <div
                          id={`faq-visual-answer-${index}`}
                          className={clsx(
                            'overflow-hidden transition-all duration-300 ease-in-out',
                            isOpen ? 'max-h-[500px] opacity-100' : 'max-h-0 opacity-0'
                          )}
                        >
                          <div className={clsx(
                            "px-5 pb-4 pt-0 relative",
                            variant === 'gradient' && isOpen
                              ? 'border-t border-primary-200/30'
                              : variant === 'dark' && isOpen
                              ? 'border-t border-gray-700/50'
                              : 'border-t border-gray-100'
                          )}>
                            {variant === 'gradient' && isOpen && (
                              <div className="absolute top-0 left-0 right-0 h-px bg-gradient-to-r from-transparent via-primary-400/50 to-transparent" />
                            )}
                            {variant === 'dark' && isOpen && (
                              <div className="absolute top-0 left-0 right-0 h-px bg-gradient-to-r from-transparent via-primary-500/50 to-transparent" />
                            )}
                            <p className={clsx(
                              "text-sm leading-relaxed",
                              variant === 'gradient' && isOpen
                                ? 'text-gray-700'
                                : variant === 'dark'
                                ? 'text-gray-300'
                                : 'text-gray-600'
                            )}>
                              {answer}
                            </p>
                          </div>
                        </div>
                      </div>
                    );
                  })}
                </div>

                {/* CTA Buttons */}
                {normalizedButtons && normalizedButtons.length > 0 && (
                  <div className={clsx(
                    'mt-8 sm:mt-10 flex flex-wrap gap-3 sm:gap-4',
                    alignment === 'center' && 'justify-center',
                    alignment === 'right' && 'justify-end',
                    alignment === 'left' && 'justify-start'
                  )}>
                    {normalizedButtons.map((btn, index) => renderButton(btn, index))}
                  </div>
                )}
              </div>
            </div>
          </>
        )}
      </div>
    </section>
  );
};

