import { useState } from 'react';
import clsx from 'clsx';
import '../../styles/product.css';

/**
 * ProductReviews Component
 * @param {Object} props - Component props
 * @param {Object} props.data - Product reviews data
 * @param {string} [props.data.title='Customer Reviews'] - Section title
 * @param {number} [props.data.averageRating=0] - Average rating
 * @param {number} [props.data.totalReviews=0] - Total number of reviews
 * @param {Array} [props.data.reviews=[]] - Array of review objects
 * @param {boolean} [props.data.showRatingBreakdown=true] - Show rating breakdown
 * @param {boolean} [props.data.allowNewReview=false] - Allow users to write new reviews
 * @param {string} [props.data.variant='default'] - Style variant: 'default' | 'compact' | 'minimal'
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute for the section element
 */
export const ProductReviews = ({ data, className, id }) => {
  // Safety check for data
  if (!data || typeof data !== 'object') {
    console.error('ProductReviews: data prop is required and must be an object');
    return null;
  }

  const [showAllReviews, setShowAllReviews] = useState(false);
  const [sortBy, setSortBy] = useState('newest');

  const {
    title = 'Customer Reviews',
    averageRating = 0,
    totalReviews = 0,
    reviews = [],
    showRatingBreakdown = true,
    allowNewReview = false,
    variant = 'default'
  } = data;

  // Rating display function
  const renderRating = (rating, size = 'sm') => {
    const stars = [];
    const fullStars = Math.floor(rating);
    const hasHalfStar = rating % 1 !== 0;

    for (let i = 0; i < fullStars; i++) {
      stars.push(
        <svg key={i} className={clsx(
          "text-yellow-400 fill-current",
          size === 'sm' && "w-4 h-4",
          size === 'md' && "w-5 h-5",
          size === 'lg' && "w-6 h-6"
        )} viewBox="0 0 20 20">
          <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
        </svg>
      );
    }

    if (hasHalfStar) {
      stars.push(
        <svg key="half" className={clsx(
          "text-yellow-400 fill-current",
          size === 'sm' && "w-4 h-4",
          size === 'md' && "w-5 h-5",
          size === 'lg' && "w-6 h-6"
        )} viewBox="0 0 20 20">
          <defs>
            <linearGradient id="halfStar">
              <stop offset="50%" stopColor="currentColor" />
              <stop offset="50%" stopColor="transparent" />
            </linearGradient>
          </defs>
          <path fill="url(#halfStar)" d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
        </svg>
      );
    }

    const emptyStars = 5 - Math.ceil(rating);
    for (let i = 0; i < emptyStars; i++) {
      stars.push(
        <svg key={`empty-${i}`} className={clsx(
          "text-gray-300 fill-current",
          size === 'sm' && "w-4 h-4",
          size === 'md' && "w-5 h-5",
          size === 'lg' && "w-6 h-6"
        )} viewBox="0 0 20 20">
          <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
        </svg>
      );
    }

    return (
      <div className="flex items-center space-x-1">
        {stars}
      </div>
    );
  };

  // Format date
  const formatDate = (dateString) => {
    const date = new Date(dateString);
    return date.toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'long',
      day: 'numeric'
    });
  };

  // Sort reviews
  const sortedReviews = [...reviews].sort((a, b) => {
    switch (sortBy) {
      case 'newest':
        return new Date(b.date) - new Date(a.date);
      case 'oldest':
        return new Date(a.date) - new Date(b.date);
      case 'highest':
        return b.rating - a.rating;
      case 'lowest':
        return a.rating - b.rating;
      default:
        return 0;
    }
  });

  const displayedReviews = showAllReviews ? sortedReviews : sortedReviews.slice(0, 3);

  // Default variant - Comprehensive review layout
  if (variant === 'default') {
    return (
      <section
        id={id}
        className={clsx(
          "product-reviews-default",
          className
        )}
      >
        <div className="space-y-8">
          {/* Header */}
          <div className="flex items-center justify-between">
            <h3 className="text-2xl font-bold text-gray-900">{title}</h3>
            {allowNewReview && (
              <button className="px-4 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition-colors text-sm font-medium">
                Write a Review
              </button>
            )}
          </div>

          {/* Rating Overview */}
          <div className="bg-gray-50 rounded-lg p-6">
            <div className="flex items-center space-x-8">
              <div className="text-center">
                <div className="text-4xl font-bold text-gray-900">{averageRating.toFixed(1)}</div>
                <div className="flex items-center justify-center mt-2">
                  {renderRating(averageRating, 'md')}
                </div>
                <div className="text-sm text-gray-600 mt-1">
                  {totalReviews} reviews
                </div>
              </div>

              {showRatingBreakdown && (
                <div className="flex-1 space-y-2">
                  {[5, 4, 3, 2, 1].map((rating) => (
                    <div key={rating} className="flex items-center space-x-3">
                      <span className="text-sm text-gray-600 w-8">{rating}</span>
                      <div className="flex-1 bg-gray-200 rounded-full h-2">
                        <div
                          className="bg-yellow-400 h-2 rounded-full"
                          style={{
                            width: `${totalReviews > 0 ? (reviews.filter(r => r.rating === rating).length / totalReviews) * 100 : 0}%`
                          }}
                        ></div>
                      </div>
                      <span className="text-sm text-gray-600 w-8">
                        {reviews.filter(r => r.rating === rating).length}
                      </span>
                    </div>
                  ))}
                </div>
              )}
            </div>
          </div>

          {/* Sort and Filters */}
          {reviews.length > 1 && (
            <div className="flex items-center justify-between">
              <div className="flex items-center space-x-4">
                <span className="text-sm text-gray-600">Sort by:</span>
                <select
                  value={sortBy}
                  onChange={(e) => setSortBy(e.target.value)}
                  className="border border-gray-300 rounded px-3 py-1 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
                >
                  <option value="newest">Newest</option>
                  <option value="oldest">Oldest</option>
                  <option value="highest">Highest Rating</option>
                  <option value="lowest">Lowest Rating</option>
                </select>
              </div>
            </div>
          )}

          {/* Reviews List */}
          <div className="space-y-6">
            {displayedReviews.map((review, index) => (
              <div key={index} className="bg-white border border-gray-200 rounded-lg p-6">
                <div className="flex items-start justify-between mb-4">
                  <div className="flex items-center space-x-4">
                    <div className="w-10 h-10 bg-gray-300 rounded-full flex items-center justify-center">
                      <span className="text-gray-700 font-medium text-sm">
                        {review.author.charAt(0).toUpperCase()}
                      </span>
                    </div>
                    <div>
                      <div className="font-medium text-gray-900">{review.author}</div>
                      <div className="text-sm text-gray-500">{formatDate(review.date)}</div>
                    </div>
                  </div>
                  <div className="flex items-center space-x-2">
                    {renderRating(review.rating)}
                    <span className="text-sm text-gray-600">({review.rating})</span>
                  </div>
                </div>

                {review.title && (
                  <h4 className="font-semibold text-gray-900 mb-2">{review.title}</h4>
                )}

                <p className="text-gray-700 leading-relaxed mb-4">{review.comment}</p>

                {review.verified && (
                  <div className="flex items-center space-x-2 text-sm text-green-600">
                    <svg className="w-4 h-4" fill="currentColor" viewBox="0 0 20 20">
                      <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                    </svg>
                    <span>Verified Purchase</span>
                  </div>
                )}
              </div>
            ))}
          </div>

          {/* Load More */}
          {reviews.length > 3 && (
            <div className="text-center">
              <button
                onClick={() => setShowAllReviews(!showAllReviews)}
                className="px-6 py-3 border border-gray-300 text-gray-700 rounded-lg hover:bg-gray-50 transition-colors"
              >
                {showAllReviews ? 'Show Less' : `Show All ${reviews.length} Reviews`}
              </button>
            </div>
          )}
        </div>
      </section>
    );
  }

  // Compact variant - Condensed review layout
  if (variant === 'compact') {
    return (
      <section
        id={id}
        className={clsx(
          "product-reviews-compact",
          className
        )}
      >
        <div className="space-y-6">
          <div className="flex items-center justify-between">
            <h3 className="text-xl font-semibold text-gray-900">{title}</h3>
            <div className="flex items-center space-x-2">
              {renderRating(averageRating)}
              <span className="text-sm text-gray-600">({totalReviews})</span>
            </div>
          </div>

          <div className="space-y-4">
            {displayedReviews.map((review, index) => (
              <div key={index} className="border-b border-gray-200 pb-4">
                <div className="flex items-center justify-between mb-2">
                  <div className="flex items-center space-x-2">
                    <span className="font-medium text-gray-900 text-sm">{review.author}</span>
                    {renderRating(review.rating)}
                  </div>
                  <span className="text-xs text-gray-500">{formatDate(review.date)}</span>
                </div>
                <p className="text-gray-700 text-sm leading-relaxed">{review.comment}</p>
              </div>
            ))}
          </div>
        </div>
      </section>
    );
  }

  // Minimal variant - Simple rating display
  if (variant === 'minimal') {
    return (
      <section
        id={id}
        className={clsx(
          "product-reviews-minimal",
          className
        )}
      >
        <div className="flex items-center space-x-4">
          <div className="flex items-center space-x-2">
            {renderRating(averageRating)}
            <span className="text-sm text-gray-600">({totalReviews} reviews)</span>
          </div>
          {allowNewReview && (
            <button className="text-sm text-blue-600 hover:text-blue-800 underline">
              Write a review
            </button>
          )}
        </div>
      </section>
    );
  }

  return null;
};
