import { useState, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/hero.css';

/**
 * HeroGlass Component - Minimalist Glassmorphism Hero
 * @param {Object} props - Component props
 * @param {Object} props.data - Hero section configuration data
 * @param {string} props.data.title - Main heading text
 * @param {string} [props.data.subtitle] - Subtitle text displayed above title
 * @param {string} [props.data.description] - Description text displayed below title
 * @param {Array} [props.data.buttons=[]] - Array of call-to-action buttons
 * @param {string} [props.data.backgroundImage] - URL or path to background image
 * @param {string} [props.data.backgroundVideo] - URL or path to background video
 * @param {string} [props.data.theme='light'] - Theme variant: 'light' | 'dark'
 * @param {boolean} [props.data.showStats=false] - Show statistics section
 * @param {Array} [props.data.stats=[]] - Array of statistics
 * @param {string} [props.data.featureAlignment='left'] - Feature alignment: 'left' | 'center' | 'right'
 * @param {string} [props.data.className] - Additional CSS classes for the section
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute for the section element
 * @param {boolean} [props.loader=false] - Show loading state
 */
export const HeroGlass = ({ data, className, id, loader = false }) => {
  if (!data || typeof data !== 'object') {
    console.error('HeroGlass: data prop is required and must be an object');
    return null;
  }

  const [showLoader, setShowLoader] = useState(loader);

  // Auto-hide loader after 2 seconds when loader prop is true
  useEffect(() => {
    if (loader) {
      const timer = setTimeout(() => {
        setShowLoader(false);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowLoader(false);
    }
  }, [loader]);

  const {
    title,
    subtitle,
    description,
    buttons = [],
    backgroundImage,
    backgroundVideo,
    theme = 'light',
    showStats = false,
    stats = [],
    featureAlignment = 'left',
    className: dataClassName,
  } = data;

  const isDark = theme === 'dark';
  const textColor = isDark ? 'text-white' : 'text-gray-900';
  const textSecondary = isDark ? 'text-gray-300' : 'text-gray-600';
  const glassBg = isDark ? 'bg-white/10 backdrop-blur-xl' : 'bg-white/80 backdrop-blur-xl';
  const borderColor = isDark ? 'border-white/20' : 'border-gray-200';

  const buttonVariantClasses = {
    primary: isDark
      ? 'bg-white text-gray-900 hover:bg-gray-100'
      : 'bg-gray-900 text-white hover:bg-gray-800',
    secondary: isDark
      ? 'bg-white/10 backdrop-blur-sm text-white border border-white/30 hover:bg-white/20'
      : 'bg-gray-100 text-gray-900 border border-gray-300 hover:bg-gray-200',
    outline: isDark
      ? 'bg-transparent text-white border-2 border-white/50 hover:bg-white/10'
      : 'bg-transparent text-gray-900 border-2 border-gray-900 hover:bg-gray-900 hover:text-white',
  };

  const backgroundStyle = backgroundImage
    ? {
        backgroundImage: `url(${backgroundImage})`,
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
      }
    : {};

  // Show skeleton loader if loader prop is true
  if (showLoader) {
    return (
      <section
        id={id}
        className={clsx(
          'relative w-full min-h-screen flex items-center justify-center overflow-hidden',
          isDark ? 'bg-gray-900' : 'bg-gray-50',
          className,
          dataClassName
        )}
      >
        {/* Background skeleton */}
        {backgroundImage && (
          <div className="absolute inset-0 bg-gray-300 animate-pulse"></div>
        )}

        {/* Overlay skeleton */}
        {backgroundImage && (
          <div
            className={clsx(
              'absolute inset-0 z-10',
              isDark ? 'bg-black/40' : 'bg-white/40'
            )}
          />
        )}

        {/* Geometric Pattern Overlay */}
        <div className="absolute inset-0 z-10 opacity-5">
          <div className="absolute inset-0 bg-[linear-gradient(to_right,#80808012_1px,transparent_1px),linear-gradient(to_bottom,#80808012_1px,transparent_1px)] bg-[size:24px_24px]"></div>
        </div>

        {/* Main Content Container Skeleton */}
        <div className="relative z-20 max-w-6xl mx-auto w-full px-4 sm:px-6 lg:px-8 py-20">
          <div className="grid lg:grid-cols-2 gap-12 items-center">
            {/* Left Column - Content Skeleton */}
            <div className="space-y-8">
              {/* Subtitle skeleton */}
              <div className="inline-block">
                <div className={clsx(
                  'h-8 bg-gray-300 rounded-full w-32 animate-pulse',
                  isDark && 'bg-gray-700'
                )}></div>
              </div>

              {/* Title skeleton */}
              <div className="space-y-3">
                <div className={clsx(
                  'h-12 bg-gray-300 rounded w-full animate-pulse',
                  isDark && 'bg-gray-700'
                )}></div>
                <div className={clsx(
                  'h-12 bg-gray-300 rounded w-4/5 animate-pulse',
                  isDark && 'bg-gray-700'
                )}></div>
                <div className={clsx(
                  'h-12 bg-gray-300 rounded w-3/5 animate-pulse',
                  isDark && 'bg-gray-700'
                )}></div>
              </div>

              {/* Description skeleton */}
              <div className="space-y-2 max-w-2xl">
                <div className={clsx(
                  'h-4 bg-gray-300 rounded w-full animate-pulse',
                  isDark && 'bg-gray-700'
                )}></div>
                <div className={clsx(
                  'h-4 bg-gray-300 rounded w-5/6 animate-pulse',
                  isDark && 'bg-gray-700'
                )}></div>
                <div className={clsx(
                  'h-4 bg-gray-300 rounded w-4/6 animate-pulse',
                  isDark && 'bg-gray-700'
                )}></div>
              </div>

              {/* Buttons skeleton */}
              <div className="flex flex-wrap gap-4 pt-4">
                <div className={clsx(
                  'h-12 bg-gray-300 rounded-lg w-32 animate-pulse',
                  isDark && 'bg-gray-700'
                )}></div>
                <div className={clsx(
                  'h-12 bg-gray-300 rounded-lg w-28 animate-pulse',
                  isDark && 'bg-gray-700'
                )}></div>
              </div>

              {/* Stats skeleton */}
              {showStats && (
                <div className="grid grid-cols-3 gap-6 pt-8 border-t border-gray-200/20">
                  {[0, 1, 2].map((index) => (
                    <div key={index} className="text-center">
                      <div className={clsx(
                        'h-10 bg-gray-300 rounded w-16 mx-auto mb-2 animate-pulse',
                        isDark && 'bg-gray-700'
                      )}></div>
                      <div className={clsx(
                        'h-4 bg-gray-300 rounded w-20 mx-auto animate-pulse',
                        isDark && 'bg-gray-700'
                      )}></div>
                    </div>
                  ))}
                </div>
              )}
            </div>

            {/* Right Column - Glass Card Skeleton */}
            <div className="relative">
              <div
                className={clsx(
                  'rounded-3xl p-8 sm:p-12 shadow-2xl border',
                  glassBg,
                  borderColor
                )}
              >
                <div className="space-y-6">
                  {/* Feature list skeleton */}
                  {data.features && data.features.length > 0 ? (
                    <div className="space-y-4">
                      {[0, 1, 2].map((index) => (
                        <div key={index} className="flex items-start gap-4">
                          <div className={clsx(
                            'w-6 h-6 bg-gray-300 rounded flex-shrink-0 animate-pulse',
                            isDark && 'bg-gray-700'
                          )}></div>
                          <div className="flex-1 space-y-2">
                            <div className={clsx(
                              'h-5 bg-gray-300 rounded w-3/4 animate-pulse',
                              isDark && 'bg-gray-700'
                            )}></div>
                            <div className={clsx(
                              'h-4 bg-gray-300 rounded w-full animate-pulse',
                              isDark && 'bg-gray-700'
                            )}></div>
                          </div>
                        </div>
                      ))}
                    </div>
                  ) : (
                    <div className="space-y-4">
                      <div className={clsx(
                        'h-4 bg-gray-300 rounded w-3/4 animate-pulse',
                        isDark && 'bg-gray-700'
                      )}></div>
                      <div className={clsx(
                        'h-4 bg-gray-300 rounded w-full animate-pulse',
                        isDark && 'bg-gray-700'
                      )}></div>
                      <div className={clsx(
                        'h-4 bg-gray-300 rounded w-5/6 animate-pulse',
                        isDark && 'bg-gray-700'
                      )}></div>
                    </div>
                  )}
                </div>
              </div>

              {/* Decorative Elements */}
              <div
                className={clsx(
                  'absolute -top-4 -right-4 w-24 h-24 rounded-full blur-2xl opacity-30',
                  isDark ? 'bg-white' : 'bg-gray-900'
                )}
              ></div>
              <div
                className={clsx(
                  'absolute -bottom-4 -left-4 w-32 h-32 rounded-full blur-2xl opacity-20',
                  isDark ? 'bg-white' : 'bg-gray-900'
                )}
              ></div>
            </div>
          </div>
        </div>

        {/* Bottom Wave */}
        <div className="absolute bottom-0 left-0 right-0 z-10">
          <svg
            className="w-full h-24"
            viewBox="0 0 1440 120"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M0 120L60 105C120 90 240 60 360 45C480 30 600 30 720 37.5C840 45 960 60 1080 67.5C1200 75 1320 75 1380 75L1440 75V120H1380C1320 120 1200 120 1080 120C960 120 840 120 720 120C600 120 480 120 360 120C240 120 120 120 60 120H0Z"
              fill={isDark ? '#111827' : '#F9FAFB'}
            />
          </svg>
        </div>
      </section>
    );
  }

  return (
    <section
      id={id}
      className={clsx(
        'relative w-full min-h-screen flex items-center justify-center overflow-hidden',
        isDark ? 'bg-gray-900' : 'bg-gray-50',
        className,
        dataClassName
      )}
      style={backgroundStyle}
    >
      {/* Background Video */}
      {backgroundVideo && (
        <video
          className="absolute inset-0 w-full h-full object-cover z-0"
          autoPlay
          loop
          muted
          playsInline
        >
          <source src={backgroundVideo} type="video/mp4" />
        </video>
      )}

      {/* Overlay for better text readability */}
      {backgroundImage || backgroundVideo ? (
        <div
          className={clsx(
            'absolute inset-0 z-10',
            isDark ? 'bg-black/40' : 'bg-white/40'
          )}
        />
      ) : null}

      {/* Geometric Pattern Overlay */}
      <div className="absolute inset-0 z-10 opacity-5">
        <div className="absolute inset-0 bg-[linear-gradient(to_right,#80808012_1px,transparent_1px),linear-gradient(to_bottom,#80808012_1px,transparent_1px)] bg-[size:24px_24px]"></div>
      </div>

      {/* Main Content Container */}
      <div className="relative z-20 max-w-6xl mx-auto w-full px-4 sm:px-6 lg:px-8 py-20">
        <div className="grid lg:grid-cols-2 gap-12 items-center">
          {/* Left Column - Content */}
          <div className={clsx('space-y-8', textColor)}>
            {/* Subtitle */}
            {subtitle && (
              <div className="inline-block">
                <span
                  className={clsx(
                    'px-4 py-2 rounded-full text-sm font-medium uppercase tracking-wider',
                    glassBg,
                    borderColor,
                    'border',
                    textSecondary
                  )}
                >
                  {subtitle}
                </span>
              </div>
            )}

            {/* Title */}
            <h1
              className={clsx(
                'text-5xl sm:text-6xl lg:text-7xl font-bold leading-tight',
                textColor
              )}
            >
              {title}
            </h1>

            {/* Description */}
            {description && (
              <p
                className={clsx(
                  'text-xl sm:text-2xl leading-relaxed max-w-2xl',
                  textSecondary
                )}
              >
                {description}
              </p>
            )}

            {/* Buttons */}
            {buttons.length > 0 && (
              <div className="flex flex-wrap gap-4 pt-4">
                {buttons.map((button, index) => {
                  const ButtonComponent = button.href ? 'a' : 'button';
                  return (
                    <ButtonComponent
                      key={index}
                      href={button.href}
                      onClick={button.onClick}
                      className={clsx(
                        'px-8 py-4 rounded-lg font-semibold text-base transition-all duration-300',
                        'transform hover:scale-105 active:scale-95',
                        'focus:outline-none focus:ring-4 focus:ring-offset-2',
                        isDark ? 'focus:ring-white/50' : 'focus:ring-gray-900/50',
                        buttonVariantClasses[button.variant || 'primary'],
                        button.className
                      )}
                    >
                      {button.text}
                    </ButtonComponent>
                  );
                })}
              </div>
            )}

            {/* Stats Section */}
            {showStats && stats.length > 0 && (
              <div className="grid grid-cols-3 gap-6 pt-8 border-t border-gray-200/20">
                {stats.map((stat, index) => (
                  <div key={index} className="text-center">
                    <div
                      className={clsx(
                        'text-3xl sm:text-4xl font-bold mb-1',
                        textColor
                      )}
                    >
                      {stat.value}
                    </div>
                    <div className={clsx('text-sm', textSecondary)}>
                      {stat.label}
                    </div>
                  </div>
                ))}
              </div>
            )}
          </div>

          {/* Right Column - Glass Card */}
          <div className="relative">
            <div
              className={clsx(
                'rounded-3xl p-8 sm:p-12 shadow-2xl border',
                glassBg,
                borderColor,
                'transform hover:scale-[1.02] transition-all duration-500'
              )}
            >
              {/* Card Content */}
              <div className="space-y-6">
                {/* Feature List or Additional Content */}
                {data.features && data.features.length > 0 ? (
                  <div className="space-y-4">
                    {data.features.map((feature, index) => {
                      const alignmentClasses = {
                        left: 'flex items-start gap-4 text-left',
                        center: 'flex flex-col items-center gap-4 text-center',
                        right: 'flex items-end gap-4 text-right flex-row-reverse',
                      };
                      
                      return (
                        <div
                          key={index}
                          className={clsx(alignmentClasses[featureAlignment])}
                        >
                          {feature.icon && (
                            <div className={clsx(
                              'flex-shrink-0',
                              textColor,
                              featureAlignment === 'right' && 'ml-4'
                            )}>
                              {typeof feature.icon === 'string' ? (
                                <span className="text-2xl">{feature.icon}</span>
                              ) : (
                                <div className="w-6 h-6">{feature.icon}</div>
                              )}
                            </div>
                          )}
                          <div className={clsx(
                            featureAlignment === 'right' && 'text-right',
                            featureAlignment === 'center' && 'text-center',
                            featureAlignment === 'left' && 'text-left'
                          )}>
                            <h3
                              className={clsx(
                                'font-semibold text-lg mb-1',
                                textColor
                              )}
                            >
                              {feature.title}
                            </h3>
                            {feature.description && (
                              <p className={clsx('text-sm', textSecondary)}>
                                {feature.description}
                              </p>
                            )}
                          </div>
                        </div>
                      );
                    })}
                  </div>
                ) : (
                  <div className={clsx('space-y-4', textSecondary)}>
                    <div className="h-4 bg-current/20 rounded w-3/4"></div>
                    <div className="h-4 bg-current/20 rounded w-full"></div>
                    <div className="h-4 bg-current/20 rounded w-5/6"></div>
                  </div>
                )}
              </div>
            </div>

            {/* Decorative Elements */}
            <div
              className={clsx(
                'absolute -top-4 -right-4 w-24 h-24 rounded-full blur-2xl opacity-30',
                isDark ? 'bg-white' : 'bg-gray-900'
              )}
            ></div>
            <div
              className={clsx(
                'absolute -bottom-4 -left-4 w-32 h-32 rounded-full blur-2xl opacity-20',
                isDark ? 'bg-white' : 'bg-gray-900'
              )}
            ></div>
          </div>
        </div>
      </div>

      {/* Bottom Wave or Decorative Element */}
      <div className="absolute bottom-0 left-0 right-0 z-10">
        <svg
          className="w-full h-24"
          viewBox="0 0 1440 120"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            d="M0 120L60 105C120 90 240 60 360 45C480 30 600 30 720 37.5C840 45 960 60 1080 67.5C1200 75 1320 75 1380 75L1440 75V120H1380C1320 120 1200 120 1080 120C960 120 840 120 720 120C600 120 480 120 360 120C240 120 120 120 60 120H0Z"
            fill={isDark ? '#111827' : '#F9FAFB'}
          />
        </svg>
      </div>
    </section>
  );
};

