import React, { useState, useEffect } from 'react';
import clsx from 'clsx';

/**
 * CartMinimal Component - Minimal cart with clean design
 * @param {Object} props - Component props
 * @param {Object} props.data - Cart configuration data
 * @param {Array} props.data.items - Cart items array
 * @param {string} [props.variant='default'] - Visual variant: 'default' | 'clean'
 * @param {boolean} [props.loader=false] - Show skeleton loader
 * @param {string} [props.data.title='Cart'] - Cart title
 * @param {boolean} [props.data.showSubtotal=true] - Show subtotal
 * @param {boolean} [props.data.showTax=true] - Show tax
 * @param {boolean} [props.data.showShipping=true] - Show shipping
 * @param {boolean} [props.data.showTotal=true] - Show total
 * @param {boolean} [props.data.showCheckoutButton=true] - Show checkout button
 * @param {boolean} [props.data.showContinueShopping=true] - Show continue shopping link
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute for the section element
 */
export const CartMinimal = ({ data, variant = 'default', loader: initialLoader = false, className, id }) => {
  const [cartItems, setCartItems] = useState(data?.items || []);
  const [isLoading, setIsLoading] = useState(initialLoader);

  // Auto-switch from skeleton to content after 2 seconds
  useEffect(() => {
    if (initialLoader) {
      const timer = setTimeout(() => {
        setIsLoading(false);
      }, 2000);

      return () => clearTimeout(timer);
    }
  }, [initialLoader]);

  const loader = initialLoader ? isLoading : false;

  if (!data || typeof data !== 'object') {
    console.error('CartMinimal: data prop is required and must be an object');
    return null;
  }

  const {
    title = 'Cart',
    showSubtotal = true,
    showTax = true,
    showShipping = true,
    showTotal = true,
    showCheckoutButton = true,
    showContinueShopping = true,
  } = data;

  // Calculate totals
  const subtotal = cartItems.reduce((total, item) => total + (item.price * item.quantity), 0);
  const tax = subtotal * 0.08; // 8% tax
  const shipping = subtotal > 100 ? 0 : 9.99; // Free shipping over $100
  const total = subtotal + tax + shipping;

  const isClean = variant === 'clean';

  // Skeleton components
  const SkeletonHeader = () => (
    <div className={clsx("mb-16", isClean ? "text-left border-b border-gray-900 pb-8" : "text-center")}>
      <div className={clsx("h-12 bg-gray-200 rounded animate-pulse mb-4", isClean ? "w-48" : "w-32 mx-auto")}></div>
    </div>
  );

  const SkeletonCartItem = () => (
    <div className={clsx("flex flex-col sm:flex-row gap-8 pb-8 last:border-b-0 animate-pulse",
      isClean ? "border-b border-dashed border-gray-300" : "border-b border-gray-200"
    )}>
      {/* Product Image Skeleton */}
      <div className="flex-shrink-0">
        <div className="w-28 h-28 bg-gray-200 rounded"></div>
      </div>

      {/* Product Details Skeleton */}
      <div className="flex-1 min-w-0">
        <div className="flex justify-between items-start">
          <div className="flex-1">
            <div className={clsx("h-6 bg-gray-200 rounded mb-1", isClean ? "w-48" : "w-40")}></div>
            <div className="h-4 bg-gray-200 rounded w-24"></div>
          </div>
          <div className="w-5 h-5 bg-gray-200 rounded"></div>
        </div>

        {/* Price and Quantity Skeleton */}
        <div className="flex justify-between items-center mt-6">
          <div className="flex items-center gap-6">
            <div className="h-6 bg-gray-200 rounded w-16"></div>
            <div className="flex items-center gap-2">
              <div className="w-8 h-8 bg-gray-200 rounded"></div>
              <div className="w-8 h-6 bg-gray-200 rounded"></div>
              <div className="w-8 h-8 bg-gray-200 rounded"></div>
            </div>
          </div>
          <div className="h-6 bg-gray-200 rounded w-20"></div>
        </div>
      </div>
    </div>
  );

  const SkeletonOrderSummary = () => (
    <div className="max-w-md ml-auto">
      <div className="space-y-4">
        {showSubtotal && (
          <div className="flex justify-between text-sm">
            <div className="h-4 bg-gray-200 rounded w-16"></div>
            <div className="h-4 bg-gray-200 rounded w-12"></div>
          </div>
        )}

        {showTax && (
          <div className="flex justify-between text-sm">
            <div className="h-4 bg-gray-200 rounded w-12"></div>
            <div className="h-4 bg-gray-200 rounded w-10"></div>
          </div>
        )}

        {showShipping && (
          <div className="flex justify-between text-sm">
            <div className="h-4 bg-gray-200 rounded w-16"></div>
            <div className="h-4 bg-gray-200 rounded w-14"></div>
          </div>
        )}

        <hr className={clsx(isClean ? "border-gray-900 border-t-2" : "border-gray-200")} />

        {showTotal && (
          <div className="flex justify-between text-xl font-black uppercase tracking-widest">
            <div className="h-6 bg-gray-200 rounded w-12"></div>
            <div className="h-6 bg-gray-200 rounded w-16"></div>
          </div>
        )}
      </div>

      {showCheckoutButton && (
        <div className="h-12 bg-gray-200 rounded mt-8 animate-pulse"></div>
      )}

      {showContinueShopping && (
        <div className="h-12 bg-gray-200 rounded mt-4 animate-pulse"></div>
      )}
    </div>
  );

  // Show skeleton loader
  if (loader) {
    return (
      <section
        id={id}
        className={clsx(
          'w-full py-16 sm:py-20 px-4 sm:px-6 lg:px-8',
          isClean ? 'bg-gray-50' : 'bg-white',
          className
        )}
      >
        <div className="max-w-4xl mx-auto">
          <SkeletonHeader />
          <div className={clsx("space-y-12", isClean && "bg-white p-8 border border-gray-200 shadow-sm")}>
            {/* Cart Items Skeleton */}
            <div className="space-y-8">
              <SkeletonCartItem />
              <SkeletonCartItem />
              <SkeletonCartItem />
            </div>
            <SkeletonOrderSummary />
          </div>
        </div>
      </section>
    );
  }

  const updateQuantity = (id, newQuantity) => {
    if (newQuantity <= 0) {
      removeItem(id);
      return;
    }
    setCartItems(items =>
      items.map(item =>
        item.id === id ? { ...item, quantity: newQuantity } : item
      )
    );
  };

  const removeItem = (id) => {
    setCartItems(items => items.filter(item => item.id !== id));
  };

  if (cartItems.length === 0) {
    return (
      <section
        id={id}
        className={clsx(
          'w-full py-16 sm:py-20 px-4 sm:px-6 lg:px-8',
          className
        )}
      >
        <div className="max-w-md mx-auto text-center">
          <div className="w-16 h-16 bg-gray-100 rounded-full flex items-center justify-center mx-auto mb-6">
            <svg
              className="w-8 h-8 text-gray-400"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={1.5}
                d="M16 11V7a4 4 0 00-8 0v4M5 9h14l1 12H4L5 9z"
              />
            </svg>
          </div>
          <h2 className="text-2xl font-light text-gray-900 mb-2">Your cart is empty</h2>
          <p className="text-gray-500 mb-8">Add some items to get started.</p>
          <button className="px-8 py-3 bg-black text-white font-medium rounded-none hover:bg-gray-800 transition-colors">
            Shop Now
          </button>
        </div>
      </section>
    );
  }

  return (
    <section
      id={id}
      className={clsx(
        'w-full py-16 sm:py-20 px-4 sm:px-6 lg:px-8',
        isClean ? 'bg-gray-50' : 'bg-white',
        className
      )}
    >
      <div className="max-w-4xl mx-auto">
        {/* Header */}
        <div className={clsx("mb-16", isClean ? "text-left border-b border-gray-900 pb-8" : "text-center")}>
          <h1 className={clsx("text-4xl text-gray-900 uppercase tracking-[0.2em]", isClean ? "font-black" : "font-light")}>{title}</h1>
        </div>

        <div className={clsx("space-y-12", isClean && "bg-white p-8 border border-gray-200 shadow-sm")}>
          {/* Cart Items */}
          <div className="space-y-8">
            {cartItems.map((item) => (
              <div
                key={item.id}
                className={clsx(
                  "flex flex-col sm:flex-row gap-8 pb-8 last:border-b-0",
                  isClean ? "border-b border-dashed border-gray-300" : "border-b border-gray-200"
                )}
              >
                {/* Product Image */}
                <div className="flex-shrink-0">
                  <img
                    src={item.image || '/api/placeholder/120/120'}
                    alt={item.name}
                    className={clsx("w-28 h-28 object-cover", isClean ? "grayscale hover:grayscale-0 transition-all duration-500" : "")}
                  />
                </div>

                {/* Product Details */}
                <div className="flex-1 min-w-0">
                  <div className="flex justify-between items-start">
                    <div className="flex-1">
                      <h3 className={clsx("text-xl text-gray-900 mb-1 uppercase tracking-wider", isClean ? "font-bold" : "font-light")}>{item.name}</h3>
                      {item.variant && (
                        <p className="text-sm text-gray-500 uppercase tracking-widest">{item.variant}</p>
                      )}
                    </div>
                    <button
                      onClick={() => removeItem(item.id)}
                      className="text-gray-400 hover:text-black transition-colors"
                      aria-label="Remove item"
                    >
                      <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                      </svg>
                    </button>
                  </div>

                  {/* Price and Quantity */}
                  <div className="flex justify-between items-center mt-6">
                    <div className="flex items-center gap-6">
                      <span className="text-lg font-bold text-gray-900 tracking-tighter">${item.price.toFixed(2)}</span>
                      <div className="flex items-center gap-2">
                        <button
                          onClick={() => updateQuantity(item.id, item.quantity - 1)}
                          className="w-8 h-8 flex items-center justify-center border border-gray-300 hover:border-black transition-colors"
                          aria-label="Decrease quantity"
                        >
                          <span className="text-sm font-bold"></span>
                        </button>
                        <span className="w-8 text-center text-sm font-bold">{item.quantity}</span>
                        <button
                          onClick={() => updateQuantity(item.id, item.quantity + 1)}
                          className="w-8 h-8 flex items-center justify-center border border-gray-300 hover:border-black transition-colors"
                          aria-label="Increase quantity"
                        >
                          <span className="text-sm font-bold">+</span>
                        </button>
                      </div>
                    </div>
                    <span className="text-lg font-black text-gray-900">
                      ${(item.price * item.quantity).toFixed(2)}
                    </span>
                  </div>
                </div>
              </div>
            ))}
          </div>

          {/* Order Summary */}
          <div className="max-w-md ml-auto">
            <div className="space-y-4">
              {showSubtotal && (
                <div className="flex justify-between text-sm">
                  <span className="text-gray-600">Subtotal</span>
                  <span>${subtotal.toFixed(2)}</span>
                </div>
              )}

              {showTax && (
                <div className="flex justify-between text-sm">
                  <span className="text-gray-600">Tax</span>
                  <span>${tax.toFixed(2)}</span>
                </div>
              )}

              {showShipping && (
                <div className="flex justify-between text-sm">
                  <span className="text-gray-600">Shipping</span>
                  <span>{shipping === 0 ? 'Free' : `$${shipping.toFixed(2)}`}</span>
                </div>
              )}

              <hr className={clsx(isClean ? "border-gray-900 border-t-2" : "border-gray-200")} />

              {showTotal && (
                <div className="flex justify-between text-xl font-black uppercase tracking-widest">
                  <span>Total</span>
                  <span>${total.toFixed(2)}</span>
                </div>
              )}
            </div>

            {showCheckoutButton && (
              <button className={clsx(
                "w-full mt-8 px-8 py-4 font-bold uppercase tracking-[0.2em] transition-all duration-300",
                isClean ? "bg-black text-white hover:bg-primary-600" : "bg-black text-white hover:bg-gray-800"
              )}>
                Checkout
              </button>
            )}

            {showContinueShopping && (
              <button className="w-full mt-4 px-8 py-4 border border-gray-300 text-gray-700 font-bold uppercase tracking-[0.2em] hover:bg-gray-50 transition-colors">
                Continue Shopping
              </button>
            )}
          </div>
        </div>
      </div>
    </section>
  );
};