import { useState, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/product.css';

/**
 * ProductWishlist Component - Wishlist/favorites button with multiple variants
 * @param {Object} props - Component props
 * @param {Object} props.data - Wishlist configuration data
 * @param {string} [props.data.variant='default'] - Style variant: 'default' | 'modern' | 'minimal' | 'compact' | 'icon' | 'dark' | 'dark-gray'
 * @param {boolean} [props.data.isFavorite=false] - Initial favorite state
 * @param {string} [props.data.productId] - Product ID
 * @param {Function} [props.onToggle] - Callback when wishlist is toggled
 * @param {boolean} [props.loading=false] - Show skeleton loader
 * @param {string} [props.className] - Additional CSS classes
 */
export const ProductWishlist = ({ 
  data = {}, 
  onToggle,
  loading = false,
  className 
}) => {
  const {
    variant = 'default',
    isFavorite: initialFavorite = false,
    productId,
  } = data;

  const [isFavorite, setIsFavorite] = useState(initialFavorite);
  const [showContent, setShowContent] = useState(!loading);

  useEffect(() => {
    if (loading) {
      setShowContent(false);
      const timer = setTimeout(() => {
        setShowContent(true);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowContent(true);
    }
  }, [loading]);

  const handleToggle = () => {
    const newState = !isFavorite;
    setIsFavorite(newState);
    if (onToggle) {
      onToggle(newState, productId);
    }
  };

  const buttonClasses = {
    default: clsx(
      'px-3 sm:px-4 py-2 sm:py-2.5 rounded-lg border border-gray-300',
      'flex items-center gap-2 text-sm sm:text-base',
      'transition-all duration-200',
      isFavorite
        ? 'bg-red-50 border-red-300 text-red-600 hover:bg-red-100'
        : 'bg-white text-gray-700 hover:bg-gray-50'
    ),
    modern: clsx(
      'px-4 sm:px-5 py-2.5 sm:py-3 rounded-xl border-2',
      'flex items-center gap-2 text-sm sm:text-base font-medium shadow-sm',
      'transition-all duration-300',
      isFavorite
        ? 'bg-red-500 border-red-500 text-white hover:bg-red-600 hover:shadow-md'
        : 'bg-white border-gray-200 text-gray-700 hover:shadow-md'
    ),
    minimal: clsx(
      'px-2 sm:px-3 py-1.5 sm:py-2 text-sm',
      'flex items-center gap-1.5 border-b-2 bg-transparent',
      'transition-all duration-200',
      isFavorite
        ? 'border-red-500 text-red-600'
        : 'border-transparent text-gray-600 hover:border-gray-300 hover:text-gray-900'
    ),
    compact: clsx(
      'px-2 sm:px-3 py-1.5 text-xs sm:text-sm rounded-md border border-gray-300',
      'flex items-center gap-1.5',
      'transition-all duration-200',
      isFavorite
        ? 'bg-red-50 border-red-300 text-red-600'
        : 'bg-white text-gray-700 hover:bg-gray-50'
    ),
    icon: clsx(
      'p-2 sm:p-2.5 rounded-lg',
      'transition-all duration-300',
      isFavorite
        ? 'bg-red-500 text-white shadow-lg hover:bg-red-600'
        : 'bg-primary-500 text-white shadow-md hover:bg-primary-600 hover:shadow-lg'
    ),
    dark: clsx(
      'px-3 sm:px-4 py-2 sm:py-2.5 rounded-lg border',
      'flex items-center gap-2 text-sm sm:text-base',
      'transition-all duration-200',
      isFavorite
        ? 'bg-red-900 border-red-700 text-red-200 hover:bg-red-800'
        : 'bg-gray-800 border-gray-600 text-gray-300 hover:bg-gray-700'
    ),
    'dark-gray': clsx(
      'px-3 sm:px-4 py-2 sm:py-2.5 rounded-lg border',
      'flex items-center gap-2 text-sm sm:text-base',
      'transition-all duration-200',
      isFavorite
        ? 'bg-red-800 border-red-700 text-red-100 hover:bg-red-700'
        : 'bg-gray-900 border-gray-700 text-gray-400 hover:bg-gray-800'
    ),
  };

  const iconSizeClasses = {
    default: 'w-4 h-4 sm:w-5 sm:h-5',
    modern: 'w-5 h-5 sm:w-6 sm:h-6',
    minimal: 'w-4 h-4 sm:w-5 sm:h-5',
    compact: 'w-3.5 h-3.5 sm:w-4 sm:h-4',
    icon: 'w-5 h-5 sm:w-6 sm:h-6',
    dark: 'w-4 h-4 sm:w-5 sm:h-5',
    'dark-gray': 'w-4 h-4 sm:w-5 sm:h-5',
  };

  const SkeletonLoader = () => {
    const isDark = variant === 'dark' || variant === 'dark-gray';
    const skeletonBg = isDark ? 'bg-gray-700' : 'bg-gray-200';

    return (
      <div className={clsx(buttonClasses[variant] || buttonClasses.default, className)}>
        <div className={clsx(
          iconSizeClasses[variant] || iconSizeClasses.default,
          'rounded product-skeleton',
          skeletonBg
        )}></div>
        {variant !== 'icon' && (
          <div className={clsx('h-4 w-16 rounded product-skeleton', skeletonBg)}></div>
        )}
      </div>
    );
  };

  if (!showContent) {
    return <SkeletonLoader />;
  }

  const HeartIcon = () => (
    <svg
      className={iconSizeClasses[variant] || iconSizeClasses.default}
      fill={isFavorite ? 'currentColor' : 'none'}
      stroke="currentColor"
      viewBox="0 0 24 24"
    >
      <path
        strokeLinecap="round"
        strokeLinejoin="round"
        strokeWidth={2}
        d="M4.318 6.318a4.5 4.5 0 000 6.364L12 20.364l7.682-7.682a4.5 4.5 0 00-6.364-6.364L12 7.636l-1.318-1.318a4.5 4.5 0 00-6.364 0z"
      />
    </svg>
  );

  return (
    <button
      type="button"
      onClick={handleToggle}
      className={clsx(buttonClasses[variant] || buttonClasses.default, className)}
      aria-label={isFavorite ? 'Remove from wishlist' : 'Add to wishlist'}
    >
      <HeartIcon />
      {variant !== 'icon' && (
        <span>{isFavorite ? 'Saved' : 'Save'}</span>
      )}
    </button>
  );
};

