import { useState, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/product.css';

/**
 * ProductSort Component - Sort dropdown with multiple variants
 * @param {Object} props - Component props
 * @param {Object} props.data - Sort configuration data
 * @param {string} [props.data.variant='default'] - Style variant: 'default' | 'modern' | 'minimal' | 'compact' | 'icon' | 'dark' | 'dark-gray'
 * @param {Array} [props.data.options=[]] - Sort options array
 * @param {string} [props.data.defaultSort] - Default sort value
 * @param {Function} [props.onSortChange] - Callback when sort changes
 * @param {boolean} [props.loading=false] - Show skeleton loader
 * @param {string} [props.className] - Additional CSS classes
 */
export const ProductSort = ({ 
  data = {}, 
  onSortChange,
  loading = false,
  className 
}) => {
  const {
    variant = 'default',
    options = [
      { value: 'default', label: 'Default' },
      { value: 'price-asc', label: 'Price: Low to High' },
      { value: 'price-desc', label: 'Price: High to Low' },
      { value: 'name-asc', label: 'Name: A to Z' },
      { value: 'name-desc', label: 'Name: Z to A' },
      { value: 'rating', label: 'Highest Rated' },
      { value: 'newest', label: 'Newest First' },
    ],
    defaultSort = 'default',
  } = data;

  const [selectedSort, setSelectedSort] = useState(defaultSort);
  const [isOpen, setIsOpen] = useState(false);
  const [showContent, setShowContent] = useState(!loading);

  useEffect(() => {
    if (loading) {
      setShowContent(false);
      const timer = setTimeout(() => {
        setShowContent(true);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowContent(true);
    }
  }, [loading]);

  const handleSortChange = (value) => {
    setSelectedSort(value);
    setIsOpen(false);
    if (onSortChange) {
      onSortChange(value);
    }
  };

  const selectedOption = options.find(opt => opt.value === selectedSort) || options[0];

  // Container classes
  const containerClasses = {
    default: 'relative inline-block w-full sm:w-auto',
    modern: 'relative inline-block w-full sm:w-auto',
    minimal: 'relative inline-block w-full sm:w-auto',
    compact: 'relative inline-block w-full sm:w-auto',
    icon: 'relative inline-block',
    dark: 'relative inline-block w-full sm:w-auto',
    'dark-gray': 'relative inline-block w-full sm:w-auto',
  };

  // Button classes
  const buttonClasses = {
    default: clsx(
      'w-full sm:w-auto px-3 sm:px-4 py-2 sm:py-2.5 text-sm sm:text-base',
      'bg-white border border-gray-300 rounded-lg',
      'flex items-center justify-between gap-2',
      'hover:bg-gray-50 transition-all duration-200',
      ''
    ),
    modern: clsx(
      'w-full sm:w-auto px-4 sm:px-5 py-2.5 sm:py-3 text-sm sm:text-base',
      'bg-white border-2 border-gray-200 rounded-xl shadow-sm',
      'flex items-center justify-between gap-2',
      'hover:shadow-md transition-all duration-300',
      ''
    ),
    minimal: clsx(
      'w-full sm:w-auto px-3 sm:px-4 py-2 text-sm',
      'bg-transparent border-b-2 border-gray-300 rounded-none',
      'flex items-center justify-between gap-2',
      'hover:border-primary-500 transition-all duration-200',
      ''
    ),
    compact: clsx(
      'w-full sm:w-auto px-2 sm:px-3 py-1.5 text-xs sm:text-sm',
      'bg-white border border-gray-300 rounded-md',
      'flex items-center justify-between gap-1.5',
      'hover:bg-gray-50 transition-all duration-200',
      ''
    ),
    icon: clsx(
      'p-2 sm:p-2.5 rounded-lg',
      'bg-primary-500 hover:bg-primary-600 text-white',
      'shadow-md hover:shadow-lg transition-all duration-300',
      ''
    ),
    dark: clsx(
      'w-full sm:w-auto px-3 sm:px-4 py-2 sm:py-2.5 text-sm sm:text-base',
      'bg-gray-800 border border-gray-600 rounded-lg text-white',
      'flex items-center justify-between gap-2',
      'hover:bg-primary-700 transition-all duration-200',
      ''
    ),
    'dark-gray': clsx(
      'w-full sm:w-auto px-3 sm:px-4 py-2 sm:py-2.5 text-sm sm:text-base',
      'bg-gray-900 border border-gray-700 rounded-lg text-white',
      'flex items-center justify-between gap-2',
      'hover:bg-gray-800 transition-all duration-200',
      ''
    ),
  };

  // Dropdown classes
  const dropdownClasses = {
    default: clsx(
      'absolute z-50 mt-1 w-full sm:w-56 bg-white border border-gray-300 rounded-lg shadow-lg',
      'max-h-60 overflow-auto',
      isOpen ? 'opacity-100 visible translate-y-0' : 'opacity-0 invisible -translate-y-2'
    ),
    modern: clsx(
      'absolute z-50 mt-2 w-full sm:w-64 bg-white border-2 border-gray-200 rounded-xl shadow-xl',
      'max-h-64 overflow-auto',
      isOpen ? 'opacity-100 visible translate-y-0' : 'opacity-0 invisible -translate-y-2'
    ),
    minimal: clsx(
      'absolute z-50 mt-1 w-full sm:w-56 bg-white border border-gray-200 rounded-lg shadow-md',
      'max-h-60 overflow-auto',
      isOpen ? 'opacity-100 visible translate-y-0' : 'opacity-0 invisible -translate-y-2'
    ),
    compact: clsx(
      'absolute z-50 mt-1 w-full sm:w-48 bg-white border border-gray-300 rounded-md shadow-lg',
      'max-h-48 overflow-auto',
      isOpen ? 'opacity-100 visible translate-y-0' : 'opacity-0 invisible -translate-y-2'
    ),
    icon: clsx(
      'absolute z-50 mt-2 right-0 w-56 sm:w-64 bg-white border border-gray-300 rounded-lg shadow-xl',
      'max-h-64 overflow-auto',
      isOpen ? 'opacity-100 visible translate-y-0' : 'opacity-0 invisible -translate-y-2'
    ),
    dark: clsx(
      'absolute z-50 mt-1 w-full sm:w-56 bg-gray-800 border border-gray-600 rounded-lg shadow-xl',
      'max-h-60 overflow-auto',
      isOpen ? 'opacity-100 visible translate-y-0' : 'opacity-0 invisible -translate-y-2'
    ),
    'dark-gray': clsx(
      'absolute z-50 mt-1 w-full sm:w-56 bg-gray-900 border border-gray-700 rounded-lg shadow-xl',
      'max-h-60 overflow-auto',
      isOpen ? 'opacity-100 visible translate-y-0' : 'opacity-0 invisible -translate-y-2'
    ),
  };

  // Skeleton Loader
  const SkeletonLoader = () => {
    const isDark = variant === 'dark' || variant === 'dark-gray';
    const skeletonBg = isDark ? 'bg-gray-700' : 'bg-gray-200';

    return (
      <div className={clsx(containerClasses[variant] || containerClasses.default, className)}>
        <div className={clsx(
          buttonClasses[variant] || buttonClasses.default,
          'product-skeleton h-10 sm:h-11'
        )}></div>
      </div>
    );
  };

  if (!showContent) {
    return <SkeletonLoader />;
  }

  const SortIcon = () => (
    <svg className="w-4 h-4 sm:w-5 sm:h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 4h13M3 8h9m-9 4h6m4 0l4-4m0 0l4 4m-4-4v12" />
    </svg>
  );

  const ChevronIcon = () => (
    <svg className={clsx(
      'w-4 h-4 transition-transform duration-200',
      isOpen && 'transform rotate-180'
    )} fill="none" stroke="currentColor" viewBox="0 0 24 24">
      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
    </svg>
  );

  return (
    <div className={clsx(containerClasses[variant] || containerClasses.default, className)}>
      <button
        type="button"
        onClick={() => setIsOpen(!isOpen)}
        className={buttonClasses[variant] || buttonClasses.default}
      >
        {variant === 'icon' ? (
          <SortIcon />
        ) : (
          <>
            <span className="flex items-center gap-2">
              {variant !== 'minimal' && <SortIcon />}
              <span>{selectedOption.label}</span>
            </span>
            <ChevronIcon />
          </>
        )}
      </button>

      {isOpen && (
        <>
          <div 
            className="fixed inset-0 z-40" 
            onClick={() => setIsOpen(false)}
          ></div>
          <div className={dropdownClasses[variant] || dropdownClasses.default}>
            <div className="p-2">
              {options.map((option) => {
                const isDark = variant === 'dark' || variant === 'dark-gray';
                return (
                  <button
                    key={option.value}
                    onClick={() => handleSortChange(option.value)}
                    className={clsx(
                      'w-full text-left px-3 py-2 rounded text-sm transition-colors',
                      selectedSort === option.value
                        ? isDark
                          ? 'bg-gray-700 text-white font-medium'
                          : 'bg-primary-50 text-primary-700 font-medium'
                        : isDark
                          ? 'hover:bg-gray-700 text-white'
                          : 'hover:bg-gray-50'
                    )}
                  >
                    {option.label}
                  </button>
                );
              })}
            </div>
          </div>
        </>
      )}
    </div>
  );
};

