import React, { useState, useEffect } from 'react';
import { clsx } from 'clsx';

export const CheckoutHeader = ({ data = {}, variant = 'standard', className, loading = false }) => {
  const [timedOut, setTimedOut] = useState(false);

  // Auto-show content after 2 seconds if loading is true
  useEffect(() => {
    if (loading) {
      setTimedOut(false);
      const timer = setTimeout(() => {
        setTimedOut(true);
      }, 2000);
      return () => clearTimeout(timer);
    }
  }, [loading]);

  const shouldShowSkeleton = loading && !timedOut;
  const { title, description } = data;
  if (!title && !description) return null;

  const variants = {
    standard: "mb-8",
    centered: "mb-12 text-center",
    boxed: "mb-8 p-8 bg-gray-50 border-l-4 border-black rounded-r-xl",
    minimal: "mb-6 border-b border-gray-100 pb-4"
  };

  // Show skeleton loading state
  if (shouldShowSkeleton) {
    return (
      <header className={clsx("checkout-header", variants[variant] || variants.standard, className)}>
        <div className={clsx(
          "font-bold tracking-tight text-gray-900 mb-4",
          variant === 'premium' ? "text-5xl font-serif italic" : "text-3xl sm:text-4xl"
        )}>
          <div className="checkout-skeleton h-12 w-64 rounded-lg"></div>
        </div>
        {description && (
          <div className={clsx(
            "text-gray-600 leading-relaxed",
            variant === 'premium' ? "text-xl max-w-2xl" : "text-lg max-w-xl"
          )}>
            <div className="checkout-skeleton h-6 w-full rounded mb-2"></div>
            <div className="checkout-skeleton h-6 w-3/4 rounded"></div>
          </div>
        )}
      </header>
    );
  }

  return (
    <header className={clsx("checkout-header", variants[variant] || variants.standard, className)}>
      {title && (
        <h1 className={clsx(
          "font-bold tracking-tight text-gray-900",
          variant === 'premium' ? "text-5xl font-serif italic" : "text-3xl sm:text-4xl"
        )}>
          {title}
        </h1>
      )}
      {description && (
        <p className={clsx(
          "mt-2 text-gray-500",
          variant === 'premium' ? "text-xs uppercase tracking-[0.3em] font-bold opacity-60" : "text-sm"
        )}>
          {description}
        </p>
      )}
    </header>
  );
};
