import { useState, useEffect } from 'react';
import clsx from 'clsx'; // Import clsx for class name management
import '../../styles/testimonial.css';

/**
 * TrustBlock Component
 * @param {Object} props - Component props
 * @param {Object} props.data - Testimonial section configuration data
 * @param {string} [props.data.title] - Main heading text
 * @param {string} [props.data.subtitle] - Subtitle text displayed above title
 * @param {string} [props.data.description] - Description text displayed below title
 * @param {Array} props.data.testimonials - Array of testimonial items
 * @param {string} [props.data.variant='default'] - Style variant: 'default' | 'bordered' | 'minimal' | 'gradient' | 'shadow' | 'colored' | 'outlined' | 'filled' | 'card' | 'modern' | 'classic' | 'minimal-card' | 'quote' | 'avatar-focused' | 'grid'
 * @param {string} [props.data.alignment='center'] - Header text alignment (for title, subtitle, description): 'left' | 'center' | 'right'
 * @param {string} [props.data.cardAlignment] - Card text alignment (for testimonial cards content): 'left' | 'center' | 'right' (independent from header alignment, defaults to 'left' if not specified)
 * @param {number} [props.data.columns=3] - Number of columns for grid layout (1-4)
 * @param {boolean} [props.data.showRating=true] - Show star ratings
 * @param {boolean} [props.data.showAvatar=true] - Show user avatars
 * @param {string} [props.data.className] - Additional CSS classes for the section
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute for the section element
 * @param {boolean} [props.loader=false] - Show loading state
 */
export const TrustBlock = ({ data, className, id, loader = false }) => {
  // Safety check for data
  if (!data || typeof data !== 'object') {
    console.error('TrustBlock: data prop is required and must be an object');
    return null;
  }

  const [showLoader, setShowLoader] = useState(loader);

  // Auto-hide loader after 2 seconds when loader prop is true
  useEffect(() => {
    if (loader) {
      const timer = setTimeout(() => {
        setShowLoader(false);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowLoader(false);
    }
  }, [loader]);

  const {
    title,
    subtitle,
    description,
    testimonials = [],
    variant = 'default',
    alignment = 'center',
    cardAlignment,
    columns = 3,
    showRating = true,
    showAvatar = true,
    className: dataClassName,
  } = data;

  // Card alignment is independent from header alignment
  // Default to 'left' if not specified (separate from header alignment)
  const effectiveCardAlignment = cardAlignment || 'left';

  // Show skeleton loader if loader prop is true
  if (showLoader) {
    const alignmentClasses = {
      left: 'text-left items-start',
      center: 'text-center items-center',
      right: 'text-right items-end',
    };

    const gridColumnsClasses = {
      1: 'grid-cols-1',
      2: 'grid-cols-1 md:grid-cols-2',
      3: 'grid-cols-1 md:grid-cols-2 lg:grid-cols-3',
      4: 'grid-cols-1 md:grid-cols-2 lg:grid-cols-4',
    };

    const effectiveAlignment = alignment || 'center';

    return (
      <section
        id={id}
        className={clsx(
          'w-full py-12 sm:py-16 lg:py-20 px-4 sm:px-6 lg:px-8',
          'bg-white',
          className,
          dataClassName
        )}
      >
        <div className="max-w-7xl mx-auto w-full">
          {/* Header skeleton */}
          <div className={clsx('mb-10 sm:mb-12 flex flex-col', alignmentClasses[effectiveAlignment])}>
            <div className="h-4 bg-gray-400 rounded w-24 mb-3 mx-auto animate-pulse"></div>
            <div className="h-10 bg-gray-400 rounded w-full max-w-2xl mb-4 mx-auto animate-pulse"></div>
            <div className="h-5 bg-gray-300 rounded w-full max-w-xl mb-6 mx-auto animate-pulse"></div>
          </div>

          {/* Testimonial cards skeleton */}
          <div className={clsx('grid gap-6', gridColumnsClasses[columns])}>
            {[0, 1, 2, 3, 4, 5].slice(0, columns * 2).map((index) => (
              <div key={index} className="bg-white border border-gray-200 rounded-lg p-6">
                {/* Rating skeleton */}
                {showRating && (
                  <div className="flex gap-1 mb-4 justify-center">
                    {[0, 1, 2, 3, 4].map((star) => (
                      <div key={star} className="w-4 h-4 bg-gray-300 rounded animate-pulse"></div>
                    ))}
                  </div>
                )}
                {/* Content skeleton */}
                <div className="space-y-2 mb-6">
                  <div className="h-4 bg-gray-400 rounded w-full animate-pulse"></div>
                  <div className="h-4 bg-gray-400 rounded w-5/6 animate-pulse"></div>
                  <div className="h-4 bg-gray-300 rounded w-4/6 animate-pulse"></div>
                </div>
                {/* Author skeleton */}
                {showAvatar && (
                  <div className="flex items-center gap-3">
                    <div className="w-12 h-12 bg-gray-300 rounded-full animate-pulse"></div>
                    <div className="flex-1">
                      <div className="h-5 bg-gray-400 rounded w-32 mb-2 animate-pulse"></div>
                      <div className="h-4 bg-gray-300 rounded w-24 animate-pulse"></div>
                    </div>
                  </div>
                )}
              </div>
            ))}
          </div>
        </div>
      </section>
    );
  }

  const alignmentClasses = {
    left: 'text-left items-start',
    center: 'text-center items-center',
    right: 'text-right items-end',
  };

  const cardAlignmentClasses = {
    left: 'text-left',
    center: 'text-center',
    right: 'text-right',
  };

  const variantClasses = {
    default: 'bg-white',
    bordered: 'bg-white',
    minimal: 'bg-transparent',
    gradient: 'bg-white',
    shadow: 'bg-white',
    colored: 'bg-gray-50',
    outlined: 'bg-white',
    filled: 'bg-white',
    card: 'bg-white',
    modern: 'bg-white',
    classic: 'bg-white',
    'minimal-card': 'bg-transparent',
    quote: 'bg-white',
    'avatar-focused': 'bg-white',
    grid: 'bg-white',
  };

  const gridColumnsClasses = {
    1: 'grid-cols-1',
    2: 'grid-cols-1 sm:grid-cols-2',
    3: 'grid-cols-1 sm:grid-cols-2 lg:grid-cols-3',
    4: 'grid-cols-1 sm:grid-cols-2 lg:grid-cols-4',
  };

  const getTestimonialClasses = (testimonial, index) => {
    const baseClasses = 'transition-all duration-300 flex flex-col h-full';

    // Default variant - clean white cards
    if (variant === 'default') {
      return clsx(
        baseClasses,
        'bg-white rounded-lg p-6 sm:p-8 border border-gray-200',
        'hover:shadow-lg hover:border-gray-300'
      );
    }

    // Bordered variant - thick borders
    if (variant === 'bordered') {
      return clsx(
        baseClasses,
        'bg-white rounded-lg p-6 sm:p-8 border-2 border-gray-300',
        'hover:border-gray-400'
      );
    }

    // Minimal variant - transparent with subtle borders
    if (variant === 'minimal') {
      return clsx(
        baseClasses,
        'bg-transparent rounded-lg p-6 sm:p-8 border border-gray-100',
        'hover:bg-gray-50'
      );
    }

    // Gradient variant - light blue gradient backgrounds
    if (variant === 'gradient') {
      return clsx(
        baseClasses,
        'bg-gradient-to-br from-primary-50 via-primary-100 to-primary-200 rounded-lg p-6 sm:p-8 border border-gray-200 shadow-sm',
        'hover:shadow-lg'
      );
    }

    // Shadow variant - prominent shadows
    if (variant === 'shadow') {
      return clsx(
        baseClasses,
        'bg-white rounded-lg p-6 sm:p-8 border border-gray-200 shadow-md',
        'hover:shadow-lg'
      );
    }

    // Colored variant - colored backgrounds
    if (variant === 'colored') {
      return clsx(
        baseClasses,
        'bg-primary-50 rounded-lg p-6 sm:p-8 border border-primary-100',
        'hover:bg-primary-100'
      );
    }

    // Outlined variant - thick outlined borders
    if (variant === 'outlined') {
      return clsx(
        baseClasses,
        'bg-white rounded-lg p-6 sm:p-8 border-2 border-gray-400',
        'hover:border-gray-500'
      );
    }

    // Filled variant - filled backgrounds
    if (variant === 'filled') {
      return clsx(
        baseClasses,
        'bg-gray-100 rounded-lg p-6 sm:p-8 border border-gray-300',
        'hover:bg-gray-200'
      );
    }

    // Card variant - modern card with shadow
    if (variant === 'card') {
      return clsx(
        baseClasses,
        'bg-white rounded-xl p-6 sm:p-8 border border-gray-200 shadow-lg',
        'hover:shadow-xl hover:scale-105'
      );
    }

    // Modern variant - rounded corners with shadows
    if (variant === 'modern') {
      return clsx(
        baseClasses,
        'bg-white rounded-2xl p-6 sm:p-8 border border-gray-100 shadow-lg',
        'hover:shadow-xl'
      );
    }

    // Classic variant - traditional style
    if (variant === 'classic') {
      return clsx(
        baseClasses,
        'bg-white rounded p-6 sm:p-8 border-2 border-gray-300',
        'hover:border-gray-400'
      );
    }

    // Minimal-card variant - minimal with subtle cards
    if (variant === 'minimal-card') {
      return clsx(
        baseClasses,
        'bg-white rounded-lg p-6 sm:p-8 border border-gray-200 shadow-sm',
        'hover:shadow-md'
      );
    }

    // Quote variant - quote-focused design
    if (variant === 'quote') {
      return clsx(
        baseClasses,
        'bg-white rounded-lg p-6 sm:p-8 border-l-4 border-primary-300 shadow-sm',
        'hover:shadow-md'
      );
    }

    // Avatar-focused variant - emphasizes avatar
    if (variant === 'avatar-focused') {
      return clsx(
        baseClasses,
        'bg-white rounded-lg p-6 sm:p-8 border border-gray-200 shadow-sm text-center',
        'hover:shadow-lg'
      );
    }

    // Grid variant - clean grid layout
    if (variant === 'grid') {
      return clsx(
        baseClasses,
        'bg-white rounded-lg p-6 sm:p-8 border border-gray-200',
        'hover:border-gray-300'
      );
    }

    return clsx(baseClasses, 'bg-white rounded-lg p-6 sm:p-8');
  };

  const renderRating = (rating, cardAlignment) => {
    if (!showRating || !rating) return null;

    const fullStars = Math.floor(rating);
    const hasHalfStar = rating % 1 !== 0;

    const ratingAlignmentClasses = {
      left: 'justify-start',
      center: 'justify-center',
      right: 'justify-end',
    };

    return (
      <div className={clsx(
        'flex items-center gap-1 mb-4',
        ratingAlignmentClasses[cardAlignment] || 'justify-start'
      )}>
        {[...Array(5)].map((_, i) => {
          if (i < fullStars) {
            return (
              <svg
                key={i}
                className="w-5 h-5 text-yellow-400 fill-current"
                viewBox="0 0 20 20"
              >
                <path d="M10 15l-5.878 3.09 1.123-6.545L.489 6.91l6.572-.955L10 0l2.939 5.955 6.572.955-4.756 4.635 1.123 6.545z" />
              </svg>
            );
          } else if (i === fullStars && hasHalfStar) {
            return (
              <svg
                key={i}
                className="w-5 h-5 text-yellow-400 fill-current"
                viewBox="0 0 20 20"
              >
                <defs>
                  <linearGradient id={`half-${i}`}>
                    <stop offset="50%" stopColor="currentColor" />
                    <stop offset="50%" stopColor="transparent" stopOpacity="1" />
                  </linearGradient>
                </defs>
                <path
                  fill={`url(#half-${i})`}
                  d="M10 15l-5.878 3.09 1.123-6.545L.489 6.91l6.572-.955L10 0l2.939 5.955 6.572.955-4.756 4.635 1.123 6.545z"
                />
              </svg>
            );
          } else {
            return (
              <svg
                key={i}
                className="w-5 h-5 text-gray-300 fill-current"
                viewBox="0 0 20 20"
              >
                <path d="M10 15l-5.878 3.09 1.123-6.545L.489 6.91l6.572-.955L10 0l2.939 5.955 6.572.955-4.756 4.635 1.123 6.545z" />
              </svg>
            );
          }
        })}
      </div>
    );
  };

  const renderAvatar = (testimonial) => {
    if (!showAvatar) return null;

    if (testimonial.avatar) {
      return (
        <img
          src={testimonial.avatar}
          alt={testimonial.name || 'User'}
          className={clsx(
            'rounded-full object-cover',
            variant === 'avatar-focused' 
              ? 'w-20 h-20 sm:w-24 sm:h-24 mx-auto mb-4 border-4 border-white shadow-lg' 
              : 'w-12 h-12 sm:w-14 sm:h-14 mb-4',
            effectiveCardAlignment === 'center' && variant !== 'avatar-focused' && 'mx-auto',
            effectiveCardAlignment === 'right' && variant !== 'avatar-focused' && 'ml-auto'
          )}
        />
      );
    }

    // Fallback avatar with initials
    const initials = testimonial.name
      ? testimonial.name
          .split(' ')
          .map((n) => n[0])
          .join('')
          .toUpperCase()
          .slice(0, 2)
      : 'U';

    return (
      <div
        className={clsx(
          'rounded-full bg-primary-500 text-white flex items-center justify-center font-semibold',
          variant === 'avatar-focused' 
            ? 'w-20 h-20 sm:w-24 sm:h-24 mx-auto mb-4 border-4 border-white shadow-lg text-xl sm:text-2xl' 
            : 'w-12 h-12 sm:w-14 sm:h-14 mb-4 text-sm',
          effectiveCardAlignment === 'center' && variant !== 'avatar-focused' && 'mx-auto',
          effectiveCardAlignment === 'right' && variant !== 'avatar-focused' && 'ml-auto'
        )}
      >
        {initials}
      </div>
    );
  };

  if (testimonials.length === 0) {
    console.warn('TrustBlock: testimonials array is empty');
    return null;
  }

  return (
    <section
      id={id}
      className={clsx(
        'w-full py-12 sm:py-16 lg:py-20 px-4 sm:px-6 lg:px-8',
        variantClasses[variant],
        className,
        dataClassName
      )}
    >
      <div className="max-w-7xl mx-auto w-full">
        {/* Header */}
        {(title || subtitle || description) && (
          <div
            className={clsx(
              'mb-10 sm:mb-12 flex flex-col',
              alignmentClasses[alignment]
            )}
          >
            {subtitle && (
              <p className="text-sm sm:text-base font-semibold text-primary-600 uppercase tracking-wider mb-3">
                {subtitle}
              </p>
            )}

            {title && (
              <h2 className="text-3xl sm:text-4xl md:text-5xl font-bold text-gray-900 mb-4 leading-tight">
                {title}
              </h2>
            )}

            {description && (
              <p
                className={clsx(
                  'text-base sm:text-lg text-gray-500 max-w-2xl',
                  alignment === 'center' && 'mx-auto',
                  alignment === 'right' && 'ml-auto',
                  alignment === 'left' && 'mr-auto'
                )}
              >
                {description}
              </p>
            )}
          </div>
        )}

        {/* Testimonials Grid */}
        <div className={clsx('grid gap-6 sm:gap-8', gridColumnsClasses[columns] || gridColumnsClasses[3])}>
          {testimonials.map((testimonial, index) => {
            return (
              <div
                key={index}
                className={clsx(
                  getTestimonialClasses(testimonial, index),
                  cardAlignmentClasses[effectiveCardAlignment]
                )}
              >
                {/* Quote Icon for quote variant */}
                {variant === 'quote' && (
                  <div className="mb-4">
                    <svg
                      className="w-8 h-8 text-primary-300"
                      fill="currentColor"
                      viewBox="0 0 24 24"
                    >
                      <path d="M14.017 21v-7.391c0-5.704 3.731-9.57 8.983-10.609l.996 2.151c-2.432.917-3.995 3.638-3.995 5.849h4v10h-9.984zm-14.017 0v-7.391c0-5.704 3.748-9.57 9-10.609l.996 2.151c-2.433.917-3.996 3.638-3.996 5.849h3.983v10h-9.983z" />
                    </svg>
                  </div>
                )}

                {/* Avatar */}
                {variant === 'avatar-focused' && renderAvatar(testimonial)}

                {/* Rating */}
                {renderRating(testimonial.rating, effectiveCardAlignment)}

                {/* Testimonial Text */}
                {testimonial.text && (
                  <p className={clsx(
                    'text-base sm:text-lg text-gray-700 mb-6 flex-1 testimonial-text',
                    variant === 'quote' && 'italic',
                    variant === 'avatar-focused' && 'text-center'
                  )}>
                    {testimonial.text}
                  </p>
                )}

                {/* Quote Icon at bottom for quote variant */}
                {variant === 'quote' && (
                  <div className="mt-auto flex justify-end">
                    <svg
                      className="w-6 h-6 text-primary-300 transform rotate-180"
                      fill="currentColor"
                      viewBox="0 0 24 24"
                    >
                      <path d="M14.017 21v-7.391c0-5.704 3.731-9.57 8.983-10.609l.996 2.151c-2.432.917-3.995 3.638-3.995 5.849h4v10h-9.984zm-14.017 0v-7.391c0-5.704 3.748-9.57 9-10.609l.996 2.151c-2.433.917-3.996 3.638-3.996 5.849h3.983v10h-9.983z" />
                    </svg>
                  </div>
                )}

                {/* Author Info */}
                <div className={clsx(
                  'flex items-center',
                  variant === 'avatar-focused' ? 'flex-col' : 'gap-4',
                  effectiveCardAlignment === 'center' && variant !== 'avatar-focused' && 'justify-center',
                  effectiveCardAlignment === 'right' && variant !== 'avatar-focused' && 'justify-end'
                )}>
                  {/* Avatar (if not avatar-focused variant) */}
                  {variant !== 'avatar-focused' && renderAvatar(testimonial)}

                  <div className={clsx(
                    variant === 'avatar-focused' && 'text-center mt-2'
                  )}>
                    {testimonial.name && (
                      <h4 className="text-lg font-bold text-gray-900">
                        {testimonial.name}
                      </h4>
                    )}
                    {testimonial.role && (
                      <p className="text-sm text-gray-600 mt-1">
                        {testimonial.role}
                      </p>
                    )}
                    {testimonial.company && (
                      <p className="text-sm text-gray-500 mt-1">
                        {testimonial.company}
                      </p>
                    )}
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </section>
  );
};

