import { useState, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/hero.css';

/**
 * HeroPrime Component
 * @param {Object} props - Component props
 * @param {Object} props.data - Hero section configuration data
 * @param {string} props.data.title - Main heading text
 * @param {string} [props.data.subtitle] - Subtitle text displayed above title
 * @param {string} [props.data.description] - Description text displayed below title
 * @param {string} [props.data.backgroundImage] - URL or path to background image
 * @param {string} [props.data.backgroundVideo] - URL or path to background video
 * @param {boolean} [props.data.overlay=true] - Show dark overlay on background
 * @param {number} [props.data.overlayOpacity=0.5] - Opacity of overlay (0-1)
 * @param {Array} [props.data.buttons=[]] - Array of call-to-action buttons
 * @param {string} [props.data.variant='single'] - Layout variant: 'single' | 'split' | 'split-reverse' | 'image-left' | 'image-right' | 'centered'
 * @param {string} [props.data.alignment='center'] - Text alignment: 'left' | 'center' | 'right'
 * @param {string} [props.data.height='large'] - Section height: 'small' | 'medium' | 'large' | 'full'
 * @param {string} [props.data.className] - Additional CSS classes for the section
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute for the section element
 * @param {boolean} [props.loader=false] - Show loading state
 */
export const HeroPrime = ({ data, className, id, loader = false }) => {
  // Safety check for data
  if (!data || typeof data !== 'object') {
    console.error('HeroPrime: data prop is required and must be an object');
    return null;
  }

  const [showLoader, setShowLoader] = useState(loader);

  // Auto-hide loader after 2 seconds when loader prop is true
  useEffect(() => {
    if (loader) {
      const timer = setTimeout(() => {
        setShowLoader(false);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowLoader(false);
    }
  }, [loader]);

  const {
    title,
    subtitle,
    description,
    backgroundImage,
    backgroundVideo,
    overlay = true,
    overlayOpacity = 0.5,
    buttons = [],
    variant = 'single',
    alignment = 'center',
    height = 'large',
    className: dataClassName,
  } = data;

  const heightClasses = {
    small: 'min-h-[400px]',
    medium: 'min-h-[500px]',
    large: 'min-h-[600px]',
    full: 'min-h-screen',
  };

  const alignmentClasses = {
    left: 'text-left items-start',
    center: 'text-center items-center',
    right: 'text-right items-end',
  };

  const buttonVariantClasses = {
    primary: 'bg-primary-600 hover:bg-primary-700 text-white',
    secondary: 'bg-gray-600 hover:bg-gray-700 text-white',
    outline: 'border-2 border-white text-white hover:bg-white hover:text-gray-900',
  };

  // Show skeleton loader if loader prop is true (only for single variant)
  if (showLoader) {
    return (
      <section
        id={id}
        className={clsx(
          'relative w-full flex flex-col justify-center px-4 sm:px-6 lg:px-8',
          heightClasses[height],
          'bg-gray-500',
          className,
          dataClassName
        )}
      >
        <div
          className={clsx(
            'max-w-7xl mx-auto w-full flex flex-col items-center text-center'
          )}
        >
          {/* Subtitle skeleton */}
          <div className="h-5 bg-gray-400 rounded-full w-40 mb-4 animate-pulse"></div>

          {/* Title skeleton */}
          <div className="h-16 bg-gray-400 rounded-lg w-full max-w-2xl mb-6 animate-pulse"></div>

          {/* Description skeleton */}
          <div className="space-y-2 mb-8 max-w-xl">
            <div className="h-5 bg-gray-400 rounded w-4/5 mb-4 animate-pulse"></div>
          </div>

          {/* Buttons skeleton */}
          <div className="flex gap-4 justify-center">
            <div className="h-12 bg-gray-400 rounded-lg w-32 animate-pulse"></div>
            <div className="h-12 bg-gray-400 rounded-lg w-28 animate-pulse"></div>
          </div>
        </div>
      </section>
    );
  }

  // Single variant - full width with background
  if (variant === 'single') {
    const backgroundStyle = backgroundImage
      ? {
        backgroundImage: `url(${backgroundImage})`,
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
      }
      : {};

    return (
      <section
        id={id}
        className={clsx(
          'relative w-full flex flex-col justify-center px-4 sm:px-6 lg:px-8',
          heightClasses[height],
          className,
          dataClassName
        )}
        style={backgroundStyle}
      >
        {/* Background Video */}
        {backgroundVideo && (
          <video
            className="absolute inset-0 w-full h-full object-cover z-0"
            autoPlay
            loop
            muted
            playsInline
          >
            <source src={backgroundVideo} type="video/mp4" />
          </video>
        )}

        {/* Overlay */}
        {overlay && (
          <div
            className="absolute inset-0 bg-black z-10"
            style={{ opacity: overlayOpacity }}
          />
        )}

        {/* Content */}
        <div
          className={clsx(
            'relative z-20 max-w-7xl mx-auto w-full flex flex-col',
            alignmentClasses[alignment]
          )}
        >
          {subtitle && (
            <p className="text-sm sm:text-base font-semibold text-primary-400 uppercase tracking-wider mb-4">
              {subtitle}
            </p>
          )}

          <h1 className="text-4xl sm:text-5xl md:text-6xl lg:text-7xl font-bold text-white mb-6 leading-tight">
            {title}
          </h1>

          {description && (
            <p className={clsx(
              "text-lg sm:text-xl md:text-2xl text-gray-200 mb-8 max-w-3xl",
              alignment === 'center' && "mx-auto",
              alignment === 'right' && "ml-auto"
            )}>
              {description}
            </p>
          )}

          {buttons.length > 0 && (
            <div
              className={clsx(
                'flex flex-wrap gap-4',
                alignment === 'center' && 'justify-center',
                alignment === 'right' && 'justify-end',
                alignment === 'left' && 'justify-start'
              )}
            >
              {buttons.map((button, index) => {
                const ButtonComponent = button.href ? 'a' : 'button';

                return (
                  <ButtonComponent
                    key={index}
                    href={button.href}
                    onClick={button.onClick}
                    className={clsx(
                      'px-6 py-3 sm:px-8 sm:py-4 rounded-lg font-semibold text-base sm:text-lg transition-all duration-300 transform hover:scale-105 focus:outline-none focus:ring-2 focus:ring-white focus:ring-offset-2 focus:ring-offset-transparent',
                      buttonVariantClasses[button.variant || 'primary'],
                      button.className
                    )}
                  >
                    {button.text}
                  </ButtonComponent>
                );
              })}
            </div>
          )}
        </div>
      </section>
    );
  }

  // Split variants - image and content side by side
  if (variant === 'split' || variant === 'split-reverse' || variant === 'image-left' || variant === 'image-right') {
    const isImageLeft = variant === 'split' || variant === 'image-left';
    const isImageRight = variant === 'split-reverse' || variant === 'image-right';

    return (
      <section
        id={id}
        className={clsx(
          'relative w-full flex flex-col lg:flex-row',
          heightClasses[height],
          className,
          dataClassName
        )}
      >
        {/* Image Section */}
        <div
          className={clsx(
            'relative w-full lg:w-1/2',
            isImageLeft ? 'order-1' : 'order-2'
          )}
        >
          {backgroundImage ? (
            <div
              className="absolute inset-0 bg-cover bg-center bg-no-repeat"
              style={{
                backgroundImage: `url(${backgroundImage})`,
              }}
            />
          ) : (
            <div className="absolute inset-0 bg-gradient-to-br from-primary-500 to-purple-600" />
          )}
          {overlay && (
            <div
              className="absolute inset-0 bg-black z-10"
              style={{ opacity: overlayOpacity }}
            />
          )}
        </div>

        {/* Content Section */}
        <div
          className={clsx(
            'relative w-full lg:w-1/2 flex flex-col justify-center px-4 sm:px-6 lg:px-8 xl:px-12',
            'bg-white',
            isImageLeft ? 'order-2' : 'order-1'
          )}
        >
          <div
            className={clsx(
              'max-w-2xl mx-auto w-full flex flex-col',
              alignmentClasses[alignment]
            )}
          >
            {subtitle && (
              <p className="text-sm sm:text-base font-semibold text-primary-600 uppercase tracking-wider mb-4">
                {subtitle}
              </p>
            )}

            <h1 className="text-4xl sm:text-5xl md:text-6xl lg:text-7xl font-bold text-gray-900 mb-6 leading-tight">
              {title}
            </h1>

            {description && (
              <p className={clsx(
                "text-lg sm:text-xl md:text-2xl text-gray-600 mb-8 max-w-3xl",
                alignment === 'center' && "mx-auto",
                alignment === 'right' && "ml-auto"
              )}>
                {description}
              </p>
            )}

            {buttons.length > 0 && (
              <div
                className={clsx(
                  'flex flex-wrap gap-4',
                  alignment === 'center' && 'justify-center',
                  alignment === 'right' && 'justify-end',
                  alignment === 'left' && 'justify-start'
                )}
              >
                {buttons.map((button, index) => {
                  const ButtonComponent = button.href ? 'a' : 'button';

                  return (
                    <ButtonComponent
                      key={index}
                      href={button.href}
                      onClick={button.onClick}
                      className={clsx(
                        'px-6 py-3 sm:px-8 sm:py-4 rounded-lg font-semibold text-base sm:text-lg transition-all duration-300 transform hover:scale-105',
                        buttonVariantClasses[button.variant || 'primary'],
                        button.className
                      )}
                    >
                      {button.text}
                    </ButtonComponent>
                  );
                })}
              </div>
            )}
          </div>
        </div>
      </section>
    );
  }

  // Centered variant - content centered with optional background
  if (variant === 'centered') {
    const backgroundStyle = backgroundImage
      ? {
        backgroundImage: `url(${backgroundImage})`,
        backgroundSize: 'cover',
        backgroundPosition: 'center',
        backgroundRepeat: 'no-repeat',
      }
      : {};

    return (
      <section
        id={id}
        className={clsx(
          'relative w-full flex flex-col justify-center px-4 sm:px-6 lg:px-8',
          heightClasses[height],
          className,
          dataClassName
        )}
        style={backgroundStyle}
      >
        {/* Background Video */}
        {backgroundVideo && (
          <video
            className="absolute inset-0 w-full h-full object-cover z-0"
            autoPlay
            loop
            muted
            playsInline
          >
            <source src={backgroundVideo} type="video/mp4" />
          </video>
        )}

        {/* Overlay */}
        {overlay && (
          <div
            className="absolute inset-0 bg-black z-10"
            style={{ opacity: overlayOpacity }}
          />
        )}

        {/* Content */}
        <div
          className={clsx(
            'relative z-20 max-w-4xl mx-auto w-full flex flex-col',
            alignmentClasses[alignment]
          )}
        >
          {subtitle && (
            <p className="text-sm sm:text-base font-semibold text-primary-400 uppercase tracking-wider mb-4">
              {subtitle}
            </p>
          )}

          <h1 className="text-4xl sm:text-5xl md:text-6xl lg:text-7xl font-bold text-white mb-6 leading-tight">
            {title}
          </h1>

          {description && (
            <p className={clsx(
              "text-lg sm:text-xl md:text-2xl text-gray-200 mb-8 max-w-2xl",
              alignment === 'center' && "mx-auto",
              alignment === 'right' && "ml-auto"
            )}>
              {description}
            </p>
          )}

          {buttons.length > 0 && (
            <div
              className={clsx(
                'flex flex-wrap gap-4',
                alignment === 'center' && 'justify-center',
                alignment === 'right' && 'justify-end',
                alignment === 'left' && 'justify-start'
              )}
            >
              {buttons.map((button, index) => {
                const ButtonComponent = button.href ? 'a' : 'button';

                return (
                  <ButtonComponent
                    key={index}
                    href={button.href}
                    onClick={button.onClick}
                    className={clsx(
                      'px-6 py-3 sm:px-8 sm:py-4 rounded-lg font-semibold text-base sm:text-lg transition-all duration-300 transform hover:scale-105 focus:outline-none focus:ring-2 focus:ring-white focus:ring-offset-2 focus:ring-offset-transparent',
                      buttonVariantClasses[button.variant || 'primary'],
                      button.className
                    )}
                  >
                    {button.text}
                  </ButtonComponent>
                );
              })}
            </div>
          )}
        </div>
      </section>
    );
  }

  // Default fallback to single
  return null;
};

