import { useState, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/product.css';

/**
 * ProductQuantity Component - Quantity selector with multiple variants
 * @param {Object} props - Component props
 * @param {Object} props.data - Quantity configuration data
 * @param {string} [props.data.variant='default'] - Style variant: 'default' | 'modern' | 'minimal' | 'compact' | 'icon' | 'dark' | 'dark-gray'
 * @param {number} [props.data.min=1] - Minimum quantity
 * @param {number} [props.data.max=99] - Maximum quantity
 * @param {number} [props.data.defaultValue=1] - Default quantity value
 * @param {Function} [props.onQuantityChange] - Callback when quantity changes
 * @param {boolean} [props.loading=false] - Show skeleton loader
 * @param {string} [props.className] - Additional CSS classes
 */
export const ProductQuantity = ({ 
  data = {}, 
  onQuantityChange,
  loading = false,
  className 
}) => {
  const {
    variant = 'default',
    min = 1,
    max = 99,
    defaultValue = 1,
  } = data;

  const [quantity, setQuantity] = useState(defaultValue);
  const [showContent, setShowContent] = useState(!loading);

  useEffect(() => {
    if (loading) {
      setShowContent(false);
      const timer = setTimeout(() => {
        setShowContent(true);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowContent(true);
    }
  }, [loading]);

  const handleDecrease = () => {
    if (quantity > min) {
      const newQuantity = quantity - 1;
      setQuantity(newQuantity);
      if (onQuantityChange) {
        onQuantityChange(newQuantity);
      }
    }
  };

  const handleIncrease = () => {
    if (quantity < max) {
      const newQuantity = quantity + 1;
      setQuantity(newQuantity);
      if (onQuantityChange) {
        onQuantityChange(newQuantity);
      }
    }
  };

  const handleChange = (e) => {
    const value = parseInt(e.target.value) || min;
    const clampedValue = Math.max(min, Math.min(max, value));
    setQuantity(clampedValue);
    if (onQuantityChange) {
      onQuantityChange(clampedValue);
    }
  };

  const containerClasses = {
    default: 'flex items-center border border-gray-300 rounded-lg overflow-hidden',
    modern: 'flex items-center border-2 border-gray-200 rounded-xl overflow-hidden shadow-sm',
    minimal: 'flex items-center border-b-2 border-gray-300',
    compact: 'flex items-center border border-gray-300 rounded-md overflow-hidden',
    icon: 'flex items-center rounded-lg overflow-hidden',
    dark: 'flex items-center border border-gray-600 rounded-lg overflow-hidden bg-gray-800',
    'dark-gray': 'flex items-center border border-gray-700 rounded-lg overflow-hidden bg-gray-900',
  };

  const buttonClasses = {
    default: clsx(
      'px-3 sm:px-4 py-2 bg-gray-50 hover:bg-gray-100 text-gray-700',
      'transition-colors duration-200'
    ),
    modern: clsx(
      'px-4 sm:px-5 py-2.5 bg-gray-50 hover:bg-gray-100 text-gray-700 font-medium',
      'transition-all duration-300'
    ),
    minimal: clsx(
      'px-2 py-1 text-gray-600 hover:text-gray-900',
      'transition-colors duration-200'
    ),
    compact: clsx(
      'px-2 py-1 bg-gray-50 hover:bg-gray-100 text-gray-700 text-xs',
      'transition-colors duration-200'
    ),
    icon: clsx(
      'p-2 bg-primary-500 hover:bg-primary-600 text-white',
      'transition-all duration-300'
    ),
    dark: clsx(
      'px-3 sm:px-4 py-2 bg-gray-700 hover:bg-gray-600 text-gray-200',
      'transition-colors duration-200'
    ),
    'dark-gray': clsx(
      'px-3 sm:px-4 py-2 bg-gray-800 hover:bg-gray-700 text-gray-300',
      'transition-colors duration-200'
    ),
  };

  const inputClasses = {
    default: clsx(
      'w-12 sm:w-16 text-center text-sm sm:text-base font-medium border-0',
      'bg-white text-gray-900'
    ),
    modern: clsx(
      'w-14 sm:w-20 text-center text-base sm:text-lg font-semibold border-0',
      'bg-white text-gray-900'
    ),
    minimal: clsx(
      'w-10 sm:w-12 text-center text-sm border-0 bg-transparent',
      'text-gray-900'
    ),
    compact: clsx(
      'w-10 text-center text-xs font-medium border-0',
      'bg-white text-gray-900'
    ),
    icon: clsx(
        'w-12 sm:w-16 text-center text-sm sm:text-base font-medium border-0',
      'bg-primary-500 text-white'
    ),
    dark: clsx(
      'w-12 sm:w-16 text-center text-sm sm:text-base font-medium border-0',
      'bg-gray-800 text-white'
    ),
    'dark-gray': clsx(
      'w-12 sm:w-16 text-center text-sm sm:text-base font-medium border-0',
      'bg-gray-900 text-gray-100'
    ),
  };

  const SkeletonLoader = () => {
    const isDark = variant === 'dark' || variant === 'dark-gray';
    const skeletonBg = isDark ? 'bg-gray-700' : 'bg-gray-200';

    return (
      <div className={clsx(containerClasses[variant] || containerClasses.default, className)}>
        <div className={clsx('w-10 h-10 product-skeleton', skeletonBg)}></div>
        <div className={clsx('w-12 h-10 product-skeleton', skeletonBg)}></div>
        <div className={clsx('w-10 h-10 product-skeleton', skeletonBg)}></div>
      </div>
    );
  };

  if (!showContent) {
    return <SkeletonLoader />;
  }

  const MinusIcon = () => (
    <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M20 12H4" />
    </svg>
  );

  const PlusIcon = () => (
    <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v16m8-8H4" />
    </svg>
  );

  return (
    <div className={clsx(containerClasses[variant] || containerClasses.default, className)}>
      <button
        type="button"
        onClick={handleDecrease}
        disabled={quantity <= min}
        className={clsx(
          buttonClasses[variant] || buttonClasses.default,
          quantity <= min && 'opacity-50 cursor-not-allowed'
        )}
      >
        {variant === 'icon' ? <MinusIcon /> : '-'}
      </button>
      <input
        type="number"
        value={quantity}
        onChange={handleChange}
        min={min}
        max={max}
        className={inputClasses[variant] || inputClasses.default}
      />
      <button
        type="button"
        onClick={handleIncrease}
        disabled={quantity >= max}
        className={clsx(
          buttonClasses[variant] || buttonClasses.default,
          quantity >= max && 'opacity-50 cursor-not-allowed'
        )}
      >
        {variant === 'icon' ? <PlusIcon /> : '+'}
      </button>
    </div>
  );
};

