import { useState, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/category.css';

/**
 * CategoryShowcase Component  Split layout with media + content
 * @param {Object} props
 * @param {Object} props.data - Configuration
 * @param {string} [props.data.label] - Small label
 * @param {string} [props.data.title] - Main title
 * @param {string} [props.data.description] - Supporting copy
 * @param {Array}  props.data.items - Featured categories
 * @param {string} [props.data.theme='light'] - 'light' | 'dark'
 * @param {string} [props.data.layout='overlay'] - Legacy layout: 'overlay' | 'stacked'
 * @param {string} [props.data.variant] - Visual variant:
 *   'overlay' | 'stacked' | 'role-select' | 'pill-select' | 'circle-select'
 * @param {string} [props.data.badge] - Optional highlight badge
 * @param {string} [props.className]
 * @param {string} [props.id]
 * @param {boolean} [props.loader=false]
 */
export const CategoryShowcase = ({ data, className, id, loader = false }) => {
  if (!data || typeof data !== 'object') {
    console.error('CategoryShowcase: data prop is required and must be an object');
    return null;
  }

  const [showLoader, setShowLoader] = useState(loader);


  useEffect(() => {
    if (loader) {
      const timer = setTimeout(() => setShowLoader(false), 2000);
      return () => clearTimeout(timer);
    }
    setShowLoader(false);
  }, [loader]);

  const {
    label,
    title,
    description,
    items = [],
    theme = 'light',
    layout = 'overlay',
    variant,
    badge,
    className: dataClassName,
  } = data;

  const isDark = theme === 'dark';

  const limitedItems = items;

  // Determine effective variant (backwards compatible with layout prop)
  const effectiveVariant =
    variant && variant !== 'overlay' && variant !== 'stacked'
      ? variant
      : layout; // 'overlay' | 'stacked'

  // Skeleton per variant
  if (showLoader) {
    // Role-select skeleton
    if (effectiveVariant === 'role-select') {
      const skeletonCount = Math.min(
        Math.max(limitedItems.length || 3, 1),
        6
      );
      return (
        <section
          id={id}
          className={clsx(
            'w-full py-10 sm:py-14 md:py-16 lg:py-20 xl:py-24 px-3 sm:px-4 md:px-6 lg:px-8',
            isDark ? 'bg-gray-950' : 'bg-white',
            'category-section',
            className,
            dataClassName
          )}
        >
          <div className="max-w-4xl mx-auto w-full">
            <div className="mb-8 sm:mb-10 md:mb-12 space-y-3 sm:space-y-4 text-center">
              {label && (
                <div className="mx-auto h-3 sm:h-4 w-24 sm:w-32 rounded-full category-skeleton" />
              )}
              {title && (
                <div className="mx-auto h-6 sm:h-7 md:h-8 w-48 sm:w-56 md:w-64 rounded-lg category-skeleton" />
              )}
              {description && (
                <div className="mx-auto h-3 sm:h-4 w-56 sm:w-64 md:w-72 max-w-full rounded category-skeleton" />
              )}
            </div>

            <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-2 lg:grid-cols-3 gap-3 sm:gap-4 md:gap-5 lg:gap-6">
              {Array.from({ length: skeletonCount }).map((_, index) => (
                <div
                  key={index}
                  className={clsx(
                    'rounded-xl sm:rounded-2xl border px-4 py-5 sm:px-5 sm:py-6 md:px-6 md:py-8 category-card-skeleton',
                    isDark
                      ? 'bg-gray-900/60 border-gray-800'
                      : 'bg-white border-gray-200'
                  )}
                >
                  <div className="mx-auto mb-3 sm:mb-4 h-16 w-16 sm:h-20 sm:w-20 md:h-24 md:w-24 rounded-xl sm:rounded-2xl category-skeleton" />
                  <div className="mx-auto mb-2 h-3 sm:h-4 w-20 sm:w-24 rounded-full category-skeleton" />
                  <div className="mx-auto h-2.5 sm:h-3 w-24 sm:w-32 rounded-full category-skeleton" />
                </div>
              ))}
            </div>
          </div>
        </section>
      );
    }

    // Pill-select skeleton
    if (effectiveVariant === 'pill-select') {
      const skeletonCount = Math.min(
        Math.max(limitedItems.length || 4, 1),
        8
      );
      return (
        <section
          id={id}
          className={clsx(
            'w-full py-10 sm:py-12 md:py-14 lg:py-18 xl:py-20 px-3 sm:px-4 md:px-6 lg:px-8',
            isDark ? 'bg-gray-950' : 'bg-slate-50',
            'category-section',
            className,
            dataClassName
          )}
        >
          <div className="max-w-5xl mx-auto w-full">
            <div className="mb-6 sm:mb-8 md:mb-10 space-y-2 sm:space-y-3 text-center">
              {label && (
                <div className="mx-auto h-3 sm:h-4 w-28 sm:w-36 rounded-full category-skeleton" />
              )}
              {title && (
                <div className="mx-auto h-6 sm:h-7 w-48 sm:w-56 md:w-60 rounded-lg category-skeleton" />
              )}
              {description && (
                <div className="mx-auto h-3 sm:h-4 w-56 sm:w-64 md:w-72 max-w-full rounded category-skeleton" />
              )}
            </div>

            <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-2 lg:grid-cols-3 gap-3 sm:gap-4 md:gap-5">
              {Array.from({ length: skeletonCount }).map((_, index) => (
                <div
                  key={index}
                  className={clsx(
                    'relative flex flex-col justify-between rounded-xl sm:rounded-2xl border px-3 py-3.5 sm:px-4 sm:py-4 md:px-5 md:py-5',
                    'category-card-skeleton',
                    isDark
                      ? 'bg-gray-900/70 border-gray-800'
                      : 'bg-white border-gray-200'
                  )}
                >
                  <div className="flex items-start gap-3">
                    <div className="mt-1 h-7 w-7 sm:h-8 sm:w-8 rounded-full category-skeleton" />
                    <div className="flex-1 space-y-1.5 sm:space-y-2">
                      <div className="h-2.5 sm:h-3 w-8 sm:w-10 rounded-full category-skeleton" />
                      <div className="h-3.5 sm:h-4 w-20 sm:w-24 rounded-full category-skeleton" />
                      <div className="h-2.5 sm:h-3 w-24 sm:w-28 rounded-full category-skeleton" />
                    </div>
                  </div>
                  <div className="mt-3 sm:mt-4 flex items-center justify-between gap-2 sm:gap-3">
                    <div className="h-2.5 sm:h-3 w-12 sm:w-16 rounded-full category-skeleton" />
                    <div className="h-6 sm:h-7 w-14 sm:w-16 rounded-full category-skeleton" />
                  </div>
                </div>
              ))}
            </div>
          </div>
        </section>
      );
    }

    // Circle-select skeleton
    if (effectiveVariant === 'circle-select') {
      const skeletonCount = Math.min(
        Math.max(limitedItems.length || 4, 1),
        6
      );
      return (
        <section
          id={id}
          className={clsx(
            'w-full py-10 sm:py-12 md:py-14 lg:py-18 xl:py-20 px-3 sm:px-4 md:px-6 lg:px-8',
            isDark ? 'bg-neutral-950' : 'bg-slate-50',
            'category-section',
            className,
            dataClassName
          )}
        >
          <div className="max-w-5xl mx-auto w-full">
            <div className="mb-6 sm:mb-8 md:mb-10 space-y-2 sm:space-y-3">
              {label && (
                <div className="h-3 sm:h-4 w-24 sm:w-32 rounded-full category-skeleton" />
              )}
              {title && (
                <div className="h-6 sm:h-7 md:h-8 w-48 sm:w-56 md:w-64 max-w-full rounded-lg category-skeleton" />
              )}
              {description && (
                <div className="h-3 sm:h-4 w-56 sm:w-64 md:w-72 max-w-full rounded category-skeleton" />
              )}
            </div>

            <div className="flex flex-wrap justify-center sm:justify-between gap-4 sm:gap-6 md:gap-8 lg:gap-10">
              {Array.from({ length: skeletonCount }).map((_, index) => (
                <div
                  key={index}
                  className="flex flex-col items-center text-center gap-3 sm:gap-4"
                >
                  <div className="rounded-full p-[2px] sm:p-[3px] mb-2 sm:mb-3 md:mb-4 bg-gradient-to-br from-rose-500 via-rose-400 to-rose-600">
                    <div className="rounded-full bg-neutral-900 p-1 sm:p-1.5">
                      <div className="h-16 w-16 sm:h-20 sm:w-20 md:h-24 md:w-24 rounded-full category-skeleton" />
                    </div>
                  </div>
                  <div className="h-3.5 sm:h-4 w-16 sm:w-20 rounded-full category-skeleton" />
                  <div className="h-2.5 sm:h-3 w-20 sm:w-24 rounded-full category-skeleton" />
                </div>
              ))}
            </div>
          </div>
        </section>
      );
    }

    // Default overlay / stacked skeleton
    const skeletonChips = Math.min(
      Math.max(limitedItems.length || 4, 1),
      6
    );
    return (
        <section
          id={id}
          className={clsx(
            'w-full py-10 sm:py-14 md:py-18 lg:py-22 xl:py-24 px-3 sm:px-4 md:px-6 lg:px-8',
            isDark ? 'bg-gray-950' : 'bg-slate-50',
            'category-section',
            className,
            dataClassName
          )}
        >
          <div className="max-w-7xl mx-auto w-full grid lg:grid-cols-2 gap-6 sm:gap-8 md:gap-10 lg:gap-14 items-center">
            {/* Left skeleton */}
            <div className="space-y-4 sm:space-y-5">
              <div className="space-y-2 sm:space-y-3">
                {label && (
                  <div className="category-skeleton h-3 sm:h-4 w-24 sm:w-28 rounded-full" />
                )}
                {title && (
                  <div className="category-skeleton h-7 sm:h-9 md:h-10 w-4/5 sm:w-5/6 rounded-lg" />
                )}
                {description && (
                  <div className="space-y-1.5 sm:space-y-2 max-w-md">
                    <div className="category-skeleton h-3 sm:h-4 w-full rounded" />
                    <div className="category-skeleton h-3 sm:h-4 w-5/6 rounded" />
                    <div className="category-skeleton h-3 sm:h-4 w-3/4 rounded" />
                  </div>
                )}
              </div>
              <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-2 lg:grid-cols-3 gap-4 sm:gap-6 pt-4 sm:pt-6">
              {Array.from({ length: skeletonChips }).map((_, index) => (
                <div
                  key={index}
                  className={clsx(
                    'category-card-skeleton rounded-lg sm:rounded-xl border p-4 sm:p-6',
                    isDark
                      ? 'bg-gray-900/80 border-gray-800'
                      : 'bg-white border-gray-200'
                  )}
                />
              ))}
            </div>
          </div>

       
        </div>
      </section>
    );
  }

  if (!limitedItems.length) {
    console.warn('CategoryShowcase: items array is empty');
    return null;
  }

  const content = (
    <div className="space-y-4 sm:space-y-5 md:space-y-6">
      {badge && (
        <span
          className={clsx(
            'inline-flex items-center gap-1.5 sm:gap-2 rounded-full px-2.5 py-0.5 sm:px-3 sm:py-1 text-[10px] sm:text-xs font-semibold tracking-wide border',
            isDark
              ? 'border-emerald-500/40 text-emerald-300 bg-emerald-500/10'
              : 'border-emerald-500/30 text-emerald-700 bg-emerald-50'
          )}
        >
          <span className="w-1 h-1 sm:w-1.5 sm:h-1.5 rounded-full bg-emerald-500" />
          {badge}
        </span>
      )}

      {label && (
        <p
          className={clsx(
            'text-[10px] sm:text-xs md:text-sm font-semibold tracking-[0.15em] sm:tracking-[0.2em] uppercase',
            isDark ? 'text-gray-400' : 'text-gray-600'
          )}
        >
          {label}
        </p>
      )}

      {title && (
        <h2
          className={clsx(
            'text-2xl sm:text-3xl md:text-4xl lg:text-5xl font-bold tracking-tight leading-tight',
            isDark ? 'text-white' : 'text-gray-900'
          )}
        >
          {title}
        </h2>
      )}

      {description && (
        <p
          className={clsx(
            'max-w-xl text-xs sm:text-sm md:text-base lg:text-lg leading-relaxed',
            isDark ? 'text-gray-400' : 'text-gray-600'
          )}
        >
          {description}
        </p>
      )}

      <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-2 lg:grid-cols-3 gap-4 sm:gap-6 pt-4 sm:pt-6">
        {limitedItems.map((item, index) => (
          <div
            key={item.id || item.slug || item.title || index}
            className={clsx(
              'group relative rounded-lg sm:rounded-xl border p-4 sm:p-6',
              'transition-all duration-200 category-chip touch-manipulation active:scale-[0.98]',
              'hover:shadow-lg focus-within:ring-2 focus-within:ring-primary-500/50',
              isDark
                ? 'bg-gray-900/80 border-gray-800 hover:border-gray-700 hover:bg-gray-900'
                : 'bg-white border-gray-200 hover:border-gray-300 hover:bg-gray-50'
            )}
          >
            <div className="flex flex-col items-center gap-3 sm:gap-4">
              {(item.icon || item.image) && (
                <div
                  className={clsx(
                    'relative flex-shrink-0 rounded-lg sm:rounded-xl overflow-hidden',
                    'w-20 h-20 sm:w-28 sm:h-28 md:w-32 md:h-32 lg:w-36 lg:h-36',
                    isDark
                      ? 'bg-gray-800 border border-gray-700'
                      : 'bg-gray-100 border border-gray-200'
                  )}
                >
                  <img
                    src={item.image || item.icon}
                    alt={item.title || 'Category image'}
                    className="w-full h-full object-cover group-hover:scale-105 transition-transform duration-200"
                  />
                </div>
              )}
              <div className="flex flex-col items-center gap-2 text-center min-w-0">
                <h3
                  className={clsx(
                    'text-sm sm:text-lg md:text-xl font-bold leading-tight line-clamp-2',
                    isDark ? 'text-white' : 'text-gray-900'
                  )}
                >
                  {item.title}
                </h3>
                {item.count != null && (
                  <span
                    className={clsx(
                      'text-xs sm:text-sm font-medium px-3 py-1 rounded-full',
                      isDark
                        ? 'bg-gray-800 text-gray-300'
                        : 'bg-gray-100 text-gray-600'
                    )}
                  >
                    {item.count} items
                  </span>
                )}
                {item.subtitle && (
                  <p
                    className={clsx(
                      'text-xs sm:text-sm leading-tight line-clamp-2 opacity-75',
                      isDark ? 'text-gray-400' : 'text-gray-600'
                    )}
                  >
                    {item.subtitle}
                  </p>
                )}
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );



  // === Variant 1: Role select cards (like first screenshot) ===
  if (effectiveVariant === 'role-select') {
    return (
      <section
        id={id}
        className={clsx(
          'w-full py-10 sm:py-14 md:py-16 lg:py-20 xl:py-24 px-3 sm:px-4 md:px-6 lg:px-8',
          isDark ? 'bg-gray-950' : 'bg-white',
          'category-section',
          className,
          dataClassName
        )}
      >
        <div className="max-w-4xl mx-auto w-full">
          {/* Header */}
          {(title || label || description) && (
            <div className="mb-8 sm:mb-10 md:mb-12 text-center space-y-2 sm:space-y-3">
              {label && (
                <p
                  className={clsx(
                    'text-[10px] sm:text-xs md:text-sm font-semibold tracking-[0.15em] sm:tracking-[0.2em] uppercase',
                    isDark ? 'text-gray-400' : 'text-gray-500'
                  )}
                >
                  {label}
                </p>
              )}
              {title && (
                <h2
                  className={clsx(
                    'text-xl sm:text-2xl md:text-3xl lg:text-4xl font-bold tracking-tight leading-tight',
                    isDark ? 'text-white' : 'text-gray-900'
                  )}
                >
                  {title}
                </h2>
              )}
              {description && (
                <p
                  className={clsx(
                    'max-w-2xl mx-auto text-xs sm:text-sm md:text-base text-gray-500 leading-relaxed px-2',
                    isDark && 'text-gray-400'
                  )}
                >
                  {description}
                </p>
              )}
            </div>
          )}

          {/* Role cards */}
          <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-3 sm:gap-4 md:gap-5 lg:gap-6">
            {limitedItems.map((item, index) => (
              <button
                key={item.id || item.slug || item.title || index}
                type="button"
                className={clsx(
                  'group relative flex flex-col items-center text-center rounded-xl sm:rounded-2xl border',
                  'transition-all duration-200 focus:outline-none focus-visible:ring-2 focus-visible:ring-primary-500 focus-visible:ring-offset-2',
                  'touch-manipulation active:scale-[0.98]',
                  isDark
                    ? 'bg-gray-900/60 border-gray-800 hover:border-primary-500 hover:bg-gray-900 focus-visible:ring-offset-gray-950'
                    : 'bg-white border-gray-200 hover:border-primary-500 hover:bg-primary-50/40 focus-visible:ring-offset-white',
                  'px-4 py-5 sm:px-5 sm:py-6 md:px-6 md:py-8'
                )}
              >
                {/* Illustration / image */}
                {(item.icon || item.image) && (
                  <div
                    className={clsx(
                      'mb-3 sm:mb-4 h-16 w-16 sm:h-20 sm:w-20 md:h-24 md:w-24 rounded-xl sm:rounded-2xl flex items-center justify-center overflow-hidden',
                      'transition-transform duration-200 group-hover:scale-105 group-hover:shadow-lg',
                      isDark
                        ? 'bg-gray-800 border border-gray-700'
                        : 'bg-primary-50 border border-primary-100'
                    )}
                  >
                    <img
                      src={item.image || item.icon}
                      alt={item.title || 'Category image'}
                      className="w-full h-full object-cover"
                    />
                  </div>
                )}

                {/* Title and subtitle */}
                <div className="space-y-1">
                  <p
                    className={clsx(
                      'text-xs sm:text-sm font-medium leading-tight',
                      isDark ? 'text-white' : 'text-gray-900'
                    )}
                  >
                    {item.title}
                  </p>
                  {item.subtitle && (
                    <p
                      className={clsx(
                        'text-[10px] sm:text-xs text-gray-500 leading-relaxed line-clamp-2',
                        isDark && 'text-gray-400'
                      )}
                    >
                      {item.subtitle}
                    </p>
                  )}
                </div>

                {/* Active indicator */}
                <div className="mt-3 sm:mt-4 flex items-center justify-center gap-1.5 sm:gap-2">
                  <span
                    className={clsx(
                      'h-1.5 w-1.5 sm:h-2 sm:w-2 rounded-full',
                      isDark ? 'bg-emerald-400' : 'bg-primary-500'
                    )}
                  />
                  <span
                    className={clsx(
                      'text-[10px] sm:text-[11px] font-medium',
                      isDark ? 'text-gray-400' : 'text-gray-500'
                    )}
                  >
                    {item.badge || 'Select'}
                  </span>
                </div>
              </button>
            ))}
          </div>
        </div>
      </section>
    );
  }

  // === Variant 2: Pill / segment select ===
  if (effectiveVariant === 'pill-select') {
    return (
      <section
        id={id}
        className={clsx(
          'w-full py-10 sm:py-12 md:py-14 lg:py-18 xl:py-20 px-3 sm:px-4 md:px-6 lg:px-8',
          isDark ? 'bg-gray-950' : 'bg-slate-50',
          'category-section',
          className,
          dataClassName
        )}
      >
        <div className="max-w-5xl mx-auto w-full">
          {(title || label || description) && (
            <div className="mb-6 sm:mb-8 md:mb-10 text-center space-y-1.5 sm:space-y-2">
              {label && (
                <p
                  className={clsx(
                    'text-[10px] sm:text-xs md:text-sm font-semibold tracking-[0.15em] sm:tracking-[0.2em] uppercase',
                    isDark ? 'text-gray-400' : 'text-gray-600'
                  )}
                >
                  {label}
                </p>
              )}
              {title && (
                <h2
                  className={clsx(
                    'text-xl sm:text-2xl md:text-3xl font-bold tracking-tight leading-tight',
                    isDark ? 'text-white' : 'text-gray-900'
                  )}
                >
                  {title}
                </h2>
              )}
              {description && (
                <p
                  className={clsx(
                    'max-w-2xl mx-auto text-xs sm:text-sm md:text-base text-gray-500 leading-relaxed px-2',
                    isDark && 'text-gray-400'
                  )}
                >
                  {description}
                </p>
              )}
            </div>
          )}

          <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-2 lg:grid-cols-4 gap-3 sm:gap-4 md:gap-5">
            {limitedItems.map((item, index) => (
              <div
                key={item.id || item.slug || item.title || index}
                className={clsx(
                  'relative flex flex-col justify-between rounded-xl sm:rounded-2xl border bg-white',
                  'px-3 py-3.5 sm:px-4 sm:py-4 md:px-5 md:py-5',
                  'transition-all duration-200 touch-manipulation active:scale-[0.98]',
                  isDark
                    ? 'bg-gray-900/70 border-gray-800 hover:border-primary-500'
                    : 'bg-white border-gray-200 hover:border-primary-500 hover:shadow-md'
                )}
              >
                <div className="flex items-start gap-3">
                  {(item.icon || item.image) && (
                    <div
                      className={clsx(
                        'mt-1 h-7 w-7 sm:h-8 sm:w-8 rounded-full flex items-center justify-center flex-shrink-0 overflow-hidden',
                        isDark
                          ? 'bg-gray-800'
                          : 'bg-primary-50'
                      )}
                    >
                      <img
                        src={item.image || item.icon}
                        alt={item.title || 'Category image'}
                        className="w-full h-full object-cover rounded-full"
                      />
                    </div>
                  )}
                  <div className="flex-1 min-w-0">
                    <p
                      className={clsx(
                        'text-[10px] sm:text-[11px] uppercase tracking-wide mb-0.5 sm:mb-1',
                        isDark ? 'text-gray-400' : 'text-gray-500'
                      )}
                    >
                      I am
                    </p>
                    <p
                      className={clsx(
                        'text-xs sm:text-sm font-semibold leading-tight',
                        isDark ? 'text-white' : 'text-gray-900'
                      )}
                    >
                      {item.title}
                    </p>
                    {item.subtitle && (
                      <p
                        className={clsx(
                          'mt-0.5 sm:mt-1 text-[10px] sm:text-xs text-gray-500 line-clamp-2',
                          isDark && 'text-gray-400'
                        )}
                      >
                        {item.subtitle}
                      </p>
                    )}
                  </div>
                </div>

                <div className="mt-3 sm:mt-4 flex items-center justify-between gap-2">
                  <span
                    className={clsx(
                      'text-[10px] sm:text-[11px] font-medium',
                      isDark ? 'text-gray-400' : 'text-gray-500'
                    )}
                  >
                    {item.badge || 'Segment'}
                  </span>
                  <button
                    type="button"
                    className={clsx(
                      'inline-flex items-center justify-center rounded-full px-3 py-1 sm:px-4 sm:py-1.5 text-[10px] sm:text-xs font-semibold',
                      'transition-colors duration-200 touch-manipulation',
                      index === 1
                        ? 'bg-primary-600 text-white hover:bg-primary-700'
                        : isDark
                        ? 'border border-gray-700 text-gray-100 hover:border-primary-500 hover:text-primary-300'
                        : 'border border-gray-300 text-gray-800 hover:border-primary-500 hover:text-primary-700'
                    )}
                  >
                    {item.button?.label || 'Select'}
                  </button>
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>
    );
  }

  // === Variant 3: Circle / avatar select (second screenshot style) ===
  if (effectiveVariant === 'circle-select') {
    return (
      <section
        id={id}
        className={clsx(
          'w-full py-10 sm:py-12 md:py-14 lg:py-18 xl:py-20 px-3 sm:px-4 md:px-6 lg:px-8',
          isDark ? 'bg-neutral-950' : 'bg-slate-50',
          'category-section',
          className,
          dataClassName
        )}
      >
        <div className="max-w-5xl mx-auto w-full">
          {/* Header */}
          {(title || label || description) && (
            <div className="mb-6 sm:mb-8 md:mb-10 space-y-2 sm:space-y-3">
              {label && (
                <p
                  className={clsx(
                    'text-[10px] sm:text-xs md:text-sm font-semibold tracking-[0.15em] sm:tracking-[0.2em] uppercase',
                    isDark ? 'text-neutral-500' : 'text-neutral-500'
                  )}
                >
                  {label}
                </p>
              )}
              {title && (
                <h2
                  className={clsx(
                    'text-xl sm:text-2xl md:text-3xl lg:text-4xl font-bold tracking-tight leading-tight',
                    isDark ? 'text-white' : 'text-neutral-900'
                  )}
                >
                  {title}
                </h2>
              )}
              {description && (
                <p
                  className={clsx(
                    'max-w-2xl text-xs sm:text-sm md:text-base leading-relaxed',
                    isDark ? 'text-neutral-400' : 'text-neutral-600'
                  )}
                >
                  {description}
                </p>
              )}
            </div>
          )}

          {/* Circle options */}
          <div className="flex flex-wrap justify-center sm:justify-between gap-4 sm:gap-6 md:gap-8 lg:gap-10">
            {limitedItems.map((item, index) => (
              <button
                key={item.id || item.slug || item.title || index}
                type="button"
                className={clsx(
                  'group flex flex-col items-center text-center touch-manipulation active:scale-[0.95]',
                  'focus:outline-none focus-visible:ring-2 focus-visible:ring-rose-500 focus-visible:ring-offset-2',
                  isDark
                    ? 'focus-visible:ring-offset-neutral-950'
                    : 'focus-visible:ring-offset-slate-50'
                )}
              >
                <div
                  className={clsx(
                    'relative rounded-full p-[2px] sm:p-[3px] mb-2 sm:mb-3 md:mb-4',
                    'bg-gradient-to-br from-rose-500 via-rose-400 to-rose-600'
                  )}
                >
                  <div className="rounded-full bg-neutral-900 p-1 sm:p-1.5">
                    {(item.image || item.icon) ? (
                      <img
                        src={item.image || item.icon}
                        alt={item.title}
                        className={clsx(
                          'h-16 w-16 sm:h-20 sm:w-20 md:h-24 md:w-24 rounded-full object-cover object-center',
                          'group-hover:scale-[1.03] transition-transform duration-200'
                        )}
                      />
                    ) : (
                      <div
                        className={clsx(
                          'h-16 w-16 sm:h-20 sm:w-20 md:h-24 md:w-24 rounded-full flex items-center justify-center text-xl sm:text-2xl md:text-3xl',
                          isDark
                            ? 'bg-neutral-800 text-white'
                            : 'bg-white text-neutral-900'
                        )}
                      >
                        {item.title?.[0]}
                      </div>
                    )}
                  </div>
                </div>

                <p
                  className={clsx(
                    'text-xs sm:text-sm md:text-base font-medium leading-tight',
                    isDark ? 'text-white' : 'text-neutral-900'
                  )}
                >
                  {item.title}
                </p>
                {item.subtitle && (
                  <p
                    className={clsx(
                      'mt-1 text-[10px] sm:text-xs md:text-sm max-w-[8rem] sm:max-w-[9rem] md:max-w-[10rem] line-clamp-2',
                      isDark ? 'text-neutral-400' : 'text-neutral-600'
                    )}
                  >
                    {item.subtitle}
                  </p>
                )}
              </button>
            ))}
          </div>
        </div>
      </section>
    );
  }

  // Default overlay / stacked layouts
  return (
    <section
      id={id}
      className={clsx(
        'w-full py-10 sm:py-14 md:py-18 lg:py-22 xl:py-24 px-3 sm:px-4 md:px-6 lg:px-8',
        isDark ? 'bg-black' : 'bg-slate-50',
        'category-section',
        className,
        dataClassName
      )}
    >
      <div className="max-w-7xl mx-auto w-full">
        <div
          className={clsx(
           
            layout === 'stacked' &&
              'lg:grid-cols-[minmax(0,1.15fr)_minmax(0,1fr)]'
          )}
        >
          {layout === 'overlay' ? (
            <>
              {content}
          
            </>
          ) : (
            <>
              
              {content}
            </>
          )}
        </div>
      </div>
    </section>
  );
};


