import { useState, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/feature.css';

/**
 * FeatureDeck Component - Inspired by Mantine UI
 * @param {Object} props - Component props
 * @param {Object} props.data - Feature section configuration data
 * @param {string} [props.data.title] - Main heading text
 * @param {string} [props.data.subtitle] - Subtitle text displayed above title
 * @param {string} [props.data.description] - Description text displayed below title
 * @param {Array} props.data.items - Array of feature items
 * @param {string} [props.data.layout='grid'] - Layout type: 'grid' | 'list' | 'card'
 * @param {string} [props.data.variant='default'] - Style variant: 'default' | 'mantine-icons' | 'mantine-cards' | 'mantine-monotone' | 'mantine-image-icons' | 'mantine-title' | 'mantine-badge-cards' | 'bordered' | 'minimal' | 'gradient' | 'shadow' | 'colored' | 'outlined' | 'filled'
 * @param {string} [props.data.alignment='center'] - Text alignment: 'left' | 'center' | 'right'
 * @param {number} [props.data.columns=3] - Number of columns for grid layout (1-4)
 * @param {boolean} [props.data.showIcons=true] - Show icons for features
 * @param {boolean} [props.data.hoverEffect=true] - Enable hover effects
 * @param {string} [props.data.iconStyle='colored'] - Icon style: 'colored' | 'monotone' | 'image'
 * @param {string} [props.data.iconAlignment] - Icon alignment: 'left' | 'center' | 'right' (auto-centered for mantine variants)
 * @param {Array} [props.data.buttons=[]] - Array of buttons for mantine-title variant
 * @param {string} [props.data.className] - Additional CSS classes for the section
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute for the section element
 * @param {boolean} [props.loader=false] - Show loading state
 */
export const FeatureDeck = ({ data, className, id, loader = false }) => {
  // Safety check for data
  if (!data || typeof data !== 'object') {
    console.error('FeatureDeck: data prop is required and must be an object');
    return null;
  }

  const [showLoader, setShowLoader] = useState(loader);

  // Auto-hide loader after 2 seconds when loader prop is true
  useEffect(() => {
    if (loader) {
      const timer = setTimeout(() => {
        setShowLoader(false);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowLoader(false);
    }
  }, [loader]);

  const {
    title,
    subtitle,
    description,
    items = [],
    layout = 'grid',
    variant = 'default',
    alignment = 'center',
    columns = 3,
    showIcons = true,
    hoverEffect = true,
    iconStyle = 'colored',
    iconAlignment,
    buttons = [],
    className: dataClassName,
  } = data;

  // Auto-center icons for mantine variants if not explicitly set
  const getIconAlignment = () => {
    if (iconAlignment) return iconAlignment;
    // Auto-center for mantine variants
    if (['mantine-icons', 'mantine-monotone', 'mantine-image-icons'].includes(variant)) {
      return 'center';
    }
    return 'left';
  };

  const currentIconAlignment = getIconAlignment();

  const [hoveredIndex, setHoveredIndex] = useState(null);

  const alignmentClasses = {
    left: 'text-left items-start',
    center: 'text-center items-center',
    right: 'text-right items-end',
  };

  const variantClasses = {
    default: 'bg-white',
    'mantine-icons': 'bg-white',
    'mantine-cards': 'bg-white',
    'mantine-monotone': 'bg-white',
    'mantine-image-icons': 'bg-white',
    'mantine-title': 'bg-white',
    'mantine-badge-cards': 'bg-white',
    bordered: 'bg-white',
    minimal: 'bg-transparent',
    gradient: 'bg-gradient-to-br from-primary-50 to-primary-50',
    shadow: 'bg-white',
    colored: 'bg-gray-50',
    outlined: 'bg-white',
    filled: 'bg-white',
  };

  const getItemClasses = (index) => {
    const baseClasses = 'transition-all duration-300';

    // Mantine Icons variant - Clean and minimal with centered icons
    if (variant === 'mantine-icons') {
      return clsx(
        baseClasses,
        'bg-transparent p-0 text-center',
        hoverEffect && hoveredIndex === index && 'opacity-80'
      );
    }

    // Mantine Cards variant - Card-based with shadow
    if (variant === 'mantine-cards') {
      return clsx(
        baseClasses,
        'bg-white rounded-lg p-6 sm:p-8 border border-gray-200',
        hoverEffect && hoveredIndex === index && 'shadow-lg border-gray-300'
      );
    }

    // Mantine Monotone variant - Clean with monotone icons, centered
    if (variant === 'mantine-monotone') {
      return clsx(
        baseClasses,
        'bg-transparent p-0 text-center',
        hoverEffect && hoveredIndex === index && 'opacity-80'
      );
    }

    // Mantine Image Icons variant - With image icons, centered
    if (variant === 'mantine-image-icons') {
      return clsx(
        baseClasses,
        'bg-transparent p-0 text-center',
        hoverEffect && hoveredIndex === index && 'opacity-80'
      );
    }

    // Mantine Title variant - Simple cards
    if (variant === 'mantine-title') {
      return clsx(
        baseClasses,
        'bg-white rounded-lg p-6 border border-gray-200',
        hoverEffect && hoveredIndex === index && 'shadow-md border-gray-300'
      );
    }

    // Mantine Badge Cards variant - Cards with badge header and underlined titles
    if (variant === 'mantine-badge-cards') {
      return clsx(
        baseClasses,
        'bg-white rounded-lg p-6 sm:p-8 border border-gray-200 shadow-sm text-left',
        hoverEffect && hoveredIndex === index && 'shadow-md border-gray-300'
      );
    }

    // Default variant
    if (variant === 'default') {
      return clsx(
        baseClasses,
        hoverEffect && 'hover:transform hover:scale-105',
        'bg-white rounded-lg p-6 border border-gray-200',
        hoveredIndex === index && 'border-primary-300 shadow-md'
      );
    }

    // Bordered variant
    if (variant === 'bordered') {
      return clsx(
        baseClasses,
        hoverEffect && 'hover:transform hover:scale-105',
        'bg-white rounded-lg p-6 border-2 border-gray-200',
        hoveredIndex === index && 'border-primary-500 shadow-lg'
      );
    }

    // Minimal variant
    if (variant === 'minimal') {
      return clsx(
        baseClasses,
        hoverEffect && 'hover:transform hover:scale-105',
        'bg-transparent rounded-lg p-6',
        hoveredIndex === index && 'bg-gray-50'
      );
    }

    // Gradient variant
    if (variant === 'gradient') {
      return clsx(
        baseClasses,
        hoverEffect && 'hover:transform hover:scale-105',
        'bg-white rounded-lg p-6 border border-gray-200 shadow-sm',
        hoveredIndex === index && 'shadow-xl border-transparent bg-gradient-to-br from-primary-50 to-primary-50'
      );
    }

    // Shadow variant
    if (variant === 'shadow') {
      return clsx(
        baseClasses,
        hoverEffect && 'hover:transform hover:scale-105',
        'bg-white rounded-lg p-6 border border-gray-200 shadow-sm',
        hoveredIndex === index && 'shadow-xl border-gray-300'
      );
    }

    // Colored variant
    if (variant === 'colored') {
      return clsx(
        baseClasses,
        hoverEffect && 'hover:transform hover:scale-105',
        'bg-white rounded-lg p-6 border border-gray-200',
        hoveredIndex === index && 'bg-primary-50 border-primary-300 shadow-md'
      );
    }

    // Outlined variant
    if (variant === 'outlined') {
      return clsx(
        baseClasses,
        hoverEffect && 'hover:transform hover:scale-105',
        'bg-white rounded-lg p-6 border-2 border-gray-300',
        hoveredIndex === index && 'border-primary-600 shadow-md'
      );
    }

    // Filled variant
    if (variant === 'filled') {
      return clsx(
        baseClasses,
        hoverEffect && 'hover:transform hover:scale-105',
        'bg-gray-100 rounded-lg p-6 border border-gray-300 transition-all duration-300',
        hoveredIndex === index && 'bg-primary-500 border-primary-600 text-white shadow-lg'
      );
    }

    return clsx(baseClasses, 'bg-white rounded-lg p-6');
  };

  const getIconClasses = (index) => {
    const baseIconClasses = 'flex-shrink-0 mb-4 transition-all duration-300';
    
    // Icon alignment classes
    const iconAlignmentClasses = {
      left: 'self-start',
      center: 'self-center mx-auto',
      right: 'self-end ml-auto',
    };
    
    const alignmentClass = iconAlignmentClasses[currentIconAlignment] || '';
    
    // Filled variant - gray when not hovered, white when hovered
    if (variant === 'filled') {
      return clsx(
        baseIconClasses,
        hoveredIndex === index ? 'text-black' : 'text-gray-700',
        alignmentClass
      );
    }
    
    // Mantine monotone variant - gray icons
    if (variant === 'mantine-monotone' || (variant === 'mantine-icons' && iconStyle === 'monotone')) {
      return clsx(baseIconClasses, 'text-gray-600', alignmentClass);
    }

    // Mantine image icons - no special classes
    if (variant === 'mantine-image-icons') {
      return clsx(baseIconClasses, alignmentClass);
    }
    
    // Gradient variant hover state
    if (variant === 'gradient' && hoveredIndex === index) {
      return clsx(baseIconClasses, 'text-primary-700', alignmentClass);
    }

    // Default - colored icons
    return clsx(baseIconClasses, 'text-primary-700', alignmentClass);
  };

  const getTitleClasses = (index) => {
    const baseTitleClasses = 'text-xl font-bold mb-2 transition-colors duration-300';
    
    // Filled variant - gray when not hovered, white when hovered
    if (variant === 'filled') {
      return clsx(
        baseTitleClasses,
        hoveredIndex === index ? 'text-black' : 'text-gray-900'
      );
    }

    // Mantine Badge Cards variant - with underline
    if (variant === 'mantine-badge-cards') {
      return clsx(baseTitleClasses, 'text-gray-900 text-lg sm:text-xl font-semibold mb-2');
    }

    // Mantine Icons, Monotone, Image Icons variants - centered, larger
    if (variant === 'mantine-icons' || variant === 'mantine-monotone' || variant === 'mantine-image-icons') {
      return clsx(baseTitleClasses, 'text-gray-900 text-lg sm:text-xl font-semibold');
    }

    // Other Mantine variants - slightly larger
    if (variant.startsWith('mantine-')) {
      return clsx(baseTitleClasses, 'text-gray-900 text-lg sm:text-xl');
    }

    return clsx(baseTitleClasses, 'text-gray-900');
  };

  const getDescriptionClasses = (index) => {
    const baseDescClasses = 'text-base leading-relaxed transition-colors duration-300';
    
    // Filled variant - gray when not hovered, white when hovered
    if (variant === 'filled') {
      return clsx(
        baseDescClasses,
        hoveredIndex === index ? 'text-black' : 'text-gray-600'
      );
    }

    // Mantine Icons, Monotone, Image Icons variants - centered, smaller text
    if (variant === 'mantine-icons' || variant === 'mantine-monotone' || variant === 'mantine-image-icons') {
      return clsx(baseDescClasses, 'text-gray-600 text-sm sm:text-base');
    }

    // Other Mantine variants - slightly smaller
    if (variant.startsWith('mantine-')) {
      return clsx(baseDescClasses, 'text-gray-600 text-sm sm:text-base');
    }

    return clsx(baseDescClasses, 'text-gray-600');
  };

  const renderIcon = (item, index) => {
    if (!showIcons || !item.icon) return null;

    const currentIconStyle = item.iconStyle || iconStyle;
    const itemIconAlignment = item.iconAlignment || currentIconAlignment;

    // Icon alignment wrapper classes
    const iconWrapperClasses = {
      left: 'flex items-start',
      center: 'flex items-center justify-center',
      right: 'flex items-end justify-end',
    };
    const wrapperClass = iconWrapperClasses[itemIconAlignment] || 'flex items-start';

    // If icon is a string (emoji or image URL)
    if (typeof item.icon === 'string') {
      // Check if it's an image URL
      if (item.icon.startsWith('http') || item.icon.startsWith('/') || item.icon.includes('.')) {
        return (
          <div className={clsx(wrapperClass, 'mb-4')}>
            <img
              src={item.icon}
              alt={item.title || 'Feature icon'}
              className={clsx(
                'w-12 h-12 sm:w-16 sm:h-16 object-contain transition-all duration-300',
                hoverEffect && hoveredIndex === index && 'transform scale-110'
              )}
            />
          </div>
        );
      }
      
      // Mantine Icons variant - Simple icon container, always centered
      if (variant === 'mantine-icons') {
        return (
          <div className="flex justify-center mb-6">
            <div
              className={clsx(
                'w-12 h-12 sm:w-14 sm:h-14 flex items-center justify-center text-2xl sm:text-3xl transition-all duration-300',
                currentIconStyle === 'monotone' ? 'text-gray-600' : 'text-primary-700',
                hoverEffect && hoveredIndex === index && 'transform scale-110'
              )}
            >
              {item.icon}
            </div>
          </div>
        );
      }

      // Mantine Monotone variant - Gray icons, always centered
      if (variant === 'mantine-monotone') {
        return (
          <div className="flex justify-center mb-6">
            <div
              className={clsx(
                'w-12 h-12 sm:w-14 sm:h-14 flex items-center justify-center text-2xl sm:text-3xl text-gray-600 transition-all duration-300',
                hoverEffect && hoveredIndex === index && 'transform scale-110'
              )}
            >
              {item.icon}
            </div>
          </div>
        );
      }

      // Mantine Image Icons variant - Image icons, always centered
      if (variant === 'mantine-image-icons') {
        return (
          <div className="flex justify-center mb-6">
            <img
              src={item.icon}
              alt={item.title || 'Feature icon'}
              className={clsx(
                'w-16 h-16 sm:w-20 sm:h-20 object-contain transition-all duration-300',
                hoverEffect && hoveredIndex === index && 'transform scale-110'
              )}
            />
          </div>
        );
      }

      // Mantine Badge Cards variant - Blue circular icons
      if (variant === 'mantine-badge-cards') {
        return (
          <div className="mb-6">
            <div
              className={clsx(
                'w-12 h-12 sm:w-14 sm:h-14 rounded-full bg-primary-700 flex items-center justify-center text-white text-xl sm:text-2xl transition-all duration-300',
                hoverEffect && hoveredIndex === index && 'transform scale-105 bg-primary-700'
              )}
            >
              {item.icon}
            </div>
          </div>
        );
      }

      // Otherwise treat as emoji or text
      // Special handling for filled variant
      if (variant === 'filled') {
        return (
          <div className={clsx(wrapperClass, 'mb-4')}>
            <div
              className={clsx(
                getIconClasses(index),
                'w-12 h-12 sm:w-16 sm:h-16 flex items-center justify-center text-3xl sm:text-4xl rounded-lg transition-all duration-300',
                hoveredIndex === index ? 'bg-primary-400' : 'bg-gray-200',
                hoverEffect && hoveredIndex === index && 'transform scale-110'
              )}
            >
              {item.icon}
            </div>
          </div>
        );
      }
      
      return (
        <div className={clsx(wrapperClass, 'mb-4')}>
          <div
              className={clsx(
                getIconClasses(index),
                'w-12 h-12 sm:w-16 sm:h-16 flex items-center justify-center text-3xl sm:text-4xl rounded-lg',
                variant === 'mantine-title' ? 'bg-primary-50 text-primary-700' : 'bg-primary-50',
                hoverEffect && hoveredIndex === index && 'transform scale-110 bg-primary-100'
              )}
          >
            {item.icon}
          </div>
        </div>
      );
    }

    // If icon is a React component
    if (typeof item.icon === 'object' && item.icon.type) {
      return (
        <div className={clsx(wrapperClass, 'mb-4')}>
          <div className={getIconClasses(index)}>
            {item.icon}
          </div>
        </div>
      );
    }

    return null;
  };

  const gridColumnsClasses = {
    1: 'grid-cols-1',
    2: 'grid-cols-1 sm:grid-cols-2',
    3: 'grid-cols-1 sm:grid-cols-2 lg:grid-cols-3',
    4: 'grid-cols-1 sm:grid-cols-2 lg:grid-cols-4',
  };

  if (items.length === 0) {
    console.warn('FeatureDeck: items array is empty');
    return null;
  }

  // Show skeleton loader if loader prop is true
  if (showLoader) {
    const gridColumnsClasses = {
      1: 'grid-cols-1',
      2: 'grid-cols-1 md:grid-cols-2',
      3: 'grid-cols-1 md:grid-cols-2 lg:grid-cols-3',
      4: 'grid-cols-1 md:grid-cols-2 lg:grid-cols-4',
    };

    return (
      <section
        id={id}
        className={clsx(
          'w-full py-12 sm:py-16 lg:py-20 px-4 sm:px-6 lg:px-8',
          variantClasses[variant] || 'bg-white',
          className,
          dataClassName
        )}
      >
        <div className="max-w-7xl mx-auto w-full">
          {/* Header skeleton */}
          <div className={clsx('mb-10 sm:mb-12 flex flex-col', alignmentClasses[alignment])}>
            <div className="h-4 bg-gray-400 rounded w-24 mb-3 mx-auto animate-pulse"></div>
            <div className="h-10 bg-gray-400 rounded w-full max-w-2xl mb-4 mx-auto animate-pulse"></div>
            <div className="h-5 bg-gray-300 rounded w-full max-w-xl mb-6 mx-auto animate-pulse"></div>
          </div>

          {/* Feature cards skeleton */}
          <div className={clsx('grid gap-6', gridColumnsClasses[columns])}>
            {[0, 1, 2, 3, 4, 5].slice(0, columns * 2).map((index) => (
              <div key={index} className="bg-white border border-gray-200 rounded-lg p-6">
                {/* Icon skeleton */}
                {showIcons && (
                  <div className="w-12 h-12 bg-gray-300 rounded-lg mb-4 mx-auto animate-pulse"></div>
                )}
                {/* Title skeleton */}
                <div className="h-6 bg-gray-400 rounded w-3/4 mb-3 mx-auto animate-pulse"></div>
                {/* Description skeleton */}
                <div className="space-y-2">
                  <div className="h-4 bg-gray-300 rounded w-full animate-pulse"></div>
                  <div className="h-4 bg-gray-300 rounded w-5/6 mx-auto animate-pulse"></div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>
    );
  }

  const handleItemClick = (item, index) => {
    if (item.onClick) {
      item.onClick(item, index);
    }
    if (item.href) {
      window.location.href = item.href;
    }
  };

  const buttonVariantClasses = {
    primary: 'bg-primary-600 hover:bg-primary-700 text-white',
    secondary: 'bg-gray-600 hover:bg-gray-700 text-white',
    outline: 'border-2 border-gray-300 text-gray-700 hover:bg-gray-50',
  };

  // Mantine Title variant - Special layout with large header
  if (variant === 'mantine-title') {
    return (
      <section
        id={id}
        className={clsx(
          'w-full py-12 sm:py-16 lg:py-20 px-4 sm:px-6 lg:px-8',
          variantClasses[variant],
          className,
          dataClassName
        )}
      >
        <div className="max-w-7xl mx-auto w-full">
          {/* Large Header Section */}
          {(title || description || buttons.length > 0) && (
            <div
              className={clsx(
                'mb-12 sm:mb-16 flex flex-col',
                alignmentClasses[alignment]
              )}
            >
              {title && (
                <h2 className="text-3xl sm:text-4xl md:text-5xl lg:text-6xl font-bold text-gray-900 mb-6 leading-tight max-w-4xl">
                  {title}
                </h2>
              )}

              {description && (
                <p
                  className={clsx(
                    'text-lg sm:text-xl text-gray-600 mb-8 max-w-3xl',
                    alignment === 'center' && 'mx-auto',
                    alignment === 'right' && 'ml-auto',
                    alignment === 'left' && 'mr-auto'
                  )}
                >
                  {description}
                </p>
              )}

              {buttons.length > 0 && (
                <div
                  className={clsx(
                    'flex flex-wrap gap-4',
                    alignment === 'center' && 'justify-center',
                    alignment === 'right' && 'justify-end',
                    alignment === 'left' && 'justify-start'
                  )}
                >
                  {buttons.map((button, index) => {
                    const ButtonComponent = button.href ? 'a' : 'button';

                    return (
                      <ButtonComponent
                        key={index}
                        href={button.href}
                        onClick={button.onClick}
                        className={clsx(
                          'px-6 py-3 sm:px-8 sm:py-4 rounded-lg font-semibold text-base sm:text-lg transition-all duration-300',
                          buttonVariantClasses[button.variant || 'primary'],
                          button.className
                        )}
                      >
                        {button.text}
                      </ButtonComponent>
                    );
                  })}
                </div>
              )}
            </div>
          )}

          {/* Features Grid */}
          <div className={clsx('grid gap-6 sm:gap-8', gridColumnsClasses[columns] || gridColumnsClasses[3])}>
            {items.map((item, index) => {
              const isClickable = item.onClick || item.href;
              const Component = isClickable ? 'button' : 'div';

              return (
                <Component
                  key={index}
                  onClick={() => isClickable && handleItemClick(item, index)}
                  onMouseEnter={() => setHoveredIndex(index)}
                  onMouseLeave={() => setHoveredIndex(null)}
                  className={clsx(
                    getItemClasses(index),
                    'flex flex-col h-full text-left',
                    
                  )}
                >
                  {renderIcon(item, index)}

                  {item.title && (
                    <div className="mb-4">
                      <h3 className={getTitleClasses(index)}>
                        {item.title}
                      </h3>
                      {variant === 'mantine-badge-cards' && (
                        <div className="w-12 h-0.5 bg-primary-600 mt-2" />
                      )}
                    </div>
                  )}

                  {item.description && (
                    <p className={getDescriptionClasses(index)}>
                      {item.description}
                    </p>
                  )}

                  {item.link && !item.onClick && (
                    <a
                      href={item.link}
                      className={clsx(
                        'inline-block mt-4 text-sm font-semibold transition-colors duration-300',
                        'text-primary-600 hover:text-primary-700'
                      )}
                    >
                      {item.linkText || 'Learn more '}
                    </a>
                  )}
                </Component>
              );
            })}
          </div>
        </div>
      </section>
    );
  }

  return (
    <section
      id={id}
      className={clsx(
        'w-full py-12 sm:py-16 lg:py-20 px-4 sm:px-6 lg:px-8',
        variantClasses[variant],
        className,
        dataClassName
      )}
    >
      <div className="max-w-7xl mx-auto w-full">
        {/* Header */}
        {(title || subtitle || description) && (
          <div
            className={clsx(
              'mb-10 sm:mb-12 flex flex-col',
              variant === 'mantine-badge-cards' ? 'text-left items-start' : alignmentClasses[alignment]
            )}
          >
            {subtitle && (
              <p className={clsx(
                variant === 'mantine-badge-cards' 
                  ? 'inline-block px-3 py-1 bg-primary-600 text-white text-xs font-semibold uppercase tracking-wider rounded-full mb-6'
                  : 'text-sm sm:text-base font-semibold text-primary-600 uppercase tracking-wider mb-3'
              )}>
                {subtitle}
              </p>
            )}

            {title && (
              <h2 className={clsx(
                'font-bold text-gray-900 leading-tight',
                variant === 'mantine-badge-cards'
                  ? 'text-3xl sm:text-4xl md:text-5xl mb-6'
                  : 'text-3xl sm:text-4xl md:text-5xl mb-4'
              )}>
                {title}
              </h2>
            )}

            {description && (
              <div className={clsx(
                'relative',
                variant === 'mantine-badge-cards' ? 'w-full flex flex-col items-center' : '',
                alignment === 'center' && variant !== 'mantine-badge-cards' && 'mx-auto',
                alignment === 'right' && variant !== 'mantine-badge-cards' && 'ml-auto',
                alignment === 'left' && variant !== 'mantine-badge-cards' && 'mr-auto'
              )}>
                <p
                  className={clsx(
                    'text-base sm:text-lg text-gray-600',
                    variant === 'mantine-badge-cards' ? 'max-w-3xl text-center mb-4' : 'max-w-2xl text-gray-500'
                  )}
                >
                  {description}
                </p>
                {variant === 'mantine-badge-cards' && (
                  <div className="w-16 h-0.5 bg-primary-600" />
                )}
              </div>
            )}
          </div>
        )}

        {/* Features */}
        {layout === 'grid' && (
          <div className={clsx('grid gap-6 sm:gap-8', gridColumnsClasses[columns] || gridColumnsClasses[3])}>
            {items.map((item, index) => {
              const isClickable = item.onClick || item.href;
              const Component = isClickable ? 'button' : 'div';
              const itemIconAlignment = item.iconAlignment || currentIconAlignment;
              
              // Text alignment based on icon alignment
              const textAlignmentClasses = {
                left: 'text-left items-start',
                center: 'text-center items-center',
                right: 'text-right items-end',
              };
              const textAlignment = textAlignmentClasses[itemIconAlignment] || 'text-left items-start';

              return (
                <Component
                  key={index}
                  onClick={() => isClickable && handleItemClick(item, index)}
                  onMouseEnter={() => setHoveredIndex(index)}
                  onMouseLeave={() => setHoveredIndex(null)}
                  className={clsx(
                    getItemClasses(index),
                    'flex flex-col',
                    variant === 'mantine-icons' || variant === 'mantine-monotone' || variant === 'mantine-image-icons' 
                      ? 'text-center items-center' 
                      : variant.startsWith('mantine-') && itemIconAlignment === 'center' 
                        ? textAlignment 
                        : 'text-left items-start',
                    
                  )}
                >
                  {renderIcon(item, index)}

                  {item.title && (
                    <div className="mb-4">
                      <h3 className={getTitleClasses(index)}>
                        {item.title}
                      </h3>
                      {variant === 'mantine-badge-cards' && (
                        <div className="w-12 h-0.5 bg-primary-600 mt-2" />
                      )}
                    </div>
                  )}

                  {item.description && (
                    <p className={getDescriptionClasses(index)}>
                      {item.description}
                    </p>
                  )}

                  {item.link && !item.onClick && (
                    <a
                      href={item.link}
                      className={clsx(
                        'inline-block mt-4 text-sm font-semibold transition-colors duration-300',
                        itemIconAlignment === 'center' && 'mx-auto',
                        itemIconAlignment === 'right' && 'ml-auto',
                        variant === 'filled' && hoveredIndex === index
                          ? 'text-white'
                          : 'text-primary-600 hover:text-primary-700'
                      )}
                    >
                      {item.linkText || 'Learn more '}
                    </a>
                  )}
                </Component>
              );
            })}
          </div>
        )}

        {layout === 'list' && (
          <div className="space-y-6">
            {items.map((item, index) => {
              const isClickable = item.onClick || item.href;
              const Component = isClickable ? 'button' : 'div';

              return (
                <Component
                  key={index}
                  onClick={() => isClickable && handleItemClick(item, index)}
                  onMouseEnter={() => setHoveredIndex(index)}
                  onMouseLeave={() => setHoveredIndex(null)}
                  className={clsx(
                    getItemClasses(index),
                    'flex flex-col sm:flex-row sm:items-start gap-4 sm:gap-6',
                    
                  )}
                >
                  {showIcons && item.icon && (
                    <div className="flex-shrink-0">
                      {renderIcon(item, index)}
                    </div>
                  )}

                  <div className="flex-1">
                    {item.title && (
                      <div className="mb-4">
                        <h3 className={getTitleClasses(index)}>
                          {item.title}
                        </h3>
                        {variant === 'mantine-badge-cards' && (
                          <div className="w-12 h-0.5 bg-primary-600 mt-2" />
                        )}
                      </div>
                    )}

                    {item.description && (
                      <p className={getDescriptionClasses(index)}>
                        {item.description}
                      </p>
                    )}

                    {item.link && !item.onClick && (
                      <a
                        href={item.link}
                        className={clsx(
                          'inline-block mt-4 text-sm font-semibold transition-colors duration-300',
                          variant === 'filled' && hoveredIndex === index
                            ? 'text-white'
                            : 'text-primary-600 hover:text-primary-700'
                        )}
                      >
                        {item.linkText || 'Learn more '}
                      </a>
                    )}
                  </div>
                </Component>
              );
            })}
          </div>
        )}

        {layout === 'card' && (
          <div className={clsx('grid gap-6 sm:gap-8', gridColumnsClasses[columns] || gridColumnsClasses[3])}>
            {items.map((item, index) => {
              const isClickable = item.onClick || item.href;
              const Component = isClickable ? 'button' : 'div';
              const itemIconAlignment = item.iconAlignment || currentIconAlignment;
              
              // Text alignment based on icon alignment
              const textAlignmentClasses = {
                left: 'text-left items-start',
                center: 'text-center items-center',
                right: 'text-right items-end',
              };
              const textAlignment = textAlignmentClasses[itemIconAlignment] || 'text-left items-start';

              return (
                <Component
                  key={index}
                  onClick={() => isClickable && handleItemClick(item, index)}
                  onMouseEnter={() => setHoveredIndex(index)}
                  onMouseLeave={() => setHoveredIndex(null)}
                  className={clsx(
                    getItemClasses(index),
                    'flex flex-col h-full',
                    itemIconAlignment === 'center' ? textAlignment : 'text-left items-start',
                    
                  )}
                >
                  {renderIcon(item, index)}

                  {item.title && (
                    <div className="flex-1 mb-4">
                      <h3 className={getTitleClasses(index)}>
                        {item.title}
                      </h3>
                      {variant === 'mantine-badge-cards' && (
                        <div className="w-12 h-0.5 bg-primary-600 mt-2" />
                      )}
                    </div>
                  )}

                  {item.description && (
                    <p className={clsx(getDescriptionClasses(index), 'flex-1')}>
                      {item.description}
                    </p>
                  )}

                  {item.link && !item.onClick && (
                    <a
                      href={item.link}
                      className={clsx(
                        'inline-block mt-4 text-sm font-semibold transition-colors duration-300',
                        itemIconAlignment === 'center' && 'mx-auto',
                        itemIconAlignment === 'right' && 'ml-auto',
                        variant === 'filled' && hoveredIndex === index
                          ? 'text-white'
                          : 'text-primary-600 hover:text-primary-700'
                      )}
                    >
                      {item.linkText || 'Learn more '}
                    </a>
                  )}
                </Component>
              );
            })}
          </div>
        )}
      </div>
    </section>
  );
};
