import { useState, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/blog.css';

/**
 * BlogAuthor Component - Author card/profile component for blog posts
 * @param {Object} props - Component props
 * @param {Object} props.data - Author configuration data
 * @param {string} props.data.name - Author name
 * @param {string} [props.data.image] - Author avatar image URL
 * @param {string} [props.data.bio] - Author biography
 * @param {string} [props.data.role] - Author role/position
 * @param {string} [props.data.company] - Author company/organization
 * @param {string} [props.data.email] - Author email
 * @param {string} [props.data.website] - Author website URL
 * @param {Array} [props.data.socialLinks=[]] - Array of social link objects {platform, url}
 * @param {number} [props.data.postCount] - Number of posts by author
 * @param {string} [props.data.variant='default'] - Style variant: 'default' | 'compact' | 'detailed' | 'card' | 'minimal'
 * @param {string} [props.data.theme='light'] - Theme: 'light' | 'dark'
 * @param {string} [props.data.alignment='left'] - Alignment: 'left' | 'center' | 'right'
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute
 * @param {boolean} [props.loader=false] - Show loading state
 */
export const BlogAuthor = ({ data, className, id, loader = false }) => {
  if (!data || typeof data !== 'object') {
    console.error('BlogAuthor: data prop is required and must be an object');
    return null;
  }

  const [showLoader, setShowLoader] = useState(loader);

  useEffect(() => {
    if (loader) {
      const timer = setTimeout(() => {
        setShowLoader(false);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowLoader(false);
    }
  }, [loader]);

  const {
    name,
    image,
    bio,
    role,
    company,
    email,
    website,
    socialLinks = [],
    postCount,
    variant = 'default',
    theme = 'light',
    alignment = 'left',
    className: dataClassName,
  } = data;

  const containerClasses = clsx(
    'blog-author',
    `blog-author-${variant}`,
    `blog-author-${theme}`,
    `blog-author-${alignment}`,
    className,
    dataClassName
  );

  // Loading skeleton
  if (showLoader) {
    return (
      <div className={containerClasses} id={id}>
        <div className="blog-author-skeleton">
          <div className="skeleton-avatar"></div>
          <div className="skeleton-content">
            <div className="skeleton-line skeleton-line-title"></div>
            <div className="skeleton-line"></div>
            <div className="skeleton-line skeleton-line-short"></div>
          </div>
        </div>
      </div>
    );
  }

  // Default variant - Standard author card
  if (variant === 'default') {
    return (
      <div className={containerClasses} id={id}>
        <div className="blog-author-content">
          {image && (
            <img src={image} alt={name} className="blog-author-avatar" />
          )}
          <div className="blog-author-info">
            <h3 className="blog-author-name">{name}</h3>
            {(role || company) && (
              <p className="blog-author-meta">
                {role && <span>{role}</span>}
                {role && company && <span>  </span>}
                {company && <span>{company}</span>}
              </p>
            )}
            {bio && (
              <p className="blog-author-bio">{bio}</p>
            )}
            {(socialLinks.length > 0 || email || website) && (
              <div className="blog-author-links">
                {email && (
                  <a href={`mailto:${email}`} className="blog-author-link" aria-label="Email">
                    <svg fill="none" stroke="currentColor" viewBox="0 0 24 24" className="w-5 h-5">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 8l7.89 5.26a2 2 0 002.22 0L21 8M5 19h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z" />
                    </svg>
                  </a>
                )}
                {website && (
                  <a href={website} target="_blank" rel="noopener noreferrer" className="blog-author-link" aria-label="Website">
                    <svg fill="none" stroke="currentColor" viewBox="0 0 24 24" className="w-5 h-5">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 12a9 9 0 01-9 9m9-9a9 9 0 00-9-9m9 9H3m9 9a9 9 0 01-9-9m9 9c1.657 0 3-4.03 3-9s-1.343-9-3-9m0 18c-1.657 0-3-4.03-3-9s1.343-9 3-9m-9 9a9 9 0 019-9" />
                    </svg>
                  </a>
                )}
                {socialLinks.map((link, index) => (
                  <a
                    key={index}
                    href={link.url}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="blog-author-link"
                    aria-label={link.platform}
                  >
                    {link.platform === 'twitter' && (
                      <svg fill="currentColor" viewBox="0 0 24 24" className="w-5 h-5">
                        <path d="M8.29 20.251c7.547 0 11.675-6.253 11.675-11.675 0-.178 0-.355-.012-.53A8.348 8.348 0 0022 5.92a8.19 8.19 0 01-2.357.646 4.118 4.118 0 001.804-2.27 8.224 8.224 0 01-2.605.996 4.107 4.107 0 00-6.993 3.743 11.65 11.65 0 01-8.457-4.287 4.106 4.106 0 001.27 5.477A4.072 4.072 0 012.8 9.713v.052a4.105 4.105 0 003.292 4.022 4.095 4.095 0 01-1.853.07 4.108 4.108 0 003.834 2.85A8.233 8.233 0 012 18.407a11.616 11.616 0 006.29 1.84" />
                      </svg>
                    )}
                    {link.platform === 'linkedin' && (
                      <svg fill="currentColor" viewBox="0 0 24 24" className="w-5 h-5">
                        <path fillRule="evenodd" d="M19 0h-14c-2.761 0-5 2.239-5 5v14c0 2.761 2.239 5 5 5h14c2.762 0 5-2.239 5-5v-14c0-2.761-2.238-5-5-5zm-11 19h-3v-11h3v11zm-1.5-12.268c-.966 0-1.75-.79-1.75-1.764s.784-1.764 1.75-1.764 1.75.79 1.75 1.764-.783 1.764-1.75 1.764zm13.5 12.268h-3v-5.604c0-3.368-4-3.113-4 0v5.604h-3v-11h3v1.765c1.396-2.586 7-2.777 7 2.476v6.759z" clipRule="evenodd" />
                      </svg>
                    )}
                    {link.platform === 'github' && (
                      <svg fill="currentColor" viewBox="0 0 24 24" className="w-5 h-5">
                        <path fillRule="evenodd" d="M12 2C6.477 2 2 6.484 2 12.017c0 4.425 2.865 8.18 6.839 9.504.5.092.682-.217.682-.483 0-.237-.008-.868-.013-1.703-2.782.605-3.369-1.343-3.369-1.343-.454-1.158-1.11-1.466-1.11-1.466-.908-.62.069-.608.069-.608 1.003.07 1.531 1.032 1.531 1.032.892 1.53 2.341 1.088 2.91.832.092-.647.35-1.088.636-1.338-2.22-.253-4.555-1.113-4.555-4.951 0-1.093.39-1.988 1.029-2.688-.103-.253-.446-1.272.098-2.65 0 0 .84-.27 2.75 1.026A9.564 9.564 0 0112 6.844c.85.004 1.705.115 2.504.337 1.909-1.296 2.747-1.027 2.747-1.027.546 1.379.202 2.398.1 2.651.64.7 1.028 1.595 1.028 2.688 0 3.848-2.339 4.695-4.566 4.943.359.309.678.92.678 1.855 0 1.338-.012 2.419-.012 2.747 0 .268.18.58.688.482A10.019 10.019 0 0022 12.017C22 6.484 17.522 2 12 2z" clipRule="evenodd" />
                      </svg>
                    )}
                    {(!link.platform || !['twitter', 'linkedin', 'github'].includes(link.platform)) && (
                      <svg fill="none" stroke="currentColor" viewBox="0 0 24 24" className="w-5 h-5">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13.828 10.172a4 4 0 00-5.656 0l-4 4a4 4 0 105.656 5.656l1.102-1.101m-.758-4.899a4 4 0 005.656 0l4-4a4 4 0 00-5.656-5.656l-1.1 1.1" />
                      </svg>
                    )}
                  </a>
                ))}
              </div>
            )}
            {postCount !== undefined && (
              <p className="blog-author-posts">{postCount} {postCount === 1 ? 'post' : 'posts'}</p>
            )}
          </div>
        </div>
      </div>
    );
  }

  // Compact variant - Smaller, inline style
  if (variant === 'compact') {
    return (
      <div className={containerClasses} id={id}>
        <div className="blog-author-compact">
          {image && (
            <img src={image} alt={name} className="blog-author-avatar-compact" />
          )}
          <div>
            <h3 className="blog-author-name-compact">{name}</h3>
            {bio && (
              <p className="blog-author-bio-compact">{bio}</p>
            )}
          </div>
        </div>
      </div>
    );
  }

  // Detailed variant - More information with card style
  if (variant === 'detailed') {
    return (
      <div className={containerClasses} id={id}>
        <div className="blog-author-detailed">
          {image && (
            <img src={image} alt={name} className="blog-author-avatar-detailed" />
          )}
          <div className="blog-author-info-detailed">
            <h3 className="blog-author-name-detailed">{name}</h3>
            {(role || company) && (
              <p className="blog-author-meta-detailed">
                {role && <span>{role}</span>}
                {role && company && <span> at {company}</span>}
                {!role && company && <span>{company}</span>}
              </p>
            )}
            {bio && (
              <p className="blog-author-bio-detailed">{bio}</p>
            )}
            {(socialLinks.length > 0 || email || website) && (
              <div className="blog-author-links-detailed">
                {email && (
                  <a href={`mailto:${email}`} className="blog-author-link-detailed">
                    <svg fill="none" stroke="currentColor" viewBox="0 0 24 24" className="w-4 h-4">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 8l7.89 5.26a2 2 0 002.22 0L21 8M5 19h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z" />
                    </svg>
                    Email
                  </a>
                )}
                {website && (
                  <a href={website} target="_blank" rel="noopener noreferrer" className="blog-author-link-detailed">
                    <svg fill="none" stroke="currentColor" viewBox="0 0 24 24" className="w-4 h-4">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 12a9 9 0 01-9 9m9-9a9 9 0 00-9-9m9 9H3m9 9a9 9 0 01-9-9m9 9c1.657 0 3-4.03 3-9s-1.343-9-3-9m0 18c-1.657 0-3-4.03-3-9s1.343-9 3-9m-9 9a9 9 0 019-9" />
                    </svg>
                    Website
                  </a>
                )}
                {socialLinks.map((link, index) => (
                  <a
                    key={index}
                    href={link.url}
                    target="_blank"
                    rel="noopener noreferrer"
                    className="blog-author-link-detailed"
                  >
                    {link.platform}
                  </a>
                ))}
              </div>
            )}
          </div>
        </div>
      </div>
    );
  }

  // Card variant - Card-style with border
  if (variant === 'card') {
    return (
      <div className={containerClasses} id={id}>
        <div className="blog-author-card">
          <div className="blog-author-card-header">
            {image && (
              <img src={image} alt={name} className="blog-author-avatar-card" />
            )}
            <div>
              <h3 className="blog-author-name-card">{name}</h3>
              {(role || company) && (
                <p className="blog-author-meta-card">
                  {role && <span>{role}</span>}
                  {role && company && <span>  </span>}
                  {company && <span>{company}</span>}
                </p>
              )}
            </div>
          </div>
          {bio && (
            <p className="blog-author-bio-card">{bio}</p>
          )}
          {(socialLinks.length > 0 || email || website) && (
            <div className="blog-author-links-card">
              {email && (
                <a href={`mailto:${email}`} className="blog-author-link-card" aria-label="Email">
                  <svg fill="none" stroke="currentColor" viewBox="0 0 24 24" className="w-5 h-5">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 8l7.89 5.26a2 2 0 002.22 0L21 8M5 19h14a2 2 0 002-2V7a2 2 0 00-2-2H5a2 2 0 00-2 2v10a2 2 0 002 2z" />
                  </svg>
                </a>
              )}
              {website && (
                <a href={website} target="_blank" rel="noopener noreferrer" className="blog-author-link-card" aria-label="Website">
                  <svg fill="none" stroke="currentColor" viewBox="0 0 24 24" className="w-5 h-5">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 12a9 9 0 01-9 9m9-9a9 9 0 00-9-9m9 9H3m9 9a9 9 0 01-9-9m9 9c1.657 0 3-4.03 3-9s-1.343-9-3-9m0 18c-1.657 0-3-4.03-3-9s1.343-9 3-9m-9 9a9 9 0 019-9" />
                  </svg>
                </a>
              )}
              {socialLinks.map((link, index) => (
                <a
                  key={index}
                  href={link.url}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="blog-author-link-card"
                  aria-label={link.platform}
                >
                  <svg fill="none" stroke="currentColor" viewBox="0 0 24 24" className="w-5 h-5">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13.828 10.172a4 4 0 00-5.656 0l-4 4a4 4 0 105.656 5.656l1.102-1.101m-.758-4.899a4 4 0 005.656 0l4-4a4 4 0 00-5.656-5.656l-1.1 1.1" />
                  </svg>
                </a>
              ))}
            </div>
          )}
        </div>
      </div>
    );
  }

  // Minimal variant - Very simple layout
  if (variant === 'minimal') {
    return (
      <div className={containerClasses} id={id}>
        <div className="blog-author-minimal">
          {image && (
            <img src={image} alt={name} className="blog-author-avatar-minimal" />
          )}
          <div>
            <h3 className="blog-author-name-minimal">{name}</h3>
            {bio && (
              <p className="blog-author-bio-minimal">{bio}</p>
            )}
          </div>
        </div>
      </div>
    );
  }

  return null;
};