import { useEffect, useState } from 'react';
import clsx from 'clsx';
import '../../styles/product.css';

/**
 * ProductBreadcrumb Component - Breadcrumb navigation with multiple variants
 * @param {Object} props - Component props
 * @param {Object} props.data - Breadcrumb configuration data
 * @param {string} [props.data.variant='default'] - Style variant: 'default' | 'modern' | 'minimal' | 'compact' | 'icon' | 'dark' | 'dark-gray' | 'icon-dark'
 * @param {Array} [props.data.items=[]] - Breadcrumb items array
 * @param {Function} [props.onItemClick] - Callback when item is clicked
 * @param {boolean} [props.loading=false] - Show skeleton loader
 * @param {string} [props.className] - Additional CSS classes
 */
export const ProductBreadcrumb = ({ 
  data = {}, 
  onItemClick,
  loading = false,
  className 
}) => {
  const {
    variant = 'default',
    items = [
      { label: 'Home', href: '/' },
      { label: 'Products', href: '/products' },
      { label: 'Current Page', href: '#', active: true },
    ],
  } = data;

  const [showContent, setShowContent] = useState(!loading);

  useEffect(() => {
    if (loading) {
      setShowContent(false);
      const timer = setTimeout(() => {
        setShowContent(true);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowContent(true);
    }
  }, [loading]);

  const containerClasses = {
    default: 'flex items-center gap-1 sm:gap-2 text-sm',
    modern: 'flex items-center gap-2 sm:gap-3 text-sm sm:text-base',
    minimal: 'flex items-center gap-1 text-xs sm:text-sm',
    compact: 'flex items-center gap-0.5 sm:gap-1 text-xs',
    icon: 'flex items-center gap-1 sm:gap-2 text-sm',
    dark: 'flex items-center gap-1 sm:gap-2 text-sm',
    'dark-gray': 'flex items-center gap-1 sm:gap-2 text-sm',
    'icon-dark': 'flex items-center gap-1 sm:gap-2 text-sm',
  };

  const itemClasses = {
    default: clsx(
      'text-gray-600 hover:text-gray-900 transition-colors',
      'active:text-gray-900 font-medium'
    ),
    modern: clsx(
      'text-gray-600 hover:text-primary-600 transition-colors font-medium',
      'active:text-gray-900'
    ),
    minimal: clsx(
      'text-gray-500 hover:text-gray-700 transition-colors',
      'active:text-gray-900'
    ),
    compact: clsx(
      'text-gray-500 hover:text-gray-700 transition-colors text-xs',
      'active:text-gray-900'
    ),
    icon: clsx(
      'text-gray-600 hover:text-primary-600 transition-colors',
      'active:text-gray-900'
    ),
    dark: clsx(
      'text-gray-400 hover:text-gray-200 transition-colors',
      'active:text-white font-medium'
    ),
    'dark-gray': clsx(
      'text-gray-500 hover:text-gray-300 transition-colors',
      'active:text-gray-100'
    ),
    'icon-dark': clsx(
      'text-gray-400 hover:text-gray-200 transition-colors',
      'active:text-white font-medium'
    ),
  };

  const SkeletonLoader = () => {
    const isDark = variant === 'dark' || variant === 'dark-gray' || variant === 'icon-dark';
    const skeletonBg = isDark ? 'bg-gray-700' : 'bg-gray-200';

    return (
      <div className={clsx(containerClasses[variant] || containerClasses.default, className)}>
        {Array.from({ length: 3 }).map((_, i) => (
          <div key={i} className="flex items-center gap-2">
            <div className={clsx('h-4 w-16 sm:w-20 rounded product-skeleton', skeletonBg)}></div>
            {i < 2 && <div className={clsx('h-4 w-4 rounded product-skeleton', skeletonBg)}></div>}
          </div>
        ))}
      </div>
    );
  };

  if (!showContent) {
    return <SkeletonLoader />;
  }

  const ChevronIcon = () => (
    <svg
      className={clsx(
        "w-4 h-4",
        (variant === 'dark' || variant === 'dark-gray' || variant === 'icon-dark') && "text-gray-500"
      )}
      fill="none"
      stroke="currentColor"
      viewBox="0 0 24 24"
    >
      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
    </svg>
  );

  const HomeIcon = () => (
    <svg
      className={clsx(
        "w-4 h-4",
        (variant === 'dark' || variant === 'dark-gray' || variant === 'icon-dark') && "text-gray-500"
      )}
      fill="none"
      stroke="currentColor"
      viewBox="0 0 24 24"
    >
      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 12l2-2m0 0l7-7 7 7M5 10v10a1 1 0 001 1h3m10-11l2 2m-2-2v10a1 1 0 01-1 1h-3m-6 0a1 1 0 001-1v-4a1 1 0 011-1h2a1 1 0 011 1v4a1 1 0 001 1m-6 0h6" />
    </svg>
  );

  return (
    <nav className={clsx(containerClasses[variant] || containerClasses.default, className)} aria-label="Breadcrumb">
      {items.map((item, index) => {
        const isLast = index === items.length - 1;
        const isActive = item.active || isLast;

        return (
          <div key={index} className="flex items-center gap-1 sm:gap-2">
            {index === 0 && (variant === 'icon' || variant === 'icon-dark') && (
              <HomeIcon />
            )}
            {item.href && !isActive ? (
              <a
                href={item.href}
                onClick={(e) => {
                  if (onItemClick) {
                    e.preventDefault();
                    onItemClick(item, index);
                  }
                }}
                className={clsx(itemClasses[variant] || itemClasses.default)}
              >
                {item.label}
              </a>
            ) : (
              <span className={clsx(
                itemClasses[variant] || itemClasses.default,
                isActive && 'font-semibold'
              )}>
                {item.label}
              </span>
            )}
            {!isLast && (
              <ChevronIcon />
            )}
          </div>
        );
      })}
    </nav>
  );
};

