import { useState, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/price.css';

/**
 * PriceModern Component - Unique modern pricing section with creative layouts
 * @param {Object} props - Component props
 * @param {Object} props.data - Price section configuration data
 * @param {string} [props.data.title] - Main heading text
 * @param {string} [props.data.subtitle] - Subtitle text displayed above title
 * @param {string} [props.data.description] - Description text displayed below title
 * @param {Array} props.data.plans - Array of pricing plans
 * @param {string} [props.data.alignment='center'] - Header text alignment: 'left' | 'center' | 'right'
 * @param {number} [props.data.columns=3] - Number of columns for grid layout (1-4)
 * @param {string} [props.data.currency='$'] - Currency symbol
 * @param {string} [props.data.billingPeriod='month'] - Billing period: 'month' | 'year' | 'week' | 'day'
 * @param {boolean} [props.data.showPopular=true] - Show popular badge on featured plan
 * @param {number} [props.data.featuredPlanIndex] - Index of the featured/popular plan
 * @param {string} [props.data.theme='light'] - Theme variant: 'light' | 'dark'
 * @param {Object} [props.data.plans[].button] - Button configuration object
 * @param {string} [props.data.plans[].button.text] - Button text (defaults to 'Get Started')
 * @param {string} [props.data.plans[].button.href] - Button link URL
 * @param {Function} [props.data.plans[].button.onClick] - Button click handler
 * @param {string} [props.data.className] - Additional CSS classes for the section
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute for the section element
 * @param {boolean} [props.loader=false] - Show loading state
 */
export const PriceModern = ({ data, className, id, loader = false }) => {
  // Safety check for data
  if (!data || typeof data !== 'object') {
    console.error('PriceModern: data prop is required and must be an object');
    return null;
  }

  const [showLoader, setShowLoader] = useState(loader);

  // Auto-hide loader after 2 seconds when loader prop is true
  useEffect(() => {
    if (loader) {
      const timer = setTimeout(() => {
        setShowLoader(false);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowLoader(false);
    }
  }, [loader]);

  const {
    title,
    subtitle,
    description,
    plans = [],
    alignment = 'center',
    columns = 3,
    currency = '$',
    billingPeriod = 'month',
    showPopular = true,
    featuredPlanIndex,
    theme = 'light',
    className: dataClassName,
  } = data;

  const isDark = theme === 'dark';

  // Show skeleton loader if loader prop is true
  if (showLoader) {
    const alignmentClasses = {
      left: 'text-left items-start',
      center: 'text-center items-center',
      right: 'text-right items-end',
    };

    const gridColumnsClasses = {
      1: 'grid-cols-1',
      2: 'grid-cols-1 md:grid-cols-2',
      3: 'grid-cols-1 md:grid-cols-2 lg:grid-cols-3',
      4: 'grid-cols-1 md:grid-cols-2 lg:grid-cols-4',
    };

    const effectiveAlignment = alignment || 'center';

    return (
      <section
        id={id}
        className={clsx(
          'w-full py-12 sm:py-16 lg:py-20 px-4 sm:px-6 lg:px-8',
          isDark ? 'bg-gray-900' : 'bg-white',
          className,
          dataClassName
        )}
      >
        <div className="max-w-7xl mx-auto w-full">
          {/* Header skeleton */}
          <div className={clsx('mb-10 sm:mb-12 flex flex-col', alignmentClasses[effectiveAlignment])}>
            <div className={clsx('h-4 rounded w-24 mb-3 mx-auto animate-pulse', isDark ? 'bg-gray-700' : 'bg-gray-400')}></div>
            <div className={clsx('h-10 rounded w-full max-w-2xl mb-4 mx-auto animate-pulse', isDark ? 'bg-gray-700' : 'bg-gray-400')}></div>
            <div className={clsx('h-5 rounded w-full max-w-xl mb-6 mx-auto animate-pulse', isDark ? 'bg-gray-800' : 'bg-gray-300')}></div>
          </div>

          {/* Pricing cards skeleton */}
          <div className={clsx('grid gap-6', gridColumnsClasses[columns])}>
            {[0, 1, 2].map((index) => (
              <div key={index} className={clsx(
                'border rounded-lg p-6',
                isDark ? 'bg-gray-800 border-gray-700' : 'bg-white border-gray-200'
              )}>
                {/* Plan name skeleton */}
                <div className={clsx('h-6 rounded w-32 mb-4 animate-pulse', isDark ? 'bg-gray-700' : 'bg-gray-400')}></div>
                
                {/* Price skeleton */}
                <div className="mb-4">
                  <div className={clsx('h-12 rounded w-24 mb-2 animate-pulse', isDark ? 'bg-gray-700' : 'bg-gray-400')}></div>
                  <div className={clsx('h-4 rounded w-20 animate-pulse', isDark ? 'bg-gray-800' : 'bg-gray-300')}></div>
                </div>

                {/* Features skeleton */}
                <div className="space-y-3 mb-6">
                  {[0, 1, 2, 3].map((featureIndex) => (
                    <div key={featureIndex} className="flex items-center gap-2">
                      <div className={clsx('h-4 w-4 rounded animate-pulse', isDark ? 'bg-gray-700' : 'bg-gray-300')}></div>
                      <div className={clsx('h-4 rounded flex-1 animate-pulse', isDark ? 'bg-gray-800' : 'bg-gray-300')}></div>
                    </div>
                  ))}
                </div>

                {/* Button skeleton */}
                <div className={clsx('h-12 rounded-lg w-full animate-pulse', isDark ? 'bg-gray-700' : 'bg-gray-400')}></div>
              </div>
            ))}
          </div>
        </div>
      </section>
    );
  }

  const alignmentClasses = {
    left: 'text-left items-start',
    center: 'text-center items-center',
    right: 'text-right items-end',
  };

  const gridColumnsClasses = {
    1: 'grid-cols-1',
    2: 'grid-cols-1 sm:grid-cols-2',
    3: 'grid-cols-1 sm:grid-cols-2 lg:grid-cols-3',
    4: 'grid-cols-1 sm:grid-cols-2 lg:grid-cols-4',
  };

  if (plans.length === 0) {
    console.warn('PriceModern: plans array is empty');
    return null;
  }

  // Color schemes for each card with proper Tailwind classes
  const getColorScheme = (index) => {
    const schemes = [
      {
        textDark: 'text-emerald-600',
        bgLight: 'bg-emerald-50',
        bgDark: 'bg-emerald-600',
        borderClass: 'border-emerald-300',
        borderHover: 'hover:border-emerald-400',
        bgGradient: 'bg-gradient-to-br from-emerald-500 to-emerald-600',
        borderLight: 'from-emerald-200',
        borderMid: 'via-emerald-400',
      },
      {
        textDark: 'text-blue-600',
        bgLight: 'bg-blue-50',
        bgDark: 'bg-blue-600',
        borderClass: 'border-blue-300',
        borderHover: 'hover:border-blue-400',
        bgGradient: 'bg-gradient-to-br from-blue-500 to-blue-600',
        borderLight: 'from-blue-200',
        borderMid: 'via-blue-400',
      },
      {
        textDark: 'text-purple-600',
        bgLight: 'bg-purple-50',
        bgDark: 'bg-purple-600',
        borderClass: 'border-purple-300',
        borderHover: 'hover:border-purple-400',
        bgGradient: 'bg-gradient-to-br from-purple-500 to-purple-600',
        borderLight: 'from-purple-200',
        borderMid: 'via-purple-400',
      },
      {
        textDark: 'text-orange-600',
        bgLight: 'bg-orange-50',
        bgDark: 'bg-orange-600',
        borderClass: 'border-orange-300',
        borderHover: 'hover:border-orange-400',
        bgGradient: 'bg-gradient-to-br from-orange-500 to-orange-600',
        borderLight: 'from-orange-200',
        borderMid: 'via-orange-400',
      },
    ];
    return schemes[index % schemes.length];
  };

  return (
    <section
      id={id}
      className={clsx(
        'w-full py-20 sm:py-24 lg:py-32 px-4 sm:px-6 lg:px-8',
        'relative',
        isDark ? 'bg-gray-950' : 'bg-white',
        className,
        dataClassName
      )}
    >
      {/* Subtle grid pattern background */}
      <div className={clsx(
        'absolute inset-0 bg-[linear-gradient(to_right,#80808012_1px,transparent_1px),linear-gradient(to_bottom,#80808012_1px,transparent_1px)] bg-[size:24px_24px]',
        isDark ? 'opacity-20' : 'opacity-40'
      )} />

      <div className="max-w-7xl mx-auto w-full relative z-10">
        {/* Header */}
        {(title || subtitle || description) && (
          <div
            className={clsx(
              'mb-20 sm:mb-24 flex flex-col',
              alignmentClasses[alignment]
            )}
          >
            {subtitle && (
              <p className={clsx(
                'text-xs sm:text-sm font-mono uppercase tracking-[0.2em] mb-6',
                isDark ? 'text-gray-400' : 'text-gray-500'
              )}>
                {subtitle}
              </p>
            )}

            {title && (
              <h2 className={clsx(
                'text-5xl sm:text-6xl md:text-7xl font-light mb-8 leading-[1.1] tracking-tight',
                isDark ? 'text-white' : 'text-gray-900'
              )}>
                {title.split(' ').map((word, i) => (
                  <span key={i} className={i % 2 === 0 ? 'font-normal' : clsx('font-light', isDark ? 'text-gray-400' : 'text-gray-400')}>
                    {word}{' '}
                  </span>
                ))}
              </h2>
            )}

            {description && (
              <p
                className={clsx(
                  'text-lg sm:text-xl max-w-2xl font-light leading-relaxed',
                  isDark ? 'text-gray-300' : 'text-gray-500',
                  alignment === 'center' && 'mx-auto',
                  alignment === 'right' && 'ml-auto',
                  alignment === 'left' && 'mr-auto'
                )}
              >
                {description}
              </p>
            )}
          </div>
        )}

        {/* Pricing Plans Grid */}
        <div className={clsx('grid gap-8 sm:gap-12', gridColumnsClasses[columns] || gridColumnsClasses[3])}>
          {plans.map((plan, index) => {
            const isFeatured = featuredPlanIndex === index || plan.featured || plan.popular;
            const colors = getColorScheme(index);
            const rotation = index % 2 === 0 ? 'rotate-[-1deg]' : 'rotate-[1deg]';
            
            return (
              <div
                key={index}
                className={clsx(
                  'relative group',
                  isFeatured && 'lg:scale-105'
                )}
              >
                {/* Popular Badge - Outside card to avoid clipping */}
                {isFeatured && showPopular && (
                  <div className={clsx(
                    'absolute -top-3 -right-3 z-50',
                    'px-5 py-2 rounded-md',
                    isDark ? 'bg-white text-gray-900' : clsx(colors.bgDark, 'text-white'),
                    'shadow-2xl transform rotate-12',
                    'font-bold text-xs sm:text-xs uppercase tracking-widest',
                    'whitespace-nowrap',
                    isDark ? 'border-2 border-gray-800' : 'border-2 border-white'
                  )}>
                    {plan.badgeText || 'Popular'}
                  </div>
                )}

                {/* Card */}
                <div
                  className={clsx(
                    'relative h-full flex flex-col',
                    'transition-all duration-500',
                    'backdrop-blur-xl',
                    isDark
                      ? clsx(
                          'bg-gray-900/40 border border-gray-800/50',
                          'backdrop-blur-xl',
                          isFeatured 
                            ? 'shadow-2xl border-gray-700/50 bg-gray-900/60' 
                            : 'shadow-md hover:shadow-2xl hover:bg-gray-900/50'
                        )
                      : clsx(
                          'bg-white border border-gray-200',
                          isFeatured 
                            ? 'shadow-2xl border-gray-300' 
                            : 'shadow-md hover:shadow-2xl'
                        ),
                    'hover:-translate-y-2',
                    !isFeatured && rotation,
                    'hover:rotate-0'
                  )}
                  style={{
                    clipPath: isFeatured 
                      ? 'polygon(0 0, 100% 0, 100% calc(100% - 20px), calc(100% - 20px) 100%, 0 100%)'
                      : 'polygon(20px 0, 100% 0, 100% 100%, 0 100%, 0 20px)'
                  }}
                >
                  {/* Accent corner */}
                  {!isDark && (
                    <div 
                      className={clsx(
                        'absolute top-0 left-0 w-20 h-20',
                        colors.bgGradient,
                        isFeatured && 'w-24 h-24'
                      )}
                      style={{
                        clipPath: 'polygon(0 0, 100% 0, 0 100%)'
                      }}
                    />
                  )}

                  {/* Content */}
                  <div className="relative z-10 p-8 sm:p-10 flex flex-col h-full">
                    {/* Plan Name */}
                    {plan.name && (
                      <div className="mb-6">
                        <h3 className={clsx(
                          'text-3xl sm:text-4xl font-bold mb-2',
                          isDark ? 'text-white' : colors.textDark
                        )}>
                          {plan.name}
                        </h3>
                        {plan.description && (
                          <p className={clsx(
                            'text-sm font-light',
                            isDark ? 'text-gray-400' : 'text-gray-500'
                          )}>
                            {plan.description}
                          </p>
                        )}
                      </div>
                    )}

                    {/* Price - Unique Layout */}
                    <div className="mb-8">
                      <div className="flex items-baseline gap-2 mb-2">
                        <span className={clsx(
                          'text-5xl sm:text-6xl font-black',
                          isDark ? 'text-white' : colors.textDark
                        )}>
                          {currency}{plan.price}
                        </span>
                        <span className={clsx(
                          'text-xl font-light',
                          isDark ? 'text-gray-400' : 'text-gray-400'
                        )}>
                          /{billingPeriod}
                        </span>
                      </div>
                      {plan.priceNote && (
                        <p className={clsx(
                          'text-xs font-light',
                          isDark ? 'text-gray-500' : 'text-gray-400'
                        )}>
                          {plan.priceNote}
                        </p>
                      )}
                    </div>

                    {/* Divider */}
                    <div className={clsx(
                      'h-px mb-8 bg-gradient-to-r',
                      isDark 
                        ? 'from-gray-700 via-gray-600 to-transparent'
                        : clsx(colors.borderLight, colors.borderMid, 'to-transparent')
                    )} />

                    {/* Features List */}
                    {plan.features && plan.features.length > 0 && (
                      <ul className="space-y-4 mb-10 flex-1">
                        {plan.features.map((feature, fIndex) => {
                          const featureText = typeof feature === 'string' ? feature : feature.text;
                          const featureIncluded = typeof feature === 'string' ? true : (feature.included !== false);
                          
                          return (
                            <li 
                              key={fIndex} 
                              className="flex items-start gap-3 group/item"
                            >
                              <div className={clsx(
                                'flex-shrink-0 w-5 h-5 rounded-full flex items-center justify-center mt-0.5 transition-all duration-300',
                                featureIncluded
                                  ? isDark 
                                    ? 'bg-gray-800 group-hover/item:scale-125' 
                                    : `${colors.bgLight} group-hover/item:scale-125`
                                  : isDark ? 'bg-gray-800' : 'bg-gray-100'
                              )}>
                                {featureIncluded ? (
                                  <svg
                                    className={clsx(
                                      'w-3 h-3',
                                      isDark ? 'text-gray-300' : colors.textDark
                                    )}
                                    fill="none"
                                    stroke="currentColor"
                                    viewBox="0 0 24 24"
                                  >
                                    <path
                                      strokeLinecap="round"
                                      strokeLinejoin="round"
                                      strokeWidth={3}
                                      d="M5 13l4 4L19 7"
                                    />
                                  </svg>
                                ) : (
                                  <div className={clsx(
                                    'w-1.5 h-1.5 rounded-full',
                                    isDark ? 'bg-gray-600' : 'bg-gray-300'
                                  )} />
                                )}
                              </div>
                              <span className={clsx(
                                'text-sm font-light leading-relaxed flex-1',
                                isDark ? 'text-gray-300' : 'text-gray-700'
                              )}>
                                {featureText}
                              </span>
                            </li>
                          );
                        })}
                      </ul>
                    )}

                    {/* CTA Button */}
                    {plan.button && (
                      <a
                        href={plan.button.href || '#'}
                        onClick={plan.button.onClick}
                        className={clsx(
                          'relative block w-full px-6 py-4 text-center font-semibold text-sm',
                          'transition-all duration-500 overflow-hidden group/btn',
                          'border-2 rounded-lg',
                          isDark
                            ? isFeatured
                              ? 'bg-white text-gray-900 hover:bg-gray-100 border-white'
                              : 'bg-gray-900 text-white hover:bg-gray-800 border-gray-700'
                            : isFeatured
                              ? clsx(colors.bgDark, 'text-white hover:bg-transparent hover:text-current')
                              : clsx(colors.borderClass, colors.textDark, colors.bgLight, colors.borderHover, 'hover:bg-opacity-80'),
                          'before:absolute before:inset-0 before:bg-gradient-to-r before:from-transparent before:via-white/20 before:to-transparent',
                          'before:translate-x-[-100%] group-hover/btn:before:translate-x-[100%] before:transition-transform before:duration-1000',
                          plan.button.className
                        )}
                      >
                        <span className="relative z-10 flex items-center justify-center gap-2">
                          {plan.button.text || 'Get Started'}
                          <svg 
                            className={clsx(
                              'w-4 h-4 transition-transform duration-300 group-hover/btn:translate-x-1',
                              isDark
                                ? isFeatured ? 'text-gray-900' : 'text-white'
                                : isFeatured ? 'text-white group-hover/btn:text-current' : colors.textDark
                            )}
                            fill="none"
                            stroke="currentColor"
                            viewBox="0 0 24 24"
                          >
                            <path
                              strokeLinecap="round"
                              strokeLinejoin="round"
                              strokeWidth={2}
                              d="M17 8l4 4m0 0l-4 4m4-4H3"
                            />
                          </svg>
                        </span>
                      </a>
                    )}
                  </div>

                  {/* Hover effect overlay */}
                  {!isDark && (
                    <div className={clsx(
                      'absolute inset-0 opacity-0 group-hover:opacity-100 transition-opacity duration-500 pointer-events-none',
                      colors.bgLight,
                      'bg-opacity-50'
                    )} />
                  )}
                </div>

                {/* Decorative element behind card */}
                {!isDark && (
                  <div 
                    className={clsx(
                      'absolute inset-0 -z-10 transition-all duration-500',
                      colors.bgLight,
                      'opacity-0 group-hover:opacity-100 blur-xl',
                      isFeatured ? 'opacity-30' : ''
                    )}
                  />
                )}
              </div>
            );
          })}
        </div>
      </div>
    </section>
  );
};
