import { useState, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/product.css';

/**
 * ProductModern Component - Modern product cards with unique latest design variants
 * @param {Object} props - Component props
 * @param {Object} props.data - Product section configuration data
 * @param {string} [props.data.title] - Main heading text
 * @param {string} [props.data.subtitle] - Subtitle text displayed above title
 * @param {string} [props.data.description] - Description text displayed below title
 * @param {Array} props.data.products - Array of product items
 * @param {string} [props.data.variant='glassmorphism'] - Style variant: 'glassmorphism' | 'minimal' | 'gradient'
 * @param {string} [props.data.alignment='center'] - Header text alignment: 'left' | 'center' | 'right'
 * @param {number} [props.data.columns=3] - Number of columns for grid layout (1-4)
 * @param {string} [props.data.currency='$'] - Currency symbol
 * @param {string} [props.className] - Additional CSS classes
 * @param {string} [props.id] - ID attribute for the section element
 * @param {boolean} [props.loader=false] - Show loading state
 */

// Glassmorphism Variant - Modern frosted glass effect with hover overlay
const GlassmorphismProductCard = ({ product, currency }) => {
  const images = Array.isArray(product.images) 
    ? product.images 
    : product.image 
      ? [product.image]
      : [];

  const buttonConfig = product.button || {};
  const buttonText = buttonConfig.text || 'ADD TO CART';
  const buttonHref = buttonConfig.href || '#';
  const buttonOnClick = product.button?.onClick;

  const [isHovered, setIsHovered] = useState(false);

  const formatPrice = (price) => {
    if (typeof price === 'number' && price % 1 === 0) {
      return price;
    } else if (typeof price === 'number') {
      return price.toFixed(2);
    }
    return price;
  };

  return (
    <div 
      className="relative group rounded-3xl overflow-hidden"
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
    >
      {/* Image Container with Overlay */}
      <div className="relative h-56 sm:h-64 md:h-72 lg:h-80 overflow-hidden bg-gradient-to-br from-gray-100 to-gray-200">
        {images.length > 0 && images[0] ? (
          <img
            src={images[0]}
            alt={product.title || 'Product'}
            className={`w-full h-full object-cover transition-transform duration-700 ${
              isHovered ? 'scale-110' : 'scale-100'
            }`}
            loading="lazy"
          />
        ) : (
          <div className="w-full h-full flex items-center justify-center bg-gray-200">
            <svg className="w-16 h-16 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z" />
            </svg>
          </div>
        )}

        {/* Glassmorphism Overlay on Hover */}
        <div className={`absolute inset-0 bg-white/20 backdrop-blur-md transition-opacity duration-500 ${
          isHovered ? 'opacity-100' : 'opacity-0'
        }`}>
          <div className="absolute inset-0 flex items-center justify-center">
            {buttonOnClick ? (
              <button
                onClick={buttonOnClick}
                className="bg-white/90 backdrop-blur-sm text-gray-900 px-6 py-3 rounded-full font-semibold text-sm shadow-lg hover:bg-white transition-all duration-300 transform hover:scale-105"
              >
                {buttonText}
              </button>
            ) : (
              <a
                href={buttonHref}
                className="bg-white/90 backdrop-blur-sm text-gray-900 px-6 py-3 rounded-full font-semibold text-sm shadow-lg hover:bg-white transition-all duration-300 transform hover:scale-105"
              >
                {buttonText}
              </a>
            )}
          </div>
        </div>

        {/* Badge */}
        {product.badge && (
          <div className="absolute top-4 left-4 z-10">
            <span className="bg-white/90 backdrop-blur-sm text-gray-900 text-xs font-bold px-3 py-1.5 rounded-full shadow-md">
              {product.badge}
            </span>
          </div>
        )}

        {/* Favorite Icon */}
        <button className="absolute top-4 right-4 z-10 w-10 h-10 rounded-full bg-white/90 backdrop-blur-sm flex items-center justify-center shadow-md hover:bg-white transition-colors">
          <svg className="w-5 h-5 text-gray-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4.318 6.318a4.5 4.5 0 000 6.364L12 20.364l7.682-7.682a4.5 4.5 0 00-6.364-6.364L12 7.636l-1.318-1.318a4.5 4.5 0 00-6.364 0z" />
          </svg>
        </button>
      </div>

      {/* Glassmorphism Content Card */}
      <div className="relative -mt-8 sm:-mt-10 md:-mt-12 mx-3 sm:mx-4 bg-white/80 backdrop-blur-xl rounded-2xl shadow-xl p-4 sm:p-5 border border-white/50">
        {product.title && (
          <h3 className="text-gray-900 font-bold text-base sm:text-lg md:text-xl mb-1 sm:mb-2">{product.title}</h3>
        )}
        {product.category && (
          <p className="text-gray-500 text-xs sm:text-sm mb-2 sm:mb-3">{product.category}</p>
        )}
        {product.price !== undefined && (
          <div className="flex items-center justify-between">
            <span className="text-gray-900 font-bold text-lg sm:text-xl md:text-2xl">
              {currency}{formatPrice(product.price)}
            </span>
            {product.rating && (
              <div className="flex items-center gap-1">
                <span className="text-yellow-400"></span>
                <span className="text-gray-600 text-sm">{product.rating}</span>
              </div>
            )}
          </div>
        )}
      </div>
    </div>
  );
};

// Minimal Variant - Clean, spacious design with large image and minimal text
const MinimalProductCard = ({ product, currency }) => {
  const images = Array.isArray(product.images) 
    ? product.images 
    : product.image 
      ? [product.image]
      : [];

  const buttonConfig = product.button || {};
  const buttonText = buttonConfig.text || 'SHOP NOW';
  const buttonHref = buttonConfig.href || '#';
  const buttonOnClick = product.button?.onClick;

  const formatPrice = (price) => {
    if (typeof price === 'number' && price % 1 === 0) {
      return price;
    } else if (typeof price === 'number') {
      return price.toFixed(2);
    }
    return price;
  };

  return (
    <div className="relative bg-white rounded-2xl overflow-hidden shadow-md hover:shadow-2xl transition-shadow duration-300 group">
      {/* Large Image Section */}
      <div className="relative bg-gray-50 aspect-square overflow-hidden">
        {images.length > 0 && images[0] ? (
          <img
            src={images[0]}
            alt={product.title || 'Product'}
            className="w-full h-full object-contain p-8 group-hover:scale-105 transition-transform duration-500"
            loading="lazy"
          />
        ) : (
          <div className="w-full h-full flex items-center justify-center">
            <svg className="w-20 h-20 text-gray-300" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z" />
            </svg>
          </div>
        )}

        {/* Badge */}
        {product.badge && (
          <div className="absolute top-4 left-4">
            <span className="bg-black text-white text-xs font-semibold px-3 py-1.5 rounded">
              {product.badge}
            </span>
          </div>
        )}
      </div>

      {/* Minimal Content Section */}
      <div className="p-4 sm:p-5 md:p-6 space-y-2 sm:space-y-3">
        {product.title && (
          <h3 className="text-gray-900 font-semibold text-lg sm:text-xl md:text-2xl">{product.title}</h3>
        )}
        
        {product.price !== undefined && (
          <div className="flex items-baseline gap-2">
            <span className="text-gray-900 font-bold text-xl sm:text-2xl md:text-3xl">
              {currency}{formatPrice(product.price)}
            </span>
            {product.originalPrice && (
              <span className="text-gray-400 text-xs sm:text-sm line-through">
                {currency}{formatPrice(product.originalPrice)}
              </span>
            )}
          </div>
        )}

        {/* Minimal Button */}
        {buttonOnClick ? (
          <button
            onClick={buttonOnClick}
            className="w-full bg-gray-900 text-white py-2.5 sm:py-3 rounded-lg font-medium text-xs sm:text-sm md:text-base hover:bg-gray-800 transition-colors"
          >
            {buttonText}
          </button>
        ) : (
          <a
            href={buttonHref}
            className="block w-full bg-gray-900 text-white py-3 rounded-lg font-medium text-sm hover:bg-gray-800 transition-colors text-center"
          >
            {buttonText}
          </a>
        )}
      </div>
    </div>
  );
};

// Gradient Variant - Vibrant gradient backgrounds with modern layout
const GradientProductCard = ({ product, currency }) => {
  const images = Array.isArray(product.images) 
    ? product.images 
    : product.image 
      ? [product.image]
      : [];

  const buttonConfig = product.button || {};
  const buttonText = buttonConfig.text || 'EXPLORE';
  const buttonHref = buttonConfig.href || '#';
  const buttonOnClick = product.button?.onClick;

  const gradientColors = product.gradient || ['#667eea', '#764ba2'];
  const gradientStyle = {
    background: `linear-gradient(135deg, ${gradientColors[0]} 0%, ${gradientColors[1]} 100%)`
  };

  const [isMenuOpen, setIsMenuOpen] = useState(false);

  const formatPrice = (price) => {
    if (typeof price === 'number' && price % 1 === 0) {
      return price;
    } else if (typeof price === 'number') {
      return price.toFixed(2);
    }
    return price;
  };

  const handleMenuToggle = (e) => {
    e.stopPropagation();
    setIsMenuOpen(!isMenuOpen);
  };

  const handleAddToCart = (e) => {
    e.stopPropagation();
    if (buttonOnClick) {
      buttonOnClick(e);
    }
    setIsMenuOpen(false);
  };

  const handleWishlist = (e) => {
    e.stopPropagation();
    // You can add custom wishlist handler here
    console.log('Add to wishlist:', product.title);
    setIsMenuOpen(false);
  };

  // Close menu when clicking outside
  useEffect(() => {
    const handleClickOutside = () => {
      setIsMenuOpen(false);
    };
    if (isMenuOpen) {
      document.addEventListener('click', handleClickOutside);
      return () => document.removeEventListener('click', handleClickOutside);
    }
  }, [isMenuOpen]);

  return (
    <div className="relative rounded-3xl overflow-hidden shadow-xl group" style={gradientStyle}>
      {/* Gradient Background with Pattern */}
      <div className="absolute inset-0 opacity-10">
        <div className="absolute inset-0" style={{
          backgroundImage: 'radial-gradient(circle at 2px 2px, white 1px, transparent 0)',
          backgroundSize: '40px 40px'
        }}></div>
      </div>

      <div className="relative p-6 sm:p-8">
        {/* Top Section - Badge and Icon */}
        <div className="flex items-start justify-between mb-6">
          {product.badge && (
            <span className="bg-white/20 backdrop-blur-sm text-white text-xs font-bold px-3 py-1.5 rounded-full border border-white/30">
              {product.badge}
            </span>
          )}
          <div className="relative">
            <button 
              onClick={handleMenuToggle}
              className="w-10 h-10 rounded-full bg-white/20 backdrop-blur-sm flex items-center justify-center hover:bg-white/30 transition-colors z-20"
            >
              <svg className="w-5 h-5 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 5v.01M12 12v.01M12 19v.01M12 6a1 1 0 110-2 1 1 0 010 2zm0 7a1 1 0 110-2 1 1 0 010 2zm0 7a1 1 0 110-2 1 1 0 010 2z" />
              </svg>
            </button>
            
            {/* Dropdown Menu */}
            {isMenuOpen && (
              <div 
                className="absolute top-12 sm:top-14 right-0 bg-white rounded-lg shadow-xl py-2 min-w-[140px] sm:min-w-[160px] md:min-w-[180px] z-30 border border-gray-100"
                onClick={(e) => e.stopPropagation()}
              >
                <button
                  onClick={handleAddToCart}
                  className="w-full px-3 sm:px-4 py-2 sm:py-2.5 text-left text-gray-700 hover:bg-gray-50 transition-colors flex items-center gap-2 text-xs sm:text-sm font-medium"
                >
                  <svg className="w-3.5 h-3.5 sm:w-4 sm:h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 3h2l.4 2M7 13h10l4-8H5.4M7 13L5.4 5M7 13l-2.293 2.293c-.63.63-.184 1.707.707 1.707H17m0 0a2 2 0 100 4 2 2 0 000-4zm-8 2a2 2 0 11-4 0 2 2 0 014 0z" />
                  </svg>
                  Add to cart
                </button>
                <button
                  onClick={handleWishlist}
                  className="w-full px-3 sm:px-4 py-2 sm:py-2.5 text-left text-gray-700 hover:bg-gray-50 transition-colors flex items-center gap-2 text-xs sm:text-sm font-medium"
                >
                  <svg className="w-3.5 h-3.5 sm:w-4 sm:h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4.318 6.318a4.5 4.5 0 000 6.364L12 20.364l7.682-7.682a4.5 4.5 0 00-6.364-6.364L12 7.636l-1.318-1.318a4.5 4.5 0 00-6.364 0z" />
                  </svg>
                  Wishlist
                </button>
              </div>
            )}
          </div>
        </div>

        {/* Product Image */}
        <div className="relative mb-4 sm:mb-5 md:mb-6 h-40 sm:h-48 md:h-56 lg:h-64 flex items-center justify-center">
          {images.length > 0 && images[0] ? (
            <img
              src={images[0]}
              alt={product.title || 'Product'}
              className="max-w-full max-h-full object-contain drop-shadow-2xl group-hover:scale-110 transition-transform duration-500"
              loading="lazy"
            />
          ) : (
            <div className="w-full h-full flex items-center justify-center">
              <svg className="w-20 h-20 text-white/50" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 16l4.586-4.586a2 2 0 012.828 0L16 16m-2-2l1.586-1.586a2 2 0 012.828 0L20 14m-6-6h.01M6 20h12a2 2 0 002-2V6a2 2 0 00-2-2H6a2 2 0 00-2 2v12a2 2 0 002 2z" />
              </svg>
            </div>
          )}
        </div>

        {/* Content Section */}
        <div className="space-y-4">
          {product.category && (
            <p className="text-white/80 text-sm uppercase tracking-wider font-medium">
              {product.category}
            </p>
          )}
          {product.title && (
            <h3 className="text-white font-bold text-2xl sm:text-3xl">
              {product.title}
            </h3>
          )}
          {product.description && (
            <p className="text-white/90 text-sm leading-relaxed line-clamp-2">
              {product.description}
            </p>
          )}
          {product.price !== undefined && (
            <div className="flex items-center justify-between pt-2">
              <span className="text-white font-bold text-xl sm:text-2xl md:text-3xl">
                {currency}{formatPrice(product.price)}
              </span>
            </div>
          )}

          {/* Gradient Button */}
          {buttonOnClick ? (
            <button
              onClick={buttonOnClick}
              className="w-full bg-white text-gray-900 py-3 rounded-xl font-bold text-sm hover:bg-white/90 transition-colors mt-4"
            >
              {buttonText}
            </button>
          ) : (
            <a
              href={buttonHref}
              className="block w-full bg-white text-gray-900 py-3 rounded-xl font-bold text-sm hover:bg-white/90 transition-colors mt-4 text-center"
            >
              {buttonText}
            </a>
          )}
        </div>
      </div>
    </div>
  );
};

export const ProductModern = ({ data, className, id, loader = false }) => {
  if (!data || typeof data !== 'object') {
    console.error('ProductModern: data prop is required and must be an object');
    return null;
  }

  const {
    title,
    subtitle,
    description,
    products = [],
    variant = 'glassmorphism',
    alignment = 'center',
    columns = 3,
    currency = '$',
    className: dataClassName,
  } = data;

  const alignmentClasses = {
    left: 'text-left items-start',
    center: 'text-center items-center',
    right: 'text-right items-end',
  };

  const gridColumnsClasses = {
    1: 'grid-cols-1',
    2: 'grid-cols-1 sm:grid-cols-2',
    3: 'grid-cols-1 sm:grid-cols-1 lg:grid-cols-3',
    4: 'grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4',
  };

  return (
    <section
      id={id}
      className={clsx(
        'w-full py-12 sm:py-16 lg:py-20 px-4 sm:px-6 lg:px-8',
        className,
        dataClassName
      )}
    >
      <div className="max-w-7xl mx-auto w-full">
        {/* Header Section */}
        {(title || subtitle || description) && (
          <div
            className={clsx(
              'mb-8 sm:mb-10 md:mb-12 lg:mb-16 flex flex-col',
              alignmentClasses[alignment] || alignmentClasses.center
            )}
          >
            {subtitle && (
              <p className="text-sm sm:text-base font-semibold text-primary-600 uppercase tracking-wide mb-2">
                {subtitle}
              </p>
            )}
            {title && (
              <h2 className="text-2xl sm:text-3xl md:text-4xl lg:text-5xl xl:text-6xl font-bold text-gray-900 mb-3 sm:mb-4">
                {title}
              </h2>
            )}
            {description && (
              <p className="text-sm sm:text-base md:text-lg text-gray-600 max-w-2xl mx-auto px-4 sm:px-0">
                {description}
              </p>
            )}
          </div>
        )}

        {/* Products Grid */}
        {products.length > 0 && (
          <div
            className={clsx(
              'grid gap-6',
              gridColumnsClasses[columns] || gridColumnsClasses[3]
            )}
          >
            {products.map((product, index) => {
              if (variant === 'glassmorphism') {
                return (
                  <GlassmorphismProductCard
                    key={index}
                    product={product}
                    currency={currency}
                  />
                );
              }
              if (variant === 'minimal') {
                return (
                  <MinimalProductCard
                    key={index}
                    product={product}
                    currency={currency}
                  />
                );
              }
              if (variant === 'gradient') {
                return (
                  <GradientProductCard
                    key={index}
                    product={product}
                    currency={currency}
                  />
                );
              }
              // Default to glassmorphism
              return (
                <GlassmorphismProductCard
                  key={index}
                  product={product}
                  currency={currency}
                />
              );
            })}
          </div>
        )}

        {/* Empty State */}
        {products.length === 0 && (
          <div className="text-center py-12">
            <p className="text-gray-500 text-lg">No products available</p>
          </div>
        )}
      </div>
    </section>
  );
};
