import React, { useState, useEffect } from 'react';
import { clsx } from 'clsx';

export const CheckoutShippingSection = ({ 
  data = {}, 
  values = {}, 
  onChange, 
  variant = 'standard', 
  className,
  loading = false
}) => {
  const [timedOut, setTimedOut] = useState(false);

  // Auto-show content after 2 seconds if loading is true
  useEffect(() => {
    if (loading) {
      setTimedOut(false);
      const timer = setTimeout(() => {
        setTimedOut(true);
      }, 2000);
      return () => clearTimeout(timer);
    }
  }, [loading]);

  const shouldShowSkeleton = loading && !timedOut;
  const { title, fields: customFields } = data;

  const defaultFields = [
    { name: 'firstName', type: 'text', placeholder: 'First Name', colSpan: 'col-span-6' },
    { name: 'lastName', type: 'text', placeholder: 'Last Name', colSpan: 'col-span-6' },
    { 
      name: 'phone', 
      type: 'tel', 
      placeholder: variant === 'standard' ? '' : 'Phone Number', 
      colSpan: 'col-span-12',
      prefix: variant === 'standard' ? { label: 'Phone', value: '+1' } : null
    },
    { name: 'address', type: 'text', placeholder: 'Address', colSpan: 'col-span-12' },
    { name: 'apt', type: 'text', placeholder: 'Apt, Suite', colSpan: 'col-span-12' },
    { name: 'city', type: 'text', placeholder: 'City*', colSpan: 'col-span-4' },
    { 
      name: 'state', 
      type: 'select', 
      placeholder: 'State*', 
      colSpan: 'col-span-4',
      options: ['NY', 'CA', 'TX', 'FL']
    },
    { name: 'zip', type: 'text', placeholder: 'ZIP*', colSpan: 'col-span-4' },
  ];

  const fields = customFields || defaultFields;

  const isModern = variant === 'modern';
  const isPremium = variant === 'premium';

  const handleInputChange = (field) => (e) => {
    if (onChange) onChange(field, e.target.value);
  };

  const inputClass = clsx(
    "w-full transition-all outline-none",
    variant === 'standard' && "border border-gray-300 px-4 py-3 focus:border-black rounded-sm",
    variant === 'modern' && "bg-gray-50 border-transparent rounded-xl px-6 py-4 focus:ring-2 focus:ring-black focus:bg-white",
    variant === 'compact' && "border-b-2 border-gray-100 py-3 text-lg font-medium focus:border-black",
    variant === 'premium' && "bg-transparent border-b border-neutral-800 py-6 text-xl font-light focus:border-white placeholder:text-neutral-200 text-white"
  );

  // Show skeleton loading state
  if (shouldShowSkeleton) {
    return (
      <section className={clsx("checkout-shipping-section", className)}>
        <h2 className={clsx(
          "mb-4 uppercase",
          variant === 'standard' && "text-sm font-bold tracking-widest text-gray-900",
          variant === 'modern' && "text-2xl font-black tracking-tight text-gray-900",
          variant === 'compact' && "text-xs font-black text-gray-400 tracking-[0.2em]",
          variant === 'premium' && "text-[10px] uppercase tracking-widest text-neutral-600 font-bold block ml-1"
        )}>
          {data.title || 'Shipping'}
        </h2>

        <div className={clsx("grid grid-cols-12 gap-x-4 gap-y-4", isModern && "gap-y-6", isPremium && "gap-y-0")}>
          {fields.slice(0, 6).map((field, index) => (
            <div key={field.name || index} className={clsx(field.colSpan || "col-span-12", "relative")}>
              <div className={clsx(inputClass, 'checkout-skeleton')}></div>
            </div>
          ))}
        </div>
      </section>
    );
  }

  return (
    <section className={clsx("checkout-shipping-section", className)}>
      <h2 className={clsx(
        "mb-4 uppercase",
        variant === 'standard' && "text-sm font-bold tracking-widest text-gray-900",
        variant === 'modern' && "text-2xl font-black tracking-tight text-gray-900",
        variant === 'compact' && "text-xs font-black text-gray-400 tracking-[0.2em]",
        variant === 'premium' && "text-[10px] uppercase tracking-widest text-neutral-600 font-bold block ml-1"
      )}>
        {title || 'Shipping'}
      </h2>
      
      <div className={clsx("grid grid-cols-12 gap-x-4 gap-y-4", isModern && "gap-y-6", isPremium && "gap-y-0")}>
        {fields.map((field, index) => (
          <div key={field.name || index} className={clsx(field.colSpan || "col-span-12", "relative")}>
            {field.type === 'select' ? (
              <div className="relative">
                <select 
                  className={clsx(inputClass, "appearance-none bg-white", isPremium && "bg-transparent")}
                  value={values[field.name] || ''}
                  onChange={handleInputChange(field.name)}
                >
                  <option value="" disabled>{field.placeholder}</option>
                  {field.options?.map(opt => (
                    <option key={opt} value={opt}>{opt}</option>
                  ))}
                </select>
                <div className="absolute right-4 top-1/2 -translate-y-1/2 pointer-events-none">
                  <svg className="w-4 h-4 text-gray-400" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
                  </svg>
                </div>
              </div>
            ) : (
              <div className="relative">
                {field.prefix && (
                  <span className="absolute left-4 top-1/2 -translate-y-1/2 text-gray-500 text-[10px] leading-tight">
                    {field.prefix.label} <br/> 
                    <span className="text-black font-medium text-xs">{field.prefix.value}</span>
                  </span>
                )}
                <input 
                  type={field.type || 'text'} 
                  placeholder={field.placeholder} 
                  className={clsx(inputClass, field.prefix && "pl-16")}
                  value={values[field.name] || ''}
                  onChange={handleInputChange(field.name)}
                />
              </div>
            )}
          </div>
        ))}
      </div>
    </section>
  );
};
