import { useState, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/category.css';

/**
 * CategoryGrid Component  Modern responsive grid of category cards
 * @param {Object} props
 * @param {Object} props.data - Configuration for the grid
 * @param {string} [props.data.title] - Section title
 * @param {string} [props.data.subtitle] - Small label above the title
 * @param {string} [props.data.description] - Supporting text under the title
 * @param {Array}  props.data.items - Category items
 * @param {string} [props.data.theme='light'] - 'light' | 'dark'
 * @param {string} [props.data.variant='default'] - 'default' | 'minimal' | 'featured' | 'compact' | 'gradient'
 * @param {number} [props.data.columns=3] - 14 columns on desktop
 * @param {string} [props.data.align='center'] - 'left' | 'center'
 * @param {string} [props.data.badgeLabel] - Optional pill in the header
 * @param {string} [props.className] - Extra classes on the section
 * @param {string} [props.id] - Section id
 * @param {boolean} [props.loader=false] - Show skeleton loader state
 */
export const CategoryGrid = ({ data, className, id, loader = false }) => {
  if (!data || typeof data !== 'object') {
    console.error('CategoryGrid: data prop is required and must be an object');
    return null;
  }

  const [showLoader, setShowLoader] = useState(loader);

  useEffect(() => {
    if (loader) {
      const timer = setTimeout(() => setShowLoader(false), 2000);
      return () => clearTimeout(timer);
    }
    setShowLoader(false);
  }, [loader]);

  const {
    title,
    subtitle,
    description,
    items = [],
    theme = 'light',
    variant = 'default',
    columns = 3,
    align = 'center',
    badgeLabel,
    className: dataClassName,
  } = data;

  const isDark = theme === 'dark';

  const gridColumns = {
    1: 'grid-cols-1',
    2: 'grid-cols-1 sm:grid-cols-2',
    3: 'grid-cols-1 sm:grid-cols-2 lg:grid-cols-3',
    4: 'grid-cols-1 sm:grid-cols-2 lg:grid-cols-4',
  }[columns] || 'grid-cols-1 sm:grid-cols-2 lg:grid-cols-3';

  const headerAlignClasses =
    align === 'left'
      ? 'items-start text-left'
      : 'items-center text-center';

  // Skeleton state
  if (showLoader) {
    return (
      <section
        id={id}
        className={clsx(
          'w-full py-16 sm:py-20 lg:py-24 px-4 sm:px-6 lg:px-8',
          isDark ? 'bg-gray-950' : 'bg-white',
          'category-section',
          className,
          dataClassName
        )}
      >
        <div className="max-w-7xl mx-auto w-full">
          {/* Header skeleton */}
          <div
            className={clsx(
              'mb-10 sm:mb-14 flex flex-col gap-4',
              headerAlignClasses
            )}
          >
            <div
              className={clsx(
                'category-skeleton rounded-full h-8 w-32'
              )}
            />
            <div
              className={clsx(
                'category-skeleton rounded-lg h-10 w-full max-w-xl'
              )}
            />
            <div
              className={clsx(
                'category-skeleton rounded-lg h-5 w-full max-w-2xl'
              )}
            />
          </div>

          {/* Grid skeleton */}
          <div
            className={clsx(
              'grid gap-6 sm:gap-8',
              gridColumns
            )}
          >
            {Array.from({ length: 6 }).map((_, index) => (
              <div
                key={index}
                className={clsx(
                  'category-card-skeleton rounded-2xl p-6 sm:p-7 border',
                  isDark
                    ? 'bg-gray-900/70 border-gray-800'
                    : 'bg-gray-50 border-gray-100'
                )}
              >
                <div className="flex items-center justify-between mb-5">
                  <div className="category-skeleton rounded-full h-10 w-10" />
                  <div className="category-skeleton rounded-full h-7 w-16" />
                </div>
                <div className="space-y-3 mb-5">
                  <div className="category-skeleton rounded h-5 w-3/4" />
                  <div className="category-skeleton rounded h-4 w-full" />
                  <div className="category-skeleton rounded h-4 w-5/6" />
                </div>
                <div className="flex items-center justify-between">
                  <div className="category-skeleton rounded-full h-7 w-24" />
                  <div className="category-skeleton rounded-full h-9 w-9" />
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>
    );
  }

  if (!items.length) {
    console.warn('CategoryGrid: items array is empty');
    return null;
  }

  const getColorForIndex = (index) => {
    const palette = [
      'from-primary-500/10 via-primary-500/5 to-transparent',
      'from-violet-500/10 via-violet-500/5 to-transparent',
      'from-emerald-500/10 via-emerald-500/5 to-transparent',
      'from-amber-500/10 via-amber-500/5 to-transparent',
    ];
    return palette[index % palette.length];
  };

  const getGradientForIndex = (index) => {
    const gradients = [
      'from-blue-500 via-purple-500 to-pink-500',
      'from-emerald-500 via-teal-500 to-cyan-500',
      'from-orange-500 via-red-500 to-pink-500',
      'from-indigo-500 via-blue-500 to-cyan-500',
      'from-violet-500 via-purple-500 to-fuchsia-500',
      'from-rose-500 via-pink-500 to-orange-500',
    ];
    return gradients[index % gradients.length];
  };

  const getFeaturedGradient = (index) => {
    const featuredGradients = [
      'from-blue-600 via-indigo-600 to-purple-600',
      'from-emerald-600 via-teal-600 to-cyan-600',
      'from-orange-600 via-red-600 to-rose-600',
      'from-violet-600 via-purple-600 to-pink-600',
    ];
    return featuredGradients[index % featuredGradients.length];
  };

  // Variant-specific section background
  const getSectionBg = () => {
    if (variant === 'gradient') {
      return isDark
        ? 'bg-gradient-to-br from-gray-950 via-gray-900 to-black'
        : 'bg-gradient-to-br from-slate-50 via-white to-gray-50';
    }
    if (variant === 'minimal') {
      return isDark ? 'bg-gray-950' : 'bg-white';
    }
    if (variant === 'compact') {
      return isDark ? 'bg-gray-950' : 'bg-gray-50';
    }
    return isDark
      ? 'bg-gradient-to-b from-black via-gray-950 to-gray-900'
      : 'bg-gradient-to-b from-white via-slate-50 to-white';
  };

  // Variant-specific grid gap
  const getGridGap = () => {
    if (variant === 'compact') return 'gap-4 sm:gap-5';
    if (variant === 'featured') return 'gap-6 sm:gap-8 lg:gap-10';
    return 'gap-6 sm:gap-8';
  };

  // Variant-specific padding
  const getSectionPadding = () => {
    if (variant === 'compact') return 'py-12 sm:py-16 lg:py-20';
    return 'py-16 sm:py-20 lg:py-24';
  };

  return (
    <section
      id={id}
      className={clsx(
        'w-full px-4 sm:px-6 lg:px-8',
        getSectionPadding(),
        getSectionBg(),
        'category-section',
        className,
        dataClassName
      )}
    >
      <div className="max-w-7xl mx-auto w-full">
        {(subtitle || title || description || badgeLabel) && (
          <div
            className={clsx(
              variant === 'compact' ? 'mb-8 sm:mb-10' : 'mb-10 sm:mb-14',
              'flex flex-col gap-4',
              headerAlignClasses
            )}
          >
            {badgeLabel && (
              <span
                className={clsx(
                  'inline-flex items-center gap-2 rounded-full px-3 py-1 text-xs font-semibold tracking-wide border',
                  variant === 'minimal'
                    ? isDark
                      ? 'border-gray-800 bg-transparent text-gray-300'
                      : 'border-gray-200 bg-transparent text-gray-700'
                    : isDark
                    ? 'border-gray-800 bg-gray-900/70 text-gray-300'
                    : 'border-gray-200 bg-white text-gray-700',
                  variant === 'gradient' && 'backdrop-blur-sm',
                  'shadow-sm'
                )}
              >
                <span className="w-1.5 h-1.5 rounded-full bg-primary-500" />
                {badgeLabel}
              </span>
            )}

            {subtitle && (
              <p
                className={clsx(
                  'text-xs sm:text-sm font-semibold tracking-[0.2em] uppercase',
                  isDark ? 'text-gray-400' : 'text-gray-500'
                )}
              >
                {subtitle}
              </p>
            )}

            {title && (
              <h2
                className={clsx(
                  variant === 'compact'
                    ? 'text-2xl sm:text-3xl lg:text-4xl'
                    : 'text-3xl sm:text-4xl lg:text-5xl',
                  'font-bold tracking-tight',
                  isDark ? 'text-white' : 'text-gray-900'
                )}
              >
                {title}
              </h2>
            )}

            {description && (
              <p
                className={clsx(
                  'max-w-2xl text-sm sm:text-base lg:text-lg leading-relaxed',
                  isDark ? 'text-gray-400' : 'text-gray-600'
                )}
              >
                {description}
              </p>
            )}
          </div>
        )}

        <div
          className={clsx(
            'grid',
            getGridGap(),
            variant === 'featured' && items.length > 0
              ? 'grid-cols-1 sm:grid-cols-2 lg:grid-cols-3'
              : gridColumns
          )}
        >
          {items.map((item, index) => {
            const accent = getColorForIndex(index);
            const isFeatured = variant === 'featured' && index === 0;
            const gradient = getGradientForIndex(index);
            const featuredGradient = getFeaturedGradient(index);
            // If a heroImage is provided, show a big image block inside the card
            // and hide the small avatar beside the title.
            const hasHeroImage = !!item.heroImage;
            const showSmallMedia = !hasHeroImage;

            // Card classes based on variant
            const getCardClasses = () => {
              if (variant === 'minimal') {
                return clsx(
                  'relative overflow-hidden rounded-xl border',
                  'transition-all duration-300 category-card hover:category-card-hover',
                  isDark
                    ? 'bg-transparent border-gray-800 hover:border-gray-700'
                    : 'bg-transparent border-gray-200 hover:border-gray-300',
                  'shadow-none hover:shadow-sm'
                );
              }
              if (variant === 'compact') {
                return clsx(
                  'relative overflow-hidden rounded-xl border',
                  'transition-all duration-300 category-card hover:category-card-hover',
                  isDark
                    ? 'bg-gray-900/50 border-gray-800 hover:bg-gray-900 hover:border-gray-700'
                    : 'bg-white border-gray-100 hover:bg-gray-50 hover:border-gray-200',
                  'shadow-sm hover:shadow-md'
                );
              }
              if (variant === 'gradient') {
                return clsx(
                  'relative overflow-hidden rounded-2xl',
                  'transition-all duration-300 category-card hover:category-card-hover',
                  'bg-gradient-to-br',
                  `bg-gradient-to-br ${gradient}`,
                  'shadow-xl hover:shadow-2xl',
                  'border border-white/20'
                );
              }
              if (variant === 'featured' && isFeatured) {
                return clsx(
                  'relative overflow-hidden rounded-3xl',
                  'transition-all duration-300 category-card hover:category-card-hover',
                  'bg-gradient-to-br',
                  `bg-gradient-to-br ${featuredGradient}`,
                  'shadow-2xl hover:shadow-3xl',
                  'border border-white/30',
                  'sm:col-span-2 lg:col-span-2'
                );
              }
              // Default variant
              return clsx(
                'relative overflow-hidden rounded-2xl border',
                'transition-all duration-300 category-card hover:category-card-hover',
                isDark
                  ? 'bg-gray-900/70 border-gray-800 hover:bg-gray-900 hover:border-gray-700'
                  : 'bg-white border-gray-100 hover:bg-white hover:border-gray-200',
                'shadow-[0_18px_45px_rgba(15,23,42,0.08)]'
              );
            };

            // Padding based on variant
            const getCardPadding = () => {
              if (variant === 'compact') return 'p-4 sm:p-5';
              if (variant === 'featured' && isFeatured) return 'p-8 sm:p-10';
              if (variant === 'minimal') return 'p-5 sm:p-6';
              return 'p-6 sm:p-7';
            };

            // Text colors for gradient variant
            const getTextColor = (element) => {
              if (variant === 'gradient' || (variant === 'featured' && isFeatured)) {
                return 'text-white';
              }
              if (element === 'title') {
                return isDark ? 'text-white' : 'text-gray-900';
              }
              if (element === 'description') {
                return isDark ? 'text-gray-400' : 'text-gray-600';
              }
              if (element === 'meta') {
                return isDark ? 'text-gray-300' : 'text-gray-700';
              }
              return isDark ? 'text-gray-400' : 'text-gray-600';
            };

            return (
              <article
                key={item.id || item.slug || item.title || index}
                className={getCardClasses()}
              >
                {/* Accent gradient for default variant */}
                {variant === 'default' && (
                  <div
                    className={clsx(
                      'pointer-events-none absolute inset-x-0 -top-px h-24 opacity-80',
                      'bg-gradient-to-b',
                      accent
                    )}
                  />
                )}

                {/* Overlay for gradient variants */}
                {(variant === 'gradient' || (variant === 'featured' && isFeatured)) && (
                  <div className="absolute inset-0 bg-black/10 pointer-events-none" />
                )}

                <div className={clsx('relative flex flex-col', getCardPadding(), variant === 'compact' ? 'gap-3' : 'gap-5')}>
                  {/* Optional big hero image block inside the card */}
                  {hasHeroImage && (
                    <div className="mb-4 sm:mb-5 rounded-2xl overflow-hidden h-40 sm:h-52 lg:h-64">
                      <img
                        src={item.heroImage}
                        alt={item.title || 'Category image'}
                        className="w-full h-full object-cover"
                      />
                    </div>
                  )}

                  <div className={clsx('flex items-center justify-between gap-4', variant === 'compact' && 'flex-wrap')}>
                    <div className="flex items-center gap-3">
                      {showSmallMedia && (item.icon || item.image) && (
                        <div
                          className={clsx(
                            'inline-flex items-center justify-center overflow-hidden',
                            // Make the featured card image noticeably bigger
                            variant === 'featured' && isFeatured
                              ? 'h-16 w-16 sm:h-20 sm:w-20 rounded-2xl'
                              : variant === 'compact'
                              ? 'h-8 w-8 rounded-lg'
                              : 'h-10 w-10 rounded-xl',
                            'category-icon',
                            variant === 'gradient' || (variant === 'featured' && isFeatured)
                              ? 'bg-white/20 backdrop-blur-sm border border-white/30'
                              : variant === 'minimal'
                              ? isDark
                                ? 'bg-transparent border border-gray-700'
                                : 'bg-transparent border border-gray-200'
                              : isDark
                              ? 'bg-gray-900 border border-gray-700'
                              : 'bg-slate-50 border border-slate-200'
                          )}
                        >
                          <img
                            src={item.image || item.icon}
                            alt={item.title || 'Category image'}
                            className="w-full h-full object-cover"
                          />
                        </div>
                      )}
                      <div>
                        <h3
                          className={clsx(
                            variant === 'compact'
                              ? 'text-sm sm:text-base font-semibold'
                              : variant === 'featured' && isFeatured
                              ? 'text-xl sm:text-2xl font-bold'
                              : 'text-base sm:text-lg font-semibold',
                            getTextColor('title')
                          )}
                        >
                          {item.title}
                        </h3>
                        {item.badge && (
                          <p
                            className={clsx(
                              'mt-1 text-xs font-medium inline-flex items-center gap-1 rounded-full px-2 py-0.5',
                              variant === 'gradient' || (variant === 'featured' && isFeatured)
                                ? 'bg-white/20 backdrop-blur-sm text-white border border-white/30'
                                : variant === 'minimal'
                                ? isDark
                                  ? 'bg-transparent text-gray-300 border border-gray-700'
                                  : 'bg-transparent text-gray-700 border border-gray-200'
                                : isDark
                                ? 'bg-gray-900 text-gray-300 border border-gray-700'
                                : 'bg-slate-50 text-slate-700 border border-slate-200'
                            )}
                          >
                            <span className="w-1.5 h-1.5 rounded-full bg-primary-500" />
                            {item.badge}
                          </p>
                        )}
                      </div>
                    </div>

                    {item.meta && (
                      <span
                        className={clsx(
                          'rounded-full px-3 py-1 text-xs font-medium',
                          variant === 'gradient' || (variant === 'featured' && isFeatured)
                            ? 'bg-white/20 backdrop-blur-sm text-white border border-white/30'
                            : variant === 'minimal'
                            ? isDark
                              ? 'bg-transparent text-gray-300 border border-gray-700'
                              : 'bg-transparent text-gray-700 border border-gray-200'
                            : isDark
                            ? 'bg-gray-900 text-gray-300 border border-gray-700'
                            : 'bg-slate-50 text-slate-700 border border-slate-200'
                        )}
                      >
                        {item.meta}
                      </span>
                    )}
                  </div>

                  {item.description && (
                    <p
                      className={clsx(
                        variant === 'compact'
                          ? 'text-xs leading-relaxed line-clamp-2'
                          : variant === 'featured' && isFeatured
                          ? 'text-base leading-relaxed line-clamp-4'
                          : 'text-sm leading-relaxed line-clamp-3',
                        getTextColor('description')
                      )}
                    >
                      {item.description}
                    </p>
                  )}

                  {(item.stats || item.tags) && (
                    <div className={clsx('flex flex-wrap items-center gap-3', variant === 'compact' ? 'text-[10px]' : 'text-xs')}>
                      {item.stats && (
                        <div
                          className={clsx(
                            'inline-flex items-center gap-2 rounded-full px-3 py-1 border',
                            variant === 'gradient' || (variant === 'featured' && isFeatured)
                              ? 'border-white/30 bg-white/20 backdrop-blur-sm text-white'
                              : variant === 'minimal'
                              ? isDark
                                ? 'border-gray-800 bg-transparent text-gray-200'
                                : 'border-slate-200 bg-transparent text-slate-800'
                              : isDark
                              ? 'border-gray-800 bg-gray-900/80 text-gray-200'
                              : 'border-slate-200 bg-slate-50 text-slate-800'
                          )}
                        >
                          {item.stats.icon && (
                            <span className="w-3.5 h-3.5">
                              {item.stats.icon}
                            </span>
                          )}
                          <span className="font-semibold">
                            {item.stats.value}
                          </span>
                          {item.stats.label && (
                            <span
                              className={clsx(
                                'text-[11px]',
                                variant === 'gradient' || (variant === 'featured' && isFeatured)
                                  ? 'text-white/80'
                                  : isDark
                                  ? 'text-gray-400'
                                  : 'text-gray-500'
                              )}
                            >
                              {item.stats.label}
                            </span>
                          )}
                        </div>
                      )}

                      {item.tags && item.tags.length > 0 && (
                        <div className="flex flex-wrap gap-1.5">
                          {item.tags.slice(0, variant === 'compact' ? 3 : 4).map((tag, tIndex) => (
                            <span
                              key={tIndex}
                              className={clsx(
                                'rounded-full px-2.5 py-0.5',
                                variant === 'gradient' || (variant === 'featured' && isFeatured)
                                  ? 'bg-white/20 backdrop-blur-sm text-white border border-white/30'
                                  : variant === 'minimal'
                                  ? isDark
                                    ? 'bg-transparent text-gray-300 border border-gray-800'
                                    : 'bg-transparent text-slate-700 border border-slate-200'
                                  : isDark
                                  ? 'bg-gray-900 text-gray-300 border border-gray-800'
                                  : 'bg-slate-50 text-slate-700 border border-slate-200'
                              )}
                            >
                              {tag}
                            </span>
                          ))}
                          {item.tags.length > (variant === 'compact' ? 3 : 4) && (
                            <span
                              className={clsx(
                                'rounded-full px-2 py-0.5',
                                variant === 'gradient' || (variant === 'featured' && isFeatured)
                                  ? 'bg-white/20 text-white/80'
                                  : isDark
                                  ? 'bg-gray-900 text-gray-400'
                                  : 'bg-slate-50 text-slate-500'
                              )}
                            >
                              +{item.tags.length - (variant === 'compact' ? 3 : 4)}
                            </span>
                          )}
                        </div>
                      )}
                    </div>
                  )}

                  <div className={clsx('mt-1 flex items-center justify-between gap-3', variant === 'compact' && 'mt-0')}>
                    {item.cta && (
                      <a
                        href={item.cta.href || '#'}
                        onClick={item.cta.onClick}
                        className={clsx(
                          'inline-flex items-center gap-2 font-semibold',
                          variant === 'compact' ? 'text-[11px] sm:text-xs' : 'text-xs sm:text-sm',
                          'transition-all duration-200 group/cta',
                          variant === 'gradient' || (variant === 'featured' && isFeatured)
                            ? 'text-white hover:text-white/90'
                            : isDark
                            ? 'text-primary-300 hover:text-primary-200'
                            : 'text-primary-700 hover:text-primary-800'
                        )}
                      >
                        {item.cta.label || 'View category'}
                        <svg
                          className={clsx('transition-transform duration-200 group-hover/cta:translate-x-0.5', variant === 'compact' ? 'w-3 h-3' : 'w-3.5 h-3.5')}
                          viewBox="0 0 24 24"
                          fill="none"
                          xmlns="http://www.w3.org/2000/svg"
                        >
                          <path
                            d="M5 12H19M19 12L13 6M19 12L13 18"
                            stroke="currentColor"
                            strokeWidth="1.8"
                            strokeLinecap="round"
                            strokeLinejoin="round"
                          />
                        </svg>
                      </a>
                    )}

                    {item.count != null && (
                      <span
                        className={clsx(
                          'inline-flex items-center gap-1.5 rounded-full px-2.5 py-1 font-medium',
                          variant === 'compact' ? 'text-[10px]' : 'text-[11px]',
                          variant === 'gradient' || (variant === 'featured' && isFeatured)
                            ? 'bg-white/20 backdrop-blur-sm text-white border border-white/30'
                            : variant === 'minimal'
                            ? isDark
                              ? 'bg-transparent text-gray-300 border border-gray-800'
                              : 'bg-transparent text-slate-700 border border-slate-200'
                            : isDark
                            ? 'bg-gray-900 text-gray-300 border border-gray-800'
                            : 'bg-slate-50 text-slate-700 border border-slate-200'
                        )}
                      >
                        <span
                          className={clsx(
                            'w-1.5 h-1.5 rounded-full',
                            variant === 'gradient' || (variant === 'featured' && isFeatured)
                              ? 'bg-white'
                              : isDark
                              ? 'bg-emerald-400'
                              : 'bg-emerald-500'
                          )}
                        />
                        {item.count} items
                      </span>
                    )}
                  </div>
                </div>
              </article>
            );
          })}
        </div>
      </div>
    </section>
  );
};


