import { useEffect, useState } from 'react';
import clsx from 'clsx';
import '../../styles/product.css';

/**
 * ProductShare Component - Social sharing buttons with multiple variants
 * @param {Object} props - Component props
 * @param {Object} props.data - Share configuration data
 * @param {string} [props.data.variant='default'] - Style variant: 'default' | 'modern' | 'minimal' | 'compact' | 'icon' | 'dark' | 'dark-gray'
 * @param {string} [props.data.url] - URL to share
 * @param {string} [props.data.title] - Title to share
 * @param {Array} [props.data.platforms=['facebook', 'twitter', 'pinterest', 'copy']] - Platforms to show
 * @param {Function} [props.onShare] - Callback when share is clicked
 * @param {boolean} [props.loading=false] - Show skeleton loader
 * @param {string} [props.className] - Additional CSS classes
 */
export const ProductShare = ({ 
  data = {}, 
  onShare,
  loading = false,
  className 
}) => {
  const {
    variant = 'default',
    url = typeof window !== 'undefined' ? window.location.href : '',
    title = 'Check out this product',
    platforms = ['facebook', 'twitter', 'pinterest', 'copy'],
  } = data;

  const [showContent, setShowContent] = useState(!loading);
  const [copied, setCopied] = useState(false);

  useEffect(() => {
    if (loading) {
      setShowContent(false);
      const timer = setTimeout(() => {
        setShowContent(true);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowContent(true);
    }
  }, [loading]);

  const handleShare = (platform) => {
    const shareUrls = {
      facebook: `https://www.facebook.com/sharer/sharer.php?u=${encodeURIComponent(url)}`,
      twitter: `https://twitter.com/intent/tweet?url=${encodeURIComponent(url)}&text=${encodeURIComponent(title)}`,
      pinterest: `https://pinterest.com/pin/create/button/?url=${encodeURIComponent(url)}&description=${encodeURIComponent(title)}`,
      linkedin: `https://www.linkedin.com/sharing/share-offsite/?url=${encodeURIComponent(url)}`,
      whatsapp: `https://wa.me/?text=${encodeURIComponent(title + ' ' + url)}`,
    };

    if (platform === 'copy') {
      navigator.clipboard.writeText(url);
      setCopied(true);
      setTimeout(() => setCopied(false), 2000);
    } else {
      window.open(shareUrls[platform], '_blank', 'width=600,height=400');
    }

    if (onShare) {
      onShare(platform);
    }
  };

  const containerClasses = {
    default: 'flex items-center gap-2 sm:gap-3',
    modern: 'flex items-center gap-3 sm:gap-4',
    minimal: 'flex items-center gap-1 sm:gap-2',
    compact: 'flex items-center gap-1 sm:gap-1.5',
    icon: 'flex items-center gap-1 sm:gap-2',
    dark: 'flex items-center gap-2 sm:gap-3',
    'dark-gray': 'flex items-center gap-2 sm:gap-3',
  };

  const buttonClasses = {
    default: clsx(
      'p-2 sm:p-2.5 rounded-lg border border-gray-300 bg-white hover:bg-gray-50',
      'transition-all duration-200'
    ),
    modern: clsx(
      'p-3 sm:p-3.5 rounded-xl border-2 border-gray-200 bg-white hover:shadow-md',
      'transition-all duration-300'
    ),
    minimal: clsx(
      'p-1.5 sm:p-2 text-gray-600 hover:text-gray-900',
      'transition-colors duration-200'
    ),
    compact: clsx(
      'p-1.5 sm:p-2 rounded-md border border-gray-300 bg-white hover:bg-gray-50',
      'transition-all duration-200'
    ),
    icon: clsx(
      'p-2 sm:p-2.5 rounded-lg bg-primary-500 hover:bg-primary-600 text-white',
      'shadow-md hover:shadow-lg transition-all duration-300',
      ''
    ),
    dark: clsx(
      'p-2 sm:p-2.5 rounded-lg border border-gray-600 bg-gray-800 hover:bg-gray-700 text-gray-200',
      'transition-all duration-200'
    ),
    'dark-gray': clsx(
      'p-2 sm:p-2.5 rounded-lg border border-gray-700 bg-gray-900 hover:bg-gray-800 text-gray-300',
      'transition-all duration-200'
    ),
  };

  const iconSizeClasses = {
    default: 'w-4 h-4 sm:w-5 sm:h-5',
    modern: 'w-5 h-5 sm:w-6 sm:h-6',
    minimal: 'w-3.5 h-3.5 sm:w-4 sm:h-4',
    compact: 'w-3.5 h-3.5 sm:w-4 sm:h-4',
    icon: 'w-5 h-5 sm:w-6 sm:h-6',
    dark: 'w-4 h-4 sm:w-5 sm:h-5',
    'dark-gray': 'w-4 h-4 sm:w-5 sm:h-5',
  };

  const SkeletonLoader = () => {
    const isDark = variant === 'dark' || variant === 'dark-gray';
    const skeletonBg = isDark ? 'bg-gray-700' : 'bg-gray-200';

    return (
      <div className={clsx(containerClasses[variant] || containerClasses.default, className)}>
        {platforms.map((_, i) => (
          <div
            key={i}
            className={clsx(
              buttonClasses[variant] || buttonClasses.default,
              'product-skeleton w-10 h-10 sm:w-12 sm:h-12',
              skeletonBg
            )}
          ></div>
        ))}
      </div>
    );
  };

  if (!showContent) {
    return <SkeletonLoader />;
  }

  const ShareIcons = {
    facebook: () => (
      <svg className={iconSizeClasses[variant] || iconSizeClasses.default} fill="currentColor" viewBox="0 0 24 24">
        <path d="M24 12.073c0-6.627-5.373-12-12-12s-12 5.373-12 12c0 5.99 4.388 10.954 10.125 11.854v-8.385H7.078v-3.47h3.047V9.43c0-3.007 1.792-4.669 4.533-4.669 1.312 0 2.686.235 2.686.235v2.953H15.83c-1.491 0-1.956.925-1.956 1.874v2.25h3.328l-.532 3.47h-2.796v8.385C19.612 23.027 24 18.062 24 12.073z"/>
      </svg>
    ),
    twitter: () => (
      <svg className={iconSizeClasses[variant] || iconSizeClasses.default} fill="currentColor" viewBox="0 0 24 24">
        <path d="M23.953 4.57a10 10 0 01-2.825.775 4.958 4.958 0 002.163-2.723c-.951.555-2.005.959-3.127 1.184a4.92 4.92 0 00-8.384 4.482C7.69 8.095 4.067 6.13 1.64 3.162a4.822 4.822 0 00-.666 2.475c0 1.71.87 3.213 2.188 4.096a4.904 4.904 0 01-2.228-.616v.06a4.923 4.923 0 003.946 4.827 4.996 4.996 0 01-2.212.085 4.936 4.936 0 004.604 3.417 9.867 9.867 0 01-6.102 2.105c-.39 0-.779-.023-1.17-.067a13.995 13.995 0 007.557 2.209c9.053 0 13.998-7.496 13.998-13.985 0-.21 0-.42-.015-.63A9.935 9.935 0 0024 4.59z"/>
      </svg>
    ),
    pinterest: () => (
      <svg className={iconSizeClasses[variant] || iconSizeClasses.default} fill="currentColor" viewBox="0 0 24 24">
        <path d="M12.017 0C5.396 0 .029 5.367.029 11.987c0 5.079 3.158 9.417 7.618 11.174-.105-.949-.199-2.403.041-3.439.219-.937 1.406-5.957 1.406-5.957s-.359-.72-.359-1.781c0-1.663.967-2.911 2.168-2.911 1.024 0 1.518.769 1.518 1.688 0 1.029-.653 2.567-.992 3.992-.285 1.193.6 2.165 1.775 2.165 2.128 0 3.768-2.245 3.768-5.487 0-2.861-2.063-4.869-5.008-4.869-3.41 0-5.409 2.562-5.409 5.199 0 1.033.394 2.143.889 2.741.099.12.112.225.085.345-.09.375-.293 1.199-.334 1.363-.053.225-.172.271-.395.165-1.495-.69-2.433-2.878-2.433-4.646 0-3.776 2.748-7.252 7.92-7.252 4.158 0 7.392 2.967 7.392 6.923 0 4.135-2.607 7.462-6.233 7.462-1.214 0-2.357-.629-2.746-1.378l-.748 2.853c-.271 1.043-1.002 2.35-1.492 3.146C9.57 23.812 10.763 24 12.017 24c6.624 0 11.99-5.367 11.99-11.987C23.97 5.39 18.565.026 11.97.026L12.017 0z"/>
      </svg>
    ),
    linkedin: () => (
      <svg className={iconSizeClasses[variant] || iconSizeClasses.default} fill="currentColor" viewBox="0 0 24 24">
        <path d="M20.447 20.452h-3.554v-5.569c0-1.328-.027-3.037-1.852-3.037-1.853 0-2.136 1.445-2.136 2.939v5.667H9.351V9h3.414v1.561h.046c.477-.9 1.637-1.85 3.37-1.85 3.601 0 4.267 2.37 4.267 5.455v6.286zM5.337 7.433c-1.144 0-2.063-.926-2.063-2.065 0-1.138.92-2.063 2.063-2.063 1.14 0 2.064.925 2.064 2.063 0 1.139-.925 2.065-2.064 2.065zm1.782 13.019H3.555V9h3.564v11.452zM22.225 0H1.771C.792 0 0 .774 0 1.729v20.542C0 23.227.792 24 1.771 24h20.451C23.2 24 24 23.227 24 22.271V1.729C24 .774 23.2 0 22.222 0h.003z"/>
      </svg>
    ),
    whatsapp: () => (
      <svg className={iconSizeClasses[variant] || iconSizeClasses.default} fill="currentColor" viewBox="0 0 24 24">
        <path d="M17.472 14.382c-.297-.149-1.758-.867-2.03-.967-.273-.099-.471-.148-.67.15-.197.297-.767.966-.94 1.164-.173.199-.347.223-.644.075-.297-.15-1.255-.463-2.39-1.475-.883-.788-1.48-1.761-1.653-2.059-.173-.297-.018-.458.13-.606.134-.133.298-.347.446-.52.149-.174.198-.298.298-.497.099-.198.05-.371-.025-.52-.075-.149-.669-1.612-.916-2.207-.242-.579-.487-.5-.669-.51-.173-.008-.371-.01-.57-.01-.198 0-.52.074-.792.372-.272.297-1.04 1.016-1.04 2.479 0 1.462 1.065 2.875 1.213 3.074.149.198 2.096 3.2 5.077 4.487.709.306 1.262.489 1.694.625.712.227 1.36.195 1.871.118.571-.085 1.758-.719 2.006-1.413.248-.694.248-1.289.173-1.413-.074-.124-.272-.198-.57-.347m-5.421 7.403h-.004a9.87 9.87 0 01-5.031-1.378l-.361-.214-3.741.982.998-3.648-.235-.374a9.86 9.86 0 01-1.51-5.26c.001-5.45 4.436-9.884 9.888-9.884 2.64 0 5.122 1.03 6.988 2.898a9.825 9.825 0 012.893 6.994c-.003 5.45-4.437 9.884-9.885 9.884m8.413-18.297A11.815 11.815 0 0012.05 0C5.495 0 .16 5.335.157 11.892c0 2.096.547 4.142 1.588 5.945L.057 24l6.305-1.654a11.882 11.882 0 005.683 1.448h.005c6.554 0 11.89-5.335 11.893-11.893a11.821 11.821 0 00-3.48-8.413Z"/>
      </svg>
    ),
    copy: () => (
      <svg className={iconSizeClasses[variant] || iconSizeClasses.default} fill="none" stroke="currentColor" viewBox="0 0 24 24">
        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 16H6a2 2 0 01-2-2V6a2 2 0 012-2h8a2 2 0 012 2v2m-6 12h8a2 2 0 002-2v-8a2 2 0 00-2-2h-8a2 2 0 00-2 2v8a2 2 0 002 2z" />
      </svg>
    ),
  };

  return (
    <div className={clsx(containerClasses[variant] || containerClasses.default, className)}>
      {platforms.map((platform) => {
        const IconComponent = ShareIcons[platform];
        if (!IconComponent) return null;

        return (
          <button
            key={platform}
            onClick={() => handleShare(platform)}
            className={clsx(buttonClasses[variant] || buttonClasses.default)}
            aria-label={`Share on ${platform}`}
            title={platform === 'copy' && copied ? 'Copied!' : `Share on ${platform}`}
          >
            <IconComponent />
          </button>
        );
      })}
    </div>
  );
};

