import React, { useState, useEffect } from 'react';
import { clsx } from 'clsx';
import { CheckoutSkeleton } from '../CheckoutSkeleton';

export const CheckoutModern = ({ data = {}, className, loading = false }) => {
  const [email, setEmail] = useState('');
  const [timedOut, setTimedOut] = useState(false);

  // Auto-show content after 2 seconds if loading is true
  useEffect(() => {
    if (loading) {
      setTimedOut(false);
      const timer = setTimeout(() => {
        setTimedOut(true);
      }, 2000);
      return () => clearTimeout(timer);
    }
  }, [loading]);

  const shouldShowSkeleton = loading && !timedOut;

  const cartItems = data.cartItems || [];
  const subtotal = cartItems.reduce((acc, item) => acc + item.price, 0);

  // Show skeleton loading state
  if (shouldShowSkeleton) {
    return <CheckoutSkeleton data={{ variant: 'modern' }} className={className} />;
  }

  return (
    <div className={clsx("checkout-modern min-h-screen bg-neutral-50 py-12 px-6", className)}>
      <div className="max-w-6xl mx-auto grid grid-cols-1 lg:grid-cols-3 gap-12">
        
        <div className="lg:col-span-2 space-y-12">
          <div className="flex items-center space-x-4 mb-8">
            <div className="bg-black text-white w-10 h-10 rounded-full flex items-center justify-center font-bold">1</div>
            <h2 className="text-2xl font-bold">{data.shipping?.title || 'Contact & Shipping'}</h2>
          </div>
          
          <div className="bg-white p-8 rounded-2xl shadow-sm border border-neutral-100 space-y-6">
            <input
              type="email"
              placeholder={data.email?.placeholder || "Email Address"}
              className="w-full bg-neutral-50 border-transparent rounded-xl focus:ring-2 focus:ring-black focus:bg-white transition-all py-4 px-6"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
            />
            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <input type="text" placeholder={data.shipping?.fields?.firstName || "First Name"} className="w-full bg-neutral-50 border-transparent rounded-xl focus:ring-2 focus:ring-black focus:bg-white py-4 px-6" />
              <input type="text" placeholder={data.shipping?.fields?.lastName || "Last Name"} className="w-full bg-neutral-50 border-transparent rounded-xl focus:ring-2 focus:ring-black focus:bg-white py-4 px-6" />
            </div>
            <input type="text" placeholder={data.shipping?.fields?.address || "Complete Address"} className="w-full bg-neutral-50 border-transparent rounded-xl focus:ring-2 focus:ring-black focus:bg-white py-4 px-6" />
          </div>

          <div className="flex items-center space-x-4 pt-4">
            <div className="bg-black text-white w-10 h-10 rounded-full flex items-center justify-center font-bold">2</div>
            <h2 className="text-2xl font-bold">{data.payment?.title || 'Payment Method'}</h2>
          </div>

          <div className="bg-white p-8 rounded-2xl shadow-sm border border-neutral-100 grid grid-cols-1 md:grid-cols-2 gap-4">
             {(data.payment?.methods || [
               { id: 'apple', title: 'Apple Pay', recommended: true },
               { id: 'card', title: 'Credit Card' }
             ]).map((method) => (
               <button key={method.id} className={clsx(
                 "flex items-center justify-between p-6 border-2 rounded-2xl transition-all",
                 method.recommended ? "border-black bg-white shadow-md" : "border-transparent bg-neutral-50 hover:bg-neutral-100"
               )}>
                  <span className={clsx("font-bold text-lg", !method.recommended && "text-neutral-500")}>{method.title}</span>
                  {method.recommended && <span className="bg-black text-white px-3 py-1 rounded text-xs">Recommended</span>}
               </button>
             ))}
          </div>
        </div>

        <div className="lg:col-span-1">
          <div className="bg-white p-8 rounded-3xl shadow-xl border border-neutral-100 sticky top-8">
            <h3 className="text-xl font-black mb-8 uppercase tracking-tighter">{data.summary?.title || 'Your Bag'}</h3>
            <div className="space-y-6 mb-8 max-h-60 overflow-y-auto pr-2">
              {cartItems.map((item, idx) => (
                <div key={idx} className="flex items-center justify-between group">
                  <div className="flex items-center space-x-4">
                    <div className="w-12 h-12 bg-neutral-100 rounded-lg flex-shrink-0"></div>
                    <div>
                      <p className="font-bold text-sm leading-tight">{item.name}</p>
                      <p className="text-xs text-neutral-400">Qty 1</p>
                    </div>
                  </div>
                  <span className="font-bold text-sm">${item.price.toFixed(2)}</span>
                </div>
              ))}
            </div>

            <div className="space-y-4 border-t pt-8">
              <div className="flex justify-between text-neutral-500">
                <span>{data.summary?.subtotalLabel || 'Subtotal'}</span>
                <span className="font-bold text-black">${subtotal.toFixed(2)}</span>
              </div>
              <div className="flex justify-between text-neutral-500 text-sm">
                <span>{data.summary?.deliveryLabel || 'Shipping'}</span>
                <span className="text-green-600 font-bold uppercase text-[10px] tracking-widest bg-green-50 px-2 py-0.5 rounded">{data.summary?.deliveryValue || 'Free'}</span>
              </div>
              <div className="flex justify-between text-2xl font-black pt-4 border-t border-neutral-50">
                <span>{data.summary?.totalLabel || 'Total'}</span>
                <span>${subtotal.toFixed(2)}</span>
              </div>
            </div>

            <button className="w-full bg-black text-white py-5 rounded-2xl mt-10 font-black text-lg hover:scale-[1.02] transition-transform active:scale-100">
              {data.summary?.buttonText || 'Checkout Now'}
            </button>
          </div>
        </div>

      </div>
    </div>
  );
};
