import { useState, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/product.css';

/**
 * ProductRating Component - Star rating display/input with multiple variants
 * @param {Object} props - Component props
 * @param {Object} props.data - Rating configuration data
 * @param {string} [props.data.variant='default'] - Style variant: 'default' | 'modern' | 'minimal' | 'compact' | 'icon' | 'dark' | 'dark-gray'
 * @param {number} [props.data.rating=0] - Current rating (0-5)
 * @param {boolean} [props.data.editable=false] - Allow user to set rating
 * @param {boolean} [props.data.showValue=false] - Show rating value text
 * @param {number} [props.data.size=5] - Number of stars
 * @param {Function} [props.onRatingChange] - Callback when rating changes
 * @param {boolean} [props.loading=false] - Show skeleton loader
 * @param {string} [props.className] - Additional CSS classes
 */
export const ProductRating = ({ 
  data = {}, 
  onRatingChange,
  loading = false,
  className 
}) => {
  const {
    variant = 'default',
    rating = 0,
    editable = false,
    showValue = false,
    size = 5,
  } = data;

  const [hoverRating, setHoverRating] = useState(0);
  const [showContent, setShowContent] = useState(!loading);

  useEffect(() => {
    if (loading) {
      setShowContent(false);
      const timer = setTimeout(() => {
        setShowContent(true);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowContent(true);
    }
  }, [loading]);

  const handleClick = (value) => {
    if (editable && onRatingChange) {
      onRatingChange(value);
    }
  };

  const starSizeClasses = {
    default: 'w-4 h-4 sm:w-5 sm:h-5',
    modern: 'w-5 h-5 sm:w-6 sm:h-6',
    minimal: 'w-3.5 h-3.5 sm:w-4 sm:h-4',
    compact: 'w-3 h-3 sm:w-3.5 sm:h-3.5',
    icon: 'w-5 h-5 sm:w-6 sm:h-6',
    dark: 'w-4 h-4 sm:w-5 sm:h-5',
    'dark-gray': 'w-4 h-4 sm:w-5 sm:h-5',
  };

  const containerClasses = {
    default: 'flex items-center gap-1 sm:gap-1.5',
    modern: 'flex items-center gap-1.5 sm:gap-2',
    minimal: 'flex items-center gap-0.5 sm:gap-1',
    compact: 'flex items-center gap-0.5',
    icon: 'flex items-center gap-1 sm:gap-1.5',
    dark: 'flex items-center gap-1 sm:gap-1.5',
    'dark-gray': 'flex items-center gap-1 sm:gap-1.5',
  };

  const SkeletonLoader = () => {
    const isDark = variant === 'dark' || variant === 'dark-gray';
    const skeletonBg = isDark ? 'bg-gray-700' : 'bg-gray-200';

    return (
      <div className={clsx(containerClasses[variant] || containerClasses.default, className)}>
        {Array.from({ length: size }).map((_, i) => (
          <div
            key={i}
            className={clsx(
              starSizeClasses[variant] || starSizeClasses.default,
              'rounded product-skeleton',
              skeletonBg
            )}
          ></div>
        ))}
      </div>
    );
  };

  if (!showContent) {
    return <SkeletonLoader />;
  }

  const StarIcon = ({ filled, className }) => (
    <svg className={className} fill={filled ? 'currentColor' : 'none'} stroke="currentColor" viewBox="0 0 24 24">
      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M11.049 2.927c.3-.921 1.603-.921 1.902 0l1.519 4.674a1 1 0 00.95.69h4.915c.969 0 1.371 1.24.588 1.81l-3.976 2.888a1 1 0 00-.363 1.118l1.518 4.674c.3.922-.755 1.688-1.538 1.118l-3.976-2.888a1 1 0 00-1.176 0l-3.976 2.888c-.783.57-1.838-.197-1.538-1.118l1.518-4.674a1 1 0 00-.363-1.118l-3.976-2.888c-.784-.57-.38-1.81.588-1.81h4.914a1 1 0 00.951-.69l1.519-4.674z" />
    </svg>
  );

  const displayRating = hoverRating || rating;
  const starColor = variant === 'dark' || variant === 'dark-gray' ? 'text-yellow-400' : 'text-yellow-400';
  const emptyColor = variant === 'dark' || variant === 'dark-gray' ? 'text-gray-600' : 'text-gray-300';

  return (
    <div className={clsx(containerClasses[variant] || containerClasses.default, className)}>
      {Array.from({ length: size }).map((_, index) => {
        const value = index + 1;
        const isFilled = value <= displayRating;
        const isHalf = value - 0.5 === displayRating;

        return (
          <span
            key={index}
            onClick={() => editable && handleClick(value)}
            onMouseEnter={() => editable && setHoverRating(value)}
            onMouseLeave={() => editable && setHoverRating(0)}
            className={clsx(
              editable && 'cursor-pointer',
              starSizeClasses[variant] || starSizeClasses.default
            )}
          >
            <StarIcon
              filled={isFilled}
              className={clsx(
                starSizeClasses[variant] || starSizeClasses.default,
                isFilled ? starColor : emptyColor,
                'transition-colors duration-150'
              )}
            />
          </span>
        );
      })}
      {showValue && (
        <span className={clsx(
          'text-xs sm:text-sm ml-1 sm:ml-2',
          variant === 'dark' || variant === 'dark-gray' ? 'text-gray-300' : 'text-gray-600'
        )}>
          ({rating.toFixed(1)})
        </span>
      )}
    </div>
  );
};

