import { useState, useEffect } from 'react';
import clsx from 'clsx';
import '../../styles/product.css';

/**
 * ProductCompare Component - Product comparison feature with multiple variants
 * @param {Object} props - Component props
 * @param {Object} props.data - Compare configuration data
 * @param {string} [props.data.variant='default'] - Style variant: 'default' | 'modern' | 'minimal' | 'compact' | 'icon' | 'dark' | 'dark-gray'
 * @param {Array} [props.data.products=[]] - Products to compare
 * @param {number} [props.data.maxCompare=4] - Maximum products to compare
 * @param {Function} [props.onCompareChange] - Callback when compare list changes
 * @param {boolean} [props.loading=false] - Show skeleton loader
 * @param {string} [props.className] - Additional CSS classes
 */
export const ProductCompare = ({ 
  data = {}, 
  onCompareChange,
  loading = false,
  className 
}) => {
  const {
    variant = 'default',
    products = [],
    maxCompare = 4,
  } = data;

  const [compareList, setCompareList] = useState([]);
  const [isOpen, setIsOpen] = useState(false);
  const [showContent, setShowContent] = useState(!loading);

  useEffect(() => {
    if (loading) {
      setShowContent(false);
      const timer = setTimeout(() => {
        setShowContent(true);
      }, 2000);
      return () => clearTimeout(timer);
    } else {
      setShowContent(true);
    }
  }, [loading]);


  const handleToggle = (product) => {
    const isInList = compareList.some(p => p.id === product.id);
    let newList;
    
    if (isInList) {
      newList = compareList.filter(p => p.id !== product.id);
    } else {
      if (compareList.length >= maxCompare) {
        return; // Max limit reached
      }
      newList = [...compareList, product];
    }
    
    setCompareList(newList);
    if (onCompareChange) {
      onCompareChange(newList);
    }
  };

  const handleRemove = (productId) => {
    const newList = compareList.filter(p => p.id !== productId);
    setCompareList(newList);
    if (onCompareChange) {
      onCompareChange(newList);
    }
  };

  const buttonClasses = {
    default: clsx(
      'px-3 sm:px-4 py-2 sm:py-2.5 text-sm sm:text-base rounded-lg border border-gray-300',
      'flex items-center gap-2 bg-white hover:bg-gray-50',
      'transition-all duration-200 '
    ),
    modern: clsx(
      'px-4 sm:px-5 py-2.5 sm:py-3 text-sm sm:text-base rounded-xl border-2 border-gray-200',
      'flex items-center gap-2 bg-white hover:shadow-md font-medium',
      'transition-all duration-300 '
    ),
    minimal: clsx(
      'px-2 sm:px-3 py-1.5 sm:py-2 text-sm border-b-2 border-transparent',
      'flex items-center gap-1.5 bg-transparent hover:border-primary-500',
      'transition-all duration-200'
    ),
    compact: clsx(
      'px-2 sm:px-3 py-1.5 text-xs sm:text-sm rounded-md border border-gray-300',
      'flex items-center gap-1.5 bg-white hover:bg-gray-50',
      'transition-all duration-200'
    ),
    icon: clsx(
      'p-2 sm:p-2.5 rounded-lg bg-primary-500 hover:bg-primary-600 text-white',
      'shadow-md hover:shadow-lg transition-all duration-300',
      ''
    ),
    dark: clsx(
      'px-3 sm:px-4 py-2 sm:py-2.5 text-sm sm:text-base rounded-lg border border-gray-600',
      'flex items-center gap-2 bg-gray-800 hover:bg-gray-700 text-gray-200',
      'transition-all duration-200 '
    ),
    'dark-gray': clsx(
      'px-3 sm:px-4 py-2 sm:py-2.5 text-sm sm:text-base rounded-lg border border-gray-700',
      'flex items-center gap-2 bg-gray-900 hover:bg-gray-800 text-gray-300',
      'transition-all duration-200 '
    ),
  };

  const SkeletonLoader = () => {
    const isDark = variant === 'dark' || variant === 'dark-gray';
    const skeletonBg = isDark ? 'bg-gray-700' : 'bg-gray-200';

    return (
      <div className={clsx(buttonClasses[variant] || buttonClasses.default, className)}>
        <div className={clsx('w-5 h-5 rounded product-skeleton', skeletonBg)}></div>
        {variant !== 'icon' && (
          <div className={clsx('h-4 w-20 rounded product-skeleton', skeletonBg)}></div>
        )}
      </div>
    );
  };

  if (!showContent) {
    return <SkeletonLoader />;
  }

  const CompareIcon = () => (
    <svg className="w-4 h-4 sm:w-5 sm:h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 17V7m0 10a2 2 0 01-2 2H5a2 2 0 01-2-2V7a2 2 0 012-2h2a2 2 0 012 2m0 10a2 2 0 002 2h2a2 2 0 002-2M9 7a2 2 0 012-2h2a2 2 0 012 2m0 10V7m0 10a2 2 0 002 2h2a2 2 0 002-2V7a2 2 0 00-2-2h-2a2 2 0 00-2 2" />
    </svg>
  );

  return (
    <div className={clsx('relative', className)}>
      <button
        type="button"
        onClick={() => setIsOpen(!isOpen)}
        className={clsx(buttonClasses[variant] || buttonClasses.default)}
      >
        {variant === 'icon' ? (
          <>
            <CompareIcon />
            {compareList.length > 0 && (
              <span className="absolute -top-1 -right-1 bg-red-500 text-white text-xs rounded-full w-5 h-5 flex items-center justify-center">
                {compareList.length}
              </span>
            )}
          </>
        ) : (
          <>
            <CompareIcon />
            <span>Compare ({compareList.length})</span>
          </>
        )}
      </button>

      {isOpen && compareList.length > 0 && (
        <div className={clsx(
          'absolute top-full mt-2 right-0 w-64 sm:w-80 bg-white border border-gray-300 rounded-lg shadow-xl z-50',
          variant === 'dark' && 'bg-gray-800 border-gray-600',
          variant === 'dark-gray' && 'bg-gray-900 border-gray-700'
        )}>
          <div className="p-4">
            <h3 className={clsx(
              'font-semibold mb-3',
              variant === 'dark' || variant === 'dark-gray' ? 'text-white' : 'text-gray-900'
            )}>Compare Products</h3>
            <div className="space-y-2">
              {compareList.map((product) => (
                <div key={product.id} className="flex items-center justify-between p-2 hover:bg-gray-50 rounded">
                  <span className={clsx(
                    'text-sm truncate flex-1',
                    variant === 'dark' || variant === 'dark-gray' ? 'text-gray-200' : 'text-gray-700'
                  )}>{product.title}</span>
                  <button
                    onClick={() => handleRemove(product.id)}
                    className="text-red-500 hover:text-red-700 ml-2"
                  >
                    <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                    </svg>
                  </button>
                </div>
              ))}
            </div>
            <button className="w-full mt-4 px-4 py-2 bg-primary-600 text-white rounded-lg hover:bg-primary-700">
              Compare Now
            </button>
          </div>
        </div>
      )}
    </div>
  );
};

